import React, {FC, ComponentType} from "react";
import {create} from "jss";
import rtl from "jss-rtl";
import {StylesProvider, jssPreset} from "@material-ui/core/styles";

type StylesProviderProps = typeof StylesProvider extends ComponentType<infer T> ? T : never;

export type BidiStylesProviderProps = StylesProviderProps;

// Configure JSS
const jss = create({plugins: [...jssPreset().plugins, rtl()]});

export const BidiStylesProvider: FC<BidiStylesProviderProps> = props => {
    return <StylesProvider jss={jss} {...props} />;
};
