import React, {FC, useCallback, useState} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import clsx from "clsx";
import ListItemText from "@material-ui/core/ListItemText";
import Collapse from "@material-ui/core/Collapse";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
import AddIcon from "@material-ui/icons/Add";
import DeleteIcon from "@material-ui/icons/Delete";
import {useFormContext, useFieldArray} from "react-hook-form";
import {useTranslation} from "react-i18next";

import {ResponseType, TaskType} from "../../api/nimflow-api";
import {TaskTypeDataSettersEditor} from "./task-type-data-setters-editor";
import {TaskTypeHeaderEditor} from "./task-type-header-editor";
// import {FormErrorViewer} from "../../components/form-error-viewer";
import {useOkCancelConfirm} from "../../components/confirm";
import {ResponseTypeEditor} from "./response-type-editor";

const menuWidth = "240px";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            flexGrow: 1,
            //backgroundColor: theme.palette.background.paper,
            backgroundColor: theme.palette.background.default,
            display: "flex",
            height: "100%",
            width: "100%",
        },
        list: {
            width: "100%",
            maxWidth: menuWidth,
            backgroundColor: theme.palette.background.paper,
            borderRight: `1px solid ${theme.palette.divider}`,
            minHeight: "500px",
        },
        panels: {
            width: `calc(100% - ${menuWidth})`,
        },
        nested: {
            paddingLeft: theme.spacing(4),
        },
        textField: {
            width: "40ch",
        },
        item: {
            border: "2px solid limegreen",
            borderRadius: 2,
            paddingRight: 48,
        },
        listItemRoot: {
            overflow: "hidden"
        },
        actionHeading: {
            fontSize: theme.typography.pxToRem(15),
            fontWeight: theme.typography.fontWeightRegular,
        },
        summaryTextContainer: {
            width: "100%",
            height: "100%",
            display: "flex",
            alignItems: "center",
        },
        divider: {
            height: 5,
            background: "transparent",
        },
        grow: {
            flexGrow: 1,
        },
        toolbarButtons: {
            display: "flex",
        },
        paper: {
            padding: theme.spacing(2),
            color: theme.palette.text.secondary,
        },
        selected: {
            fontWeight: "bold",
        },
        content: {
            backgroundColor: theme.palette.background.default,
            minWidth: "100%",
        },
    })
);

interface TabPanelProps {
    children?: React.ReactNode;
    visible: boolean;
}

function TabPanel(props: TabPanelProps) {
    const {children, visible, ...other} = props;

    return (
        <div role="tabpanel" hidden={!visible} {...other} style={{width: "100%"}}>
            {children}
        </div>
    );
}

const mainSelectionTypes = {
    header: "header",
    setters: "setters",
    responses: "responses",
} as const;

interface Selection {
    main: typeof mainSelectionTypes[keyof typeof mainSelectionTypes];
    responseIndex?: number;
}

const useSelection = (initialSelection?: Selection) => {
    const [currentSelection, setCurrentSelection] = useState<Selection>(
        initialSelection ?? {main: mainSelectionTypes.header}
    );
    const setHeaderSectionSelected = useCallback(() => {
        setCurrentSelection({main: mainSelectionTypes.header});
    }, [setCurrentSelection]);
    const headerSectionSelected = currentSelection?.main === mainSelectionTypes.header;
    const setSettersSectionSelected = useCallback(() => {
        setCurrentSelection({main: mainSelectionTypes.setters});
    }, [setCurrentSelection]);
    const settersSectionSelected = currentSelection?.main === mainSelectionTypes.setters;
    const setResponseIndexSelected = useCallback(
        (index: number) => {
            setCurrentSelection({main: mainSelectionTypes.responses, responseIndex: index});
        },
        [setCurrentSelection]
    );
    const responsesSectionSelected = currentSelection?.main === mainSelectionTypes.responses;
    const responseIndexSelected =
        currentSelection?.main === mainSelectionTypes.responses ? currentSelection?.responseIndex : undefined;
    const isResponseSelected = (index: number) => {
        return responseIndexSelected === index;
    };

    return {
        headerSectionSelected,
        setHeaderSectionSelected,
        settersSectionSelected,
        setSettersSectionSelected,
        responsesSectionSelected,
        responseIndexSelected,
        isResponseSelected,
        setResponseIndexSelected,
    };
};

export interface TaskTypeEditorProps {
    definitionJsonSchemas: Record<string, string | undefined>;
    onDefinitionJsonSchemasChange?: (value: Record<string, string | undefined>) => void;
}

export const TaskTypeEditor: FC<TaskTypeEditorProps> = props => {
    const {definitionJsonSchemas, onDefinitionJsonSchemasChange} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const [responsesListOpen, setResponsesListOpen] = React.useState(true);
    const {
        headerSectionSelected,
        setHeaderSectionSelected,
        settersSectionSelected,
        setSettersSectionSelected,
        responsesSectionSelected,
        //responseIndexSelected,
        isResponseSelected,
        setResponseIndexSelected,
    } = useSelection();
    const {control, errors, getValues, setValue, watch, clearErrors} = useFormContext<TaskType>();
    const {
        fields: validResponses,
        append,
        remove,
    } = useFieldArray<ResponseType, "name">({
        control,
        name: "validResponses",
        keyName: "name",
    });
    const confirm = useOkCancelConfirm();

    const handleClick = useCallback(() => {
        setResponsesListOpen(!responsesListOpen);
    }, [responsesListOpen]);

    const handleNewResponseTypeClick = useCallback(
        (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
            const baseName = t("NewResponseDefaultName");
            let cnt = 2;
            let name = baseName;
            // eslint-disable-next-line no-loop-func
            while (validResponses.findIndex(s => s.name === name) >= 0) {
                name = baseName + cnt++;
            }
            append({name, validators: [], jsSetters: {}});
            setResponseIndexSelected(validResponses.length);
            clearErrors("validResponses");
            event.stopPropagation();
        },
        [append, clearErrors, setResponseIndexSelected, t, validResponses]
    );

    const handleDeleteResponseTypeClick = useCallback(
        async (index: number) => {
            let result = await confirm({
                description: t("ConfirmDeleteResponseType", {name: validResponses[index].name}),
            });
            if (result) {
                const defName = validResponses[index]?.payloadSchemaDefinitionName;
                if (defName && defName in definitionJsonSchemas) {
                    const newDefs = {
                        ...definitionJsonSchemas,
                    };
                    delete newDefs[defName];
                    onDefinitionJsonSchemasChange?.(newDefs);
                }
                remove(index);
            }
        },
        [confirm, definitionJsonSchemas, onDefinitionJsonSchemasChange, remove, t, validResponses]
    );

    const getTaskTypographyColor = useCallback(() => {
        return errors.name || errors.titleTemplate ? "error" : "initial";
    }, [errors.name, errors.titleTemplate]);

    const getSettersTypographyColor = useCallback(() => {
        return errors.jsTaskDataSetters ? "error" : "initial";
    }, [errors.jsTaskDataSetters]);

    const getValidResponsesTypographyColor = useCallback(() => {
        return errors.validResponses ? "error" : "initial";
    }, [errors.validResponses]);

    const getDefinitionJsonSchema = useCallback(
        (response: Partial<ResponseType>) => {
            const defName = response.payloadSchemaDefinitionName;
            if (!defName) {
                return undefined;
            }
            return definitionJsonSchemas[defName];
        },
        [definitionJsonSchemas]
    );

    const handleDefinitionJsonSchemaChange = useCallback(
        (response: Partial<ResponseType>, idx: number, newDefName: string, schema: string | undefined) => {
            const newDefs = {
                ...definitionJsonSchemas,
            };
            if (!schema && response.payloadSchemaDefinitionName) {
                delete newDefs[response.payloadSchemaDefinitionName];
            } else if (schema && newDefName) {
                newDefs[newDefName] = schema;
            } else {
                return;
            }
            const path = `validResponses[${idx}].payloadSchemaDefinitionName`;
            if (getValues(path) !== newDefName) {
                setValue(path, newDefName, {shouldDirty: true});
                response.payloadSchemaDefinitionName = newDefName;
            }
            onDefinitionJsonSchemasChange?.(newDefs);
        },
        [definitionJsonSchemas, getValues, onDefinitionJsonSchemasChange, setValue]
    );

    return (
        <div className={classes.root}>
            <List component="nav" aria-labelledby="nested-list-subheader" className={classes.list}>
                <ListItem button selected={headerSectionSelected} onClick={setHeaderSectionSelected}>
                    <ListItemText
                        classes={{root: classes.listItemRoot}}
                        primary={t("Task")}
                        primaryTypographyProps={{
                            className: clsx({
                                [classes.selected]: headerSectionSelected,
                            }),
                            color: getTaskTypographyColor(),
                        }}
                    />
                </ListItem>
                <ListItem button selected={settersSectionSelected} onClick={setSettersSectionSelected}>
                    <ListItemText
                        classes={{root: classes.listItemRoot}}
                        primary={t("Setters")}
                        primaryTypographyProps={{
                            className: clsx({
                                [classes.selected]: settersSectionSelected,
                            }),
                            color: getSettersTypographyColor(),
                        }}
                    />
                </ListItem>
                <ListItem button onClick={handleClick}>
                    <ListItemText
                        classes={{root: classes.listItemRoot}}
                        primary={t("Responses")}
                        primaryTypographyProps={{
                            className: clsx({
                                [classes.selected]: responsesSectionSelected,
                            }),
                            color: getValidResponsesTypographyColor(),
                        }}
                        // secondary={<FormErrorViewer errors={errors} names="validResponses" />}
                    />
                    {responsesListOpen ? <ExpandLessIcon /> : <ExpandMoreIcon />}
                    <Tooltip title={t("NewResponseType") as string}>
                        <IconButton onClick={handleNewResponseTypeClick}>
                            <AddIcon />
                        </IconButton>
                    </Tooltip>
                </ListItem>
                <Collapse in={responsesListOpen} timeout="auto" unmountOnExit>
                    {
                        //TODO: Make orderable
                    }
                    <List component="div" disablePadding>
                        {validResponses?.map((s, i) => {
                            const isSelected = isResponseSelected(i);
                            return (
                                <ListItem
                                    key={s.name}
                                    button
                                    selected={isSelected}
                                    onClick={() => setResponseIndexSelected(i)}
                                    className={classes.nested}
                                >
                                    <ListItemText
                                        classes={{root: classes.listItemRoot}}
                                        primary={watch(`validResponses[${i}].name`)}
                                        primaryTypographyProps={{
                                            variant: "subtitle1",
                                            className: clsx({[classes.selected]: isSelected}),
                                            color: errors?.validResponses?.[i] ? "error" : "textSecondary",
                                        }}
                                    />
                                    <ListItemSecondaryAction>
                                        <Tooltip title={t("Delete") as string}>
                                            <IconButton
                                                edge="end"
                                                aria-label="delete"
                                                onClick={() => handleDeleteResponseTypeClick(i)}
                                            >
                                                <DeleteIcon />
                                            </IconButton>
                                        </Tooltip>
                                    </ListItemSecondaryAction>
                                </ListItem>
                            );
                        })}
                    </List>
                </Collapse>
            </List>
            <div className={classes.panels}>
                <TabPanel visible={headerSectionSelected}>
                    <TaskTypeHeaderEditor />
                </TabPanel>
                <TabPanel visible={settersSectionSelected}>
                    <TaskTypeDataSettersEditor path="jsTaskDataSetters" />
                </TabPanel>
                {validResponses?.map((s, i) => {
                    return (
                        <TabPanel key={s.name} visible={isResponseSelected(i)}>
                            <ResponseTypeEditor
                                path={`validResponses[${i}]`}
                                defaultValue={s}
                                taskTypeName={getValues("name")}
                                definitionJsonSchema={getDefinitionJsonSchema(s)}
                                onDefinitionJsonSchemaChange={(defSchema, defName) =>
                                    handleDefinitionJsonSchemaChange(s, i, defName, defSchema)
                                }
                            />
                        </TabPanel>
                    );
                })}
            </div>
        </div>
    );
};
