import {FC, Suspense} from "react";
import ReactDOM from "react-dom";
import {I18nextProvider} from "react-i18next";
import i18next from "i18next";
import {SnackbarProvider} from "notistack";
import {BrowserRouter} from "react-router-dom";
import {RecoilState} from "recoil";
import RecoilizeDebugger from "recoilize";

import App from "./App";
import {ThemeProvider} from "./components/theme-provider";
import {BidiStylesProvider} from "./components/bidi-styles-provider";
import {ConfirmProvider} from "./components/confirm";
import {currentUnitIdState} from "./api/nimflow-business-directory-provider";
import {LocalizationProvider} from "./components/localization-provider";
import {BackgroundJobMonitorContextProvider} from "./services/background-job-monitoring-service";
import * as nodes from "./recoil-store";
import {PersistentRecoilRoot} from "./components/persistent-recoil-root";
import {viewContextTypeAsDiagram} from "./features/context-type-management/context-type-edition.state";
import {DatePickersProvider} from "./libs/material-ui-pickers";
import {SystemInfoContextProvider} from "./services/system-info-service";
import {WaitPage} from "./components/wait-page";
import {AuthenticationServiceProvider} from "./features/auth/authentication-service";
import {persistedConfigurationStorageKey} from "./env-vars";

const rootElement = document.getElementById("root");

export const RecoilDebugger: FC<{}> = () => {
    if (process.env.NODE_ENV !== "development") {
        return null;
    }
    return <RecoilizeDebugger nodes={nodes} root={rootElement} />;
};

const persistedAtoms: RecoilState<any>[] = [currentUnitIdState, viewContextTypeAsDiagram];

const renderApp = () => {
    const spinner = <WaitPage />;

    const Root = () => (
        <Suspense fallback={spinner}>
            <I18nextProvider i18n={i18next}>
                <BidiStylesProvider>
                    <PersistentRecoilRoot storageKey={persistedConfigurationStorageKey} persistedAtoms={persistedAtoms}>
                        <ThemeProvider persistKey="theme">
                            <ConfirmProvider
                                defaultOptions={{
                                    title: i18next.t("AreYouSure"),
                                    confirmationText: i18next.t("Ok"),
                                    cancellationText: i18next.t("Cancel"),
                                }}
                            >
                                <SnackbarProvider maxSnack={3}>
                                    <DatePickersProvider>
                                        <BrowserRouter>
                                            <AuthenticationServiceProvider>
                                                <BackgroundJobMonitorContextProvider>
                                                    <SystemInfoContextProvider>
                                                        <LocalizationProvider />
                                                        {/* <RecoilDebugger /> */}
                                                        <App />
                                                    </SystemInfoContextProvider>
                                                </BackgroundJobMonitorContextProvider>
                                            </AuthenticationServiceProvider>
                                        </BrowserRouter>
                                    </DatePickersProvider>
                                </SnackbarProvider>
                            </ConfirmProvider>
                        </ThemeProvider>
                    </PersistentRecoilRoot>
                </BidiStylesProvider>
            </I18nextProvider>
        </Suspense>
    );

    ReactDOM.render(<Root />, rootElement);
};

if (process.env.NODE_ENV !== "production" && module.hot) {
    module.hot.accept("./App", renderApp);
}

renderApp();
