import React, {FC, useCallback, useMemo, useRef} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import * as yup from "yup";
import type {SchemaOf} from "yup";
import {FormikProps, Formik, FormikHelpers, Form} from "formik";
import i18next from "i18next";
import {useTranslation} from "react-i18next";

import {useEditionScope, EditionScopeProvider} from "../../components/edition-scope";
import {DashboardWidgetDataSource} from "../../api/nimflow-dashboard-widgets-api";
import {DashboardWidgetDataSourceSchema, resolveDashboardWidgetDataSourceSchema} from "./widget-definition-schema";
import {DashboardWidgetQueryDataSourceEditor} from "./dashboard-widget-query-data-source-editor";
import {DashboardWidgetFunctionDataSourceEditor} from "./dashboard-widget-function-data-source-editor";
import {ErrorBoundary} from "../../components/error-boundary";
import {validateAndSubmit} from "../../utils/formikHelpers";

const titleHeight = 64;
const footerHeight = 52;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            backgroundColor: theme.palette.background.default,
            height: "100%",
            width: "100%",
            minHeight: "90vh",
            maxHeight: "90vh",
            "& form": {
                height: "100%",
            },
        },
        dialogTitle: {
            height: titleHeight,
        },
        dialogContent: {
            height: `calc(100% - ${titleHeight}px - ${footerHeight}px)`,
            width: "100%",
        },
        dialogFooter: {
            height: footerHeight,
        },
    })
);

export interface DashboardWidgetDataSourceEditorDialogProps {
    open: boolean;
    defaultValue: DashboardWidgetDataSource;
    existingDataSources?: DashboardWidgetDataSource[];
    onChange?: (value: DashboardWidgetDataSource) => void;
    onClose: () => void;
}

const getExtendedSchema = (usedNames?: string[]) => {
    if (!usedNames || usedNames.length === 0) {
        return DashboardWidgetDataSourceSchema;
    }
    return yup.lazy<SchemaOf<DashboardWidgetDataSource>>((s: DashboardWidgetDataSource): any => {
        if (!s) {
            return DashboardWidgetDataSourceSchema;
        }
        let schema = resolveDashboardWidgetDataSourceSchema(s);
        if (schema) {
            if (!schema.fields) {
                return schema;
            }
            return schema.shape({
                name: schema.fields.name.concat(
                    yup
                        .string()
                        .defined()
                        .test({
                            message: ({value}) =>
                                i18next.t("DashboardWidgetDataSourceNameAlreadyExists", {name: value}),
                            test: function (value: any) {
                                return !usedNames.includes(value);
                            },
                        })
                ),
            });
        }
        return yup.mixed().test({
            test: function (val: any) {
                return this.createError({
                    path: this.path,
                    message: i18next.t("InvalidDashboardWidgetDataSourceType", {type: val.type}),
                });
            },
        });
    }) as unknown as SchemaOf<DashboardWidgetDataSource>;
};

export const DashboardWidgetDataSourceEditorDialog: FC<DashboardWidgetDataSourceEditorDialogProps> = props => {
    const {open, defaultValue: value, existingDataSources, onChange, onClose} = props;
    const classes = useStyles();
    const schema = useMemo(() => {
        const names = existingDataSources?.map(s => s.name).filter(s => s !== value?.name);
        return getExtendedSchema(names);
    }, [existingDataSources, value?.name]);

    const formRef = useRef<FormikProps<DashboardWidgetDataSource> | null>();
    const submitHandlerRef = useRef<(values: DashboardWidgetDataSource) => void>();

    const {t} = useTranslation();
    const editionScope = useEditionScope();

    const handleAccept = useCallback(() => {
        const form = formRef.current;
        if (form && onChange) {
            submitHandlerRef.current = (data: DashboardWidgetDataSource) => {
                onChange?.(data);
                onClose?.();
            };
            validateAndSubmit(form);
        }
    }, [onChange, onClose]);

    const handleSubmit = useCallback(
        async (values: DashboardWidgetDataSource, actions: FormikHelpers<DashboardWidgetDataSource>) => {
            try {
                if (submitHandlerRef.current) {
                    submitHandlerRef.current(values);
                    submitHandlerRef.current = undefined;
                }
            } finally {
                actions.setSubmitting(false);
            }
        },
        []
    );

    return (
        <Dialog
            open={open}
            fullWidth
            maxWidth="lg"
            classes={{paper: classes.paper}}
        >
            <Formik<DashboardWidgetDataSource>
                innerRef={s => (formRef.current = s)}
                initialValues={value}
                onSubmit={handleSubmit}
                validationSchema={schema}
                validateOnChange={true}
                validateOnBlur={true}
            >
                <Form autoComplete="off">
                    <EditionScopeProvider {...editionScope}>
                        <DialogTitle className={classes.dialogTitle}>
                            {t("EditingWidgetDataSource", {type: t(`DashboardWidgetDataSource_type_${value.type}`)})}
                        </DialogTitle>
                        <DialogContent className={classes.dialogContent}>
                            <ErrorBoundary>
                                {value.type === "query" && <DashboardWidgetQueryDataSourceEditor />}
                                {value.type === "function" && <DashboardWidgetFunctionDataSourceEditor />}
                                {/* <FormikErrorViewer errors={formRef.current.errors} /> */}
                            </ErrorBoundary>
                        </DialogContent>
                        <DialogActions className={classes.dialogFooter}>
                            <Button onClick={handleAccept} color="primary" disabled={!editionScope.canAccept}>
                                {t("Accept")}
                            </Button>
                            <Button onClick={onClose} color="primary">
                                {t("Cancel")}
                            </Button>
                        </DialogActions>
                    </EditionScopeProvider>
                </Form>
            </Formik>
        </Dialog>
    );
};
