import React, {FC, useCallback, useState, useMemo} from "react";
import {makeStyles, createStyles, Theme, useTheme} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Button from "@material-ui/core/Button";
import {useTranslation} from "react-i18next";
import MonacoEditor, {EditorDidMount, EditorWillMount} from "react-monaco-editor";
import type {languages} from "monaco-editor";
import FormHelperText from "@material-ui/core/FormHelperText";
import * as yup from "yup";
import Autosizer from "react-virtualized-auto-sizer";

import {validateWithYupSchema} from "../libs/yup";
import {useLoseChangesConfirm} from "./specialized-confirmations";
import {defaultMonacoOptions} from "../libs/monaco";
import {isPlainObject} from "../utils/types";
import contextTypeSchema from "../schemas/context-type.schema.json";
import simulationStateSchema from "../schemas/simulation-state.schema.json";
import uiSchemaSchema from "../schemas/ui-schema.schema.json";
import uiSchemaLibrarySchema from "../schemas/ui-schema-library.schema.json";
import widgetDefinitionSchema from "../schemas/widget-definition.schema.json";
import dashboardDefinitionSchema from "../schemas/dashboard-definition.schema.json";
import recognizedImageBatchSchema from "../schemas/recognized-image-batch.schema.json";
import {ErrorInfo} from "../models/error-info";

const errorContainerHeight = 100;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        dialogPaper: {
            height: "80vh",
        },
        dialogContent: {
            display: "grid",
            gridTemplateColumns: "auto",
            gridTemplateRows: (props: {showErrors: boolean}) =>
                props.showErrors ? `auto ${errorContainerHeight}px` : "auto",
            rowGap: "5px",
            placeItems: "stretch",
            height: "100%",
            overflowY: "hidden",
        },
        editorContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
        },
        errorContainer: {
            gridColumn: "1 / 2",
            gridRow: "2 / 3",
            overflowY: "auto",
        },
    })
);

export type JsonSchemaEditorModalProps = {
    open: boolean;
    defaultValue?: string;
    onChange?: (value?: string) => void;
    onClose: () => void;
    title?: React.ReactNode;
    extraJsonSchemas?: any[];
    validator?: (serializedJson: string) => Promise<ErrorInfo[]>;
};

const configureMonaco = (monaco: Parameters<EditorWillMount>[0], extraJsonSchemas?: any[]) => {
    const opts: languages.json.DiagnosticsOptions = {
        validate: true,
        schemas: [
            {
                uri: "http://json-schema.org/draft-07/schema",
                fileMatch: ["inmemory://model/*"],
            },
        ],
        enableSchemaRequest: true,
    };
    const extraSchemas: any[] = [
        uiSchemaSchema,
        uiSchemaLibrarySchema,
        contextTypeSchema,
        simulationStateSchema,
        widgetDefinitionSchema,
        dashboardDefinitionSchema,
        recognizedImageBatchSchema,
    ];
    if (extraJsonSchemas) {
        extraSchemas.push(...extraJsonSchemas);
    }
    for (const schema of extraSchemas) {
        if (isPlainObject(schema) && schema.$id) {
            opts.schemas!.push({
                uri: schema.$id,
                schema,
            });
        }
    }

    monaco.languages.json.jsonDefaults.setDiagnosticsOptions(opts);

    const options: ReturnType<EditorWillMount> = {
        selectOnLineNumbers: true,
        //codeLens: false,
        minimap: {
            enabled: false,
        },
    };
    return options;
};

const editorDidMount: EditorDidMount = editor => {
    editor.focus();
};

const defaultEmptySchema = `{
    "type": "object",
    "definitions": {

    }
}`;

const validationSchema = yup.string().jsonSchema();

const defaultValidator = (code: string) => validateWithYupSchema(validationSchema, code);

export const JsonSchemaEditorModal: FC<JsonSchemaEditorModalProps> = props => {
    const {open, defaultValue, onChange, onClose, title, extraJsonSchemas, validator} = props;
    const {t} = useTranslation();
    const theme = useTheme();
    const loseChangesConfirm = useLoseChangesConfirm();
    const [errors, setErrors] = useState<string[]>([]);
    const classes = useStyles({showErrors: errors.length > 0});
    const initialValue = useMemo(() => {
        if (!defaultValue) {
            return defaultEmptySchema;
        }
        if (defaultValue.includes("\n")) {
            return defaultValue;
        }
        return JSON.stringify(JSON.parse(defaultValue), undefined, 2);
    }, [defaultValue]);
    const [code, setCode] = useState(initialValue);
    const handleOnAccept = useCallback(async () => {
        const val = validator ?? defaultValidator;
        const einfos = await val(code);
        if (einfos.length > 0) {
            setErrors(einfos.map(s => s.message));
        } else {
            onChange?.(code);
            onClose();
        }
    }, [code, onChange, onClose, validator]);

    const handleOnClose = useCallback(async () => {
        if (initialValue !== code && !(await loseChangesConfirm())) {
            return;
        }
        onClose();
    }, [code, initialValue, loseChangesConfirm, onClose]);

    const handleDialogClose = useCallback(
        async (event: object, reason: "backdropClick" | "escapeKeyDown") => {
            if (initialValue !== code && !(await loseChangesConfirm())) {
                return;
            }
            if (reason !== "backdropClick") {
                onClose?.();
            }
        },
        [code, initialValue, loseChangesConfirm, onClose]
    );

    const editorWillMount: EditorWillMount = useCallback(
        monaco => {
            configureMonaco(monaco, extraJsonSchemas);
        },
        [extraJsonSchemas]
    );

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            maxWidth="xl"
            fullWidth
            classes={{
                paper: classes.dialogPaper,
            }}
        >
            <DialogTitle>{title ?? t("EditingJsonSchema")}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <div className={classes.editorContainer}>
                    <Autosizer>
                        {({height, width}) => (
                            <MonacoEditor
                                width={width}
                                height={height}
                                language="json"
                                theme={theme.palette.type === "light" ? "vs-light" : "vs-dark"}
                                value={code}
                                options={defaultMonacoOptions}
                                onChange={setCode}
                                editorWillMount={editorWillMount}
                                editorDidMount={editorDidMount}
                            />
                        )}
                    </Autosizer>
                </div>
                {errors.length > 0 && (
                    <div className={classes.errorContainer}>
                        <FormHelperText component="span" error>
                            {errors.map((s, i) => (
                                <p key={i}>{s}</p>
                            ))}
                        </FormHelperText>
                    </div>
                )}
            </DialogContent>
            <DialogActions>
                <Button onClick={handleOnAccept} color="primary">
                    {t("Accept")}
                </Button>
                <Button onClick={handleOnClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
