import {MutableRefObject, useRef, useMemo} from "react";

export const useForwardedRef = <T>(ref: ((instance: T | null) => void) | MutableRefObject<T | null> | null) => {
    const targetRef = useRef<T | undefined>(!!ref && typeof ref !== "function" ? ref.current ?? undefined : undefined);
    const proxy = useMemo(() => {
        return new Proxy(targetRef, {
            set: (obj, prop, value) => {
                if (prop === "current") {
                    if (typeof ref === "function") {
                        ref(value);
                    } else if (ref) {
                        ref.current = value;
                    }
                } 
                return Reflect.set(obj, prop, value);
            },
        });
    }, [targetRef, ref]);
    return proxy;
};
