import React, {FC, useEffect, useState, useCallback} from "react";
import axios from "axios";
import {useParams, useNavigate} from "react-router";
import {useSnackbar} from "notistack";
import {useTranslation} from "react-i18next";

import {ContextTypeEditor} from "./context-type-editor";
import {readJsonFromBlob} from "../../utils/io-helpers";
import {importContextTypeFromJson} from "../../models/context-type-importer";
import {useNimflow} from "../../api/nimflow-api-provider";
import {ContextType} from "../../api/nimflow-api";
import {useShowErrorInfos} from "../../components/use-show-error-infos";
import {useReportError} from "../../utils/use-report-error";
import {useOkCancelConfirm} from "../../components/confirm";

export const ContextTypeImporter: FC<{}> = () => {
    const {t} = useTranslation();
    const {blobUrl} = useParams();
    const navigate = useNavigate();
    const {enqueueSnackbar} = useSnackbar();
    const showErrors = useShowErrorInfos();
    const {contextTypes_FindOne, contextTypes_Update, contextTypes_Add} = useNimflow();
    const [contextType, setContextType] = useState<ContextType>();
    const reportErrors = useReportError();
    const confirm = useOkCancelConfirm();

    useEffect(() => {
        const dataUrl = atob(blobUrl);
        if (!dataUrl) {
            return;
        }
        axios
            .get(dataUrl, {responseType: "blob"})
            .then(async response => {
                const json = await readJsonFromBlob(response.data);
                const importResult = await importContextTypeFromJson(json);
                if (importResult.type === "success") {
                    setContextType(importResult.result); // Pass the raw json without caches.
                    return;
                } else {
                    reportErrors(t("FileIsNotAValidContextType"), importResult.errors, {
                        dialogProps: {
                            maxWidth: "lg",
                        },
                    });
                    navigate("../..");
                }
            })
            .catch(() => {
                navigate("../..");
            })
            .finally(() => {
                URL.revokeObjectURL(dataUrl);
            });
    }, [blobUrl, navigate, enqueueSnackbar, showErrors, t, reportErrors]);

    const handleOnChange = useCallback(
        async (value: ContextType) => {
            const existingContextType = await contextTypes_FindOne({name: value.name});
            let result: ContextType;
            if (!!existingContextType) {
                const overwrite = await confirm({
                    description: t("ConfirmOverwriteContextType", {name: value.name}),
                });
                if (!overwrite) {
                    return false;
                }
                value.version = existingContextType.version;
                result = await contextTypes_Update(value);
            } else {
                result = await contextTypes_Add(value);
            }
            setContextType(result);
            navigate(`../../edit/${result.name}`, {replace: true});
            return true;
        },
        [contextTypes_FindOne, navigate, confirm, t, contextTypes_Update, contextTypes_Add]
    );

    if (!contextType) {
        return null;
    }

    return <ContextTypeEditor value={contextType} onApplyChanges={handleOnChange} />;
};
