import React, {FC, useState, useEffect, useCallback, useMemo} from "react";
import {useParams, useNavigate} from "react-router-dom";
import {clone} from "ramda";
import * as yup from "yup";
import {useTranslation} from "react-i18next";

import {
    useNimflowDashboardWidgets,
    DashboardWidgetDefOrJsSpec,
} from "../../api/nimflow-dashboard-widgets-api";
import {
    DashboardWidgetDefinitionSchema,
    resolveDashboardWidgetDefOrJsSpecSchema,
} from "./widget-definition-schema";
import {WidgetDefOrJsSpecEditor} from "./widget-def-or-js-spec-editor";

export const WidgetDefinitionUpdater: FC<{}> = () => {
    const {name} = useParams();
    const {t} = useTranslation();
    const navigate = useNavigate();
    const [widgetDefOrJsSpec, setWidgetDefOrJsSpec] = useState<DashboardWidgetDefOrJsSpec>();
    const {
        dashboardWidgets_FindOne,
        dashboardWidgets_Replace,
        dashboardWidgets_InsertOne,
        dashboardWidgets_GetInfos,
    } = useNimflowDashboardWidgets();

    useEffect(() => {
        dashboardWidgets_FindOne(name).then(s => {
            setWidgetDefOrJsSpec(s);
        });
    }, [name, dashboardWidgets_FindOne]);

    const handleOnChange = useCallback(
        async (value: DashboardWidgetDefOrJsSpec) => {
            await dashboardWidgets_Replace(value);
            setWidgetDefOrJsSpec(value);
            return true;
        },
        [setWidgetDefOrJsSpec, dashboardWidgets_Replace]
    );

    const handleOnSaveAs = useCallback(
        async (value: DashboardWidgetDefOrJsSpec, newName: string) => {
            const newWidgetDefinition = clone(value);
            newWidgetDefinition.name = newName;
            delete newWidgetDefinition.id;
            delete (newWidgetDefinition as any)["_id"];
            newWidgetDefinition.id = await dashboardWidgets_InsertOne(newWidgetDefinition);
            setWidgetDefOrJsSpec(newWidgetDefinition);
            setTimeout(() => {
                navigate(`../${newWidgetDefinition.id}`, {replace: true});
            }, 0);
            return true;
        },
        [dashboardWidgets_InsertOne, navigate]
    );

    const schema = useMemo(() => {
        if (!widgetDefOrJsSpec) {
            return DashboardWidgetDefinitionSchema;
        }
        const schema = resolveDashboardWidgetDefOrJsSpecSchema(widgetDefOrJsSpec);
        return schema.shape({
            name: schema.fields!.name.concat(
                yup
                    .string()
                    .defined()
                    .test({
                        message: ({value}) => t("WidgetDefinitionNameAlreadyExists", {name: value}),
                        test: async function (value: any) {
                            if (typeof value !== "string") {
                                return true;
                            }
                            const infos = await dashboardWidgets_GetInfos({});
                            return !infos.some(
                                s => s.name.toLowerCase() === value.toLowerCase() && s.id !== this.parent.id
                            );
                        },
                    })
            ),
        });
    }, [dashboardWidgets_GetInfos, t, widgetDefOrJsSpec]);

    if (!widgetDefOrJsSpec) {
        return null;
    }

    return (
        <WidgetDefOrJsSpecEditor
            value={widgetDefOrJsSpec}
            onApplyChanges={handleOnChange}
            onSaveAs={handleOnSaveAs}
            schema={schema}
            isPersistent={true}
        />
    );
};
