import React, {FC} from "react";
import FormHelperText, { FormHelperTextProps } from "@material-ui/core/FormHelperText";
import {FormikErrors} from "formik";

import {ErrorInfo} from "../models/error-info";

export interface FormikErrorViewerProps extends FormHelperTextProps {
    errors: FormikErrors<any>;
    names?: string | string[];
}

const collectErrors = (errors: any, prefix: string, result: ErrorInfo[]) => {
    if (typeof errors === "string") {
        result.push({
            dataPath: prefix,
            message: errors,
        });
        return;
    }
    if (Array.isArray(errors)) {
        errors.forEach((s, i) => {
            collectErrors(s, `${prefix}[${i}]`, result);
        });
    } else if (typeof errors === "object") {
        if (prefix.length > 0) {
            prefix += ".";
        }
        for (const key of Object.keys(errors)) {
            collectErrors(errors[key], prefix + key, result);
        }
    }
};

export const FormikErrorViewer: FC<FormikErrorViewerProps> = props => {
    const {errors, names: n} = props;
    let infos: ErrorInfo[] = [];
    collectErrors(errors, "", infos);
    if (n) {
        const ns = Array.isArray(n) ? n : [n];
        infos = infos.filter(i => {
            return ns.some(n => i.dataPath && (i.dataPath === n || i.dataPath.startsWith(n + ".") || i.dataPath.startsWith(n + "[")));
        });
    }
    return (
        <>
            <FormHelperText component="span" error>
                {infos.map(s => (
                    <p key={s.dataPath}>{`${s.dataPath}: ${s.message}`}</p>
                ))}
            </FormHelperText>
        </>
    );
};
