import React, {FC, useCallback, Fragment, useRef, useEffect, useState, useMemo} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import clsx from "clsx";
import Tooltip from "@material-ui/core/Tooltip";
import HelpIcon from "@material-ui/icons/Help";
import {useTranslation} from "react-i18next";
import MonacoEditor, {EditorDidMount, EditorWillMount} from "react-monaco-editor";
import {useTheme} from "@material-ui/core/styles";
import * as monaco from "monaco-editor/esm/vs/editor/editor.api";
import {isPresent} from "ts-is-present";
import i18next from "i18next";
import Autosizer from "react-virtualized-auto-sizer";
import {ascend, sortWith, equals} from "ramda";
import {useAsync} from "react-use";

import {ensurePromise} from "../utils/promise-helpers";
import {useJsContextDefinitionsContext} from "./js-context-definitions-provider";
import {useGlobalImports} from "../services/global-imports";
import {defaultMonacoOptions} from "../libs/monaco";
import { useArrayContentsMemo } from "../utils/use-array-contents-memo";

//See ESLint, prettier integration and autocompletion: https://blog.expo.io/building-a-code-editor-with-monaco-f84b3a06deaf

const bodyWrapperLineHeight = 21;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        editorRow: {
            display: "table",
            height: (props: {wrapperHeight: number}) => `calc(100% - ${props.wrapperHeight}px)`,
            width: "100%",
        },
        return: {
            display: "table-cell",
            width: "150px",
            paddingLeft: 10,
            whiteSpace: "nowrap",
        },
        editorContainer: {
            display: "table-cell",
            height: "100%",
            width: "100%",
            verticalAlign: "top",
        },
        scriptBodyWrapper: {
            height: bodyWrapperLineHeight,
        },
        returnEnd: {
            paddingLeft: 10,
        },
    })
);

export interface FreeVariableDefinition {
    name: string;
    description?: string;
    isOut?: boolean;
}

export interface JavascriptMonacoEditorProps {
    value?: string;
    onChange?: (value: string | undefined) => void;
    onErrorsChange?: (errors: string[]) => void;
    freeVariables?: FreeVariableDefinition[];
    returnType?: string;
    isExpression?: boolean;
    importJsCommon?: boolean;
    height?: string | number;
    width?: string | number;
}

interface FreeVariableViewerProps {
    variable: FreeVariableDefinition;
}

const FreeVariableViewer: FC<FreeVariableViewerProps> = props => {
    const {t} = useTranslation();
    const {
        variable: {name, description, isOut},
    } = props;
    return (
        <Fragment>
            {isOut && "out "}
            {name}
            {!!description && (
                <Tooltip title={t(description) as string}>
                    <HelpIcon style={{fontSize: "14px"}} />
                </Tooltip>
            )}
        </Fragment>
    );
};

const defaultDeclarations = `
interface Console {
    assert(condition?: boolean, ...data: any[]): void;
    clear(): void;
    count(label?: string): void;
    countReset(label?: string): void;
    debug(...data: any[]): void;
    dir(item?: any, options?: any): void;
    dirxml(...data: any[]): void;
    error(...data: any[]): void;
    group(...data: any[]): void;
    groupCollapsed(...data: any[]): void;
    groupEnd(): void;
    info(...data: any[]): void;
    log(...data: any[]): void;
    table(tabularData?: any, properties?: string[]): void;
    time(label?: string): void;
    timeEnd(label?: string): void;
    timeLog(label?: string, ...data: any[]): void;
    timeStamp(label?: string): void;
    trace(...data: any[]): void;
    warn(...data: any[]): void;
}

declare var console: Console;
`;

const jsCommonFreeVariableDef: FreeVariableDefinition = {
    name: "common",
    description: i18next.t("JsCommonExportsDescription"),
};

//const mainUri = monaco.Uri.parse("file:///main.js");
const defaultDeclarationsUri = monaco.Uri.parse("file:///defaultsDeclarations.ts");
const commonUri = monaco.Uri.parse("file:///common.ts");
const commonDefsUri = monaco.Uri.parse("node_modules/@types/jsevaluationcommoncontext/index.d.ts");
const contextDefsUri = monaco.Uri.parse("node_modules/@types/jsevaluationcontext/index.d.ts");

export interface ExtraLib {
    content: string;
    filePath?: string;
}

const updateMonacoIntellisense = (extraLibs: ExtraLib[], monaco: Parameters<EditorWillMount>[0]) => {
    monaco.languages.typescript.typescriptDefaults.setExtraLibs(extraLibs);
    // When resolving definitions and references, the editor will try to use created models.
    // Creating a model for the library allows "peek definition/references" commands to work with the library.
    const oldModels = [...monaco.editor.getModels().filter(s => s.uri.scheme !== "inmemory")];
    for (const extraLib of extraLibs) {
        const uri = monaco.Uri.parse(extraLib.filePath!);
        const index = oldModels.findIndex(s => s.uri.toString() === uri.toString());
        if (index >= 0) {
            oldModels[index].setValue(extraLib.content);
            oldModels.splice(index, 1);
        } else {
            monaco.editor.createModel(extraLib.content, "typescript", uri);
        }
    }
    // Remove models from previous activations.
    for (const model of oldModels) {
        model.dispose();
    }
    // console.log(monaco.editor.getModels().map(s => ({
    //     uri: s.uri.toString(),
    //     contents: s.getValue(),
    // })));
};

const declareErroredFreeVariable = (varDef: FreeVariableDefinition, reason: any) => {
    return `
    /**
     @description Error resolving type - ${reason}
     */
    declare const ${varDef.name}: never;
    `;
};

const useContextExtraLibs = (freeVariables: FreeVariableDefinition[] | undefined) => {
    const {getTypingForFreeVariable} = useJsContextDefinitionsContext();
    const vars = useArrayContentsMemo(freeVariables ?? [], true);
    const {value: extraLibs} = useAsync(async () => {
        const extraLibs: ExtraLib[] = [];
        let typings = "";
        if (vars && vars.length > 0) {
            const promises = vars
                .map(s => {
                    const typings = getTypingForFreeVariable(s.name);
                    return typings ? ensurePromise(typings) : undefined;
                })
                .filter(isPresent);
            typings = (await Promise.allSettled(promises))
                .map((s, i) => (s.status === "fulfilled" ? s.value : declareErroredFreeVariable(vars[i], s.reason)))
                .filter(isPresent)
                .join("\n\n");
        }
        extraLibs.push({
            content: typings,
            filePath: contextDefsUri.toString(),
        });
        return extraLibs;
    }, [vars, getTypingForFreeVariable]);
    return extraLibs;
};

const useCommonJsExtraLibs = (importJsCommon: boolean) => {
    const {getJsCommonTypings} = useJsContextDefinitionsContext();

    const {value: extraLibs} = useAsync(async () => {
        const extraLibs: ExtraLib[] = [];
        if (importJsCommon) {
            const jsCommonTypings = getJsCommonTypings();
            if (jsCommonTypings) {
                extraLibs.push({
                    content: await ensurePromise(jsCommonTypings),
                    filePath: commonUri.toString(),
                });
                extraLibs.push({
                    content: `declare const common: typeof import('file:///common');`,
                    filePath: commonDefsUri.toString(),
                });
            }
        } else {
            extraLibs.push({
                content: "",
                filePath: commonUri.toString(),
            });
            extraLibs.push({
                content: "",
                filePath: commonDefsUri.toString(),
            });
        }
        return extraLibs;
    }, [getJsCommonTypings]);

    return extraLibs;
};

const useExtraLibs = (freeVariables?: FreeVariableDefinition[], importJsCommon?: boolean) => {
    const {monacoExtraLibs: globalExtraLibs} = useGlobalImports();
    const contextExtraLibs = useContextExtraLibs(freeVariables);
    const commonJsExtraLibs = useCommonJsExtraLibs(importJsCommon ?? true);
    const [extraLibs, setExtraLibs] = useState<ExtraLib[]>();

    useEffect(() => {
        const extraLibs: ExtraLib[] = [];
        if (contextExtraLibs) {
            extraLibs.push(...contextExtraLibs);
        }

        extraLibs.push({
            content: defaultDeclarations,
            filePath: defaultDeclarationsUri.toString(),
        });

        if (commonJsExtraLibs) {
            extraLibs.push(...commonJsExtraLibs);
        }

        extraLibs.push(...globalExtraLibs);

        setExtraLibs(oldExtraLibs => {
            if (oldExtraLibs?.length === extraLibs.length) {
                const oldContext = oldExtraLibs.find(s => s.filePath === contextDefsUri.toString());
                const newContext = extraLibs.find(s => s.filePath === contextDefsUri.toString());
                const oldJsCommon = oldExtraLibs.find(s => s.filePath === commonUri.toString());
                const newJsCommon = extraLibs.find(s => s.filePath === commonUri.toString());
                if (oldContext?.content === newContext?.content && oldJsCommon?.content === newJsCommon?.content) {
                    return oldExtraLibs;
                }
            }
            return extraLibs;
        });
    }, [commonJsExtraLibs, contextExtraLibs, globalExtraLibs]);

    return extraLibs;
};

const configureMonaco = (monacoEditor: typeof monaco, importJsCommon: boolean | undefined) => {
    monacoEditor.languages.typescript.typescriptDefaults.setCompilerOptions({
        allowJs: true,
        allowNonTsExtensions: true,
        allowSyntheticDefaultImports: true,
        esModuleInterop: true,
        moduleResolution: monaco.languages.typescript.ModuleResolutionKind.NodeJs,
        alwaysStrict: true,
        checkJs: true,
        lib: ["es2020"],
        module:
            importJsCommon !== false
                ? monacoEditor.languages.typescript.ModuleKind.ESNext // Allow dynamic imports() when editing expressions.
                : monacoEditor.languages.typescript.ModuleKind.ES2015,
        noEmit: true,
        target: monacoEditor.languages.typescript.ScriptTarget.ES2015,
    });
    monacoEditor.languages.typescript.typescriptDefaults.setDiagnosticsOptions({
        noSemanticValidation: false,
        noSyntaxValidation: false,
        diagnosticCodesToIgnore: [
            2686 /*'XXX' refers to a UMD global, but the current file is a module. Consider adding an import instead.*/,
        ],
    });
    /*if (importJsCommon !== false && !monaco.editor.getModel(mainUri)) {
        monaco.editor.createModel(defaultValue ?? "", "typescript", mainUri);
    }*/
};

const extractTypescriptErrors = (editor: monaco.editor.IStandaloneCodeEditor) => {
    const model = editor.getModel();
    if (model?.getModeId() !== "typescript") {
        return undefined;
    }

    const owner = model.getModeId();
    const errorMarkers = monaco.editor
        .getModelMarkers({owner})
        .filter(s => s.severity === monaco.MarkerSeverity.Error && s.resource === model.uri);
    const newErrors = sortWith(
        [ascend(s => s.startLineNumber), ascend(s => s.startColumn), ascend(s => s.message)],
        errorMarkers
    ).map(s => `[${s.startLineNumber},${s.startColumn}]: ${s.message}`);
    return newErrors;
};

export const JavascriptMonacoEditor: FC<JavascriptMonacoEditorProps> = props => {
    const {
        value: code,
        freeVariables,
        returnType,
        isExpression,
        importJsCommon,
        onChange,
        onErrorsChange,
        height,
        width,
    } = props;
    const theme = useTheme();
    const editorRef = useRef<monaco.editor.IStandaloneCodeEditor>();
    const shouldWrapBody = isExpression || (freeVariables && freeVariables.length > 0);
    const wrapperCount = (shouldWrapBody ? 2 : 0) - (isExpression ? 1 : 0);
    const classes = useStyles({wrapperHeight: wrapperCount * bodyWrapperLineHeight});
    const monacoRef = useRef<Parameters<EditorWillMount>[0]>();
    const [errors, setErrors] = useState<string[]>([]);
    const extraLibs = useExtraLibs(freeVariables, importJsCommon);
    const handleModelDecorationsChangeRef = useRef<typeof handleModelDecorationsChange>();

    useEffect(() => {
        const monaco = monacoRef.current;
        if (monaco && extraLibs) {
            updateMonacoIntellisense(extraLibs, monaco);
        }
    }, [extraLibs]);

    const handleModelDecorationsChange = useCallback(
        (editor: monaco.editor.IStandaloneCodeEditor, _monaco: typeof monaco) => {
            const newErrors = extractTypescriptErrors(editor);
            if (newErrors) {
                setErrors(s => {
                    return equals(newErrors, s) ? s : newErrors;
                });
            }
        },
        []
    );

    handleModelDecorationsChangeRef.current = handleModelDecorationsChange;

    useEffect(() => {
        //console.log(errors);
        onErrorsChange?.(errors);
    }, [errors, onErrorsChange]);

    const editorWillMount: EditorWillMount = useCallback(
        monaco => {
            monacoRef.current = monaco;
            configureMonaco(monaco, importJsCommon);
        },
        [importJsCommon]
    );

    const editorDidMount: EditorDidMount = useCallback((editor, _monaco) => {
        editorRef.current = editor as any;
        editor.onDidChangeModelDecorations(ev => {
            handleModelDecorationsChangeRef.current?.(editor, _monaco);
        });
        editor.focus();
    }, []);

    const containerStyle: React.CSSProperties = useMemo(
        () => ({width: width ?? "100%", height: height ?? "100%"}),
        [height, width]
    );

    return (
        <div style={containerStyle}>
            {shouldWrapBody && (
                <div className={classes.scriptBodyWrapper}>
                    function (
                    {importJsCommon !== false && (
                        <>
                            <FreeVariableViewer variable={jsCommonFreeVariableDef} />
                            {freeVariables && freeVariables.length > 0 && ", "}
                        </>
                    )}
                    {freeVariables?.map((s, i) => (
                        <Fragment key={i}>
                            {i > 0 && ", "}
                            <FreeVariableViewer variable={s} />
                        </Fragment>
                    ))}
                    ){returnType ? `: ${returnType} {` : " {"}
                </div>
            )}
            <div className={classes.editorRow}>
                {isExpression && <div className={classes.return}>return</div>}
                <div className={classes.editorContainer}>
                    <Autosizer>
                        {({height, width}) => (
                            <MonacoEditor
                                width={width}
                                height={height}
                                language="typescript"
                                theme={theme.palette.type === "light" ? "vs-light" : "vs-dark"}
                                value={code}
                                options={defaultMonacoOptions}
                                onChange={onChange}
                                editorWillMount={editorWillMount}
                                editorDidMount={editorDidMount}
                            />
                        )}
                    </Autosizer>
                </div>
            </div>
            {isExpression && <div className={clsx(classes.returnEnd, classes.scriptBodyWrapper)}>;</div>}
            {shouldWrapBody && (
                <div className={classes.scriptBodyWrapper}>
                    <span>{"}"}</span>
                </div>
            )}
        </div>
    );
};
