import React, {FC, useCallback} from "react";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";
import clsx from "clsx";
import {PortWidget, PortProps} from "@projectstorm/react-diagrams";
import {CSSProperties} from "@material-ui/core/styles/withStyles";
import {useSetRecoilState} from "recoil";

import {ContextTypePortModel, defaultContextTypePortWidgetSize} from "./context-type-port-model";
import {selectedLinkIds} from "../context-type-diagram-state";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        portBody: {
            width: (s: ContextTypePortWidgetProps) => s.portSize ?? defaultContextTypePortWidgetSize,
            height: (s: ContextTypePortWidgetProps) => s.portSize ?? defaultContextTypePortWidgetSize,
            background: "rgba(128, 128, 128, 1)",
            opacity: 0.3,
            // cursor: "pointer",
            // "&:hover": {
            //     background: "rgba(0, 0, 0, 1)",
            // },
        },
        port: {
            width: "100%",
            height: "100%",
        },
    })
);

export interface ContextTypePortWidgetProps extends PortProps {
    port: ContextTypePortModel;
    onPortHovering?: (port: ContextTypePortModel, hovering: boolean) => void;
    portSize?: CSSProperties["width"];
}

export const ContextTypePortWidget: FC<ContextTypePortWidgetProps> = props => {
    const {port, onPortHovering, className, ...rest} = props;
    const classes = useStyles(props);
    const setSelectedLinkIds = useSetRecoilState(selectedLinkIds);

    const handlePortHovering = useCallback(
        (hovering: boolean) => {
            onPortHovering?.(port, hovering);
            setSelectedLinkIds(ids => {
                if (!hovering) {
                    return [];
                }
                const newIds = Object.keys(port.links);
                if (ids.length === newIds.length && newIds.every(s => ids.includes(s))) {
                    return ids;
                }
                return newIds;
            });
        },
        [onPortHovering, port, setSelectedLinkIds]
    );

    return (
        <div
            className={clsx(classes.portBody, className)}
            onMouseEnter={() => handlePortHovering(true)}
            onMouseLeave={() => handlePortHovering(false)}
        >
            <PortWidget className={classes.port} port={port} {...rest}></PortWidget>
        </div>
    );
};
