import React, {FC, useCallback, useMemo, useState, useEffect} from "react";
import {createStyles, Theme, makeStyles, useTheme} from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import InputLabel from "@material-ui/core/InputLabel";
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import Box from "@material-ui/core/Box";
import Tabs, {TabsProps} from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import {useFormContext, Controller} from "react-hook-form";
import {useTranslation} from "react-i18next";
import {FieldError} from "react-hook-form/dist/types/errors";
import {DeepMap} from "react-hook-form/dist/types/utils";
import {JSONSchema7} from "json-schema";
import * as yup from "yup";

import {ResponseType} from "../../api/nimflow-api";
import {ValidatorsEditor} from "./validators-editor";
import {getErrors} from "../../libs/react-hooks-form";
import {ResponseTypeSettersEditor} from "./response-type-setters-editor";
import {useContextTypeContext} from "./context-type-context";
import {UiSchemaEditorModal} from "../../components/ui-schema-editor-modal";
import {getSettings, getResponsePayloadDefinitionName, IconSpec} from "../../api/nimflow-api-extensions";
import {JsonSchemaIcon} from "../../components/icons/json-schema-icon";
import {JsonFormsIcon} from "../../components/icons/json-forms-icon";
import {compile} from "../../libs/json-schema-to-typescript";
import {JsContextDefinitionsContextProvider} from "../../components/js-context-definitions-provider";
import {ResponseTypeSchema, jsCommonScriptKey} from "../../models/context-type-schema";
import {ErrorInfo} from "../../models/error-info";
import {validateWithYupSchema} from "../../libs/yup";
import {JsExpressionEditor} from "../../components/jsexpression-editor";
import {freeVarDefs} from "./free-variable-defs";
import {useReportError} from "../../utils/use-report-error";
import {JsonSchemaEditorModal} from "../../components/json-schema-editor-modal";
import {defaultPayloadJsonSchemaString} from "../../models/default-values";
import {IconSelector} from "../../components/icon-selector/icon-selector";
import {dereferenceSchema} from "../../libs/json-schema-ref-parser";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        list: {
            width: "100%",
            //maxWidth: '36ch',
            backgroundColor: theme.palette.background.paper,
        },
        textField: {
            width: "40ch",
        },
        item: {
            border: "2px solid dodgerblue",
            borderRadius: 2,
            paddingRight: 48,
        },
        actionHeading: {
            fontSize: theme.typography.pxToRem(15),
            fontWeight: theme.typography.fontWeightRegular,
        },
        summaryTextContainer: {
            width: "100%",
            height: "100%",
            display: "flex",
            alignItems: "center",
        },
        divider: {
            height: 5,
            background: "transparent",
        },
        paper: {
            padding: theme.spacing(2),
            color: theme.palette.text.secondary,
        },
        actionButton: {
            marginRight: theme.spacing(1),
        },
    })
);

const useTabClassesErrorStyles = makeStyles((theme: Theme) =>
    createStyles({
        wrapper: {
            color: theme.palette.error.main,
        },
    })
);

export interface ResponseTypeEditorProps {
    path?: string;
    defaultValue?: Partial<ResponseType>;
    taskTypeName: string;
    definitionJsonSchema: string | undefined;
    onDefinitionJsonSchemaChange?: (value: string | undefined, definitionName: string) => void;
}

const getPayloadTypingsProvider = (
    payloadJsonSchema: JSONSchema7 | undefined
): Record<string, () => string | Promise<string>> => {
    return {
        payload: async () => {
            if (!payloadJsonSchema) {
                return "declare const payload: any;";
            }
            const schema = await dereferenceSchema(payloadJsonSchema);
            const intf = await compile(schema, "PayloadType", {
                ignoreMinAndMaxItems: true,
            });
            return intf + `\n\ndeclare const payload: PayloadType;`;
        },
    };
};

const freeVariables = [freeVarDefs.state, freeVarDefs.task];

const validateJsonSchema = async (localSchemaString: string, globalSchema: any): Promise<ErrorInfo[]> => {
    let localSchema;
    try {
        localSchema = JSON.parse(localSchemaString);
    } catch (e: any) {
        return [
            {
                message: e.toString(),
            },
        ];
    }
    const fullSchema = {
        ...globalSchema,
        ...localSchema,
    };
    const errors = await validateWithYupSchema(yup.object().jsonSchema(), fullSchema);
    return errors;
};

export const ResponseTypeEditor: FC<ResponseTypeEditorProps> = props => {
    const {path, defaultValue, taskTypeName: taskName, definitionJsonSchema, onDefinitionJsonSchemaChange} = props;
    const prefix = useMemo(() => {
        return path ? path + "." : "";
    }, [path]);
    const classes = useStyles();
    const theme = useTheme();
    const {t: origT} = useTranslation();
    const t = useCallback((s: string | undefined | null) => origT(s ?? ""), [origT]);
    const {errors, control, setValue, register, getValues, watch} = useFormContext();
    const responseErrors = path ? getErrors<ResponseType>(errors, path) : (errors as DeepMap<ResponseType, FieldError>);
    const [activeTab, setActiveTab] = useState(0);
    const {jsonSchema} = useContextTypeContext();
    const tabClassesError = useTabClassesErrorStyles();
    const [editJsonSchema, setEditJsonSchema] = useState(false);
    const [editUiSchema, setEditUiSchema] = useState(false);
    const {jsCommon, uiSchemaCommon} = useContextTypeContext();
    const payloadSchemaDefinitionName = getValues(`${prefix}payloadSchemaDefinitionName`);
    const jsInitialPayloadBuilderValidator = useCallback(
        (js: string | undefined): Promise<ErrorInfo[]> => {
            return validateWithYupSchema(ResponseTypeSchema.fields?.jsInitialPayloadBuilder, js, {
                context: {[jsCommonScriptKey]: jsCommon},
            });
        },
        [jsCommon]
    );
    const reportError = useReportError();

    useEffect(() => {
        const settings = getSettings(defaultValue as ResponseType);
        if (settings) {
            for (const key of Object.keys(settings)) {
                register(`${prefix}settings.${key}`);
            }
        }
        register(`${prefix}settings.icon`);
    }, [register, defaultValue, prefix]);

    const validatorsClasses = useMemo(() => {
        return !!responseErrors?.validators ? tabClassesError : {};
    }, [responseErrors, tabClassesError]);
    const settersClasses = useMemo(() => {
        return !!responseErrors?.jsSetters ? tabClassesError : {};
    }, [responseErrors, tabClassesError]);
    const tabIndicatorProps: TabsProps["TabIndicatorProps"] = useMemo(() => {
        const tabIndicatorColor = theme.palette.error.main;
        if ((activeTab === 0 && !!responseErrors?.validators) || (activeTab === 1 && !!responseErrors?.jsSetters)) {
            return {
                style: {
                    backgroundColor: tabIndicatorColor,
                },
            };
        }
        return undefined;
    }, [activeTab, theme, responseErrors]);

    const handleTabChange = (event: React.ChangeEvent<{}>, newValue: number) => {
        setActiveTab(newValue);
    };

    const handleEditJsonSchemaClick = useCallback(() => {
        setEditJsonSchema(true);
    }, [setEditJsonSchema]);

    const setJsonSchema = useCallback(
        (json?: string) => {
            let isEmptySchema = !json;
            if (!isEmptySchema && !!json) {
                try {
                    const schema = JSON.parse(json);
                    isEmptySchema = !schema || (typeof schema === "object" && Object.keys(schema).length === 0);
                } catch (e) {
                    reportError(t("InvalidJsonSchema"), e);
                    return;
                }
            }
            let defName: string | undefined = getValues(`${prefix}payloadSchemaDefinitionName`);
            if (!defName && isEmptySchema) {
                return;
            }
            if (!defName) {
                defName = getResponsePayloadDefinitionName(taskName, getValues(`${prefix}name`));
            }
            onDefinitionJsonSchemaChange?.(!isEmptySchema ? json : undefined, defName);
        },
        [getValues, onDefinitionJsonSchemaChange, prefix, reportError, t, taskName]
    );

    const handleEditUiSchemaClick = useCallback(() => {
        setEditUiSchema(true);
    }, [setEditUiSchema]);

    const getUiSchema = () => {
        const uiSchema: string | undefined = getValues(`${prefix}settings.uiSchema`);
        return uiSchema;
    };

    const setUiSchema = (uiSchema?: string) => {
        setValue(`${prefix}settings.uiSchema`, uiSchema, {shouldDirty: true});
    };

    const responseTypeJsonSchema = useMemo(() => {
        if (!payloadSchemaDefinitionName || !jsonSchema) {
            return undefined;
        }
        if (!definitionJsonSchema) {
            return jsonSchema;
        }
        return {
            ...jsonSchema,
            ...JSON.parse(definitionJsonSchema),
        };
    }, [payloadSchemaDefinitionName, jsonSchema, definitionJsonSchema]);

    const jsonSchemaValidator = useCallback(
        async (jsonString: string): Promise<ErrorInfo[]> => {
            return validateJsonSchema(jsonString, jsonSchema);
        },
        [jsonSchema]
    );

    const freeVariableTypingProviders: Record<string, () => string | Promise<string>> = useMemo(() => {
        return getPayloadTypingsProvider(responseTypeJsonSchema);
    }, [responseTypeJsonSchema]);

    const handleIconSpecChange = useCallback(
        (icon: IconSpec | undefined) => {
            setValue(`${prefix}settings.icon`, icon ?? null);
        },
        [setValue, prefix]
    );

    const currentIcon = watch(`${prefix}settings.icon`);

    const icon = useMemo(() => {
        const icon = currentIcon;
        if (typeof icon === "string") {
            return {name: icon};
        }
        return icon;
    }, [currentIcon]);

    return (
        <JsContextDefinitionsContextProvider freeVariableTypingProviders={freeVariableTypingProviders}>
            <Toolbar>
                <Typography variant="h6">{t("Response")}</Typography>
            </Toolbar>
            <Box pl={3} pr={3}>
                <Grid container spacing={1}>
                    <Grid item xs={4}>
                        <Controller
                            as={TextField}
                            control={control}
                            name={`${prefix}name`}
                            label={t("Name")}
                            defaultValue={defaultValue?.name}
                            autoFocus
                            InputLabelProps={{required: true}}
                            className={classes.textField}
                            fullWidth
                            error={!!responseErrors?.name}
                            helperText={t(responseErrors?.name?.message)}
                        />
                        {/* Hidden uncontrolled controls to provide state for payloadSchemaDefinitionName and settings.uiSchema. 
                            Otherwise their value is undefined */}
                        <input
                            style={{display: "none"}}
                            name={`${prefix}payloadSchemaDefinitionName`}
                            defaultValue={defaultValue?.payloadSchemaDefinitionName}
                            ref={register({
                                setValueAs: s => (s ? s : undefined),
                            })}
                        />
                        <input
                            style={{display: "none"}}
                            name={`${prefix}settings.uiSchema`}
                            defaultValue={defaultValue?.settings?.uiSchema}
                            ref={register({
                                setValueAs: s => (s ? s : undefined),
                            })}
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <InputLabel shrink>{t("Icon")}</InputLabel>
                        <IconSelector value={icon} onChange={handleIconSpecChange} />
                        {responseErrors?.settings?.icon?.message}
                    </Grid>
                    <Grid item xs={4}>
                        <Grid container direction="row" justifyContent="flex-end">
                            <Button
                                className={classes.actionButton}
                                variant="outlined"
                                onClick={handleEditJsonSchemaClick}
                                startIcon={<JsonSchemaIcon />}
                            >
                                {t("EditJsonSchema")}
                            </Button>
                            {!!getValues(`${prefix}payloadSchemaDefinitionName`) && (
                                <Button
                                    className={classes.actionButton}
                                    variant="outlined"
                                    onClick={handleEditUiSchemaClick}
                                    startIcon={<JsonFormsIcon />}
                                >
                                    {t("EditUiSchema")}
                                </Button>
                            )}
                        </Grid>
                    </Grid>
                    <Grid item xs={12}>
                        <Controller
                            as={JsExpressionEditor}
                            control={control}
                            name={`${prefix}jsInitialPayloadBuilder`}
                            label={t("JsInitialPayloadBuilder")}
                            defaultValue={
                                watch(`${prefix}jsInitialPayloadBuilder`, defaultValue?.jsInitialPayloadBuilder) ?? ""
                            }
                            fullWidth
                            placeholder={t("EnterObjectExpression")}
                            error={!!responseErrors?.jsInitialPayloadBuilder}
                            helperText={t(responseErrors?.jsInitialPayloadBuilder?.message)}
                            validator={jsInitialPayloadBuilderValidator}
                            freeVariables={freeVariables}
                            returnType="object"
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <Tabs
                            value={activeTab}
                            onChange={handleTabChange}
                            variant="scrollable"
                            scrollButtons="on"
                            TabIndicatorProps={tabIndicatorProps}
                            indicatorColor="primary"
                            textColor="primary"
                        >
                            <Tab label={t("Setters")} classes={settersClasses} />
                            <Tab label={t("Validators")} classes={validatorsClasses} />
                            <Tab label={t("Texts")} />
                        </Tabs>
                        <div role="tabpanel" hidden={activeTab !== 0}>
                            <Paper className={classes.paper} elevation={3}>
                                <ResponseTypeSettersEditor
                                    path={`${prefix}jsSetters`}
                                    defaultValue={defaultValue?.jsSetters}
                                />
                            </Paper>
                        </div>
                        <div role="tabpanel" hidden={activeTab !== 1}>
                            <Paper className={classes.paper} elevation={3}>
                                <ValidatorsEditor
                                    path={`${prefix}validators`}
                                    defaultValue={defaultValue?.validators}
                                />
                            </Paper>
                        </div>
                        <div role="tabpanel" hidden={activeTab !== 2}>
                            <Paper className={classes.paper} elevation={3}>
                                <Grid container spacing={3}>
                                    <Grid item xs={12}>
                                        <Controller
                                            as={TextField}
                                            control={control}
                                            name={`${prefix}settings.commandText`}
                                            defaultValue={defaultValue?.settings?.commandText ?? ""}
                                            label={t("CommandText")}
                                            fullWidth
                                            error={!!responseErrors?.settings?.commandText}
                                            helperText={t(responseErrors?.settings?.commandText?.message)}
                                        />
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Controller
                                            as={TextField}
                                            control={control}
                                            name={`${prefix}settings.actionInProgressText`}
                                            defaultValue={defaultValue?.settings?.actionInProgressText ?? ""}
                                            label={t("ActionInProgressText")}
                                            fullWidth
                                            error={!!responseErrors?.settings?.actionInProgressText}
                                            helperText={t(responseErrors?.settings?.actionInProgressText?.message)}
                                        />
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Controller
                                            as={TextField}
                                            control={control}
                                            name={`${prefix}settings.actionCompletedText`}
                                            defaultValue={defaultValue?.settings?.actionCompletedText ?? ""}
                                            label={t("ActionCompletedText")}
                                            fullWidth
                                            error={!!responseErrors?.settings?.actionCompletedText}
                                            helperText={t(responseErrors?.settings?.actionCompletedText?.message)}
                                        />
                                    </Grid>
                                </Grid>
                            </Paper>
                        </div>
                    </Grid>
                </Grid>
                {editJsonSchema && (
                    <JsonSchemaEditorModal
                        open={editJsonSchema}
                        onClose={() => setEditJsonSchema(false)}
                        defaultValue={definitionJsonSchema ?? defaultPayloadJsonSchemaString}
                        onChange={setJsonSchema}
                        validator={jsonSchemaValidator}
                    />
                )}
                {editUiSchema && (
                    <UiSchemaEditorModal
                        open={editUiSchema}
                        onClose={() => setEditUiSchema(false)}
                        defaultValue={getUiSchema()}
                        jsonSchema={responseTypeJsonSchema}
                        uiSchemaLibrary={uiSchemaCommon}
                        onChange={value => setUiSchema(value ?? undefined)}
                    />
                )}
            </Box>
        </JsContextDefinitionsContextProvider>
    );
};
