import React, {FC, useState, useCallback} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Button from "@material-ui/core/Button";
import {useTranslation} from "react-i18next";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import ToggleButton from "@material-ui/lab/ToggleButton";
import Autosizer from "react-virtualized-auto-sizer";

import {JavascriptMonacoEditor} from "../../components/javascript-monaco-editor";
import {useConfirm} from "../../components/confirm";
import {ErrorInfo} from "../../models/error-info";
import {useNimflow} from "../../api/nimflow-api-provider";
import {PromptIcon} from "../../components/icons/prompt-icon";
import Tooltip from "@material-ui/core/Tooltip";
import {ConsoleViewer} from "../../components/console-viewer";
import {JsConsoleEntry} from "../../api/nimflow-api";
import {useLoseChangesConfirm} from "../../components/specialized-confirmations";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        dialogPaper: {
            height: "100%",
            width: "100%",
            minHeight: "90vh",
            maxHeight: "90vh",
        },
        dialogContent: {
            display: "grid",
            gridTemplateColumns: "auto",
            gridTemplateRows: (props: {showRepl: boolean}) => (props.showRepl ? "auto 200px" : "auto"),
            rowGap: "5px",
            placeItems: "stretch",
            height: "100%",
            overflowY: "hidden",
        },
        grow: {
            flexGrow: 1,
        },
        mosaic: {
            height: "100%",
            width: "100%",
            margin: 0,
        },
        toggleButton: {
            border: "none",
            borderRadius: "50%",
        },
        editorContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
            //overflowY: "auto",
        },
        replContainer: {
            gridColumn: "1 / 2",
            gridRow: "2 / 3",
            //overflowY: "auto",
        },
        consoleFeed: {
            border: "solid 1px black",
            height: "85px",
        },
    })
);

const useReplStyles = makeStyles((theme: Theme) =>
    createStyles({
        consoleFeed: {
            border: `solid 1px ${theme.palette.divider}`,
            borderRadius: theme.shape.borderRadius,
            height: "85px",
        },
    })
);

interface JsCommonReplProps {
    jsCommon?: string;
}

const JsCommonRepl: FC<JsCommonReplProps> = props => {
    const {jsCommon} = props;
    const classes = useReplStyles();
    const {t} = useTranslation();
    const [snippet, setSnippet] = useState("");
    const [lastEvaluation, setLastEvaluation] = useState<JsConsoleEntry[]>([]);
    const {contextTypes_EvalJsExpression} = useNimflow();

    const handleEvaluate = useCallback(async () => {
        if (!snippet) {
            return;
        }
        try {
            const result = await contextTypes_EvalJsExpression({
                expression: snippet,
                common: jsCommon,
            });
            setLastEvaluation(s => []); // Force console log reset
            setLastEvaluation(s => [...(result.consoleEntries ?? []), {type: "result", values: [result.result]}]);
        } catch (e: any) {
            setLastEvaluation([{type: "error", values: [e.toString()]}]);
        }
    }, [contextTypes_EvalJsExpression, snippet, jsCommon, setLastEvaluation]);

    return (
        <Grid container spacing={3} style={{width: "100%"}} alignItems="center">
            <Grid item xs={10}>
                <TextField
                    value={snippet}
                    multiline={true}
                    fullWidth
                    variant="outlined"
                    minRows={2}
                    maxRows={2}
                    onChange={ev => setSnippet(ev.target.value)}
                />
            </Grid>
            <Grid item xs={2} style={{textAlign: "center"}}>
                <Button onClick={handleEvaluate} variant="outlined">
                    {t("Evaluate")}
                </Button>
            </Grid>
            <Grid item xs={12}>
                <div className={classes.consoleFeed}>
                    <ConsoleViewer entries={lastEvaluation} />
                </div>
            </Grid>
        </Grid>
    );
};

export interface JsCommonEditorProps {
    open: boolean;
    defaultValue?: string;
    onChange?: (value: string | undefined) => void;
    onClose: () => void;
    validator?: (js: string | undefined) => Promise<ErrorInfo[]>;
}

export const JsCommonEditor: FC<JsCommonEditorProps> = props => {
    const {defaultValue, open, onClose, onChange, validator} = props;
    const {t} = useTranslation();
    const confirm = useConfirm();
    const loseChangesConfirm = useLoseChangesConfirm();
    const [code, setCode] = useState<string | undefined>(defaultValue);
    const [showRepl, setShowRepl] = useState(true);
    const classes = useStyles({showRepl});
    const handleOnAccept = useCallback(async () => {
        if (validator) {
            const errors = await validator(code);
            if (errors.length > 0) {
                await confirm({
                    title: "",
                    description: t("ErrorsExistInSourceCode"),
                    mapCloseTo: "ok",
                    actions: [{type: "ok", text: t("Ok")}],
                });
                return;
            }
        }
        onChange?.(code);
        onClose();
    }, [onChange, code, onClose, confirm, t, validator]);

    const handleOnChange = useCallback((value: string | undefined) => {
        setCode(value);
    }, []);

    const handleOnClose = useCallback(async () => {
        if (defaultValue !== code && !(await loseChangesConfirm())) {
            return;
        }
        onClose();
    }, [code, defaultValue, loseChangesConfirm, onClose]);

    const handleDialogClose = useCallback(async (event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (defaultValue !== code && !(await loseChangesConfirm())) {
            return;
        }
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [code, defaultValue, loseChangesConfirm, onClose]);

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            maxWidth="xl"
            classes={{
                paper: classes.dialogPaper,
            }}
        >
            <DialogTitle>{t("EditingCommonModule")}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <div className={classes.editorContainer}>
                    <Autosizer>
                        {({height, width}) => (
                            <JavascriptMonacoEditor
                                {...props}
                                height={height}
                                width={width}
                                value={code}
                                onChange={handleOnChange}
                            ></JavascriptMonacoEditor>
                        )}
                    </Autosizer>
                </div>
                {showRepl && (
                    <div className={classes.replContainer}>
                        <JsCommonRepl jsCommon={code} />
                    </div>
                )}
            </DialogContent>
            <DialogActions>
                <Tooltip title={t("ShowRepl") as string}>
                    <div>
                        <ToggleButton
                            value="showRepl"
                            selected={showRepl}
                            onClick={() => setShowRepl(!showRepl)}
                            classes={{
                                root: classes.toggleButton,
                            }}
                        >
                            <PromptIcon />
                        </ToggleButton>
                    </div>
                </Tooltip>
                <div className={classes.grow} />
                <Button onClick={handleOnAccept} color="primary">
                    {t("Accept")}
                </Button>
                <Button onClick={handleOnClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
