import React, {FC, useCallback, useMemo} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import Checkbox from "@material-ui/core/Checkbox";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import {useTranslation} from "react-i18next";
import {useAsync} from "react-use";
import {useForm, FormProvider, Controller} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import * as yup from "yup";
import {isDefined} from "ts-is-present";
import {DevTool} from "@hookform/devtools";

import {SimulationStateSelectorStepProps} from "./simulation-data-selector";
import {useReportError} from "../../../utils/use-report-error";
import {useNimflow} from "../../../api/nimflow-api-provider";
import {StepContainer} from "./step-container";
import {WaitSpinner} from "../../../components/wait-spinner";
import {Context, SimulationEvent, ActionEvent, TaskResponseEvent, InitialStateEvent} from "../../../api/nimflow-api";
import {AutocompleteController} from "../../../components/autocomplete-controller";
import {FormErrorViewer} from "../../../components/form-error-viewer";
import {isActionEvent, isResponseEvent} from "../../../api/nimflow-api-extensions";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            width: "50%",
            margin: "0 auto",
        },
        form: {
            height: "100%",
        },
    })
);

interface EditionData {
    fromEvent: {label: string; index: number};
    includeFollowingEvents: boolean;
}

const schema = yup.object().shape({
    fromEvent: yup.object().required(),
    includeFollowingEvents: yup.boolean(),
});

const useContextEventsAndStates = (context: Context) => {
    const {t} = useTranslation();
    const reportError = useReportError();
    const {events_AggregateEvents, contextTypes_History} = useNimflow();

    return useAsync(async () => {
        const actionDispatched  = "ActionDispatched";
        const responseSent = "ResponseSent";
        const publishedEventsPromise = events_AggregateEvents({
            stages: [
                {
                    $match: 
                    {
                        contextTypeName: context.typeName,
                        contextId: context.id,
                        event: {$in: [actionDispatched, responseSent]}
                    }
                },
                {
                    $sort: {
                        timestamp: 1
                    }
                },
                {
                    $limit: 1000
                }
            ]
        }).catch(e => {
            reportError(t("ErrorRetrievingContextHistory"), e);
            throw e;
        });
        let publishedEvents = await publishedEventsPromise;
        const result: Array<{event: SimulationEvent; state: Record<string, any>}> = [];
        for (let publishedEvent of publishedEvents) {
            if (publishedEvent.event === actionDispatched) {
                const event: ActionEvent = {
                    action: publishedEvent.action!,
                    reference: publishedEvent.reference,
                    payload: publishedEvent.payload,
                };
                result.push({event, state: publishedEvent.newContextState ?? {}});
            } else if (publishedEvent.event === responseSent) {
                const event: TaskResponseEvent = {
                    taskTypeName: publishedEvent.taskTypeName!,
                    typeName: publishedEvent.responseTypeName,
                    payload: publishedEvent.payload,
                };
                result.push({event, state: publishedEvent.newContextState ?? {}});
            }
        }
        return result;
    }, [events_AggregateEvents, contextTypes_History]);
};

export const ContextStateSelectionStep: FC<SimulationStateSelectorStepProps> = props => {
    const {data: initialData, onBack, onNext, onCancel} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const {value, loading, error} = useContextEventsAndStates(initialData.selectedContext!);

    const formMethods = useForm<EditionData>({
        mode: "onChange",
        reValidateMode: "onChange",
        defaultValues: {includeFollowingEvents: false},
        resolver: yupResolver(schema, {
            strict: true,
            abortEarly: false,
        }),
    });
    const {control, handleSubmit, errors, trigger, watch} = formMethods;

    const options = useMemo(() => {
        if (!value) {
            return [];
        }
        const options = value.map((s, index) => {
            let label = "";
            if (isActionEvent(s.event)) {
                label = s.event.action;
            } else if (isResponseEvent(s.event)) {
                label = s.event.taskTypeName + " - " + s.event.taskTypeName;
            }
            return {
                label: t("ExecutedContextEventOption", {number: index + 1, description: label}),
                index,
            };
        });
        options.reverse();
        options.unshift({
            label: t("ExecutedContextEventOption", {number: value.length + 1, description: t("LastContextStatus")}),
            index: value.length,
        });
        return options;
    }, [t, value]);

    const handleNext = useCallback(async () => {
        const result = await trigger();
        if (!result) {
            return;
        }
        handleSubmit(data => {
            const fromIndex = data.fromEvent!.index;
            const pendingEvents = data.includeFollowingEvents
                ? value!.filter((s, i) => i >= fromIndex).map(s => s.event)
                : [];
            if (fromIndex > 0) {
                const initialStateEvent: InitialStateEvent = {
                    payload: {},
                    initialState: value![fromIndex - 1].state,
                    reference: initialData.selectedContext?.reference,
                }
                pendingEvents.unshift(initialStateEvent);
            }
            onNext({...initialData, pendingEvents});
        })();
    }, [handleSubmit, initialData, onNext, trigger, value]);

    if (loading) {
        return <WaitSpinner />;
    }

    const checkBoxVisible = isDefined(watch("fromEvent")) && watch("fromEvent")?.index < options.length - 1;

    return (
        <StepContainer onBack={onBack} onNext={!loading && !error ? handleNext : undefined} onCancel={onCancel}>
            {!error && (
                <FormProvider {...formMethods}>
                    <form className={classes.form}>
                        <Grid container alignItems="center" spacing={3} className={classes.root}>
                            <Grid item xs={12}>
                                <AutocompleteController
                                    control={control}
                                    name="fromEvent"
                                    options={options}
                                    defaultValue={options[0]}
                                    getOptionLabel={s => s.label}
                                    size="small"
                                    fullWidth
                                    renderInput={params => (
                                        <TextField {...params} label={t("FromEvent")} required autoFocus />
                                    )}
                                />
                            </Grid>
                            <Grid item xs={12} style={{display: checkBoxVisible ? undefined : "none"}}>
                                <Controller
                                    control={control}
                                    name="includeFollowingEvents"
                                    render={({name, onChange, value}) => {
                                        return (
                                            <FormControlLabel
                                                control={
                                                    <Checkbox
                                                        name={name}
                                                        color="default"
                                                        checked={!!value}
                                                        onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                                                            onChange(e.target.checked);
                                                        }}
                                                    />
                                                }
                                                label={t("IncludeFollowingEvents")}
                                                //error={!!errors.includeFollowingEvents}
                                                //helperText={errors.includeFollowingEvents?.message}
                                            />
                                        );
                                    }}
                                />
                            </Grid>
                        </Grid>
                        <FormErrorViewer errors={errors} />
                    </form>
                </FormProvider>
            )}
            <DevTool control={control} />
        </StepContainer>
    );
};
