import {FC, useCallback, useState, useMemo} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import {useTranslation} from "react-i18next";
import useSWR from "swr";
import MenuItem from "@material-ui/core/MenuItem";
import Alert from "@material-ui/lab/Alert";
import {clone} from "ramda";
import {Column, Options} from "@material-table/core";

import {
    DashboardWidgetInstantiation,
    DashboardParameter,
    getVariableNames,
    getParameterVariablesType,
} from "../../api/nimflow-dashboard-api";
import {
    useNimflowDashboardWidgets,
    DashboardWidgetParameter,
    isListParameter,
} from "../../api/nimflow-dashboard-widgets-api";
import {validateWithYupSchema} from "../../libs/yup";
import {DashboardWidgetInstantiationSchema} from "./dashboard-definition-schema";
import {ErrorViewer} from "../../components/error-viewer";
import {ErrorInfo} from "../../models/error-info";
import MaterialTable from "../../libs/material-table";
import {useKey} from "../../utils/use-key";

const titleHeight = 64;
const footerHeight = 52;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            backgroundColor: theme.palette.background.default,
            //height: "100%",
            width: "100%",
            minHeight: "50vh",
            maxHeight: "70vh",
        },
        dialogTitle: {
            height: titleHeight,
        },
        dialogContent: {
            height: `calc(100% - ${titleHeight}px - ${footerHeight}px)`,
            width: "100%",
        },
        dialogFooter: {
            height: footerHeight,
        },
        textField: {
            width: "40ch",
        },
    })
);

export interface DashboardWidgetInstanceEditorDialogProps {
    open: boolean;
    defaultValue: Partial<DashboardWidgetInstantiation>;
    dashboardParameters: DashboardParameter[];
    onChange: (widget: DashboardWidgetInstantiation) => void;
    onClose: () => void;
}

const getWidgetParameterCompatibleDashboardVariables = (
    widgetParam: DashboardWidgetParameter,
    dashboardParameters: DashboardParameter[]
) => {
    return dashboardParameters
        .filter(s => {
            if (getParameterVariablesType(s) !== widgetParam.type) {
                return false;
            }
            if (s.selectionType === "single" && s.allowEmptySelection && widgetParam.required) {
                return false;
            }
            if (s.selectionType === "multiple" && !isListParameter(widgetParam)) {
                return false;
            }
            return true;
        })
        .flatMap(s => getVariableNames(s));
};

interface ParameterBinding {
    widgetParameter: string;
    dashboardVariable: string | undefined;
    required: boolean;
}

const options: Options<ParameterBinding> = {
    paging: false,
};

const useWidget = (widgetName?: string) => {
    const {dashboardWidgets_FindOneByName, getWidgetDefinition} = useNimflowDashboardWidgets();
    const key = useKey(dashboardWidgets_FindOneByName, widgetName);
    const {data, error, isValidating, mutate} = useSWR(
        key,
        async () => {
            if (!widgetName) {
                return undefined;
            }
            const defOrSpec = await dashboardWidgets_FindOneByName(widgetName);
            const result = await getWidgetDefinition(defOrSpec);
            return result;
        },
        {revalidateOnFocus: false}
    );
    return useMemo(() => {
        return {
            widget: data,
            isValidating,
            error,
            revalidate: mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export const DashboardWidgetInstanceEditorDialog: FC<DashboardWidgetInstanceEditorDialogProps> = props => {
    const {open, defaultValue, dashboardParameters, onChange, onClose} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const [value, setValue] = useState(clone(defaultValue));
    const [errors, setErrors] = useState<ErrorInfo[]>();
    const {widget, error: swrError, isValidating} = useWidget(defaultValue.widgetName);

    const handleAccept = useCallback(async () => {
        const errors = await validateWithYupSchema(DashboardWidgetInstantiationSchema, value);
        if (errors.length === 0) {
            setErrors(undefined);
            onChange?.(value as DashboardWidgetInstantiation);
            onClose();
        } else {
            setErrors(errors);
        }
    }, [onChange, onClose, value]);

    const handleChangeParameterBinding = useCallback((widgetParam: string, dashboardParam: string | undefined) => {
        setValue(s => {
            const parametersBindings: Record<string, string> = {...s.parametersBindings};
            if (dashboardParam) {
                parametersBindings[widgetParam] = dashboardParam;
            } else {
                delete parametersBindings[widgetParam];
            }
            const result: Partial<DashboardWidgetInstantiation> = {
                ...s,
                parametersBindings,
            };
            return result;
        });
    }, []);

    const getCompatibleDashboardVariables = useCallback(
        (widgetParamName: string): string[] => {
            const widgetParam = widget?.parameters?.find(p => p.name === widgetParamName);
            if (!widgetParam) {
                return [];
            }
            return getWidgetParameterCompatibleDashboardVariables(widgetParam, dashboardParameters);
        },
        [dashboardParameters, widget]
    );

    const initialBindings = useMemo(() => {
        const bindings: ParameterBinding[] = [];
        if (widget?.parameters) {
            for (const param of widget.parameters) {
                bindings.push({
                    widgetParameter: param.name,
                    dashboardVariable: defaultValue.parametersBindings?.[param.name],
                    required: param.required,
                });
            }
        }
        return bindings;
    }, [defaultValue.parametersBindings, widget]);

    const columns = useMemo(() => {
        const cols: Column<ParameterBinding>[] = [
            {
                field: "widgetParameter",
                title: t("WidgetParameter"),
                render: data => {
                    let str = data.widgetParameter;
                    if (data.required) {
                        str += " (*)";
                    }
                    return str;
                },
                editable: "never",
            },
            {
                field: "dashboardVariable",
                title: t("DashboardVariable"),
                render: data => {
                    return (
                        <TextField
                            select
                            defaultValue={data.dashboardVariable}
                            onChange={e => handleChangeParameterBinding(data.widgetParameter, e.target.value)}
                            className={classes.textField}
                            required={data.required}
                        >
                            {getCompatibleDashboardVariables(data.widgetParameter).map(s => (
                                <MenuItem key={s} value={s}>
                                    {s}
                                </MenuItem>
                            ))}
                        </TextField>
                    );
                },
            },
        ];
        return cols;
    }, [classes.textField, getCompatibleDashboardVariables, handleChangeParameterBinding, t]);

    const handleDialogClose = useCallback(async (event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [onClose]);

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            fullWidth
            maxWidth="md"
            classes={{paper: classes.paper}}
        >
            <DialogTitle className={classes.dialogTitle}>{t("EditingWidget")}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <MaterialTable data={initialBindings} columns={columns} title=" " options={options} />
                {!isValidating && !!swrError && <Alert severity="error">{swrError.toString()}</Alert>}
                {!!errors && <ErrorViewer error={errors} />}
            </DialogContent>
            <DialogActions className={classes.dialogFooter}>
                <Button onClick={handleAccept} color="primary">
                    {t("Accept")}
                </Button>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
