import {useCallback, useMemo, useState} from "react";
import {intersection} from "ramda";

// Retrieved from https://fonts.google.com/metadata/icons
import iconMetadata from "./icon-metadata.json";
import {toUpperCamelCase} from "../../utils/string-utils";


export interface IconInfo {
    name: string;
    title: string;
}

const allIcons: IconInfo[] = [];
const iconsMap: Record<string, IconInfo> = {};
const tagMap: Record<string, string[]> = {};
const categoryMap: Record<string, string[]> = {};
const availableCategories: string[] = [];
const availableTags: string[] = [];

const addTag = (tag: string, name: string) => {
    let tags = tagMap[tag];
    if (!tags) {
        tagMap[tag] = tags = [];
        availableTags.push(tag);
    }
    tags.push(name);
};

const ensureIconMaps = () => {
    if (allIcons.length > 0) {
        return;
    }
    for (const iconInfo of iconMetadata.icons) {
        const name = iconInfo.name;
        iconsMap[name] = {
            name,
            title: name.split("_").map(toUpperCamelCase).join(" "),
        };
        allIcons.push(iconsMap[name]);
        let nameAdded = false;
        for (const tag of iconInfo.tags) {
            addTag(tag, name);
            nameAdded = nameAdded || tag === name;
        }
        if (!nameAdded) {
            addTag(name, name);
        }
        for (const category of iconInfo.categories) {
            let icons = categoryMap[category];
            if (!icons) {
                categoryMap[category] = icons = [];
                availableCategories.push(category);
            }
            icons.push(name);
        }
    }
    availableTags.sort();
    availableCategories.sort();
    Object.freeze(allIcons);
    Object.freeze(iconsMap);
    Object.freeze(tagMap);
    Object.freeze(categoryMap);
    Object.freeze(availableTags);
    Object.freeze(availableCategories);
};

export const useIcons = () => {
    const [currentCategory, setCurrentCategory] = useState<string>();
    const [currentTag, setCurrentTag] = useState<string>();

    ensureIconMaps();

    const getIconByName = useCallback((name: string) => {
        return iconsMap[name];
    }, []);

    const filteredIcons = useMemo(() => {
        let names: string[] | undefined = undefined;
        if (currentTag) {
            names = tagMap[currentTag];
        }
        if (currentCategory) {
            const catNames = categoryMap[currentCategory];
            if (catNames) {
                names = names ? intersection(names, catNames) : catNames;
            }
        }
        if (!names) {
            return allIcons;
        }
        return names.map(s => iconsMap[s]);
    }, [currentCategory, currentTag]);

    return {
        availableCategories,
        availableTags,
        currentCategory,
        currentTag,
        setCurrentCategory,
        setCurrentTag,
        filteredIcons,
        allIcons,
        getIconByName,
    };
};
