import {FC, useState} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import {useDebounce} from "react-use";
import {useFormikContext} from "formik";
import Autosizer from "react-virtualized-auto-sizer";

import {ContextEventListener} from "../../api/nimflow-api-extensions";
import {JavascriptMonacoEditor} from "../../components/javascript-monaco-editor";
import {FormikErrorViewer} from "../../components/formik-error-viewer";
import {freeVarDefs} from "./free-variable-defs";

const errorContainerHeight = 100;

interface StyleProps {
    showErrors: boolean;
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        content: {
            height: "60vh",
            //height: "calc(100% - 50px)",
            marginTop: 5,
            display: "grid",
            gridTemplateColumns: "auto",
            gridTemplateRows: (props: StyleProps) => (props.showErrors ? `auto ${errorContainerHeight}px` : "auto"),
            rowGap: "5px",
            placeItems: "stretch",
            overflowY: "hidden",
        },
        editorContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
        },
        errorContainer: {
            gridColumn: "1 / 2",
            gridRow: "2 / 3",
            overflowY: "auto",
        },
        title: {
            display: "inline-block",
        },
        grow: {
            flexGrow: 1,
        },
        openIcon: {
            fontSize: "1rem",
            verticalAlign: "middle",
        },
    })
);

const freeVariables = [freeVarDefs.event, freeVarDefs.args];

export const JsParametersEditor: FC<{onErrorChange?: (errors: string[]) => void}> = props => {
    const {onErrorChange} = props;
    const {errors, values: eventListener, setFieldValue} = useFormikContext<ContextEventListener>();
    const argsErrors = (errors.call as any)?.jsParametersBinding;
    const classes = useStyles({showErrors: !!argsErrors});
    const [code, setCode] = useState(eventListener.call?.jsParametersBinding);

    useDebounce(
        () => {
            setFieldValue("call.jsParametersBinding", code);
        },
        1000,
        [code]
    );

    return (
        <div className={classes.content}>
            <div className={classes.editorContainer}>
                <Autosizer>
                    {({height, width}) => {
                        return (
                            <JavascriptMonacoEditor
                                height={height}
                                width={width}
                                value={code}
                                onChange={setCode}
                                onErrorsChange={onErrorChange}
                                importJsCommon={false}
                                freeVariables={freeVariables}
                            />
                        );
                    }}
                </Autosizer>
            </div>
            {!!argsErrors && (
                <div className={classes.errorContainer}>
                    <FormikErrorViewer errors={errors} names="call.jsParametersBinding" />
                </div>
            )}
        </div>
    );
};
