import {FC, useMemo} from "react";
import {Column, Action} from "@material-table/core";
import Alert from "@material-ui/lab/Alert";
import {useTranslation} from "react-i18next";
import i18next from "i18next";
import RefreshIcon from "@material-ui/icons/Refresh";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import {useNavigate} from "react-router";
import useSWR from "swr";

import MaterialTable from "../../libs/material-table";
import {useNimflow} from "../../api/nimflow-api-provider";
import {ContextEventListener} from "../../api/nimflow-api-extensions";
import {useOkCancelConfirm} from "../../components/confirm";
import {useKey} from "../../utils/use-key";

const columns: Column<ContextEventListener>[] = [
    {field: "event", title: i18next.t("Event")},
    {field: "contextTypeName", title: i18next.t("ContextTypeName")},
    {field: "taskTypeName", title: i18next.t("TaskTypeName")},
    {
        field: "functionCall",
        title: i18next.t("FunctionCall"),
        render: row => `${row.call?.moduleName}/${row.call?.functionName}`,
    },
    {field: "subscribingUserEmail", title: i18next.t("SubscribingUserEmail")},
];

const buildRefreshAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: RefreshIcon,
        tooltip: i18next.t("RefreshData"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const buildAddAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: AddIcon,
        tooltip: i18next.t("AddContextType"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const adaptHandler = <T extends object>(handler: (info: T) => void): Action<T>["onClick"] => {
    return (_, info) => {
        if (Array.isArray(info)) {
            info = info[0];
        }
        handler(info);
    };
};

const buildEditAction = (handler: (info: ContextEventListener) => void) => {
    const action: Action<ContextEventListener> = {
        icon: EditIcon,
        tooltip: i18next.t("Edit"),
        onClick: adaptHandler(handler),
    };
    return action;
};

const buildDeleteAction = (handler: (info: ContextEventListener) => void) => {
    const action: Action<ContextEventListener> = {
        icon: DeleteIcon,
        tooltip: i18next.t("Delete"),
        onClick: adaptHandler(handler),
    };
    return action;
};

const useContextEventListener = () => {
    const {events_SearchContextEventListeners} = useNimflow();
    const queryKey = useKey(events_SearchContextEventListeners);
    const {data, error, mutate, isValidating} = useSWR(queryKey, async () => {
        const result = await events_SearchContextEventListeners({contextTypeName: ""}) as ContextEventListener[];
        return result;
    });
    return useMemo(() => {
        return {
            eventListeners: data ?? [],
            isValidating,
            error,
            revalidate: mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export const EventListenersViewer: FC<{}> = () => {
    const {t} = useTranslation();
    const confirm = useOkCancelConfirm();
    const {events_RemoveListener} = useNimflow();
    const navigate = useNavigate();
    const {eventListeners, error, revalidate, isValidating} = useContextEventListener();

    const refreshAction = useMemo(() => {
        return buildRefreshAction(revalidate);
    }, [revalidate]);

    const addAction = useMemo(() => {
        return buildAddAction(() => {
            navigate("new");
        });
    }, [navigate]);

    const rowActions = useMemo(() => {
        return [
            buildEditAction(row => {
                navigate(`edit/${row.id}`);
            }),
            buildDeleteAction(async row => {
                let result = await confirm({description: t("ConfirmDeleteEventListener", {event: row.event, contextTypeName: row.contextTypeName})});
                if (result) {
                    result = await events_RemoveListener({
                        id: row.id!
                    });
                    revalidate();
                }
                return result;
            }),
        ];
    }, [navigate, confirm, t, events_RemoveListener, revalidate]);

    return (
        <>
            <MaterialTable
                columns={columns}
                data={eventListeners ?? []}
                title={t("Automation")}
                actions={[addAction, refreshAction, ...rowActions]}
            />
            {!isValidating && !!error && <Alert severity="error">{error.toString()}</Alert>}
        </>
    );
};
