import React, {FC, useEffect, useState, useCallback} from "react";
import axios from "axios";
import {useParams, useNavigate} from "react-router";
import {useSnackbar} from "notistack";
import {useTranslation} from "react-i18next";

import {readJsonFromBlob} from "../../utils/io-helpers";
import {useShowErrorInfos} from "../../components/use-show-error-infos";
import {useReportError} from "../../utils/use-report-error";
import {useOkCancelConfirm} from "../../components/confirm";
import {useNimflowDashboardWidgets, DashboardWidgetDefOrJsSpec} from "../../api/nimflow-dashboard-widgets-api";
import {importWidgetDefinitionFromJson} from "./widget-definition-json-importer";
import {WidgetDefOrJsSpecEditor} from "./widget-def-or-js-spec-editor";

export const WidgetDefinitionImporter: FC<{}> = () => {
    const {t} = useTranslation();
    const {blobUrl} = useParams();
    const navigate = useNavigate();
    const {enqueueSnackbar} = useSnackbar();
    const showErrors = useShowErrorInfos();
    const {
        dashboardWidgets_GetInfos,
        dashboardWidgets_Replace,
        dashboardWidgets_InsertOne,
    } = useNimflowDashboardWidgets();
    const [widgetDefOrJsSpec, setWidgetDefOrJsSpec] = useState<DashboardWidgetDefOrJsSpec>();
    const reportErrors = useReportError();
    const confirm = useOkCancelConfirm();

    useEffect(() => {
        const dataUrl = atob(blobUrl);
        if (!dataUrl) {
            return;
        }
        axios
            .get(dataUrl, {responseType: "blob"})
            .then(async response => {
                const json = await readJsonFromBlob(response.data);
                const importResult = await importWidgetDefinitionFromJson(json);
                if (importResult.type === "success") {
                    setWidgetDefOrJsSpec(importResult.result); // Pass the raw json without caches.
                    return;
                } else {
                    reportErrors(t("FileIsNotAValidWidgetDefinition"), importResult.errors, {
                        dialogProps: {
                            maxWidth: "lg",
                        },
                    });
                    navigate("../..");
                }
            })
            .catch(() => {
                navigate("../..");
            })
            .finally(() => {
                URL.revokeObjectURL(dataUrl);
            });
    }, [blobUrl, navigate, enqueueSnackbar, showErrors, t, reportErrors]);

    const handleOnChange = useCallback(
        async (value: DashboardWidgetDefOrJsSpec) => {
            const infos = await dashboardWidgets_GetInfos();
            const info = infos.find(s => s.name.toLocaleLowerCase() === value.name.toLocaleLowerCase());
            if (info) {
                const overwrite = await confirm({
                    description: t("ConfirmOverwriteWidgetDashboard", {name: value.name}),
                });
                if (!overwrite) {
                    return false;
                }
                value.id = info.id;
                await dashboardWidgets_Replace(value);
            } else {
                value.id = await dashboardWidgets_InsertOne(value);
            }
            setWidgetDefOrJsSpec(value);
            navigate(`../../edit/${value.id}`, {replace: true});
            return true;
        },
        [confirm, dashboardWidgets_GetInfos, dashboardWidgets_InsertOne, dashboardWidgets_Replace, navigate, t]
    );

    if (!widgetDefOrJsSpec) {
        return null;
    }

    return <WidgetDefOrJsSpecEditor value={widgetDefOrJsSpec} onApplyChanges={handleOnChange} />;
};
