import {JSONSchema7} from "json-schema";
import {clone, omit} from "ramda";

import {DashboardWidgetDefOrJsSpec} from "../../api/nimflow-dashboard-widgets-api";

export const exportWidgetDefinitionToJson = (
    widgetDefinition: DashboardWidgetDefOrJsSpec
): Omit<DashboardWidgetDefOrJsSpec, "jsonSchema"> & {$schema: string; jsonSchema?: JSONSchema7} => {
    const result: any = {
        $schema: "http://nimflow.com/schemas/widget-definition.schema.json",
        name: widgetDefinition.name, // Force order of insertion of name before the rest
        ...clone(omit(["name", "id", "_id"], widgetDefinition)),
    };
    return result;
};
