import React, {FC, useCallback, useEffect, useState, useMemo} from "react";
import {createStyles, makeStyles, Theme} from "@material-ui/core";
import clsx from "clsx";
import Grid from "@material-ui/core/Grid";
import Divider from "@material-ui/core/Divider";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import SpeedDial from "@material-ui/lab/SpeedDial";
import SpeedDialIcon from "@material-ui/lab/SpeedDialIcon";
import SpeedDialAction from "@material-ui/lab/SpeedDialAction";
import InputIcon from "@material-ui/icons/Input";
import FunctionsIcon from "@material-ui/icons/Functions";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
// import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
// import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import AssignmentTurnedInIcon from "@material-ui/icons/AssignmentTurnedIn";
import {useFormContext, Controller} from "react-hook-form";
import {useSetRecoilState} from "recoil";
import {NodeModel} from "@projectstorm/react-diagrams";
import {clone, difference} from "ramda";
import {useTranslation} from "react-i18next";
import {isDefined, isPresent} from "ts-is-present";
import i18next from "i18next";

import {ActionType, ContextType, TaskType, ResponseType, Selector} from "../../api/nimflow-api";
import {StandardSection, StandardSectionProps} from "../../components/standard-section";
import {useArrayField} from "../../utils/use-array-field";
import {ContextTypeDiagram} from "./context-type-diagram";
import {contextTypeSelectionRequestedState} from "./context-type-edition.state";
import {ContextTypeNodeModel, ContextTypeNodeModelCommand} from "./diagram-nodes/context-type-node-model";
import {TaskTypeEditorDialog} from "./task-type-editor-dialog";
import {ActionTypeEditorDialog} from "./action-type-editor-dialog";
import {useOkCancelConfirm} from "../../components/confirm";
import {ResponseTypeEditorDialog} from "./response-type-editor-dialog";
import {SelectorEditorDialog} from "./selector-editor-dialog";
import {ErrorBoundary} from "../../components/error-boundary";
import {useTaggings, TaggingType} from "./taggings";
import {useContextTypeContext} from "./context-type-context";
import {inferStateNames} from "../../api/nimflow-api-extensions";
import {ActionTypeNodeModel} from "./diagram-nodes/action-type-node-model";
import {TaskTypeNodeModel} from "./diagram-nodes/task-type-node-model";
import {SelectorNodeModel} from "./diagram-nodes/selector-node-model";
import {extractStateRefs} from "../../utils/js-analysis";
import {contextElementTypes, ContextElementType, PinStatus} from "./context-type-diagram-model";
//import {ContextTypeElementVisibilitySelector} from "./context-type-element-visibility-selector";
import {ContextTypeDiagramStateList} from "./context-type-diagram-state-list";

const sideMenuWidth = 360;
const sideMenuBorderWidth = 2;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            backgroundColor: theme.palette.background.default,
            padding: 1,
            margin: 1
        },
        speedDial: {
            position: "absolute",
            bottom: theme.spacing(2),
            right: theme.spacing(2),
        },
        menuButton: {
            position: "absolute",
            top: 100,
            right: 0,
            minWidth: 25,
            borderTopRightRadius: 0,
            borderBottomRightRadius: 0,
            padding: "2px 2px",
            borderRight: "none",
            color: theme.palette.grey[500]
        },
        search: {
            position: "absolute",
            top: theme.spacing(2),
            right: theme.spacing(2),
        },
        content: {
            position: "relative",
            width: "100%",
            height: "100%",
            minWidth: 1200,
            overflow: "hidden",
        },
        diagram: {
            position: "relative",
            width: "100%",
            height: "100%",
            transition: theme.transitions.create(["margin", "width"], {
                easing: theme.transitions.easing.sharp,
                duration: theme.transitions.duration.leavingScreen,
            }),
        },
        diagramShift: {
            width: `calc(100% - ${sideMenuWidth}px)`,
            marginRight: sideMenuWidth,
            transition: theme.transitions.create(["margin", "width"], {
                easing: theme.transitions.easing.easeOut,
                duration: theme.transitions.duration.enteringScreen,
            }),
        },
        diagramSidePanel: {
            width: sideMenuWidth,
            position: "absolute",
            top: 0,
            bottom: 0,
            right: -sideMenuWidth + sideMenuBorderWidth,
            borderLeftStyle: "solid",
            borderLeftWidth: sideMenuBorderWidth,
            borderLeftColor: theme.palette.divider,
            transition: theme.transitions.create(["right"], {
                easing: theme.transitions.easing.sharp,
                duration: theme.transitions.duration.leavingScreen,
            }),
            height: "100%",
            overflow: "auto",
            display: "flex",
            flexDirection: "column",
        },
        diagramSidePanelShift: {
            right: 0,
            transition: theme.transitions.create(["right"], {
                easing: theme.transitions.easing.easeOut,
                duration: theme.transitions.duration.enteringScreen,
            }),
        },
        sidePanelEditors: {
            padding: theme.spacing(1),
        },
        visibilityButtons: {
            textAlign: "center",
            margin: "10px 0",
        },
        stateList: {
            overflow: "auto",
        },
        stateListTitle: {
            margin: "10px 10px 5px 10px",
        },
        button: {
            margin: theme.spacing(1),
        },
        editActionContainer: {
            paddingTop: 5,
        },
        backArrow: {
            marginLeft: 3,
            marginRight: -3,
        },
    })
);

export interface ContextTypeDiagramEditorProps extends StandardSectionProps {
    visibleElementTypes?: ContextElementType[];
    onEditJsCommon?: () => void;
    onEditJsonSchema?: () => void;
}

interface ActionTypeEditionData {
    type: "actionType";
    index?: number;
    actionType: Partial<ActionType>;
}

interface SelectorEditionData {
    type: "selector";
    index?: number;
    selector: Partial<Selector>;
}

interface TaskTypeEditionData {
    type: "taskType";
    index?: number;
    taskType: Partial<TaskType>;
}

interface ResponseTypeEditionData {
    type: "responseType";
    taskIndex: number;
    responseIndex: number;
    responseType: Partial<ResponseType>;
}

type EditionData = ActionTypeEditionData | SelectorEditionData | TaskTypeEditionData | ResponseTypeEditionData;

interface SpeedDialActionDef {
    icon: React.ReactNode;
    name: EditionData["type"];
    tooltip?: string;
}

const actions: SpeedDialActionDef[] = [
    {icon: <InputIcon />, name: "actionType", tooltip: i18next.t("NewActionType")},
    {icon: <FunctionsIcon />, name: "selector", tooltip: i18next.t("NewSelector")},
    {icon: <AssignmentTurnedInIcon />, name: "taskType", tooltip: i18next.t("NewTaskType")},
];

export const ContextTypeDiagramEditor: FC<ContextTypeDiagramEditorProps> = props => {
    const {
        className,
        visibleElementTypes = [...contextElementTypes],
        onEditJsCommon,
        onEditJsonSchema,
        ...rest
    } = props;
    const classes = useStyles();
    const [editionData, setEditionData] = useState<EditionData>();
    const [speedDialOpen, setSpeedDialOpen] = React.useState(false);
    const [sideBarOpen, setSideBarOpen] = React.useState(true);
    const [statesIncluded, setStatesIncluded] = React.useState<string[]>([]);
    const [pinStatuses, setPinStatuses] = React.useState<Record<string, PinStatus>>({});
    //const [visibleElementTypes, setVisibleElementTypes] = React.useState(() => [...contextElementTypes]);
    const {t} = useTranslation();
    const confirm = useOkCancelConfirm();
    const {control, errors} = useFormContext<ContextType>();
    const {
        fields: actionTypes,
        setAt: setActionTypeAt,
        append: appendActionType,
        remove: removeActionType,
    } = useArrayField<ActionType>({
        name: "actions",
        control,
    });
    const {taggings, toggleTagging, setTaggingType, removeInvalidTaggings} = useTaggings();
    const {fields: initialActions, append: initialActionsAppend, remove: initialActionsRemove} = useArrayField<string>({
        name: "initialActions",
        control,
    });

    const {fields: selectors, setAt: setSelectorAt, append: appendSelector, remove: removeSelector} = useArrayField<
        Selector
    >({
        name: "selectors",
        control,
    });
    const {fields: taskTypes, setAt: setTaskTypeAt, append: appendTaskType, remove: removeTaskType} = useArrayField<
        TaskType
    >({
        name: "tasks",
        control,
    });
    const setContextTypeInstanceSelectionRequested = useSetRecoilState(contextTypeSelectionRequestedState);
    const {
        getJsonSchemaDefinition,
        getJsonSchemaDefinitions,
        setJsonSchemaDefinition,
        updateActionJsonSchemaDefinition,
        updateResponseJsonSchemaDefinition,
        updateTaskJsonSchemaDefinitions,
    } = useContextTypeContext();
    const [selectedStates, setSelectedStates] = useState<string[]>([]);

    const handleNodeSelectionChanged = useCallback(
        (node: NodeModel<any>, isSelected: boolean) => {
            if (node instanceof ContextTypeNodeModel) {
                setContextTypeInstanceSelectionRequested(() => {
                    return isSelected ? node.instance : undefined;
                });
                let newSelectedStates: string[] = [];
                if (isSelected) {
                    if (node instanceof ActionTypeNodeModel) {
                        newSelectedStates = Object.keys(node.instance.jsSetters ?? {}) ?? [];
                    } else if (node instanceof TaskTypeNodeModel) {
                        newSelectedStates =
                            node.instance?.validResponses?.flatMap(s => Object.keys(s?.jsSetters ?? {})) ?? [];
                    } else if (node instanceof SelectorNodeModel) {
                        const allDeps = extractStateRefs(node.instance?.jsExpression);
                        newSelectedStates = difference(allDeps, node.instance?.dependencies ?? []);
                    }
                }
                setSelectedStates(s => {
                    if (s.length === 0 && newSelectedStates.length === 0) {
                        return s;
                    }
                    return newSelectedStates;
                });
            }
        },
        [setContextTypeInstanceSelectionRequested]
    );

    useEffect(() => {
        return () => {
            setContextTypeInstanceSelectionRequested(undefined);
        };
    }, [setContextTypeInstanceSelectionRequested]);

    const handleActionTypeCommandInvoked = useCallback(
        async (actionType: Partial<ActionType>, command: ContextTypeNodeModelCommand) => {
            const index = actionTypes.findIndex(s => s === actionType);
            if (index <= -1) {
                return;
            }
            switch (command.type) {
                case "edit":
                    setEditionData({type: "actionType", index, actionType: clone(actionType)});
                    break;
                case "delete":
                    let result = await confirm({
                        description: t("ConfirmDeleteActionType", {name: actionTypes[index].name}),
                    });
                    if (result) {
                        let defName = actionType.payloadSchemaDefinitionName;
                        if (defName) {
                            setJsonSchemaDefinition(defName, undefined);
                        }
                        removeActionType(index);
                        removeInvalidTaggings();
                        const initialActionIndex = initialActions.indexOf(actionType.name!);
                        if (initialActionIndex >= 0) {
                            initialActionsRemove(initialActionIndex);
                        }
                    }
                    break;
                case "toggleInitialAction":
                    const initialActionIndex = initialActions.indexOf(actionType.name!);
                    if (initialActionIndex >= 0) {
                        initialActionsRemove(initialActionIndex);
                    } else {
                        initialActionsAppend(actionType.name!);
                    }
                    break;
            }
        },
        [
            actionTypes,
            confirm,
            initialActions,
            initialActionsAppend,
            initialActionsRemove,
            removeActionType,
            removeInvalidTaggings,
            setJsonSchemaDefinition,
            t,
        ]
    );

    const actionTypePayloadSchemaDefinition = useMemo(() => {
        if (editionData?.type !== "actionType") {
            return undefined;
        }
        const defname = editionData.actionType.payloadSchemaDefinitionName;
        if (!defname) {
            return undefined;
        }
        return getJsonSchemaDefinition(defname);
    }, [editionData, getJsonSchemaDefinition]);

    const handleActionTypeChanged = useCallback(
        (actionType: ActionType, definitionJsonSchema: string | undefined) => {
            if (!editionData) {
                return;
            }
            updateActionJsonSchemaDefinition(actionType, definitionJsonSchema);
            const {index} = editionData as ActionTypeEditionData;
            if (isDefined(index)) {
                const oldActionName = actionTypes[index]?.name;
                if (oldActionName !== actionType.name && oldActionName) {
                    const index = initialActions.indexOf(oldActionName);
                    if (index >= 0) {
                        initialActionsRemove(index);
                        initialActionsAppend(actionType.name);
                    }
                }
                if (!actionType.payloadSchemaDefinitionName) {
                    const defName = actionTypes[index].payloadSchemaDefinitionName;
                    if (defName) {
                        setJsonSchemaDefinition(defName, undefined);
                    }
                }
                setActionTypeAt(index, actionType);
                removeInvalidTaggings();
            } else {
                appendActionType(actionType);
            }
        },
        [
            actionTypes,
            appendActionType,
            editionData,
            initialActions,
            initialActionsAppend,
            initialActionsRemove,
            removeInvalidTaggings,
            setActionTypeAt,
            setJsonSchemaDefinition,
            updateActionJsonSchemaDefinition,
        ]
    );

    const handleStateCommandInvoked = useCallback(
        async (state: string, command: ContextTypeNodeModelCommand) => {
            switch (command.type) {
                case "toggleTagging": {
                    toggleTagging(state);
                    break;
                }
                case "setTagging": {
                    setTaggingType(state, command.tagging);
                    break;
                }
            }
        },
        [setTaggingType, toggleTagging]
    );

    const editSelector = useCallback(
        (selector: Partial<Selector>) => {
            const index = selectors.findIndex(s => s === selector);
            if (index <= -1) {
                return;
            }
            setEditionData({type: "selector", index, selector: clone(selector)});
        },
        [selectors]
    );

    const deleteSelector = useCallback(
        async (selector: Partial<Selector>) => {
            const index = selectors.findIndex(s => s === selector);
            if (index <= -1) {
                return;
            }
            let result = await confirm({
                description: t("ConfirmDeleteSelector", {name: selectors[index].key}),
            });
            if (result) {
                removeSelector(index);
                removeInvalidTaggings();
            }
        },
        [confirm, removeInvalidTaggings, removeSelector, selectors, t]
    );

    const handleSelectorCommandInvoked = useCallback(
        async (selector: Partial<Selector>, command: ContextTypeNodeModelCommand) => {
            switch (command.type) {
                case "edit":
                    editSelector(selector);
                    break;
                case "delete":
                    await deleteSelector(selector);
                    break;
                case "toggleTagging": {
                    toggleTagging(selector.key!);
                    break;
                }
                case "setTagging": {
                    setTaggingType(selector.key!, command.tagging);
                    break;
                }
            }
        },
        [editSelector, deleteSelector, toggleTagging, setTaggingType]
    );

    const handleSelectorChanged = useCallback(
        (selector: Selector) => {
            if (!editionData) {
                return;
            }
            const {index} = editionData as SelectorEditionData;
            if (isDefined(index)) {
                setSelectorAt(index, selector);
                removeInvalidTaggings();
            } else {
                appendSelector(selector);
            }
        },
        [editionData, setSelectorAt, removeInvalidTaggings, appendSelector]
    );

    const handleTaskTypeCommandInvoked = useCallback(
        async (taskType: Partial<TaskType>, command: ContextTypeNodeModelCommand) => {
            const index = taskTypes.findIndex(s => s === taskType);
            if (index <= -1) {
                return;
            }
            switch (command.type) {
                case "edit":
                    setEditionData({type: "taskType", index, taskType: clone(taskType)});
                    break;
                case "delete":
                    let result = await confirm({
                        description: t("ConfirmDeleteTaskType", {name: taskTypes[index].name}),
                    });
                    if (result) {
                        const defNames =
                            taskType.validResponses?.map(s => s.payloadSchemaDefinitionName).filter(isDefined) ?? [];
                        for (const defName of defNames) {
                            setJsonSchemaDefinition(defName, undefined);
                        }
                        removeTaskType(index);
                        removeInvalidTaggings();
                    }
                    break;
            }
        },
        [confirm, removeInvalidTaggings, removeTaskType, setJsonSchemaDefinition, t, taskTypes]
    );

    const taskTypePayloadSchemaDefinitions = useMemo(() => {
        if (editionData?.type !== "taskType") {
            return {};
        }
        const names = (editionData.taskType.validResponses ?? [])
            .map(s => s.payloadSchemaDefinitionName)
            .filter(isPresent);
        return getJsonSchemaDefinitions(names);
    }, [editionData, getJsonSchemaDefinitions]);

    const handleTaskTypeChanged = useCallback(
        (taskType: TaskType, definitionJsonSchemas: Record<string, string | undefined>) => {
            if (!editionData) {
                return;
            }
            const {index} = editionData as TaskTypeEditionData;
            if (isDefined(index)) {
                const originalDefNames = taskTypes[index].validResponses
                    ?.map(s => s.payloadSchemaDefinitionName)
                    .filter(isPresent);
                for (const name of originalDefNames ?? []) {
                    if (!(name in definitionJsonSchemas)) {
                        definitionJsonSchemas[name] = undefined;
                    }
                }
                setTaskTypeAt(index, taskType);
                removeInvalidTaggings();
            } else {
                appendTaskType(taskType);
            }
            updateTaskJsonSchemaDefinitions(taskType, definitionJsonSchemas);
        },
        [editionData, updateTaskJsonSchemaDefinitions, taskTypes, setTaskTypeAt, removeInvalidTaggings, appendTaskType]
    );

    const handleResponseTypeCommandInvoked = useCallback(
        async (
            responseType: Partial<ResponseType>,
            taskType: Partial<TaskType>,
            command: ContextTypeNodeModelCommand
        ) => {
            const taskIndex = taskTypes.findIndex(s => s === taskType);
            if (taskIndex <= -1) {
                return;
            }
            if (!taskType.validResponses) {
                return;
            }
            const responseIndex = taskType.validResponses.findIndex(s => s === responseType);
            if (responseIndex <= -1) {
                return;
            }
            switch (command.type) {
                case "editResponse":
                    setEditionData({
                        type: "responseType",
                        taskIndex,
                        responseIndex,
                        responseType: clone(responseType),
                    });
                    break;
                case "deleteResponse":
                    let result = await confirm({
                        description: t("ConfirmDeleteResponseType", {
                            name: taskType.validResponses[responseIndex].name,
                        }),
                    });
                    if (result) {
                        let defName = responseType.payloadSchemaDefinitionName;
                        if (defName) {
                            setJsonSchemaDefinition(defName, undefined);
                        }

                        setTaskTypeAt(taskIndex, {
                            ...taskType,
                            validResponses: [...taskType.validResponses.filter(s => s !== responseType)],
                        });
                        removeInvalidTaggings();
                    }
                    break;
            }
        },
        [confirm, removeInvalidTaggings, setJsonSchemaDefinition, setTaskTypeAt, t, taskTypes]
    );

    const responseTypePayloadSchemaDefinition = useMemo(() => {
        if (editionData?.type !== "responseType") {
            return undefined;
        }
        const defname = editionData.responseType.payloadSchemaDefinitionName;
        if (!defname) {
            return undefined;
        }
        return getJsonSchemaDefinition(defname);
    }, [editionData, getJsonSchemaDefinition]);

    const handleResponseTypeChanged = useCallback(
        (responseType: ResponseType, definitionJsonSchema: string | undefined) => {
            if (!editionData) {
                return;
            }
            const {taskIndex, responseIndex} = editionData as ResponseTypeEditionData;
            const taskType = taskTypes[taskIndex];
            updateResponseJsonSchemaDefinition(responseType, taskType.name!, definitionJsonSchema);
            if (isDefined(responseIndex)) {
                if (!responseType.payloadSchemaDefinitionName) {
                    const defName = taskTypes[taskIndex].validResponses?.[responseIndex].payloadSchemaDefinitionName;
                    if (defName) {
                        setJsonSchemaDefinition(defName, undefined);
                    }
                }
                setTaskTypeAt(taskIndex, {
                    ...taskType,
                    validResponses: taskType.validResponses!.map((s, idx) =>
                        idx === responseIndex ? responseType : s
                    ),
                });
                removeInvalidTaggings();
            } else {
                setTaskTypeAt(taskIndex, {
                    ...taskType,
                    validResponses: [...(taskType.validResponses ?? []), responseType],
                });
            }
        },
        [
            editionData,
            removeInvalidTaggings,
            setJsonSchemaDefinition,
            setTaskTypeAt,
            taskTypes,
            updateResponseJsonSchemaDefinition,
        ]
    );

    const handleOnCloseDialog = useCallback(() => {
        setEditionData(undefined);
    }, [setEditionData]);

    const handleCloseSpeedDial = useCallback(() => {
        setSpeedDialOpen(false);
    }, []);

    const handleOpenSpeedDial = useCallback(() => {
        setSpeedDialOpen(true);
    }, []);

    const handleSpeedDialActionSelected = useCallback((action: SpeedDialActionDef) => {
        setSpeedDialOpen(false);
        switch (action.name) {
            case "actionType":
                setEditionData({
                    type: "actionType",
                    actionType: {
                        name: "",
                        jsSetters: {},
                        validators: [],
                    },
                });
                break;
            case "selector":
                setEditionData({
                    type: "selector",
                    selector: {
                        key: "",
                        jsExpression: "",
                        dependencies: [],
                    },
                });
                break;
            case "taskType":
                setEditionData({
                    type: "taskType",
                    taskType: {
                        name: "",
                        jsTaskDataSetters: {},
                        validResponses: [],
                    },
                });
                break;
        }
    }, []);

    const handleChangeTagging = useCallback(
        (state: string, tagging: TaggingType) => {
            setTaggingType(state, tagging);
        },
        [setTaggingType]
    );

    const states = useMemo(() => {
        return inferStateNames(actionTypes, taskTypes).sort();
    }, [actionTypes, taskTypes]);

    // const handleVisibleElementTypesChange = useCallback((newVisibleElementTypes: ContextElementType[]) => {
    //     setVisibleElementTypes(newVisibleElementTypes);
    // }, []);

    const handlePinStatusesChanged = useCallback((pinStatuses: Record<string, PinStatus>) => {
        setPinStatuses(pinStatuses);
    }, []);

    const handleChangePinning = useCallback((state: string, pinned: boolean) => {
        setStatesIncluded(s => {
            if (s.includes(state) && !pinned) {
                return s.filter(i => i !== state);
            } else if (!s.includes(state) && pinned) {
                return [...s, state];
            }
            return s;
        });
    }, []);

    const toggleSideMenu = useCallback(() => {
        setSideBarOpen(s => !s);
    }, []);

    return (
        <ErrorBoundary>
            <StandardSection className={clsx(classes.root, className)} {...rest} elevation={0}>
                <div className={classes.content}>
                    <div
                        className={clsx(classes.diagram, {
                            [classes.diagramShift]: sideBarOpen,
                        })}
                    >
                        <ContextTypeDiagram
                            actionTypes={actionTypes}
                            taskTypes={taskTypes}
                            selectors={selectors}
                            taggings={taggings}
                            initialActions={initialActions}
                            statesIncluded={statesIncluded}
                            visibleElementTypes={visibleElementTypes}
                            onNodeSelectionChanged={handleNodeSelectionChanged}
                            onActionTypeCommandInvoked={handleActionTypeCommandInvoked}
                            onStateCommandInvoked={handleStateCommandInvoked}
                            onSelectorCommandInvoked={handleSelectorCommandInvoked}
                            onTaskTypeCommandInvoked={handleTaskTypeCommandInvoked}
                            onResponseTypeCommandInvoked={handleResponseTypeCommandInvoked}
                            onPinStatusesChanged={handlePinStatusesChanged}
                        />
                        <SpeedDial
                            ariaLabel="speedDialMenu"
                            className={classes.speedDial}
                            icon={<SpeedDialIcon />}
                            onClose={handleCloseSpeedDial}
                            onOpen={handleOpenSpeedDial}
                            open={speedDialOpen}
                            direction="up"
                            FabProps={{
                                size: "medium",
                            }}
                        >
                            {actions.map(action => (
                                <SpeedDialAction
                                    key={action.name}
                                    icon={action.icon}
                                    tooltipTitle={action.tooltip ?? action.name}
                                    onClick={() => handleSpeedDialActionSelected(action)}
                                    FabProps={{
                                        size: "small",
                                    }}
                                />
                            ))}
                        </SpeedDial>
                        <Button
                            size="small"
                            variant="outlined"
                            className={classes.menuButton}
                            disableElevation
                            onClick={toggleSideMenu}
                            color="default"
                        >
                            {sideBarOpen ? (
                                <ArrowForwardIosIcon fontSize="small" />
                            ) : (
                                <ArrowBackIosIcon fontSize="small" className={classes.backArrow} />
                            )}
                            {/* {sideBarOpen ? <ChevronRightIcon fontSize="small"/> : <ChevronLeftIcon fontSize="small"/>} */}
                        </Button>
                    </div>
                    <div
                        className={clsx(classes.diagramSidePanel, {
                            [classes.diagramSidePanelShift]: sideBarOpen,
                        })}
                    >
                        {/* <div className={classes.visibilityButtons}>
                            <ContextTypeElementVisibilitySelector
                                value={visibleElementTypes}
                                onChange={handleVisibleElementTypesChange}
                            />
                        </div>
                        <Divider /> */}
                        <div className={classes.sidePanelEditors}>
                            <Grid container spacing={1}>
                                <Grid item xs={12}>
                                    <Controller
                                        as={TextField}
                                        control={control}
                                        name="name"
                                        label={t("Name")}
                                        fullWidth
                                        required
                                        error={!!errors.name}
                                        helperText={errors.name?.message}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                    <Controller
                                        as={TextField}
                                        control={control}
                                        name="description"
                                        label={t("Description")}
                                        multiline
                                        fullWidth
                                        maxRows={4}
                                        error={!!errors.description}
                                        helperText={errors.description?.message}
                                    />
                                </Grid>
                                {/* <Grid item xs={12}>
                                    <Button
                                        variant="outlined"
                                        onClick={onEditJsCommon}
                                        startIcon={<JavascriptIcon />}
                                        className={classes.button}
                                        size="small"
                                    >
                                        {t("EditJsCommon")}
                                    </Button>
                                    <Button
                                        variant="outlined"
                                        onClick={onEditJsonSchema}
                                        startIcon={<JsonSchemaIcon />}
                                        className={classes.button}
                                        size="small"
                                    >
                                        {t("EditJsonSchema")}
                                    </Button>
                                </Grid> */}
                            </Grid>
                        </div>
                        <Divider />
                        <div className={classes.stateList}>
                            <ContextTypeDiagramStateList
                                states={states}
                                selectors={selectors}
                                selectedStates={selectedStates}
                                taggings={taggings}
                                pinStatuses={pinStatuses}
                                onChangeTagging={handleChangeTagging}
                                onEditSelector={editSelector}
                                onDeleteSelector={deleteSelector}
                                onChangePinning={handleChangePinning}
                            />
                        </div>
                    </div>
                </div>
                {editionData?.type === "actionType" && (
                    <ActionTypeEditorDialog
                        open={editionData?.type === "actionType"}
                        onClose={handleOnCloseDialog}
                        defaultValue={editionData.actionType}
                        definitionJsonSchema={actionTypePayloadSchemaDefinition}
                        onChange={handleActionTypeChanged}
                    />
                )}
                {editionData?.type === "selector" && (
                    <SelectorEditorDialog
                        open={editionData?.type === "selector"}
                        onClose={handleOnCloseDialog}
                        defaultValue={editionData.selector}
                        onChange={handleSelectorChanged}
                    />
                )}
                {editionData?.type === "taskType" && (
                    <TaskTypeEditorDialog
                        open={editionData?.type === "taskType"}
                        onClose={handleOnCloseDialog}
                        defaultValue={editionData.taskType}
                        definitionJsonSchemas={taskTypePayloadSchemaDefinitions}
                        onChange={handleTaskTypeChanged}
                    />
                )}
                {editionData?.type === "responseType" && (
                    <ResponseTypeEditorDialog
                        open={editionData?.type === "responseType"}
                        onClose={handleOnCloseDialog}
                        defaultValue={editionData.responseType}
                        taskTypeName={taskTypes[editionData.taskIndex].name!}
                        definitionJsonSchema={responseTypePayloadSchemaDefinition}
                        onChange={handleResponseTypeChanged}
                    />
                )}
            </StandardSection>
        </ErrorBoundary>
    );
};
