import React, {FC, useCallback, useEffect, useMemo} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import {useTranslation} from "react-i18next";
import {useFormikContext} from "formik";
import useSWR from "swr";
import {sortBy} from "ramda";

import {
    DashboardWidgetQueryDataSource,
    dashboardWidgetAggregateDataSourceSourceCollections,
    QueryExpression,
    CalculateExpression,
    CalculateTableExpression,
    SummarizeExpression,
    DashboardWidgetAggregateDataSourceSourceCollection,
} from "../../api/nimflow-dashboard-widgets-api";
import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {CalculateQueryEditor} from "./calculate-query-editor";
import {CalculateTableQueryEditor} from "./calculate-table-query-editor";
import {SummarizeQueryEditor} from "./summarize-query-editor";
import {useNimflow} from "../../api/nimflow-api-provider";
import {useKey} from "../../utils/use-key";

const headerContainerHeight = 60;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        textField: {
            maxWidth: "60ch",
            width: "100%",
        },
        dialogContent: {
            display: "grid",
            gridTemplateColumns: "1fr 1fr",
            gridTemplateRows: `${headerContainerHeight}px ${headerContainerHeight}px auto`,
            rowGap: `${theme.spacing(2)}px`,
            columnGap: `${theme.spacing(3)}px`,
            placeItems: "stretch",
            height: "100%",
            overflowY: "auto",
        },
        nameContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
        },
        queryTypeContainer: {
            gridColumn: "2 / 3",
            gridRow: "1 / 2",
        },
        sourceCollectionContainer: {
            gridColumn: "1 / 2",
            gridRow: "2 / 3",
        },
        contextTypeContainer: {
            gridColumn: "2 / 3",
            gridRow: "2 / 3",
        },
        editorContainer: {
            gridColumn: "1 / 3",
            gridRow: "3 / 4",
        },
    })
);

export interface DashboardWidgetQueryDataSourceEditorProps {}

const buildDefaultQueryExpression = (type: QueryExpression["type"]): QueryExpression => {
    switch (type) {
        case "Calculate": {
            const result: CalculateExpression = {
                type: "Calculate",
                property: "",
                aggregation: "count",
            };
            return result;
        }
        case "CalculateTable": {
            const result: CalculateTableExpression = {
                type: "CalculateTable",
            };
            return result;
        }
        case "Summarize": {
            const result: SummarizeExpression = {
                type: "Summarize",
                groupings: [],
            };
            return result;
        }
    }
};

const useContextInfos = (sourceCollection?: DashboardWidgetAggregateDataSourceSourceCollection) => {
    const {contextTypes_Search} = useNimflow();
    const queryKey = useKey(contextTypes_Search, sourceCollection);
    const {data, isValidating, mutate, error} = useSWR(
        queryKey,
        async () => {
            if (sourceCollection !== "contexts") {
                return [];
            }
            const result = await contextTypes_Search({limit: 100});
            return sortBy(s => s.name, result);
        },
        {
            revalidateOnFocus: false,
        }
    );
    return useMemo(() => {
        return {
            contextTypeInfos: data ?? [],
            isValidating,
            error,
            revalidate: mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export const DashboardWidgetQueryDataSourceEditor: FC<DashboardWidgetQueryDataSourceEditorProps> = props => {
    const {t} = useTranslation();
    const {values: dataSource, setFieldValue} = useFormikContext<Partial<DashboardWidgetQueryDataSource>>();
    const classes = useStyles();
    const {contextTypeInfos} = useContextInfos(dataSource.sourceCollection);

    const queryTypeChange = useCallback(
        (ev: React.ChangeEvent<HTMLInputElement>) => {
            const type = ev.target.value as QueryExpression["type"];
            if (type !== dataSource.query?.type) {
                const query = buildDefaultQueryExpression(type);
                query.filter = dataSource.query?.filter;
                setFieldValue("query", query);
            }
        },
        [dataSource.query, setFieldValue]
    );

    useEffect(() => {
        if (dataSource.sourceCollection !== "contexts" && dataSource.contextTypeName) {
            setFieldValue("contextTypeName", undefined, true);
        }
    }, [dataSource.contextTypeName, dataSource.sourceCollection, setFieldValue]);

    return (
        <div className={classes.dialogContent}>
            <div className={classes.nameContainer}>
                <FormikTextField
                    name={`name`}
                    label={t("Name")}
                    autoFocus
                    InputLabelProps={{required: true}}
                    className={classes.textField}
                    fullWidth
                />
            </div>
            <div className={classes.queryTypeContainer}>
                <TextField
                    label={t("QueryType")}
                    InputLabelProps={{required: true}}
                    value={dataSource.query?.type}
                    onChange={queryTypeChange}
                    select
                    className={classes.textField}
                >
                    {["CalculateTable", "Calculate", "Summarize"].map(s => (
                        <MenuItem key={s} value={s}>
                            {t(`DashboardWidgetAggregateDataSource_queryType_${s}`)}
                        </MenuItem>
                    ))}
                </TextField>
            </div>
            <div className={classes.sourceCollectionContainer}>
                <FormikTextField
                    name={`sourceCollection`}
                    label={t("SourceCollection")}
                    InputLabelProps={{required: true}}
                    select
                    className={classes.textField}
                >
                    {dashboardWidgetAggregateDataSourceSourceCollections.map(s => (
                        <MenuItem key={s} value={s}>
                            {t(`DashboardWidgetAggregateDataSource_sourceCollection_${s}`)}
                        </MenuItem>
                    ))}
                </FormikTextField>
            </div>
            {dataSource.sourceCollection === "contexts" && (
                <div className={classes.contextTypeContainer}>
                    <FormikTextField
                        name={`contextTypeName`}
                        label={t("ContextType")}
                        select
                        className={classes.textField}
                    >
                        <MenuItem key={0} value={undefined}>&nbsp;</MenuItem>
                        {contextTypeInfos.map(s => (
                            <MenuItem key={s.name} value={s.name}>
                                {s.name}
                            </MenuItem>
                        ))}
                    </FormikTextField>
                </div>
            )}
            <div className={classes.editorContainer}>
                {dataSource.query?.type === "CalculateTable" && <CalculateTableQueryEditor />}
                {dataSource.query?.type === "Calculate" && <CalculateQueryEditor />}
                {dataSource.query?.type === "Summarize" && <SummarizeQueryEditor />}
            </div>
        </div>
    );
};
