import React, {FC, useState, useCallback} from "react";
import {useParams, useNavigate} from "react-router";
import {useAsync} from "react-use";
import Alert from "@material-ui/lab/Alert";
import axios from "axios";

import {useNimflow} from "../../api/nimflow-api-provider";
import {WaitSpinner} from "../../components/wait-spinner";
import {newServerContextTypeProvider, SimulationContextTypeProvider} from "./simulation-context-type-provider";
import {ContextSimulationContextProvider} from "./simulation-context";
import {ContextSimulationEditor} from "./context-simulation-editor";
import {ContextSimulation, useNimflowSimulation} from "../../api/nimflow-simulation-api";
import {ApiException, Context, SimulationEvent} from "../../api/nimflow-api";
import {readJsonFromBlob} from "../../utils/io-helpers";

export interface SimulationData {
    context: Context;
    contextState?: Record<string, any>;
    pendingEvents: SimulationEvent[];
}

export const ContextSimulationGeneratorFromContext: FC<{}> = () => {
    const navigate = useNavigate();
    const {blobUrl} = useParams();
    const [simulation, setSimulation] = useState<ContextSimulation>();
    const [contextTypeProvider, setContextTypeProvider] = useState<SimulationContextTypeProvider | undefined>();
    const {contextTypes_FindOne} = useNimflow();
    const {simulations_InsertOne} = useNimflowSimulation();

    const {loading, error} = useAsync(async () => {
        const dataUrl = atob(blobUrl);
        if (!dataUrl) {
            return;
        }
        let simData: SimulationData | undefined;
        try {
            const response = await axios.get(dataUrl, {responseType: "blob"});
            simData = await readJsonFromBlob(response.data);
        } catch (e) {
            navigate("../..");
        } finally {
            URL.revokeObjectURL(dataUrl);
        }
        if (simData) {
            const contextType = await contextTypes_FindOne({name: simData.context.typeName});
            const simulation: ContextSimulation = {
                name: `${simData.context.typeName}-${simData.context.id}`,
                contextTypeName: simData.context.typeName,
                pendingEvents: simData.pendingEvents,
                history: [],
            };
            const provider = await newServerContextTypeProvider(contextType.name, name => contextTypes_FindOne({name}));
            setSimulation(simulation);
            setContextTypeProvider(provider);
        }
    }, [blobUrl, navigate]);

    const handleOnSaveChanges = useCallback(
        async (value: ContextSimulation) => {
            try {
                const id = await simulations_InsertOne(value);
                navigate(`../../edit/${id}`, {replace: true});
            } catch (e: any) {
                let msg = e.toString();
                if (ApiException.isApiException(e)) {
                    msg = e.message;
                    if ("status" in e) {
                        msg += ` (status: ${e.status})`;
                    }
                }
                return [{message: msg}];
            }
            return [];
        },
        [navigate, simulations_InsertOne]
    );

    if (loading) {
        return <WaitSpinner />;
    }

    if (error) {
        return <Alert severity="error">{error.toString()}</Alert>;
    }

    if (!contextTypeProvider) {
        return null;
    }

    return (
        <ContextSimulationContextProvider contextTypeProvider={contextTypeProvider} simulation={simulation}>
            <ContextSimulationEditor onSaveChanges={handleOnSaveChanges} />
        </ContextSimulationContextProvider>
    );
};
