import {forwardRef, useMemo, FC, CSSProperties} from "react";
import {useTheme, Theme} from "@material-ui/core/styles";
import Table, {
    MaterialTableProps as TableProps,
    Icons,
    Options,
    Localization,
    Components,
    MTableEditField,
} from "@material-table/core";
import FormControl from "@material-ui/core/FormControl";
import FormGroup from "@material-ui/core/FormGroup";
import Checkbox from "@material-ui/core/Checkbox";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import AddIcon from "@material-ui/icons/Add";
import ArrowDownward from "@material-ui/icons/ArrowDownward";
import Check from "@material-ui/icons/Check";
import ChevronLeft from "@material-ui/icons/ChevronLeft";
import ChevronRight from "@material-ui/icons/ChevronRight";
import Clear from "@material-ui/icons/Clear";
import Delete from "@material-ui/icons/Delete";
import Edit from "@material-ui/icons/Edit";
import FilterList from "@material-ui/icons/FilterList";
import FirstPage from "@material-ui/icons/FirstPage";
import LastPage from "@material-ui/icons/LastPage";
import Remove from "@material-ui/icons/Remove";
import SaveAlt from "@material-ui/icons/SaveAlt";
import Search from "@material-ui/icons/Search";
import ViewColumn from "@material-ui/icons/ViewColumn";
import i18next from "i18next";
import {mergeDeepRight} from "ramda";

export interface MaterialTableProps<RowData extends object> extends TableProps<RowData> {}

// Set design system compatible icons
const defaultIcons: Icons = {
    Add: forwardRef((props, ref) => <AddIcon {...props} ref={ref} />),
    Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
    Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Delete: forwardRef((props, ref) => <Delete {...props} ref={ref} />),
    DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
    Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
    Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
    FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
    LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
    NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
    ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
    SortArrow: forwardRef((props, ref) => <ArrowDownward {...props} ref={ref} />),
    ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
    ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />),
};

const localizationTemplate: Localization = {
    body: {
        //dateTimePickerLocalization: {}, // The date-fns locale object applied to the datepickers
        emptyDataSourceMessage: "",
        filterRow: {
            filterTooltip: "",
        },
        editRow: {
            saveTooltip: "",
            cancelTooltip: "",
            deleteText: "",
        },
        addTooltip: "",
        deleteTooltip: "",
        editTooltip: "",
    },
    header: {
        actions: "",
    },
    grouping: {
        groupedBy: "",
        placeholder: "",
    },
    pagination: {
        firstTooltip: "",
        firstAriaLabel: "",
        previousTooltip: "",
        previousAriaLabel: "",
        nextTooltip: "",
        nextAriaLabel: "",
        labelDisplayedRows: "",
        labelRowsPerPage: "",
        lastTooltip: "",
        lastAriaLabel: "",
        labelRowsSelect: "",
    },
    toolbar: {
        addRemoveColumns: "",
        nRowsSelected: (rowCount: number) => "",
        showColumnsTitle: "",
        showColumnsAriaLabel: "",
        exportTitle: "",
        exportAriaLabel: "",
        exportCSVName: "",
        exportPDFName: "",
        searchTooltip: "",
        searchPlaceholder: "",
        searchAriaLabel: "",
        clearSearchAriaLabel: "",
    },
};

const buildDefaultLocalization = (obj: any, path: string[]): any => {
    const result: any = {};
    for (const [key, value] of Object.entries(obj)) {
        if (typeof value === "object") {
            path.push(key);
            result[key] = buildDefaultLocalization(value, path);
            path.pop();
        } else {
            result[key] = i18next.t(`${path.join(".")}.${key}`);
        }
    }
    return result;
};

const defaultLocalization = buildDefaultLocalization(localizationTemplate, ["MaterialTable"]);

const getDefaultOptions = <RowData extends object>(theme: Theme): Options<RowData> => ({
    actionsCellStyle: {
        color: theme.palette.text.secondary,
    },
    actionsColumnIndex: -1, // Action column at the end by default
    padding: "dense",
    pageSize: 10,
});

const checkboxCss: CSSProperties = {
    padding: 0,
    width: 24,
    marginLeft: 24,
};

const BooleanField: FC<any> = ({forwardedRef, ...props}) => {
    return (
        <FormControl error={Boolean(props.error)} ref={forwardedRef} component="fieldset">
            <FormGroup>
                <FormControlLabel
                    label=""
                    control={
                        <Checkbox
                            {...props}
                            value={String(props.value)}
                            checked={Boolean(props.value)}
                            onChange={event => props.onChange(event.target.checked)}
                            style={checkboxCss}
                            color="default"
                            inputProps={{
                                "aria-label": props.columnDef.title,
                            }}
                        />
                    }
                />
            </FormGroup>
            <FormHelperText>{props.helperText}</FormHelperText>
        </FormControl>
    );
};

const TableEditField: FC<any> = props => {
    if (props.columnDef.type === "boolean") {
        return <BooleanField {...props} />;
    }
    return <MTableEditField {...props} />;
};

const defaultComponents: Components = {
    EditField: props => <TableEditField {...props} />,
};

const MaterialTable = <RowData extends object>(props: MaterialTableProps<RowData>) => {
    const theme = useTheme();

    const icons = useMemo<Icons>(() => {
        if (props.icons) {
            return {
                ...defaultIcons,
                ...props.icons,
            };
        }
        return defaultIcons;
    }, [props.icons]);

    const localization = useMemo<Localization>(() => {
        if (props.localization) {
            return {
                ...defaultLocalization,
                ...props.localization,
            };
        }
        return defaultLocalization;
    }, [props.localization]);

    const options = useMemo(() => {
        let opts = getDefaultOptions<RowData>(theme);
        if (props.options) {
            opts = mergeDeepRight(opts, props.options) as Options<RowData>;
        }
        return opts;
    }, [theme, props.options]);

    const components = useMemo(() => {
        if (props.components) {
            return {
                ...defaultComponents,
                ...props.components,
            };
        }
        return defaultComponents;
    }, [props.components]);

    return <Table {...props} icons={icons} options={options} components={components} localization={localization} />;
};

export default MaterialTable;
