import React, {FC, useCallback} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import {useFormikContext} from "formik";
import MenuItem from "@material-ui/core/MenuItem";
import {useTranslation} from "react-i18next";

import {
    DashboardWidgetQueryDataSource,
    getScalarPropertyNamesFor,
    CalculateTableExpression,
} from "../../api/nimflow-dashboard-widgets-api";
import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {QueryFilterEditor} from "./query-filter-editor";
import {SortDirection} from "../../api/nimflow-api";
import TextField from "@material-ui/core/TextField";
import { FormikNumberField } from "../../components/formik-components/formik-number-field";

const headerContainerHeight = 60;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        textField: {
            maxWidth: "60ch",
            width: "100%"
        },
        main: {
            display: "grid",
            gridTemplateColumns: "1fr 1fr",
            gridTemplateRows: `${headerContainerHeight}px ${headerContainerHeight}px auto`,
            rowGap: `${theme.spacing(2)}px`,
            columnGap: `${theme.spacing(3)}px`,
            placeItems: "stretch",
            height: "100%",
            overflowY: "hidden",
        },
        sortPropertyContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
        },
        sortDirectionContainer: {
            gridColumn: "2 / 3",
            gridRow: "1 / 2",
        },
        skipContainer: {
            gridColumn: "1 / 2",
            gridRow: "2 / 3",
        },
        limitContainer: {
            gridColumn: "2 / 3",
            gridRow: "2 / 3",
        },
        filterContainer: {
            gridColumn: "1 / 3",
            gridRow: "3 / 4",
        },
    })
);

export interface CalculateTableQueryEditorProps {}

export const CalculateTableQueryEditor: FC<CalculateTableQueryEditorProps> = props => {
    const {t} = useTranslation();
    const {values: dataSource, setFieldValue} = useFormikContext<Partial<DashboardWidgetQueryDataSource>>();
    const classes = useStyles();
    const query = dataSource.query as CalculateTableExpression;

    const handleSortPropertyChange = useCallback(
        (ev: React.ChangeEvent<HTMLInputElement>) => {
            if (!ev.target.value) {
                setFieldValue("query.sortDirection", undefined);
            }
        },
        [setFieldValue]
    );

    if (!dataSource.sourceCollection) {
        return null;
    }

    return (
        <div className={classes.main}>
            <div className={classes.sortPropertyContainer}>
                <FormikTextField
                    name={`query.sortProperty`}
                    label={t("SortProperty")}
                    select
                    className={classes.textField}
                    onChange={handleSortPropertyChange}
                >
                    <MenuItem value={undefined}>&nbsp;</MenuItem>
                    {getScalarPropertyNamesFor(dataSource.sourceCollection).map(s => (
                        <MenuItem key={s} value={s}>
                            {s}
                        </MenuItem>
                    ))}
                </FormikTextField>
            </div>
            <div className={classes.sortDirectionContainer}>
                {!!query.sortProperty && (
                    <FormikTextField
                        name={`query.sortDirection`}
                        label={t("SortDirection")}
                        InputLabelProps={{required: true}}
                        select
                        className={classes.textField}
                    >
                        {[SortDirection.Ascending, SortDirection.Descending].map(s => (
                            <MenuItem key={s} value={s}>
                                {t(SortDirection[s])}
                            </MenuItem>
                        ))}
                    </FormikTextField>
                )}
                {!query.sortProperty && <TextField label={t("SortDirection")} disabled className={classes.textField} />}
            </div>
            <div className={classes.skipContainer}>
                <FormikNumberField
                    name={`query.skip`}
                    label={t("Skip")}
                    className={classes.textField}
                />
            </div>
            <div className={classes.limitContainer}>
                <FormikNumberField
                    name={`query.limit`}
                    label={t("Limit")}
                    className={classes.textField}
                />
            </div>
            <div className={classes.filterContainer}>
                <QueryFilterEditor />
            </div>
        </div>
    );
};
