import React, {createContext, FC, useState, useCallback, useEffect} from "react";
import useSWR from "swr";
import {isDefined} from "ts-is-present";
import {parseISO} from "date-fns";
import i18next from "i18next";
import {union, groupBy, mapObjIndexed} from "ramda";

import {useNimflow} from "../api/nimflow-api-provider";
import {useKey} from "../utils/use-key";
import {GetJobsStatusResult, GetJobsStatus} from "../api/nimflow-api";
import {Notification, useNotificationCommands, NotificationType} from "./notification-service";

export interface BackgroundJobMonitorService {
    cancelJob: (jobId: string) => Promise<any>;
    jobAdded: (jobId: string) => Promise<any>;
}

const stub = (): never => {
    throw new Error("You forgot to wrap your component in <BackgroundJobMonitorContextProvider>.");
};

const initialBackgroundJobMonitorContext: BackgroundJobMonitorService = {
    cancelJob: stub,
    jobAdded: stub,
};

const BackgroundJobMonitorContext = createContext<BackgroundJobMonitorService>(initialBackgroundJobMonitorContext);

interface BackgroundJobStatus {
    jobId: string;
    createdAt?: string;
    jobType?: string;
    status?: string;
    lastStatusUpdate?: string;
    notificationId?: string;
    cancelationRequested?: boolean;
}

interface JobProcessingState {
    jobStatuses: BackgroundJobStatus[];
    lastCheck?: string;
}

const updateJobStatuses = async (
    jobProcessingState: JobProcessingState,
    pendingJobIds: string[],
    getStatuses: (query: GetJobsStatus) => Promise<GetJobsStatusResult>
) => {
    const existingStatuses = jobProcessingState.jobStatuses;
    const lastCheck = jobProcessingState.lastCheck ?? "";
    const existingStatusesMap = mapObjIndexed(
        (s: BackgroundJobStatus[]) => s[0],
        groupBy(s => s.jobId, existingStatuses)
    );
    const existingJobIds = Object.keys(existingStatusesMap);
    const {status: newStatuses = [], statusCheckMoment} = await getStatuses({
        backgroundJobIds: union(existingJobIds, pendingJobIds),
    });
    const added: BackgroundJobStatus[] = [];
    const completed: BackgroundJobStatus[] = [];
    const updated: BackgroundJobStatus[] = [];
    const unchanged: BackgroundJobStatus[] = [];
    for (const newStatus of newStatuses) {
        const existing = existingStatusesMap[newStatus.id];
        if (!existing) {
            added.push({
                jobId: newStatus.id,
                createdAt: newStatus.createAt,
                jobType: newStatus.jobType,
                status: newStatus.status,
                lastStatusUpdate: newStatus.lastStatusUpdate,
            });
        } else {
            delete existingStatusesMap[newStatus.id];
            if (newStatus.lastStatusUpdate && lastCheck > newStatus.lastStatusUpdate) {
                unchanged.push(existing);
            } else if (
                newStatus.status === "Succeeded" ||
                newStatus.status === "Failed" ||
                newStatus.status === "Deleted"
            ) {
                completed.push({
                    ...existing,
                    status: newStatus.status,
                    lastStatusUpdate: newStatus.lastStatusUpdate,
                });
            } else {
                updated.push({
                    ...existing,
                    status: newStatus.status,
                    lastStatusUpdate: newStatus.lastStatusUpdate,
                });
            }
        }
    }
    return {
        added,
        updated,
        unchanged,
        completed,
        missing: Object.values(existingStatusesMap),
        statusCheckMoment,
    };
};

const jobStatusToNotificationTypeMap: Record<string, NotificationType> = {
    Awaiting: NotificationType.waiting,
    Deleted: NotificationType.canceled,
    Enqueued: NotificationType.waiting,
    Failed: NotificationType.error,
    Processing: NotificationType.inProgress,
    Scheduled: NotificationType.waiting,
    Succeeded: NotificationType.completed,
};

const createNotification = (status: BackgroundJobStatus): Notification => {
    const type = jobStatusToNotificationTypeMap[status.status ?? ""] ?? NotificationType.inProgress;
    return {
        type,
        title: status.jobType ? i18next.t(status.jobType) : "",
        description: "",
        moment: !!status.lastStatusUpdate ? parseISO(status.lastStatusUpdate) : new Date(),
    };
};

export const BackgroundJobMonitorContextProvider: FC<{}> = props => {
    const {children} = props;
    const {
        isHubSelected: isUnitSelected,
        backgroundJobs_CancelJob,
        backgroundJobs_GetJobsStatus,
        backgroundJobs_GetPendingJobs,
    } = useNimflow();
    const {addNotification, dismissNotification, replaceNotification} = useNotificationCommands();
    const [jobProcessingState, setJobProcessingState] = useState<JobProcessingState>({
        jobStatuses: [],
    });
    const [pendingJobIds, setPendingJobIds] = useState<string[]>([]);
    const key = useKey(backgroundJobs_GetPendingJobs, isUnitSelected);
    const {mutate} = useSWR(
        [key],
        async () => {
            if (!isUnitSelected) {
                if (jobProcessingState.jobStatuses.length > 0) {
                    setJobProcessingState({
                        jobStatuses: [],
                    });
                }
                return [];
            }
            const result = await backgroundJobs_GetPendingJobs({});
            setPendingJobIds(result.backgroundJobIds ?? []);
        },
        {
            refreshInterval:
                jobProcessingState.jobStatuses.length > 0 || !isUnitSelected ? 30000 : undefined,
        }
    );

    useEffect(() => {
        (async function perform() {
            if (!pendingJobIds || (pendingJobIds.length === 0 && jobProcessingState.jobStatuses.length === 0)) {
                return;
            }
            const {added, completed, missing, unchanged, updated, statusCheckMoment} = await updateJobStatuses(
                jobProcessingState,
                pendingJobIds,
                backgroundJobs_GetJobsStatus
            );
            for (const status of added) {
                const notification = createNotification(status);
                status.notificationId = addNotification(notification);
            }
            for (const status of updated) {
                const notification = createNotification(status);
                if (status.notificationId) {
                    replaceNotification(status.notificationId, notification);
                }
            }
            for (const status of completed) {
                const notification = createNotification(status);
                if (status.notificationId) {
                    replaceNotification(status.notificationId, notification, true);
                }
            }
            for (const notificationId of missing.map(s => s.notificationId).filter(isDefined)) {
                dismissNotification(notificationId);
            }
            if (updated.length > 0 || added.length > 0 || completed.length > 0) {
                setJobProcessingState({
                    jobStatuses: [...unchanged, ...updated, ...added],
                    lastCheck: statusCheckMoment,
                });
            }
        })().catch(() => {});
    }, [
        addNotification,
        backgroundJobs_GetJobsStatus,
        dismissNotification,
        jobProcessingState,
        pendingJobIds,
        replaceNotification,
    ]);

    const cancelJob = useCallback(
        async (jobId: string) => {
            const result = await backgroundJobs_CancelJob({jobId});
            if (!result.succeeded || !result.status) {
                return;
            }
            const statuses = jobProcessingState.jobStatuses.map(s => {
                if (s.jobId !== jobId) {
                    return s;
                }
                const newStatus = {
                    ...s,
                    cancelationRequested: true,
                    status: result.status!.status ?? "Deleted",
                    lastStatusUpdate: result.status!.lastStatusUpdate,
                };
                if (s.notificationId) {
                    const notification = createNotification(newStatus);
                    replaceNotification(s.notificationId, notification);
                }
                return newStatus;
            });
            setJobProcessingState({
                jobStatuses: statuses,
                lastCheck: jobProcessingState.lastCheck,
            });
        },
        [backgroundJobs_CancelJob, jobProcessingState.jobStatuses, jobProcessingState.lastCheck, replaceNotification]
    );

    const jobAdded = useCallback(
        (jobId: string) => {
            setTimeout(() => mutate(), 3000);
            return Promise.resolve();
        },
        [mutate]
    );

    const backgroundJobMonitorService: BackgroundJobMonitorService = {
        cancelJob,
        jobAdded,
    };

    return (
        <BackgroundJobMonitorContext.Provider value={backgroundJobMonitorService}>
            {children}
        </BackgroundJobMonitorContext.Provider>
    );
};

export const useBackgroundJobMonitor = () => React.useContext(BackgroundJobMonitorContext);
