import React, {FC, Fragment, useState, useCallback} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Divider from "@material-ui/core/Divider";

import {useContextSimulationContext} from "./simulation-context";
import {TaskViewer} from "./task-viewer";
import {TaskResponseEditor} from "./task-response-editor";
import {ContextTask, ResponseType} from "../../api/nimflow-api";

export interface TasksViewerProps {
    stateIndex: number;
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            width: "100%",
            //maxWidth: '36ch',
            backgroundColor: theme.palette.background.paper,
        },
        item: {
            border: `2px solid ${theme.palette.common.task}`,
            borderRadius: 2,
        },
        divider: {
            height: 5,
            background: "transparent",
        },
    })
);

interface ResponseState {
    task: ContextTask;
    responseType: ResponseType;
}

export const TaskListViewer: FC<TasksViewerProps> = props => {
    const {stateIndex} = props;
    const classes = useStyles();
    const {getState} = useContextSimulationContext();
    const {contextType, simulation} = getState();

    const [responseState, setResponseState] = useState<ResponseState>();

    const handleAddResponse = useCallback(
        (task: ContextTask, responseType: ResponseType) => {
            setResponseState({task, responseType});
        },
        [setResponseState]
    );

    const handleOnCloseDialog = useCallback(() => {
        setResponseState(undefined);
    }, [setResponseState]);

    const state = simulation.history[stateIndex];
    if (!state) {
        return null;
    }

    return (
        <>
            <List className={classes.root}>
                {state.tasks.map((s, i) => {
                    const taskType = contextType.tasks?.find(t => t.name === s.typeName);
                    return (
                        <Fragment key={i}>
                            <ListItem className={classes.item}>
                                <ListItemText
                                    primary={
                                        <TaskViewer task={s} taskType={taskType} onAddResponse={handleAddResponse} />
                                    }
                                />
                            </ListItem>
                            <Divider className={classes.divider} />
                        </Fragment>
                    );
                })}
            </List>
            {!!responseState && (
                <TaskResponseEditor
                    open={!!responseState}
                    onClose={handleOnCloseDialog}
                    stateIndex={stateIndex}
                    task={responseState.task}
                    responseType={responseState.responseType}
                />
            )}
        </>
    );
};
