import React, {FC, useCallback, useState} from "react";
import Button from "@material-ui/core/Button";
import {useTranslation} from "react-i18next";
import i18next from "i18next";
import {useFormikContext} from "formik";

import {
    DashboardWidgetVisualization,
    DashboardWidgetDefinition,
    DashboardWidgetMarkdownVisualization,
    DashboardWidgetVegaLiteVisualization,
} from "../../api/nimflow-dashboard-widgets-api";
import {SimplePopupSelector, SimplePopupSelectorOption} from "../../components/simple-popup-selector";
import {DashboardWidgetMarkdownVisualizationEditor} from "./dashboard-widget-markdown-visualization-editor";
import {DashboardWidgetVegaLiteVisualizationEditor} from "./dashboard-widget-vega-lite-visualization-editor";

export interface WidgetDefinitionVisualizationEditorProps {
    path: string;
    visible: boolean;
}

const visualizationTypes: SimplePopupSelectorOption<DashboardWidgetVisualization["type"]>[] = ([
    "markdown",
    "vega-lite",
] as const).map(s => ({
    value: s,
    description: i18next.t(`DashboardWidgetVisualization_type_${s}`),
}));

const instantiateNewVisualization = (
    type: DashboardWidgetVisualization["type"]
): Partial<DashboardWidgetVisualization> => {
    switch (type) {
        case "markdown": {
            const result: Partial<DashboardWidgetMarkdownVisualization> = {
                type,
                template: "",
            };
            return result;
        }
        case "vega-lite": {
            const result: Partial<DashboardWidgetVegaLiteVisualization> = {
                type,
                jsSpecification: "",
            };
            return result;
        }
    }
    return {type};
};

export const WidgetDefinitionVisualizationEditor: FC<WidgetDefinitionVisualizationEditorProps> = props => {
    const {visible} = props;
    const {t} = useTranslation();
    const {values: widgetDefinition, setFieldValue} = useFormikContext<Partial<DashboardWidgetDefinition>>();
    const [selectingType, setSelectingType] = useState(false);
    const visualizationType = widgetDefinition.visualization?.type;

    const handleChangeVisualization = useCallback(() => {
        setSelectingType(true);
    }, []);

    const handleVisualizationSelected = useCallback(
        (type: DashboardWidgetVisualization["type"]) => {
            setFieldValue("visualization", instantiateNewVisualization(type));
        },
        [setFieldValue]
    );

    const handleCloseSelector = useCallback(() => {
        setSelectingType(false);
    }, []);

    return (
        <div>
            {visualizationType === "markdown" && (
                <DashboardWidgetMarkdownVisualizationEditor
                    onChangeVisualization={handleChangeVisualization}
                    visible={visible}
                />
            )}
            {visualizationType === "vega-lite" && (
                <DashboardWidgetVegaLiteVisualizationEditor
                    onChangeVisualization={handleChangeVisualization}
                    visible={visible}
                />
            )}
            {!visualizationType && <Button onClick={handleChangeVisualization}>{t("CreateVisualization")}</Button>}
            {selectingType && (
                <SimplePopupSelector
                    options={visualizationTypes}
                    title={t("SelectingVisualizationType")}
                    open={selectingType}
                    onClose={handleCloseSelector}
                    onSelected={handleVisualizationSelected}
                />
            )}
        </div>
    );
};
