import React, {FC, useCallback, useMemo, useState} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import * as yup from "yup";
import {useForm, useFormContext, FormProvider} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import i18next from "i18next";
import {useTranslation} from "react-i18next";
import {DevTool} from "@hookform/devtools";

import {ActionType, ContextType} from "../../api/nimflow-api";
import {ActionTypeSchema, jsCommonScriptKey, jsonSchemaScriptKey} from "../../models/context-type-schema";
import {ActionTypeEditor} from "./action-type-editor";
import {FormErrorViewer} from "../../components/form-error-viewer";
import {useEditionScope, EditionScopeProvider} from "../../components/edition-scope";
import {defaultContextJsonSchemaString} from "../../models/default-values";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            backgroundColor: theme.palette.background.default,
        },
    })
);
export interface ActionTypeEditorDialogProps {
    open: boolean;
    defaultValue?: Partial<ActionType>;
    definitionJsonSchema: string | undefined;
    onChange: (value: ActionType, definitionJsonSchema: string | undefined) => void;
    onClose: () => void;
}

const getExtendedSchema = (usedNames?: string[]) => {
    if (!usedNames) {
        return ActionTypeSchema;
    }
    return ActionTypeSchema.shape({
        name: ActionTypeSchema.fields!.name.concat(
            yup
                .string()
                .defined()
                .test({
                    message: ({value}) => i18next.t("ActionTypeNameAlreadyExists", {name: value}),
                    test: function (value: any) {
                        return !usedNames.includes(value);
                    },
                })
        ),
        payloadSchemaDefinitionName: yup.string() /*.jsonSchemaDefinitionName(jsonSchemaScriptKey)*/, // Guaranteed by editor
    });
};

export const ActionTypeEditorDialog: FC<ActionTypeEditorDialogProps> = props => {
    const {open, defaultValue, definitionJsonSchema: initialDefinitionJsonSchema, onChange, onClose} = props;
    const classes = useStyles();
    const contextTypeFormMethods = useFormContext<ContextType>();
    const schema = useMemo(() => {
        const names = contextTypeFormMethods
            .getValues()
            .actions?.map(s => s.name)
            .filter(s => s !== defaultValue?.name);
        return getExtendedSchema(names);
    }, [contextTypeFormMethods, defaultValue]);
    const [definitionName, setDefinitionName] = useState("");
    const [definitionJsonSchema, setDefinitionJsonSchema] = useState(initialDefinitionJsonSchema);
    const formMethods = useForm({
        mode: "onChange",
        reValidateMode: "onChange",
        defaultValues: defaultValue ?? {},
        context: {
            [jsCommonScriptKey]: contextTypeFormMethods.getValues("jsCommon"),
            [jsonSchemaScriptKey]: () => {
                let schema = contextTypeFormMethods.getValues("jsonSchema");
                const schemaObj = JSON.parse(schema ?? defaultContextJsonSchemaString);
                if (definitionJsonSchema !== initialDefinitionJsonSchema) {
                    if (definitionJsonSchema) {
                        schemaObj.definitions[definitionName] = JSON.parse(definitionJsonSchema);
                    } else {
                        delete schemaObj.definitions[definitionName];
                    }
                }
                return schemaObj;
            },
        },
        resolver: yupResolver(schema, {
            strict: true,
            abortEarly: false,
        }),
    });
    const {control, handleSubmit, errors, getValues, setValue} = formMethods;
    const {t} = useTranslation();
    const editionScope = useEditionScope();

    const onSubmit = useCallback(
        (value: Partial<ActionType>) => {
            onChange?.(value as ActionType, definitionJsonSchema);
            onClose?.();
        },
        [definitionJsonSchema, onChange, onClose]
    );

    const handleDefinitionJsonSchemaChange = useCallback(
        (value: string | undefined, definitionName: string) => {
            const newDefName = value ? definitionName : undefined;
            if (getValues("payloadSchemaDefinitionName") !== newDefName) {
                setValue(`payloadSchemaDefinitionName`, newDefName, {shouldDirty: true});
            }
            setDefinitionJsonSchema(value);
            setDefinitionName(newDefName ?? "");
        },
        [getValues, setValue]
    );

    return (
        <Dialog
            open={open}
            fullWidth
            maxWidth="md"
            classes={{paper: classes.paper}}
        >
            <FormProvider {...formMethods}>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <EditionScopeProvider {...editionScope}>
                        <DialogTitle>{t("EditingActionType")}</DialogTitle>
                        <DialogContent style={{minWidth: "500px"}}>
                            <ActionTypeEditor
                                definitionJsonSchema={definitionJsonSchema}
                                onDefinitionJsonSchemaChange={handleDefinitionJsonSchemaChange}
                            />
                            <FormErrorViewer errors={errors} />
                        </DialogContent>
                        <DialogActions>
                            <Button type="submit" color="primary" disabled={!editionScope.canAccept}>
                                {t("Accept")}
                            </Button>
                            <Button onClick={onClose} color="primary">
                                {t("Cancel")}
                            </Button>
                        </DialogActions>
                    </EditionScopeProvider>
                </form>
            </FormProvider>
            <DevTool control={control} />
        </Dialog>
    );
};
