import React, {FC, useCallback, useMemo, useRef} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Grid from "@material-ui/core/Grid";
import MenuItem from "@material-ui/core/MenuItem";
import {useTranslation} from "react-i18next";
import i18next from "i18next";
import {FormikProps, FormikHelpers, Formik, Form, useFormikContext} from "formik";

import {
    DashboardWidgetAggregateDataSourceSourceCollection,
    getScalarPropertyNamesFor,
    SummarizeExpressionGrouping,
    getScalarPropertyTypeFor,
    timeGranularities,
} from "../../api/nimflow-dashboard-widgets-api";
import {validateAndSubmit} from "../../utils/formikHelpers";
import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {contextListPropertyNames} from "../../api/nimflow-api-extensions";
import {DataSourcePropertySelector} from "./datasource-property-selector";
import {getSummarizeExpressionGrouping} from "./widget-definition-schema";

const titleHeight = 64;
const footerHeight = 52;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            backgroundColor: theme.palette.background.default,
            height: "100%",
            width: "100%",
            //minHeight: "90vh",
            maxHeight: "40vh",
            "& form": {
                height: "100%",
            },
        },
        dialogTitle: {
            height: titleHeight,
        },
        dialogContent: {
            height: `calc(100% - ${titleHeight}px - ${footerHeight}px)`,
            width: "100%",
        },
        dialogFooter: {
            height: footerHeight,
        },
    })
);

interface EditorProps {
    collection: DashboardWidgetAggregateDataSourceSourceCollection;
    contextTypeName?: string;
}

const Editor: FC<EditorProps> = props => {
    const {collection, contextTypeName} = props;
    const {t} = useTranslation();
    const {values: grouping, setFieldValue} = useFormikContext<SummarizeExpressionGrouping>();

    const valueType = useMemo(() => {
        return getScalarPropertyTypeFor(collection, grouping.property);
    }, [collection, grouping.property]);

    const propertyNames = useMemo(() => {
        let result = getScalarPropertyNamesFor(collection);
        if (collection === "contexts" && contextTypeName) {
            result = [...result, ...contextListPropertyNames];
        }
        return result;
    }, [collection, contextTypeName]);

    const propertyChanged = useCallback(() => {
        setFieldValue("timeGranularity", undefined);
    }, [setFieldValue]);

    return (
        <Grid container spacing={1}>
            <Grid item xs={12}>
                <DataSourcePropertySelector
                    name="property"
                    valuePath="value"
                    sourceCollection={collection}
                    contextTypeName={contextTypeName}
                    label={t("Property")}
                    fullWidth
                    InputLabelProps={{required: true}}
                    propertyNames={propertyNames}
                    required
                    onChange={propertyChanged}
                />
            </Grid>
            {valueType === "dateTime" && (
                <Grid item xs={12}>
                    <FormikTextField name="timeGranularity" label={t("TimeGranularity")} select fullWidth>
                        {timeGranularities.map(s => (
                            <MenuItem key={s} value={s}>
                                {s}
                            </MenuItem>
                        ))}
                    </FormikTextField>
                </Grid>
            )}
        </Grid>
    );
};

export interface SummarizeGroupingEditorDialogProps {
    open: boolean;
    defaultValue: SummarizeExpressionGrouping;
    collection: DashboardWidgetAggregateDataSourceSourceCollection;
    contextTypeName?: string;
    existingProperties?: string[];
    onChange?: (value: SummarizeExpressionGrouping) => void;
    onClose: () => void;
}

const getExtendedSchema = (
    collection: DashboardWidgetAggregateDataSourceSourceCollection,
    usedProperties?: string[]
) => {
    let schema = getSummarizeExpressionGrouping(collection);
    if (!usedProperties || usedProperties.length === 0) {
        return schema;
    }
    return schema.test({
        test: function (value: any) {
            let prop = value.property;
            if (value.value) {
                prop += "." + value.value;
            }
            if (usedProperties.includes(prop)) {
                this.createError({
                    path: "property",
                    message: i18next.t("SummarizeExpressionGroupingPropertyAlreadyUsed", {name: prop}),
                });
            }
            return true;
        },
    });
};

export const SummarizeGroupingEditorDialog: FC<SummarizeGroupingEditorDialogProps> = props => {
    const {open, defaultValue, collection, contextTypeName, existingProperties, onChange, onClose} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const schema = useMemo(() => {
        let thisProp = defaultValue?.property;
        if (thisProp && defaultValue.value) {
            thisProp += "." + defaultValue.value;
        }
        const names = existingProperties?.map(s => s).filter(s => s !== thisProp);
        return getExtendedSchema(collection, names);
    }, [defaultValue?.property, defaultValue.value, existingProperties, collection]);
    const formRef = useRef<FormikProps<SummarizeExpressionGrouping> | null>();
    const submitHandlerRef = useRef<(values: SummarizeExpressionGrouping) => void>();

    const handleAccept = useCallback(() => {
        const form = formRef.current;
        if (form && onChange) {
            submitHandlerRef.current = (data: SummarizeExpressionGrouping) => {
                onChange?.(data);
                onClose?.();
            };
            validateAndSubmit(form);
        }
    }, [onChange, onClose]);

    const handleSubmit = useCallback(
        async (values: SummarizeExpressionGrouping, actions: FormikHelpers<SummarizeExpressionGrouping>) => {
            try {
                if (submitHandlerRef.current) {
                    submitHandlerRef.current(values);
                    submitHandlerRef.current = undefined;
                }
            } finally {
                actions.setSubmitting(false);
            }
        },
        []
    );

    return (
        <Dialog
            open={open}
            fullWidth
            maxWidth="sm"
            classes={{paper: classes.paper}}
        >
            <Formik<SummarizeExpressionGrouping>
                innerRef={s => (formRef.current = s)}
                initialValues={defaultValue}
                onSubmit={handleSubmit}
                validationSchema={schema}
                validateOnChange={true}
                validateOnBlur={true}
            >
                <Form autoComplete="off">
                    <DialogTitle className={classes.dialogTitle}>{t("EditingGrouping")}</DialogTitle>
                    <DialogContent className={classes.dialogContent}>
                        <Editor collection={collection} contextTypeName={contextTypeName}/>
                    </DialogContent>
                    <DialogActions className={classes.dialogFooter}>
                        <Button onClick={handleAccept} color="primary">
                            {t("Accept")}
                        </Button>
                        <Button onClick={onClose} color="primary">
                            {t("Cancel")}
                        </Button>
                    </DialogActions>
                </Form>
            </Formik>
        </Dialog>
    );
};
