import React, {FC, useCallback, useMemo, useState, useRef} from "react";
import {useNavigate} from "react-router";
import * as yup from "yup";
import {useTranslation} from "react-i18next";
import i18next from "i18next";

import {
    useNimflowDashboardWidgets,
    DashboardWidgetDefinition,
    DashboardWidgetDefOrJsSpec,
    DashboardWidgetJsSpec,
} from "../../api/nimflow-dashboard-widgets-api";
import {resolveDashboardWidgetDefOrJsSpecSchema} from "./widget-definition-schema";
import {SimplePopupSelector, SimplePopupSelectorOption} from "../../components/simple-popup-selector";
import {WidgetDefOrJsSpecEditor} from "./widget-def-or-js-spec-editor";

const widgetTypes: SimplePopupSelectorOption<string>[] = (["Definition", "JsSpec"] as const).map(s => ({
    value: s,
    description: i18next.t(`WidgetType_${s}`),
}));

interface InnerDashboardWidgetDefOrJsSpecCreatorProps {
    defaultValue: DashboardWidgetDefOrJsSpec;
    onChange: (value: DashboardWidgetDefOrJsSpec) => void;
}

const getDefaultDashboardWidgetDefinition = () => {
    const result: DashboardWidgetDefinition = {
        name: "",
        title: "",
        parameters: [],
        dataSources: [],
        visualization: {
            type: "markdown",
            template: "",
        },
    };
    return result;
};

const defaultWidgetJsSpec = `parameters = {

};

dataSources = {

};

visualization = {
    type: "markdown",
    template: \`
\`
}`;

const getDefaultDashboardWidgetJsSpec = () => {
    const result: DashboardWidgetJsSpec = {
        name: "",
        title: "",
        widgetJsSpec: defaultWidgetJsSpec,
    };
    return result;
};

const InnerDashboardWidgetDefOrJsSpecCreator: FC<InnerDashboardWidgetDefOrJsSpecCreatorProps> = props => {
    const {defaultValue, onChange} = props;
    const {t} = useTranslation();
    const {dashboardWidgets_GetInfos} = useNimflowDashboardWidgets();
    const [widgetJsSpec, setWidgetJsSpec] = useState<DashboardWidgetDefOrJsSpec>(defaultValue);

    const handleOnChange = useCallback(
        async (value: DashboardWidgetDefOrJsSpec) => {
            setWidgetJsSpec(value);
            onChange?.(value);
            return true;
        },
        [onChange]
    );

    const schema = useMemo(() => {
        const schema = resolveDashboardWidgetDefOrJsSpecSchema(defaultValue);
        return schema.shape({
            name: schema.fields!.name.concat(
                yup
                    .string()
                    .defined()
                    .test({
                        message: ({value}) => t("WidgetDefinitionNameAlreadyExists", {name: value}),
                        test: async function (value: any) {
                            if (typeof value !== "string") {
                                return true;
                            }
                            const infos = await dashboardWidgets_GetInfos({});
                            return !infos.some(s => s.name.toLowerCase() === value.toLowerCase());
                        },
                    })
            ),
        });
    }, [dashboardWidgets_GetInfos, defaultValue, t]);

    return <WidgetDefOrJsSpecEditor value={widgetJsSpec} schema={schema} onApplyChanges={handleOnChange} />;
};

export const DashboardWidgetDefOrJsSpecCreator: FC<{}> = () => {
    const navigate = useNavigate();
    const {t} = useTranslation();
    const {dashboardWidgets_InsertOne} = useNimflowDashboardWidgets();
    const typeSelected = useRef(false);
    const [selectedType, setSelectedType] = useState<string>();

    const handleOnChange = useCallback(
        async (value: DashboardWidgetDefOrJsSpec) => {
            const id = await dashboardWidgets_InsertOne(value);
            value.id = id;
            navigate(`../edit/${id}`, {replace: true});
            return true;
        },
        [navigate, dashboardWidgets_InsertOne]
    );

    const handleOnClose = useCallback(() => {
        if (!typeSelected.current) {
            navigate(-1);
        }
    }, [navigate]);

    const handleOnSelect = useCallback((value: string) => {
        typeSelected.current = true;
        setSelectedType(value);
    }, []);

    const defaultValue = useMemo(() => {
        return selectedType === "JsSpec" ? getDefaultDashboardWidgetJsSpec() : getDefaultDashboardWidgetDefinition();
    }, [selectedType]);

    if (!selectedType) {
        return (
            <SimplePopupSelector
                options={widgetTypes}
                title={t("SelectingWidgetType")}
                open={!selectedType}
                onClose={handleOnClose}
                onSelected={handleOnSelect}
            />
        );
    }

    return <InnerDashboardWidgetDefOrJsSpecCreator defaultValue={defaultValue} onChange={handleOnChange} />;
};
