import * as yup from "yup";
import type {SchemaOf} from "yup";
import i18next from "i18next";
import {mapObjIndexed, uniq, groupBy} from "ramda";
import {ObjectShape, AnyObject, TypeOfShape, AssertsShape, ObjectSchemaSpec} from "yup/lib/object";
import {Callback, InternalOptions, Maybe, Optionals} from "yup/lib/types";

import {map} from "../utils/map-schema";
import {detectCycle} from "../utils/graph-algorithms";
import {ContextType, Metric, Milestone, Selector, Tag, TaskType, ValidatorEntry} from "../api/nimflow-api";
import {buildDefinitionSchema} from "../utils/json-schema";
import {
    ActionTypeSettings,
    ContextTypeSettings,
    IconSpec,
    ResponseTypeSettings,
    TaskTypeSettings,
} from "../api/nimflow-api-extensions";

/*eslint-disable no-template-curly-in-string*/

//type InferDefinedType<T> = T extends yup.Schema<infer U> ? yup.InferType<yup.Schema<Exclude<U, undefined>>> : never;

export const jsCommonScriptKey = "jsCommon";
export const jsonSchemaScriptKey = "jsonSchema";
export const uiSchemaJsonSchemaKey = "uiSchemaJsonSchema";
export const uiSchemaCommonKey = "uiSchemaCommon";

class ObjectWithPayloadUiSchema<
    TShape extends ObjectShape,
    TContext extends AnyObject = AnyObject,
    TIn extends Maybe<TypeOfShape<TShape>> = TypeOfShape<TShape>,
    TOut extends Maybe<AssertsShape<TShape>> = AssertsShape<TShape> | Optionals<TIn>
> extends yup.ObjectSchema<TShape, TContext, TIn, TOut> {
    constructor(private definitionNameKey: string, private contextKey: string, private jsonSchemaKey: string) {
        super();
    }

    protected _validate(_value: any, options: InternalOptions<TContext> = {}, callback: Callback): void {
        let opts = options;
        let jsonSchema = opts.context?.[this.jsonSchemaKey];
        const definitionName = _value[this.definitionNameKey];
        if (jsonSchema && typeof definitionName === "string") {
            if (typeof jsonSchema === "function") {
                jsonSchema = jsonSchema();
            }
            if (typeof jsonSchema === "string") {
                jsonSchema = JSON.parse(jsonSchema);
            }
            const defSchema = buildDefinitionSchema(jsonSchema, definitionName);
            const context: TContext = {
                ...opts?.context,
                [this.contextKey]: defSchema,
            } as any;
            opts = {
                ...opts,
                context,
            };
        }
        return super._validate(_value, opts, callback);
    }

    public clone(spec?: ObjectSchemaSpec): this {
        const next = super.clone(spec);
        next.definitionNameKey = this.definitionNameKey;
        next.contextKey = this.contextKey;
        next.jsonSchemaKey = this.jsonSchemaKey;
        return next;
    }
}

export const objectWithPayloadUiSchema = <
    TShape extends ObjectShape,
    TContext extends AnyObject = AnyObject,
    TIn extends Maybe<TypeOfShape<TShape>> = TypeOfShape<TShape>,
    TOut extends Maybe<AssertsShape<TShape>> = AssertsShape<TShape> | Optionals<TIn>
>(
    definitionNameKey: string,
    contextKey: string,
    jsonSchemaKey: string
) => new ObjectWithPayloadUiSchema<TShape, TContext, TIn, TOut>(definitionNameKey, contextKey, jsonSchemaKey);

export const ValidatorEntrySchema = yup.object().shape({
    /**
     * Javascript expression with these variables already bound:
     *     -state: a reference to the state property of current context.
     *     -payload: the payload passed with the action or response.
     *     -task: the task owning the current response (undefined when used in actions).
     * Must evaluate to a boolean value indicating whether the action/response is applicable in the current context.
     */
    jsValidator: yup
        .string()
        .required()
        .jsExpression({paramNames: ["state", "payload", "task"], scriptContextKey: jsCommonScriptKey}),
    /**
     * Handlebars template with the 'state' and 'payload' variables defined in the environment
     */
    errorMessageTemplate: yup.string().required(),
}) as SchemaOf<ValidatorEntry>;

export const ActionTypeSettingsSchema = yup.object().shape({
    uiSchema: yup.string().uiSchema(uiSchemaJsonSchemaKey),
}) as SchemaOf<ActionTypeSettings>;

export const ActionTypeJsSetterSchema = map(
    yup.string().required().identifier(),
    yup
        .string()
        .required()
        .jsExpression({paramNames: ["state", "payload"], scriptContextKey: jsCommonScriptKey})
);

export const ActionTypeSchema = objectWithPayloadUiSchema(
    "payloadSchemaDefinitionName",
    uiSchemaJsonSchemaKey,
    jsonSchemaScriptKey
) /*yup.object()*/
    .shape({
        name: yup.string().required().identifier().min(3).max(50),
        payloadSchemaDefinitionName: yup.string().jsonSchemaDefinitionName(jsonSchemaScriptKey),
        validators: yup.array().of(ValidatorEntrySchema.required()),
        /**
         * Javascript expression with these variables already bound:
         *     -state: a reference to the state property of current context.
         *     -payload: the payload passed with the action.
         * Must evaluate to an operation with arguments to apply to the state key in the current context designated by the key of each entry.
         */
        jsSetters: ActionTypeJsSetterSchema,
        settings: ActionTypeSettingsSchema,
    });

export const IconSpecSchema = yup.object().shape({
    name: yup.string().required(),
    family: yup.string(),
}) as SchemaOf<IconSpec>;

export const ResponseTypeSettingsSchema = yup.object().shape({
    uiSchema: yup.string().uiSchema(uiSchemaJsonSchemaKey),
    commandText: yup.string(),
    actionInProgressText: yup.string(),
    actionCompletedText: yup.string(),
    icon: yup.mixed(), // IconSpecSchema.optional(),
}) as SchemaOf<ResponseTypeSettings>;

export const ResponseTypeJsSettersSchema = map(
    yup.string().required().identifier(),
    yup
        .string()
        .required()
        .jsExpression({paramNames: ["state", "payload", "task"], scriptContextKey: jsCommonScriptKey})
).required();

export const ResponseTypeSchema = objectWithPayloadUiSchema(
    "payloadSchemaDefinitionName",
    uiSchemaJsonSchemaKey,
    jsonSchemaScriptKey
) /*yup.object()*/
    .shape({
        name: yup.string().required().identifier().min(3),
        payloadSchemaDefinitionName: yup.string().jsonSchemaDefinitionName(jsonSchemaScriptKey),
        validators: yup.array().of(ValidatorEntrySchema.required()),
        /**
         * Javascript expression with these variables already bound:
         *     -state: a reference to the state property of current context.
         *     -payload: the payload passed with the response.
         *     -task: a reference to the task instance defining this response.
         * Must evaluate to the value to assign to the state key in the current context designated by the key of each entry.
         */
        jsSetters: ResponseTypeJsSettersSchema,
        settings: ResponseTypeSettingsSchema,
        /**
         * Javascript expression with these variables already bound:
         *     -task: a reference to the current task
         *     -state: a reference to the state property of current context (after setter where applied).
         * Must evaluate to a object representing an initial payload.
         */
        jsInitialPayloadBuilder: yup
            .string()
            .jsExpression({paramNames: ["state", "task"], scriptContextKey: jsCommonScriptKey}),
    });

export const TaskTypeSettingsSchema = yup.object() as SchemaOf<TaskTypeSettings>;

export const TaskDataJsSettersSchema = map(
    yup.string().required().identifier(),
    yup
        .string()
        .required()
        .jsExpression({paramNames: ["state"], scriptContextKey: jsCommonScriptKey})
).required();

export const TaskTypeSchema = yup.object().shape({
    name: yup.string().required().identifier().min(3),
    /**
     * Handlebars template using the context state as environment
     */
    titleTemplate: yup.string().required(),
    /**
     * Javascript expression with these variables already bound:
     *     -state: a reference to the state property of current context.
     * Must evaluate to a boolean value indicating whether the task should be instantiated.
     */
    jsCondition: yup.string().jsExpression({paramNames: ["state"], scriptContextKey: jsCommonScriptKey}),
    /**
     * Javascript expression with these variables already bound:
     *     -state: a reference to the state property of current context.
     * Must evaluate to the value to assign to the data key in each instantiated Task designated by the key of each entry.
     */
    jsTaskDataSetters: TaskDataJsSettersSchema,
    /**
     * Javascript expression with these variables already bound:
     *     -task: a reference to the current task which assignedTo property is being assigned
     *     -state: a reference to the state property of current context (after setter where applied).
     * Must evaluate to a string representing the new user's email.
     */
    jsAssignedTo: yup.string().jsExpression({paramNames: ["state", "task"], scriptContextKey: jsCommonScriptKey}),
    /**
     * Javascript expression with these variables already bound:
     *     -task: a reference to the current task which dueDate property is being assigned
     *     -state: a reference to the state property of current context (after setter where applied).
     * Must evaluate to a Date or a string in ISO 8601 format which represent the new dueDate for the task.
     */
    jsDueDate: yup.string().jsExpression({paramNames: ["state", "task"], scriptContextKey: jsCommonScriptKey}),
    /**
     * Javascript expression with these variables already bound:
     *     -task: a reference to the current task which importance property is being assigned
     *     -state: a reference to the state property of current context (after setter where applied).
     * Must evaluate either to one of the strings: "low", "normal" or "high", or one of the numbers: -1, 0 or 1.
     */
     jsImportance: yup.string().jsExpression({paramNames: ["state", "task"], scriptContextKey: jsCommonScriptKey}),
     validResponses: yup
        .array()
        .of(ResponseTypeSchema.required())
        .defined()
        .uniqueBy(
            s => s.name,
            ({duplicates}) => i18next.t("DuplicateResponseTypeNames", {names: duplicates.join(", ")})
        ),
    settings: TaskTypeSettingsSchema,
}) as SchemaOf<TaskType>;

export const MilestoneSchema = yup.object().shape({
    name: yup.string().required(),
    final: yup.boolean(),
}) as SchemaOf<Milestone>;

export const TagSchema = yup.object().shape({
    name: yup.string().required(),
}) as SchemaOf<Tag>;

export const MetricSchema = yup.object().shape({
    name: yup.string().required(),
}) as SchemaOf<Metric>;

export const ContextTypeSettingsSchema = yup.object().shape({
    uiSchemaCommon: yup.string().uiSchema(uiSchemaCommonKey),
}) as SchemaOf<ContextTypeSettings>;

export const SelectorSchema = yup.object().shape({
    key: yup.string().required().identifier(),
    /**
     * A Javascript expression with these variables already bound:
     *     -state: a reference to the state property current context (with the declared selector dependencies guaranteed to be present).
     * This script evaluate to a JSON value which will be assigned to the key specified of the new context state.
     */
    jsExpression: yup
        .string()
        .required()
        .jsExpression({paramNames: ["state"], scriptContextKey: jsCommonScriptKey}),
    dependencies: yup.array().of(yup.string()),
}) as SchemaOf<Selector>;

export const ContextTypeSchema = yup.object().shape({
    version: yup.number(),
    name: yup.string().required().identifier().min(3).max(50),
    description: yup.string(),
    actions: yup
        .array()
        .of(ActionTypeSchema.required())
        .defined()
        .uniqueBy(
            s => s.name,
            ({duplicates}) => i18next.t("DuplicateActionNames", {names: duplicates.join(", ")})
        ),
    jsInitializers: map(yup.string().required().identifier(), yup.string().required()),
    selectors: yup
        .array()
        .of(SelectorSchema)
        .test({
            message: () => i18next.t("SelectorCircularDependencyFound"),
            test: function (val: any) {
                const value = val as Selector[];
                if (!value || value.length === 0) {
                    return true;
                }
                const selectorMap = mapObjIndexed(
                    (s: Selector[]) => uniq(s.flatMap(sel => sel.dependencies ?? [])),
                    groupBy(
                        s => s.key!,
                        value.filter(s => s.key)
                    )
                );
                for (const key of Object.keys(selectorMap)) {
                    const cycle = detectCycle(key, {
                        getNeighbors: v => selectorMap[v] ?? [],
                    });
                    if (cycle) {
                        return this.createError({
                            path: this.path,
                            message: i18next.t("SelectorCircularDependencyFound", {cycle: cycle.join(" -> ")}),
                        });
                    }
                }
                return true;
            },
        }),
    tasks: yup
        .array()
        .of(TaskTypeSchema.required())
        .defined()
        .uniqueBy(
            s => s.name,
            ({duplicates}) => i18next.t("DuplicateTaskTypeNames", {names: duplicates.join(", ")})
        ),
    initialActions: yup
        .array()
        .of(yup.string().required())
        .defined()
        //.min(1, () => i18next.t("AtLeastOneInitialActionRequired"))
        .test({
            message: () => i18next.t("InvalidInitialActions"),
            test: function (val: any) {
                const value = val as string[];
                const actions = this.parent?.actions;
                if (Array.isArray(actions)) {
                    for (const name of value) {
                        if (!actions.find(s => s.name === name)) {
                            return this.createError({
                                path: this.path,
                                message: i18next.t("InvalidActionName", {actionName: value}),
                            });
                        }
                    }
                }
                return true;
            },
        }),
    milestones: yup.array().of(MilestoneSchema.required()),
    // .test({
    //     message: () => i18next.t("AtLeastOneFinalMilestoneRequired"),
    //     test: function (value: Milestone[]) {
    //         if (!value || value.length === 0) {
    //             return false;
    //         }
    //         return value.some(s => s.final);
    //     },
    // }),
    tags: yup.array().of(TagSchema),
    metrics: yup.array().of(MetricSchema),
    jsCommon: yup.string().jsModule(),
    jsonSchema: yup.string().jsonSchema(),
    settings: ContextTypeSettingsSchema,
}) as SchemaOf<ContextType>;
