import * as yup from "yup";
import i18next from "i18next";
import type {SchemaOf} from "yup";

import {
    getVariableNames,
    SelectionDashboardParameterDataSource,
    DashboardParameterValue,
    dashboardParameterSelectionTypes,
    SelectionDashboardParameter,
    TimeRangeDashboardParameter,
    FreeTextDashboardParameter,
    dashboardParameterDataTypes,
    DashboardParameter,
    StaticSelectionDashboardParameterDataSource,
    FunctionSelectionDashboardParameterDataSource,
    DashboardWidgetInstantiation,
    DashboardDefinition,
    DashboardParameterState,
    BaseDashboardParameter,
    DashboardAutoRefresh,
    autoRefreshIntervals,
} from "../../api/nimflow-dashboard-api";
import {map} from "../../utils/map-schema";
import {freeVarDefs} from "./free-variable-defs";

export const DashboardParameterValueSchema = yup.object().shape({
    value: yup.mixed().required(),
    displayText: yup.string(),
}) as SchemaOf<DashboardParameterValue>;

export const DashboardParameterStateSchema = yup.object().shape({
    selectAll: yup.boolean().required(),
    values: yup
        .array()
        .of(DashboardParameterValueSchema.required())
        .when("selectAll", (selectAll: boolean, schema: any) => (selectAll ? schema : schema.required())),
}) as SchemaOf<DashboardParameterState>;

export const BaseDashboardParameterSchema = yup.object().shape({
    id: yup.string(),
    displayName: yup.string().required(),
    selectionType: yup
        .string()
        .oneOf([...dashboardParameterSelectionTypes])
        .required(),
    dataType: yup
        .string()
        .oneOf([...dashboardParameterDataTypes])
        .required(),
    defaultValue: DashboardParameterStateSchema.required(),
}) as SchemaOf<BaseDashboardParameter>;

export const StaticSelectionDashboardParameterDataSourceSchema = yup.object().shape({
    type: yup.string().oneOf(["static"]).required(),
    options: yup.array().of(DashboardParameterValueSchema.required()).required(),
}) as SchemaOf<StaticSelectionDashboardParameterDataSource>;

export const FunctionSelectionDashboardParameterDataSourceSchema = yup.object().shape({
    type: yup.string().oneOf(["function"]).required(),
    moduleName: yup.string().required(),
    functionName: yup.string().required(),
    jsArguments: yup.string().jsScript(freeVarDefs.fnargs.name),
    columns: yup
        .object()
        .shape({
            value: yup.string().required(),
            label: yup.string(),
        })
        .required(),
}) as SchemaOf<FunctionSelectionDashboardParameterDataSource>;

export const resolveSelectionDashboardParameterDataSourceSchema = (
    s: Partial<SelectionDashboardParameterDataSource>
): SchemaOf<SelectionDashboardParameterDataSource> | undefined => {
    switch (s.type) {
        case "static":
            return StaticSelectionDashboardParameterDataSourceSchema;
        case "function":
            return FunctionSelectionDashboardParameterDataSourceSchema;
    }
};

export const SelectionDashboardParameterDataSourceSchema = yup.lazy<SchemaOf<SelectionDashboardParameterDataSource>>(
    (s: SelectionDashboardParameterDataSource): any => {
        if (!s) {
            return yup.mixed();
        }
        const result = resolveSelectionDashboardParameterDataSourceSchema(s);
        if (result) {
            return result;
        }
        return yup.mixed().test({
            test: function (val: any) {
                return this.createError({
                    path: this.path,
                    message: i18next.t("InvalidDashboardParameterDataSource", {type: val.type}),
                });
            },
        });
    }
) as unknown as SchemaOf<SelectionDashboardParameterDataSource>;

export const SelectionDashboardParameterSchema = BaseDashboardParameterSchema.shape({
    variableName: yup.string().required().identifier(),
    allowEmptySelection: yup.boolean().required(),
    dataSource: SelectionDashboardParameterDataSourceSchema,
}) as SchemaOf<SelectionDashboardParameter>;

export const TimeRangeDashboardParameterSchema = BaseDashboardParameterSchema.shape({
    beginVariableName: yup.string().required().identifier(),
    endVariableName: yup.string().required().identifier(),
}) as SchemaOf<TimeRangeDashboardParameter>;

export const FreeTextDashboardParameterSchema = BaseDashboardParameterSchema.shape({
    variableName: yup.string().required().identifier(),
}) as SchemaOf<FreeTextDashboardParameter>;

export const resolveDashboardParameterSchema = (
    s: Partial<DashboardParameter>
): SchemaOf<DashboardParameter> | undefined => {
    switch (s.selectionType) {
        case "timeRange":
            return TimeRangeDashboardParameterSchema;
        case "freeText":
            return FreeTextDashboardParameterSchema;
        default:
            return SelectionDashboardParameterSchema;
    }
};

export const DashboardParameterSchema = yup.lazy<SchemaOf<DashboardParameter>>((s: DashboardParameter): any => {
    if (!s) {
        return yup.mixed();
    }
    const result = resolveDashboardParameterSchema(s);
    if (result) {
        return result;
    }
    return yup.mixed().test({
        test: function (val: any) {
            return this.createError({
                path: this.path,
                message: i18next.t("InvalidDashboardParameter", {type: val.type}),
            });
        },
    });
}) as unknown as SchemaOf<DashboardParameter>;

export const DashboardWidgetInstantiationParameterBindingsSchema = map(
    yup.string().required().identifier(),
    yup.string().required().identifier()
);

export const DashboardWidgetInstantiationSchema = yup.object().shape({
    instantiationId: yup.string().required(),
    widgetName: yup.string().required(),
    parametersBindings: DashboardWidgetInstantiationParameterBindingsSchema.required(),
}) as SchemaOf<DashboardWidgetInstantiation>;

export const DashboardResponsiveLayoutSchema = yup.object().shape({
    name: yup.string().required(),
    breakpoint: yup.number().required().min(0),
    cols: yup.number().required().min(1),
    margin: yup.array().of(yup.number()).min(2).max(2),
    containerPadding: yup.array().of(yup.number().min(0)).min(2).max(2),
    rowHeight: yup.number().min(0),
    widgetsLayouts: yup.array(),
}); // as SchemaOf<DashboardResponsiveLayout>;

export const DashboardResponsiveLayoutSetSchema = yup.object().shape({
    margin: yup.array().of(yup.number()).min(2).max(2),
    containerPadding: yup.array().of(yup.number().min(0)).min(2).max(2),
    rowHeight: yup.number().min(0),
    layouts: yup.array().of(DashboardResponsiveLayoutSchema.required()).required().min(1),
});

export const DashboardAutoRefreshSchema = yup.object().shape({
    enabled: yup.boolean().required(),
    defaultInterval: yup.string().oneOf([...autoRefreshIntervals]),
    minInterval: yup.string().oneOf([...autoRefreshIntervals]),
}) as SchemaOf<DashboardAutoRefresh>;

export const DashboardDefinitionSchema = yup.object().shape({
    id: yup.string(),
    name: yup.string().required().identifier(),
    title: yup.string().required(),
    description: yup.string(),
    parameters: yup
        .array()
        .of(DashboardParameterSchema)
        .uniqueBy(
            s => s?.displayName,
            ({duplicates}) => i18next.t("DuplicateParameterNames", {names: duplicates.join(", ")})
        )
        .test({
            test: function (val: any) {
                if (!val || val.length === 0) {
                    return true;
                }
                const refs: Record<string, string[]> = {};
                for (const p of val as DashboardParameter[]) {
                    const vars = getVariableNames(p);
                    for (const varname of vars) {
                        const pars = refs[varname] ?? [];
                        pars.push(p.displayName);
                        refs[varname] = pars;
                    }
                }
                const dups: string[] = [];
                for (const [key, val] of Object.entries(refs)) {
                    if (val.length > 1) {
                        dups.push(`${key} (${val.join(", ")})`);
                    }
                }
                if (dups.length > 0) {
                    return this.createError({
                        path: this.path,
                        message: i18next.t("DuplicateParameterVariableNames", {names: dups.join(", ")}),
                    });
                }
                return true;
            },
        }),
    widgets: yup.array().of(DashboardWidgetInstantiationSchema.required()),
    layoutSet: DashboardResponsiveLayoutSetSchema,
    autoRefresh: DashboardAutoRefreshSchema,
}) as SchemaOf<DashboardDefinition>;
