import React, {FC, useState, useCallback, useMemo} from "react";
import {makeStyles, createStyles, Theme, useTheme} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Button from "@material-ui/core/Button";
import {useTranslation} from "react-i18next";
import MonacoEditor, {EditorDidMount, EditorWillMount} from "react-monaco-editor";
import FormHelperText from "@material-ui/core/FormHelperText";
import Autosizer from "react-virtualized-auto-sizer";

import {ContextType} from "../../api/nimflow-api";
import {defaultMonacoOptions} from "../../libs/monaco";
import {useLoseChangesConfirm} from "../../components/specialized-confirmations";
import {importContextTypeFromJson} from "../../models/context-type-importer";
import {exportContextTypeToJson} from "../../models/context-type-exporter";
import contextTypeSchema from "../../schemas/context-type.schema.json";
import uiSchemaSchema from "../../schemas/ui-schema.schema.json";
import uiSchemaLibrarySchema from "../../schemas/ui-schema-library.schema.json";

const errorContainerHeight = 100;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        dialogPaper: {
            height: "90vh",
        },
        dialogContent: {
            display: "grid",
            gridTemplateColumns: "auto",
            gridTemplateRows: (props: {showErrors: boolean}) => (props.showErrors ? `auto ${errorContainerHeight}px` : "auto"),
            rowGap: "5px",
            placeItems: "stretch",
            height: "100%",
            overflowY: "hidden",
        },
        editorContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
        },
        errorContainer: {
            gridColumn: "1 / 2",
            gridRow: "2 / 3",
            overflowY: "auto",
        },
    })
);

const editorWillMount: EditorWillMount = monaco => {
    monaco.languages.json.jsonDefaults.setDiagnosticsOptions({
        validate: true,
        schemas: [
            {
                uri: "http://nimflow.com/schemas/ui-schema.schema.json",
                schema: uiSchemaSchema,
            },
            {
                uri: "http://nimflow.com/schemas/ui-schema-library.schema.json",
                schema: uiSchemaLibrarySchema,
            },
            {
                uri: "http://nimflow.com/schemas/context-type.schema.json",
                fileMatch: ["inmemory://model/*"],
                schema: contextTypeSchema,
            },
        ],
    });

    const options: ReturnType<EditorWillMount> = {
        selectOnLineNumbers: true,
        //codeLens: false,
        minimap: {
            enabled: false,
        },
    };
    return options;
};

const editorDidMount: EditorDidMount = editor => {
    editor.focus();
};

export interface ContextTypeJsonEditorModalProps {
    open: boolean;
    defaultValue: ContextType;
    onChange?: (value: ContextType) => void;
    onClose: () => void;
}

export const ContextTypeJsonEditorModal: FC<ContextTypeJsonEditorModalProps> = props => {
    const {open, defaultValue, onChange, onClose} = props;
    const {t} = useTranslation();
    const theme = useTheme();
    const loseChangesConfirm = useLoseChangesConfirm();
    const [errors, setErrors] = useState<string[]>([]);
    const classes = useStyles({showErrors: errors.length > 0});
    const initialValue = useMemo(() => {
        const exported = exportContextTypeToJson(defaultValue);
        return JSON.stringify(exported, undefined, 4);
    }, [defaultValue]);

    const [code, setCode] = useState(initialValue);

    const handleOnAccept = useCallback(async () => {
        if (errors.length > 0) {
            return;
        }
        const json = JSON.parse(code);
        const importResult = await importContextTypeFromJson(json);
        if (importResult.type === "success") {
            onChange?.(importResult.result);
            onClose();
        } else {
            setErrors(importResult.errors.map(s => s.message));
        }
    }, [code, errors.length, onChange, onClose]);

    const handleOnClose = useCallback(async () => {
        if (initialValue !== code && !(await loseChangesConfirm())) {
            return;
        }
        onClose();
    }, [code, initialValue, loseChangesConfirm, onClose]);

    const handleDialogClose = useCallback(async (event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (initialValue !== code && !(await loseChangesConfirm())) {
            return;
        }
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [code, initialValue, loseChangesConfirm, onClose]);

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            maxWidth="xl"
            fullWidth
            classes={{
                paper: classes.dialogPaper,
            }}
        >
            <DialogTitle>{t("EditingContextType")}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <div className={classes.editorContainer}>
                    <Autosizer>
                        {({height, width}) => (
                            <MonacoEditor
                                width={width}
                                height={height}
                                language="json"
                                theme={theme.palette.type === "light" ? "vs-light" : "vs-dark"}
                                value={code}
                                options={defaultMonacoOptions}
                                onChange={setCode}
                                editorWillMount={editorWillMount}
                                editorDidMount={editorDidMount}
                            />
                        )}
                    </Autosizer>
                </div>
                {errors.length > 0 && (
                    <div className={classes.errorContainer}>
                        <FormHelperText component="span" error>
                            {errors.map((s, i) => (
                                <p key={i}>{s}</p>
                            ))}
                        </FormHelperText>
                    </div>
                )}
            </DialogContent>
            <DialogActions>
                <Button onClick={handleOnAccept} color="primary">
                    {t("Accept")}
                </Button>
                <Button onClick={handleOnClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
