import React, {FC, useState, useEffect, useCallback} from "react";
import {useParams, useNavigate} from "react-router";
import {clone} from "ramda";

import {useNimflow} from "../../api/nimflow-api-provider";
import {WaitSpinner} from "../../components/wait-spinner";
import {
    newServerContextTypeProvider,
    newImportedContextTypeProvider,
    SimulationContextTypeProvider,
} from "./simulation-context-type-provider";
import {ContextSimulationContextProvider} from "./simulation-context";
import {ContextSimulationEditor} from "./context-simulation-editor";
import {ContextSimulation, useNimflowSimulation} from "../../api/nimflow-simulation-api";
import {ApiException} from "../../api/nimflow-api";

export const ContextSimulationUpdater: FC<{}> = props => {
    const {id} = useParams();
    const navigate = useNavigate();
    const [simulation, setSimulation] = useState<ContextSimulation>();
    const [contextTypeProvider, setContextTypeProvider] = useState<SimulationContextTypeProvider | undefined>();
    const {contextTypes_FindOne} = useNimflow();
    const {simulations_FindOne, simulations_Replace, simulations_InsertOne} = useNimflowSimulation();

    useEffect(() => {
        simulations_FindOne(id).then(async s => {
            const simulation: ContextSimulation = s;
            const provider = simulation.contextType
                ? newImportedContextTypeProvider(simulation.contextType)
                : await newServerContextTypeProvider(simulation.contextTypeName!, name => contextTypes_FindOne({name}));
            setSimulation(simulation);
            setContextTypeProvider(provider);
        });
    }, [id, simulations_FindOne, contextTypes_FindOne]);

    const handleOnSaveChanges = useCallback(
        async (value: ContextSimulation) => {
            try {
                await simulations_Replace(value);
                setSimulation(value);
            } catch (e: any) {
                let msg = e.toString();
                if (ApiException.isApiException(e)) {
                    msg = e.message;
                    if ("status" in e) {
                        msg += ` (status: ${e.status})`;
                    }
                }
                return [{message: msg}];
            }
            return [];
        },
        [simulations_Replace]
    );

    const handleOnSaveAs = useCallback(
        async (value: ContextSimulation, newName: string) => {
            const newSimulation = clone(value);
            newSimulation.name = newName;
            newSimulation.id = undefined;

            const result = await simulations_InsertOne(newSimulation);
            setSimulation(undefined);
            navigate(`../${result}`, {replace: true});
        },
        [navigate, simulations_InsertOne]
    );

    if (!simulation || !contextTypeProvider) {
        return <WaitSpinner />;
    }

    return (
        <ContextSimulationContextProvider contextTypeProvider={contextTypeProvider} simulation={simulation}>
            <ContextSimulationEditor onSaveChanges={handleOnSaveChanges} onSaveAs={handleOnSaveAs}/>
        </ContextSimulationContextProvider>
    );
};
