import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Toolbar from "@material-ui/core/Toolbar";
import ToggleButton from "@material-ui/lab/ToggleButton";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import SaveIcon from "@material-ui/icons/Save";
import AssignmentTurnedInIcon from "@material-ui/icons/AssignmentTurnedIn";
import {Form, Formik, FormikHelpers, FormikProps, useFormikContext} from "formik";
import * as yup from "yup";
import {useTranslation} from "react-i18next";
import {useSnackbar} from "notistack";
import {useThrottleCallback} from "@react-hook/throttle";
import Autosizer from "react-virtualized-auto-sizer";
import {useDebounce} from "react-use";
import {useNavigate} from "react-router";

import {JsConsoleEntry, UserDefinedFunction} from "../../api/nimflow-api";
import {ErrorBoundary} from "../../components/error-boundary";
import {UserDefinedFunctionSchema} from "./ud-function-schema";
import {validateAndSubmit, validateForm} from "../../utils/formikHelpers";
import {ErrorViewer} from "../../components/error-viewer";
import {useConfirm} from "../../components/confirm";
import {useReportError} from "../../utils/use-report-error";
import {useExitWithoutSavingBlocker} from "../../utils/use-exit-without-saving-blocker";
import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {validateWithYupSchema} from "../../libs/yup";
import {JavascriptMonacoEditor} from "../../components/javascript-monaco-editor";
import {FormikErrorViewer} from "../../components/formik-error-viewer";
import {PromptIcon} from "../../components/icons/prompt-icon";
import {ConsoleViewer} from "../../components/console-viewer";
import {useNimflow} from "../../api/nimflow-api-provider";
import {JsContextDefinitionsContextProvider} from "../../components/js-context-definitions-provider";
import {JsonSchemaIcon} from "../../components/icons/json-schema-icon";
import {JsonSchemaEditorModal} from "../../components/json-schema-editor-modal";
import {getTypingFromSchema} from "../../utils/json-schema-to-typescript";

const toolbarHeight = 48;
const errorHeight = 60;

interface StyleProps {
    showRepl: boolean;
    showError: boolean;
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        form: {
            height: "100%",
        },
    })
);

const useInnerStyles = makeStyles((theme: Theme) =>
    createStyles({
        toolbar: {
            minHeight: toolbarHeight,
            paddingLeft: 0,
        },
        grow: {
            flexGrow: 1,
        },
        button: {
            margin: theme.spacing(1),
            whiteSpace: "nowrap",
        },
        content: {
            height: `calc(100% - ${toolbarHeight}px)`,
            overflow: "auto",
            display: "grid",
            gridTemplateColumns: "25% 74%",
            gridTemplateRows: (props: {showRepl: boolean}) => "60px auto" + (props.showRepl ? " 250px" : ""),
        },
        nameContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
        },
        descriptionContainer: {
            gridColumn: "2 / 3",
            gridRow: "1 / 2",
            paddingLeft: 10,
        },
        moduleEditorContainer: {
            paddingTop: 10,
            gridColumn: "1 / 3",
            gridRow: "2 / 3",
        },
        replContainer: {
            gridColumn: "1 / 3",
            gridRow: "3 / 4",
        },
        editorContainer: {
            height: (props: StyleProps) => (props.showError ? `calc(100% - ${errorHeight}px)` : "100%"),
        },
        errorContainer: {
            height: errorHeight,
            overflowY: "auto",
        },
        fixHeight: {
            height: 24,
        },
        toggleButton: {
            border: "none",
            borderRadius: "50%",
        },
    })
);

const useReplStyles = makeStyles((theme: Theme) =>
    createStyles({
        consoleFeed: {
            border: `solid 1px ${theme.palette.divider}`,
            borderRadius: theme.shape.borderRadius,
            height: "135px",
            overflow: "auto",
        },
    })
);

interface JsModuleReplProps {
    moduleName?: string;
    jsModule?: string;
}

const JsModuleRepl: FC<JsModuleReplProps> = props => {
    const {moduleName, jsModule} = props;
    const classes = useReplStyles();
    const {t} = useTranslation();
    const [snippet, setSnippet] = useState("");
    const [lastEvaluation, setLastEvaluation] = useState<JsConsoleEntry[]>([]);
    const {contextTypes_EvalJsExpression} = useNimflow();

    const handleEvaluate = useCallback(async () => {
        try {
            let modname = moduleName;
            if (!modname) {
                modname = "module";
            }
            let actualSnippet = !snippet || !snippet.startsWith(modname) ? `${modname}.default(${snippet})` : snippet;
            const instrumentedSnippet = `((${modname}) => ${actualSnippet})(common)`;
            const result = await contextTypes_EvalJsExpression({
                expression: instrumentedSnippet,
                common: jsModule,
            });
            setLastEvaluation(s => []); // Force console log reset
            setLastEvaluation(s => [...(result.consoleEntries ?? []), {type: "result", values: [result.result]}]);
        } catch (e: any) {
            setLastEvaluation([{type: "error", values: [e.toString()]}]);
        }
    }, [snippet, moduleName, contextTypes_EvalJsExpression, jsModule]);

    return (
        <Grid container spacing={3} alignItems="center">
            <Grid item xs={10}>
                <TextField
                    value={snippet}
                    multiline={true}
                    fullWidth
                    variant="outlined"
                    minRows={2}
                    maxRows={2}
                    onChange={ev => setSnippet(ev.target.value)}
                />
            </Grid>
            <Grid item xs={2} style={{textAlign: "center"}}>
                <Button onClick={handleEvaluate} variant="outlined">
                    {t("Evaluate")}
                </Button>
            </Grid>
            <Grid item xs={12}>
                <div className={classes.consoleFeed}>
                    <ConsoleViewer entries={lastEvaluation} />
                </div>
            </Grid>
        </Grid>
    );
};

const getJsCommonTypingProvider = (inputSchema?: string, outputSchema?: string): (() => string | Promise<string>) => {
    let cachedInputTypings: string | undefined = undefined;
    let cachedOutputTypings: string | undefined = undefined;
    return async () => {
        if (inputSchema) {
            cachedInputTypings = await getTypingFromSchema(inputSchema);
        } else {
            cachedInputTypings = "any";
        }
        if (outputSchema) {
            cachedOutputTypings = await getTypingFromSchema(outputSchema);
        } else {
            cachedOutputTypings = "any";
        }
        return `
        /** Declared only to provide typings. Do not call in actual code. */  
        export const exportedFn = (args: ${cachedInputTypings}): ${cachedOutputTypings} => () => throw new Error("Not supported")`;
    };
};

const defaultJsonSchema = `{
    "$schema": "http://json-schema.org/draft-07/schema#",
    "type": "object",
    "properties": {

    }
}`;

interface InnerUdFunctionEditorProps {
    schema: yup.ObjectSchema<any>;
    onSave?: () => Promise<void>;
    onJsModuleErrorsChange?: (errors: string[]) => void;
    jsModuleErrors: string[];
}

const InnerUdFunctionEditor: FC<InnerUdFunctionEditorProps> = props => {
    const {schema, onSave, onJsModuleErrorsChange, jsModuleErrors} = props;
    const {t} = useTranslation();
    const navigate = useNavigate();
    const {enqueueSnackbar} = useSnackbar();
    const {
        isSubmitting,
        validateForm,
        values: udFunction,
        dirty,
        setFieldValue,
        errors,
    } = useFormikContext<UserDefinedFunction>();
    const canSave = !isSubmitting;
    const reportErrors = useReportError();
    const [editJsonSchema, setEditJsonSchema] = useState<"input" | "output">();
    const [code, setCode] = useState<string | undefined>(udFunction.script);
    const [showRepl, setShowRepl] = useState(false);
    const scriptErrors = errors.script;
    const classes = useInnerStyles({
        showError: !!scriptErrors,
        showRepl,
    });

    const opts = useMemo(() => {
        return {
            description: t("ExitingUserDefinedFunctionEditorWithoutSavingWarning"),
        };
    }, [t]);
    useExitWithoutSavingBlocker(dirty, opts);

    const handleSaveUserDefinedFunctionClick = useThrottleCallback(
        useCallback(async () => {
            if (onSave) {
                await onSave();
            }
        }, [onSave])
    );

    const handleValidateUserDefinedFunctionClick = useCallback(async () => {
        const errorInfos = await validateWithYupSchema(schema, udFunction);
        if (jsModuleErrors.length > 0) {
            if (!errorInfos.find(s => s.dataPath === "script")) {
                errorInfos.push({
                    dataPath: "script",
                    message: jsModuleErrors.map(s => "\n\t" + s).join(""),
                });
            }
        }
        await validateForm();
        if (errorInfos.length > 0) {
            reportErrors(t("UserDefinedFunctionHasValidationErrors"), errorInfos, {
                dialogProps: {
                    maxWidth: "lg",
                },
            });
        } else {
            enqueueSnackbar(t("UserDefinedFunctionIsValid"), {variant: "success"});
        }
    }, [schema, udFunction, jsModuleErrors, validateForm, reportErrors, t, enqueueSnackbar]);

    const handleGoBack = useCallback(() => {
        navigate(-1);
    }, [navigate]);

    const handleOnChange = useCallback((value: string | undefined) => {
        setCode(value);
    }, []);

    const jsCommonTypingProvider: () => string | Promise<string> = useMemo(() => {
        return getJsCommonTypingProvider(udFunction.inputSchema, udFunction.outputSchema);
    }, [udFunction.inputSchema, udFunction.outputSchema]);

    const handleEditInputJsonSchemaClick = useCallback(() => {
        setEditJsonSchema("input");
    }, []);

    const handleEditOutputJsonSchemaClick = useCallback(() => {
        setEditJsonSchema("output");
    }, []);

    useDebounce(
        () => {
            setFieldValue("script", code);
        },
        1000,
        [code]
    );

    return (
        <JsContextDefinitionsContextProvider jsCommonTypingProvider={jsCommonTypingProvider}>
            <Toolbar className={classes.toolbar}>
                <Tooltip title={t("GoBack") as string}>
                    <IconButton onClick={handleGoBack} disabled={isSubmitting}>
                        <ArrowBackIcon />
                    </IconButton>
                </Tooltip>
                <Tooltip title={t("SaveChanges") as string}>
                    <IconButton onClick={handleSaveUserDefinedFunctionClick} disabled={!canSave}>
                        <SaveIcon />
                    </IconButton>
                </Tooltip>
                <Tooltip title={t("Validate") as string}>
                    <IconButton onClick={handleValidateUserDefinedFunctionClick}>
                        <AssignmentTurnedInIcon />
                    </IconButton>
                </Tooltip>
                <ToggleButton
                    value="showRepl"
                    selected={showRepl}
                    onClick={() => setShowRepl(!showRepl)}
                    classes={{
                        root: classes.toggleButton,
                    }}
                >
                    <Tooltip title={t("ShowRepl") as string}>
                        <div className={classes.fixHeight}>
                            <PromptIcon />
                        </div>
                    </Tooltip>
                </ToggleButton>
                <div className={classes.grow} />
                <Button
                    variant="outlined"
                    onClick={handleEditInputJsonSchemaClick}
                    startIcon={<JsonSchemaIcon />}
                    className={classes.button}
                    size="small"
                >
                    {t("EditInputJsonSchema")}
                </Button>
                <Button
                    variant="outlined"
                    onClick={handleEditOutputJsonSchemaClick}
                    startIcon={<JsonSchemaIcon />}
                    className={classes.button}
                    size="small"
                >
                    {t("EditOutputJsonSchema")}
                </Button>
            </Toolbar>
            <div className={classes.content}>
                <div className={classes.nameContainer}>
                    <FormikTextField
                        name="name"
                        label={t("Name")}
                        fullWidth
                        InputLabelProps={{required: true}}
                        required
                    />
                </div>
                <div className={classes.descriptionContainer}>
                    <FormikTextField name="description" label={t("Description")} fullWidth multiline maxRows={4} />
                </div>
                <div className={classes.moduleEditorContainer}>
                    {/* {JSON.stringify(udFunction)} */}
                    <div className={classes.editorContainer}>
                        <Autosizer>
                            {({height, width}) => (
                                <JavascriptMonacoEditor
                                    height={height}
                                    width={width}
                                    value={code}
                                    onChange={handleOnChange}
                                    onErrorsChange={onJsModuleErrorsChange}
                                    importJsCommon={true}
                                />
                            )}
                        </Autosizer>
                    </div>
                    {!!scriptErrors && (
                        <div className={classes.errorContainer}>
                            <FormikErrorViewer errors={errors} names="script" />
                        </div>
                    )}
                </div>
                {showRepl && (
                    <div className={classes.replContainer}>
                        <JsModuleRepl jsModule={code} moduleName={udFunction.name} />
                    </div>
                )}
            </div>

            {!!editJsonSchema && (
                <JsonSchemaEditorModal
                    open={!!editJsonSchema}
                    onClose={() => setEditJsonSchema(undefined)}
                    title={t(editJsonSchema === "input" ? "EditingInputJsonSchema" : "EditingOutputJsonSchema")}
                    defaultValue={
                        (editJsonSchema === "input" ? udFunction.inputSchema : udFunction.outputSchema) ??
                        defaultJsonSchema
                    }
                    onChange={value =>
                        setFieldValue(editJsonSchema === "input" ? "inputSchema" : "outputSchema", value ?? undefined)
                    }
                />
            )}
        </JsContextDefinitionsContextProvider>
    );
};

export interface UdFunctionEditorProps {
    value: UserDefinedFunction;
    schema?: yup.ObjectSchema<any>;
    onSave: (value: UserDefinedFunction) => Promise<boolean>;
}

export const UdFunctionEditor: FC<UdFunctionEditorProps> = props => {
    const {value, schema = UserDefinedFunctionSchema, onSave} = props;

    const {t} = useTranslation();
    const classes = useStyles();
    const confirm = useConfirm();
    const {enqueueSnackbar} = useSnackbar();
    const [jsModuleErrors, setJsModuleErrors] = useState<string[]>([]);
    const formRef = useRef<FormikProps<UserDefinedFunction> | null>();

    useEffect(() => {
        formRef.current?.resetForm({values: value});
    }, [value]);

    const internalValidateAndSubmit = useCallback(
        async (form: FormikProps<UserDefinedFunction>) => {
            if (jsModuleErrors.length > 0) {
                await validateForm(form);
                form.setFieldError("script", jsModuleErrors.join("\n"));
                return;
            }
            validateAndSubmit(form);
        },
        [jsModuleErrors]
    );

    const submitHandlerRef = useRef<(values: UserDefinedFunction) => Promise<void>>();

    const handleSave = useCallback(() => {
        const form = formRef.current;
        if (form && onSave) {
            submitHandlerRef.current = async (data: UserDefinedFunction) => {
                let error: React.ReactNode;
                try {
                    const saved = await onSave(data);
                    if (!saved) {
                        return;
                    }
                } catch (e) {
                    error = <ErrorViewer error={e} />;
                }
                if (error) {
                    await confirm({
                        title: t("ErrorSavingUserDefinedFunction"),
                        description: error,
                        mapCloseTo: "ok",
                        actions: [{type: "ok", text: t("Ok")}],
                    });
                } else {
                    enqueueSnackbar(t("UserDefinedFunctionSaved"), {variant: "success"});
                }
            };
            internalValidateAndSubmit(form);
        }
        return Promise.resolve();
    }, [confirm, enqueueSnackbar, internalValidateAndSubmit, onSave, t]);

    const handleSubmit = useCallback(
        async (values: UserDefinedFunction, actions: FormikHelpers<UserDefinedFunction>) => {
            try {
                if (submitHandlerRef.current) {
                    await submitHandlerRef.current(values);
                    submitHandlerRef.current = undefined;
                }
            } finally {
                actions.setSubmitting(false);
            }
        },
        []
    );

    return (
        <ErrorBoundary>
            <Formik<UserDefinedFunction>
                innerRef={s => (formRef.current = s)}
                initialValues={value}
                onSubmit={handleSubmit}
                validationSchema={schema}
                validateOnChange={true}
                validateOnBlur={true}
            >
                <Form autoComplete="off" className={classes.form}>
                    <InnerUdFunctionEditor
                        schema={schema}
                        onSave={handleSave}
                        jsModuleErrors={jsModuleErrors}
                        onJsModuleErrorsChange={setJsModuleErrors}
                    />
                </Form>
            </Formik>
        </ErrorBoundary>
    );
};
