import {ContextType} from "../api/nimflow-api";
import {JSONSchema7} from "json-schema";
import {clone} from "ramda";

export const exportContextTypeToJson = (
    contextType: ContextType
): Omit<ContextType, "jsonSchema"> & {$schema: string; jsonSchema?: JSONSchema7} => {
    const result: any = {
        $schema: "http://nimflow.com/schemas/context-type.schema.json",
        name: contextType.name, // Force order of insertion of name before the rest
        ...clone(contextType as Omit<ContextType, "name" | "jsonSchema">),
    };
    if (contextType.jsonSchema) {
        result.jsonSchema = JSON.parse(contextType.jsonSchema);
    }
    if (contextType.settings?.uiSchemaCommon) {
        result.settings!.uiSchemaCommon = JSON.parse(contextType.settings!.uiSchemaCommon);
    }
    for (const action of result.actions) {
        const settings = action.settings;
        const uiSchema = settings?.uiSchema;
        if (typeof uiSchema === "string") {
            settings!.uiSchema = JSON.parse(uiSchema);
        }
    }
    for (const response of result.tasks.flatMap((s: any) => s.validResponses)) {
        const settings = response.settings;
        const uiSchema = settings?.uiSchema;
        if (typeof uiSchema === "string") {
            settings!.uiSchema = JSON.parse(uiSchema);
        }
    }
    return result;
};
