import * as React from "react";
import {AbstractReactFactory, GenerateWidgetEvent} from "@projectstorm/react-canvas-core";
import {DiagramEngine} from "@projectstorm/react-diagrams-core";

import {StateNodeModel} from "./state-node-model";
import {StateNodeWidget} from "./state-node-widget";

export class StateNodeFactory extends AbstractReactFactory<StateNodeModel, DiagramEngine> {
    constructor() {
        super("state");
    }

    generateModel(initialConfig: any) {
        const milestoneType = initialConfig.milestoneType;
        const instanceId = initialConfig.instanceId ?? "";
        const selector = {...initialConfig};
        delete selector.milestoneType;
        delete selector.instanceId;
        return new StateNodeModel(selector, instanceId, milestoneType);
    }

    generateReactWidget(event: GenerateWidgetEvent<StateNodeModel>): JSX.Element {
        return <StateNodeWidget engine={this.engine} node={event.model} />;
    }
}
