import React, {FC, useCallback} from "react";
import {createStyles, makeStyles, Theme, emphasize} from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardHeader, {CardHeaderProps} from "@material-ui/core/CardHeader";
import CardContent from "@material-ui/core/CardContent";
import Fab from "@material-ui/core/Fab";
import Tooltip from "@material-ui/core/Tooltip";
import Divider from "@material-ui/core/Divider";
import {DiagramEngine} from "@projectstorm/react-diagrams-core";
import {useRecoilState, useSetRecoilState, useRecoilValue} from "recoil";
import {uniq} from "ramda";

import {ContextTypeNodeModel} from "./context-type-node-model";
import {ContextTypePortWidget} from "./context-type-port-widget";
import {ContextTypePortModel, defaultContextTypePortWidgetSize} from "./context-type-port-model";
import {hoveredContextTypeNodeModelIdState, selectedLinkIds, focusedNodeIdsState} from "../context-type-diagram-state";

export interface ContextTypeNodePanelAction {
    icon: NonNullable<React.ReactNode>;
    tooltip?: string;
    onClick: (event: any) => void;
}

export interface ContextTypeNodePanelProps {
    node: ContextTypeNodeModel<any>;
    engine: DiagramEngine;
    onPortHovering?: (port: ContextTypePortModel, hovering: boolean) => void;
    onPanelHovering?: (hovering: boolean) => void;
    title?: CardHeaderProps["title"];
    titleTypographyProps?: CardHeaderProps["titleTypographyProps"];
    subheader?: CardHeaderProps["subheader"];
    subheaderTypographyProps?: CardHeaderProps["subheaderTypographyProps"];
    disableHeaderTypography?: CardHeaderProps["disableTypography"];
    avatar?: CardHeaderProps["avatar"];
    actions?: ContextTypeNodePanelAction[];
    selected?: boolean;
}

interface StyleProps {
    selected: boolean;
    unfocused: boolean;
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            position: "relative",
            marginTop: 20, // force some whitespace above and below each node to improve actions legibility
            marginBottom: 20,
            cursor: "pointer",
            opacity: (props: StyleProps) => (props.unfocused ? 0.2 : 1),
        },
        cardContainer: {
            position: "relative",
        },
        card: {
            backgroundColor: (props: StyleProps) =>
                props.selected ? theme.palette.action.selected : theme.palette.background.paper,
        },
        inPort: {
            top: 20,
            left: -defaultContextTypePortWidgetSize,
            position: "absolute",
        },
        outPort: {
            top: 20,
            right: -defaultContextTypePortWidgetSize,
            position: "absolute",
        },
        headerRoot: {
            padding: `${theme.spacing(1)}px ${theme.spacing(2)}px`,
        },
        contentRoot: {
            padding: `${theme.spacing(1)}px ${theme.spacing(1)}px`,
        },
        actions: {
            display: "table",
            position: "absolute",
            //top: -48,
            paddingBottom: 10,
            zIndex: 100,
            width: "100%",
            textAlign: "right",
            //border: "1px solid black"
        },
        action: {
            marginTop: 5,
            marginRight: 10,
            color: theme.palette.text.secondary,
            backgroundColor: theme.palette.background.paper,
            "&:hover": {
                backgroundColor: emphasize(theme.palette.background.paper, 0.15),
            },
        },
    })
);

export const ContextTypeNodePanel: FC<ContextTypeNodePanelProps> = props => {
    const {
        node,
        engine,
        onPortHovering,
        onPanelHovering,
        title,
        titleTypographyProps,
        subheader,
        subheaderTypographyProps,
        avatar,
        actions,
        children,
    } = props;
    const [hoveredNodeId, setHoveredNodeId] = useRecoilState(hoveredContextTypeNodeModelIdState);
    const focusdNodeIds = useRecoilValue(focusedNodeIdsState);
    const setSelectedLinkIds = useSetRecoilState(selectedLinkIds);
    const isActive = hoveredNodeId === node.getID();
    const isFocused = focusdNodeIds.includes(node.getID());
    const classes = useStyles({
        selected: props.selected ?? false,
        unfocused: focusdNodeIds.length > 0 && !isFocused,
    });

    const handleMouseEnter = useCallback(() => {
        onPanelHovering?.(true);
        setHoveredNodeId(node.getID());
        setSelectedLinkIds(ids => {
            const newIds = uniq(Object.values(node.getPorts()).flatMap(s => Object.keys(s.links)));
            if (ids.length === newIds.length && newIds.every(s => ids.includes(s))) {
                return ids;
            }
            return newIds;
        });
    }, [onPanelHovering, setHoveredNodeId, node, setSelectedLinkIds]);

    const handleMouseLeave = useCallback(() => {
        onPanelHovering?.(false);
        const nodeId = node.getID();
        setHoveredNodeId(id => (id === nodeId ? undefined : id));
        setSelectedLinkIds(s => (s.length > 0 ? [] : s));
    }, [onPanelHovering, node, setHoveredNodeId, setSelectedLinkIds]);

    const handleDoubleClick = useCallback(() => {
        node.dispatchCommand({
            type: "focusNode",
        });
    }, [node]);

    return (
        <div className={classes.root}>
            <div onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave} className={classes.cardContainer}>
                <Card className={classes.card} elevation={2} onDoubleClick={handleDoubleClick}>
                    <CardHeader
                        title={title ?? node.title}
                        titleTypographyProps={titleTypographyProps}
                        subheader={subheader}
                        subheaderTypographyProps={subheaderTypographyProps}
                        avatar={avatar}
                        classes={{
                            root: classes.headerRoot,
                        }}
                    />
                    {!!children && (
                        <>
                            <Divider />
                            <CardContent
                                classes={{
                                    root: classes.contentRoot,
                                }}
                            >
                                {children}
                            </CardContent>
                        </>
                    )}
                </Card>
                {!!actions && isActive && (
                    <div className={classes.actions}>
                        {actions.map((action, index) => {
                            let element = (
                                <Fab
                                    key={index}
                                    onClick={e => action.onClick(e)}
                                    size="small"
                                    className={classes.action}
                                >
                                    {action.icon}
                                </Fab>
                            );
                            if (action.tooltip) {
                                element = (
                                    <Tooltip key={index} title={action.tooltip}>
                                        {element}
                                    </Tooltip>
                                );
                            }
                            return element;
                        })}
                    </div>
                )}
            </div>

            {node.inPort && (
                <ContextTypePortWidget
                    port={node.inPort}
                    engine={engine}
                    onPortHovering={onPortHovering}
                    className={classes.inPort}
                />
            )}
            {node.outPort && (
                <ContextTypePortWidget
                    port={node.outPort}
                    engine={engine}
                    onPortHovering={onPortHovering}
                    className={classes.outPort}
                />
            )}
        </div>
    );
};
