import React, {FC, useCallback, useState} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Button from "@material-ui/core/Button";
import Checkbox from "@material-ui/core/Checkbox";
import Alert from "@material-ui/lab/Alert";
import {useTranslation} from "react-i18next";
import {append, remove, groupBy, mapObjIndexed, uniq} from "ramda";

import {Selector} from "../../api/nimflow-api";
import {detectCycle} from "../../utils/graph-algorithms";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            backgroundColor: theme.palette.background.default,
        },
        list: {
            width: "100%",
            //maxWidth: 360,
            backgroundColor: theme.palette.background.paper,
        },
    })
);

export interface ContextTypeSelectorDependenciesEditorProps {
    open: boolean;
    selector: Selector;
    availableSelectors: Selector[];
    onChange: (value: Selector) => void;
    onClose: () => void;
}

const findSelectorCycle = (selector: Selector, availableSelectors: Selector[]): string[] | undefined => {
    const rootKey = selector.key;
    if (!rootKey || !selector.dependencies || selector.dependencies.length === 0) {
        return undefined;
    }
    const map = mapObjIndexed(
        (s: Selector[]) => uniq(s.flatMap(sel => sel.dependencies ?? [])),
        groupBy(
            s => s.key!,
            append(selector, availableSelectors).filter(s => s.key)
        )
    );
    return detectCycle(rootKey, {
        getNeighbors: v => map[v] ?? [],
    });
};

export const ContextTypeSelectorDependenciesEditor: FC<ContextTypeSelectorDependenciesEditorProps> = props => {
    const {open, selector, availableSelectors, onChange, onClose} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const [dependencies, setDependencies] = useState(selector?.dependencies ?? []);
    const [error, setError] = useState<string>();

    const onAccept = useCallback(() => {
        const newSelector = {
            ...selector,
            dependencies,
        };
        const cycle = findSelectorCycle(newSelector, availableSelectors);
        if (cycle) {
            setError(t("SelectorCircularDependencyFound", {cycle: cycle.join(" -> ")}));
            return;
        }
        setError(undefined);
        onChange?.(newSelector);
        onClose?.();
    }, [dependencies, onChange, onClose, availableSelectors, selector, t]);

    const handleToggle = useCallback(
        (selector: string) => {
            setDependencies(s => {
                const idx = s.indexOf(selector);
                if (idx === -1) {
                    return append(selector, s);
                } else {
                    return remove(idx, 1, s);
                }
            });
        },
        [setDependencies]
    );

    return (
        <Dialog open={open} onClose={onClose} fullWidth maxWidth="xs" classes={{paper: classes.paper}}>
            <DialogTitle>{t("EditingSelectorDependencies")}</DialogTitle>
            <DialogContent>
                <List className={classes.list}>
                    {availableSelectors.map(selector => {
                        return (
                            <ListItem
                                key={selector.key}
                                role={undefined}
                                dense
                                button
                                onClick={() => handleToggle(selector.key!)}
                            >
                                <ListItemIcon>
                                    <Checkbox
                                        edge="start"
                                        checked={dependencies.includes(selector.key!)}
                                        tabIndex={-1}
                                        disableRipple
                                        color="primary"
                                    />
                                </ListItemIcon>
                                <ListItemText primary={selector.key} />
                            </ListItem>
                        );
                    })}
                </List>
                {!!error && <Alert severity="error">{error}</Alert>}
            </DialogContent>
            <DialogActions>
                <Button onClick={onAccept} color="primary">
                    {t("Accept")}
                </Button>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
