import {useMemo, useCallback} from "react";
import {isPresent} from "ts-is-present";
import {FieldArrayRenderProps, useFormikContext, FieldArray, getIn} from "formik";

import {BaseTable, BaseTableProps} from "./base-table";

export type FormikArrayFieldEditorInternalProps<T extends object> = {
    data: T[];
    editable?: BaseTableProps<Partial<T>>["editable"] & {
        allowAddition?: boolean | (() => boolean);
        allowEdition?: boolean | (() => boolean);
        allowDeletion?: boolean | (() => boolean);
    };
    arrayHelpers: FieldArrayRenderProps;
    validate: () => void;
} & Omit<BaseTableProps<Partial<T>>, "data">;

const isActionAllowed = (test: boolean | (() => boolean) | undefined): boolean => {
    if (typeof test === "function") {
        test = test();
    }
    return typeof test === "boolean" ? test : !isPresent(test);
};

const FormikArrayFieldEditorInternal = <T extends object>(props: FormikArrayFieldEditorInternalProps<T>) => {
    const {data, arrayHelpers, validate, editable, ...rest} = props;

    const tableEditionHandler = useMemo(() => {
        const tableEditionHandler: BaseTableProps<Partial<T>>["editable"] = {
            ...editable,
        };
        const allowAddition = editable?.allowAddition;
        if (isActionAllowed(allowAddition)) {
            tableEditionHandler.onRowAdd = async newData => {
                await editable?.onRowAdd?.(newData);
                arrayHelpers.push(newData);
                validate();
            };
        }
        const allowEdition = editable?.allowEdition;
        if (isActionAllowed(allowEdition)) {
            tableEditionHandler.onRowUpdate = async (newData, oldData) => {
                const index = oldData && "id" in oldData ? data.findIndex(s => (s as any).id === (oldData as any).id) : (oldData as any).tableData.id;
                //const index = data.findIndex(s => s === oldData);
                const oldFields = index >= 0 ? data[index] : undefined;
                await editable?.onRowUpdate?.(newData, oldFields);
                if (index >= 0) {
                    arrayHelpers.remove(index);
                    arrayHelpers.insert(index, newData);
                } else {
                    arrayHelpers.push(newData);
                }
                validate();
            };
        }
        const allowDeletion = editable?.allowDeletion;
        if (isActionAllowed(allowDeletion)) {
            tableEditionHandler.onRowDelete = async oldData => {
                const index = oldData && "id" in oldData ? data.findIndex(s => (s as any).id === (oldData as any).id) : (oldData as any).tableData.id;
                //const index = data.findIndex(s => s === oldData);
                if (index >= 0) {
                    await editable?.onRowDelete?.(data[index]);
                    arrayHelpers.remove(index);
                }
                validate();
            };
        }
        return tableEditionHandler;
    }, [arrayHelpers, data, editable, validate]);

    return <BaseTable data={data} editable={tableEditionHandler} {...rest} />;
};

export type FormikArrayFieldEditorProps<T extends object> = {
    path: string;
    editable?: BaseTableProps<Partial<T>>["editable"] & {
        allowAddition?: boolean | (() => boolean);
        allowEdition?: boolean | (() => boolean);
        allowDeletion?: boolean | (() => boolean);
    };
} & Omit<BaseTableProps<Partial<T>>, "data">;

export const FormikArrayFieldEditor = <T extends object>(props: FormikArrayFieldEditorProps<T>) => {
    const {path, ...rest} = props;
    const {values, validateField} = useFormikContext();
    const data: T[] = getIn(values, path);

    const validate = useCallback(() => {
        validateField(path);
    }, [path, validateField]);

    return (
        <FieldArray name={path}>
            {helpers => {
                return (
                    <FormikArrayFieldEditorInternal data={data} arrayHelpers={helpers} validate={validate} {...rest} />
                );
            }}
        </FieldArray>
    );
};
