import {FC, useState, useEffect, useCallback, useMemo} from "react";
import {useTranslation} from "react-i18next";
import {useParams} from "react-router";
import * as yup from "yup";

import {UserDefinedFunction} from "../../api/nimflow-api";
import {useNimflowFunctions} from "../../api/nimflow-functions-api";
import {UdFunctionEditor} from "./ud-function-editor";
import {UserDefinedFunctionSchema} from "./ud-function-schema";

export const UdFunctionUpdater: FC<{}> = () => {
    const {id} = useParams();
    const {t} = useTranslation();
    const [udFunction, setUdFunction] = useState<UserDefinedFunction>();
    const {updateUdFunction, getUdFunction, searchUdFunctions} = useNimflowFunctions();

    useEffect(() => {
        getUdFunction(id).then(s => {
            setUdFunction(s);
        });
    }, [getUdFunction, id]);

    const handleOnChange = useCallback(
        async (value: UserDefinedFunction) => {
            const result = await updateUdFunction(value);
            setUdFunction(result);
            return true;
        },
        [updateUdFunction]
    );

    const schema = useMemo(
        () =>
            UserDefinedFunctionSchema.shape({
                name: UserDefinedFunctionSchema.fields!.name.concat(
                    yup
                        .string()
                        .defined()
                        .test({
                            message: ({value}) => t("UserDefinedFunctionNameAlreadyExists", {name: value}),
                            test: async function (value: any) {
                                if (!value) {
                                    return true;
                                }
                                const fns = await searchUdFunctions({name: value});
                                return fns.filter(s => s.name === value && s.id !== id).length === 0;
                            },
                        })
                ),
            }),
        [id, searchUdFunctions, t]
    );

    if (!udFunction) {
        return null;
    }

    return <UdFunctionEditor value={udFunction} schema={schema} onSave={handleOnChange} />;
};
