import React, {FC, useCallback, useState} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Button from "@material-ui/core/Button";
import {useTranslation} from "react-i18next";

import {ErrorInfo} from "../models/error-info";
import {JavascriptMonacoEditor, FreeVariableDefinition} from "./javascript-monaco-editor";
import {useConfirm} from "./confirm";
import {ErrorViewer} from "./error-viewer";
import {useLoseChangesConfirm} from "./specialized-confirmations";

//See ESLint, prettier integration and autocompletion: https://blog.expo.io/building-a-code-editor-with-monaco-f84b3a06deaf

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        dialogContent: {
            height: "400px",
        },
        grow: {
            flexGrow: 1,
        },
    })
);

export interface JavascriptExpressionMonacoEditorModalProps {
    open: boolean;
    defaultValue?: string;
    onChange?: (value: string | undefined) => void;
    onClose: () => void;
    validator?: (js: string | undefined) => Promise<ErrorInfo[]>;
    freeVariables?: FreeVariableDefinition[];
    returnType?: string;
    importJsCommon?: boolean;
}

export const JavascriptExpressionMonacoEditorModal: FC<JavascriptExpressionMonacoEditorModalProps> = props => {
    const {defaultValue, open, onClose, onChange, validator} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const confirm = useConfirm();
    const loseChangesConfirm = useLoseChangesConfirm();
    const [code, setCode] = useState<string | undefined>(defaultValue);
    const handleOnAccept = useCallback(async () => {
        if (validator) {
            const errors = await validator(code);
            if (errors.length > 0) {
                await confirm({
                    title: t("ErrorsExistInSourceCode"),
                    description: <ErrorViewer error={errors} />,
                    mapCloseTo: "ok",
                    actions: [{type: "ok", text: t("Ok")}],
                });
                return;
            }
        }
        onChange?.(code);
        onClose();
    }, [onChange, code, onClose, confirm, t, validator]);

    const handleOnChange = useCallback((value: string | undefined) => {
        setCode(value);
    }, []);

    const handleOnClose = useCallback(async () => {
        if (defaultValue !== code && !(await loseChangesConfirm())) {
            return;
        }
        onClose();
    }, [code, defaultValue, loseChangesConfirm, onClose]);

    const handleDialogClose = useCallback(async (event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (defaultValue !== code && !(await loseChangesConfirm())) {
            return;
        }
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [code, defaultValue, loseChangesConfirm, onClose]);

    return (
        <Dialog open={open} onClose={handleDialogClose} maxWidth="lg" fullWidth>
            <DialogTitle>{t("EditingJavascriptExpression")}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <JavascriptMonacoEditor
                    {...props}
                    value={code}
                    onChange={handleOnChange}
                    isExpression={true}
                ></JavascriptMonacoEditor>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleOnAccept} color="primary">
                    {t("Accept")}
                </Button>
                <Button onClick={handleOnClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
