import React, {FC, useCallback, useState, useMemo} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Typography from "@material-ui/core/Typography";
import EditIcon from "@material-ui/icons/Edit";
import AddIcon from "@material-ui/icons/Add";
import DeleteIcon from "@material-ui/icons/Delete";
import Divider from "@material-ui/core/Divider";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import IconButton from "@material-ui/core/IconButton";
import ToggleButton from "@material-ui/lab/ToggleButton";
import Tooltip from "@material-ui/core/Tooltip";
import PlayCircleFilledWhiteIcon from "@material-ui/icons/PlayCircleFilledWhite";
import {useTranslation} from "react-i18next";
import {isDefined} from "ts-is-present";
import {useFormContext} from "react-hook-form";
import {clone} from "ramda";

import {ActionType, ContextType} from "../../api/nimflow-api";
import {ActionTypeEditorDialog} from "./action-type-editor-dialog";
import {useArrayField} from "../../utils/use-array-field";
import {OrderableList, OrderableListItem} from "../../components/orderable-list";
import {StandardSection} from "../../components/standard-section";
import {StandardSectionHeader} from "../../components/standard-section-header";
import {FormErrorViewer} from "../../components/form-error-viewer";
import {useOkCancelConfirm} from "../../components/confirm";
import {useContextTypeContext} from "./context-type-context";
import {useTaggings} from "./taggings";

export interface ActionTypesEditorProps {
    onSelectionChanged?: (actionTypeName?: string) => void;
    onDialogVisibilityChanged?: (open: boolean) => void;
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            width: "100%",
            //maxWidth: '36ch',
            backgroundColor: theme.palette.background.paper,
        },
        item: {
            border: `2px solid ${theme.palette.common.action}`,
            borderRadius: 2,
        },
        divider: {
            height: 5,
            background: "transparent",
        },
        inline: {
            display: "inline",
        },
        actionHeading: {
            fontSize: theme.typography.pxToRem(15),
            fontWeight: theme.typography.fontWeightRegular,
        },
        summaryTextContainer: {
            width: "100%",
            height: "100%",
            display: "flex",
            alignItems: "center",
        },
        initialAction: {
            border: "none",
            borderRadius: "50%",
            "&.Mui-selected": {
                color: theme.palette.primary.main,
                backgroundColor: "inherit",
            },
        },
    })
);
interface EditionData {
    index?: number;
    actionType: Partial<ActionType>;
}

export const ActionTypesEditor: FC<ActionTypesEditorProps> = props => {
    const {onSelectionChanged, onDialogVisibilityChanged} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const [selectedIndex, setSelectedIndex] = useState<number>();
    const [editionData, setEditionData] = useState<EditionData>();
    const {errors, control} = useFormContext<ContextType>();
    const {
        fields: actions,
        append: actionsAppend,
        remove: actionsRemove,
        setAt: actionsSetAt,
        move: actionsMove,
    } = useArrayField<ActionType>({name: "actions", control});
    const {fields: initialActions, append: initialActionsAppend, remove: initialActionsRemove} = useArrayField<string>({
        name: "initialActions",
        control,
    });
    const confirm = useOkCancelConfirm();
    const {removeInvalidTaggings} = useTaggings();
    const {
        getJsonSchemaDefinition,
        setJsonSchemaDefinition,
        updateActionJsonSchemaDefinition,
    } = useContextTypeContext();

    const handleListItemClick = (_event: React.MouseEvent<HTMLLIElement>, index: number) => {
        setSelectedIndex(index);
        onSelectionChanged?.(actions[index]?.name);
    };

    const handleNewActionClick = useCallback(() => {
        setEditionData({
            actionType: {
                name: "",
                jsSetters: {},
                validators: [],
            },
        });
        onDialogVisibilityChanged?.(true);
    }, [setEditionData, onDialogVisibilityChanged]);

    const handleDeleteActionTypeClick = useCallback(
        async (index: number) => {
            const actionName = actions[index]?.name;
            let result = await confirm({description: t("ConfirmDeleteActionType", {name: actionName})});
            if (!result) {
                return;
            }
            if (actionName) {
                const initialActionIndex = initialActions.indexOf(actionName);
                if (initialActionIndex >= 0) {
                    initialActionsRemove(initialActionIndex);
                }
            }
            const defName = actions[index]?.payloadSchemaDefinitionName;
            if (defName) {
                setJsonSchemaDefinition(defName, undefined);
            }

            actionsRemove(index);
            removeInvalidTaggings();
            if (selectedIndex === index) {
                onSelectionChanged?.(actions[index]?.name);
            }
        },
        [
            actions,
            confirm,
            t,
            actionsRemove,
            removeInvalidTaggings,
            selectedIndex,
            initialActions,
            initialActionsRemove,
            setJsonSchemaDefinition,
            onSelectionChanged,
        ]
    );

    const actionTypePayloadSchemaDefinition = useMemo(() => {
        if (!editionData) {
            return undefined;
        }
        const defname = editionData.actionType.payloadSchemaDefinitionName;
        if (!defname) {
            return undefined;
        }
        return getJsonSchemaDefinition(defname);
    }, [editionData, getJsonSchemaDefinition]);

    const handleActionTypeChanged = useCallback(
        (actionType: ActionType, definitionJsonSchema: string | undefined) => {
            if (!editionData) {
                return;
            }
            const {index} = editionData;
            if (isDefined(index)) {
                const oldActionName = actions[index]?.name;
                if (oldActionName !== actionType.name && oldActionName) {
                    const index = initialActions.indexOf(oldActionName);
                    if (index >= 0) {
                        initialActionsRemove(index);
                        initialActionsAppend(actionType.name);
                    }
                }
                if (!actionType.payloadSchemaDefinitionName) {
                    const defName = actions[index].payloadSchemaDefinitionName;
                    if (defName) {
                        setJsonSchemaDefinition(defName, undefined);
                    }
                }
                actionsSetAt(index, actionType);
                removeInvalidTaggings();
            } else {
                actionsAppend(actionType);
            }
            updateActionJsonSchemaDefinition(actionType, definitionJsonSchema);
        },
        [
            actions,
            actionsAppend,
            actionsSetAt,
            editionData,
            initialActions,
            initialActionsAppend,
            initialActionsRemove,
            removeInvalidTaggings,
            setJsonSchemaDefinition,
            updateActionJsonSchemaDefinition,
        ]
    );

    const handleToggleInitialAction = useCallback(
        (name?: string) => {
            if (!name) {
                return;
            }
            const index = initialActions.indexOf(name);
            if (index >= 0) {
                initialActionsRemove(index);
            } else {
                initialActionsAppend(name);
            }
        },
        [initialActions, initialActionsAppend, initialActionsRemove]
    );

    const handleMoveAction = useCallback(
        (from: number, to: number) => {
            actionsMove(from, to);
            if (selectedIndex === from) {
                setSelectedIndex(to);
            }
        },
        [actionsMove, selectedIndex]
    );

    const handleEditActionTypeClick = useCallback(
        (index: number, actionType: Partial<ActionType>) => {
            setEditionData({index, actionType: clone(actionType)});
            onDialogVisibilityChanged?.(true);
        },
        [setEditionData, onDialogVisibilityChanged]
    );

    const handleOnCloseDialog = useCallback(() => {
        setEditionData(undefined);
        onDialogVisibilityChanged?.(false);
    }, [setEditionData, onDialogVisibilityChanged]);

    return (
        <StandardSection>
            <StandardSectionHeader
                title={t("Actions")}
                actions={
                    <Tooltip title={t("NewActionType") as string}>
                        <IconButton onClick={handleNewActionClick}>
                            <AddIcon />
                        </IconButton>
                    </Tooltip>
                }
            />
            <OrderableList onItemReorder={handleMoveAction} className={classes.root}>
                {actions.map((s, i) => {
                    const isInitialAction = !!s.name && initialActions.includes(s.name);
                    return (
                        <OrderableListItem
                            key={s.name}
                            index={i}
                            selected={selectedIndex === i}
                            onClick={s => handleListItemClick(s, i)}
                            className={classes.item}
                            afterItem={<Divider className={classes.divider} />}
                        >
                            <ListItemIcon>
                                <Tooltip title={t("MakeInitialAction") as string}>
                                    <ToggleButton
                                        value="initialAction"
                                        selected={isInitialAction}
                                        onClick={() => handleToggleInitialAction(s.name)}
                                        className={classes.initialAction}
                                    >
                                        <PlayCircleFilledWhiteIcon />
                                    </ToggleButton>
                                </Tooltip>
                            </ListItemIcon>
                            <ListItemText
                                primary={<Typography className={classes.actionHeading}>{s.name}</Typography>}
                            />
                            <ListItemSecondaryAction>
                                <Tooltip title={t("Edit") as string}>
                                    <IconButton aria-label="edit" onClick={() => handleEditActionTypeClick(i, s)}>
                                        <EditIcon />
                                    </IconButton>
                                </Tooltip>
                                <Tooltip title={t("Delete") as string}>
                                    <IconButton
                                        edge="end"
                                        aria-label="delete"
                                        onClick={() => handleDeleteActionTypeClick(i)}
                                    >
                                        <DeleteIcon />
                                    </IconButton>
                                </Tooltip>
                            </ListItemSecondaryAction>
                        </OrderableListItem>
                    );
                })}
            </OrderableList>
            <FormErrorViewer errors={errors} names={["initialActions", "actions"]} />
            {isDefined(editionData) && (
                <ActionTypeEditorDialog
                    open={isDefined(editionData)}
                    onClose={handleOnCloseDialog}
                    defaultValue={editionData.actionType}
                    definitionJsonSchema={actionTypePayloadSchemaDefinition}
                    onChange={handleActionTypeChanged}
                />
            )}
        </StandardSection>
    );
};
