import React, {FC, useState, useCallback, useMemo} from "react";
import MenuItem from "@material-ui/core/MenuItem";
import {useFormikContext, getIn} from "formik";
import {isPresent} from "ts-is-present";

import {FormikTextField, FormikTextFieldProps} from "../../components/formik-components/formik-text-field";
import {contextListPropertyNames} from "../../api/nimflow-api-extensions";
import {DashboardWidgetAggregateDataSourceSourceCollection} from "../../api/nimflow-dashboard-widgets-api";
import {MultivaluePropertyValueSelector} from "./multivalue-property-selector-dialog";

export type DataSourcePropertySelectorProps = {
    propertyNames: string[];
    valuePath: string;
    sourceCollection: DashboardWidgetAggregateDataSourceSourceCollection;
    contextTypeName?: string;
} & Omit<FormikTextFieldProps, "select" | "children">;

export const DataSourcePropertySelector: FC<DataSourcePropertySelectorProps> = props => {
    const {propertyNames, name, valuePath, sourceCollection, contextTypeName, SelectProps: userSelectProps,...rest} = props;
    const {values, setFieldValue} = useFormikContext<any>();
    const [editingMultivalueProperty, setEditingMultivalueProperty] = useState<string>();
    const property = getIn(values, name) as string;
    const value = getIn(values, valuePath) as string | undefined;
    const SelectProps = useMemo(() => {
        const result: FormikTextFieldProps["SelectProps"] = {
            ...userSelectProps,
            renderValue: val => {
                const prop = val as string;
                if (
                    sourceCollection === "contexts" &&
                    contextListPropertyNames.includes(prop as any) &&
                    value
                ) {
                    return `${prop}_${value}`;
                }
                return prop;
            },
        };
        return result;
    }, [sourceCollection, userSelectProps, value]);

    const handlePropertyChange = useCallback(
        (value: string | undefined) => {
            if (contextListPropertyNames.includes(value as any)) {
                setEditingMultivalueProperty(property ?? "");
            } else {
                setFieldValue(valuePath, undefined);
            }
        },
        [property, setFieldValue, valuePath]
    );

    const handleMultivaluePropertyValueChange = useCallback(
        (value: string | undefined) => {
            if (value) {
                setFieldValue(valuePath, value);
            } else {
                setFieldValue(name, editingMultivalueProperty ? editingMultivalueProperty : undefined);
            }
        },
        [editingMultivalueProperty, name, setFieldValue, valuePath]
    );

    const handleMultivaluePropertyEditorClose = useCallback(() => {
        setEditingMultivalueProperty(undefined);
    }, []);

    return (
        <>
            <FormikTextField
                name={name}
                InputLabelProps={{required: true}}
                select
                SelectProps={SelectProps}
                {...rest}
            >
                <MenuItem key="undefined" value={undefined} onClick={() => handlePropertyChange(undefined)}>
                    &nbsp;
                </MenuItem>
                {propertyNames.map(s => (
                    <MenuItem key={s} value={s} onClick={() => handlePropertyChange(s)}>
                        {s}
                    </MenuItem>
                ))}
            </FormikTextField>
            {isPresent(editingMultivalueProperty) && (
                <MultivaluePropertyValueSelector
                    open={isPresent(editingMultivalueProperty)}
                    defaultValue={editingMultivalueProperty === property ? value : undefined}
                    property={property}
                    contextTypeName={contextTypeName}
                    onChange={handleMultivaluePropertyValueChange}
                    onClose={handleMultivaluePropertyEditorClose}
                />
            )}
        </>
    );
};
