import {useMemo} from "react";
import {useFormContext} from "react-hook-form";
import {isPresent} from "ts-is-present";

import {BaseTable, BaseTableProps} from "./base-table";
import {useArrayField} from "../utils/use-array-field";

export type RhfArrayFieldEditorProps<T extends object> = {
    path: string;
    defaultValue?: Partial<T>[];
    editable?: BaseTableProps<Partial<T>>["editable"] & {
        allowAddition?: boolean | (() => boolean);
        allowEdition?: boolean | (() => boolean);
        allowDeletion?: boolean | (() => boolean);
    };
} & Omit<BaseTableProps<Partial<T>>, "data">;

const isActionAllowed = (test: boolean | (() => boolean) | undefined): boolean => {
    if (typeof test === "function") {
        test = test();
    }
    return typeof test === "boolean" ? test : !isPresent(test);
};

export const RhfArrayFieldEditor = <T extends object>(props: RhfArrayFieldEditorProps<T>) => {
    const {path, editable, defaultValue, ...rest} = props;
    const {control, trigger} = useFormContext();
    const {fields, append, remove, insert} = useArrayField<T>({
        control,
        name: path,
        defaultValue,
    });

    const tableEditionHandler = useMemo(() => {
        const tableEditionHandler: BaseTableProps<Partial<T>>["editable"] = {
            ...editable,
        };

        const allowAddition = editable?.allowAddition;
        if (isActionAllowed(allowAddition)) {
            tableEditionHandler.onRowAdd = async newData => {
                await editable?.onRowAdd?.(newData);
                append(newData);
                trigger(path);
            };
        }

        const allowEdition = editable?.allowEdition;
        if (isActionAllowed(allowEdition)) {
            tableEditionHandler.onRowUpdate = async (newData, oldData) => {
                const index = oldData && "id" in oldData ? fields.findIndex(s => (s as any).id === (oldData as any).id) : (oldData as any).tableData.id;
                const oldFields = index >= 0 ? fields[index] : undefined;
                await editable?.onRowUpdate?.(newData, oldFields);
                if (index >= 0) {
                    remove(index);
                    insert(index, newData);
                } else {
                    append(newData);
                }
                trigger(path);
            };
        }

        const allowDeletion = editable?.allowDeletion;
        if (isActionAllowed(allowDeletion)) {
            tableEditionHandler.onRowDelete = async oldData => {
                const index = oldData && "id" in oldData ? fields.findIndex(s => (s as any).id === (oldData as any).id) : (oldData as any).tableData.id;
                //const index = entries.findIndex(s => s === oldData);
                if (index >= 0) {
                    await editable?.onRowDelete?.(fields[index]);
                    remove(index);
                }
                trigger(path);
            };
        }
        return tableEditionHandler;
    }, [append, editable, fields, insert, path, remove, trigger]);

    return <BaseTable data={fields} editable={tableEditionHandler} {...rest} />;
};
