import React, {FC, useCallback} from "react";
import {useTranslation} from "react-i18next";
import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import MenuItem from "@material-ui/core/MenuItem";
import {useFormikContext} from "formik";
import {isDefined} from "ts-is-present";

import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {FormikNumberField} from "../../components/formik-components/formik-number-field";
import {FreeTextDashboardParameter, DashboardParameterDataType} from "../../api/nimflow-dashboard-api";

export const FreeTextParameterEditor: FC<{}> = props => {
    const {t} = useTranslation();
    const {values: param, setFieldValue} = useFormikContext<FreeTextDashboardParameter>();

    const handleDataTypeChange = useCallback(
        (ev: React.ChangeEvent<HTMLInputElement>) => {
            const newDataType = ev.target.value as DashboardParameterDataType;
            let defaultValue: any = param.defaultValue.values[0]?.value;
            if (isDefined(defaultValue)) {
                if (newDataType === "string") {
                    defaultValue = defaultValue.toString();
                } else if (typeof defaultValue === "string") /*number*/ {
                    defaultValue = parseFloat(defaultValue);
                    if (isNaN(defaultValue)) {
                        defaultValue = 0;
                    }
                } else {
                    defaultValue = 0;
                }
            }
            setFieldValue("dataType", newDataType);
            setFieldValue("defaultValue.values[0]", {value: defaultValue});
        },
        [param.defaultValue.values, setFieldValue]
    );

    return (
        <>
            <Grid item xs={12}>
                <FormikTextField
                    name="variableName"
                    label={t("VariableName")}
                    InputLabelProps={{required: true}}
                    fullWidth
                    required
                />
            </Grid>
            <Grid item xs={12}>
                <TextField
                    label={t("DataType")}
                    select
                    InputLabelProps={{required: true}}
                    fullWidth
                    required
                    value={param.dataType}
                    onChange={handleDataTypeChange}
                >
                    {["string", "number"].map(s => (
                        <MenuItem key={s} value={s}>
                            {s}
                        </MenuItem>
                    ))}
                </TextField>
            </Grid>
            <Grid item xs={12}>
                {param.dataType === "string" && (
                    <FormikTextField
                        name="defaultValue.values[0].value"
                        label={t("DefaultValue")}
                        InputLabelProps={{required: true}}
                        fullWidth
                        required
                    />
                )}
                {param.dataType !== "string" && (
                    <FormikNumberField
                        name="defaultValue.values[0].value"
                        label={t("DefaultValue")}
                        InputLabelProps={{required: true}}
                        fullWidth
                        required
                    />
                )}
            </Grid>
        </>
    );
};
