import React, {FC, useCallback, useState, useEffect, useMemo} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import {useTranslation} from "react-i18next";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import SaveAltIcon from "@material-ui/icons/SaveAlt";
import SaveIcon from "@material-ui/icons/Save";
import AssignmentTurnedInIcon from "@material-ui/icons/AssignmentTurnedIn";
import Typography from "@material-ui/core/Typography";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import SyncIcon from "@material-ui/icons/Sync";
import PlayArrowIcon from "@material-ui/icons/PlayArrow";
import Grid from "@material-ui/core/Grid";
import Divider from "@material-ui/core/Divider";
//import MuiLink from "@material-ui/core/Link";
import Tooltip from "@material-ui/core/Tooltip";
import AccountTreeIcon from "@material-ui/icons/AccountTree";
import ViewColumnIcon from "@material-ui/icons/ViewColumn";
import {useForm, FormProvider, Controller, UseFormMethods, useFormContext} from "react-hook-form";
import {DevTool} from "@hookform/devtools";
import {useRecoilState} from "recoil";
import {yupResolver} from "@hookform/resolvers/yup";
import {useSnackbar} from "notistack";
import * as yup from "yup";
import {useNavigate, Link} from "react-router-dom";
import {useThrottleCallback} from "@react-hook/throttle";
import {uniq, concat, equals} from "ramda";
import {isPresent} from "ts-is-present";

import {downloadJson} from "../../utils/io-helpers";
import {ContextType} from "../../api/nimflow-api";
import {ContextTypeSchema, jsCommonScriptKey, jsonSchemaScriptKey} from "../../models/context-type-schema";
import {TaskTypesEditor} from "./task-types-editor";
import {ActionTypesEditor} from "./action-types-editor";
import {ContextTypeStateEditor} from "./context-type-state-editor";
import {ContextTypeSelectorsEditor} from "./context-type-selectors-editor";
import {dynamicResolver, get} from "../../libs/react-hooks-form";
import {useExitWithoutSavingBlocker} from "../../utils/use-exit-without-saving-blocker";
import {ContextTypeContextProvider} from "./context-type-context";
import {JsonSchemaIcon} from "../../components/icons/json-schema-icon";
import {JavascriptIcon} from "../../components/icons/javascript-icon";
import {JsonSchemaEditorModal} from "../../components/json-schema-editor-modal";
import {FormErrorViewer} from "../../components/form-error-viewer";
import {ErrorInfo} from "../../models/error-info";
import {JsContextDefinitionsContextProvider} from "../../components/js-context-definitions-provider";
import {useConfirm, useOkCancelConfirm} from "../../components/confirm";
import {JsCommonEditor} from "./js-common-editor";
import {validateWithYupSchema} from "../../libs/yup";
import {ErrorViewer} from "../../components/error-viewer";
import {useNimflow} from "../../api/nimflow-api-provider";
import {useBackgroundJobMonitor} from "../../services/background-job-monitoring-service";
import {ContextTypeDiagramEditor} from "./context-type-diagram-editor";
import {ErrorBoundary} from "../../components/error-boundary";
import {viewContextTypeAsDiagram} from "./context-type-edition.state";
import {exportContextTypeToJson} from "../../models/context-type-exporter";
import {ContextTypeSaveAsDialog} from "./context-type-save-as-dialog";
import {getValidTaggings} from "./taggings";
import {useReportError} from "../../utils/use-report-error";
import {JsonIcon} from "../../components/icons/json-icon";
import {JsonFormsIcon} from "../../components/icons/json-forms-icon";
import {ContextTypeJsonEditorModal} from "./context-type-json-editor-modal";
import {contextElementTypes, ContextElementType} from "./context-type-diagram-model";
import {ContextTypeElementVisibilitySelector} from "./context-type-element-visibility-selector";
import {UiSchemaLibraryEditorModal} from "../../components/ui-schema-library-editor-modal";
import {ContextTypeSettings} from "../../api/nimflow-api-extensions";
import {enableContextTypeDiagram, enableContextTypeRecalculate} from "../../env-vars";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            flexGrow: 1,
        },
        toolbar: {
            minHeight: "48px",
            paddingLeft: 0,
        },
        grow: {
            flexGrow: 1,
        },
        actionToolbarButtons: {
            display: "flex",
        },
        fileInput: {
            opacity: 0,
            position: "absolute",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
        },
        textField: {
            width: "40ch",
        },
        form: {
            height: "100%",
        },
        button: {
            margin: theme.spacing(1),
            whiteSpace: "nowrap",
        },
        diagram: {
            marginTop: 20,
            marginBottom: 20,
            height: "calc(100% - 40px)",
        },
        table: {},
        tableRow: {},
        diagramTableRow: {
            height: "max(calc(100vh - 150px), 600px)",
        },
        saveAsEllipsis: {
            position: "absolute",
            left: 17,
            bottom: 4,
            fontWeight: "bold",
        },
        divider: {
            margin: theme.spacing(1, 0.5),
        },
    })
);

const SaveAsButton = (props: {name: string; canSave: boolean; onSaveAs?: (newName: string) => void}) => {
    const {name, canSave, onSaveAs} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const {trigger} = useFormContext();
    const [saveAsDialogOpen, setSaveAsDialogOpen] = useState(false);

    const handleSaveAsContextTypeClick = useCallback(async () => {
        const result = await trigger();
        if (!result) {
            return;
        }
        setSaveAsDialogOpen(true);
    }, [trigger]);

    const handleOnChange = useCallback(
        (value: string) => {
            onSaveAs?.(value);
        },
        [onSaveAs]
    );
    const handleOnClose = useCallback(() => {
        setSaveAsDialogOpen(false);
    }, []);

    return (
        <>
            <Tooltip title={t("SaveAs") as string}>
                <IconButton onClick={handleSaveAsContextTypeClick} disabled={!canSave}>
                    <SaveIcon />
                    <Typography className={classes.saveAsEllipsis}>...</Typography>
                </IconButton>
            </Tooltip>
            {saveAsDialogOpen && (
                <ContextTypeSaveAsDialog
                    open={saveAsDialogOpen}
                    defaultValue={name}
                    onChange={handleOnChange}
                    onClose={handleOnClose}
                />
            )}
        </>
    );
};

export interface ContextTypeEditorProps {
    value: ContextType;
    schema?: yup.ObjectSchema<any>;
    onApplyChanges?: (contextType: ContextType) => Promise<boolean>;
    onSaveAs?: (contextType: ContextType, newName: string) => Promise<boolean>;
    isPersistent?: boolean;
}

const getContextTypingsProvider = (
    getValues: UseFormMethods<ContextType>["getValues"]
): Record<string, () => string | Promise<string>> => {
    return {
        state: () => {
            const actions = getValues("actions");
            const tasks = getValues("tasks");
            const selectors = getValues("selectors");
            const keys = uniq(
                concat(
                    concat(
                        actions.flatMap(s => Object.keys(s.jsSetters)),
                        tasks.flatMap(s => s.validResponses).flatMap(s => Object.keys(s.jsSetters))
                    ),
                    selectors?.map(s => s.key).filter(isPresent) ?? []
                )
            );
            return `declare const state: {
                ${keys.map(s => s + ": any;").join("\n")}
            }`;
        },
        task: () => {
            return `
            interface ContextTask {
                id: string;
                contextId: string;
                contextTypeName: string;
                contextReference?: string | undefined;
                typeName: string;
                title: string;
                data: { [key: string]: any; };
                props?: { [key: string]: any; } | undefined;
                dateCreated: string;
                dateUpdated: string;
                assignedTo?: string | undefined;
                status?: string | undefined;
                archived: boolean;
                dueDate?: string | undefined;
                /** 1 = high, 0 = normal, -1 = low */
                importance: 1 | 0 | -1;
                focused: boolean;
            }

            declare const task: ContextTask;`;
        },
    };
};

const getJsCommonTypingProvider = (
    getValues: UseFormMethods<ContextType>["getValues"]
): (() => string | Promise<string>) => {
    return () => {
        return getValues("jsCommon") ?? "";
    };
};

export const ContextTypeEditor: FC<ContextTypeEditorProps> = props => {
    const {value, schema = ContextTypeSchema, onApplyChanges, onSaveAs, isPersistent = false} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const navigate = useNavigate();
    const [selectedAction, setSelectedAction] = useState<string>();
    // Workaround because RHF DevTools does not support multiple forms.
    const [actionTypeEditorOpen, setActionTypeEditorOpen] = useState(false);
    const [taskTypeEditorOpen, setTaskTypeEditorOpen] = useState(false);
    const [editJsCommon, setEditJsCommon] = useState(false);
    const [editJsonSchema, setEditJsonSchema] = useState(false);
    const [editUiSchemaCommon, setEditUiSchemaCommon] = useState(false);
    const [editContextTypeJson, setEditContextTypeJson] = useState(false);
    const [visibleElementTypes, setVisibleElementTypes] = React.useState(() => [...contextElementTypes]);
    const {enqueueSnackbar} = useSnackbar();
    const [isSaving, setIsSaving] = useState(false);
    const canSave = !isSaving;
    const {jobAdded} = useBackgroundJobMonitor();
    const {contexts_BeginActiveContextRecalculation} = useNimflow();
    const [viewAsDiagram, setViewAsDiagram] = useRecoilState(viewContextTypeAsDiagram);
    const reportErrors = useReportError();
    const confirm = useConfirm();
    const okCancelConfirm = useOkCancelConfirm();
    const defaultValues = useMemo(() => {
        return {
            ...value,
            version: value.version ?? 0,
            description: value.description ?? "",
        };
    }, [value]);
    const formMethods = useForm<ContextType>({
        mode: "onChange",
        reValidateMode: "onChange",
        defaultValues,
        resolver: dynamicResolver(
            () => {
                return yupResolver(schema, {
                    strict: true,
                    abortEarly: false,
                });
            },
            value => {
                return {
                    [jsCommonScriptKey]: value.jsCommon,
                    [jsonSchemaScriptKey]: value.jsonSchema,
                };
            }
        ),
    });
    const {control, handleSubmit, getValues, setValue, errors, trigger, register, formState, reset} = formMethods;
    const opts = useMemo(() => {
        return {
            description: t("ExitingContextTypeEditorWithoutSavingWarning"),
        };
    }, [t]);
    useExitWithoutSavingBlocker(formState.isDirty, opts);

    useEffect(() => {
        reset(defaultValues);
    }, [defaultValues, reset]);

    useMemo(() => {
        //Force registration before first rendering
        register("jsCommon");
        register("jsonSchema");
        register("version");
        register("initialActions");
        register("actions");
        register("tasks");
        register("selectors");
        register("settings");
    }, [register]);

    useEffect(() => {
        const selectors = getValues("selectors");
        if (selectors) {
            for (let i = 0; i < selectors.length; i++) {}
        }
    }, [getValues]);

    const removeInvalidTaggings = useCallback(() => {
        const contextType = getValues();
        const [cleanedMilestones, cleanedTags] = getValidTaggings(contextType);
        if (cleanedMilestones.length !== contextType.milestones.length) {
            setValue("milestones", cleanedMilestones, {shouldDirty: true});
        }
        if (contextType.tags && cleanedTags.length !== contextType.tags.length) {
            setValue("tags", cleanedTags, {shouldDirty: true});
        }
    }, [getValues, setValue]);

    const handleSaveContextTypeClick = useThrottleCallback(
        useCallback(async () => {
            removeInvalidTaggings();
            const result = await trigger();
            if (!result) {
                return;
            }
            handleSubmit(async data => {
                if (onApplyChanges) {
                    setIsSaving(true);
                    let error: React.ReactNode;
                    try {
                        const saved = await onApplyChanges(data);
                        if (!saved) {
                            return true;
                        }
                    } catch (e) {
                        error = <ErrorViewer error={e} />;
                    } finally {
                        setIsSaving(false);
                    }
                    if (error) {
                        await confirm({
                            title: t("ErrorSavingContextType"),
                            description: error,
                            mapCloseTo: "ok",
                            actions: [{type: "ok", text: t("Ok")}],
                        });
                    } else {
                        enqueueSnackbar(t("ContextTypeSaved"), {variant: "success"});
                    }
                }
            })();
        }, [confirm, enqueueSnackbar, handleSubmit, onApplyChanges, removeInvalidTaggings, t, trigger])
    );

    const handleOnSaveAs = useCallback(
        async (newName: string) => {
            handleSubmit(async data => {
                if (onSaveAs) {
                    setIsSaving(true);
                    let error: React.ReactNode;
                    try {
                        const saved = await onSaveAs(data, newName);
                        if (!saved) {
                            return;
                        }
                    } catch (e) {
                        error = <ErrorViewer error={e} />;
                    } finally {
                        setIsSaving(false);
                    }
                    if (error) {
                        await confirm({
                            title: t("ErrorSavingContextType"),
                            description: error,
                            mapCloseTo: "ok",
                            actions: [{type: "ok", text: t("Ok")}],
                        });
                    } else {
                        enqueueSnackbar(t("ContextTypeSaved"), {variant: "success"});
                    }
                }
            })();
        },
        [handleSubmit, onSaveAs, confirm, t, enqueueSnackbar]
    );

    const handleExportContextTypeClick = useCallback(() => {
        const value = getValues();
        downloadJson(exportContextTypeToJson(value), `${value.name}.context.json`);
    }, [getValues]);

    const handleValidateContextTypeClick = useCallback(async () => {
        removeInvalidTaggings();
        const value = getValues();
        const errorInfos = await validateWithYupSchema(schema, value, {
            context: {
                [jsCommonScriptKey]: value.jsCommon,
                [jsonSchemaScriptKey]: value.jsonSchema,
            },
        });
        await trigger();
        if (errorInfos.length > 0) {
            reportErrors(t("ContextTypeHasValidationErrors"), errorInfos, {
                dialogProps: {
                    maxWidth: "lg",
                },
            });
        } else {
            enqueueSnackbar(t("ContextTypeIsValid"), {variant: "success"});
        }
    }, [removeInvalidTaggings, getValues, schema, trigger, reportErrors, t, enqueueSnackbar]);

    const handleViewAsDiagramClick = useCallback(async () => {
        setViewAsDiagram(!viewAsDiagram);
    }, [viewAsDiagram, setViewAsDiagram]);

    const handleEditJsCommonClick = useCallback(() => {
        setEditJsCommon(true);
    }, []);

    const handleEditJsonSchemaClick = useCallback(() => {
        setEditJsonSchema(true);
    }, []);

    const handleEditUiSchemaCommonClick = useCallback(() => {
        setEditUiSchemaCommon(true);
    }, []);

    const handleGoBack = useCallback(() => {
        navigate(-1);
    }, [navigate]);

    const handleRecalculate = useCallback(async () => {
        const result = await okCancelConfirm({
            description: t("ConfirmContextRecalculation"),
        });
        if (!result) {
            return;
        }
        const {backgroundJobId} = await contexts_BeginActiveContextRecalculation({
            contextTypeName: value.name,
            ignoreMilestones: false,
            allVersions: true,
            continueOnError: true,
        });
        jobAdded(backgroundJobId);
    }, [contexts_BeginActiveContextRecalculation, jobAdded, okCancelConfirm, t, value.name]);

    const freeVariableTypingProviders: Record<string, () => string | Promise<string>> = useMemo(() => {
        return getContextTypingsProvider(getValues);
    }, [getValues]);

    const jsCommonTypingProvider: () => string | Promise<string> = useMemo(() => {
        return getJsCommonTypingProvider(getValues);
    }, [getValues]);

    const jsCommonValidator = useCallback((js: string | undefined): Promise<ErrorInfo[]> => {
        return validateWithYupSchema(ContextTypeSchema.fields?.jsCommon, js);
    }, []);

    const handleEditContextTypeJsonClick = useCallback(() => {
        setEditContextTypeJson(true);
    }, []);

    const handleContextTypeEdited = useCallback(
        (value: ContextType) => {
            const fields = control.fieldsRef.current;
            if (!fields) {
                return;
            }
            for (const name of Object.keys(fields)) {
                const currentValue = getValues(name);
                const newValue = get(value, name);
                if (!equals(currentValue, newValue)) {
                    setValue(name, newValue, {shouldDirty: true, shouldValidate: true});
                }
            }
        },
        [control.fieldsRef, getValues, setValue]
    );

    const handleVisibleElementTypesChange = useCallback((newVisibleElementTypes: ContextElementType[]) => {
        setVisibleElementTypes(newVisibleElementTypes);
    }, []);

    const getUiSchemaCommon = () => {
        const settings = getValues("settings") as ContextTypeSettings;
        return settings?.uiSchemaCommon;
    };

    const setUiSchemaCommon = (uiSchemaCommon?: string) => {
        const settings: ContextTypeSettings = getValues("settings") ?? {};
        setValue(
            "settings",
            {
                ...settings,
                uiSchemaCommon,
            },
            {shouldDirty: true}
        );
    };

    return (
        <ErrorBoundary>
            <JsContextDefinitionsContextProvider
                freeVariableTypingProviders={freeVariableTypingProviders}
                jsCommonTypingProvider={jsCommonTypingProvider}
            >
                <FormProvider {...formMethods}>
                    <form className={classes.form}>
                        <ContextTypeContextProvider control={control}>
                            <div className={classes.table}>
                                <div className={classes.tableRow}>
                                    <Grid container spacing={1}>
                                        <Grid item xs={12}>
                                            <Toolbar className={classes.toolbar}>
                                                <Tooltip title={t("GoBack") as string}>
                                                    <IconButton onClick={handleGoBack} disabled={isSaving}>
                                                        <ArrowBackIcon />
                                                    </IconButton>
                                                </Tooltip>
                                                <Tooltip title={t("SaveChanges") as string}>
                                                    <IconButton
                                                        onClick={handleSaveContextTypeClick}
                                                        disabled={!canSave}
                                                    >
                                                        <SaveIcon />
                                                    </IconButton>
                                                </Tooltip>
                                                {isPersistent && (
                                                    <SaveAsButton
                                                        name={getValues("name")}
                                                        canSave={canSave}
                                                        onSaveAs={handleOnSaveAs}
                                                    />
                                                )}
                                                <Tooltip title={t("Export") as string}>
                                                    <IconButton onClick={handleExportContextTypeClick}>
                                                        <SaveAltIcon />
                                                    </IconButton>
                                                </Tooltip>
                                                <Tooltip title={t("Validate") as string}>
                                                    <IconButton onClick={handleValidateContextTypeClick}>
                                                        <AssignmentTurnedInIcon />
                                                    </IconButton>
                                                </Tooltip>
                                                {enableContextTypeDiagram && (
                                                    <>
                                                        {viewAsDiagram ? (
                                                            <Tooltip title={t("StandardView") as string}>
                                                                <IconButton onClick={handleViewAsDiagramClick}>
                                                                    <ViewColumnIcon />
                                                                </IconButton>
                                                            </Tooltip>
                                                        ) : (
                                                            <Tooltip title={t("DiagramView") as string}>
                                                                <IconButton onClick={handleViewAsDiagramClick}>
                                                                    <AccountTreeIcon />
                                                                </IconButton>
                                                            </Tooltip>
                                                        )}
                                                    </>
                                                )}
                                                <Tooltip title={t("EditContextTypeAsJson") as string}>
                                                    <IconButton onClick={handleEditContextTypeJsonClick}>
                                                        <JsonIcon />
                                                    </IconButton>
                                                </Tooltip>
                                                {viewAsDiagram && (
                                                    <>
                                                        <Divider
                                                            flexItem
                                                            orientation="vertical"
                                                            className={classes.divider}
                                                        />
                                                        <ContextTypeElementVisibilitySelector
                                                            value={visibleElementTypes}
                                                            onChange={handleVisibleElementTypesChange}
                                                        />
                                                    </>
                                                )}
                                                <div className={classes.grow} />
                                                {isPersistent && enableContextTypeRecalculate && (
                                                    <Button
                                                        variant="outlined"
                                                        onClick={handleRecalculate}
                                                        startIcon={<SyncIcon />}
                                                        className={classes.button}
                                                        size="small"
                                                    >
                                                        {t("Recalculate")}
                                                    </Button>
                                                )}
                                                {isPersistent && (
                                                    <Link
                                                        to={`/simulator?contextTypeName=${value.name}`}
                                                        style={{textDecoration: "none"}}
                                                    >
                                                        <Button
                                                            variant="outlined"
                                                            startIcon={<PlayArrowIcon />}
                                                            className={classes.button}
                                                            size="small"
                                                        >
                                                            {t("Simulate")}
                                                        </Button>
                                                    </Link>
                                                )}
                                                <Button
                                                    variant="outlined"
                                                    onClick={handleEditJsCommonClick}
                                                    startIcon={<JavascriptIcon />}
                                                    className={classes.button}
                                                    size="small"
                                                >
                                                    {t("EditJsCommon")}
                                                </Button>
                                                <Button
                                                    variant="outlined"
                                                    onClick={handleEditJsonSchemaClick}
                                                    startIcon={<JsonSchemaIcon />}
                                                    className={classes.button}
                                                    size="small"
                                                >
                                                    {t("EditJsonSchema")}
                                                </Button>
                                                <Button
                                                    variant="outlined"
                                                    onClick={handleEditUiSchemaCommonClick}
                                                    startIcon={<JsonFormsIcon />}
                                                    className={classes.button}
                                                    size="small"
                                                >
                                                    {t("EditUiSchemaCommon")}
                                                </Button>
                                            </Toolbar>
                                        </Grid>
                                        {!viewAsDiagram && (
                                            <>
                                                <Grid item xs={3}>
                                                    <Controller
                                                        as={TextField}
                                                        control={control}
                                                        name="name"
                                                        label={t("Name")}
                                                        className={classes.textField}
                                                        required
                                                        error={!!errors.name}
                                                        helperText={errors.name?.message}
                                                    />
                                                </Grid>
                                                <Grid item xs={9}>
                                                    <Controller
                                                        as={TextField}
                                                        control={control}
                                                        name="description"
                                                        label={t("Description")}
                                                        multiline
                                                        fullWidth
                                                        maxRows={4}
                                                        error={!!errors.description}
                                                        helperText={errors.description?.message}
                                                    />
                                                </Grid>
                                                {/* <Grid item xs={3}>
                                                    <Grid container direction="row" justifyContent="flex-end">
                                                        <Button
                                                            variant="outlined"
                                                            onClick={handleEditJsCommonClick}
                                                            startIcon={<JavascriptIcon />}
                                                            className={classes.button}
                                                        >
                                                            {t("EditJsCommon")}
                                                        </Button>
                                                        <Button
                                                            variant="outlined"
                                                            onClick={handleEditJsonSchemaClick}
                                                            startIcon={<JsonSchemaIcon />}
                                                            className={classes.button}
                                                        >
                                                            {t("EditJsonSchema")}
                                                        </Button>
                                                    </Grid>
                                                </Grid> */}
                                                <Grid item xs>
                                                    <ActionTypesEditor
                                                        onSelectionChanged={setSelectedAction}
                                                        onDialogVisibilityChanged={setActionTypeEditorOpen}
                                                    />
                                                </Grid>
                                                <Grid item xs={6}>
                                                    <Grid container spacing={3}>
                                                        <Grid item xs={12}>
                                                            <ContextTypeStateEditor
                                                                currentActionTypeName={selectedAction}
                                                            />
                                                        </Grid>
                                                        <Grid item xs={12}>
                                                            <ContextTypeSelectorsEditor path="selectors" />
                                                        </Grid>
                                                    </Grid>
                                                </Grid>
                                                <Grid item xs>
                                                    <TaskTypesEditor
                                                        onDialogVisibilityChanged={setTaskTypeEditorOpen}
                                                    />
                                                </Grid>
                                            </>
                                        )}
                                    </Grid>
                                </div>
                                <FormErrorViewer
                                    errors={errors}
                                    names={["jsCommon", "jsonSchema"]}
                                    className={classes.tableRow}
                                />
                                {viewAsDiagram && (
                                    <div className={classes.diagramTableRow}>
                                        <ContextTypeDiagramEditor
                                            className={classes.diagram}
                                            visibleElementTypes={visibleElementTypes}
                                            onEditJsCommon={handleEditJsCommonClick}
                                            onEditJsonSchema={handleEditJsonSchemaClick}
                                        />
                                    </div>
                                )}
                            </div>
                        </ContextTypeContextProvider>
                    </form>
                </FormProvider>
                {!actionTypeEditorOpen && !taskTypeEditorOpen && <DevTool control={control} />}
                {editJsCommon && (
                    <JsCommonEditor
                        open={editJsCommon}
                        onClose={() => setEditJsCommon(false)}
                        defaultValue={getValues("jsCommon")}
                        onChange={value => setValue("jsCommon", value ?? undefined, {shouldDirty: true})}
                        validator={jsCommonValidator}
                    />
                )}
                {editJsonSchema && (
                    <JsonSchemaEditorModal
                        open={editJsonSchema}
                        onClose={() => setEditJsonSchema(false)}
                        defaultValue={getValues("jsonSchema")}
                        onChange={value => setValue("jsonSchema", value ?? undefined, {shouldDirty: true})}
                    />
                )}
                {editUiSchemaCommon && (
                    <UiSchemaLibraryEditorModal
                        open={editUiSchemaCommon}
                        onClose={() => setEditUiSchemaCommon(false)}
                        defaultValue={getUiSchemaCommon()}
                        onChange={value => setUiSchemaCommon(value ?? undefined)}
                    />
                )}
                {editContextTypeJson && (
                    <ContextTypeJsonEditorModal
                        open={editContextTypeJson}
                        onClose={() => setEditContextTypeJson(false)}
                        defaultValue={getValues()}
                        onChange={handleContextTypeEdited}
                    />
                )}
            </JsContextDefinitionsContextProvider>
        </ErrorBoundary>
    );
};
