import React, {FC, useState, useCallback, Suspense} from "react";
import clsx from "clsx";
import {makeStyles, useTheme, Theme, createStyles} from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import CssBaseline from "@material-ui/core/CssBaseline";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import Button from "@material-ui/core/Button";
import useScrollTrigger from "@material-ui/core/useScrollTrigger";
import ToggleButtonGroup from "@material-ui/lab/ToggleButtonGroup";
import MenuItem from "@material-ui/core/MenuItem";
import {useTranslation} from "react-i18next";
import {Outlet} from "react-router-dom";

import {UserMenuButton} from "./user-menu-button";
import {TogglePaletteTypeButton} from "./toggle-palette-type-button";
import {SideMenu} from "./side-menu";
import logoDark from "./logo_negative.png";
import logoLight from "./logo_positive.png";
import {NotificationsViewer} from "./notifications-viewer";
import {NotificationsButton} from "./notifications-button";
import {WaitSpinner} from "../../components/wait-spinner";
import {useAuthenticationService} from "../auth/authentication-service";

const drawerWidth = 160;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            display: "flex",
            height: "100%",
        },
        appBar: {
            transition: theme.transitions.create(["margin", "width"], {
                easing: theme.transitions.easing.sharp,
                duration: theme.transitions.duration.leavingScreen,
            }),
            maxHeight: 64,
            backgroundColor:
                theme.palette.type === "dark" ? theme.palette.background.paper : theme.palette.primary.main,
            zIndex: theme.zIndex.drawer + 1,
        },
        appBarShift: {
            width: `calc(100% - ${drawerWidth}px)`,
            marginLeft: drawerWidth,
            transition: theme.transitions.create(["margin", "width"], {
                easing: theme.transitions.easing.easeOut,
                duration: theme.transitions.duration.enteringScreen,
            }),
        },
        menuButton: {
            marginRight: theme.spacing(2),
        },
        hide: {
            display: "none",
        },
        drawer: {
            width: drawerWidth,
            flexShrink: 0,
        },
        drawerPaper: {
            width: drawerWidth,
        },
        drawerHeader: {
            display: "flex",
            alignItems: "center",
            padding: theme.spacing(0, 1),
            // necessary for content to be below app bar
            ...theme.mixins.toolbar,
            justifyContent: "flex-end",
        },
        drawerCloseIcon: {
            padding: 0,
        },
        content: {
            flexGrow: 1,
            padding: `${theme.spacing(2)}px ${theme.spacing(3)}px`,
            transition: theme.transitions.create("margin", {
                easing: theme.transitions.easing.sharp,
                duration: theme.transitions.duration.leavingScreen,
            }),
            marginLeft: -drawerWidth,
        },
        contentShift: {
            transition: theme.transitions.create("margin", {
                easing: theme.transitions.easing.easeOut,
                duration: theme.transitions.duration.enteringScreen,
            }),
            marginLeft: 0,
        },
        grow: {
            flexGrow: 1,
        },
        actionToolbarButtons: {
            display: "flex",
        },
        logo: {
            width: "60%",
            height: "auto",
        },
        mainContent: {
            height: `calc(100% - ${theme.mixins.toolbar.minHeight}px)`,
        },
    })
);

interface ElevationScrollProps {
    children: React.ReactElement;
}

const ElevationScroll: FC<ElevationScrollProps> = props => {
    const {children} = props;
    const trigger = useScrollTrigger({
        disableHysteresis: true,
        threshold: 0,
    });

    return React.cloneElement(children, {
        elevation: trigger ? 4 : 0,
    });
};

const UserPanel: FC<{}> = () => {
    const {t} = useTranslation();
    const {isAuthenticated, login} = useAuthenticationService();

    return (
        <>
            {!isAuthenticated && (
                <Button onClick={login} color="inherit">
                    {t("Login")}
                </Button>
            )}
            {isAuthenticated && <UserMenuButton color="inherit" />}
        </>
    );
};

export const Shell: FC<{}> = () => {
    const {t} = useTranslation();
    const classes = useStyles();
    const theme = useTheme();
    const [open, setOpen] = useState(true);
    const [selectedPanel, setSelectedPanel] = useState<string>();

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    const handleSelectPanel = useCallback((event: React.MouseEvent<HTMLElement>, newSelection: string | null) => {
        setSelectedPanel(newSelection ?? undefined);
    }, []);

    const spinner = <WaitSpinner>{t("SuspenseLoading")}</WaitSpinner>;

    return (
        <div className={classes.root}>
            <CssBaseline />
            <ElevationScroll>
                <AppBar
                    position="fixed"
                    className={clsx(classes.appBar, {
                        [classes.appBarShift]: open,
                    })}
                >
                    <Toolbar>
                        <IconButton
                            color="inherit"
                            aria-label="open drawer"
                            onClick={handleDrawerOpen}
                            edge="start"
                            className={clsx(classes.menuButton, open && classes.hide)}
                        >
                            <MenuIcon />
                        </IconButton>
                        <Typography variant="h6" noWrap>
                            Studio
                        </Typography>
                        <div className={classes.grow} />
                        <div className={classes.actionToolbarButtons}>
                            <ToggleButtonGroup value={selectedPanel} exclusive onChange={handleSelectPanel}>
                                <NotificationsButton value="notifications" />
                            </ToggleButtonGroup>
                            <TogglePaletteTypeButton color="inherit" />
                            <UserPanel />
                        </div>
                    </Toolbar>
                </AppBar>
            </ElevationScroll>
            <Drawer
                className={classes.drawer}
                variant="persistent"
                anchor="left"
                open={open}
                classes={{
                    paper: classes.drawerPaper,
                }}
            >
                <div className={classes.drawerHeader}>
                    <MenuItem disableGutters>
                        <img
                            src={theme.palette.type === "light" ? logoLight : logoDark}
                            alt="logo"
                            width="100%"
                            height="auto"
                        />
                    </MenuItem>
                    <IconButton onClick={handleDrawerClose} className={classes.drawerCloseIcon}>
                        {theme.direction === "ltr" ? <ChevronLeftIcon /> : <ChevronRightIcon />}
                    </IconButton>
                </div>
                <Divider />
                <SideMenu />
            </Drawer>
            <main
                className={clsx(classes.content, {
                    [classes.contentShift]: open,
                })}
            >
                <div className={classes.drawerHeader} />
                <div className={classes.mainContent}>
                    <Suspense fallback={spinner}>
                        <Outlet />
                    </Suspense>
                </div>
            </main>
            {selectedPanel === "notifications" && (
                <NotificationsViewer
                    open={selectedPanel === "notifications"}
                    onClose={() => setSelectedPanel(undefined)}
                />
            )}
        </div>
    );
};
