import {Direction} from "@material-ui/core/styles";
import {PaletteType} from "@material-ui/core";
import createTheme from "@material-ui/core/styles/createTheme";

export interface ThemeOptions {
    direction: Direction;
    paletteType: PaletteType;
}

export const getTheme = (options: ThemeOptions) => {
    const {direction, paletteType: type} = options;
    return createTheme({
        direction,
        palette: {
            type,
            primary: {
                main: "#EF7D00",
                contrastText: "#ffff",
            },
            secondary: {
                main: "#393D41",
            },
            // background: {
            //     default: type === "light" ? "#fff" : "#121212",
            // },
            common: {
                task: "#EF7D00",
                action: "#EF7D00",
            },
        },
        overrides: {
            MuiCssBaseline: {
                "@global": {
                    "*": {
                        "scrollbar-width": "thin",
                    },
                    // "*::-webkit-scrollbar": {
                    //     width: "14px",
                    //     height: "6px",
                    // },
                },
            },
        },
    });
};
