import {FC, useCallback, useState, useEffect, useMemo, useRef} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Button from "@material-ui/core/Button";
import {useTranslation} from "react-i18next";
import MonacoEditor, {EditorDidMount, EditorWillMount} from "react-monaco-editor";
import {useTheme} from "@material-ui/core/styles";
import FormHelperText from "@material-ui/core/FormHelperText";
import * as yup from "yup";
import {clone, head} from "ramda";
import {JSONSchema7} from "json-schema";
import Autosizer from "react-virtualized-auto-sizer";
import {isPresent} from "ts-is-present";
import useSWR from "swr";

import {validateWithYupSchema} from "../libs/yup";
import uiSchemaSchema from "../schemas/ui-schema.schema.json";
import {useLoseChangesConfirm} from "./specialized-confirmations";
import {jsonSchemaScriptKey} from "../models/context-type-schema";
import {defaultMonacoOptions} from "../libs/monaco";
import {useNimflowFunctions} from "../api/nimflow-functions-api";
import {useKey} from "../utils/use-key";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        dialogPaper: {
            height: "90vh",
        },
        dialogContent: {
            display: "grid",
            gridTemplateColumns: "auto",
            gridTemplateRows: "auto",
            rowGap: "5px",
            height: "100%",
            overflowY: "hidden",
        },
        editorContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 99",
            alignSelf: "stretch",
        },
        errorContainer: {
            gridColumn: "1 / 2",
            gridRow: "99 / 100",
            overflowY: "auto",
        },
    })
);

export type UiSchemaEditorModalProps = {
    open: boolean;
    defaultValue?: string;
    jsonSchema?: JSONSchema7;
    uiSchemaLibrary?: Record<string, any>;
    onChange?: (value?: string) => void;
    onClose: () => void;
};

export const useUiSchema = (uiSchemaLibrary?: Record<string, any>) => {
    const {getModules} = useNimflowFunctions();
    const key = useKey(getModules);
    const {data: modules} = useSWR(
        key,
        async () => {
            return await getModules(true);
        },
        {
            revalidateOnFocus: false,
        }
    );
    const schema = useMemo(() => {
        if (!modules && !uiSchemaLibrary) {
            return uiSchemaSchema;
        }
        const schema = clone(uiSchemaSchema);
        if (uiSchemaLibrary) {
            const use = schema.definitions.UseElement as any;
            const name = use.properties.name;
            name.enum = Object.keys(uiSchemaLibrary);
        }
        if (modules) {
            const fndef: any = {
                properties: {
                    moduleName: {
                        enum: modules.map(s => s.name).filter(isPresent),
                    },
                },
                allOf: modules.map(s => {
                    const result =                 {
                        if: {
                            properties: {
                                moduleName: {const: s.name}
                            },
                            required: ["moduleName"]
                        },
                        then: {
                            properties: {
                                functionName: {
                                    enum: s.functions?.map(f => f.name).filter(isPresent) ?? []
                                }
                            },
                            required: ["moduleName", "functionName"]
                        }
                    };
                    return result;
                })
            };
            const opts = schema.definitions.OptionsSource as any;
            opts.properties = fndef.properties;
            opts.allOf = fndef.allOf;
            const srcs = schema.definitions.DocumentSchemasSource as any;
            srcs.properties = fndef.properties;
            srcs.allOf = fndef.allOf;
        }
        return schema;
    }, [modules, uiSchemaLibrary]);

    return schema;
};

const updateUiSchema = (schema: any, monaco: Parameters<EditorWillMount>[0]) => {
    monaco.languages.json.jsonDefaults.setDiagnosticsOptions({
        validate: true,
        schemas: [
            {
                uri: schema.$id,
                fileMatch: ["inmemory://model/*"],
                schema,
            },
        ],
    });
    // for (const model of monaco.editor.getModels()) {
    //     console.log(model);
    // }
};

const configureMonaco: EditorWillMount = monaco => {
    const options: ReturnType<EditorWillMount> = {
        selectOnLineNumbers: true,
        //codeLens: false,
        minimap: {
            enabled: false,
        },
    };
    return options;
};

const editorDidMount: EditorDidMount = editor => {
    editor.focus();
};

const validationSchema = yup.string().uiSchema(jsonSchemaScriptKey);

export const UiSchemaEditorModal: FC<UiSchemaEditorModalProps> = props => {
    const {open, defaultValue, jsonSchema, uiSchemaLibrary, onChange, onClose} = props;
    const {t} = useTranslation();
    const theme = useTheme();
    const classes = useStyles();
    const loseChangesConfirm = useLoseChangesConfirm();
    const [error, setError] = useState<string>();
    const monacoRef = useRef<Parameters<EditorWillMount>[0]>();
    const uiSchema = useUiSchema(uiSchemaLibrary);
    const initialValue = useMemo(() => {
        if (!defaultValue) {
            return "";
        }
        if (defaultValue.includes("\n")) {
            return defaultValue;
        }
        return JSON.stringify(JSON.parse(defaultValue), undefined, 2);
    }, [defaultValue]);

    const [code, setCode] = useState(initialValue);

    useEffect(() => {
        const monaco = monacoRef.current;
        if (monaco && uiSchema) {
            updateUiSchema(uiSchema, monaco);
        }
    }, [uiSchema]);

    const handleOnAccept = useCallback(async () => {
        if (code.trim().length === 0) {
            onChange?.(undefined);
            onClose();
            return;
        }
        const einfo = head(
            await validateWithYupSchema(validationSchema, code, {
                context: {
                    [jsonSchemaScriptKey]: jsonSchema,
                },
            })
        );
        if (einfo) {
            setError(einfo.message);
            return;
        }
        onChange?.(code);
        onClose();
    }, [code, onChange, onClose, jsonSchema]);

    const handleOnClose = useCallback(async () => {
        if (initialValue !== code && !(await loseChangesConfirm())) {
            return;
        }
        onClose();
    }, [code, initialValue, loseChangesConfirm, onClose]);

    const handleDialogClose = useCallback(
        async (event: object, reason: "backdropClick" | "escapeKeyDown") => {
            if (initialValue !== code && !(await loseChangesConfirm())) {
                return;
            }
            if (reason !== "backdropClick") {
                onClose?.();
            }
        },
        [code, initialValue, loseChangesConfirm, onClose]
    );

    const editorWillMount: EditorWillMount = useCallback(
        monaco => {
            monacoRef.current = monaco;
            configureMonaco(monaco);
        },
        []
    );

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            maxWidth="xl"
            fullWidth
            classes={{
                paper: classes.dialogPaper,
            }}
        >
            <DialogTitle>{t("EditingUiSchema")}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <div className={classes.editorContainer}>
                    <Autosizer>
                        {({height, width}) => (
                            <MonacoEditor
                                width={width}
                                height={height - 50}
                                language="json"
                                theme={theme.palette.type === "light" ? "vs-light" : "vs-dark"}
                                value={code}
                                options={defaultMonacoOptions}
                                onChange={setCode}
                                editorWillMount={editorWillMount}
                                editorDidMount={editorDidMount}
                            />
                        )}
                    </Autosizer>
                </div>
                <div className={classes.errorContainer}>
                    <FormHelperText component="span" error>
                        {error}
                    </FormHelperText>
                </div>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleOnAccept} color="primary">
                    {t("Accept")}
                </Button>
                <Button onClick={handleOnClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
