const stateRegex = /(?:^|[^a-zA-Z0-9_.]|\.\.\.)state\.([a-zA-Z_][a-zA-Z0-9_]*)/g;

// Assumes that references are of the form "state.<name>"
export const extractStateRefs = (jsExprs?: string | string[]): string[] => {
    const result: string[] = [];
    if (!jsExprs) {
        return result;
    }
    if (!Array.isArray(jsExprs)) {
        jsExprs = [jsExprs];
    }
    let matchArray: RegExpExecArray | null;
    for (const jsExpr of jsExprs) {
        while ((matchArray = stateRegex.exec(jsExpr)) !== null) {
            if (!result.includes(matchArray[1])) {
                result.push(matchArray[1]);
            }
        }
    }
    return result;
};
