import React, {FC, Fragment, useCallback, useState} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import MuiListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import MuiAccordion from "@material-ui/core/Accordion";
import MuiAccordionSummary from "@material-ui/core/AccordionSummary";
import MuiAccordionDetails from "@material-ui/core/AccordionDetails";
import Typography from "@material-ui/core/Typography";
import EmailIcon from "@material-ui/icons/Email";
import DraftsIcon from "@material-ui/icons/Drafts";
import ErrorIcon from "@material-ui/icons/Error";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import DeleteIcon from "@material-ui/icons/Delete";
import withStyles, {CSSProperties} from "@material-ui/core/styles/withStyles";
import Divider from "@material-ui/core/Divider";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import {useTranslation} from "react-i18next";
import AssignmentIcon from "@material-ui/icons/Assignment";
import Chip from "@material-ui/core/Chip";
import ReactMarkdown from "react-markdown";
import EditIcon from "@material-ui/icons/Edit";
import {clone} from "ramda";

import JsonViewer from "../../components/json-viewer";
import {isActionEvent, isResponseEvent, isInitialStateEvent} from "../../api/nimflow-api-extensions";
import {useContextSimulationContext} from "./simulation-context";
import {useConfirm} from "../../components/confirm";
import {SimulationEvent} from "../../api/nimflow-api";
import {EventEditor} from "./event-editor";
import {ExistingEvent} from "../../api/nimflow-simulation-api";

export interface EventsViewerProps {
    existingEvents: ExistingEvent[];
    value: number;
    onChange?: (value: number) => void;
}

const finishedIconStyles: CSSProperties = {
    position: "relative",
    fontSize: 12,
    left: -10,
};

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            width: "100%",
            //maxWidth: '36ch',
            backgroundColor: theme.palette.background.paper,
        },
        item: {
            border: `2px solid ${theme.palette.common.action}`,
            borderRadius: 2,
            paddingRight: 96, // Make room for second button
        },
        divider: {
            height: 5,
            background: "transparent",
        },
        inline: {
            display: "inline",
        },
        completed: {
            ...finishedIconStyles,
            color: theme.palette.success.main,
        },
        failed: {
            ...finishedIconStyles,
            color: theme.palette.error.main,
        },
        actionHeading: {
            fontSize: theme.typography.pxToRem(15),
            fontWeight: theme.typography.fontWeightRegular,
        },
        summaryTextContainer: {
            width: "100%",
            height: "100%",
            display: "flex",
            alignItems: "center",
        },
    })
);

const ListItemIcon = withStyles({
    root: {
        minWidth: 40,
    },
})(MuiListItemIcon);

const Accordion = withStyles({
    root: {
        backgroundColor: "transparent",
        boxShadow: "none",
        "&:not(:last-child)": {
            borderBottom: 0,
        },
        "&:before": {
            display: "none",
        },
        "&$expanded": {
            border: "1px solid rgba(0, 0, 0, .125)",
            margin: "auto",
        },
    },
    expanded: {},
})(MuiAccordion);

const AccordionSummary = withStyles({
    root: {
        marginBottom: -1,
        height: 32,
        minHeight: 32,
        papdding: 0,
        "&$expanded": {
            borderBottom: "1px solid rgba(0, 0, 0, .125)",
            minHeight: 56,
        },
    },
    content: {
        margin: 0,
        height: "100%",
        alignItems: "center",
        cursor: "initial",
        "&$expanded": {
            margin: 0,
        },
    },
    expandIcon: {
        cursor: "pointer",
        paddingRight: "16px",
    },
    expanded: {},
})(MuiAccordionSummary);

const AccordionDetails = withStyles(theme => ({
    root: {
        padding: theme.spacing(2),
        flexDirection: "column",
    },
}))(MuiAccordionDetails);

interface EventIconProps {
    event: ExistingEvent;
}

const EventIcon: FC<EventIconProps> = props => {
    const {event} = props;
    const classes = useStyles();

    if (event.status === "pending") {
        return isActionEvent(event.event) || isInitialStateEvent(event.event) ? <EmailIcon /> : <AssignmentIcon />;
    }
    return (
        <>
            {isActionEvent(event.event) || isInitialStateEvent(event.event) ? <DraftsIcon /> : <AssignmentIcon />}
            {event.status === "completed" ? (
                <CheckCircleIcon className={classes.completed} />
            ) : (
                <ErrorIcon className={classes.failed} />
            )}
        </>
    );
};

interface EventSummaryProps {
    event: ExistingEvent;
    onClick?: (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => void;
}

const EventSummary: FC<EventSummaryProps> = ({event, onClick}) => {
    const classes = useStyles();
    const {t} = useTranslation();

    return (
        <>
            <div className={classes.summaryTextContainer} onClick={onClick}>
                <Typography className={classes.actionHeading}>
                    {isActionEvent(event.event)
                        ? event.event.action
                        : isResponseEvent(event.event)
                        ? event.event.typeName
                        : isInitialStateEvent(event.event)
                        ? t("InitialState")
                        : ""}
                </Typography>
            </div>
            {isResponseEvent(event.event) && (
                <Chip
                    label={event.event.taskTypeName}
                    variant="outlined"
                    title={event.event.taskTypeName}
                    style={{maxWidth: 150, textOverflow: "ellipsis"}}
                />
            )}
        </>
    );
};

interface EditionData {
    index: number;
    event: Partial<SimulationEvent>;
}

export const EventsViewer: FC<EventsViewerProps> = props => {
    const {existingEvents, value, onChange} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const confirm = useConfirm();
    const [expanded, setExpanded] = useState(-1);
    const [editionData, setEditionData] = useState<EditionData>();

    const {deleteEvent, updateEventData} = useContextSimulationContext();

    const handleListItemClick = useCallback(
        (event: React.MouseEvent<HTMLLIElement, MouseEvent>, index: number) => {
            onChange?.(index);
        },
        [onChange]
    );

    const handleSummaryClick = useCallback(
        (event: React.MouseEvent<HTMLDivElement, MouseEvent>, index: number) => {
            onChange?.(index);
            event.stopPropagation();
        },
        [onChange]
    );

    const handleEditEvent = useCallback(
        (_event: React.MouseEvent<HTMLButtonElement, MouseEvent>, index: number) => {
            const ev = existingEvents[index].event;
            setEditionData({index, event: clone(ev)});
        },
        [existingEvents]
    );

    const handleDeleteEvent = useCallback(
        (_event: React.MouseEvent<HTMLButtonElement, MouseEvent>, index: number) => {
            const ev = existingEvents[index].event;
            const name = isActionEvent(ev) ? ev.action : isResponseEvent(ev) ? ev.typeName : "";
            confirm({
                description: <ReactMarkdown>{t("ConfirmDeleteSimulationEvent", {name})}</ReactMarkdown>,
                mapCloseTo: "cancel",
                actions: [
                    {type: "deleteAll", text: t("DeleteAll")},
                    {type: "deleteCurrent", text: t("DeleteOnlyCurrent")},
                    {type: "cancel", text: t("Cancel")},
                ],
            }).then(action => {
                deleteEvent(index, {deleteFollowingEvents: action === "deleteAll"});
            });
        },
        [confirm, t, existingEvents, deleteEvent]
    );

    const handleExpansionPanelChange = useCallback((index: number, expanded: boolean) => {
        setExpanded(expanded ? index : -1);
    }, []);

    const handleOnClose = useCallback(() => {
        setEditionData(undefined);
    }, [setEditionData]);

    const handleEventChange = useCallback(
        (event: SimulationEvent) => {
            if (editionData) {
                updateEventData(editionData.index, event);
            }
        },
        [updateEventData, editionData]
    );

    return (
        <>
            <List className={classes.root}>
                {existingEvents.map((s, i) => {
                    return (
                        <Fragment key={i}>
                            <ListItem
                                selected={value === i}
                                onClick={s => handleListItemClick(s, i)}
                                className={classes.item}
                            >
                                <ListItemIcon>
                                    <EventIcon event={s} />
                                </ListItemIcon>
                                <ListItemText
                                    primary={
                                        <Accordion
                                            expanded={expanded === i}
                                            onChange={(_event, expanded) => handleExpansionPanelChange(i, expanded)}
                                        >
                                            <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                                                <EventSummary
                                                    event={s}
                                                    onClick={event => handleSummaryClick(event, i)}
                                                />
                                            </AccordionSummary>
                                            <AccordionDetails>
                                                <Typography variant="body1" component="div">
                                                    Payload
                                                </Typography>
                                                <div style={{width: "100%"}}>
                                                    <JsonViewer data={s.event.payload ?? {}} />
                                                </div>
                                            </AccordionDetails>
                                        </Accordion>
                                    }
                                />
                                <ListItemSecondaryAction>
                                    <Tooltip title={t("Edit") as string}>
                                        <span>
                                            <IconButton
                                                aria-label="edit"
                                                disabled={isInitialStateEvent(s.event)}
                                                onClick={ev => handleEditEvent(ev, i)}
                                            >
                                                <EditIcon />
                                            </IconButton>
                                        </span>
                                    </Tooltip>
                                    <Tooltip title={t("Delete") as string}>
                                        <IconButton
                                            edge="end"
                                            aria-label="delete"
                                            onClick={ev => handleDeleteEvent(ev, i)}
                                        >
                                            <DeleteIcon />
                                        </IconButton>
                                    </Tooltip>
                                </ListItemSecondaryAction>
                            </ListItem>
                            <Divider className={classes.divider} />
                        </Fragment>
                    );
                })}
            </List>
            {!!editionData && (
                <EventEditor
                    event={editionData.event}
                    open={!!editionData}
                    onClose={handleOnClose}
                    onChange={handleEventChange}
                />
            )}
        </>
    );
};
