import * as yup from "yup";
import type {SchemaOf} from "yup";
import {FunctionDataSourceData} from "./function-data-source-editor-context";

import {freeVarDefs} from "./free-variable-defs";

export const FunctionDataSourceDataSchema = yup.object().shape({
    moduleName: yup.string().required(),
    functionName: yup.string().required(),
    jsArguments: yup.string().jsScript(freeVarDefs.fnargs.name),
}) as SchemaOf<FunctionDataSourceData>;
