import React, {FC, useCallback, useState, useMemo} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import {useTranslation} from "react-i18next";
import * as yup from "yup";

import {validateWithYupSchema} from "../../libs/yup";
import {useNimflowDashboardWidgets} from "../../api/nimflow-dashboard-widgets-api";
import {DashboardWidgetDefinitionSchema} from "./widget-definition-schema";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            backgroundColor: theme.palette.background.default,
        },
        content: {
            minWidth: "500px",
        },
    })
);

export interface WidgetDefinitionSaveAsDialogProps {
    open: boolean;
    defaultValue?: string;
    onChange: (value: string) => void;
    onClose: () => void;
}

export const WidgetDefinitionSaveAsDialog: FC<WidgetDefinitionSaveAsDialogProps> = props => {
    const {open, defaultValue, onChange, onClose} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const {dashboardWidgets_FindOneByName} = useNimflowDashboardWidgets();
    const [errorMessage, setErrorMessage] = useState<string>();
    const [name, setName] = useState(defaultValue);

    const schema = useMemo(
        () =>
            DashboardWidgetDefinitionSchema.fields?.name.concat(
                yup
                    .string()
                    .required()
                    .test({
                        message: ({value}) => t("WidgetDefinitionNameAlreadyExists", {name: value}),
                        test: async function (value: any) {
                            const searchResult = await dashboardWidgets_FindOneByName(value);
                            return searchResult == null;
                        },
                    })
            ),
        [dashboardWidgets_FindOneByName, t]
    );

    const handleAccept = useCallback(async () => {
        const errors = await validateWithYupSchema(schema, name);
        if (errors.length > 0) {
            setErrorMessage(errors[0].message);
            return;
        }
        if (name) {
            onChange(name);
            onClose();
        }
    }, [name, onChange, onClose, schema]);

    const handleDialogClose = useCallback(async (event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [onClose]);

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            fullWidth
            maxWidth="md"
            classes={{paper: classes.paper}}
        >
            <DialogTitle>{t("SaveAs")}</DialogTitle>
            <DialogContent className={classes.content}>
                <TextField
                    type="text"
                    defaultValue={name}
                    onChange={e => setName(e.target.value)}
                    label={t("NewName")}
                    fullWidth
                    required
                    error={!!errorMessage}
                    helperText={errorMessage}
                />
            </DialogContent>
            <DialogActions>
                <Button onClick={handleAccept} color="primary">
                    {t("Accept")}
                </Button>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
