import {uniqBy} from "ramda";

import {AST, T_ANY} from "./types/AST";

export function optimize(ast: AST, processed = new Map<AST, AST>()): AST {
    if (processed.has(ast)) {
        return processed.get(ast)!;
    }

    processed.set(ast, ast);

    switch (ast.type) {
        case "INTERFACE":
            return {
                ...ast,
                params: ast.params.map(s => Object.assign(s, {ast: optimize(s.ast, processed)})),
            };
        case "INTERSECTION":
        case "UNION":
            // [A, B, C, Any] -> Any
            if (ast.params.some(s => s.type === "ANY")) {
                return T_ANY;
            }

            // [A, B, B] -> [A, B]
            const shouldTakeStandaloneNameIntoAccount = ast.params.filter(s => s.standaloneName).length > 1;
            ast.params = uniqBy(
                s => `
                  ${s.type}-
                  ${shouldTakeStandaloneNameIntoAccount ? s.standaloneName : ""}-
                  ${JSON.stringify((s as any).params)}
                `,
                ast.params
            );

            return {
                ...ast,
                params: ast.params.map(s => optimize(s, processed)),
            };
        default:
            return ast;
    }
}
