import { ContextSimulation, ExistingEvent } from '../../api/nimflow-simulation-api';

export const getExistingActions = (simulation: ContextSimulation): ExistingEvent[] => {
    const finished: ExistingEvent[] = simulation.history.map(step => ({
        event: step.event,
        status: !step.errors || step.errors.length === 0 ? "completed" : "failed",
    }));
    const pending: ExistingEvent[] = simulation.pendingEvents.map(event => ({
        event,
        status: "pending",
    }));
    return [...finished, ...pending];
};
