import React, {FC, useState, useEffect, useCallback} from "react";
import {useParams, useNavigate} from "react-router-dom";
import {clone} from "ramda";

import {ContextTypeEditor} from "./context-type-editor";
import {useNimflow} from "../../api/nimflow-api-provider";
import {ContextType} from "../../api/nimflow-api";

export const ContextTypeUpdater: FC<{}> = () => {
    const {name} = useParams();
    const navigate = useNavigate();
    const [contextType, setContextType] = useState<ContextType>();
    const {contextTypes_FindOne, contextTypes_Update, contextTypes_Add} = useNimflow();

    useEffect(() => {
        contextTypes_FindOne({name}).then(s => {
            setContextType(s);
        });
    }, [name, contextTypes_FindOne]);

    const handleOnChange = useCallback(
        async (value: ContextType) => {
            const result = await contextTypes_Update(value);
            setContextType(result);
            return true;
        },
        [setContextType, contextTypes_Update]
    );

    const handleOnSaveAs = useCallback(
        async (value: ContextType, newName: string) => {
            const newContextType = clone(value);
            newContextType.name = newName;
            newContextType.version = 0;

            const result = await contextTypes_Add(newContextType);
            setContextType(undefined);
            navigate(`../${result.name}`, {replace: true});
            return true;
        },
        [contextTypes_Add, navigate]
    );

    if (!contextType) {
        return null;
    }

    return (
        <ContextTypeEditor
            value={contextType}
            onApplyChanges={handleOnChange}
            onSaveAs={handleOnSaveAs}
            isPersistent={true}
        />
    );
};
