import * as yup from "yup";
import type {SchemaOf} from "yup";
import {CastOptions, SchemaSpec} from "yup/lib/schema";
import {DefinedStringSchema, RequiredStringSchema} from "yup/lib/string";
import {AnyObject, Callback, InternalOptions, Maybe} from "yup/lib/types";
import runTests, {RunTest} from "yup/lib/util/runTests";

export function create<Key extends string = string, Value extends any = any>(
    key: yup.StringSchema<Key>,
    value: yup.BaseSchema<Value>
) {
    return new MapSchema(key, value);
}
export class MapSchema<
    Key extends string = string,
    Value extends any = any,
    TContext = AnyObject
> extends yup.MixedSchema<{[key in Key]: Value}, TContext, {[key in Key]: Value}> {
    constructor(
        public key: yup.StringSchema<Key> = yup.string() as any,
        public value: yup.BaseSchema<Value> = yup.mixed()
    ) {
        super({type: "map"});
    }

    protected _typeCheck(_value: any): _value is NonNullable<{[key in Key]: Value}> {
        return _value && typeof _value === "object";
    }

    protected _cast(rawValue: any, _options: CastOptions<TContext>): any {
        let value = super._cast(rawValue, _options);

        const result: any = {};
        Object.entries(value).forEach(([key, value]) => {
            result[this.key.cast(key)] = this.value.cast(value);
        });

        return result;
    }

    protected _validate(_value: any, options: InternalOptions<TContext> = {}, callback: Callback): void {
        const errors: any[] = [];
        const {abortEarly, sync, path = ""} = options;
        let originalValue = options.originalValue != null ? options.originalValue : _value;
        const keySchema = this.key;
        const valueSchema = this.value;
        super._validate(_value, options, (err, value) => {
            if (err) {
                if (!yup.ValidationError.isError(err) || abortEarly) {
                    return callback(err, value);
                }
                errors.push(err);
            }
            if (!this._typeCheck(value)) {
                if (errors.length) 
                    callback(errors[0]);
                callback(null, value);
                return;
            }
            originalValue = originalValue ?? value;

            let tests: RunTest[] = []; //(opts: yup.TestOptions, cb: Callback) => void;
            for (const [field, fieldValue] of Object.entries(value)) {
                const innerOptions = {
                    ...options,
                    strict: true,
                    parent: value,
                    path: path ? `${path}.${field}` : field,
                    originalValue: originalValue[field],
                };
                tests.push((_, cb) => {
                    keySchema
                        .validate(field, innerOptions)
                        .then(s => cb(null, s))
                        .catch(err => cb(err));
                });
                tests.push((_, cb) => {
                    valueSchema
                        .validate(fieldValue, innerOptions)
                        .then(s => cb(null, s))
                        .catch(err => cb(err));
                });
            }

            return runTests(
                {
                    sync,
                    path,
                    value,
                    errors,
                    tests,
                    endEarly: abortEarly,
                },
                callback
            );
        });
    }

    public clone(spec?: Partial<SchemaSpec<any>>): this {
        const next = super.clone(spec);
        next.key = this.key?.clone();
        next.value = this.value?.clone();
        return next;
    }
}

create.prototype = MapSchema.prototype;

export type MapSchemaOf<Key extends string, Value extends any> = SchemaOf<Record<Key, Value>> & {
    key: yup.StringSchema<Key>;
    value: yup.BaseSchema<Value>; // SchemaOf<Value>;
};

export function map<Key extends Maybe<string> = string, Value = any>(
    key: DefinedStringSchema<Key>,
    value: yup.BaseSchema<Value>
): MapSchemaOf<NonNullable<Key>, Value>;
export function map<Key extends Maybe<string> = string, Value = any>(
    key: RequiredStringSchema<Key>,
    value: yup.BaseSchema<Value>
): MapSchemaOf<NonNullable<Key>, Value>;
export function map<Key extends string = string, Value = any>(
    key: yup.StringSchema<Key>,
    value: yup.BaseSchema<Value>
): MapSchemaOf<Key, Value> {
    return create(key, value) as unknown as MapSchemaOf<Key, Value>;
}
