const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        canvas: {
            height: "100%",
            minHeight: "250px",
        },
        actionTypeNode: {
            color: theme.palette.primary.main,
        },
        taskTypeNode: {
            color: "rgb(0,177,235)",
        },
        canvasContainer: {
            height: "100%",
            width: "100%",
        },
        search: {
            position: "absolute",
            top: theme.spacing(2),
            right: theme.spacing(2),
        },
        layout: {
            position: "absolute",
            bottom: theme.spacing(2),
            left: theme.spacing(2),
            color: theme.palette.text.secondary,
            backgroundColor: theme.palette.background.default,
        },
    })
);

const partitionTasks = (model: DiagramModel) => {
    const successorMap = mapObjIndexed(
        (link: LinkModel[]) => link.map(s => s.getTargetPort().getNode()),
        groupBy(s => s.getSourcePort().getNode().getID(), model.getLinks())
    );
    const tasks: TaskTypeNodeModel[] = [];
    const taskAdjList: number[][] = [];
    const getTaskIndex = (task: TaskTypeNodeModel) => {
        let index = tasks.indexOf(task);
        if (index === -1) {
            index = tasks.length;
            tasks.push(task);
            taskAdjList.push([]);
        }
        return index;
    };

    for (const node of filterInstancesOf(TaskTypeNodeModel, model.getNodes())) {
        const index = getTaskIndex(node);
        depthFirstSearch<NodeModel>(node, {
            getNeighbors: s => {
                return s instanceof TaskTypeNodeModel && s !== node ? [] : successorMap[s.getID()] ?? [];
            },
            enterVertex: ({currentVertex}) => {
                if (currentVertex instanceof TaskTypeNodeModel) {
                    taskAdjList[index].push(getTaskIndex(currentVertex));
                }
            },
        });
    }
    const {components, adjacencyList: sccAdjList} = getStronglyConnectedComponents(taskAdjList);
    const partitions: TaskTypeNodeModel[][] = [];
    const deps: Array<number[] | undefined> = clone(sccAdjList);
    let progressMade = true;
    while (progressMade) {
        progressMade = false;
        const partition: TaskTypeNodeModel[] = [];
        const comps: number[] = [];
        for (let i = 0; i < deps.length; i++) {
            const links = deps[i];
            if (links && links.length === 0) {
                comps.push(i);
                deps[i] = undefined;
                partition.push(...components[i].map(s => tasks[s]));
            }
        }
        for (const links of deps.filter(isPresent)) {
            for(const i of comps) {
                const idx = links.indexOf(i);
                if (idx > -1) {
                    links.splice(idx, 1);
                }
            }
        }
        if (partition.length > 0) {
            partitions.unshift(partition);
            progressMade = true;
        }
    }
    return partitions;
};

const finalMilestonePartition = 10000;

const assignPartitions = (model: DiagramModel) => {
    const result: Record<string, number> = {};
    for (const node of model.getNodes()) {
        let partition = 1;
        if (node instanceof ActionTypeNodeModel) {
            partition = 0;
        } else if (
            (node instanceof StateNodeModel || node instanceof SelectorNodeModel) &&
            node.taggingType === "final"
        ) {
            partition = finalMilestonePartition;
        } else if (node instanceof StateNodeModel) {
            partition = 2;
        } else if (node instanceof SelectorNodeModel) {
            partition = 3;
        } else if (node instanceof TaskTypeNodeModel) {
            partition = 4;
        }
        result[node.getID()] = partition;
    }

    const predecessorMap = mapObjIndexed(
        (link: LinkModel[]) => link.map(s => s.getSourcePort().getNode()),
        groupBy(
            s => s.getTargetPort().getNode().getID(),
            model.getLinks().filter(s => result[s.getSourcePort().getNode().getID()] < finalMilestonePartition)
        )
    );

    const taskPartitions = partitionTasks(model);

    for(let i = 0; i < taskPartitions.length; i++) {
        for(const task of taskPartitions[i]) {
            result[task.getID()] = 10 * i + 4;
        }
    }

    const relocateIndependentNodes = (baseLevel: number): number => {
        let relocations = 0;
        for (const node of filterInstancesOf(StateNodeModel, model.getNodes()).filter(
            s => result[s.getID()] === baseLevel + 2
        )) {
            const predecessors = predecessorMap[node.getID()];
            if (predecessors && predecessors.some(s => result[s.getID()] > baseLevel + 2)) {
                result[node.getID()] = baseLevel + 12;
                relocations++;
            }
        }
        let selectorsMoved = true;
        while (selectorsMoved) {
            selectorsMoved = false;
            for (const node of filterInstancesOf(SelectorNodeModel, model.getNodes()).filter(
                s => result[s.getID()] === baseLevel + 3
            )) {
                const predecessors = predecessorMap[node.getID()];
                if (predecessors && predecessors.some(s => result[s.getID()] > baseLevel + 3)) {
                    result[node.getID()] = baseLevel + 13;
                    selectorsMoved = true;
                    relocations++;
                }
            }
        }
        return relocations;
    };

    let prevRelocations = 0;
    let relocations = Number.MAX_VALUE;
    let level = 0;
    do {
        prevRelocations = relocations;
        relocations = relocateIndependentNodes(level);
        level += 10;
    } while (relocations > 0 && relocations < prevRelocations);

    return result;
};

const layoutModel = async (model: DiagramModel) => {
    let partitionAssignment: Record<string, number> = {};
    const engine = new ElkEngine({
        algorithm: "layered",
        layoutOptions: {
            "elk.alignment": "LEFT",
            "elk.partitioning.activate": true,
            "elk.layered.feedbackEdges": true,
            "elk.portConstraints": "FIXED_POS",
            "elk.separateConnectedComponents": false,
            "elk.interactiveLayout": true,

            "elk.layered.spacing.nodeNodeBetweenLayers": 60,
            "elk.spacing.edgeEdge": 20,
            "elk.spacing.edgeNode": 20,
            "elk.spacing.nodeNode": 20,
            "elk.layered.spacing.edgeEdgeBetweenLayers": 20,
            "elk.layered.spacing.edgeNodeBetweenLayers": 20,

            "elk.edgeRouting": "ORTHOGONAL",
            //"elk.edgeRouting": "POLYLINE",

            "elk.layered.layering.strategy": "INTERACTIVE",
            //"elk.layered.layering.strategy": "NETWORK_SIMPLEX",

            //"elk.layered.nodePlacement.strategy": "INTERACTIVE",
            //"elk.layered.nodePlacement.favorStraightEdges": true,

            //"elk.layered.crossingMinimization.strategy": "INTERACTIVE",
            //"elk.layered.interactiveReferencePoint": "CENTER",
        },
        nodeLayoutOptions: node => {
            if (Object.keys(partitionAssignment).length === 0) {
                partitionAssignment = assignPartitions(model);
            }
            const result: ElkLayoutOptions = {
                "elk.partitioning.partition": partitionAssignment[node.getID()] ?? 1,
            };
            delete partitionAssignment[node.getID()];
            return result;
        },
        includeLinks: true,
    });
    await engine.redistribute(model);
};

class ContextStateDefaultDiagramState extends DefaultDiagramState {
    constructor() {
        super();
        this.childStates = [];
    }
}

const createDiagramEngine = () => {
    const engine = createEngine();
    const nodeFactories = engine.getNodeFactories();
    nodeFactories.registerFactory(new ActionTypeNodeFactory());
    nodeFactories.registerFactory(new TaskTypeNodeFactory());
    nodeFactories.registerFactory(new StateNodeFactory());
    nodeFactories.registerFactory(new SelectorNodeFactory());
    engine.getLinkFactories().registerFactory(new ContextTypeLinkFactory());
    engine.getStateMachine().pushState(new ContextStateDefaultDiagramState());
    return engine;
};

const selectNodeAndScrollIntoView = (node: ContextTypeNodeModel<any>, engine: DiagramEngine) => {
    const model = engine.getModel();
    model.clearSelection();
    node.setSelected(true);

    // OffsetX/Y and zoomFactor/100 are the translation and scale of the transform matrix of the model
    const canvas = engine.getCanvas();
    let {width: canvasWidth, height: canvasHeight} = canvas.getBoundingClientRect();
    const canvasXLeft = -model.getOffsetX();
    const canvasXRight = -model.getOffsetX() + canvasWidth;
    const canvasYTop = -model.getOffsetY();
    const canvasYBottom = -model.getOffsetY() + canvasHeight;

    // Node position and dimension are in the original model (unstransformed) coordinate system
    const scale = model.getZoomLevel() / 100;
    let {x: nodeXLeft, y: nodeYTop} = node.getPosition();
    nodeXLeft *= scale;
    nodeYTop *= scale;
    const nodeXRight = nodeXLeft + node.width * scale;
    const nodeYBottom = nodeYTop + node.height * scale;

    let newOffsetX = model.getOffsetX();
    let newOffsetY = model.getOffsetY();

    const safetyMargin = 40;
    if (nodeXRight > canvasXRight) {
        newOffsetX = -nodeXRight - safetyMargin + canvasWidth;
    } else if (nodeXLeft < canvasXLeft) {
        newOffsetX = -nodeXLeft + safetyMargin;
    }
    if (nodeYBottom > canvasYBottom) {
        newOffsetY = -nodeYBottom - safetyMargin + canvasHeight;
    } else if (nodeYTop < canvasYTop) {
        newOffsetY = -nodeYTop + safetyMargin;
    }

    if (model.getOffsetX() !== newOffsetX || model.getOffsetY() !== newOffsetY) {
        animateTransform(engine, {
            offsetX: [model.getOffsetX(), newOffsetX],
            offsetY: [model.getOffsetY(), newOffsetY],
        });
    }
};

const animateTransform = (
    engine: DiagramEngine,
    values: {offsetX?: [number, number]; offsetY?: [number, number]; zoomLevel?: [number, number]}
) => {
    const model = engine.getModel();
    const vars = {x: 0, y: 0, zoom: 0};
    const vals = {
        offsetX: [model.getOffsetX(), model.getOffsetX()],
        offsetY: [model.getOffsetY(), model.getOffsetY()],
        zoomLevel: [model.getZoomLevel(), model.getZoomLevel()],
        ...values,
    };
    gsap.fromTo(
        vars,
        {x: vals.offsetX[0], y: vals.offsetY[0], zoom: vals.zoomLevel[0]},
        {
            x: vals.offsetX[1],
            y: vals.offsetY[1],
            zoom: vals.zoomLevel[1],
            duration: 0.5,
            ease: "power2",
            onUpdate: () => {
                model.setZoomLevel(vars.zoom);
                model.setOffset(vars.x, vars.y);
                engine.repaintCanvas();
            },
        }
    );
};

const getNeighbors = (node: ContextTypeNodeModel<any>) => {
    const links = uniq(Object.values(node.getPorts()).flatMap(s => Object.values(s.getLinks())));
    const nodes = uniq(
        links.flatMap(s => {
            const src = s.getSourcePort().getNode();
            const tgt = s.getTargetPort().getNode();
            return src === node ? tgt : src;
        })
    );
    return [nodes, links] as const;
};

const getCombinedBoundingBox = (rect1: Rectangle, rect2: Rectangle, margin: number = 20) => {
    const tl1 = rect1.getTopLeft();
    const linkstl = rect2.getTopLeft();
    const br1 = rect1.getBottomRight();
    const br2 = rect2.getBottomRight();
    const tl = new Point(Math.min(tl1.x, linkstl.x) - margin, Math.min(tl1.y, linkstl.y) - margin);
    const br = new Point(Math.max(br1.x, br2.x) + margin, Math.max(br1.y, br2.y) + margin);
    const width = br.x - tl.x;
    const height = br.y - tl.y;
    return new Rectangle(tl, width, height);
};

const centerNodeAndFitNeighbors = (
    node: ContextTypeNodeModel<any>,
    neighbors: NodeModel<any>[],
    links: LinkModel<any>[],
    engine: DiagramEngine,
    margin: number = 20
) => {
    const model = engine.getModel();
    const canvas = engine.getCanvas();
    const nodesRect = engine.getBoundingNodesRect([node, ...neighbors]);
    const linksRect = Polygon.boundingBoxFromPoints(links.flatMap(s => s.getPoints()).map(s => s.getPosition()));
    const rect = getCombinedBoundingBox(nodesRect, linksRect, margin);

    const canvasRect = canvas.getBoundingClientRect();
    const canvasTopLeftPoint = {
        x: canvasRect.left,
        y: canvasRect.top,
    };
    const nodeLayerTopLeftPoint = {
        x: canvasTopLeftPoint.x + model.getOffsetX(),
        y: canvasTopLeftPoint.y + model.getOffsetY(),
    };
    const xFactor = canvas.clientWidth / rect.getWidth();
    const yFactor = canvas.clientHeight / rect.getHeight();
    const zoomFactor = Math.min(xFactor < yFactor ? xFactor : yFactor, 1);

    const tl = rect.getTopLeft();
    const nodesRectTopLeftPoint = {
        x: nodeLayerTopLeftPoint.x + tl.x * zoomFactor,
        y: nodeLayerTopLeftPoint.y + tl.y * zoomFactor,
    };

    const newOffsetX = model.getOffsetX() + canvasTopLeftPoint.x - nodesRectTopLeftPoint.x;
    const newOffsetY = model.getOffsetY() + canvasTopLeftPoint.y - nodesRectTopLeftPoint.y;

    animateTransform(engine, {
        offsetX: [model.getOffsetX(), newOffsetX],
        offsetY: [model.getOffsetY(), newOffsetY],
        zoomLevel: [model.getZoomLevel(), zoomFactor * 100],
    });
};

const executeWhenBrowserTabVisible = (action: (executionDelayed: boolean) => void) => {
    if (document.visibilityState === "hidden") {
        const handler = function (this: Document, ev: Event) {
            if (document.visibilityState !== "visible") {
                return;
            }
            document.removeEventListener("visibilitychange", handler);
            action(true);
        };
        document.addEventListener("visibilitychange", handler);
    } else {
        action(false);
    }
};

export interface ContextTypeDiagramProps {
    actionTypes: Partial<ActionType>[];
    taskTypes: Partial<TaskType>[];
    selectors?: Selector[];
    taggings?: Record<string, TaggingType>;
    initialActions?: string[];
    statesIncluded?: string[];
    visibleElementTypes?: ContextElementType[];
    onNodeSelectionChanged?: (node: NodeModel<any>, isSelected: boolean) => void;
    onNodeHighlighted?: (node: NodeModel<any>) => void;
    onSelectionChanged?: (selection: NodeModel<any>[]) => void;
    onActionTypeCommandInvoked?: (actionType: Partial<ActionType>, command: ContextTypeNodeModelCommand) => void;
    onStateCommandInvoked?: (state: string, command: ContextTypeNodeModelCommand) => void;
    onSelectorCommandInvoked?: (selector: Partial<Selector>, command: ContextTypeNodeModelCommand) => void;
    onTaskTypeCommandInvoked?: (taskType: Partial<TaskType>, command: ContextTypeNodeModelCommand) => void;
    onPinStatusesChanged?: (pinStatuses: Record<string, PinStatus>) => void;
    onResponseTypeCommandInvoked?: (
        responseType: Partial<ResponseType>,
        taskType: Partial<TaskType>,
        command: ContextTypeNodeModelCommand
    ) => void;
}

export const ContextTypeDiagram: FC<ContextTypeDiagramProps> = props => {
    const {
        actionTypes,
        taskTypes,
        selectors,
        taggings,
        initialActions,
        statesIncluded,
        visibleElementTypes,
        onNodeSelectionChanged,
        onNodeHighlighted: onNodeFocused,
        onActionTypeCommandInvoked,
        onStateCommandInvoked,
        onSelectorCommandInvoked,
        onTaskTypeCommandInvoked,
        onResponseTypeCommandInvoked,
        onSelectionChanged,
        onPinStatusesChanged,
    } = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const containerRef = useRef<HTMLDivElement>(null);
    const setSelectedNodeIds = useSetRecoilState(selectedNodeIdsState);
    const setFocusedNodeIds = useSetRecoilState(focusedNodeIdsState);
    const model = useMemo(() => {
        return new ContextTypeDiagramModel({locked: true});
    }, []);

    const engine = useMemo(() => {
        const newEngine = createDiagramEngine();
        newEngine.setModel(model);
        return newEngine;
    }, [model]);

    useEffect(() => {
        setFocusedNodeIds([]);
    }, [setFocusedNodeIds]);

    const performLayout = useCallback(() => {
        executeWhenBrowserTabVisible(executionDelayed => {
            setTimeout(
                () => {
                    const container = containerRef.current;
                    layoutModel(model).finally(() => {
                        if (container) {
                            container.style["visibility"] = "visible";
                        }
                        engine.repaintCanvas();
                    });
                },
                executionDelayed ? 100 : 0
            );
        });
    }, [engine, model]);

    useEffect(() => {
        const container = containerRef.current;
        if (!container) {
            return;
        }
        const oldStatuses = model.pinStatuses;
        const itemsAdded = model.updateData(
            actionTypes,
            taskTypes,
            selectors,
            taggings,
            initialActions,
            statesIncluded,
            visibleElementTypes
        );
        if (oldStatuses !== model.pinStatuses) {
            onPinStatusesChanged?.(model.pinStatuses);
        }
        if (itemsAdded) {
            container.style["visibility"] = "hidden";
        }
        engine.repaintCanvas();
        performLayout();
    }, [
        actionTypes,
        engine,
        initialActions,
        taggings,
        model,
        selectors,
        taskTypes,
        performLayout,
        visibleElementTypes,
        statesIncluded,
        onPinStatusesChanged,
    ]);

    useEffect(() => {
        const listener: ContextTypeDiagramListener = {
            nodeSelectionChanged: e => {
                onNodeSelectionChanged?.(e.node, e.isSelected);
                const selection = model.getSelectedEntities().filter(s => s instanceof NodeModel) as NodeModel<any>[];
                onSelectionChanged?.(selection);
                setSelectedNodeIds(selection.map(s => s.getID()));
                if (!e.isSelected) {
                    setFocusedNodeIds([]);
                }
            },

            contextTypeNodeCommandInvoked: ({command, node}) => {
                if (command.type === "focusNode") {
                    const [nodes, links] = getNeighbors(node);
                    centerNodeAndFitNeighbors(node, nodes, links, engine);
                    nodes.unshift(node);
                    setFocusedNodeIds(nodes.map(s => s.getID()));
                    onNodeFocused?.(node);
                }
                if (node instanceof ActionTypeNodeModel) {
                    onActionTypeCommandInvoked?.(node.instance, command);
                } else if (node instanceof StateNodeModel) {
                    onStateCommandInvoked?.(node.instance, command);
                } else if (node instanceof SelectorNodeModel) {
                    onSelectorCommandInvoked?.(node.instance, command);
                } else if (node instanceof TaskTypeNodeModel) {
                    if (isResponseCommand(command)) {
                        const response = node.instance.validResponses!.find(s => s.name === command.responseName);
                        if (response) {
                            onResponseTypeCommandInvoked?.(response, node.instance, command);
                        }
                    } else {
                        onTaskTypeCommandInvoked?.(node.instance, command);
                    }
                }
            },
        } as ContextTypeDiagramListener;
        const listenerHandle = model.registerListener(listener);

        return () => {
            model.deregisterListener(listenerHandle);
        };
    }, [
        engine,
        model,
        onActionTypeCommandInvoked,
        onNodeFocused,
        onNodeSelectionChanged,
        onResponseTypeCommandInvoked,
        onSelectionChanged,
        onSelectorCommandInvoked,
        onStateCommandInvoked,
        onTaskTypeCommandInvoked,
        setFocusedNodeIds,
        setSelectedNodeIds,
    ]);

    const [, /*selectedElement*/ setSelectedElement] = useState<BaseModel<any>>();
    const handleMouseOver = useCallback(
        (event: React.MouseEvent<HTMLDivElement>) => {
            const element = engine.getMouseElement(event);
            if (element) {
                setSelectedElement(element);
            }
        },
        [engine]
    );

    const handleNodeSelected = useCallback(
        (node: ContextTypeNodeModel<any>) => {
            selectNodeAndScrollIntoView(node, engine);
        },
        [engine]
    );

    return (
        <>
            {/* <div>
                {JSON.stringify(selectedElement?.serialize())}
            </div> */}
            <div ref={containerRef} className={classes.canvasContainer} onMouseOver={handleMouseOver}>
                <CanvasWidget engine={engine} className={classes.canvas} />
            </div>
            <ContextTypeDiagramSearch
                nodes={engine.getModel().getNodes() as ContextTypeNodeModel<any>[]}
                className={classes.search}
                onNodeSelected={handleNodeSelected}
            />
            <Tooltip title={t("PerformLayout") as string}>
                <Fab className={classes.layout} onClick={performLayout} size="small">
                    <RefreshIcon fontSize="small" />
                </Fab>
            </Tooltip>
        </>
    );
};
