import {FC, useState} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import {useFormikContext} from "formik";
import Autosizer from "react-virtualized-auto-sizer";

import {DashboardWidgetJsSpec} from "../../api/nimflow-dashboard-widgets-api";
import {useDebounce} from "react-use";
import {FormikErrorViewer} from "../../components/formik-error-viewer";
import {JavascriptMonacoEditor} from "../../components/javascript-monaco-editor";
import {freeVarDefs} from "./free-variable-defs";

const errorContainerHeight = 100;

interface StyleProps {
    showErrors: boolean;
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        content: {
            height: "70vh",
            //height: "calc(100% - 50px)",
            marginTop: 5,
            display: "grid",
            gridTemplateColumns: "auto",
            gridTemplateRows: (props: StyleProps) => (props.showErrors ? `auto ${errorContainerHeight}px` : "auto"),
            rowGap: "5px",
            placeItems: "stretch",
            overflowY: "hidden",
        },
        editorContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
        },
        errorContainer: {
            gridColumn: "1 / 2",
            gridRow: "2 / 3",
            overflowY: "auto",
        },
        title: {
            display: "inline-block",
        },
        grow: {
            flexGrow: 1,
        },
        openIcon: {
            fontSize: "1rem",
            verticalAlign: "middle",
        },
    })
);

export interface WidgetJsSpecScriptEditorProps {
    path: string;
    visible: boolean;
    onErrorChange?: (errors: string[]) => void;
}

const freeVariables = [freeVarDefs.specParameters, freeVarDefs.specDataSources, freeVarDefs.specVisualization];

export const WidgetJsSpecScriptEditor: FC<WidgetJsSpecScriptEditorProps> = props => {
    const {visible, onErrorChange} = props;
    const {errors, values: widgetDefOrJsSpec, setFieldValue} = useFormikContext<Partial<DashboardWidgetJsSpec>>();
    const specErrors = errors.widgetJsSpec;
    const classes = useStyles({showErrors: !!specErrors});
    const [code, setCode] = useState(widgetDefOrJsSpec.widgetJsSpec);

    useDebounce(
        () => {
            setFieldValue("widgetJsSpec", code);
        },
        1000,
        [code]
    );

    return (
        <div className={classes.content}>
            <div className={classes.editorContainer}>
                <Autosizer>
                    {({height, width}) => {
                        if (!visible) {
                            return null;
                        }
                        return (
                            <JavascriptMonacoEditor
                                height={height}
                                width={width}
                                value={code}
                                onChange={setCode}
                                onErrorsChange={onErrorChange}
                                freeVariables={freeVariables}
                                importJsCommon={false}
                            />
                        );
                    }}
                </Autosizer>
            </div>
            {!!specErrors && (
                <div className={classes.errorContainer}>
                    <FormikErrorViewer errors={errors} names="visualization.jsSpecification" />
                </div>
            )}
        </div>
    );
};
