import {FC, useMemo, useState} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import {useTranslation} from "react-i18next";
import {useFormikContext} from "formik";
import Autosizer from "react-virtualized-auto-sizer";
import {useDebounce} from "react-use";

import {DashboardWidgetFunctionDataSource} from "../../api/nimflow-dashboard-widgets-api";
import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {FormikFunctionSelect, useFunctionInfos} from "../../components/formik-components/formik-function-select";
import {JavascriptMonacoEditor} from "../../components/javascript-monaco-editor";
import {freeVarDefs} from "./free-variable-defs";
import {FormikErrorViewer} from "../../components/formik-error-viewer";
import Typography from "@material-ui/core/Typography";
import {JsContextDefinitionsContextProvider} from "../../components/js-context-definitions-provider";
import {ModuleFunctionInfo} from "../../api/nimflow-functions-api";
import { getTypingFromSchema } from "../../utils/json-schema-to-typescript";

const headerContainerHeight = 60;
const errorContainerHeight = 100;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        textField: {
            width: "40ch",
        },
        dialogContent: {
            display: "grid",
            gridTemplateColumns: "auto",
            gridTemplateRows: (props: {showErrors: boolean}) =>
                `${headerContainerHeight}px ${props.showErrors ? `auto ${errorContainerHeight}px` : "auto"}`,
            rowGap: `${theme.spacing(1)}px`,
            columnGap: `${theme.spacing(3)}px`,
            placeItems: "stretch",
            height: "100%",
            overflowY: "hidden",
        },
        nameContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
        },
        functionContainer: {
            gridColumn: "2 / 3",
            gridRow: "1 / 2",
        },
        editorContainer: {
            gridColumn: "1 / 3",
            gridRow: "2 / 3",
            marginTop: 15,
        },
        errorContainer: {
            gridColumn: "1 / 3",
            gridRow: "3 / 4",
            overflowY: "auto",
        },
    })
);

const freeVariables = [freeVarDefs.params, freeVarDefs.fnargs];

export const JsParametersEditor: FC<{onErrorChange?: (errors: string[]) => void}> = props => {
    const {onErrorChange} = props;
    const {t} = useTranslation();
    const {errors, values: dataSource, setFieldValue} = useFormikContext<DashboardWidgetFunctionDataSource>();
    const argsErrors = errors.jsArguments;
    const classes = useStyles({showErrors: !!argsErrors});
    const [code, setCode] = useState(dataSource.jsArguments);

    useDebounce(
        () => {
            setFieldValue("jsArguments", code);
        },
        1000,
        [code]
    );

    return (
        <>
            <div className={classes.editorContainer}>
                <Typography variant="subtitle1">{t("JsParametersBinding")}</Typography>
                <Autosizer>
                    {({height, width}) => {
                        return (
                            <JavascriptMonacoEditor
                                height={height}
                                width={width}
                                value={code}
                                onChange={setCode}
                                onErrorsChange={onErrorChange}
                                importJsCommon={false}
                                freeVariables={freeVariables}
                            />
                        );
                    }}
                </Autosizer>
            </div>
            {!!argsErrors && (
                <div className={classes.errorContainer}>
                    <FormikErrorViewer errors={errors} names="jsArguments" />
                </div>
            )}
        </>
    );
};

const getArgsTypings = async (functionInfo: ModuleFunctionInfo | undefined) => {
    const schema = functionInfo?.inputSchema;
    const argsTyping = schema ? await getTypingFromSchema(schema) : "{}";
    return `declare let args: ${argsTyping};`;
};

const getEventListenerTypingsProvider = (
    functionInfo: ModuleFunctionInfo | undefined
): Record<string, () => string | Promise<string>> => {
    return {
        args: () => {
            return getArgsTypings(functionInfo);
        },
    };
};

export interface DashboardWidgetFunctionDataSourceEditorProps {}

export const DashboardWidgetFunctionDataSourceEditor: FC<DashboardWidgetFunctionDataSourceEditorProps> = props => {
    const {t} = useTranslation();
    const {values: dataSource, errors} = useFormikContext<Partial<DashboardWidgetFunctionDataSource>>();
    const classes = useStyles({showErrors: !!errors?.name});
    const {functionInfos} = useFunctionInfos();

    const functionInfo = useMemo(() => {
        const key = `${dataSource.moduleName}/${dataSource.functionName}`;
        return functionInfos[key];
    }, [dataSource.functionName, dataSource.moduleName, functionInfos]);

    const freeVariableTypingProviders: Record<string, () => string | Promise<string>> = useMemo(() => {
        return getEventListenerTypingsProvider(functionInfo);
    }, [functionInfo]);

    return (
        <JsContextDefinitionsContextProvider freeVariableTypingProviders={freeVariableTypingProviders}>
            <div className={classes.dialogContent}>
                <div className={classes.nameContainer}>
                    <FormikTextField
                        name={`name`}
                        label={t("Name")}
                        autoFocus
                        InputLabelProps={{required: true}}
                        className={classes.textField}
                        fullWidth
                    />
                </div>
                <div className={classes.functionContainer}>
                    <FormikFunctionSelect
                        moduleNamePath="moduleName"
                        functionNamePath="functionName"
                        TextFieldProps={{
                            className: classes.textField,
                            label: t("FunctionName"),
                            required: true,
                        }}
                    />
                </div>
                <JsParametersEditor />
            </div>
        </JsContextDefinitionsContextProvider>
    );
};
