import React, {FC, useCallback, useState, useMemo} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import MenuItem from "@material-ui/core/MenuItem";
import {useTranslation} from "react-i18next";
import TextField from "@material-ui/core/TextField";
import {useDebounce} from "react-use";
import useSWR from "swr";

import {useNimflow} from "../../api/nimflow-api-provider";
import {useKey} from "../../utils/use-key";

const titleHeight = 64;
const footerHeight = 52;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            backgroundColor: theme.palette.background.default,
            height: "100%",
            width: "100%",
            //minHeight: "90vh",
            maxHeight: "30vh",
            "& form": {
                height: "100%",
            },
        },
        dialogTitle: {
            height: titleHeight,
        },
        dialogContent: {
            height: `calc(100% - ${titleHeight}px - ${footerHeight}px)`,
            width: "100%",
        },
        dialogFooter: {
            height: footerHeight,
        },
    })
);

export interface MultivaluePropertyValueSelectorProps {
    open: boolean;
    defaultValue?: string;
    property: string;
    contextTypeName?: string;
    onChange?: (value: string | undefined) => void;
    onClose: () => void;
}

const useCandidates = (property: string, contextTypeName?: string) => {
    const {contextTypes_FindOne} = useNimflow();
    const queryKey = useKey(contextTypes_FindOne, property, contextTypeName);
    const {data, isValidating, mutate, error} = useSWR(
        queryKey,
        async () => {
            const contextType = await contextTypes_FindOne({name: contextTypeName});
            let result: string[] = [];
            switch (property) {
                case "milestones":
                    result = contextType.milestones.map(s => s.name);
                    break;
                case "tags":
                    result = contextType.tags.map(s => s.name);
                    break;
                case "metrics":
                    result = contextType.metrics.map(s => s.name);
                    break;
            }
            return result.sort();
        },
        {
            revalidateOnFocus: false,
        }
    );
    return useMemo(() => {
        return {
            candidates: data ?? [],
            isValidating,
            error,
            revalidate: mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export const MultivaluePropertyValueSelector: FC<MultivaluePropertyValueSelectorProps> = props => {
    const {open, defaultValue, property, contextTypeName, onChange, onClose} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const [, setSearchText] = useState(defaultValue);
    const [propertyValue, setPropertyValue] = useState(defaultValue);
    const {candidates} = useCandidates(property, contextTypeName);

    useDebounce(
        () => {
            setSearchText(propertyValue);
        },
        2000,
        [propertyValue]
    );

    const handleAccept = useCallback(() => {
        onChange?.(propertyValue);
        onClose?.();
    }, [onChange, onClose, propertyValue]);

    const handleCancel = useCallback(() => {
        onChange?.(undefined);
        onClose?.();
    }, [onChange, onClose]);

    const title = useMemo(() => {
        switch (property) {
            case "milestones":
                return "Milestone";
            case "tags":
                return "Tag";
            case "metrics":
                return "Metric";
            default:
                return property[0].toUpperCase() + property.slice(1);
        }
    }, [property]);

    return (
        <Dialog
            open={open}
            fullWidth
            maxWidth="sm"
            classes={{paper: classes.paper}}
        >
            <DialogTitle className={classes.dialogTitle}>
                {t("SelectingMultivaluePropertyValue", {name: t(title)})}
            </DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <TextField
                    label={t(title)}
                    fullWidth
                    InputLabelProps={{required: true}}
                    select
                    required
                    autoFocus
                    value={propertyValue}
                >
                    {candidates?.map(s => (
                        <MenuItem key={s} value={s} onClick={() => setPropertyValue(s)}>
                            {s}
                        </MenuItem>
                    ))}
                </TextField>
            </DialogContent>
            <DialogActions className={classes.dialogFooter}>
                <Button onClick={handleAccept} color="primary" disabled={!propertyValue}>
                    {t("Accept")}
                </Button>
                <Button onClick={handleCancel} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
