import {FC, Suspense} from "react";
import {Routes, Route} from "react-router-dom";
import {useTranslation} from "react-i18next";

import {Shell} from "./features/shell/shell";
import {ContextTypesViewer} from "./features/context-type-management/context-types-viewer";
import {ContextTypeCreator} from "./features/context-type-management/context-type-creator";
import {ContextTypeImporter} from "./features/context-type-management/context-type-importer";
import {ContextTypeUpdater} from "./features/context-type-management/context-type-updater";
import {getBasePath} from "./utils/get-base-url";
import {permissionRequests} from "./utils/authorization";
import {PermissionRestrictedRoute} from "./components/permission-restricted-route";
import {Simulator} from "./features/simulation/simulator";
import {ContextSimulationCreator} from "./features/simulation/context-simulation-creator";
import {ContextSimulationUpdater} from "./features/simulation/context-simulation-updater";
import {ContextSimulationImporter} from "./features/simulation/context-simulation-importer";
import {TranslationEditor} from "./features/localization/translation-editor";
import {ContextSimulationGeneratorFromContext} from "./features/simulation/context-simulation-generator-from-context";
import {WidgetDefinitionsViewer} from "./features/dashboard-widget-definition/widget-definitions-viewer";
import {DashboardWidgetDefOrJsSpecCreator} from "./features/dashboard-widget-definition/widget-definition-creator";
import {WidgetDefinitionImporter} from "./features/dashboard-widget-definition/widget-definition-importer";
import {WidgetDefinitionUpdater} from "./features/dashboard-widget-definition/widget-definition-updater";
import {DashboardDefinitionsViewer} from "./features/dashboard-definition/dashboard-definitions-viewer";
import {DashboardDefinitionCreator} from "./features/dashboard-definition/dashboard-definition-creator";
import {DashboardDefinitionImporter} from "./features/dashboard-definition/dashboard-definition-importer";
import {DashboardDefinitionUpdater} from "./features/dashboard-definition/dashboard-definition-updater";
import {EventListenersViewer} from "./features/event-listeners/event-listeners-viewer";
import {EventListenerCreator} from "./features/event-listeners/event-listener-creator";
import {EventListenerUpdater} from "./features/event-listeners/event-listener-updater";
import {DigitalWorkersViewer} from "./features/digital-workers/digital-workers-viewer";
import {DigitalWorkerCreator} from "./features/digital-workers/digital-worker-creator";
import {DigitalWorkerUpdater} from "./features/digital-workers/digital-worker-updater";
import {UdFunctionsViewer} from "./features/ud-functions/ud-functions-viewer";
import {UdFunctionCreator} from "./features/ud-functions/ud-function-creator";
import {UdFunctionUpdater} from "./features/ud-functions/ud-function-updater";
import {WaitPage} from "./components/wait-page";

const {
    modifyContextTypes,
    simulate,
    modifyLocalizations,
    modifyWidgetDefinitions,
    modifyDashboardDefinitions,
    modifyDigitalWorkers,
} = permissionRequests;

const App: FC<{}> = () => {
    const {t} = useTranslation();
    const spinner = <WaitPage>{t("SuspenseLoading")}</WaitPage>;
    return (
        <Suspense fallback={spinner}>
            <Routes basename={getBasePath()}>
                <Route path="/" element={<Shell />}>
                    <PermissionRestrictedRoute
                        permissions={modifyContextTypes}
                        path="contextTypes"
                        element={<ContextTypesViewer />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyContextTypes}
                        path="contextTypes/new"
                        element={<ContextTypeCreator />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyContextTypes}
                        path="contextTypes/import/:blobUrl"
                        element={<ContextTypeImporter />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyContextTypes}
                        path="contextTypes/edit/:name"
                        element={<ContextTypeUpdater />}
                    />
                    <PermissionRestrictedRoute
                        permissions={simulate}
                        path="simulator"
                        element={
                            <Suspense fallback={spinner}>
                                <Simulator />
                            </Suspense>
                        }
                    />
                    <PermissionRestrictedRoute
                        permissions={simulate}
                        path="simulator/new/:contextTypeName"
                        element={<ContextSimulationCreator />}
                    />
                    <PermissionRestrictedRoute
                        permissions={simulate}
                        path="simulator/edit/:id"
                        element={<ContextSimulationUpdater />}
                    />
                    <PermissionRestrictedRoute
                        permissions={simulate}
                        path="simulator/import/:blobUrl"
                        element={<ContextSimulationImporter />}
                    />
                    <PermissionRestrictedRoute
                        permissions={simulate}
                        path="simulator/generateFromContext/:blobUrl"
                        element={<ContextSimulationGeneratorFromContext />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyLocalizations}
                        path="localization"
                        element={<TranslationEditor />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyLocalizations}
                        path="localization"
                        element={<TranslationEditor />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyWidgetDefinitions}
                        path="widgetDefinitions"
                        element={
                            <Suspense fallback={spinner}>
                                <WidgetDefinitionsViewer />
                            </Suspense>
                        }
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyWidgetDefinitions}
                        path="widgetDefinitions/new"
                        element={<DashboardWidgetDefOrJsSpecCreator />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyWidgetDefinitions}
                        path="widgetDefinitions/import/:blobUrl"
                        element={<WidgetDefinitionImporter />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyWidgetDefinitions}
                        path="widgetDefinitions/edit/:name"
                        element={<WidgetDefinitionUpdater />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyDashboardDefinitions}
                        path="dashboardDefinitions"
                        element={
                            <Suspense fallback={spinner}>
                                <DashboardDefinitionsViewer />
                            </Suspense>
                        }
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyDashboardDefinitions}
                        path="dashboardDefinitions/new"
                        element={<DashboardDefinitionCreator />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyDashboardDefinitions}
                        path="dashboardDefinitions/import/:blobUrl"
                        element={<DashboardDefinitionImporter />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyDashboardDefinitions}
                        path="dashboardDefinitions/edit/:name"
                        element={<DashboardDefinitionUpdater />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyContextTypes}
                        path="eventListeners"
                        element={<EventListenersViewer />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyContextTypes}
                        path="eventListeners/new"
                        element={<EventListenerCreator />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyContextTypes}
                        path="eventListeners/edit/:id"
                        element={<EventListenerUpdater />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyDigitalWorkers}
                        path="digitalWorkers"
                        element={<DigitalWorkersViewer />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyDigitalWorkers}
                        path="digitalWorkers/new"
                        element={<DigitalWorkerCreator />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyDigitalWorkers}
                        path="digitalWorkers/edit/:name"
                        element={<DigitalWorkerUpdater />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyContextTypes}
                        path="udfunctions"
                        element={<UdFunctionsViewer />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyContextTypes}
                        path="udfunctions/new"
                        element={<UdFunctionCreator />}
                    />
                    <PermissionRestrictedRoute
                        permissions={modifyContextTypes}
                        path="udfunctions/edit/:id"
                        element={<UdFunctionUpdater />}
                    />
                </Route>
            </Routes>
        </Suspense>
    );
};

export default App;
