import {ContextTypePortModel} from "./diagram-nodes/context-type-port-model";
import {ContextTypeLinkModel} from "./diagram-nodes/context-type-link-model";
import {DiagramModel, DiagramModelGenerics, NodeModel} from "@projectstorm/react-diagrams";
import {DiagramListener, BaseEntityEvent} from "@projectstorm/react-canvas-core";
import {equals} from "ramda";

import {ActionType, TaskType, Selector, ResponseType} from "../../api/nimflow-api";
import {ActionTypeNodeModel} from "./diagram-nodes/action-type-node-model";
import {TaskTypeNodeModel} from "./diagram-nodes/task-type-node-model";
import {
    ContextTypeNodeModel,
    ContextTypeNodeListener,
    ContextTypeNodeModelCommand,
} from "./diagram-nodes/context-type-node-model";
import {StateNodeModel} from "./diagram-nodes/state-node-model";
import {SelectorNodeModel} from "./diagram-nodes/selector-node-model";
import {Constructor} from "../../utils/types";
import {TaggingType} from "./taggings";
import {extractStateRefs} from "../../utils/js-analysis";

type DiagramModelOptions = DiagramModelGenerics["OPTIONS"];

export const contextElementTypes = ["actionTypes", "states", "selectors", "taskTypes"] as const;
Object.freeze(contextElementTypes);

export type ContextElementType = typeof contextElementTypes[number];

export interface ContextTypeDiagramModelOptions extends DiagramModelOptions {}

export type ContextTypeDiagramListener = DiagramListener & {
    nodeSelectionChanged?: (event: BaseEntityEvent & {node: NodeModel; isSelected: boolean}) => void;
    contextTypeNodeCommandInvoked?: (
        event: BaseEntityEvent & {command: ContextTypeNodeModelCommand; node: ContextTypeNodeModel<any>}
    ) => void;
};

export interface ContextTypeDiagramModelGenerics extends DiagramModelGenerics {
    OPTIONS: ContextTypeDiagramModelOptions;
    LISTENER: ContextTypeDiagramListener;
}
type InferredReferencedState = Record<
    string,
    {
        actionTypes: Set<Partial<ActionType>>;
        taskTypes: Set<Partial<TaskType>>;
        responses: Map<Partial<ResponseType>, Partial<TaskType>>;
        selectors: Set<Selector>;
    }
>;

const getInstanceId = (instance: Partial<ActionType> | Partial<TaskType> | Partial<Selector> | string) => {
    if (typeof instance === "string") {
        return `state-${instance}`;
    } else if ("key" in instance) {
        return `selector-${instance.key}`;
    } else if ("validResponses" in instance) {
        return `task-${instance.name}`;
    } else if ("jsSetters" in instance) {
        return `action-${instance.name}`;
    }
    return `UNKNOWN`;
};

interface ActionTypeNode {
    type: "actionTypes";
    actionType: Partial<ActionType>;
}

interface StateNode {
    type: "states";
    name: string;
    tagging: TaggingType;
}

interface SelectorNode {
    type: "selectors";
    selector: Selector;
    tagging: TaggingType;
}

interface TaskTypeNode {
    type: "taskTypes";
    taskType: Partial<TaskType>;
}

type ContextTypeNode = ActionTypeNode | StateNode | SelectorNode | TaskTypeNode;

interface ContextTypeLink {
    fromNode: ContextTypeNode;
    fromPort?: string;
    toNode: ContextTypeNode;
}

interface ContextTypeGraph {
    nodes: ContextTypeNode[];
    links: ContextTypeLink[];
}

export const pinStatuses = ["pinned", "fixed"] as const;
Object.freeze(pinStatuses);

export type PinStatus = typeof pinStatuses[number] | undefined;

export class ContextTypeDiagramModel<
    G extends ContextTypeDiagramModelGenerics = ContextTypeDiagramModelGenerics
> extends DiagramModel<G> {
    constructor(options: G["OPTIONS"] = {}) {
        super(options);
    }

    private addContextTypeNode<T>(node: ContextTypeNodeModel<T>) {
        this.addNode(node);
        const listener: ContextTypeNodeListener = {
            selectionChanged: event => {
                this.fireEvent(
                    {
                        ...event,
                        node,
                    },
                    "nodeSelectionChanged"
                );
            },
            commandInvoked: event => {
                this.fireEvent(event, "contextTypeNodeCommandInvoked");
            },
        } as ContextTypeNodeListener;
        node.registerListener(listener);
    }

    private removeContextTypeNode<T>(node: ContextTypeNodeModel<T>) {
        node.clearListeners();
        this.removeNode(node);
    }

    private reconcileNodeList<TInstance, TModel extends ContextTypeNodeModel<TInstance>>(
        existingNodes: TModel[],
        instances: TInstance[],
        nodeBuilder: (instance: TInstance) => TModel
    ): Map<TInstance, TModel> {
        const result: Map<TInstance, TModel> = new Map();
        for (const instance of instances) {
            const idx = existingNodes.findIndex(s => s.instance === instance);
            if (idx >= 0) {
                result.set(instance, existingNodes[idx]);
                existingNodes.splice(idx, 1);
            } else {
                const node = nodeBuilder(instance);
                result.set(instance, node);
                this.addContextTypeNode(node);
            }
        }
        for (const node of existingNodes) {
            this.removeContextTypeNode(node);
        }
        return result;
    }

    private inferReferencedState(
        actionTypes: Partial<ActionType>[],
        taskTypes: Partial<TaskType>[],
        selectors?: Selector[]
    ): InferredReferencedState {
        const states: InferredReferencedState = {};

        function ensureState(stateName: string) {
            if (!(stateName in states)) {
                states[stateName] = {
                    actionTypes: new Set(),
                    taskTypes: new Set(),
                    responses: new Map(),
                    selectors: new Set(),
                };
            }
            return states[stateName];
        }

        for (const actionType of actionTypes) {
            if (!actionType.jsSetters) {
                continue;
            }
            for (const key of Object.keys(actionType.jsSetters)) {
                ensureState(key).actionTypes.add(actionType);
            }
        }

        for (const taskType of taskTypes) {
            const texts: string[] = [];
            //Optional links? (does not deterane flow)
            // if (taskType.jsTaskDataSetters) {
            //     texts.push(...Object.values(taskType.jsTaskDataSetters));
            // }
            // if (!!taskType.jsDueDate) {
            //     texts.push(taskType.jsDueDate);
            // }
            // if (!!taskType.jsAssignedTo) {
            //     texts.push(taskType.jsAssignedTo);
            // }
            //Important links (determines flow)
            if (!!taskType.jsCondition) {
                texts.push(taskType.jsCondition);
            }
            for (const key of extractStateRefs(texts)) {
                ensureState(key).taskTypes.add(taskType);
            }

            if (taskType.validResponses) {
                for (const response of taskType.validResponses) {
                    for (const key of Object.keys(response.jsSetters)) {
                        ensureState(key).responses.set(response, taskType);
                    }
                    // for (const key of extractStateRefs(Object.values(response.jsSetters))) {
                    //     ensureState(key).responses.set(response, taskType);
                    // }
                }
            }
        }
        if (!!selectors) {
            for (const selector of selectors) {
                if (selector.dependencies) {
                    for (const key of selector.dependencies) {
                        ensureState(key).selectors.add(selector);
                    }
                }
                for (const key of extractStateRefs(selector.jsExpression)) {
                    ensureState(key).selectors.add(selector);
                }
            }
        }

        return states;
    }

    private classifyNodes<T extends any[]>(
        ...types: {[K in keyof T]: Constructor<T[K]>}
    ): {[K in keyof T]: Array<T[K]>} {
        const data = types.map(() => [] as any) as {[K in keyof T]: Array<T[K]>};
        for (const node of this.getNodes()) {
            for (let i = 0; i < data.length; i++) {
                if (node instanceof types[i]) {
                    data[i].push(node);
                }
            }
        }
        return data;
    }

    private buildContextTypeGraph(
        actionTypes: Partial<ActionType>[],
        taskTypes: Partial<TaskType>[],
        selectors: Selector[] = [],
        taggings: Record<string, TaggingType>,
        statesIncluded: string[],
        visibleElementTypes?: ContextElementType[]
    ): [ContextTypeGraph, Record<string, PinStatus>] {
        const nodes: ContextTypeNode[] = [];
        const links: ContextTypeLink[] = [];

        const referencedStates = this.inferReferencedState(actionTypes, taskTypes, selectors);
        const nonVisibleElements: ContextTypeNode[] = [];

        const actionTypeMap: Record<string, ActionTypeNode> = {};
        for (const actionType of actionTypes) {
            if (!actionType.name) {
                continue;
            }
            const node: ActionTypeNode = {
                type: "actionTypes",
                actionType,
            };
            const target = visibleElementTypes?.includes("actionTypes") !== false ? nodes : nonVisibleElements;
            target.push(node);
            actionTypeMap[actionType.name!] = node;
        }

        const refStates = Object.keys(referencedStates).filter(s => selectors.findIndex(sel => sel.key === s) === -1);
        const stateMap: Record<string, StateNode> = {};
        for (const state of refStates) {
            const node: StateNode = {
                type: "states",
                name: state,
                tagging: taggings[state],
            };
            const target = visibleElementTypes?.includes("states") !== false ? nodes : nonVisibleElements;
            target.push(node);
            stateMap[state] = node;
        }

        const selectorMap: Record<string, SelectorNode> = {};
        for (const selector of selectors) {
            if (!selector.key) {
                continue;
            }
            const node: SelectorNode = {
                type: "selectors",
                selector,
                tagging: taggings[selector.key],
            };
            const target = visibleElementTypes?.includes("selectors") !== false ? nodes : nonVisibleElements;
            target.push(node);
            selectorMap[selector.key] = node;
        }

        const taskTypeMap: Record<string, TaskTypeNode> = {};
        for (const taskType of taskTypes) {
            if (!taskType.name) {
                continue;
            }
            const node: TaskTypeNode = {
                type: "taskTypes",
                taskType,
            };
            const target = visibleElementTypes?.includes("taskTypes") !== false ? nodes : nonVisibleElements;
            target.push(node);
            taskTypeMap[taskType.name] = node;
        }

        for (const [key, value] of Object.entries(referencedStates)) {
            const stateOrSelectorNode = stateMap[key] ?? selectorMap[key];
            if (!stateOrSelectorNode) {
                continue;
            }
            Array.from(value.actionTypes).forEach(s => {
                if (s?.name) {
                    links.push({fromNode: actionTypeMap[s.name], toNode: stateOrSelectorNode});
                }
            });
            Array.from(value.responses).forEach(([responseType, taskType]) => {
                if (taskType?.name && responseType.name) {
                    links.push({
                        fromNode: taskTypeMap[taskType.name],
                        fromPort: responseType.name,
                        toNode: stateOrSelectorNode,
                    });
                }
            });
            Array.from(value.taskTypes).forEach(s => {
                if (s?.name) {
                    links.push({fromNode: stateOrSelectorNode, toNode: taskTypeMap[s.name]});
                }
            });
            Array.from(value.selectors).forEach(s => {
                if (s?.key) {
                    links.push({fromNode: stateOrSelectorNode, toNode: selectorMap[s.key]});
                }
            });
        }

        const removeNodes = (toRemove: ContextTypeNode[]) => {
            for (const node of toRemove) {
                const sources: ContextTypeLink[] = [];
                const targets: ContextTypeLink[] = [];
                for (let i = links.length - 1; i >= 0; i--) {
                    const link = links[i];
                    let list: ContextTypeLink[] | undefined = undefined;
                    if (link.fromNode === node) {
                        list = sources;
                    } else if (link.toNode === node) {
                        list = targets;
                    }
                    if (list) {
                        list.push(link);
                        links.splice(i, 1);
                    }
                }
                for (const src of sources) {
                    for (const tgt of targets) {
                        const newLink: ContextTypeLink = {
                            fromNode: tgt.fromNode,
                            fromPort: tgt.fromPort,
                            toNode: src.toNode,
                        };
                        if (
                            newLink.fromNode !== newLink.toNode &&
                            links.findIndex(
                                s =>
                                    s.fromNode === newLink.fromNode &&
                                    s.fromPort === newLink.fromPort &&
                                    s.toNode === newLink.toNode
                            ) === -1
                        ) {
                            links.push(newLink);
                        }
                    }
                }
                const idx = nodes.indexOf(node);
                if (idx >= 0) {
                    nodes.splice(idx, 1);
                }
                switch (node.type) {
                    case "actionTypes":
                        delete actionTypeMap[node.actionType.name!];
                        break;
                    case "selectors":
                        delete selectorMap[node.selector.key!];
                        break;
                    case "states":
                        delete stateMap[node.name];
                        break;
                    case "taskTypes":
                        delete taskTypeMap[node.taskType.name!];
                        break;
                }
            }
        };

        const pinStatuses: Record<string, PinStatus> = {};

        let unusedNonFinalSelectors: SelectorNode[] = [];
        do {
            unusedNonFinalSelectors = Object.values(selectorMap).filter(s => {
                const isFixed =
                    s.tagging === "final" ||
                    links.findIndex(
                        l =>
                            l.fromNode === s &&
                            (l.toNode.type !== "selectors" || !statesIncluded.includes(l.toNode.selector.key!))
                    ) > -1;
                if (isFixed) {
                    pinStatuses[s.selector.key!] = "fixed";
                    return false;
                }
                const isPinned = statesIncluded.includes(s.selector.key!);
                if (isPinned) {
                    pinStatuses[s.selector.key!] = "pinned";
                    return false;
                }
                delete pinStatuses[s.selector.key!];
                return true;
            });
            removeNodes(unusedNonFinalSelectors);
        } while (unusedNonFinalSelectors.length > 0);

        const unusedNonFinalStates = Object.values(stateMap).filter(s => {
            const isFixed =
                s.tagging === "final" ||
                links.findIndex(
                    l =>
                        l.fromNode === s &&
                        (l.toNode.type !== "selectors" || !statesIncluded.includes(l.toNode.selector.key!))
                ) > -1;
            if (isFixed) {
                pinStatuses[s.name] = "fixed";
                return false;
            }
            const isPinned = statesIncluded.includes(s.name);
            if (isPinned) {
                pinStatuses[s.name] = "pinned";
                return false;
            }
            delete pinStatuses[s.name];
            return true;
        });
        removeNodes(unusedNonFinalStates);

        removeNodes(nonVisibleElements);

        const result: ContextTypeGraph = {nodes, links};
        return [result, pinStatuses];
    }

    public pinStatuses: Record<string, PinStatus> = {};

    public updateData(
        actionTypes: Partial<ActionType>[],
        taskTypes: Partial<TaskType>[],
        selectors: Selector[] = [],
        taggings: Record<string, TaggingType> = {},
        initiaActions: string[] = [],
        statesIncluded: string[] = [],
        visibleElementTypes?: ContextElementType[]
    ) {
        const [
            existingActionTypeNodes,
            existingTaskTypeNodes,
            existingStateNodes,
            existingSelectorNodes,
        ] = this.classifyNodes(ActionTypeNodeModel, TaskTypeNodeModel, StateNodeModel, SelectorNodeModel);

        let itemAdded = false;

        const [graph, pinStatuses] = this.buildContextTypeGraph(
            actionTypes,
            taskTypes,
            selectors,
            taggings,
            statesIncluded,
            visibleElementTypes
        );
        if (!equals(this.pinStatuses, pinStatuses)) {
            this.pinStatuses = pinStatuses;
        }

        const visibleActionTypes: Partial<ActionType>[] = [];
        const visibleTaskTypes: Partial<TaskType>[] = [];
        const visibleStates: string[] = [];
        const visibleSelectors: Selector[] = [];

        for (const node of graph.nodes) {
            switch (node.type) {
                case "actionTypes":
                    visibleActionTypes.push(node.actionType);
                    break;
                case "selectors":
                    visibleSelectors.push(node.selector);
                    break;
                case "states":
                    visibleStates.push(node.name);
                    break;
                case "taskTypes":
                    visibleTaskTypes.push(node.taskType);
                    break;
            }
        }

        const actionTypeMap = this.reconcileNodeList(existingActionTypeNodes, visibleActionTypes, s => {
            itemAdded = true;
            return new ActionTypeNodeModel(s, getInstanceId(s), initiaActions.includes(s.name ?? ""));
        });
        for (const model of Array.from(actionTypeMap.values())) {
            model.isInitialAction = initiaActions.includes(model.instance.name ?? "");
        }
        const taskTypeMap = this.reconcileNodeList(existingTaskTypeNodes, visibleTaskTypes, s => {
            itemAdded = true;
            return new TaskTypeNodeModel(s, getInstanceId(s));
        });
        const responseTypeToTaskTypeMap = new Map<Partial<ResponseType>, Partial<TaskType>>();
        for (const taskType of visibleTaskTypes.filter(s => !!s.validResponses)) {
            for (const responseType of taskType.validResponses!) {
                responseTypeToTaskTypeMap.set(responseType, taskType);
            }
        }
        const selectorMap = this.reconcileNodeList(existingSelectorNodes, visibleSelectors, s => {
            itemAdded = true;
            return new SelectorNodeModel(s, getInstanceId(s), s.key ? taggings[s.key] : undefined);
        });
        for (const model of Array.from(selectorMap.values())) {
            model.taggingType = taggings[model.instance.key!];
        }

        const stateMap = this.reconcileNodeList(existingStateNodes, visibleStates, s => {
            itemAdded = true;
            return new StateNodeModel(s, getInstanceId(s), taggings[s]);
        });
        for (const model of Array.from(stateMap.values())) {
            model.taggingType = taggings[model.instance];
        }

        const links = this.getLinks().filter(s => s instanceof ContextTypeLinkModel) as ContextTypeLinkModel[];

        const ensureLink = (
            from: ContextTypeNodeModel<any> | ContextTypePortModel,
            to: ContextTypeNodeModel<any> | ContextTypePortModel
        ) => {
            const fromPort = from instanceof ContextTypePortModel ? from : from.outPort;
            const toPort = to instanceof ContextTypePortModel ? to : to.inPort;
            const idx = links.findIndex(s => s.getSourcePort() === fromPort && s.getTargetPort() === toPort);
            if (idx === -1 && fromPort && toPort) {
                const link = fromPort.link(toPort);
                this.addLink(link);
            } else if (idx >= 0) {
                links.splice(idx, 1);
            }
        };

        const getLinkTerminal = (node: ContextTypeNode, port?: string) => {
            switch (node.type) {
                case "actionTypes":
                    return actionTypeMap.get(node.actionType);
                case "selectors":
                    return selectorMap.get(node.selector);
                case "states":
                    return stateMap.get(node.name);
                case "taskTypes":
                    const taskNode = taskTypeMap.get(node.taskType);
                    if (!port) {
                        return taskNode;
                    }
                    const responseType = node.taskType.validResponses?.find(s => s.name === port);
                    return responseType ? taskNode?.getResponsePort(responseType) : undefined;
            }
        };

        for (const link of graph.links) {
            const from = getLinkTerminal(link.fromNode, link.fromPort);
            const to = getLinkTerminal(link.toNode);
            if (from && to && from !== to) {
                ensureLink(from, to);
            }
        }

        for (const link of links) {
            link.remove();
            this.removeLink(link);
        }
        return itemAdded;
    }
}
