import React, {FC, useCallback, useEffect, useMemo} from "react";
import {useFormContext} from "react-hook-form";
import {SvgIconProps} from "@material-ui/core/SvgIcon";
import {isDefined} from "ts-is-present";
import LocalOfferIcon from "@material-ui/icons/LocalOffer";
import LooksOneIcon from "@material-ui/icons/LooksOne";
import i18next from "i18next";

import {MilestoneIcon} from "../../components/icons/milestone-icon";
import {FinalIcon} from "../../components/icons/final-icon";
import {DashIcon} from "../../components/icons/dash-icon";
import {ContextType, Milestone, Tag, Metric} from "../../api/nimflow-api";

export const taggingType = ["milestone", "final", "tag", "metric"] as const;
export type TaggingType = typeof taggingType[number] | undefined;

export type TaggingTypeIconProps = {
    taggingType?: TaggingType;
} & SvgIconProps;

export const TaggingTypeIcon: FC<TaggingTypeIconProps> = ({taggingType, ...rest}) => {
    return taggingType === "milestone" ? (
        <MilestoneIcon {...rest} />
    ) : taggingType === "final" ? (
        <FinalIcon {...rest} />
    ) : taggingType === "tag" ? (
        <LocalOfferIcon {...rest} />
    ) : taggingType === "metric" ? (
        <LooksOneIcon {...rest} />
    ) : (
        <DashIcon {...rest} />
    );
};

export const getValidTaggings = (contextType: ContextType) => {
    const selNames = contextType.selectors?.map(s => s.key).filter(isDefined) ?? [];
    const actionStateNames = contextType.actions.flatMap(s => Object.keys(s.jsSetters));
    const responseStateNames = contextType.tasks.flatMap(s => s.validResponses).flatMap(s => Object.keys(s.jsSetters));
    const stateNames = [...selNames, ...actionStateNames, ...responseStateNames];
    const milestones = contextType.milestones.filter(s => stateNames.includes(s.name));
    const tags = contextType.tags ? contextType.tags.filter(s => stateNames.includes(s.name)) : [];
    const metrics = contextType.metrics ? contextType.metrics.filter(s => stateNames.includes(s.name)) : [];
    return [milestones, tags, metrics];
};

export const getTaggingDescription = (taggingType: TaggingType) => {
    return i18next.t(
        taggingType === "milestone"
            ? "Milestone"
            : taggingType === "final"
            ? "FinalMilestone"
            : taggingType === "tag"
            ? "Tag"
            : taggingType === "metric"
            ? "Metric"
            : "Standard"
    ) as string;
};

const milestonesPath = "milestones";
const tagsPath = "tags";
const metricsPath = "metrics";

export const useTaggings = () => {
    const {register, watch, getValues, setValue, control} = useFormContext<ContextType>();
    const milestones: Milestone[] = watch(milestonesPath);
    const tags: Tag[] = watch(tagsPath);
    const metrics: Metric[] = watch(metricsPath);

    useEffect(() => {
        if (!control.fieldsRef.current[milestonesPath]) {
            register(milestonesPath);
        }
        if (!control.fieldsRef.current[tagsPath]) {
            register(tagsPath);
        }
        if (!control.fieldsRef.current[metricsPath]) {
            register(metricsPath);
        }
    });

    const removeTaggings = useCallback(
        (names: string | string[]) => {
            names = Array.isArray(names) ? names : [names];
            if (names.length === 0) {
                return;
            }
            const newTags = tags.filter(s => !names.includes(s.name));
            if (newTags.length < tags.length) {
                setValue(tagsPath, newTags);
            }
            const newMilestones = milestones.filter(s => !names.includes(s.name));
            if (newMilestones.length < milestones.length) {
                setValue(milestonesPath, newMilestones);
            }
            const newMetrics = metrics.filter(s => !names.includes(s.name));
            if (newMetrics.length < metrics.length) {
                setValue(metricsPath, newMetrics);
            }
        },
        [milestones, tags, metrics, setValue]
    );

    const getTaggingType = useCallback(
        (name: string): TaggingType => {
            const tag = tags ? tags.find(s => s.name === name) : undefined;
            if (tag) {
                return "tag";
            }
            const metric = metrics ? metrics.find(s => s.name === name) : undefined;
            if (metric) {
                return "metric";
            }
            const milestone = milestones.find(s => s.name === name);
            if (!milestone) {
                return undefined;
            }
            if (milestone.final) {
                return "final";
            }
            return "milestone";
        },
        [milestones, tags, metrics]
    );

    const setTaggingType = useCallback(
        (name: string, type: TaggingType) => {
            const tag = tags ? tags.find(s => s.name === name) : undefined;
            const metric = metrics ? metrics.find(s => s.name === name) : undefined;
            const ms = milestones.find(s => s.name === name);
            const newTag: Tag | undefined = type === "tag" ? {name} : undefined;
            const newMetric: Metric | undefined = type === "metric" ? {name} : undefined;
            const newMilestone: Milestone | undefined =
                type === "milestone" || type === "final" ? {name, final: type === "final"} : undefined;
            if ((tag && newTag) || (metric && newMetric) || (!!ms && !!newMilestone && ms.final === newMilestone.final)) {
                return;
            }
            let newTags = tags;
            let newMetrics = metrics;
            let newMilestones = milestones;
            if (!!tag) {
                newTags = tags ? tags.filter(s => s !== tag) : [];
            }
            if (!!metric) {
                newMetrics = metrics ? metrics.filter(s => s !== metric) : [];
            }
            if (!!ms) {
                newMilestones = milestones.filter(s => s !== ms);
            }
            if (newTag) {
                newTags = [...newTags, newTag];
            }
            if (newMetric){
                newMetrics = [...newMetrics, newMetric];
            }
            if (newMilestone) {
                newMilestones = [...newMilestones, newMilestone];
            }
            if (newTags !== tags) {
                setValue(tagsPath, newTags, {shouldDirty: true});
            }
            if (newMetrics !== metrics){
                setValue(metricsPath, newMetrics, {shouldDirty: true})
            }
            if (newMilestones !== milestones) {
                setValue(milestonesPath, newMilestones, {shouldDirty: true});
            }
        },
        [milestones, setValue, tags, metrics]
    );

    const toggleTagging = useCallback(
        (name: string) => {
            let tag = getTaggingType(name);
            switch (tag) {
                case "milestone":
                    tag = "final";
                    break;
                case "final":
                    tag = "tag";
                    break;
                case "tag":
                    tag = "metric";
                    break;
                case "metric":
                    tag = undefined;
                    break;
                default:
                    tag = "milestone";
                break;
            }
            setTaggingType(name, tag);
        },
        [getTaggingType, setTaggingType]
    );

    const removeInvalidTaggings = useCallback(() => {
        const contextType = getValues();
        const [cleanedMilestones, cleanedTags, cleanedMetrics] = getValidTaggings(contextType);
        if (cleanedMilestones.length !== contextType.milestones.length) {
            setValue(milestonesPath, cleanedMilestones, {shouldDirty: true});
        }
        if (contextType.tags && cleanedTags.length !== contextType.tags.length) {
            setValue(tagsPath, cleanedTags, {shouldDirty: true});
        }
        if (contextType.metrics && cleanedMetrics.length !== contextType.metrics.length) {
            setValue(metricsPath, cleanedMetrics, {shouldDirty: true});
        }
    }, [getValues, setValue]);

    const taggings = useMemo(() => {
        const result: Record<string, TaggingType> = {};
        for (const m of milestones) {
            result[m.name] = m.final ? "final" : "milestone";
        }
        if (tags) {
            for (const t of tags) {
                result[t.name] = "tag";
            }
        }
        if (metrics) {
            for (const metric of metrics) {
                result[metric.name] = "metric";
            }
        }
        return result;
    }, [milestones, tags, metrics]);

    return {
        taggings,
        getTaggingType,
        setTaggingType,
        toggleTagging,
        removeTaggings,
        removeInvalidTaggings,
    };
};
