import {FC, useCallback, useRef} from "react";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import {JSONSchema7} from "json-schema";
import {UISchemaElement, JsonFormsCore} from "@jsonforms/core";
import {useTranslation} from "react-i18next";

import {translateValidationErrors} from "../../libs/ajv";
import {JsonForms} from "../../libs/jsonforms";
import {PayloadJsonEditorModal} from "./payload-json-editor-modal";
import {ErrorBoundary} from "../../components/error-boundary";

const NoSchema: FC<{}> = ({children}) => (
    <Grid container alignItems="center" justifyContent="center">
        <Grid item xs={12} style={{textAlign: "center"}}>
            <Typography variant="body2">{children}</Typography>
        </Grid>
    </Grid>
);

export interface PayloadEditorState {
    payload?: any;
    hasErrors: boolean;
}

export interface PayloadEditorProps {
    context?: Record<string, any>;
    bindContext?: boolean;
    schema?: JSONSchema7;
    uischema?: UISchemaElement;
    uiSchemaLibrary?: Record<string, UISchemaElement>;
    initialPayload?: any;
    onChange?: (state: PayloadEditorState) => void;
    jsonEditorOpen: boolean;
    onJsonEditorClose?: () => void;
}

export const PayloadEditor: FC<PayloadEditorProps> = props => {
    const {schema, uischema, initialPayload, onChange, context, bindContext, uiSchemaLibrary, jsonEditorOpen, onJsonEditorClose} = props;
    const {t} = useTranslation();
    const payloadRef = useRef(initialPayload ?? {});
    const hasErrorsRef = useRef(false);

    const handlePayloadChange = useCallback(
        (state: Pick<JsonFormsCore, "data" | "errors">) => {
            if (state.errors && state.errors.length > 0) {
                translateValidationErrors(
                    state.errors.map(s => ({
                        ...s,
                        instancePath: s.dataPath,
                    }))
                );
                if (!hasErrorsRef.current) {
                    hasErrorsRef.current = true;
                    onChange?.({
                        hasErrors: true,
                    });
                }
            } else if (payloadRef.current !== state.data) {
                payloadRef.current = state.data;
                hasErrorsRef.current = false;
                onChange?.({
                    payload: state.data,
                    hasErrors: false,
                });
            }
        },
        [onChange]
    );

    const handleJsonEditorChange = useCallback(
        (value: any) => {
            payloadRef.current = value;
            hasErrorsRef.current = false;
            onChange?.({
                payload: value,
                hasErrors: false,
            });
        },
        [onChange]
    );

    if (!schema) {
        return <NoSchema>{t("NoDataNecessary")}</NoSchema>;
    }
    return (
        <ErrorBoundary>
            <JsonForms
                schema={schema as any}
                uischema={uischema}
                data={payloadRef.current}
                onChange={handlePayloadChange}
                context={context}
                bindContext={bindContext}
                uiSchemaLibrary={uiSchemaLibrary}
            />
            {jsonEditorOpen && (
                <PayloadJsonEditorModal
                    defaultValue={payloadRef.current}
                    jsonSchema={schema}
                    open={jsonEditorOpen}
                    onChange={handleJsonEditorChange}
                    onClose={onJsonEditorClose}
                />
            )}
        </ErrorBoundary>
    );
};
