import React, {FC, useState, useCallback} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import Autocomplete from "@material-ui/lab/Autocomplete";
import TextField from "@material-ui/core/TextField";
import {useTranslation} from "react-i18next";
import {getCodes, LangCode} from "./lang-helpers";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        list: {
            flexGrow: 1,
            maxHeight: "400",
            overflowY: "auto",
        },
        paper: {
            backgroundColor: theme.palette.background.default,
        },
        localeSelector: {
            width: "55ch",
        },

    })
);

export interface LocaleSelectorProps {
    open: boolean;
    onSelected: (locale: LangCode) => void;
    onClose: () => void;
}

export const LocaleSelector: FC<LocaleSelectorProps> = props => {
    const {open, onSelected, onClose} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const codes = getCodes();
    const [selectedCode, setSelectedCode] = useState<LangCode>();

    const handleCodeSelected = useCallback(() => {
        if (!selectedCode) {
            return;
        }
        onSelected?.(selectedCode);
        onClose?.();
    }, [onSelected, onClose, selectedCode]);

    const handleDialogClose = useCallback(async (event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [onClose]);

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            fullWidth
            maxWidth="sm"
            classes={{paper: classes.paper}}
        >
            <DialogTitle>{t("SelectLocale")}</DialogTitle>
            <DialogContent style={{minWidth: "400px"}}>
                <Autocomplete
                    options={codes}
                    getOptionLabel={s => {
                        let result = s.code;
                        if (s.names.result) {
                            result += ` (${s.names.result})`
                        }
                        return result;
                    }}
                    value={selectedCode ?? null}
                    onChange={(_event, value) => {
                        setSelectedCode(value ?? undefined);
                    }}
                    renderInput={params => {
                        return (
                            <TextField
                                placeholder={t("EnterALocale")}
                                {...params}
                            />
                        );
                    }}
                    className={classes.localeSelector}
                ></Autocomplete>
                {/* {!isValidating && !!error && <Alert severity="error">{error.toString()}</Alert>} */}
            </DialogContent>
            <DialogActions>
                <Button onClick={handleCodeSelected} color="primary" disabled={!selectedCode}>
                    {t("Accept")}
                </Button>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
