import React, {FC, useState, useEffect, useCallback, useRef} from "react";
import {DiagramEngine} from "@projectstorm/react-diagrams";
import {isPresent} from "ts-is-present";

import {ContextTypeLinkModel, ContextTypeLinkModelOptions} from "./context-type-link-model";
import {selectedLinkIds, selectedNodeIdsState, focusedNodeIdsState, hoveredContextTypeNodeModelIdState} from "../context-type-diagram-state";
import {useRecoilValue} from "recoil";
import {ContextTypeLinkSegmentWidget} from "./context-type-link-segment-widget";

export enum ContextTypeLinkStatus {
    normal,
    selected,
    unfocused,
}

export interface ContextTypeLinkWidgetProps {
    link: ContextTypeLinkModel;
    diagramEngine: DiagramEngine;
}

interface Point {
    x: number;
    y: number;
}
interface CustomLinkArrowWidgetProps {
    linkId: string;
    point: Point;
    previousPoint: Point;
    linkStatus: ContextTypeLinkStatus;
    selectedColor?: string;
    color?: string;
}

const CustomLinkArrowWidget: FC<CustomLinkArrowWidgetProps> = props => {
    const {linkId, point, previousPoint, color, selectedColor, linkStatus} = props;

    const {x: x1, y: y1} = point;
    const {x: x0, y: y0} = previousPoint;
    const angle = 90 + (Math.atan2(y1 - y0, x1 - x0) * 180) / Math.PI;

    return (
        <g className="arrow" transform={`translate(${x1}, ${y1})`}>
            <g style={{transform: `rotate(${angle}deg)`}}>
                <g transform={"translate(0, -5)"}>
                    <polygon
                        points="0,5 4,15 -4,15"
                        fill={linkStatus === ContextTypeLinkStatus.selected ? selectedColor : color}
                        opacity={linkStatus === ContextTypeLinkStatus.selected ? 0.8 : ContextTypeLinkStatus.unfocused ? 0.1 : 0.3}
                        data-linkid={linkId}
                    ></polygon>
                </g>
            </g>
        </g>
    );
};

const cornerRadius = 5;

const shortenLine = (p: Point, origin: Point, adjustment: number): Point => {
    const {x: x0, y: y0} = origin;
    const {x: x1, y: y1} = p;
    const dx = x1 - x0;
    const dy = y1 - y0;
    if (Math.abs(dy) < 0.0001) {
        const sign = Math.sign(dx);
        return {x: x1 - adjustment * sign, y: y1};
    } else if (Math.abs(dx) < 0.0001) {
        const sign = Math.sign(dy);
        return {x: x1, y: y1 - adjustment * sign};
    } else {
        const len = Math.sqrt(dx * dx + dy * dy);
        const adjustedLen = len - adjustment;
        const angle = Math.atan2(dy, dx);
        const x = adjustedLen * Math.cos(angle);
        const y = adjustedLen * Math.sin(angle);
        return {x: x0 + x, y: y0 + y};
    }
};

const svgPoint = (p: Point) => `${p.x} ${p.y}`;
const svgMoveTo = (p: Point) => `M ${svgPoint(p)} `;
const svgLineTo = (p: Point) => `L ${svgPoint(p)} `;
const svgCubicBezierTo = (p1: Point, p2: Point, p3: Point) => `C ${svgPoint(p1)}, ${svgPoint(p2)}, ${svgPoint(p3)} `;

const moveTowardsLength = (movingPoint: Point, targetPoint: Point, amount: number): Point => {
    const width = targetPoint.x - movingPoint.x;
    const height = targetPoint.y - movingPoint.y;
    const distance = Math.sqrt(width * width + height * height);
    return moveTowardsFractional(movingPoint, targetPoint, Math.min(1, amount / distance));
};

const moveTowardsFractional = (movingPoint: Point, targetPoint: Point, fraction: number): Point => {
    return {
        x: movingPoint.x + (targetPoint.x - movingPoint.x) * fraction,
        y: movingPoint.y + (targetPoint.y - movingPoint.y) * fraction,
    };
};

const getLinkPaths = (p: Point[]): string[] => {
    const paths: string[] = [];
    const lastIdx = p.length - 1;
    let p0 = p[0];
    let path = "";
    for (let i = 1; i < lastIdx; i++) {
        path = svgMoveTo(p0);
        let p1 = p[i];

        // Curved corner link - begin
        let p2 = p[i + 1];
        let curveStart: Point;
        let curveEnd: Point;
        curveStart = moveTowardsLength(p1, p0, cornerRadius);
        curveEnd = moveTowardsLength(p1, p2, cornerRadius);
        path += svgLineTo(curveStart);

        // The curve control points are halfway between the start/end of the curve and the original point
        const startControl = moveTowardsFractional(curveStart, p1, 0.5);
        const endControl = moveTowardsFractional(p1, curveEnd, 0.5);
        path += svgCubicBezierTo(startControl, endControl, curveEnd);
        p0 = curveEnd;
        // Curved corner link - end

        // Straight corner link - begin
        // path += svgLineTo(p1);
        // p0 = p1;
        // Straight corner link - end

        paths.push(path);
    }
    path = svgMoveTo(p0);
    path += svgLineTo(p[lastIdx]);
    paths.push(path);
    return paths;
};

export const ContextTypeLinkWidget: FC<ContextTypeLinkWidgetProps> = props => {
    const {link, diagramEngine} = props;
    const [selected, setSelected] = useState(false);
    const hoveredNodeId = useRecoilValue(hoveredContextTypeNodeModelIdState);
    const globalSelected = useRecoilValue(selectedLinkIds);
    const selectedNodeIds = useRecoilValue(selectedNodeIdsState);
    const focusdNodeIds = useRecoilValue(focusedNodeIdsState);
    const refsRef = useRef<React.RefObject<SVGPathElement>[]>([]);

    const srcSelected = selectedNodeIds.includes(link.getSourcePort()?.getNode().getID());
    const tgtSelected = selectedNodeIds.includes(link.getTargetPort()?.getNode().getID());
    const isSelected = selected || globalSelected.includes(link.getID()) || srcSelected || tgtSelected;
    const linkStatus: ContextTypeLinkStatus = isSelected
        ? ContextTypeLinkStatus.selected
        : focusdNodeIds.length > 0
        ? ContextTypeLinkStatus.unfocused
        : ContextTypeLinkStatus.normal;
    const isOutgoing = srcSelected || link.getSourcePort()?.getNode().getID() === hoveredNodeId;

    useEffect(
        () => {
            if (refsRef.current) {
                link.setRenderedPaths(refsRef.current.map(ref => ref.current).filter(isPresent));
            }
            return () => link.setRenderedPaths([]);
        } /* run always */
    );

    const handleSelected = useCallback((selected: boolean) => {
        setSelected(selected);
    }, []);

    const points = link.getPoints();

    if (points.length <= 1) {
        link.setRenderedPaths([]);
        return <g />;
    }

    const linkId = link.getID();
    const linkOpts = link.getOptions() as ContextTypeLinkModelOptions;

    const p: Point[] = points.map(s => ({x: s.getX(), y: s.getY()}));
    const lastIdx = p.length - 1;
    const origEndPoint = p[lastIdx];
    //Shorten last link so that it does not stick through the arrowhead.
    const adjustment = 8;
    p[lastIdx] = shortenLine(p[lastIdx], p[lastIdx - 1], adjustment);

    const svgPaths = getLinkPaths(p);
    refsRef.current = svgPaths.map(() => React.createRef<SVGPathElement>());

    return (
        <g data-default-link-test={linkOpts.testName}>
            {svgPaths.map((path, i) => (
                <ContextTypeLinkSegmentWidget
                    key={`link-${i}`}
                    path={path}
                    diagramEngine={diagramEngine}
                    factory={diagramEngine.getFactoryForLink(link)}
                    link={link}
                    linkStatus={linkStatus}
                    isOutgoing={isOutgoing}
                    forwardRef={refsRef.current[i]}
                    onSelection={handleSelected}
                    extras={{
                        "data-linkid": linkId,
                    }}
                />
            ))}
            <CustomLinkArrowWidget
                key="link-arrowhead"
                linkId={linkId}
                point={origEndPoint}
                previousPoint={p[lastIdx - 1]}
                selectedColor={isOutgoing ? linkOpts.selectedOutgoingColor : linkOpts.selectedColor}
                color={linkOpts.color}
                linkStatus={linkStatus}
            />
        </g>
    );
};
