import React, {FC, useMemo, useCallback} from "react";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import FunctionsIcon from "@material-ui/icons/Functions";
import {DiagramEngine} from "@projectstorm/react-diagrams-core";

import {ContextTypeNodePanel, ContextTypeNodePanelAction} from "./context-type-node-panel";
import {SelectorNodeModel} from "./selector-node-model";
import {ContextTypeNodeAvatar} from "./context-type-node-avatar";
import {ContextTypeNodeTagging} from "./context-type-node-tagging";
import {TaggingType} from "../taggings";

export interface SelectorNodeWidgetProps {
    node: SelectorNodeModel;
    engine: DiagramEngine;
}

export const SelectorNodeWidget: FC<SelectorNodeWidgetProps> = props => {
    const {engine, node} = props;

    const handleChange = useCallback(
        (tagging: TaggingType) => {
            node.dispatchCommand({
                type: "setTagging",
                tagging,
            });
        },
        [node]
    );
    const actions = useMemo(() => {
        const actions: ContextTypeNodePanelAction[] = [
            {
                icon: <EditIcon />,
                onClick: () => {
                    node.dispatchCommand({
                        type: "edit",
                    });
                },
            },
            {
                icon: <DeleteIcon />,
                onClick: () => {
                    node.dispatchCommand({
                        type: "delete",
                    });
                },
            },
        ];
        return actions;
    }, [node]);

    return (
        <ContextTypeNodePanel
            engine={engine}
            node={node}
            avatar={
                <ContextTypeNodeTagging value={node.taggingType} onChange={handleChange}>
                    <ContextTypeNodeAvatar>
                        <FunctionsIcon />
                    </ContextTypeNodeAvatar>
                </ContextTypeNodeTagging>
            }
            actions={actions}
            selected={node.isSelected()}
        ></ContextTypeNodePanel>
    );
};
