import React from "react";
import {GenerateWidgetEvent} from "@projectstorm/react-canvas-core";
import {DefaultLinkFactory} from "@projectstorm/react-diagrams";
import {ContextTypeLinkModel} from "./context-type-link-model";
import {ContextTypeLinkWidget} from "./context-type-link-widget";

export class ContextTypeLinkFactory extends DefaultLinkFactory {
    constructor() {
        super("contextTypeLink");
    }

    generateModel(): ContextTypeLinkModel {
        return new ContextTypeLinkModel();
    }

    generateReactWidget(event: GenerateWidgetEvent<ContextTypeLinkModel>): JSX.Element {
        return <ContextTypeLinkWidget link={event.model} diagramEngine={this.engine} />;
    }
}
