import {FC, useState, useCallback, useMemo} from "react";
import {differenceInCalendarDays, isValid} from "date-fns";
import {parseISO} from "date-fns/esm";
import {useNavigate} from "react-router";
import * as yup from "yup";
import {useTranslation} from "react-i18next";

import {DigitalWorker} from "../../api/nimflow-api";
import {useNimflow} from "../../api/nimflow-api-provider";
import {DigitalWorkerEditor} from "./digital-worker-editor";
import {DigitalWorkerSchema} from "./digital-worker-schema";

const createDigitalWorker = () => {
    const result: DigitalWorker = {
        id: "",
        name: "",
        active: true,
    };
    return result;
};

export const DigitalWorkerCreator: FC<{}> = () => {
    const navigate = useNavigate();
    const {t} = useTranslation();
    const {digitalWorkers_Add, digitalWorkers_Search} = useNimflow();
    const [digitalWorker, setDigitalWorker] = useState<DigitalWorker>(() => {
        return createDigitalWorker();
    });

    const handleOnChange = useCallback(
        async (value: DigitalWorker) => {
            let expiresInDays: number | undefined = undefined;
            if (value.expires) {
                const date = parseISO(value.expires);
                if (isValid(date)) {
                    expiresInDays = differenceInCalendarDays(date, new Date());
                }
            }
            await digitalWorkers_Add({
                name: value.name,
                role: value.role!,
                expiresInDays,
            });
            setDigitalWorker(value);
            setTimeout(() => navigate(`../edit/${value.name}`, {replace: true}), 0);
            return true;
        },
        [digitalWorkers_Add, navigate]
    );

    const DigitalWorkerCreatorSchema = useMemo(
        () =>
            DigitalWorkerSchema.shape({
                name: DigitalWorkerSchema.fields!.name.concat(
                    yup
                        .string()
                        .defined()
                        .test({
                            message: ({value}) => t("DigitalWorkerNameAlreadyExists", {name: value}),
                            test: async function (value: any) {
                                if (!value) {
                                    return true;
                                }
                                const dws = await digitalWorkers_Search({name: value});
                                return dws.length === 0;
                            },
                        })
                ),
            }),
        [digitalWorkers_Search, t]
    );

    return <DigitalWorkerEditor value={digitalWorker} schema={DigitalWorkerCreatorSchema} onSave={handleOnChange} />;
};
