import React, {FC, HTMLAttributes} from "react";
import clsx from "clsx";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import Drawer, {DrawerProps} from "@material-ui/core/Drawer";
import Typography, {TypographyProps} from "@material-ui/core/Typography";
import IconButton, {IconButtonProps} from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Toolbar from "@material-ui/core/Toolbar";

const drawerMinWidth = 300;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        drawer: {
            minWidth: drawerMinWidth,
            flexShrink: 0,
        },
        drawerPaper: {
            minWidth: drawerMinWidth,
            
        },
        title: {
            margin: 0,
        },
        header: {
            display: "flex",
            justifyContent: "space-between",
            padding: "8px 20px",
        },
        content: {
            flexGrow: 1,
            flexShrink: 1,
            overflow: "auto",
            paddingTop: 0,
            "&.with-footer": {
                paddingBottom: 0,
            },
        },
        footer: {
            flexGrow: 0,
            flexShrink: 0,
        },
    })
);

export interface ContextPanelProps {
    open: boolean;
    onClose?: () => void;
    classes?: {
        root?: string;
    },
    header?: React.ReactNode;
    children?: React.ReactNode;
    footer?: React.ReactNode;
    containerProps?: Omit<DrawerProps, "anchor" | "open">;
    headerProps?: HTMLAttributes<HTMLDivElement>;
    headerTitleProps?: TypographyProps;
    headerCloseButtonProps?: IconButtonProps;
    contentContainerProps?: HTMLAttributes<HTMLDivElement>;
    footerProps?: HTMLAttributes<HTMLDivElement>;
}

export const ContextPanel: FC<ContextPanelProps> = props => {
    const {
        open,
        onClose,
        header,
        children,
        footer,
        containerProps,
        headerProps,
        headerTitleProps,
        headerCloseButtonProps,
        contentContainerProps,
        footerProps,
    } = props;
    const classes = useStyles();

    return (
        <Drawer
            variant="persistent"
            open={open}
            classes={{
                paper: classes.drawerPaper,
            }}
            anchor="right"
            {...containerProps}
            className={clsx(classes.drawer, containerProps?.className)}
        >
            <Toolbar />
            <div {...headerProps} className={clsx(classes.header, headerProps?.className)}>
                <Typography
                    variant="h5"
                    {...headerTitleProps}
                    className={clsx(classes.title, headerTitleProps?.className)}
                >
                    {header}
                </Typography>
                <IconButton size="small" onClick={onClose} {...headerCloseButtonProps}>
                    <CloseIcon />
                </IconButton>
            </div>
            <div
                {...contentContainerProps}
                className={clsx(classes.content, {"with-footer": !!footer}, contentContainerProps?.className)}
            >
                {children}
            </div>
            {!!footer && (
                <div {...footerProps} className={clsx(classes.footer, footerProps?.className)}>
                    {footer}
                </div>
            )}
        </Drawer>
    );
};
