import React, {useCallback} from "react";
import {Control, SetValueConfig} from "react-hook-form/dist/types/form";
import {
    update,
    insert as rinsert,
    append as rappend,
    prepend as rprepend,
    move as rmove,
    concat,
    insertAll,
} from "ramda";
import {isDefined, isPresent} from "ts-is-present";
import {isNameInFieldArray, get} from "../libs/react-hooks-form";

type UpdaterMethods<TItem> = {
    getValue: () => Partial<TItem>[] | undefined;
    setValue: (value?: Partial<TItem>[], options?: SetValueConfig) => void;
};

const updateValue = <T>(methods: UpdaterMethods<T>, updater: (array: Partial<T>[]) => Partial<T>[]) => {
    const originalValue = (methods.getValue() as T[] | undefined) ?? [];
    const curValue = originalValue ?? [];
    const newValue = updater(curValue);
    if (curValue !== newValue) {
        methods.setValue(newValue, {shouldDirty: true});
    }
};

export type useArrayFieldOptions<TKeyName extends string = "id", TControl extends Control = Control> = {
    name: string;
    control: TControl;
    keyName?: TKeyName;
    defaultValue?: any[];
};

export const useArrayField = <TItem, TKeyName extends string = "id", TControl extends Control = Control>({
    control,
    name,
    keyName = "id" as TKeyName,
    defaultValue,
}: useArrayFieldOptions<TKeyName, TControl>) => {
    const {
        register,
        unregister,
        fieldsRef,
        setValue,
        updateFormState,
        readFormStateRef,
        fieldArrayNamesRef,
        shallowFieldsStateRef,
        defaultValuesRef,
        validateResolver,
    } = control;
    const updaterMethods: UpdaterMethods<TItem> = React.useMemo(() => {
        return {
            getValue: () => {
                const fld = fieldsRef.current?.[name];
                if (fld) {
                    const {
                        ref: {value},
                    } = fld;
                    return value as Partial<TItem>[];
                }
                return undefined;
            },
            setValue: (value?: Partial<TItem>[], options?: SetValueConfig) => {
                setValue(name, value as any, options);
                if (readFormStateRef.current.isValid && validateResolver) {
                    validateResolver({
                        [name]: value,
                    });
                }
                updateFormState();
            },
        };
    }, [fieldsRef, setValue, name, readFormStateRef, validateResolver, updateFormState]);

    const getInitialValue = useCallback((): Partial<TItem>[] => {
        const isNotFieldArray = !isNameInFieldArray(fieldArrayNamesRef.current, name);
        return isPresent(shallowFieldsStateRef.current[name]) && isNotFieldArray
            ? shallowFieldsStateRef.current[name]
            : defaultValue ?? get(defaultValuesRef.current, name) ?? [];
    }, [name, fieldArrayNamesRef, shallowFieldsStateRef, defaultValuesRef, defaultValue]);

    const fields = updaterMethods.getValue() ?? getInitialValue();
    const registerField = useCallback(() => {
        register({name, type: "array-field"}); //Define type to detect changes
        if (!updaterMethods.getValue()) {
            const initialValue = getInitialValue();
            updaterMethods.setValue(initialValue);
        }
        updateFormState();
        return () => unregister(name);
    }, [register, unregister, updateFormState, updaterMethods, name, getInitialValue]);

    React.useEffect(() => {
        registerField();
    }, [registerField]);

    // eslint-disable-next-line react-hooks/exhaustive-deps
    React.useEffect(() => {
        // Always check if registered because sometimes fields are reset by RHF and the become unregistered
        if (!fieldsRef.current[name]) {
            registerField();
        }
    });

    const swap = React.useCallback(
        (indexA: number, indexB: number) => {
            updateValue(updaterMethods, currArray => {
                if (currArray.length <= 1) {
                    return currArray;
                }
                const oldA = currArray[indexA];
                const oldB = currArray[indexB];
                return currArray.map((value, i) => {
                    if (i === indexA) {
                        return oldB;
                    }
                    if (i === indexB) {
                        return oldA;
                    }
                    return value;
                });
            });
        },
        [updaterMethods]
    );

    const move = React.useCallback(
        (from: number, to: number) => {
            updateValue(updaterMethods, currArray => {
                if (currArray.length <= 1) {
                    return currArray;
                }
                return rmove(from, to, currArray);
            });
        },
        [updaterMethods]
    );

    const prepend = React.useCallback(
        (value: Partial<TItem> | Partial<TItem>[]) => {
            updateValue(updaterMethods, currArray => {
                if (Array.isArray(value)) {
                    return concat(value, currArray);
                }
                return rprepend(value, currArray);
            });
        },
        [updaterMethods]
    );

    const append = React.useCallback(
        (value: Partial<TItem> | Partial<TItem>[]) => {
            updateValue(updaterMethods, currArray => {
                if (Array.isArray(value)) {
                    return concat(currArray, value);
                }
                return rappend(value, currArray);
            });
        },
        [updaterMethods]
    );

    const remove = React.useCallback(
        (index?: number | number[] | undefined) => {
            updateValue(updaterMethods, currArray => {
                if (currArray.length === 0) {
                    return currArray;
                }
                if (!isDefined(index)) {
                    return [];
                }
                if (currArray) {
                    const newArray: Partial<TItem>[] = [];
                    const indices = Array.isArray(index) ? index : [index];
                    for (let i = 0; i < currArray.length; i++) {
                        if (!indices.includes(i)) {
                            newArray.push(currArray[i]);
                        }
                    }
                    return newArray;
                }
                return currArray;
            });
        },
        [updaterMethods]
    );

    const insert = React.useCallback(
        (index: number, value: Partial<TItem> | Partial<TItem>[]) => {
            updateValue(updaterMethods, currArray => {
                if (Array.isArray(value)) {
                    return insertAll(index, value, currArray);
                }
                return rinsert(index, value, currArray);
            });
        },
        [updaterMethods]
    );

    const setAt = React.useCallback(
        (index: number, value: Partial<TItem> | Partial<TItem>[]) => {
            updateValue(updaterMethods, currArray => {
                if (Array.isArray(value)) {
                    const newArray = [...currArray];
                    newArray.splice(index, value.length, ...value);
                    return newArray;
                }
                return update(index, value, currArray);
            });
        },
        [updaterMethods]
    );

    return {
        fields,
        swap,
        move,
        prepend,
        append,
        remove,
        insert,
        setAt,
    };
};
