import React, {useCallback} from "react";
import {FieldValues} from "react-hook-form/dist/types/fields";
import {Control, SetValueConfig} from "react-hook-form/dist/types/form";
import {isPresent} from "ts-is-present";
import {get, isNameInFieldArray} from "../libs/react-hooks-form";

type UpdaterMethods<TValue> = {
    getValue: () => Partial<Record<string, TValue>> | undefined;
    setValue: (value?: Partial<Record<string, TValue>>, options?: SetValueConfig) => void;
};

const updateValue = <T>(
    methods: UpdaterMethods<T>,
    updater: (obj: Partial<Record<string, T>>) => Partial<Record<string, T>>
) => {
    const originalValue = methods.getValue();
    const curValue = originalValue ?? {};
    const newValue = updater(curValue);
    if (curValue !== newValue) {
        methods.setValue(newValue, {shouldDirty: true});
    }
};

export type useObjectFieldOptions<TControl extends Control = Control> = {
    name: string;
    control: TControl;
    defaultValue?: Record<string, any>;
};

export const useObjectField = <TValue = FieldValues, TControl extends Control = Control>({
    control,
    name,
    defaultValue,
}: useObjectFieldOptions<TControl>) => {
    const {
        register,
        unregister,
        fieldsRef,
        setValue,
        updateFormState,
        readFormStateRef,
        fieldArrayNamesRef,
        defaultValuesRef,
        shallowFieldsStateRef,
        validateResolver,
    } = control;
    const updaterMethods: UpdaterMethods<TValue> = React.useMemo(() => {
        return {
            getValue: () => {
                const fld = fieldsRef.current?.[name];
                if (fld) {
                    const {
                        ref: {value},
                    } = fld;
                    return value as Record<string, TValue>;
                }
                return undefined;
            },
            setValue: (value?: Partial<Record<string, TValue>>, options?: SetValueConfig) => {
                setValue(name, value as any, options);
                if (readFormStateRef.current.isValid && validateResolver) {
                    validateResolver({
                        [name]: value,
                    });
                }
                updateFormState();
            },
        };
    }, [fieldsRef, setValue, name, readFormStateRef, validateResolver, updateFormState]);
    const getInitialValue = useCallback((): Partial<Record<string, TValue>> => {
        const isNotFieldArray = !isNameInFieldArray(fieldArrayNamesRef.current, name);
        return isPresent(shallowFieldsStateRef.current[name]) && isNotFieldArray
            ? shallowFieldsStateRef.current[name]
            : defaultValue ?? get(defaultValuesRef.current, name) ?? {};
    }, [fieldArrayNamesRef, name, shallowFieldsStateRef, defaultValuesRef, defaultValue]);

    const value = updaterMethods.getValue() ?? getInitialValue();

    const registerField = useCallback(() => {
        register({name, type: "object-field"});
        if (!updaterMethods.getValue()) {
            const initialValue = getInitialValue();
            updaterMethods.setValue(initialValue);
        }
        updateFormState();
        return () => unregister(name);
    }, [register, unregister, updateFormState, updaterMethods, name, getInitialValue]);

    React.useEffect(() => {
        registerField();
    }, [registerField]);

    // eslint-disable-next-line react-hooks/exhaustive-deps
    React.useEffect(() => {
        // Always check if registered because sometimes fields are reset by RHF and the become unregistered
        if (!fieldsRef.current[name]) {
            registerField();
        }
    });

    const set = React.useCallback(
        (key: string, value: TValue) => {
            updateValue(updaterMethods, currObj => {
                return {
                    ...currObj,
                    [key]: value,
                };
            });
        },
        [updaterMethods]
    );

    const deleteKey = React.useCallback(
        (key: string) => {
            updateValue(updaterMethods, currObj => {
                const result = {
                    ...currObj,
                };
                delete result[key];
                return result;
            });
        },
        [updaterMethods]
    );

    return {
        value,
        set,
        deleteKey,
    };
};
