import React, {FC, useCallback} from "react";
import {createStyles, makeStyles, withStyles, Theme} from "@material-ui/core";
import {ContextElementType} from "./context-type-diagram-model";
import ToggleButtonGroup from "@material-ui/lab/ToggleButtonGroup";
import Tooltip from "@material-ui/core/Tooltip";
import ToggleButton from "@material-ui/lab/ToggleButton";
import InputIcon from '@material-ui/icons/Input';
import FunctionsIcon from "@material-ui/icons/Functions";
import AssignmentTurnedInIcon from "@material-ui/icons/AssignmentTurnedIn";
import EditAttributesIcon from "@material-ui/icons/EditAttributes";
import {useTranslation} from "react-i18next";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        group: {
            marginLeft: 5
        },
        fixHeight: {
            height: 24,
        },
    })
);

const StyledToggleButtonGroup = withStyles((theme) => ({
    grouped: {
      margin: theme.spacing(0.5),
      border: 'none',
      '&:not(:first-child)': {
        borderRadius: theme.shape.borderRadius,
      },
      '&:first-child': {
        borderRadius: theme.shape.borderRadius,
      },
    },
  }))(ToggleButtonGroup);
export interface ContextTypeElementVisibilitySelectorProps {
    value: ContextElementType[];
    onChange?: (value: ContextElementType[]) => void;
}

export const ContextTypeElementVisibilitySelector: FC<ContextTypeElementVisibilitySelectorProps> = props => {
    const {value, onChange} = props;
    const {t} = useTranslation();
    const classes = useStyles();

    const handleChange = useCallback(
        (event: React.MouseEvent<HTMLElement>, newValue: ContextElementType[]) => {
            onChange?.(newValue);
        },
        [onChange]
    );

    return (
        <StyledToggleButtonGroup value={value} onChange={handleChange} size="small" className={classes.group}>
            <ToggleButton value="actionTypes">
                <Tooltip title={t("ActionTypes") as string}>
                    <div className={classes.fixHeight}>
                        <InputIcon />
                    </div>
                </Tooltip>
            </ToggleButton>
            <ToggleButton value="states">
                <Tooltip title={t("States") as string}>
                    <div className={classes.fixHeight}>
                        <EditAttributesIcon />
                    </div>
                </Tooltip>
            </ToggleButton>
            <ToggleButton value="selectors">
                <Tooltip title={t("Selectors") as string}>
                    <div className={classes.fixHeight}>
                        <FunctionsIcon />
                    </div>
                </Tooltip>
            </ToggleButton>
            <ToggleButton value="taskTypes">
                <Tooltip title={t("TaskTypes") as string}>
                    <div className={classes.fixHeight}>
                        <AssignmentTurnedInIcon />
                    </div>
                </Tooltip>
            </ToggleButton>
        </StyledToggleButtonGroup>
    );
};
