const convertLayoutOptions = (layoutOptions?: ElkLayoutOptions) => {
    if (!layoutOptions) {
        return undefined;
    }
    const result: LayoutOptions = {};
    for (const [key, value] of Object.entries(layoutOptions)) {
        switch (`org.eclipse.${key}`) {
            case "org.eclipse.elk.contentAlignment":
            case "org.eclipse.elk.nodeSize.constraints":
            case "org.eclipse.elk.nodeSize.options":
            case "org.eclipse.elk.portLabels.placement":
            case "org.eclipse.elk.layered.wrapping.cutting.cuts":
            case "org.eclipse.elk.layered.wrapping.validify.forbiddenIndices":
            case "org.eclipse.elk.nodeLabels.placement":
                if (Array.isArray(value)) {
                    result[key] = value.map(s => "" + s).join(" ");
                    break;
                }
                result[key] = value;
                break;
            default:
                result[key] = value;
                break;
        }
    }
    return result;
};

export interface ElkEngineOptions {
    algorithm: ElkAlgorithm;
    layoutOptions?: ElkLayoutOptions;
    nodeLayoutOptions?: (node: NodeModel<any>) => ElkLayoutOptions;
    linkLayoutOptions?: (link: LinkModel<any>) => ElkLayoutOptions;
    includeLinks?: boolean;
}

const isContextTypeNodeModel = (node: NodeModel<any>): node is ContextTypeNodeModel<any> => {
    return node instanceof ContextTypeNodeModel;
};

const elkPointToLinkPoint = (point: ElkPoint, link: LinkModel<any>) => {
    return new PointModel({
        link,
        position: new Point(point.x, point.y),
    });
};

export class ElkEngine {
    options: ElkEngineOptions;
    constructor(options: ElkEngineOptions) {
        this.options = options;
    }

    async redistribute(model: DiagramModel) {
        const elk = instantiateElkEngine({
            algorithms: [this.options.algorithm],
            defaultLayoutOptions: convertLayoutOptions(this.options.layoutOptions),
        });

        const processedlinks: {[id: string]: boolean} = {};

        const nodes = sortBy(s => s.instanceId, model.getNodes().filter(isContextTypeNodeModel)).map(node => {
            const nodeOptions = this.options.nodeLayoutOptions?.(node);
            const result: ElkNode = {
                id: node.getID(),
                width: node.width,
                height: node.height,
                //x: node.getX(),
                //y: node.getY(),
                layoutOptions: convertLayoutOptions(nodeOptions),
                ports: Object.values(node.getPorts()).map(s => ({
                    id: s.getID(),
                    height: s.height,
                    width: s.width,
                    x: s.getX() - node.getX(),
                    y: s.getY() - node.getY(),
                })),
            };
            return result;
        });

        const edges: ElkExtendedEdge[] = sortBy(
            s => (s instanceof ContextTypeLinkModel ? s.getInstanceId() : "edge"),
            model.getLinks().filter(s => s.getSourcePort() && s.getTargetPort())
        ).map(link => {
            processedlinks[link.getID()] = true;
            const linkOptions = this.options.linkLayoutOptions?.(link);
            const result: ElkExtendedEdge = {
                id: link.getID(),
                sources: [link.getSourcePort().getID()],
                targets: [link.getTargetPort().getID()],
                sections: [],
                layoutOptions: convertLayoutOptions(linkOptions),
            };
            return result;
        });

        const newGraph = await elk.layout({
            id: "root",
            children: nodes,
            edges,
        });

        //console.log(newGraph);

        for (const node of newGraph.children!) {
            model.getNode(node.id).setPosition(node.x!, node.y!);
        }

        // also include links?
        if (this.options.includeLinks && newGraph.edges) {
            for (const edge of newGraph.edges as ElkExtendedEdge[]) {
                const link = model.getLink(edge.id);
                const section = edge.sections[0];
                const points = [elkPointToLinkPoint(section.startPoint, link)];
                if (section.bendPoints) {
                    for (const bendPoint of section.bendPoints) {
                        points.push(elkPointToLinkPoint(bendPoint, link));
                    }
                }
                link.setPoints(points.concat(elkPointToLinkPoint(section.endPoint, link)));
            }
        }
    }
}

