//Use this function to update type ElkLayoutOptions in file elk.d.ts
export async function generateOptionsToFile() {
    var elk = new ELK({
        algorithms: [
            "layered",
            "stress",
            "mrtree",
            "radial",
            "force",
            "disco",
            "rectpacking",
            "sporeCompaction",
            "sporeOverlap",
        ],
    });

    //const algs = await elk.knownLayoutAlgorithms();
    const opts = await elk.knownLayoutOptions();
    //const cats = await elk.knownLayoutCategories();

    let result = "export interface ElkLayoutOptions extends LayoutOptions {\n";
    for (const opt of opts as ElkLayoutOptionDescription[]) {
        result += "/**\n";
        if (opt.name) {
            result += ` * ${opt.name}\n`;
        }
        if (opt.description) {
            result += ` * ${opt.description}\n`;
        }
        if (opt.targets && opt.targets.length > 0) {
            result += ` * Targets: ${opt.targets!.join(", ")}\n`;
        }
        let id = opt.id;
        if (id?.startsWith("org.eclipse.elk")) {
            id = id.slice(12);
        }
        let type = opt.type ?? "any";
        if (opt.id && opt.id in optionTypeMap) {
            type = optionTypeMap[opt.id];
        } else if (opt.type) {
            switch (opt.type) {
                case "STRING":
                    type = "string";
                    break;
                case "DOUBLE":
                case "INT":
                    type = "number";
                    break;
                case "BOOLEAN":
                    type = "boolean";
                    break;
                case "ENUMSET":
                    type = `any[] /*${opt.type}*/`;
                    break;
                default:
                    type = `any /*${opt.type}*/`;
                    break;
            }
        }
        result += " */\n";
        result += `"${id}"?: ${type};\n`;
    }
    result += "}";
    //console.log(result);
    downloadString(result, "ElkLayoutOptions.js");
}

