import { useTranslation } from 'react-i18next';
import {useCallback} from "react";

import {useOkCancelConfirm, OkCancelConfirmOptions} from "./confirm";

export const useLoseChangesConfirm = (): ((opts?: OkCancelConfirmOptions) => Promise<boolean>) => {
    const {t} = useTranslation();
    const confirm = useOkCancelConfirm();
    return useCallback((opts = {}) => {
        const resolvedOpts: OkCancelConfirmOptions = {
            title: t("ThereArePendingChanges"),
            description: t("ChangesWillBeLost_Continue"),
            ...opts,
        }
        return confirm(resolvedOpts);
    }, [confirm, t]);
};
