import React, {FC, useState, useCallback} from "react";
import {useTranslation} from "react-i18next";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import Typography from "@material-ui/core/Typography";
import MenuItem from "@material-ui/core/MenuItem";
import {useFormikContext} from "formik";
import i18next from "i18next";
import {subMinutes, parseISO, differenceInDays, differenceInMinutes, addDays, addMinutes} from "date-fns";
import {clone} from "ramda";
import {KeyboardTimePicker, KeyboardDatePicker} from "@material-ui/pickers";

import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {
    DashboardParameterTimeRangeValue,
    TimeRangeDashboardParameter,
    DynamicTimeRangeUnit,
    DynamicDashboardParameterTimeRangeValue,
} from "../../api/nimflow-dashboard-api";

interface TimeRangeOption {
    id: string;
    label: string;
    value: DashboardParameterTimeRangeValue;
}

const makeDynamicTimeRangeUnit = (count: number, unit: DynamicTimeRangeUnit): TimeRangeOption => {
    const id = `Last${count}${unit}`;
    return {
        id,
        label: i18next.t(`Last${unit}`, {count}),
        value: {
            type: "dynamic",
            range: {
                count,
                unit,
            },
        },
    };
};

const options: TimeRangeOption[] = [
    {
        id: "CustomTimeRange",
        label: i18next.t("CustomTimeRange") as string,
        value: {type: "static", range: {start: "", end: ""}},
    },
    makeDynamicTimeRangeUnit(15, "minutes"),
    makeDynamicTimeRangeUnit(30, "minutes"),
    makeDynamicTimeRangeUnit(1, "hours"),
    makeDynamicTimeRangeUnit(3, "hours"),
    makeDynamicTimeRangeUnit(6, "hours"),
    makeDynamicTimeRangeUnit(12, "hours"),
    makeDynamicTimeRangeUnit(24, "hours"),
    makeDynamicTimeRangeUnit(2, "days"),
    makeDynamicTimeRangeUnit(7, "days"),
    makeDynamicTimeRangeUnit(14, "days"),
    makeDynamicTimeRangeUnit(30, "days"),
    makeDynamicTimeRangeUnit(60, "days"),
];

const getValueFromOptions = (option: TimeRangeOption) => {
    let value = clone(option.value);
    if (value.type === "static") {
        const now = new Date();
        const prev = subMinutes(now, 15);
        value.range = {start: prev.toISOString(), end: now.toISOString()};
    }
    return value;
};

const adjustDate = (refDate: Date, newDate: Date, targetDate: Date) => {
    let diff = differenceInDays(newDate, refDate);
    if (diff === 0) {
        diff = differenceInMinutes(newDate, refDate);
        return addMinutes(targetDate, diff);
    } else {
        return addDays(targetDate, diff);
    }
};

export const TimeRangeParameterEditor: FC<{}> = props => {
    const {values: param, setFieldValue} = useFormikContext<TimeRangeDashboardParameter>();
    const {t} = useTranslation();
    const [selectedOption, setSelectedOption] = useState<TimeRangeOption>(() => {
        const defvalue = param.defaultValue.values[0]?.value as DashboardParameterTimeRangeValue | undefined;
        if (!defvalue) {
            return options[1];
        }
        if (defvalue.type === "static") {
            return options[0];
        }
        const range = defvalue.range;
        for (let i = 1; i < options.length; i++) {
            const opt = options[i];
            const optrange = opt.value.range as DynamicDashboardParameterTimeRangeValue["range"];
            if (range.count === optrange.count && range.unit === optrange.unit) {
                return opt;
            }
        }
        return options[1];
    });
    const [fromDate, setFromDate] = useState<Date>(() => {
        const defvalue = param.defaultValue.values[0]?.value as DashboardParameterTimeRangeValue | undefined;
        return defvalue?.type === "static" ? parseISO(defvalue.range.start) : new Date();
    });
    const [toDate, setToDate] = useState<Date>(() => {
        const defvalue = param.defaultValue.values[0]?.value as DashboardParameterTimeRangeValue | undefined;
        return defvalue?.type === "static" ? parseISO(defvalue.range.end) : new Date();
    });

    const handleDefaultValueChange = useCallback(
        (ev: React.ChangeEvent<HTMLInputElement>) => {
            const id = ev.target.value;
            const opt = options.find(s => s.id === id);
            if (!opt) {
                return;
            }
            let value = getValueFromOptions(opt);
            setFromDate(s => {
                if (value.type === "static") {
                    return parseISO(value.range.start);
                }
                return new Date();
            });
            setToDate(s => {
                if (value.type === "static") {
                    return parseISO(value.range.end);
                }
                return new Date();
            });
            setSelectedOption(opt);
            setFieldValue(`defaultValue.values[0].value`, value);
        },
        [setFieldValue]
    );

    const handleFromDateChange = useCallback((date: Date | null) => {
        if (date) {
            let to = toDate;
            if (date > toDate) {
                setToDate(s => {
                    to = adjustDate(fromDate, date, s);
                    return to;
                });
            }
            setFromDate(date);
            setFieldValue(`defaultValue.values[0].value`, {
                type: "static", 
                range: {
                    start: date.toISOString(), 
                    end: to.toISOString(),
                }
            });
        }
    }, [fromDate, setFieldValue, toDate]);

    const handleToDateChange = useCallback((date: Date | null) => {
        if (date) {
            let from = fromDate;
            if (date < fromDate) {
                setFromDate(s => {
                    from = adjustDate(toDate, date, s);
                    return from;
                });
            }
            setToDate(date);
            setFieldValue(`defaultValue.values[0].value`, {
                type: "static", 
                range: {
                    start: from.toISOString(), 
                    end: date.toISOString(),
                }
            });
        }
    }, [fromDate, setFieldValue, toDate]);

    return (
        <>
            <Grid item xs={12}>
                <FormikTextField
                    name="beginVariableName"
                    label={t("BeginVariableName")}
                    InputLabelProps={{required: true}}
                    fullWidth
                    required
                />
            </Grid>
            <Grid item xs={12}>
                <FormikTextField
                    name="endVariableName"
                    label={t("EndVariableName")}
                    InputLabelProps={{required: true}}
                    fullWidth
                    required
                />
            </Grid>
            <Grid item xs={12}>
                <TextField
                    label={t("DefaultValue")}
                    select
                    InputLabelProps={{required: true}}
                    fullWidth
                    required
                    value={selectedOption.id}
                    onChange={handleDefaultValueChange}
                >
                    {options.map(s => (
                        <MenuItem key={s.id} value={s.id}>
                            {s.label}
                        </MenuItem>
                    ))}
                </TextField>
            </Grid>
            {selectedOption.value.type === "static" && (
                <Grid item xs={12}>
                    <Typography variant="h6">{options[0].label}</Typography>
                    <Grid container spacing={2}>
                        <Grid item xs={6}>
                            <KeyboardDatePicker
                                margin="normal"
                                label={t("StartTime")}
                                format={t("DATE_FORMAT")}
                                required
                                fullWidth
                                value={fromDate}
                                onChange={handleFromDateChange}
                                KeyboardButtonProps={{
                                    "aria-label": "change date",
                                }}
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <KeyboardTimePicker
                                margin="normal"
                                label=" "
                                fullWidth
                                value={fromDate}
                                onChange={handleFromDateChange}
                                KeyboardButtonProps={{
                                    "aria-label": "change time",
                                }}
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <KeyboardDatePicker
                                margin="normal"
                                label={t("EndTime")}
                                format={t("DATE_FORMAT")}
                                fullWidth
                                required
                                value={toDate}
                                onChange={handleToDateChange}
                                KeyboardButtonProps={{
                                    "aria-label": "change date",
                                }}
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <KeyboardTimePicker
                                margin="normal"
                                label=" "
                                fullWidth
                                value={toDate}
                                onChange={handleToDateChange}
                                KeyboardButtonProps={{
                                    "aria-label": "change time",
                                }}
                            />
                        </Grid>
                    </Grid>
                </Grid>
            )}
        </>
    );
};
