import {DefaultPortModel, DefaultPortModelOptions} from "@projectstorm/react-diagrams";

import {ContextTypeLinkModel} from "./context-type-link-model";

export const defaultContextTypePortWidgetSize = 10;
export class ContextTypePortModel extends DefaultPortModel {
    constructor(isIn: boolean, name?: string, label?: string);
    constructor(options: DefaultPortModelOptions);
    constructor(isInOrOptions: any, name?: string, label?: string) {
        super(isInOrOptions, name, label);
        this.height = defaultContextTypePortWidgetSize;
        this.width = defaultContextTypePortWidgetSize;
    }
    createLinkModel(): ContextTypeLinkModel {
        return new ContextTypeLinkModel();
    }
}
