import React, {FC, useCallback} from "react";
import {makeStyles, createStyles} from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import EditIcon from "@material-ui/icons/Edit";
import MoreHorizIcon from "@material-ui/icons/MoreHoriz";
import {usePopupState, bindTrigger, bindMenu} from "material-ui-popup-state/hooks";
import {useTranslation} from "react-i18next";

import {DashboardWidgetInstantiation} from "../../api/nimflow-dashboard-api";
import {DashboardWidgetDefinition} from "../../api/nimflow-dashboard-widgets-api";

const useStyles = makeStyles(theme =>
    createStyles({
        widget: {
            display: "flex",
            overflow: "hidden",
            position: "relative",
            flexDirection: "column",
            zIndex: 0,
            height: "100%",
            borderRadius: 10,
        },
        widgetHeader: {
            paddingLeft: 8,
            minHeight: 36,
            display: "flex",
            alignItems: "center",
        },
        widgetContent: {
            position: "relative",
            flex: "1 1",
            overflow: "hidden",
            padding: "0 8px 8px",
            zIndex: 0,
        },
        widgetTitle: {
            flex: "0 1 auto",
            overflow: "hidden",
            maxWidth: "100%",
            textOverflow: "ellipsis",
            lineHeight: "20px",
        },
        actionsContainer: {
            flex: "0 0 auto",
            display: "flex",
            flexDirection: "row",
            paddingLeft: 6,
            height: "100%",
            minWidth: 8,
            //box-sizing: border-box;
        },
        spaceFiller: {
            flex: "1 1",
        },
    })
);

export interface WidgetInstanceViewerProps {
    instantiation: DashboardWidgetInstantiation;
    widgetDefinition: DashboardWidgetDefinition;
    onEditWidgetInstantiation?: (
        instantiation: DashboardWidgetInstantiation,
        widgetDefinition: DashboardWidgetDefinition
    ) => void;
    onDuplicateWidgetInstantiation?: (
        instantiation: DashboardWidgetInstantiation,
        widgetDefinition: DashboardWidgetDefinition
    ) => void;
    onDeleteWidgetInstantiation?: (
        instantiation: DashboardWidgetInstantiation,
        widgetDefinition: DashboardWidgetDefinition
    ) => void;
}

export const WidgetInstanceViewer: FC<WidgetInstanceViewerProps> = props => {
    const {
        instantiation,
        widgetDefinition,
        onEditWidgetInstantiation,
        onDuplicateWidgetInstantiation,
        onDeleteWidgetInstantiation,
    } = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const popupState = usePopupState({
        variant: "popover",
        popupId: "widgetInstanceMenu",
    });

    const handleDuplicate = useCallback(() => {
        popupState.close();
        onDuplicateWidgetInstantiation?.(instantiation, widgetDefinition);
    }, [instantiation, onDuplicateWidgetInstantiation, popupState, widgetDefinition]);

    const handleDelete = useCallback(() => {
        popupState.close();
        onDeleteWidgetInstantiation?.(instantiation, widgetDefinition);
    }, [instantiation, onDeleteWidgetInstantiation, popupState, widgetDefinition]);

    const canEdit = !!widgetDefinition.parameters?.length;

    return (
        <Paper className={classes.widget} elevation={1} >
            <div className={classes.widgetHeader}>
                <Typography className={classes.widgetTitle} variant="subtitle2" noWrap display="block">
                    {widgetDefinition.title}
                </Typography>
                <div className={classes.spaceFiller} />
                <div className={classes.actionsContainer}>
                    {canEdit && (
                        <Tooltip title={t("Edit") as string}>
                            <IconButton
                                size="small"
                                onClick={() => onEditWidgetInstantiation?.(instantiation, widgetDefinition)}
                            >
                                <EditIcon />
                            </IconButton>
                        </Tooltip>
                    )}
                    <Tooltip title={t("OpenWidgetMenu") as string}>
                        <IconButton size="small" {...bindTrigger(popupState)}>
                            <MoreHorizIcon />
                        </IconButton>
                    </Tooltip>
                    <Menu {...bindMenu(popupState)}>
                        <MenuItem onClick={handleDuplicate}>{t("DuplicateWidget")}</MenuItem>
                        <MenuItem onClick={handleDelete}>{t("DeleteWidget")}</MenuItem>
                    </Menu>
                </div>
            </div>
            <div className={classes.widgetContent}></div>
        </Paper>
    );
};
