import {ContextTypeNodeModel} from "./context-type-node-model";
import {TaggingType} from "../taggings";

export class StateNodeModel extends ContextTypeNodeModel<string> {
    constructor(instance: string, instanceId: string, public taggingType?: TaggingType) {
        super("state", instance, instanceId, instance);
    }

    public serialize(): any {
        return {
            ...super.serialize(),
            taggingType: this.taggingType,
        }
    }
}
