import {FC, useState, useMemo, useCallback, useRef, useEffect} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import PlayArrowIcon from "@material-ui/icons/PlayArrow";
import {useTranslation} from "react-i18next";
import Autosizer from "react-virtualized-auto-sizer";
import {Form, FormikProps, Formik, FormikHelpers, useFormikContext} from "formik";
import {useDebounce} from "react-use";
import Divider from "@material-ui/core/Divider";
import {isPlainObject} from "is-plain-object";
import Splitter from "m-react-splitters";
import {DataGrid, GridColDef} from "@material-ui/data-grid";

import {FormikFunctionSelect, useFunctionInfos} from "../../components/formik-components/formik-function-select";
import {JavascriptMonacoEditor} from "../../components/javascript-monaco-editor";
import {freeVarDefs} from "./free-variable-defs";
import {FormikErrorViewer} from "../../components/formik-error-viewer";
import {JsContextDefinitionsContextProvider} from "../../components/js-context-definitions-provider";
import {ModuleFunctionInfo} from "../../api/nimflow-functions-api";
import {ErrorBoundary} from "../../components/error-boundary";
import {
    FunctionDataSourceData,
    FunctionDataSourceDataContextProvider,
    useFunctionDataSourceData,
    useFunctionDataSourceDataContext,
} from "./function-data-source-editor-context";
import {ErrorViewer} from "../../components/error-viewer";
import {validateForm} from "../../utils/formikHelpers";
import {FunctionDataSourceDataSchema} from "./function-data-source-data-schema";
import { getTypingFromSchema } from "../../utils/json-schema-to-typescript";

const titleHeight = 64;
const footerHeight = 52;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {},
        paper: {
            padding: theme.spacing(2),
            height: "100%",
            color: theme.palette.text.secondary,
        },
        form: {
            height: "100%",
        },
        content: {
            height: `calc(100% - ${titleHeight}px - ${footerHeight}px)`,
            overflow: "auto",
        },
        textField: {
            width: "40ch",
        },
        editorContainer: {
            height: "100%",
        },
        errorContainer: {
            overflowY: "auto",
        },
        runButton: {
            alignSelf: "flex-end",
        },
        splitter: {
            minHeight: 420,
            "& .handle-bar": {
                //display: "flex",
                alignSelf: "center",
            },
        },
        funcDefSplitterPane: {
            width: "100%",
            height: "100%",
            display: "grid",
            gridTemplateColumns: "auto auto",
            gridTemplateRows: `60px 30px auto`,
        },
        resultsSplitterPane: {
            width: "100%",
            height: "100%",
        },
        fnSelectorContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
        },
        runBtnContainer: {
            paddingLeft: 10,
            gridColumn: "2 / 3",
            gridRow: "1 / 2",
            alignSelf: "flex-end",
        },
        argsEditorTitleContainer: {
            gridColumn: "1 / 3",
            gridRow: "2 / 3",
        },
        argsEditorContainer: {
            borderWidth: 1,
            borderStyle: "solid",
            borderColor: theme.palette.divider,
            borderRadius: theme.shape.borderRadius,
            gridColumn: "1 / 3",
            gridRow: "3 / 4",
            //overflowY: "auto",
        },
        errorOverlay: {
            overflow: "auto",
            height: "100%",
        },
    })
);

const freeVariables = [freeVarDefs.fnargs];

export const JsParametersEditor: FC<{onErrorChange?: (errors: string[]) => void}> = props => {
    const {onErrorChange} = props;
    const {values: dataSource, setFieldValue, errors} = useFormikContext<FunctionDataSourceData>();

    const argsErrors = errors.jsArguments;
    const classes = useStyles({showErrors: !!argsErrors});
    const [code, setCode] = useState(dataSource.jsArguments);

    useDebounce(
        () => {
            setFieldValue("jsArguments", code);
        },
        1000,
        [code]
    );

    return (
        <>
            <div className={classes.editorContainer}>
                <Autosizer>
                    {({height, width}) => {
                        return (
                            <JavascriptMonacoEditor
                                height={height}
                                width={width}
                                value={code}
                                onChange={setCode}
                                onErrorsChange={onErrorChange}
                                importJsCommon={false}
                                freeVariables={freeVariables}
                            />
                        );
                    }}
                </Autosizer>
            </div>
            {!!argsErrors && (
                <div className={classes.errorContainer}>
                    <FormikErrorViewer errors={errors} names="jsArguments" />
                </div>
            )}
        </>
    );
};

const getArgsTypings = async (functionInfo: ModuleFunctionInfo | undefined) => {
    const schema = functionInfo?.inputSchema;
    const argsTyping = schema ? await getTypingFromSchema(schema) : "{}";
    return `declare let args: ${argsTyping};`;
};

const getArgsTypingsProvider = (
    functionInfo: ModuleFunctionInfo | undefined
): Record<string, () => string | Promise<string>> => {
    return {
        args: () => {
            return getArgsTypings(functionInfo);
        },
    };
};

interface InnerFunctionDataSourceEditorProps {}

const InnerFunctionDataSourceEditor: FC<InnerFunctionDataSourceEditorProps> = props => {
    const {values: ds} = useFormikContext<FunctionDataSourceData>();
    const {t} = useTranslation();
    const classes = useStyles();
    const {functionInfos} = useFunctionInfos();
    const {data, error, isRefreshing, refreshData, idKey} = useFunctionDataSourceDataContext();

    const columns = useMemo(() => {
        if (!Array.isArray(data)) {
            return [];
        }
        const item = data[0];
        if (!isPlainObject(item)) {
            return [];
        }
        const columns: GridColDef[] = Object.keys(item)
            .filter(s => s !== idKey)
            .map(s => ({field: s, headerName: s}));
        //const columns: Column<any>[] = Object.keys(item).map(s => ({field: s, title: s}));
        return columns;
    }, [data, idKey]);

    const functionInfo = useMemo(() => {
        const key = `${ds.moduleName}/${ds.functionName}`;
        return functionInfos[key];
    }, [ds.functionName, ds.moduleName, functionInfos]);

    const handleRun = useCallback(async () => {
        await refreshData(ds, true);
    }, [refreshData, ds]);

    const freeVariableTypingProviders: Record<string, () => string | Promise<string>> = useMemo(() => {
        return getArgsTypingsProvider(functionInfo);
    }, [functionInfo]);

    return (
        <JsContextDefinitionsContextProvider freeVariableTypingProviders={freeVariableTypingProviders}>
            <Splitter
                position="horizontal"
                primaryPaneHeight="50%"
                primaryPaneMaxHeight="calc(100% - 200px)"
                primaryPaneMinHeight="200px"
                dispatchResize={true}
                postPoned={true}
                className={classes.splitter}
            >
                <div className={classes.funcDefSplitterPane}>
                    <div className={classes.fnSelectorContainer}>
                        <FormikFunctionSelect
                            moduleNamePath="moduleName"
                            functionNamePath="functionName"
                            AutocompleteProps={{
                                fullWidth: true,
                            }}
                            TextFieldProps={{
                                label: t("FunctionName"),
                                required: true,
                                autoFocus: true,
                            }}
                        />
                    </div>
                    <div className={classes.runBtnContainer}>
                        <Button
                            variant="outlined"
                            onClick={handleRun}
                            startIcon={<PlayArrowIcon />}
                            disabled={isRefreshing}
                        >
                            {t("Run")}
                        </Button>
                    </div>
                    <div className={classes.argsEditorTitleContainer}>
                        <Typography variant="subtitle1">{t("JsParametersBinding")}</Typography>
                    </div>
                    <div className={classes.argsEditorContainer}>
                        <JsParametersEditor />
                    </div>
                </div>
                <div className={classes.resultsSplitterPane}>
                    {!error && (
                        <DataGrid
                            rows={Array.isArray(data) ? data : []}
                            columns={columns}
                            components={{
                                Toolbar: () => (
                                    <>
                                        <div>
                                            <Typography variant="h6">{t("Results")}</Typography>
                                        </div>
                                        <Divider />
                                    </>
                                ),
                                ErrorOverlay: () => (
                                    <div className={classes.errorOverlay}>
                                        <ErrorViewer error={error} />
                                    </div>
                                ),
                            }}
                            getRowId={s => s[idKey]}
                            density="compact"
                            error={error}
                            loading={isRefreshing}
                        />
                    )}
                    {!!error && (
                        <div className={classes.errorOverlay}>
                            <ErrorViewer error={error} />
                        </div>
                    )}
                </div>
            </Splitter>
        </JsContextDefinitionsContextProvider>
    );
};

export interface FunctionDataSourceEditorDialogProps {
    open: boolean;
    value?: FunctionDataSourceData;
    onChange: (data: FunctionDataSourceData) => void;
    onClose: () => void;
}

export const FunctionDataSourceEditorDialog: FC<FunctionDataSourceEditorDialogProps> = props => {
    const {open, value, onChange, onClose} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const formRef = useRef<FormikProps<FunctionDataSourceData> | null>();
    const [submitRequested, setSubmitRequested] = useState(false);

    const initialValue = useMemo(() => {
        const initialValue = value ?? {
            moduleName: "",
            functionName: "",
        };
        if (!initialValue.jsArguments) {
            initialValue.jsArguments = "args = {\n\n};";
        }
        return initialValue;
    }, [value]);

    const api = useFunctionDataSourceData();

    const handleSubmit = useCallback(
        (value: Partial<FunctionDataSourceData>, actions: FormikHelpers<FunctionDataSourceData>) => {
            onChange?.(value as FunctionDataSourceData);
            onClose?.();
        },
        [onChange, onClose]
    );

    const handleAccept = useCallback(async () => {
        const form = formRef.current;
        if (!form) {
            return;
        }
        try {
            const errors = await validateForm(form);
            if (Object.keys(errors).length === 0) {
                await api.refreshData(form.values, false);
                setSubmitRequested(true);
            }
        } catch {
            /**/
        }
    }, [api]);

    useEffect(() => {
        if (submitRequested) {
            setSubmitRequested(false);
            if (!api.error) {
                formRef.current?.submitForm();
            }
        }
    }, [api, submitRequested]);

    const handleDialogClose = useCallback(
        async (event: object, reason: "backdropClick" | "escapeKeyDown") => {
            if (reason !== "backdropClick") {
                onClose?.();
            }
        },
        [onClose]
    );

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            fullWidth
            maxWidth="md"
            classes={{
                root: classes.root,
                paper: classes.paper,
            }}
        >
            <FunctionDataSourceDataContextProvider api={api}>
                <Formik<FunctionDataSourceData>
                    innerRef={s => (formRef.current = s)}
                    initialValues={initialValue}
                    onSubmit={handleSubmit}
                    validationSchema={FunctionDataSourceDataSchema}
                    validateOnChange={true}
                >
                    <Form autoComplete="off" className={classes.form}>
                        <ErrorBoundary>
                            <DialogTitle>{t("EditingFunctionDataSource")}</DialogTitle>
                            <DialogContent className={classes.content}>
                                <InnerFunctionDataSourceEditor />
                            </DialogContent>
                            <DialogActions>
                                <Button onClick={handleAccept} color="primary">
                                    {t("Accept")}
                                </Button>
                                <Button onClick={onClose} color="primary">
                                    {t("Cancel")}
                                </Button>
                            </DialogActions>
                        </ErrorBoundary>
                    </Form>
                </Formik>
            </FunctionDataSourceDataContextProvider>
        </Dialog>
    );
};
