import * as yup from "yup";
import type {SchemaOf} from "yup";

import {
    requestProcessedEventType,
    actionDispatchedEventType,
    contextsRecalculationCompletedEventType,
    contextRecalculationErrorEventType,
    taskCreatedEventType,
    taskUpdatedEventType,
    taskArchivedEventType,
    responseSentEventType,
    milestoneReachedEventType,
    milestoneClearedEventType,
    GenericEventListener,
    ContextTaskEventListener,
    customEventType,
    blobStorageUpdatedType,
} from "../../api/nimflow-api-extensions";
import {
    ActionDispatchedEventListener,
    ContextRecalculationErrorListener,
    ContextsRecalculationCompletedListener,
    CustomEventListener,
    BlobStorageUpdatedEventListener,
    FunctionCall,
    MilestoneClearedEventListener,
    MilestoneReachedEventListener,
    TaskArchivedEventListener,
    TaskCreatedEventListener,
    TaskUpdatedEventListener,
} from "../../api/nimflow-api";

export const FunctionCallSchema = yup.object().shape({
    moduleName: yup.string().required().identifier(),
    functionName: yup.string().required().identifier(),
    jsParametersBinding: yup.string().jsScript(),
}) as SchemaOf<FunctionCall>;

export const BaseEventListenerSchema = yup.object().shape({
    id: yup.string(),
    call: FunctionCallSchema.required(),
    subscribingUserEmail: yup.string(),
});

export const ContextEventListenerSchema = BaseEventListenerSchema.shape({
    contextTypeName: yup
        .string() /*.required()*/
        .identifier(),
});

export const ContextTaskEventListenerSchema = ContextEventListenerSchema.shape({
    taskTypeName: yup
        .string() /*.required()*/
        .identifier(),
});

export const MilestoneEventListenerSchema = ContextEventListenerSchema.shape({
    milestoneName: yup
        .string() /*.required()*/
        .identifier(),
});

export const RequestProcessedEventListener = BaseEventListenerSchema.shape({
    event: yup.string().oneOf([requestProcessedEventType]).required(),
    requestTypeName: yup.string().required().identifier(),
});

export const ActionDispatchedEventListenerSchema = ContextEventListenerSchema.shape({
    event: yup.string().oneOf([actionDispatchedEventType]).required(),
    action: yup
        .string() /*.required()*/
        .identifier(),
}) as SchemaOf<ActionDispatchedEventListener>;

export const ContextsRecalculationCompletedListenerSchema = ContextEventListenerSchema.shape({
    event: yup.string().oneOf([contextsRecalculationCompletedEventType]).required(),
}) as SchemaOf<ContextsRecalculationCompletedListener>;

export const ContextRecalculationErrorListenerSchema = ContextEventListenerSchema.shape({
    event: yup.string().oneOf([contextRecalculationErrorEventType]).required(),
}) as SchemaOf<ContextRecalculationErrorListener>;

export const TaskCreatedEventListenerSchema = ContextTaskEventListenerSchema.shape({
    event: yup.string().oneOf([taskCreatedEventType]).required(),
}) as SchemaOf<TaskCreatedEventListener>;

export const TaskUpdatedEventListenerSchema = ContextTaskEventListenerSchema.shape({
    event: yup.string().oneOf([taskUpdatedEventType]).required(),
}) as SchemaOf<TaskUpdatedEventListener>;

export const TaskArchivedEventListenerSchema = ContextTaskEventListenerSchema.shape({
    event: yup.string().oneOf([taskArchivedEventType]).required(),
}) as SchemaOf<TaskArchivedEventListener>;

export const ResponseSentEventListenerSchema = ContextTaskEventListenerSchema.shape({
    event: yup.string().oneOf([responseSentEventType]).required(),
    responseTypeName: yup
        .string() /*.required()*/
        .identifier(),
}) as SchemaOf<ContextTaskEventListener>;

export const MilestoneReachedEventListenerSchema = MilestoneEventListenerSchema.shape({
    event: yup.string().oneOf([milestoneReachedEventType]).required(),
}) as SchemaOf<MilestoneReachedEventListener>;

export const MilestoneClearedEventListenerSchema = MilestoneEventListenerSchema.shape({
    event: yup.string().oneOf([milestoneClearedEventType]).required(),
}) as SchemaOf<MilestoneClearedEventListener>;

export const CustomEventListenerSchema = BaseEventListenerSchema.shape({
    event: yup.string().oneOf([customEventType]).required(),
    eventName: yup.string(),
    category: yup.string(),
}) as SchemaOf<CustomEventListener>;

export const BlobStorageUpdatedEventListenerSchema = BaseEventListenerSchema.shape({
    event: yup.string().oneOf([blobStorageUpdatedType]).required(),
    pathPattern: yup.string()
}) as SchemaOf<BlobStorageUpdatedEventListener>;

export const resolveGenericEventListenerSchema = (
    s: GenericEventListener
): SchemaOf<GenericEventListener> | undefined => {
    switch (s.event) {
        case requestProcessedEventType:
            return RequestProcessedEventListener.required();
        case actionDispatchedEventType:
            return ActionDispatchedEventListenerSchema.required();
        case contextsRecalculationCompletedEventType:
            return ContextsRecalculationCompletedListenerSchema.required();
        case contextRecalculationErrorEventType:
            return ContextRecalculationErrorListenerSchema.required();
        case taskCreatedEventType:
            return TaskCreatedEventListenerSchema.required();
        case taskUpdatedEventType:
            return TaskUpdatedEventListenerSchema.required();
        case taskArchivedEventType:
            return TaskArchivedEventListenerSchema.required();
        case responseSentEventType:
            return ResponseSentEventListenerSchema.required();
        case milestoneReachedEventType:
            return MilestoneReachedEventListenerSchema.required();
        case customEventType:
            return CustomEventListenerSchema.required();
        case blobStorageUpdatedType:
            return BlobStorageUpdatedEventListenerSchema.required();
    }
};

export const GenericEventListenerSchema = yup.lazy<SchemaOf<GenericEventListener>>((s: GenericEventListener): any => {
    return resolveGenericEventListenerSchema(s);
}) as unknown as SchemaOf<GenericEventListener>;
