import {FC, useMemo, useCallback, useState} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import TextField from "@material-ui/core/TextField";
import Tooltip from "@material-ui/core/Tooltip";
import {useFormContext} from "react-hook-form";
import {useTranslation} from "react-i18next";
import {Components} from "@material-table/core";
import i18next from "i18next";
import {remove, insert, intersection, mapObjIndexed, uniq, groupBy, append} from "ramda";
import {isDefined, isPresent} from "ts-is-present";
import * as yup from "yup";

import {StandardSection} from "../../components/standard-section";
import {StandardSectionTitle} from "../../components/standard-section-title";
import {ContextType, Selector} from "../../api/nimflow-api";
import {JsExpressionEditor} from "../../components/jsexpression-editor";
import {SelectorSchema, jsCommonScriptKey} from "../../models/context-type-schema";
import {TaggingTypeIcon, useTaggings, getTaggingDescription} from "./taggings";
import {useContextTypeContext} from "./context-type-context";
import {ErrorInfo} from "../../models/error-info";
import {validateWithYupSchema} from "../../libs/yup";
import {freeVarDefs} from "./free-variable-defs";
import {FormErrorViewer} from "../../components/form-error-viewer";
import {RhfArrayFieldEditor, RhfArrayFieldEditorProps} from "../../components/rhf-array-field-editor";
import {BaseEditComponentProps, BaseColumn} from "../../components/base-table";
import {ContextTypeSelectorDependenciesEditor} from "./context-type-selector-dependencies-editor";
import {TaggingTableTitle} from "./tagging-table-title";
import {extractStateRefs} from "../../utils/js-analysis";
import {detectCycle} from "../../utils/graph-algorithms";

const useStyles = makeStyles((_theme: Theme) =>
    createStyles({
        textField: {
            width: "40ch",
        },
        name: {
            maxWidth: "15ch",
            maxHeight: "10ch",
            overflowY: "auto",
            wordWrap: "break-word",
        },
        jsExpression: {
            maxWidth: "40ch",
            overflowX: "hidden",
            whiteSpace: "nowrap",
            textOverflow: "ellipsis",
        },
        taggingButton: {
            textAlign: "center",
        },
        fixTaggingColSize: {
            "& .MuiTableCell-head:first-child": {
                width: "40px !important",
            },
        },
    })
);

export interface ContextTypeSelectorsEditorProps {
    path: string;
    defaultValue?: Partial<Selector>[];
}

interface DependenciesEditionData {
    selector: Selector;
    availableSelectors: Selector[];
    selectorIndex: number;
}

const tableComponents: Components = {
    Container: props => <div {...props} />,
};

const findSelectorCycle = (selector: Selector, availableSelectors: Selector[]): string[] | undefined => {
    const rootKey = selector.key;
    if (!rootKey || !selector.dependencies || selector.dependencies.length === 0) {
        return undefined;
    }
    const map = mapObjIndexed(
        (s: Selector[]) => uniq(s.flatMap(sel => sel.dependencies ?? [])),
        groupBy(
            s => s.key!,
            append(selector, availableSelectors).filter(s => s.key)
        )
    );
    return detectCycle(rootKey, {
        getNeighbors: v => map[v] ?? [],
    });
};

const freeVariables =[freeVarDefs.state];

export const ContextTypeSelectorsEditor: FC<ContextTypeSelectorsEditorProps> = props => {
    const {path, defaultValue} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const {getValues, setValue, errors} = useFormContext<ContextType>();
    const {getTaggingType, toggleTagging, removeTaggings, removeInvalidTaggings} = useTaggings();
    const {jsCommon} = useContextTypeContext();
    const [editionData, setEditionData] = useState<DependenciesEditionData>();
    const selectors = getValues(path) as Selector[];
    const setSelectors = useCallback((selectors: Selector[]) => setValue(path, selectors), [setValue, path]);
    const keyValidator = useCallback((key: string): Promise<ErrorInfo[]> => {
        const schema = SelectorSchema.fields?.key.concat(
            yup
                .string()
                .defined()
                .test({
                    message: ({value}) => i18next.t("SelectorKeyAlreadyExists", {name: value}),
                    test: function (value: any) {
                        return !selectors.some(s => s.key === value);
                    },
                })
        );
        return validateWithYupSchema(schema, key);
    }, [selectors]);
    const jsExpressionValidator = useCallback(
        (js: string | undefined, rowData: Partial<Selector>): Promise<ErrorInfo[]> => {
            const schema = SelectorSchema.fields?.jsExpression.concat(
                yup
                    .string()
                    .defined()
                    .test({
                        message: () => i18next.t("SelectorCircularDependencyFound"),
                        test: function (value: any) {
                            const selectors = getValues(path) as Selector[];
                            let dependencies: string[] = [];
                            if (rowData.jsExpression) {
                                dependencies = extractStateRefs(value);
                            }
                            const selector: Partial<Selector> = {
                                ...rowData,
                                dependencies,
                            };
                            const cycle = findSelectorCycle(
                                selector,
                                selectors.filter(s => s.key !== rowData.key)
                            );
                            if (cycle) {
                                return this.createError({
                                    path: "jsExpression",
                                    message: i18next.t("SelectorCircularDependencyFound", {cycle: cycle.join(" -> ")}),
                                });
                            }
                            return true;
                        },
                    })
            );
            return validateWithYupSchema(schema, js, {
                context: {[jsCommonScriptKey]: jsCommon},
            });
        },
        [jsCommon, getValues, path]
    );

    const columns = useMemo(() => {
        const cols: BaseColumn<Partial<Selector>>[] = [
            {
                title: <TaggingTableTitle />,
                width: "5ch",
                render: rowData => {
                    const isEditing = !rowData || !!(rowData as any).tableData?.editing;
                    const key = rowData?.key;
                    if (!key) {
                        return null;
                    }
                    const milestoneType = getTaggingType(key);
                    const result = (
                        <div className={classes.taggingButton}>
                            <Tooltip title={getTaggingDescription(milestoneType)}>
                                <IconButton onClick={() => toggleTagging(key)} disabled={isEditing}>
                                    <TaggingTypeIcon taggingType={milestoneType} />
                                </IconButton>
                            </Tooltip>
                        </div>
                    );
                    return result;
                },
                editable: "never",
                sorting: false,
                headerStyle: {
                    width: "30px",
                    paddingRight: "2px",
                    paddingLeft: "8px",
                },
                cellStyle: {
                    width: "30px",
                    paddingRight: "2px",
                    paddingLeft: "0",
                },
            },
            {
                field: "key",
                title: t("Name"),
                width: "20ch",
                validateValue: keyValidator,
                defaultSort: "asc",
                render: rowData => (
                    <div className={classes.name}>
                        <span>{rowData.key}</span>
                    </div>
                ),
                editComponent: ({value, onChange, errorMessage}: BaseEditComponentProps<Partial<Selector>>) => (
                    <TextField
                        defaultValue={value}
                        onChange={e => onChange(e.target.value)}
                        autoFocus
                        fullWidth
                        error={!!errorMessage}
                        helperText={errorMessage}
                    />
                ),
            },
            {
                field: "jsExpression",
                title: t("Expression"),
                width: "20ch",
                render: rowData => (
                    <div className={classes.jsExpression}>
                        <span>{rowData.jsExpression}</span>
                    </div>
                ),
                editComponent: ({
                    value,
                    onChange,
                    errorMessage,
                    rowData,
                }: BaseEditComponentProps<Partial<Selector>>) => (
                    <JsExpressionEditor
                        defaultValue={value}
                        onChange={onChange}
                        className={classes.textField}
                        error={!!errorMessage}
                        helperText={errorMessage}
                        validator={js => jsExpressionValidator(js, rowData)}
                        freeVariables={freeVariables}
                    />
                ),
                validateValue: (newValue, _oldValue, rowData) => jsExpressionValidator(newValue, rowData),
                sorting: false,
            },
        ];
        return cols;
    }, [classes, getTaggingType, jsExpressionValidator, keyValidator, t, toggleTagging]);

    const tableEditionHandler = useMemo(() => {
        const updateDependencies = (selector: Partial<Selector>) => {
            if (selector.jsExpression) {
                const selnames = selectors.map(s => s.key).filter(isPresent);
                selector.dependencies = intersection(selnames, extractStateRefs(selector.jsExpression));
            } else {
                selector.dependencies = [];
            }
        };
        const replaceSelector = (oldSelector: string, newSelector?: string) => {
            let update = false;
            const newSelectors = selectors.map(s => {
                const idx = s.dependencies?.indexOf(oldSelector);
                if (!isPresent(idx) || idx < 0) {
                    return s;
                }
                update = true;
                let dependencies = remove(idx, 1, s.dependencies!);
                if (newSelector) {
                    dependencies = insert(idx, newSelector, dependencies);
                }
                return {
                    ...s,
                    dependencies,
                };
            });
            if (update) {
                setSelectors(newSelectors);
            }
        };

        const tableEditionHandler: RhfArrayFieldEditorProps<Selector>["editable"] = {
            onRowAdd: async newData => {
                updateDependencies(newData);
            },
            onRowUpdate: async (newData, oldData) => {
                updateDependencies(newData);
                const oldSelector = oldData?.key;
                if (oldSelector && oldSelector !== newData.key) {
                    removeTaggings(oldSelector);
                    replaceSelector(oldSelector, newData.key);
                    removeInvalidTaggings();
                }
            },
            onRowDelete: async oldData => {
                const selector = oldData.key;
                if (selector) {
                    removeTaggings(selector);
                    replaceSelector(selector);
                    removeInvalidTaggings();
                }
            },
        };
        return tableEditionHandler;
    }, [removeInvalidTaggings, removeTaggings, selectors, setSelectors]);

    const handleDependenciesChanged = useCallback(
        (selector: Selector) => {
            const newSelectors = selectors.map((s, i) => {
                if (i !== editionData?.selectorIndex) {
                    return s;
                }
                return selector;
            });
            setSelectors(newSelectors);
        },
        [selectors, editionData, setSelectors]
    );

    const handleOnCloseDialog = useCallback(() => {
        setEditionData(undefined);
    }, [setEditionData]);

    return (
        <StandardSection>
            <div className={classes.fixTaggingColSize}>
                <RhfArrayFieldEditor
                    path={path}
                    defaultValue={defaultValue}
                    columns={columns}
                    title={<StandardSectionTitle>{t("CalculatedFields")}</StandardSectionTitle>}
                    editable={tableEditionHandler}
                    components={tableComponents}
                />
            </div>
            <FormErrorViewer errors={errors} names={path} />
            {isDefined(editionData) && (
                <ContextTypeSelectorDependenciesEditor
                    open={isDefined(editionData)}
                    onClose={handleOnCloseDialog}
                    selector={editionData.selector}
                    availableSelectors={editionData.availableSelectors}
                    onChange={handleDependenciesChanged}
                />
            )}
        </StandardSection>
    );
};
