import {useCallback} from "react";

import {DashboardWidgetFunctionDataSource} from "../../api/nimflow-dashboard-widgets-api";
import {useNimflowFunctions} from "../../api/nimflow-functions-api";

export const useFunctionDataSourceEvaluator = () => {
    const {evaluateArgsAndCall} = useNimflowFunctions();

    const evaluateFunctionDataSource = useCallback(
        async (ds: DashboardWidgetFunctionDataSource, args: Record<string, any>): Promise<any> => {
            const result = await evaluateArgsAndCall(ds.moduleName, ds.functionName, ds.jsArguments, {params: args});
            return result;
        },
        [evaluateArgsAndCall]
    );

    return {
        evaluateFunctionDataSource,
    };
};
