import React, {FC, useCallback, useMemo, useState, useEffect} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import clsx from "clsx";
import Button from "@material-ui/core/Button";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import {format} from "date-fns";
import useSWR from "swr";
import {isPresent} from "ts-is-present";

import {
    DashboardWidgetDataSource,
    DashboardWidgetParameter,
    DashboardWidgetVisualization,
} from "../../api/nimflow-dashboard-widgets-api";
import {DashboardWidgetVegaLiteVisualizationPreviewer} from "./dashboard-widget-vega-lite-visualization-previewer";
import {DashboardWidgetMarkdownVisualizationPreviewer} from "./dashboard-widget-markdown-visualization-previewer";
import {useQueryDataSourceEvaluator} from "./query-data-source-evaluator";
import {useKey} from "../../utils/use-key";
import {ParameterValuesEditor} from "./parameter-values-editor";
import {WaitSpinner} from "../../components/wait-spinner";
import {DashboardWidgetTabularVisualizationPreviewer} from "./dashboard-widget-tabular-visualization-previewer";
import { useFunctionDataSourceEvaluator } from "./function-data-source-evaluator";

const parametersPaneWidth = 300;
const parametersPaneBorderWidth = 2;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        content: {
            height: "70vh",
            overflowY: "auto",
            width: "100%",
            //height: "100%",
            //minWidth: 800,
            display: "grid",
            gridTemplateRows: "auto",
            gridTemplateColumns: `auto ${parametersPaneWidth}px`,
            //placeItems: "stretch",
            transition: theme.transitions.create(["gridTemplateColumns"], {
                easing: theme.transitions.easing.sharp,
                duration: theme.transitions.duration.leavingScreen,
            }),
        },
        contentShift: {
            gridTemplateColumns: `auto 1px`,
            transition: theme.transitions.create(["gridTemplateColumns"], {
                easing: theme.transitions.easing.easeOut,
                duration: theme.transitions.duration.enteringScreen,
            }),
            "& .parameters-pane": {
                display: "none",
            },
        },
        viewerPane: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
            overflow: "auto",
            position: "relative",
        },
        parametersPane: {
            gridRow: "1 / 2",
            gridColumn: "2 / 3",
            overflow: "auto",
            borderLeftStyle: "solid",
            borderLeftWidth: parametersPaneBorderWidth,
            borderLeftColor: theme.palette.divider,
            paddingLeft: 10,
        },
        menuButton: {
            position: "absolute",
            top: 100,
            right: 0,
            minWidth: 25,
            borderTopRightRadius: 0,
            borderBottomRightRadius: 0,
            padding: "2px 2px",
            borderRight: "none",
            color: theme.palette.grey[500],
        },
        backArrow: {
            //marginLeft: 3,
            //marginRight: -3,
        },
    })
);

export interface WidgetDefinitionPreviewerProps {
    visible: boolean;
    refreshing?: boolean;
    parameters?: DashboardWidgetParameter[];
    dataSources?: DashboardWidgetDataSource[];
    visualization: DashboardWidgetVisualization;
}

const useWidgetDataSourcesEvaluator = () => {
    const {evaluateQueryDataSource} = useQueryDataSourceEvaluator();
    const {evaluateFunctionDataSource} = useFunctionDataSourceEvaluator();

    const evaluateDataSources = useCallback(
        async (dataSources: DashboardWidgetDataSource[], args: Record<string, any>) => {
            const result: Record<string, any> = {};
            const promises: Promise<any>[] = [];
            for (const ds of dataSources) {
                let promise = Promise.resolve<any>([]);
                switch (ds.type) {
                    case "query": {
                        const timezone = format(new Date(), "XXX");
                        promise = evaluateQueryDataSource(ds, args, timezone);
                        break;
                    }
                    case "function": {
                        promise = evaluateFunctionDataSource(ds, args);
                        break;
                    }
                }
                promise = promise.then(s => {
                    result[ds.name] = s;
                });
                promises.push(promise);
            }
            await Promise.all(promises);
            return result;
        },
        [evaluateFunctionDataSource, evaluateQueryDataSource]
    );

    return evaluateDataSources;
};

const useDataSourceData = (dataSources: DashboardWidgetDataSource[] | undefined, args: Record<string, any>) => {
    const evaluateDataSources = useWidgetDataSourcesEvaluator();
    const key = useKey(evaluateDataSources, args, dataSources);
    const {data, isValidating, mutate, error} = useSWR(key, async () => {
        if (!dataSources) {
            return undefined;
        }
        return await evaluateDataSources(dataSources, args);
    });
    return useMemo(() => {
        return {
            data,
            isValidating,
            error,
            revalidate: mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export const WidgetDefinitionPreviewer: FC<WidgetDefinitionPreviewerProps> = props => {
    const {visible, refreshing, parameters, dataSources, visualization} = props;
    const classes = useStyles();
    const [args, setArgs] = useState<Record<string, any>>({});
    const {data, isValidating} = useDataSourceData(dataSources, args);
    const [sideBarOpen, setSideBarOpen] = React.useState(true);
    const [refreshingViewer, setRefreshingViewer] = React.useState(false);

    const requiredParamsHaveValue =
        !parameters || parameters.filter(s => s.required).every(s => isPresent(args[s.name]));

    const toggleSideMenu = useCallback(() => {
        setSideBarOpen(s => !s);
        setRefreshingViewer(true);
    }, []);

    useEffect(() => {
        if (refreshing) {
            setRefreshingViewer(true);
        }
    }, [refreshing]);

    useEffect(() => {
        if (refreshingViewer) {
            setRefreshingViewer(false);
        }
    }, [refreshingViewer]);

    return (
        <div className={clsx(classes.content, {[classes.contentShift]: !sideBarOpen})}>
            <div className={classes.viewerPane}>
                {!data && isValidating && <WaitSpinner />}
                {(data || !isValidating) && visible && (
                    <>
                        {visualization.type === "vega-lite" && !refreshingViewer && (
                            <DashboardWidgetVegaLiteVisualizationPreviewer
                                visualization={visualization}
                                requiredParamsHaveValue={requiredParamsHaveValue}
                                args={args}
                                data={data}
                            />
                        )}
                        {visualization.type === "markdown" && (
                            <DashboardWidgetMarkdownVisualizationPreviewer
                                visualization={visualization}
                                requiredParamsHaveValue={requiredParamsHaveValue}
                                args={args}
                                data={data}
                            />
                        )}
                        {visualization.type === "tabular" && (
                            <DashboardWidgetTabularVisualizationPreviewer
                                visualization={visualization}
                                requiredParamsHaveValue={requiredParamsHaveValue}
                                args={args}
                                data={data}
                            />
                        )}
                    </>
                )}
                <Button
                    size="small"
                    variant="outlined"
                    className={classes.menuButton}
                    disableElevation
                    onClick={toggleSideMenu}
                    color="default"
                >
                    {sideBarOpen ? (
                        <ArrowForwardIosIcon fontSize="small" />
                    ) : (
                        <ArrowBackIosIcon fontSize="small" className={classes.backArrow} />
                    )}
                </Button>

                {/* {JSON.stringify(spec)}
                    {JSON.stringify(data)} */}
            </div>
            <div className={clsx(classes.parametersPane, "parameters-pane")}>
                <ParameterValuesEditor parameters={parameters} onParameterValueChange={setArgs} />
            </div>
        </div>
    );
};
