import React, {FC, useCallback, useMemo, useState} from "react";
import Dialog, {DialogProps} from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import {useTranslation} from "react-i18next";

import {ActionEvent, ContextType} from "../../../api/nimflow-api";
import {ActionTypeSelectorStep} from "./action-type-selector-step";
import {ActionDataEditorStep} from "./action-data-editor-step";
import {getDialogMaxWidthForActionType} from "../../../utils/dialog-max-width";

export interface NewActionEditorProps {
    isInitialAction: boolean;
    contextType: ContextType;
    open: boolean;
    onClose: () => void;
    onChange?: (action: ActionEvent) => void;
}

export type ActionEventData = Partial<ActionEvent>;

export interface NewActionStep {
    value: ActionEventData;
    isInitialAction: boolean;
    contextType: ContextType;
    onNext: (data: ActionEventData) => void;
    onBack: () => void;
    onCancel: () => void;
}

const stepComponents = [ActionTypeSelectorStep, ActionDataEditorStep];

export const NewActionEditor: FC<NewActionEditorProps> = props => {
    const {open, onClose, onChange, contextType, isInitialAction} = props;
    const {t} = useTranslation();
    const [activeStep, setActiveStep] = useState(0);
    const [actionData, setActionData] = useState<ActionEventData>({});
    const steps = useMemo(() => {
        return [
            activeStep > 0 ? t("SelectedActionType", {name: actionData.action}) : t("SelectActionType"),
            t("CompleteActionData"),
        ];
    }, [activeStep, actionData, t]);

    const handleBack = useCallback(() => {
        setActiveStep(prevActiveStep => prevActiveStep - 1);
    }, [setActiveStep]);

    const handleNext = useCallback(
        (data: ActionEventData) => {
            if (activeStep === stepComponents.length - 1) {
                onChange?.(data as ActionEvent);
                onClose();
            }
            setActionData(data);
            setActiveStep(prevActiveStep => prevActiveStep + 1);
        },
        [activeStep, setActiveStep, onClose, onChange]
    );

    const dialogWidth: DialogProps["maxWidth"] = useMemo(() => {
        if (!actionData.action || activeStep === 0) {
            return "sm";
        }
        return getDialogMaxWidthForActionType(contextType, actionData.action);
    }, [actionData.action, activeStep, contextType]);

    const Component = stepComponents[activeStep];

    return (
        <Dialog open={open} fullWidth maxWidth={dialogWidth}>
            <DialogTitle>{t("CreatingNewAction")}</DialogTitle>
            <DialogContent style={{minWidth: "400px", minHeight: "50px"}}>
                <Stepper activeStep={activeStep} alternativeLabel>
                    {steps.map((label, i) => (
                        <Step key={i}>
                            <StepLabel>{label}</StepLabel>
                        </Step>
                    ))}
                </Stepper>
                {!!Component && (
                    <Component
                        value={actionData}
                        isInitialAction={isInitialAction}
                        contextType={contextType}
                        onNext={handleNext}
                        onBack={handleBack}
                        onCancel={onClose}
                    />
                )}
            </DialogContent>
        </Dialog>
    );
};
