import React, {FC, useCallback, useState} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import Autocomplete from "@material-ui/lab/Autocomplete/Autocomplete";
import TextField from "@material-ui/core/TextField";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Radio from "@material-ui/core/Radio";
import Typography from "@material-ui/core/Typography";

import {Icons} from "./icons";
import {useIcons} from "./icon-service";
import {useTranslation} from "react-i18next";
import {iconFamilies, IconFamily, IconSpec} from "../../api/nimflow-api-extensions";

const titleHeight = 64;
const footerHeight = 52;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            backgroundColor: theme.palette.background.default,
            width: "100%",
            height: "90vh",
            minHeight: 500,
        },
        dialogTitle: {
            height: titleHeight,
        },
        dialogContent: {
            height: `calc(100% - ${titleHeight}px - ${footerHeight}px)`,
            width: "100%",
        },
        dialogFooter: {
            height: footerHeight,
        },
        gridContainer: {
            height: "100%",
        },
        results: {
            marginBottom: theme.spacing(1),
        },
    })
);

export interface IconSelectorDialogProps {
    open: boolean;
    onChange?: (value?: IconSpec) => void;
    onClose: () => void;
}

export const IconSelectorDialog: FC<IconSelectorDialogProps> = props => {
    const {open, onChange, onClose} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const [iconFamily, setIconFamily] = useState<IconFamily>("Filled");
    const [selectedIcon, setSelectedIcon] = useState<string>();
    const {
        availableCategories,
        availableTags,
        currentCategory,
        currentTag,
        setCurrentCategory,
        setCurrentTag,
        filteredIcons,
    } = useIcons();

    const handleTagChange = useCallback(
        (event: React.ChangeEvent<{}>, newValue: string | null, reason: string) => {
            setCurrentTag(newValue ?? undefined);
        },
        [setCurrentTag]
    );

    const handleCategoryChange = useCallback(
        (event: React.ChangeEvent<{}>, newValue: string | null, reason: string) => {
            setCurrentCategory(newValue ?? undefined);
        },
        [setCurrentCategory]
    );

    const handleOnAccept = useCallback(() => {
        onChange?.(
            selectedIcon
                ? {
                      name: selectedIcon,
                      family: iconFamily,
                  }
                : undefined
        );
        onClose?.();
    }, [iconFamily, onChange, onClose, selectedIcon]);

    const handleDialogClose = useCallback(async (event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [onClose]);

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            maxWidth="lg"
            fullWidth
            classes={{
                paper: classes.paper,
            }}
        >
            <DialogTitle>{t("SelectingIcon")}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <Grid container className={classes.gridContainer}>
                    <Grid item xs={12} sm={2} className={classes.gridContainer}>
                        <RadioGroup>
                            {iconFamilies.map(s => {
                                return (
                                    <FormControlLabel
                                        key={s}
                                        control={
                                            <Radio
                                                checked={iconFamily === s}
                                                onChange={() => setIconFamily(s)}
                                                value={s}
                                            />
                                        }
                                        label={s}
                                    />
                                );
                            })}
                        </RadioGroup>
                    </Grid>
                    <Grid item xs={12} sm={10} className={classes.gridContainer}>
                        <Grid container className={classes.gridContainer} spacing={3} alignContent="flex-start">
                            <Grid item xs={8}>
                                <Autocomplete
                                    options={availableTags}
                                    value={currentTag}
                                    onChange={handleTagChange}
                                    renderInput={params => <TextField {...params} label={t("Tag")} autoFocus />}
                                />
                            </Grid>
                            <Grid item xs={4}>
                                <Autocomplete
                                    options={availableCategories}
                                    value={currentCategory}
                                    getOptionLabel={s => s ?? t("All")}
                                    onChange={handleCategoryChange}
                                    renderInput={params => <TextField {...params} label={t("Category")} />}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <Typography className={classes.results}>
                                    {t("MatchingResults", {count: filteredIcons.length})}
                                </Typography>
                                <Icons
                                    icons={filteredIcons}
                                    family={iconFamily}
                                    selected={selectedIcon}
                                    onIconSelect={setSelectedIcon}
                                />
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleOnAccept} color="primary" disabled={!selectedIcon}>
                    {t("Accept")}
                </Button>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
