import {clone} from "ramda";

import {DashboardDefinitionSchema} from "./dashboard-definition-schema";
import {mapAjvErrors, ImportResult, importFailure, importSuccess} from "../../models/importers";
import {isValidationFailure, validateDashboardDefinitionSchema} from "../../libs/ajv";
import {validateWithYupSchema} from "../../libs/yup";
import {DashboardDefinition} from "../../api/nimflow-dashboard-api";

export const importDashboardDefinitionFromJson = async (json: any): Promise<ImportResult<DashboardDefinition>> => {
    const result = validateDashboardDefinitionSchema(json);
    if (isValidationFailure(result)) {
        return importFailure(mapAjvErrors(result.errors));
    }
    const dashboardDefinition = clone(json) as DashboardDefinition;
    delete (dashboardDefinition as any)["$schema"];
    const einfos = await validateWithYupSchema(DashboardDefinitionSchema, dashboardDefinition);
    if (einfos.length > 0) {
        return importFailure(einfos);
    }
    return importSuccess(dashboardDefinition);
};
