import {ContextType, SimulateSteps, SimulateStepsResult, SimulateStepsResultStatus} from "../../api/nimflow-api";
import { isPresent } from "ts-is-present";

export enum SimulationResultAnalysis {
    ProceedNormally,
    ReexecuteFromInitialState,
    UnrecognizedExecutionStatus = 1000, //Initial error
    UnexpectedNewContextType,
    MissingContextType,
}

export interface SimulationContextTypeProvider {
    contextType: ContextType;
    configureSimulationRequest(request: SimulateSteps): SimulateSteps;
    analyzeSimulationResponse(result: SimulateStepsResult): any;
}

class ImportedContextTypeProvider implements SimulationContextTypeProvider {
    constructor(public readonly contextType: ContextType) {}
    public configureSimulationRequest(request: SimulateSteps): SimulateSteps {
        const result = {
            ...request,
            contextType: this.contextType,
        }
        delete result.contextTypeName;
        delete result.contextTypeClientVersion;
        return result;
    }
    public analyzeSimulationResponse(result: SimulateStepsResult): SimulationResultAnalysis {
        if (result.status === SimulateStepsResultStatus.Completed) {
            return SimulationResultAnalysis.ProceedNormally;    
        }
        if (result.status === SimulateStepsResultStatus.NewerContextTypeFound) {
            return SimulationResultAnalysis.UnexpectedNewContextType;
        }
        return SimulationResultAnalysis.UnrecognizedExecutionStatus;
    }
}

class ServerContextTypeProvider implements SimulationContextTypeProvider {
    constructor(
        public contextType: ContextType,
        private contextTypeName: string,
        private resolver: (name: string) => Promise<ContextType>
    ) {}

    public configureSimulationRequest(request: SimulateSteps): SimulateSteps {
        const result = {
            ...request,
            contextTypeName: this.contextTypeName,
            contextTypeClientVersion: this.contextType.version,
        }
        delete result.contextType;
        return result;
    }

    public analyzeSimulationResponse(result: SimulateStepsResult): SimulationResultAnalysis {
        if (result.status === SimulateStepsResultStatus.Completed) {
            return SimulationResultAnalysis.ProceedNormally;    
        }
        if (result.status === SimulateStepsResultStatus.NewerContextTypeFound) {
            if (!result.contextType) {
                return SimulationResultAnalysis.MissingContextType;
            }
            const newVersion = result.contextType.version;
            const oldVersion = this.contextType.version;
            if (!isPresent(newVersion) || (isPresent(oldVersion) && oldVersion >= newVersion)) {
                return SimulationResultAnalysis.UnexpectedNewContextType;
            }
            this.contextType = result.contextType;
            return SimulationResultAnalysis.ReexecuteFromInitialState;
        }
        return SimulationResultAnalysis.UnrecognizedExecutionStatus;
    }
}

export const newImportedContextTypeProvider = (contextType: ContextType): SimulationContextTypeProvider => {
    return new ImportedContextTypeProvider(contextType);
};

export const newServerContextTypeProvider = async (
    contextTypeName: string,
    resolver: (name: string) => Promise<ContextType>,
    initialContextType?: ContextType
): Promise<SimulationContextTypeProvider> => {
    const contextType = initialContextType ?? (await resolver(contextTypeName));
    return new ServerContextTypeProvider(contextType, contextTypeName, resolver);
};
