import React, {FC, useMemo, useCallback} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import {useNavigate} from "react-router";
import useSWR from "swr";
import Alert from "@material-ui/lab/Alert";
import RefreshIcon from "@material-ui/icons/Refresh";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import SaveAltIcon from "@material-ui/icons/SaveAlt";
import DeleteIcon from "@material-ui/icons/Delete";
import OpenInBrowserIcon from "@material-ui/icons/OpenInBrowser";
import i18next from "i18next";
import {useTranslation} from "react-i18next";
import {Column, Action} from "@material-table/core";

import MaterialTable from "../../libs/material-table";
import {DashboardWidgetDefinitionInfo, useNimflowDashboardWidgets} from "../../api/nimflow-dashboard-widgets-api";
import {useOkCancelConfirm} from "../../components/confirm";
import {useKey} from "../../utils/use-key";
import {downloadJson} from "../../utils/io-helpers";
import {ErrorBoundary} from "../../components/error-boundary";
import {exportWidgetDefinitionToJson} from "./widget-definition-json-exporter";

const useStyles = makeStyles((_theme: Theme) =>
    createStyles({
        fileInput: {
            opacity: 0,
            position: "absolute",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            width: "100%",
        },
    })
);

const columns: Column<any>[] = [
    {field: "name", title: i18next.t("Name")},
    {field: "title", title: i18next.t("Title")},
];

const buildRefreshAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: RefreshIcon,
        tooltip: i18next.t("RefreshData"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const buildAddAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: AddIcon,
        tooltip: i18next.t("AddWidgetDefinition"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const buildImportAction = (icon: Action<any>["icon"], handler: () => void) => {
    const action: Action<any> = {
        icon,
        tooltip: i18next.t("ImportWidgetDefinition"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const adaptHandler = <T extends object>(handler: (info: T) => void): Action<T>["onClick"] => {
    return (_, info) => {
        if (Array.isArray(info)) {
            info = info[0];
        }
        handler(info);
    };
};

const buildEditAction = (handler: (info: DashboardWidgetDefinitionInfo) => void, disabled?: boolean) => {
    const action: Action<DashboardWidgetDefinitionInfo> = {
        icon: EditIcon,
        tooltip: i18next.t("Edit"),
        onClick: adaptHandler(handler),
        disabled,
    };
    return action;
};

const buildExportAction = (handler: (info: DashboardWidgetDefinitionInfo) => void) => {
    const action: Action<DashboardWidgetDefinitionInfo> = {
        icon: SaveAltIcon,
        tooltip: i18next.t("Export"),
        onClick: adaptHandler(handler),
    };
    return action;
};

const buildDeleteAction = (handler: (info: DashboardWidgetDefinitionInfo) => void, disabled?: boolean) => {
    const action: Action<DashboardWidgetDefinitionInfo> = {
        icon: DeleteIcon,
        tooltip: i18next.t("Delete"),
        onClick: adaptHandler(handler),
        disabled,
    };
    return action;
};

const useDashboardDefinitions = () => {
    const {dashboardWidgets_GetInfos} = useNimflowDashboardWidgets();
    const queryKey = useKey(dashboardWidgets_GetInfos);
    const {data, error, mutate, isValidating} = useSWR(
        [queryKey],
        async (_key: string) => {
            const result: DashboardWidgetDefinitionInfo[] = await dashboardWidgets_GetInfos({});
            return result;
        },
        {revalidateOnFocus: false}
    );
    return useMemo(() => {
        return {
            data: data ?? [],
            isValidating,
            error,
            revalidate: mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export const WidgetDefinitionsViewer: FC<{}> = () => {
    const {t} = useTranslation();
    const navigate = useNavigate();
    const classes = useStyles();
    const confirm = useOkCancelConfirm();
    const {dashboardWidgets_FindOne, dashboardWidgets_Delete} = useNimflowDashboardWidgets();
    const {data, error, revalidate, isValidating} = useDashboardDefinitions();

    const handleFileUploaded = useCallback(
        async (e: React.ChangeEvent<HTMLInputElement>) => {
            const file = e.target?.files?.[0];
            if (file?.type.match(/.+\/json/)) {
                const url = URL.createObjectURL(file);
                navigate(`import/${btoa(url)}`);
            }
        },
        [navigate]
    );

    const refreshAction = useMemo(() => {
        return buildRefreshAction(revalidate);
    }, [revalidate]);

    const addAction = useMemo(() => {
        return buildAddAction(() => {
            navigate(`new`);
        });
    }, [navigate]);

    const importAction = useMemo(() => {
        return buildImportAction(
            () => (
                <>
                    <OpenInBrowserIcon />
                    <input
                        type="file"
                        title=""
                        accept=".widget-definition.json"
                        className={classes.fileInput}
                        onChange={handleFileUploaded}
                    />
                </>
            ),
            () => {}
        );
    }, [classes.fileInput, handleFileUploaded]);

    const rowActions = useMemo(() => {
        return [
            buildEditAction(row => {
                navigate(`edit/${row.id}`);
            }),
            buildExportAction(async row => {
                const widget = await dashboardWidgets_FindOne(row.id!);
                downloadJson(exportWidgetDefinitionToJson(widget), `${widget.name}.widget-definition.json`);
            }),
            buildDeleteAction(async row => {
                let result = await confirm({description: t("ConfirmDeleteWidgetDefinition", {name: row.name})});
                if (result) {
                    result = (await dashboardWidgets_Delete(row.id!)).deletedCount > 0;
                    revalidate();
                }
                return result;
            }),
        ];
    }, [navigate, dashboardWidgets_FindOne, confirm, t, dashboardWidgets_Delete, revalidate]);

    const actions = useMemo(() => {
        return [addAction, importAction, refreshAction, ...rowActions];
    }, [addAction, importAction, refreshAction, rowActions]);

    return (
        <ErrorBoundary>
            <MaterialTable
                columns={columns}
                data={data ?? []}
                title={t("WidgetDefinitionsCatalog")}
                actions={actions}
            />
            {!isValidating && !!error && <Alert severity="error">{error.toString()}</Alert>}
        </ErrorBoundary>
    );
};
