import React, {useCallback} from "react";
import {atom, useSetRecoilState, selector} from "recoil";
import {sort} from "ramda";
import {v4 as uuid} from "uuid";

export enum NotificationType {
    informational,
    warning,
    error,
    waiting,
    inProgress,
    completed,
    canceled,
}

export interface Notification {
    type: NotificationType;
    title: React.ReactNode;
    description: React.ReactNode;
    moment: Date;
}

interface NotificationEntry {
    id: string;
    notification: Notification;
    read: boolean;
}

interface NotificationState {
    entries: NotificationEntry[];
}

export const notificationServiceState = atom<NotificationState>({
    key: "notificationServiceState",
    default: {
        entries: [],
    },
});

export const notificationsState = selector({
    key: "notificationsState",
    get: ({get}) => {
        const {entries} = get(notificationServiceState);
        return entries.map(s => s.notification);
    }
});

export const newNotificationsState = selector({
    key: "newNotificationsState",
    get: ({get}) => {
        const {entries} = get(notificationServiceState);
        return entries.filter(s => !s.read).length;
    }
});

const compareEntries = (a: NotificationEntry, b: NotificationEntry) => {
    return a.notification.moment < b.notification.moment ? 1 : a.notification.moment === b.notification.moment ? 0 : -1;
};

export const useNotificationCommands = () => {
    const setNotifications = useSetRecoilState(notificationServiceState);

    const addNotification = useCallback(
        (notification: Notification) => {
            const id = uuid();
            setNotifications(s => {
                const entry: NotificationEntry = {
                    id,
                    notification,
                    read: false,
                };
                const result: NotificationState = {
                    entries: sort((a, b) => compareEntries(a, b), [...s.entries, entry]),
                };
                return result;
            });
            return id;
        },
        [setNotifications]
    );

    const resetNewNotifications = useCallback(() => {
        setNotifications(s => {
            let anyChanged = false;
            const result = s.entries.map(e => {
                if (e.read) {
                    return e;
                }
                anyChanged = true;
                return {
                    ...e,
                    read: true,
                };
            });
            return anyChanged
                ? {
                      ...s,
                      entries: result,
                  }
                : s;
        });
    }, [setNotifications]);

    const replaceNotification = useCallback(
        (notificationId: string, newNotification: Notification, addIfMissing: boolean = false) => {
            setNotifications(s => {
                let missing = true;
                const entries = s.entries.filter(s => {
                    const found = s.id === notificationId;
                    if (found) {
                        missing = false;
                    }
                    return !found;
                });
                if (missing && !addIfMissing) {
                    return s;
                }
                const newEntry: NotificationEntry = {
                    id: notificationId,
                    notification: newNotification,
                    read: false,
                };
                const result: NotificationState = {
                    entries: sort((a, b) => compareEntries(a, b), [...entries, newEntry]),
                };
                return result;
            });
        },
        [setNotifications]
    );

    const dismissNotification = useCallback(
        (notificationOrId: Notification | string) => {
            setNotifications(s => {
                const result: NotificationState = {
                    entries: s.entries.filter(e =>
                        typeof notificationOrId === "string"
                            ? e.id !== notificationOrId
                            : e.notification !== notificationOrId
                    ),
                };
                return result;
            });
        },
        [setNotifications]
    );

    const dismissAll = useCallback(
        (filter?: (notification: Notification) => boolean) => {
            setNotifications(s => {
                const result: NotificationState = {
                    entries: !!filter ? s.entries.filter(e => filter(e.notification)) : [],
                };
                return result;
            });
        },
        [setNotifications]
    );

    return {
        addNotification,
        resetNewNotifications,
        replaceNotification,
        dismissNotification,
        dismissAll,
    };
};
