import React, {FC, useCallback, useMemo, useState} from "react";
import {UISchemaElement} from "@jsonforms/core";
import {useTranslation} from "react-i18next";
import {useAsync} from "react-use";

import {TaskResponseEvent, ContextTask, ContextType} from "../../api/nimflow-api";
import {getResponseJsonSchema} from "../../utils/json-schema";
import {PayloadEditorState, PayloadEditor} from "./payload-editor";
import {getSettings} from "../../api/nimflow-api-extensions";
import {WaitSpinner} from "../../components/wait-spinner";

export interface TaskResponseDataEditorState {
    value: TaskResponseEvent;
    hasErrors: boolean;
}

export interface TaskResponseDataEditorProps {
    task?: ContextTask;
    value: TaskResponseEvent;
    contextType: ContextType;
    onChange?: (state: TaskResponseDataEditorState) => void;
    payloadInitializer?: () => Promise<any>;
    jsonEditorOpen: boolean;
    onJsonEditorClose?: () => void;
}

export const TaskResponseDataEditor: FC<TaskResponseDataEditorProps> = props => {
    const {task, value, contextType, onChange, payloadInitializer, jsonEditorOpen, onJsonEditorClose} = props;
    const {taskTypeName, typeName: responseTypeName} = value;
    const {t} = useTranslation();
    const [payloadState, setPayloadState] = useState<PayloadEditorState>({
        payload: {},
        hasErrors: false,
    });

    const responseType = useMemo(() => {
        const taskType = contextType.tasks.find(s => s.name === taskTypeName);
        return taskType?.validResponses.find(s => s.name === responseTypeName);
    }, [contextType.tasks, responseTypeName, taskTypeName]);

    const context = useMemo(() => {
        if (!task || !payloadState) {
            return undefined;
        }
        return {
            task,
            payload: payloadState.payload,
        };
    }, [task, payloadState]);

    const [initialPayload, setInitialPayload] = useState<any>(value.payload);
    const {loading: isValidating} = useAsync(async () => {
        if (initialPayload) {
            return;
        }
        let payload = {};
        if (payloadInitializer) {
            const result = await payloadInitializer().catch(() => undefined);
            if (result) {
                payload = result;
            }
        }
        setInitialPayload(payload);
    }, []);

    const schema = useMemo(() => {
        return getResponseJsonSchema(contextType, taskTypeName, responseTypeName);
    }, [contextType, responseTypeName, taskTypeName]);

    const uischema = useMemo(() => {
        const schema = responseType ? getSettings(responseType)?.uiSchema : undefined;
        return schema ? (JSON.parse(schema) as UISchemaElement) : undefined;
    }, [responseType]);

    const handlePayloadChange = useCallback(
        (state: PayloadEditorState) => {
            setPayloadState(state);
            if (!state) {
                return;
            }
            onChange?.({
                value: {
                    taskTypeName,
                    typeName: responseTypeName,
                    payload: state.payload,
                },
                hasErrors: state.hasErrors,
            });
        },
        [onChange, taskTypeName, responseTypeName]
    );

    const uiSchemaLibrary = useMemo(() => {
        const uiSchemaStr = getSettings(contextType)?.uiSchemaCommon;
        if (!uiSchemaStr) {
            return undefined;
        }
        return JSON.parse(uiSchemaStr) as Record<string, UISchemaElement>;
    }, [contextType]);

    if (isValidating) {
        return <WaitSpinner>{t("Loading")}</WaitSpinner>;
    }

    if (!responseType || !schema || !initialPayload) {
        return null;
    }

    return (
        <PayloadEditor
            initialPayload={initialPayload}
            schema={schema}
            uischema={uischema}
            onChange={handlePayloadChange}
            context={context}
            bindContext={!!task}
            jsonEditorOpen={jsonEditorOpen}
            onJsonEditorClose={onJsonEditorClose}
            uiSchemaLibrary={uiSchemaLibrary}
        />
    );
};
