import React, {FC, useMemo} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import {useFormikContext} from "formik";
import MenuItem from "@material-ui/core/MenuItem";
import {useTranslation} from "react-i18next";

import {
    DashboardWidgetQueryDataSource,
    getScalarPropertyNamesFor,
    CalculateExpression,
    ExpressionPropertyAggregator,
    getAggregatorsForProperty,
} from "../../api/nimflow-dashboard-widgets-api";
import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {QueryFilterEditor} from "./query-filter-editor";
import {contextListPropertyNames} from "../../api/nimflow-api-extensions";
import {DataSourcePropertySelector} from "./datasource-property-selector";

const headerContainerHeight = 60;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        textField: {
            maxWidth: "60ch",
            width: "100%"
        },
        main: {
            display: "grid",
            gridTemplateColumns: "1fr 1fr",
            gridTemplateRows: `${headerContainerHeight}px auto`,
            rowGap: `${theme.spacing(2)}px`,
            columnGap: `${theme.spacing(3)}px`,
            placeItems: "stretch",
            height: "100%",
            overflowY: "hidden",
        },
        propertyContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
        },
        aggregationContainer: {
            gridColumn: "2 / 3",
            gridRow: "1 / 2",
        },
        filterContainer: {
            gridColumn: "1 / 3",
            gridRow: "2 / 3",
        },
    })
);

export interface CalculateQueryEditorProps {}

export const CalculateQueryEditor: FC<CalculateQueryEditorProps> = props => {
    const {t} = useTranslation();
    const {values: dataSource} = useFormikContext<Partial<DashboardWidgetQueryDataSource>>();
    const classes = useStyles();
    const query = dataSource.query as CalculateExpression;

    const propertyNames = useMemo(() => {
        if (!dataSource.sourceCollection) {
            return [];
        }
        let result = getScalarPropertyNamesFor(dataSource.sourceCollection);
        if (dataSource.sourceCollection === "contexts" && dataSource.contextTypeName) {
            result = [...result, ...contextListPropertyNames];
        }
        return result;
    }, [dataSource.contextTypeName, dataSource.sourceCollection]);

    const aggregators = useMemo(() => {
        if (!dataSource.sourceCollection) {
            return [] as ExpressionPropertyAggregator[];
        }
        return getAggregatorsForProperty(dataSource.sourceCollection, query.property);
    }, [dataSource.sourceCollection, query.property]);

    if (!dataSource.sourceCollection) {
        return null;
    }

    return (
        <div className={classes.main}>
            <div className={classes.propertyContainer}>
                <DataSourcePropertySelector
                    name="query.property"
                    valuePath="query.value"
                    sourceCollection={dataSource.sourceCollection}
                    contextTypeName={dataSource.contextTypeName}
                    label={t("Property")}
                    InputLabelProps={{required: true}}
                    propertyNames={propertyNames}
                    required
                    className={classes.textField}
                />
            </div>
            <div className={classes.aggregationContainer}>
                <FormikTextField
                    name={`query.aggregation`}
                    label={t("Aggregation")}
                    InputLabelProps={{required: true}}
                    select
                    className={classes.textField}
                >
                    {aggregators.map(s => (
                        <MenuItem key={s} value={s}>
                            {s}
                        </MenuItem>
                    ))}
                </FormikTextField>
            </div>
            <div className={classes.filterContainer}>
                <QueryFilterEditor />
            </div>
        </div>
    );
};
