import {FC, useCallback, useState, useMemo} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Button from "@material-ui/core/Button";
import {useTranslation} from "react-i18next";
import MonacoEditor, {EditorDidMount, EditorWillMount} from "react-monaco-editor";
import {useTheme} from "@material-ui/core/styles";
import FormHelperText from "@material-ui/core/FormHelperText";
import * as yup from "yup";
import {head} from "ramda";
import {JSONSchema7} from "json-schema";
import Autosizer from "react-virtualized-auto-sizer";

import {validateWithYupSchema} from "../../libs/yup";
import {useLoseChangesConfirm} from "../../components/specialized-confirmations";
import {jsonSchemaScriptKey} from "../../models/context-type-schema";
import {defaultPayloadJsonSchema} from "../../models/default-values";
import {defaultMonacoOptions} from "../../libs/monaco";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        dialogPaper: {
            height: "90vh",
        },
        dialogContent: {
            display: "grid",
            gridTemplateColumns: "auto",
            gridTemplateRows: "auto",
            rowGap: "5px",
            height: "100%",
            overflowY: "hidden",
        },
        editorContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 99",
            alignSelf: "stretch",
        },
        errorContainer: {
            gridColumn: "1 / 2",
            gridRow: "99 / 100",
            overflowY: "auto",
        },
    })
);

export type PayloadJsonEditorModalProps = {
    open: boolean;
    defaultValue?: any;
    jsonSchema?: JSONSchema7;
    onChange?: (value?: any) => void;
    onClose?: () => void;
};

const editorDidMount: EditorDidMount = editor => {
    editor.focus();
};

export const PayloadJsonEditorModal: FC<PayloadJsonEditorModalProps> = props => {
    const {open, defaultValue, jsonSchema, onChange, onClose} = props;
    const {t} = useTranslation();
    const theme = useTheme();
    const classes = useStyles();
    const loseChangesConfirm = useLoseChangesConfirm();
    const [error, setError] = useState<string>();
    const resolvedJsonSchema = useMemo(() => {
        if (typeof jsonSchema !== "object" || Object.keys.length === 0) {
            return defaultPayloadJsonSchema;
        }
        return jsonSchema;
    }, [jsonSchema]);

    const initialCode = useMemo(() => {
        if (!defaultValue) {
            return "";
        }
        return JSON.stringify(defaultValue, undefined, 2);
    }, [defaultValue]);

    const [code, setCode] = useState(initialCode);

    const editorWillMount: EditorWillMount = useCallback(
        monaco => {
            monaco.languages.json.jsonDefaults.setDiagnosticsOptions({
                validate: true,
                schemas: [
                    {
                        uri: "http://nimflow.com/schemas/payload.json",
                        fileMatch: ["inmemory://model/*"],
                        schema: resolvedJsonSchema,
                    },
                ],
            });

            const options: ReturnType<EditorWillMount> = {
                selectOnLineNumbers: true,
                minimap: {
                    enabled: false,
                },
            };
            return options;
        },
        [resolvedJsonSchema]
    );

    const handleOnAccept = useCallback(async () => {
        if (code.trim().length === 0) {
            onChange?.(undefined);
            onClose?.();
            return;
        }
        const validationSchema = yup.string().json(jsonSchemaScriptKey);
        const einfo = head(
            await validateWithYupSchema(validationSchema, code, {
                context: {
                    [jsonSchemaScriptKey]: resolvedJsonSchema,
                },
            })
        );
        if (einfo) {
            setError(einfo.message);
            return;
        }
        onChange?.(code?.trim() ? JSON.parse(code) : undefined);
        onClose?.();
    }, [code, onChange, onClose, resolvedJsonSchema]);

    const handleOnClose = useCallback(async () => {
        if (initialCode !== code && !(await loseChangesConfirm())) {
            return;
        }
        onClose?.();
    }, [code, initialCode, loseChangesConfirm, onClose]);

    const handleDialogClose = useCallback(async (event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (initialCode !== code && !(await loseChangesConfirm())) {
            return;
        }
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [code, initialCode, loseChangesConfirm, onClose]);

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            maxWidth="lg"
            fullWidth
            classes={{
                paper: classes.dialogPaper,
            }}
        >
            <DialogTitle>{t("EditingPayload")}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <div className={classes.editorContainer}>
                    <Autosizer>
                        {({height, width}) => (
                            <MonacoEditor
                                width={width}
                                height={height - 50}
                                language="json"
                                theme={theme.palette.type === "light" ? "vs-light" : "vs-dark"}
                                value={code}
                                options={defaultMonacoOptions}
                                onChange={setCode}
                                editorWillMount={editorWillMount}
                                editorDidMount={editorDidMount}
                            />
                        )}
                    </Autosizer>
                </div>
                <div className={classes.errorContainer}>
                    <FormHelperText component="span" error>
                        {error}
                    </FormHelperText>
                </div>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleOnAccept} color="primary">
                    {t("Accept")}
                </Button>
                <Button onClick={handleOnClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
