import React, {useState, useCallback, FC} from "react";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import AddIcon from "@material-ui/icons/Add";
import PlayArrowIcon from "@material-ui/icons/PlayArrow";
import FastForwardIcon from "@material-ui/icons/FastForward";
import {useTranslation} from "react-i18next";
import {useSnackbar} from "notistack";

import {StandardSection} from "../../components/standard-section";
import {StandardSectionHeader} from "../../components/standard-section-header";
import {EventsViewer} from "./events-viewer";
import {useContextSimulationContext} from "./simulation-context";
import {NewActionEditor} from "./new-action-editor/new-action-editor";
import {ExistingEvent} from "../../api/nimflow-simulation-api";
import {ActionEvent} from "../../api/nimflow-api";

export interface EventsSectionProps {
    existingEvents: ExistingEvent[];
    currentEventIndex: number;
    onCurrentEventIndexChange?: (index: number) => void;
}

export const EventsSection: FC<EventsSectionProps> = props => {
    const {existingEvents, currentEventIndex, onCurrentEventIndexChange} = props;
    const {t} = useTranslation();
    const {enqueueSnackbar} = useSnackbar();
    const {
        getState,
        executeNextPendingEvent,
        executeAllPendingEvents,
        updateSimulation,
    } = useContextSimulationContext();
    const {simulation, contextType} = getState();
    const [creatingAction, setCreatingAction] = useState(false);

    const handleNewActionClick = useCallback(() => {
        setCreatingAction(true);
    }, [setCreatingAction]);

    const handleExecuteNextPendingEvent = useCallback(() => {
        executeNextPendingEvent().catch(e => {
            enqueueSnackbar(t("ErrorSimulatingEvent", {error: e.toString()}), {
                variant: "error",
            });
        });
    }, [executeNextPendingEvent, enqueueSnackbar, t]);

    const handleExecuteAllPendingEvents = useCallback(() => {
        executeAllPendingEvents().catch(e => {
            enqueueSnackbar(t("ErrorSimulatingEvent", {error: e.toString()}), {
                variant: "error",
            });
        });
    }, [executeAllPendingEvents, enqueueSnackbar, t]);

    const handleNewActionCreated = useCallback(
        async (action: ActionEvent) => {
            updateSimulation(draft => {
                draft.pendingEvents.push(action);
            });
        },
        [updateSimulation]
    );

    const handleOnCloseDialog = useCallback(() => {
        setCreatingAction(false);
    }, [setCreatingAction]);

    return (
        <StandardSection>
            <StandardSectionHeader
                title={t("Events")}
                actions={
                    <>
                        <Tooltip title={t("NewAction") as string}>
                            <IconButton onClick={handleNewActionClick}>
                                <AddIcon />
                            </IconButton>
                        </Tooltip>
                        <Tooltip title={t("DispatchNextPendingEvent") as string}>
                            <span>
                                <IconButton
                                    onClick={handleExecuteNextPendingEvent}
                                    disabled={simulation.pendingEvents.length === 0}
                                >
                                    <PlayArrowIcon />
                                </IconButton>
                            </span>
                        </Tooltip>
                        <Tooltip title={t("DispatchAllPendingEvents") as string}>
                            <span>
                                <IconButton
                                    onClick={handleExecuteAllPendingEvents}
                                    disabled={simulation.pendingEvents.length === 0}
                                    edge="end"
                                >
                                    <FastForwardIcon />
                                </IconButton>
                            </span>
                        </Tooltip>
                    </>
                }
            />
            <EventsViewer
                existingEvents={existingEvents}
                value={currentEventIndex}
                onChange={onCurrentEventIndexChange}
            />
            {creatingAction && (
                <NewActionEditor
                    isInitialAction={simulation.pendingEvents.length === 0 && simulation.history.length === 0}
                    contextType={contextType}
                    open={!!creatingAction}
                    onClose={handleOnCloseDialog}
                    onChange={handleNewActionCreated}
                />
            )}
        </StandardSection>
    );
};
