import React, {FC, useCallback, useMemo, useState} from "react";
import {useNavigate} from "react-router";
import * as yup from "yup";
import {useTranslation} from "react-i18next";

import {DashboardDefinitionEditor} from "./dashboard-definition-editor";
import {DashboardDefinitionSchema} from "./dashboard-definition-schema";
import {DashboardDefinition, useNimflowDashboards} from "../../api/nimflow-dashboard-api";

const creteDefaultDashboardDefinition = () => {
    const result: DashboardDefinition = {
        name: "",
        title: "",
        parameters: [],
        widgets: [],
        layoutSet: {
            rowHeight: 100,
            layouts: [
                {
                    name: "phone",
                    breakpoint: 0,
                    cols: 1,
                },
                {
                    name: "tablet",
                    breakpoint: 420,
                    cols: 3,
                },
                {
                    name: "desktop",
                    breakpoint: 840,
                    cols: 12,
                },
            ],
        },
    };
    return result;
};

export const DashboardDefinitionCreator: FC<{}> = () => {
    const navigate = useNavigate();
    const {t} = useTranslation();
    const {dashboards_InsertOne, dashboards_GetInfos} = useNimflowDashboards();
    const [dashboardDefinition, setDashboardDefinition] = useState<DashboardDefinition>(() =>
        creteDefaultDashboardDefinition()
    );

    const handleOnChange = useCallback(
        async (value: DashboardDefinition) => {
            const id = await dashboards_InsertOne(value);
            value.id = id;
            setDashboardDefinition(value);
            navigate(`../edit/${id}`, {replace: true});
        },
        [navigate, dashboards_InsertOne]
    );

    const DashboardDefinitionCreatorSchema = useMemo(
        () =>
            DashboardDefinitionSchema.shape({
                name: DashboardDefinitionSchema.fields!.name.concat(
                    yup
                        .string()
                        .defined()
                        .test({
                            message: ({value}) => t("DashboardDefinitionNameAlreadyExists", {name: value}),
                            test: async function (value: any) {
                                if (!value) {
                                    return true;
                                }
                                const infos = await dashboards_GetInfos();
                                return !infos.some(s => s.name.toLowerCase() === value.toLowerCase());
                            },
                        })
                ),
            }),
        [dashboards_GetInfos, t]
    );

    return (
        <DashboardDefinitionEditor
            value={dashboardDefinition}
            schema={DashboardDefinitionCreatorSchema}
            onApplyChanges={handleOnChange}
        />
    );
};
