import React, {FC, useCallback, useMemo, useState, useEffect} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import * as yup from "yup";
import {useForm, useFormContext, FormProvider, Controller} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import i18next from "i18next";
import {useTranslation} from "react-i18next";
import {DevTool} from "@hookform/devtools";
import {isPresent, isDefined} from "ts-is-present";
import {intersection, uniq, mapObjIndexed, groupBy, append} from "ramda";

import {ContextType, Selector} from "../../api/nimflow-api";
import {jsCommonScriptKey, jsonSchemaScriptKey, SelectorSchema} from "../../models/context-type-schema";
import {freeVarDefs} from "./free-variable-defs";
import {JavascriptMonacoEditor} from "../../components/javascript-monaco-editor";
import {ContextTypeSelectorDependenciesEditor} from "./context-type-selector-dependencies-editor";
import {extractStateRefs} from "../../utils/js-analysis";
import {detectCycle} from "../../utils/graph-algorithms";
import {FormErrorViewer} from "../../components/form-error-viewer";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            backgroundColor: theme.palette.background.default,
        },
        textField: {
            width: "40ch",
            paddingBottom: 15,
        },
        dialogContent: {
            width: "100%",
            height: "400px",
            display: "table",
        },
        keyContainer: {
            display: "table-row",
        },
        jsExpressionContainer: {
            display: "table-row",
            height: "100%",
        },
    })
);

export interface SelectorEditorDialogProps {
    open: boolean;
    defaultValue?: Partial<Selector>;
    onChange: (value: Selector) => void;
    onClose: () => void;
}

const findSelectorCycle = (selector: Selector, availableSelectors: Selector[]): string[] | undefined => {
    const rootKey = selector.key;
    if (!rootKey || !selector.dependencies || selector.dependencies.length === 0) {
        return undefined;
    }
    const map = mapObjIndexed(
        (s: Selector[]) => uniq(s.flatMap(sel => sel.dependencies ?? [])),
        groupBy(
            s => s.key!,
            append(selector, availableSelectors).filter(s => s.key)
        )
    );
    return detectCycle(rootKey, {
        getNeighbors: v => map[v] ?? [],
    });
};

const getExtendedSchema = (selectors?: Selector[]) => {
    if (!selectors) {
        return SelectorSchema;
    }
    return SelectorSchema.shape({
        key: SelectorSchema.fields!.key.concat(
            yup
                .string()
                .defined()
                .test({
                    message: ({value}) => i18next.t("SelectorKeyAlreadyExists", {name: value}),
                    test: function (value: any) {
                        return !selectors.some(s => s.key === value);
                    },
                })
        ),
        dependencies: SelectorSchema.fields!.dependencies.concat(
            yup
                .array()
                .of(yup.string())
                .test({
                    message: () => i18next.t("SelectorCircularDependencyFound"),
                    test: function () {
                        const cycle = findSelectorCycle(this.parent, selectors);
                        if (cycle) {
                            return this.createError({
                                path: "jsExpression",
                                message: i18next.t("SelectorCircularDependencyFound", {cycle: cycle.join(" -> ")}),
                            });
                        }
                        return true;
                    },
                })
        ),
    });
};

interface DependenciesEditionData {
    selector: Selector;
    availableSelectors: Selector[];
    selectorIndex: number;
}

const freeVariables = [freeVarDefs.state];


export const SelectorEditorDialog: FC<SelectorEditorDialogProps> = props => {
    const {open, defaultValue, onChange, onClose} = props;
    const classes = useStyles();
    const contextTypeFormMethods = useFormContext<ContextType>();
    const [editionData, setEditionData] = useState<DependenciesEditionData>();
    const schema = useMemo(() => {
        const selectors = contextTypeFormMethods.getValues().selectors?.filter(s => s.key !== defaultValue?.key);
        return getExtendedSchema(selectors);
    }, [contextTypeFormMethods, defaultValue]);
    const formMethods = useForm({
        mode: "onSubmit",
        reValidateMode: "onSubmit",
        defaultValues: defaultValue ?? {},
        context: {
            [jsCommonScriptKey]: contextTypeFormMethods.getValues("jsCommon"),
            [jsonSchemaScriptKey]: contextTypeFormMethods.getValues("jsonSchema"),
        },
        resolver: yupResolver(schema, {
            strict: true,
            abortEarly: false,
        }),
    });
    const {getValues, setValue, control, handleSubmit, errors, register} = formMethods;
    const {t} = useTranslation();

    const selectors = contextTypeFormMethods.getValues("selectors") as Selector[];

    useEffect(() => {
        register("dependencies");
    }, [register]);

    const onSubmit = useCallback(
        (value: Partial<Selector>) => {
            onChange?.(value as Selector);
            onClose?.();
        },
        [onChange, onClose]
    );

    const handleOnSubmit = useCallback(
        (e: React.FormEvent) => {
            //Update dependencies before submitting.
            const jsExpr = getValues("jsExpression");
            let deps: string[] = [];
            if (jsExpr) {
                const selnames = selectors.map(s => s.key).filter(isPresent);
                deps = intersection(selnames, extractStateRefs(jsExpr));
            }
            setValue("dependencies", deps);

            handleSubmit(onSubmit)(e);
        },
        [getValues, handleSubmit, onSubmit, selectors, setValue]
    );

    const handleDependenciesChanged = useCallback(
        (selector: Selector) => {
            setValue("dependencies", selector.dependencies);
        },
        [setValue]
    );

    const handleOnCloseDialog = useCallback(() => {
        setEditionData(undefined);
    }, [setEditionData]);

    return (
        <Dialog
            open={open}
            fullWidth
            maxWidth="md"
            classes={{paper: classes.paper}}
        >
            <FormProvider {...formMethods}>
                <form onSubmit={handleOnSubmit}>
                    <DialogTitle>{t("EditingSelector")}</DialogTitle>
                    <DialogContent className={classes.dialogContent}>
                        <div className={classes.keyContainer}>
                            <Controller
                                name={`key`}
                                as={TextField}
                                control={control}
                                label={t("Key")}
                                className={classes.textField}
                                autoFocus
                                InputLabelProps={{required: true}}
                                error={!!errors.key}
                                helperText={errors.key?.message}
                            />
                        </div>
                        <div className={classes.jsExpressionContainer}>
                            <Controller
                                name={`jsExpression`}
                                control={control}
                                defaultValue={defaultValue?.jsExpression}
                                render={props => (
                                    <JavascriptMonacoEditor
                                        onChange={e => props.onChange(e)}
                                        value={props.value}
                                        isExpression={true}
                                        freeVariables={freeVariables}
                                    />
                                )}
                            />
                            {errors.jsExpression && <FormErrorViewer errors={errors.jsExpression} />}
                        </div>
                        {/* <FormErrorViewer errors={errors}/> */}
                    </DialogContent>
                    <DialogActions>
                        <Button type="submit" color="primary">
                            {t("Accept")}
                        </Button>
                        <Button onClick={onClose} color="primary">
                            {t("Cancel")}
                        </Button>
                    </DialogActions>
                    {isDefined(editionData) && (
                        <ContextTypeSelectorDependenciesEditor
                            open={isDefined(editionData)}
                            onClose={handleOnCloseDialog}
                            selector={editionData.selector}
                            availableSelectors={editionData.availableSelectors}
                            onChange={handleDependenciesChanged}
                        />
                    )}
                </form>
            </FormProvider>
            <DevTool control={control} />
        </Dialog>
    );
};
