import React, {FC, useCallback, useState, useMemo} from "react";
import {createStyles, Theme, makeStyles, withStyles} from "@material-ui/core/styles";
import ListItemText from "@material-ui/core/ListItemText";
import MuiAccordion from "@material-ui/core/Accordion";
import MuiAccordionSummary from "@material-ui/core/AccordionSummary";
import MuiAccordionDetails from "@material-ui/core/AccordionDetails";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import AddIcon from "@material-ui/icons/Add";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Divider from "@material-ui/core/Divider";
import Chip from "@material-ui/core/Chip";
import AssignmentIcon from "@material-ui/icons/Assignment";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import Tooltip from "@material-ui/core/Tooltip";
import {useTranslation} from "react-i18next";
import {isDefined, isPresent} from "ts-is-present";
import {clone} from "ramda";
import {useFormContext} from "react-hook-form";

import {TaskType, ContextType, ResponseType} from "../../api/nimflow-api";
import {TaskTypeEditorDialog} from "./task-type-editor-dialog";
import {useArrayField} from "../../utils/use-array-field";
import {OrderableList, OrderableListItem} from "../../components/orderable-list";
import {StandardSection} from "../../components/standard-section";
import {StandardSectionHeader} from "../../components/standard-section-header";
import {FormErrorViewer} from "../../components/form-error-viewer";
import {useOkCancelConfirm} from "../../components/confirm";
import {getSettings} from "../../api/nimflow-api-extensions";
import {useContextTypeContext} from "./context-type-context";
import {useTaggings} from "./taggings";
import {ResponseTypeEditorDialog} from "./response-type-editor-dialog";

export interface TaskTypesViewerProps {
    onDialogVisibilityChanged?: (open: boolean) => void;
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            width: "100%",
            //maxWidth: '36ch',
            backgroundColor: theme.palette.background.paper,
        },
        item: {
            border: `2px solid ${theme.palette.common.task}`,
            borderRadius: 2,
            paddingRight: 96, // Make room for second button
        },
        divider: {
            height: 5,
            background: "transparent",
        },
        inline: {
            display: "inline",
        },
        actionHeading: {
            fontSize: theme.typography.pxToRem(15),
            fontWeight: theme.typography.fontWeightRegular,
        },
        summaryTextContainer: {
            width: "100%",
            height: "100%",
            display: "flex",
            alignItems: "center",
        },
        responses: {
            display: "flex",
            justifyContent: "center",
            flexWrap: "wrap",
            "& > *": {
                margin: theme.spacing(0.5),
            },
        },
        textField: {
            width: "40ch",
        },
    })
);

const Accordion = withStyles({
    root: {
        backgroundColor: "transparent",
        boxShadow: "none",
        "&:not(:last-child)": {
            borderBottom: 0,
        },
        "&:before": {
            display: "none",
        },
        "&$expanded": {
            border: "1px solid rgba(0, 0, 0, .125)",
            margin: "auto",
        },
    },
    expanded: {},
})(MuiAccordion);

const AccordionSummary = withStyles({
    root: {
        marginBottom: -1,
        height: 32,
        minHeight: 32,
        papdding: 0,
        "&$expanded": {
            borderBottom: "1px solid rgba(0, 0, 0, .125)",
            minHeight: 56,
        },
    },
    content: {
        margin: 0,
        height: "100%",
        alignItems: "center",
        "&$expanded": {
            margin: 0,
        },
    },
    expandIcon: {
        paddingRight: "16px",
    },
    expanded: {},
})(MuiAccordionSummary);

const AccordionDetails = withStyles(theme => ({
    root: {
        padding: theme.spacing(2),
        flexDirection: "column",
    },
}))(MuiAccordionDetails);

interface TaskTypeEditionData {
    type: "taskType";
    index?: number;
    taskType: Partial<TaskType>;
}

interface ResponseTypeEditionData {
    type: "responseType";
    taskIndex: number;
    responseIndex?: number;
    responseType: Partial<ResponseType>;
}

type EditionData = TaskTypeEditionData | ResponseTypeEditionData;

export const TaskTypesEditor: FC<TaskTypesViewerProps> = props => {
    const {onDialogVisibilityChanged} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const [expanded, setExpanded] = useState<number>(-1);
    const [editionData, setEditionData] = useState<EditionData>();
    const {control, errors} = useFormContext<ContextType>();
    const {fields: taskTypes, append, remove, setAt, move} = useArrayField<TaskType>({name: "tasks", control});
    const confirm = useOkCancelConfirm();
    const {
        getJsonSchemaDefinition,
        getJsonSchemaDefinitions,
        setJsonSchemaDefinition,
        updateResponseJsonSchemaDefinition,
        updateTaskJsonSchemaDefinitions,
    } = useContextTypeContext();
    const {removeInvalidTaggings} = useTaggings();

    const handleNewTaskTypeClick = useCallback(() => {
        setEditionData({
            type: "taskType",
            taskType: {
                name: "",
                jsTaskDataSetters: {},
                validResponses: [],
            },
        });
        onDialogVisibilityChanged?.(true);
        setExpanded(taskTypes.length);
    }, [setEditionData, taskTypes, onDialogVisibilityChanged]);

    const handleDeleteTaskTypeClick = useCallback(
        async (index: number) => {
            let result = await confirm({description: t("ConfirmDeleteTaskType", {name: taskTypes[index].name})});
            if (!result) {
                return;
            }
            const defNames = taskTypes
                ?.flatMap(s => s.validResponses ?? [])
                .map(s => s.payloadSchemaDefinitionName)
                .filter(isPresent);
            if (defNames && defNames.length > 0) {
                for (const defName of defNames) {
                    setJsonSchemaDefinition(defName, undefined);
                }
            }
            remove(index);
            removeInvalidTaggings();
            if (expanded > index) {
                setExpanded(expanded - 1);
            }
        },
        [confirm, expanded, remove, removeInvalidTaggings, setJsonSchemaDefinition, t, taskTypes]
    );

    const taskTypePayloadSchemaDefinitions = useMemo(() => {
        const names = ((editionData?.type === "taskType" ? editionData.taskType.validResponses : undefined) ?? [])
            .map(s => s.payloadSchemaDefinitionName)
            .filter(isPresent);
        return getJsonSchemaDefinitions(names);
    }, [editionData, getJsonSchemaDefinitions]);

    const handleTaskTypeChanged = useCallback(
        (taskType: TaskType, definitionJsonSchemas: Record<string, string | undefined>) => {
            if (editionData?.type !== "taskType") {
                return;
            }
            const {index} = editionData;
            if (isDefined(index)) {
                const originalDefNames = taskTypes[index].validResponses
                    ?.map(s => s.payloadSchemaDefinitionName)
                    .filter(isPresent);
                for (const name of originalDefNames ?? []) {
                    if (!(name in definitionJsonSchemas)) {
                        definitionJsonSchemas[name] = undefined;
                    }
                }
                setAt(index, taskType);
                removeInvalidTaggings();
            } else {
                append(taskType);
            }
            updateTaskJsonSchemaDefinitions(taskType, definitionJsonSchemas);
        },
        [append, editionData, removeInvalidTaggings, setAt, taskTypes, updateTaskJsonSchemaDefinitions]
    );

    const handlerReorder = useCallback(
        (from: number, to: number) => {
            move(from, to);
            if (expanded === from) {
                setExpanded(to);
            } else if (expanded > from && expanded < to) {
                setExpanded(expanded - 1);
            }
        },
        [move, expanded]
    );

    const handleExpansionPanelChange = useCallback((index: number, expanded: boolean) => {
        setExpanded(expanded ? index : -1);
    }, []);

    const handleEditTaskTypeClick = useCallback(
        (index: number, taskType: Partial<TaskType>) => {
            setEditionData({type: "taskType", index, taskType: clone(taskType)});
            onDialogVisibilityChanged?.(true);
        },
        [setEditionData, onDialogVisibilityChanged]
    );

    const handleNewResponseTypeClick = useCallback(
        (taskType: Partial<TaskType>) => {
            const taskIndex = taskTypes.findIndex(s => s === taskType);
            if (taskIndex <= -1) {
                return;
            }
            setEditionData({
                type: "responseType",
                taskIndex,
                responseType: {
                    name: "",
                    validators: [],
                    jsSetters: {},
                },
            });
            onDialogVisibilityChanged?.(true);
            setExpanded(taskTypes.length);
        },
        [setEditionData, taskTypes, onDialogVisibilityChanged]
    );

    const handleResponseTypeClick = useCallback(
        (taskType: Partial<TaskType>, responseType: Partial<ResponseType>) => {
            const taskIndex = taskTypes.findIndex(s => s === taskType);
            if (taskIndex <= -1) {
                return;
            }
            if (!taskType.validResponses) {
                return;
            }
            const responseIndex = taskType.validResponses.findIndex(s => s === responseType);
            if (responseIndex <= -1) {
                return;
            }
            setEditionData({type: "responseType", taskIndex, responseIndex, responseType: clone(responseType)});
            onDialogVisibilityChanged?.(true);
        },
        [taskTypes, onDialogVisibilityChanged]
    );

    const handleResponseTypeDelete = useCallback(
        async (taskType: Partial<TaskType>, responseType: Partial<ResponseType>) => {
            const taskIndex = taskTypes.findIndex(s => s === taskType);
            if (taskIndex <= -1) {
                return;
            }
            if (!taskType.validResponses) {
                return;
            }
            const responseIndex = taskType.validResponses.findIndex(s => s === responseType);
            if (responseIndex <= -1) {
                return;
            }
            let result = await confirm({
                description: t("ConfirmDeleteResponseType", {
                    name: taskType.validResponses[responseIndex].name,
                }),
            });
            if (result) {
                let defName = responseType.payloadSchemaDefinitionName;
                if (defName) {
                    setJsonSchemaDefinition(defName, undefined);
                }

                setAt(taskIndex, {
                    ...taskType,
                    validResponses: [...taskType.validResponses.filter(s => s !== responseType)],
                });
                removeInvalidTaggings();
            }
        },
        [taskTypes, confirm, t, setAt, removeInvalidTaggings, setJsonSchemaDefinition]
    );

    const responseTypePayloadSchemaDefinition = useMemo(() => {
        if (editionData?.type !== "responseType") {
            return undefined;
        }
        const defname = editionData.responseType.payloadSchemaDefinitionName;
        if (!defname) {
            return undefined;
        }
        return getJsonSchemaDefinition(defname);
    }, [editionData, getJsonSchemaDefinition]);

    const handleResponseTypeChanged = useCallback(
        (responseType: ResponseType, definitionJsonSchema: string | undefined) => {
            if (!editionData) {
                return;
            }
            const {taskIndex, responseIndex} = editionData as ResponseTypeEditionData;
            const taskType = taskTypes[taskIndex];
            updateResponseJsonSchemaDefinition(responseType, taskType.name!, definitionJsonSchema);
            if (isDefined(responseIndex)) {
                if (!responseType.payloadSchemaDefinitionName) {
                    const defName = taskTypes[taskIndex].validResponses?.[responseIndex].payloadSchemaDefinitionName;
                    if (defName) {
                        setJsonSchemaDefinition(defName, undefined);
                    }
                }
                setAt(taskIndex, {
                    ...taskType,
                    validResponses: taskType.validResponses!.map((s, idx) =>
                        idx === responseIndex ? responseType : s
                    ),
                });
                removeInvalidTaggings();
            } else {
                setAt(taskIndex, {
                    ...taskType,
                    validResponses: [...(taskType.validResponses ?? []), responseType],
                });
            }
        },
        [
            editionData,
            removeInvalidTaggings,
            setAt,
            setJsonSchemaDefinition,
            taskTypes,
            updateResponseJsonSchemaDefinition,
        ]
    );

    const handleOnCloseDialog = useCallback(() => {
        setEditionData(undefined);
        onDialogVisibilityChanged?.(false);
    }, [setEditionData, onDialogVisibilityChanged]);

    return (
        <StandardSection>
            <StandardSectionHeader
                title={t("Tasks")}
                actions={
                    <Tooltip title={t("NewTaskType") as string}>
                        <IconButton onClick={handleNewTaskTypeClick}>
                            <AddIcon />
                        </IconButton>
                    </Tooltip>
                }
            />
            <OrderableList onItemReorder={handlerReorder} className={classes.root}>
                {taskTypes.map((task: Partial<TaskType>, taskIndex) => {
                    const responseTypes = task.validResponses ?? [];
                    return (
                        <OrderableListItem
                            key={task.name}
                            index={taskIndex}
                            className={classes.item}
                            afterItem={<Divider className={classes.divider} />}
                        >
                            <ListItemText
                                primary={
                                    <Accordion
                                        expanded={expanded === taskIndex}
                                        onChange={(_event, expanded) => handleExpansionPanelChange(taskIndex, expanded)}
                                    >
                                        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                                            <div className={classes.summaryTextContainer}>
                                                <Typography className={classes.actionHeading}>{task.name}</Typography>
                                            </div>
                                        </AccordionSummary>
                                        <AccordionDetails>
                                            {responseTypes.length > 0 && (
                                                <div className={classes.responses}>
                                                    {responseTypes.map((response, responseIndex) => {
                                                        let label = getSettings(response)?.commandText;
                                                        if (!label) {
                                                            label = response.name;
                                                        }
                                                        return (
                                                            <Chip
                                                                key={responseIndex}
                                                                icon={<AssignmentIcon />}
                                                                label={label}
                                                                onClick={() => handleResponseTypeClick(task, response)}
                                                                onDelete={() =>
                                                                    handleResponseTypeDelete(task, response)
                                                                }
                                                            />
                                                        );
                                                    })}
                                                    <Tooltip title={t("NewResponseType") as string}>
                                                        <IconButton size="small" onClick={() => handleNewResponseTypeClick(task)}>
                                                            <AddIcon />
                                                        </IconButton>
                                                    </Tooltip>
                                                </div>
                                            )}
                                        </AccordionDetails>
                                    </Accordion>
                                }
                            />
                            <ListItemSecondaryAction>
                                <Tooltip title={t("Edit") as string}>
                                    <IconButton
                                        aria-label="edit"
                                        onClick={() => handleEditTaskTypeClick(taskIndex, task)}
                                    >
                                        <EditIcon />
                                    </IconButton>
                                </Tooltip>
                                <Tooltip title={t("Delete") as string}>
                                    <IconButton
                                        edge="end"
                                        aria-label="delete"
                                        onClick={() => handleDeleteTaskTypeClick(taskIndex)}
                                    >
                                        <DeleteIcon />
                                    </IconButton>
                                </Tooltip>
                            </ListItemSecondaryAction>
                        </OrderableListItem>
                    );
                })}
            </OrderableList>
            <FormErrorViewer errors={errors} names="tasks" />
            {editionData?.type === "taskType" && (
                <TaskTypeEditorDialog
                    open={isDefined(editionData)}
                    onClose={handleOnCloseDialog}
                    defaultValue={editionData.taskType}
                    definitionJsonSchemas={taskTypePayloadSchemaDefinitions}
                    onChange={handleTaskTypeChanged}
                />
            )}
            {editionData?.type === "responseType" && (
                <ResponseTypeEditorDialog
                    open={editionData?.type === "responseType"}
                    onClose={handleOnCloseDialog}
                    defaultValue={editionData.responseType}
                    taskTypeName={taskTypes[editionData.taskIndex].name!}
                    definitionJsonSchema={responseTypePayloadSchemaDefinition}
                    onChange={handleResponseTypeChanged}
                />
            )}
        </StandardSection>
    );
};
