import React, {FC, useMemo, useCallback} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import clsx from "clsx";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import {Components, Column} from "@material-table/core";
import {useTranslation} from "react-i18next";
import {sortBy} from "ramda";
import {useSpring, animated} from "react-spring";

import {Selector} from "../../api/nimflow-api";
import {TaggingType, getTaggingDescription, TaggingTypeIcon} from "./taggings";
import {BaseTable, BaseTableProps} from "../../components/base-table";
import {ContextTypeNodeTagging} from "./diagram-nodes/context-type-node-tagging";
import {PinIcon} from "../../components/icons/pin-icon";
import {UnpinIcon} from "../../components/icons/unpin-icon";
import {PinStatus} from "./context-type-diagram-model";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        title: {
            marginTop: 5,
            marginLeft: 5,
            marginBottom: -10,
        },
        nameContainer: {
            position: "relative",
        },
        textContainer: {
            textOverflow: "ellipsis",
            whiteSpace: "nowrap",
            overflow: "hidden",
        },
        name: {
            "&.highlighted": {
                textDecoration: "underline",
            },
        },
        actions: {
            position: "absolute",
            top: -3,
            right: 0,
            bottom: -4,
            borderRadius: 5,
        },
        action: {
            marginLeft: 3,
            backgroundColor: theme.palette.background.paper,
            "&:hover": {
                backgroundColor: theme.palette.background.default,
            },
        },
        table: {},
        taggingSelector: {
            textAlign: "center",
            width: 25,
        },
        icon: {
            width: 18,
            height: 18,
            verticalAlign: "middle",
            textAlign: "center",
        },
    })
);

export interface ContextTypeDiagramStateListProps {
    states: string[];
    selectors: Selector[];
    selectedStates?: string[];
    taggings?: Record<string, TaggingType>;
    pinStatuses?: Record<string, PinStatus>;
    onChangeTagging?: (state: string, tagging: TaggingType) => void;
    onEditSelector?: (selector: Selector) => void;
    onDeleteSelector?: (selector: Selector) => void;
    onChangePinning?: (state: string, pinned: boolean) => void;
}

interface TaggingSelectorProps {
    value?: TaggingType;
    onChange?: (value: TaggingType) => void;
}

const TaggingSelector: FC<TaggingSelectorProps> = props => {
    const {value, onChange} = props;
    const [tooltipIsOpen, setTooltipIsOpen] = React.useState(false);
    const classes = useStyles();

    const handleChange = useCallback(
        (value: TaggingType) => {
            setTooltipIsOpen(false);
            onChange?.(value);
        },
        [onChange]
    );

    return (
        <Tooltip
            title={getTaggingDescription(value)}
            open={tooltipIsOpen}
            onOpen={() => setTooltipIsOpen(true)}
            onClose={() => setTooltipIsOpen(false)}
        >
            <div className={classes.taggingSelector}>
                <ContextTypeNodeTagging
                    value={value}
                    onChange={handleChange}
                    displayEmptyTagging={true}
                    displayStandalone={true}
                />
            </div>
        </Tooltip>
    );
};

const tableComponents: Components = {
    Container: props => <div {...props} />,
};

interface StateData {
    type: "state";
    id: string;
    name: string;
    tagging: TaggingType;
    pinStatus: PinStatus;
}

interface SelectorData {
    type: "selector";
    id: string;
    name: string;
    selector: Selector;
    tagging: TaggingType;
    pinStatus: PinStatus;
}

type Data = StateData | SelectorData;

const options: BaseTableProps<any>["options"] = {
    search: false,
    //maxBodyHeight: "calc(100vh - 400px)",
    //tableLayout: "fixed",
    showTitle: false,
    toolbar: false,
};

interface NameColumnRendererProps {
    rowData: Data;
    isSelected: boolean;
    onChangePinning?: (state: string, pinned: boolean) => void;
    onEditSelector?: (selector: Selector) => void;
    onDeleteSelector?: (selector: Selector) => void;
}

const NameColumnRenderer: FC<NameColumnRendererProps> = props => {
    const {rowData, isSelected, onChangePinning, onEditSelector, onDeleteSelector} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const [{opacity}, set] = useSpring(() => ({opacity: 0}));

    const handlePin = useCallback(() => {
        onChangePinning?.(rowData.name, true);
    }, [onChangePinning, rowData.name]);

    const handleUnpin = useCallback(() => {
        onChangePinning?.(rowData.name, false);
    }, [onChangePinning, rowData.name]);

    const handleEdit = useCallback(() => {
        if (rowData.type === "selector") {
            set({opacity: 0});
            onEditSelector?.(rowData.selector);
        }
    }, [onEditSelector, rowData, set]);

    const handleDelete = useCallback(() => {
        if (rowData.type === "selector") {
            set({opacity: 0});
            onDeleteSelector?.(rowData.selector);
        }
    }, [onDeleteSelector, rowData, set]);

    const name = rowData?.name;
    if (!name) {
        return null;
    }

    const ColumnButton = (props: {tooltip: string; onClick: () => void; children: React.ReactNode}) => {
        return (
            <Tooltip title={props.tooltip}>
                <IconButton size="small" color="inherit" onClick={props.onClick} className={classes.action}>
                    {props.children}
                </IconButton>
            </Tooltip>
        );
    };

    return (
        <div
            onMouseEnter={() => set({opacity: 1})}
            onMouseLeave={() => set({opacity: 0})}
            className={classes.nameContainer}
        >
            <div className={classes.textContainer}>
                <span className={clsx(classes.name, {highlighted: isSelected})}>{name}</span>
            </div>
            <animated.div style={{opacity}} className={classes.actions}>
                {rowData.pinStatus === undefined && (
                    <ColumnButton tooltip={t("Pin")} onClick={handlePin}>
                        <PinIcon className={classes.icon} />
                    </ColumnButton>
                )}
                {rowData.pinStatus === "pinned" && (
                    <ColumnButton tooltip={t("Unpin")} onClick={handleUnpin}>
                        <UnpinIcon className={classes.icon} />
                    </ColumnButton>
                )}
                {rowData.type === "selector" && (
                    <>
                        <ColumnButton tooltip={t("Edit")} onClick={handleEdit}>
                            <EditIcon className={classes.icon} />
                        </ColumnButton>
                        <ColumnButton tooltip={t("Delete")} onClick={handleDelete}>
                            <DeleteIcon className={classes.icon} />
                        </ColumnButton>
                    </>
                )}
            </animated.div>
        </div>
    );
};

export const ContextTypeDiagramStateList: FC<ContextTypeDiagramStateListProps> = props => {
    const {
        taggings,
        onChangeTagging,
        states,
        selectors,
        selectedStates,
        pinStatuses,
        onEditSelector,
        onDeleteSelector,
        onChangePinning,
    } = props;
    const {t} = useTranslation();
    const classes = useStyles();

    const data = useMemo(() => {
        const stateData = states.map(s => {
            const data: StateData = {
                type: "state",
                id: s,
                name: s,
                tagging: taggings?.[s],
                pinStatus: pinStatuses?.[s],
            };
            return data;
        });
        const selectorData = selectors.map(s => {
            const data: SelectorData = {
                type: "selector",
                id: s.key ?? "",
                name: s.key ?? "",
                tagging: s.key ? taggings?.[s.key] : undefined,
                selector: s,
                pinStatus: s.key ? pinStatuses?.[s.key] : undefined,
            };
            return data;
        });
        return sortBy(s => s.name, [...stateData, ...selectorData]);
    }, [pinStatuses, selectors, states, taggings]);

    const renderTagging = useCallback(
        (rowData: Data) => {
            const name = rowData?.name;
            if (!name) {
                return null;
            }
            return <TaggingSelector value={rowData.tagging} onChange={type => onChangeTagging?.(name, type)} />;
        },
        [onChangeTagging]
    );

    const renderName = useCallback(
        (rowData: Data) => {
            return (
                <NameColumnRenderer
                    rowData={rowData}
                    isSelected={!!selectedStates?.includes(rowData.name)}
                    onChangePinning={onChangePinning}
                    onEditSelector={onEditSelector}
                    onDeleteSelector={onDeleteSelector}
                />
            );
        },
        [onChangePinning, onDeleteSelector, onEditSelector, selectedStates]
    );

    const columns: Column<Data>[] = useMemo(
        () => [
            {
                title: <TaggingTypeIcon taggingType="tag" className={classes.icon} />,
                width: 20,
                render: renderTagging,
                editable: "never",
                sorting: false,
                headerStyle: {
                    paddingRight: 2,
                    paddingLeft: 8,
                },
                cellStyle: {
                    width: 25,
                    paddingRight: 2,
                    paddingLeft: 5,
                },
            },

            {
                field: "name",
                title: t("Field"),
                width: 120,
                headerStyle: {
                    paddingRight: 5,
                    paddingLeft: 2,
                },
                cellStyle: {
                    width: "100%",
                    paddingLeft: 5,
                    paddingRight: 5,
                },
                render: renderName,
            },
        ],
        [classes.icon, renderName, renderTagging, t]
    );

    return (
        <div className={classes.table}>
            {/* <Typography variant="h6" className={classes.title}>{t("Fields")}</Typography> */}
            <BaseTable columns={columns} data={data} components={tableComponents} options={options} />
        </div>
    );
};
