import React, {FC, useCallback, useMemo, useState} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import Tooltip from "@material-ui/core/Tooltip";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import ToggleButton from "@material-ui/lab/ToggleButton";
import Typography from "@material-ui/core/Typography";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import SaveIcon from "@material-ui/icons/Save";
//import AddCircleIcon from "@material-ui/icons/AddCircle";
import RotateLeftIcon from "@material-ui/icons/RotateLeft";
import SaveAltIcon from "@material-ui/icons/SaveAlt";
import VerticalAlignBottomIcon from "@material-ui/icons/VerticalAlignBottom";
import {useTranslation} from "react-i18next";
import {useNavigate} from "react-router";
import {useThrottleCallback} from "@react-hook/throttle";

import {TaskListViewer} from "./task-list-viewer";
import {StandardSection} from "../../components/standard-section";
import {StandardSectionHeader} from "../../components/standard-section-header";
import {useContextSimulationContext} from "./simulation-context";
import {downloadJson} from "../../utils/io-helpers";
import {exportSimulationStateToJson} from "./simulation-state-exporter";
import {getExistingActions} from "./simulation-model";
import {ContextType} from "../../api/nimflow-api";
import {ConsoleViewer} from "../../components/console-viewer";
import {useNimflowAuthorization} from "../../api/nimflow-authorization";
import {ContextSimulation} from "../../api/nimflow-simulation-api";
import {ErrorInfo, mapRhfErrorsToErrorInfos} from "../../models/error-info";
import {useShowErrorInfos} from "../../components/use-show-error-infos";
import {EventsSection} from "./events-section";
import {StateSection} from "./state-section";
import {ContextSimulationSaveAsDialog} from "./context-simulation-save-as-dialog";
import {useConfirm} from "../../components/confirm";
import {ErrorViewer} from "../../components/error-viewer";

const useStyles = makeStyles((_theme: Theme) =>
    createStyles({
        toolbar: {
            minHeight: "48px",
            paddingLeft: 0,
        },
        fileInput: {
            opacity: 0,
            position: "absolute",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
        },
        grow: {
            flexGrow: 1,
        },
        toggleButton: {
            border: "none",
            borderRadius: "50%",
        },
        console: {
            maxHeight: "200px",
            overflow: "auto",
        },
        saveAsEllipsis: {
            position: "absolute",
            left: 17,
            bottom: 4,
            fontWeight: "bold",
        },
    })
);

export interface ContextSimulationEditorProps {
    onSaveChanges?: (contextSimulation: ContextSimulation) => Promise<ErrorInfo[]>;
    onSaveAs?: (contextSimulation: ContextSimulation, newName: string) => Promise<void>;
}

const GoBackButton = () => {
    const navigate = useNavigate();
    const {t} = useTranslation();
    const handleGoBack = useCallback(() => {
        navigate(-1);
    }, [navigate]);

    return (
        <Tooltip title={t("GoBack") as string}>
            <IconButton onClick={handleGoBack}>
                <ArrowBackIcon />
            </IconButton>
        </Tooltip>
    );
};

const DownloadSimulationButton = (props: {simulation: ContextSimulation; contextType: ContextType}) => {
    const {simulation, contextType} = props;
    const {t} = useTranslation();
    const handleDownloadSimulationClick = useCallback(() => {
        downloadJson(exportSimulationStateToJson(simulation, contextType), `${contextType.name}.simulation-state.json`);
    }, [simulation, contextType]);

    return (
        <Tooltip title={t("DownloadSimulation") as string}>
            <IconButton onClick={handleDownloadSimulationClick}>
                <SaveAltIcon />
            </IconButton>
        </Tooltip>
    );
};

const SaveChangesButton = (props: {
    simulation: ContextSimulation;
    onSaveChanges?: (contextSimulation: ContextSimulation) => Promise<ErrorInfo[]>;
}) => {
    const {simulation, onSaveChanges} = props;
    const {t} = useTranslation();
    const showErrorInfos = useShowErrorInfos();
    const [savingAllowed, setSavingAllowed] = useState(true);
    const {canUpdateSimulations, canInsertSimulations} = useNimflowAuthorization();

    const canSave = simulation.id ? canUpdateSimulations() : canInsertSimulations();

    const handleSaveContextSimulation = useThrottleCallback(
        useCallback(async () => {
            if (!onSaveChanges) {
                return;
            }
            setSavingAllowed(false);
            let errorInfos: ErrorInfo[];
            try {
                errorInfos = await onSaveChanges(simulation);
            } catch (e: any) {
                errorInfos = [
                    {
                        message: e.toString(),
                    },
                ];
            } finally {
                setSavingAllowed(true);
            }
            if (errorInfos && errorInfos.length > 0) {
                await showErrorInfos(errorInfos, {
                    title: t("ErrorSavingContextSimulation"),
                });
            }
        }, [onSaveChanges, showErrorInfos, t, simulation])
    );

    return (
        <Tooltip title={t("SaveChanges") as string}>
            <IconButton onClick={handleSaveContextSimulation} disabled={!canSave || !savingAllowed}>
                <SaveIcon />
            </IconButton>
        </Tooltip>
    );
};

const SaveAsButton = (props: {name: string; canSave: boolean; onSaveAs?: (newName: string) => void}) => {
    const {name, canSave, onSaveAs} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const confirm = useConfirm();
    const {validate} = useContextSimulationContext();
    const {canInsertSimulations} = useNimflowAuthorization();
    const [saveAsDialogOpen, setSaveAsDialogOpen] = useState(false);

    const handleSaveAsContextSimulation = useCallback(async () => {
        const errors = await validate();
        if (Object.keys(errors).length > 0) {
            const errorInfos = mapRhfErrorsToErrorInfos(errors);
            await confirm({
                title: t("ErrorSavingContextSimulation"),
                description: <ErrorViewer error={errorInfos} />,
                mapCloseTo: "ok",
                actions: [{type: "ok", text: t("Ok")}],
            });
            return; 
        }
        setSaveAsDialogOpen(true);
    }, [confirm, t, validate]);

    const handleOnChange = useCallback(
        (value: string) => {
            onSaveAs?.(value);
        },
        [onSaveAs]
    );
    const handleOnClose = useCallback(() => {
        setSaveAsDialogOpen(false);
    }, []);

    return (
        <>
            <Tooltip title={t("SaveAs") as string}>
                <IconButton onClick={handleSaveAsContextSimulation} disabled={!canSave || !canInsertSimulations()}>
                    <SaveIcon />
                    <Typography className={classes.saveAsEllipsis}>...</Typography>
                </IconButton>
            </Tooltip>
            {saveAsDialogOpen && (
                <ContextSimulationSaveAsDialog
                    open={saveAsDialogOpen}
                    defaultValue={name}
                    onChange={handleOnChange}
                    onClose={handleOnClose}
                />
            )}
        </>
    );
};

export const ContextSimulationEditor: FC<ContextSimulationEditorProps> = props => {
    const {onSaveChanges, onSaveAs} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const {
        getState,
        resetSimulation,
        updateSimulation,
        validate,
        errors,
        options,
        setOptions,
    } = useContextSimulationContext();
    const {simulation, contextType} = getState();
    const existingEvents = useMemo(() => {
        return simulation ? getExistingActions(simulation) : [];
    }, [simulation]);
    const [currentEventIndex, setCurrentEventIndex] = useState(() => {
        let idx = existingEvents.findIndex(s => s.status === "pending");
        if (idx === -1) {
            idx = existingEvents.length;
        }
        return Math.max(0, idx - 1);
    });

    const handleExecuteAllPendingEventsClick = useCallback(() => {
        setOptions({
            ...options,
            shouldExecuteAllPendingEvents: !options.shouldExecuteAllPendingEvents,
        });
    }, [options, setOptions]);

    const handleSimulationNameChanged = useCallback(
        (event: React.ChangeEvent<any>) => {
            updateSimulation(draft => {
                draft.name = event.target.value;
            });
        },
        [updateSimulation]
    );

    const handleSimulationDescriptionChanged = useCallback(
        (event: React.ChangeEvent<any>) => {
            updateSimulation(draft => {
                draft.description = event.target.value;
            });
        },
        [updateSimulation]
    );

    const handleSave = useCallback(
        async (simulation: ContextSimulation) => {
            const errors = await validate();
            if (Object.keys(errors).length > 0) {
                return mapRhfErrorsToErrorInfos(errors);
            }
            return onSaveChanges?.(simulation) ?? [];
        },
        [onSaveChanges, validate]
    );

    const handleSaveAs = useCallback(
        async (newName: string) => {
            const errors = await validate();
            if (Object.keys(errors).length > 0) {
                return mapRhfErrorsToErrorInfos(errors);
            }
            onSaveAs?.(simulation, newName);
        },
        [onSaveAs, simulation, validate]
    );

    const currentStep = simulation.history[currentEventIndex];

    return (
        <Grid container spacing={3}>
            <Grid item xs={12} style={{padding: 0}}>
                <Toolbar className={classes.toolbar}>
                    <GoBackButton />
                    <SaveChangesButton simulation={simulation} onSaveChanges={handleSave} />
                    <SaveAsButton name={simulation.name ?? ""} canSave={true} onSaveAs={handleSaveAs} />
                    <DownloadSimulationButton simulation={simulation} contextType={contextType} />
                    <Tooltip title={t("ResetSimulationHistory") as string}>
                        <span>
                            <IconButton onClick={resetSimulation} disabled={simulation.history.length === 0}>
                                <RotateLeftIcon />
                            </IconButton>
                        </span>
                    </Tooltip>
                    <Tooltip title={t("ExecuteAllPendingEventsAfterEveryUpdate") as string}>
                        <ToggleButton
                            value="executeAllPendingEventsAfterEveryUpdate"
                            selected={options?.shouldExecuteAllPendingEvents}
                            onClick={handleExecuteAllPendingEventsClick}
                            classes={{
                                root: classes.toggleButton,
                            }}
                        >
                            <VerticalAlignBottomIcon />
                        </ToggleButton>
                    </Tooltip>
                    <div className={classes.grow} />
                    <Typography variant="body1" noWrap>
                        {t("SimulationContextTypeHeader", {name: contextType.name})}
                    </Typography>
                </Toolbar>
            </Grid>
            <Grid item xs={6}>
                <TextField
                    label={t("Name")}
                    value={simulation.name ?? ""}
                    onChange={handleSimulationNameChanged}
                    fullWidth
                    error={!!errors.name}
                    helperText={errors.name?.message}
                />
            </Grid>
            <Grid item xs={6}></Grid>
            <Grid item xs={12}>
                <TextField
                    label={t("Description")}
                    value={simulation.description ?? ""}
                    onChange={handleSimulationDescriptionChanged}
                    multiline
                    fullWidth
                    maxRows={4}
                    error={!!errors.description}
                    helperText={errors.description?.message}
                />
            </Grid>
            <Grid item xs={4}>
                <EventsSection
                    existingEvents={existingEvents}
                    currentEventIndex={currentEventIndex}
                    onCurrentEventIndexChange={setCurrentEventIndex}
                />
            </Grid>
            <Grid item xs={4}>
                <Grid container spacing={3}>
                    <Grid item xs={12}>
                        <StateSection
                            simulation={simulation}
                            contextType={contextType}
                            currentEventIndex={currentEventIndex}
                        />
                    </Grid>
                    {currentStep?.errors && currentStep.errors.length > 0 && (
                        <Grid item xs={12}>
                            <StandardSection>
                                <StandardSectionHeader title={t("Errors")} />
                                <div style={{overflow: "auto", maxHeight: "50vh"}}>
                                    {currentStep.errors.map((error, i) => (
                                        <div key={i}>{error}</div>
                                    ))}
                                </div>
                            </StandardSection>
                        </Grid>
                    )}
                </Grid>
            </Grid>
            <Grid item xs={4}>
                <StandardSection>
                    <StandardSectionHeader title={t("Tasks")} />
                    {currentStep && <TaskListViewer stateIndex={currentEventIndex} />}
                </StandardSection>
            </Grid>
            {!!currentStep?.consoleEntries && currentStep.consoleEntries.length > 0 && (
                <Grid item xs={12}>
                    <StandardSection>
                        <StandardSectionHeader title={t("Console")} />
                        <div className={classes.console}>
                            <ConsoleViewer entries={currentStep.consoleEntries} />
                        </div>
                    </StandardSection>
                </Grid>
            )}
        </Grid>
    );
};
