import React, {FC, useMemo} from "react";
import i18next from "i18next";
import useSWR from "swr";
import {useTranslation} from "react-i18next";
import {Column, Action} from "@material-table/core";
import RefreshIcon from "@material-ui/icons/Refresh";
import CheckIcon from "@material-ui/icons/Check";
import {format, parseISO} from "date-fns";

import {SimulationStateSelectorStepProps} from "./simulation-data-selector";
import {useNimflow} from "../../../api/nimflow-api-provider";
import {StepContainer} from "./step-container";
import {WaitSpinner} from "../../../components/wait-spinner";
import {useReportError} from "../../../utils/use-report-error";
import MaterialTable from "../../../libs/material-table";
import {Context, SearchContexts} from "../../../api/nimflow-api";
import {useKey} from "../../../utils/use-key";

let columns: Column<Context>[] = [
    {field: "id", title: i18next.t("Id")},
    {
        field: "typeName",
        title: i18next.t("ContextType"),
        render: s => {
            return `${s.typeName} (v${s.contextTypeVersion})`;
        },
    },
    {field: "reference", title: i18next.t("Reference")},
    {
        field: "dateCreated",
        title: i18next.t("DateCreated"),
        render: s => {
            return s.dateCreated ? format(parseISO(s.dateCreated), i18next.t("DATETIME_FORMAT")) : "";
        },
    },
    {
        field: "dateModified",
        title: i18next.t("DateModified"),
        render: s => {
            return s.dateModified ? format(parseISO(s.dateModified), i18next.t("DATETIME_FORMAT")) : "";
        },
    },
];
columns = columns.map(s => ({
    ...s,
    title: <span style={{whiteSpace: "nowrap"}}>{s.title}</span>,
}));

const adaptHandler = <T extends object>(handler: (info: T) => void): Action<T>["onClick"] => {
    return (_, info) => {
        if (Array.isArray(info)) {
            info = info[0];
        }
        handler(info);
    };
};

const buildRefreshAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: RefreshIcon,
        tooltip: i18next.t("RefreshData"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const buildSelectAction = (handler: (ctx: Context) => void) => {
    const action: Action<Context> = {
        icon: () => <CheckIcon style={{color: "green"}} />,
        tooltip: i18next.t("Select"),
        onClick: adaptHandler(handler),
    };
    return action;
};

const useContexts = (searchFilter: Partial<SearchContexts>) => {
    const {t} = useTranslation();
    const reportError = useReportError();
    const {contexts_Search} = useNimflow();
    const key = useKey(contexts_Search, searchFilter);
    const {data, mutate, isValidating, error} = useSWR(
        key,
        async () => {
            try {
                return await contexts_Search({...{limit: 10}, ...(searchFilter as SearchContexts)});
            } catch (e) {
                reportError(t("ErrorSearchingContexts"), e);
                return [];
            }
        },
        {
            revalidateOnFocus: false,
        }
    );
    return useMemo(() => {
        return {
            data: data ?? [],
            isValidating,
            error,
            revalidate: mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export const ContextSelectionStep: FC<SimulationStateSelectorStepProps> = props => {
    const {data: initialData, onBack, onNext, onCancel} = props;
    const {t} = useTranslation();
    const {data, revalidate, isValidating} = useContexts(initialData.searchFilter!);

    const refreshAction = useMemo(() => {
        return buildRefreshAction(revalidate);
    }, [revalidate]);

    const rowActions = useMemo(() => {
        return [
            buildSelectAction(row => {
                onNext({
                    searchFilter: initialData.searchFilter,
                    selectedContext: row,
                });
            }),
        ];
    }, [initialData.searchFilter, onNext]);

    if (isValidating) {
        return <WaitSpinner>{t("Loading")}</WaitSpinner>;
    }

    return (
        <StepContainer onBack={onBack} onCancel={onCancel}>
            <MaterialTable
                columns={columns}
                data={data}
                title={t("Contexts")}
                actions={[refreshAction, ...rowActions]}
            />
        </StepContainer>
    );
};
