import React, {FC} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import {useTranslation} from "react-i18next";
import Tooltip from "@material-ui/core/Tooltip";
import Box from "@material-ui/core/Box";
import {TaggingTypeIcon} from "./taggings";

const useStyles = makeStyles((_theme: Theme) =>
    createStyles({
        icon: {
            width: "15px",
            verticalAlign: "middle",
        },
    })
);
export const TaggingTableTitle: FC<{}> = () => {
    const {t} = useTranslation();
    const classes = useStyles();
    return (
        <Tooltip title={t("Tagging") as string}>
            <Box whiteSpace="nowrap" fontSize="10px" display="inline">
                <TaggingTypeIcon taggingType="milestone" className={classes.icon} />
                /
                <TaggingTypeIcon taggingType="final" className={classes.icon} />
                /
                <TaggingTypeIcon taggingType="tag" className={classes.icon} />
                /
                <TaggingTypeIcon taggingType="metric" className={classes.icon} />
            </Box>
        </Tooltip>
    );
};
