import {useCallback} from "react";
import {CancelToken} from "axios";

import {useNimflow} from "./nimflow-api-provider";
import {ContextType, ContextSimulationState, SimulationEvent, Stages} from "./nimflow-api";

export interface ContextSimulationInfo {
    id: string;
    name: string;
    description?: string;
    contextTypeName?: string;
}

export interface ContextSimulation {
    id?: string;
    name?: string;
    description?: string;
    contextType?: ContextType | undefined;
    contextTypeName?: string | undefined;
    history: ContextSimulationState[];
    initialState?: {[key: string]: any};
    pendingEvents: SimulationEvent[];
}

export interface ExistingEvent {
    event: SimulationEvent;
    status: "pending" | "completed" | "failed";
}

export interface SimulationGetInfos {
    contextTypeName?: string;
}

export const contextSimulationCollectionName = "NimflowContextSimulations";

export const useNimflowSimulation = () => {
    const {
        collections_Aggregate,
        collections_FindById,
        collections_InsertOne,
        collections_DeleteOne,
        collections_Replace,
    } = useNimflow();

    const simulations_GetInfos = useCallback(
        async (query?: SimulationGetInfos, cancelToken?: CancelToken) => {
            let stages: Stages[] = [
                {
                    $project: {
                        _id: false,
                        id: "$_id",
                        name: 1,
                        description: 1,
                        contextTypeName: {
                            $cond: {
                                if: {$eq: ["", "$contextTypeName"]},
                                then: "$contextType.name",
                                else: "$contextTypeName",
                            },
                        },
                    },
                },
                {
                    $sort: {
                        name: 1,
                    },
                },
            ];
            const contextTypeName = query?.contextTypeName;
            if (contextTypeName) {
                stages = [
                    {
                        $match: {
                            $or: [
                                {
                                    contextTypeName,
                                },
                                {
                                    contextType: {
                                        name: contextTypeName,
                                    },
                                },
                            ],
                        },
                    },
                    ...stages,
                ];
            }
            const result: ContextSimulationInfo[] = await collections_Aggregate(
                {
                    collectionName: contextSimulationCollectionName,
                    stages,
                },
                cancelToken
            );
            return result;
        },
        [collections_Aggregate]
    );

    const simulations_FindOne = useCallback(
        async (id: string, cancelToken?: CancelToken) => {
            const result: ContextSimulation = await collections_FindById(
                {
                    collectionName: contextSimulationCollectionName,
                    id,
                },
                cancelToken
            );
            if (!result.id) {
                result.id = (result as any)._id;
            }
            return result;
        },
        [collections_FindById]
    );

    const simulations_InsertOne = useCallback(
        async (simulation: ContextSimulation, cancelToken?: CancelToken) => {
            const result = await collections_InsertOne(
                {
                    collectionName: contextSimulationCollectionName,
                    document: simulation,
                },
                cancelToken
            );
            return result.id;
        },
        [collections_InsertOne]
    );

    const simulations_Replace = useCallback(
        async (simulation: ContextSimulation, cancelToken?: CancelToken) => {
            const result = await collections_Replace(
                {
                    id: simulation.id ?? (simulation as any)._id,
                    collectionName: contextSimulationCollectionName,
                    replacement: simulation,
                },
                cancelToken
            );
            return result;
        },
        [collections_Replace]
    );

    const simulations_Delete = useCallback(
        async (id: string, cancelToken?: CancelToken) => {
            const result = await collections_DeleteOne(
                {
                    collectionName: contextSimulationCollectionName,
                    id,
                },
                cancelToken
            );
            return result;
        },
        [collections_DeleteOne]
    );

    return {
        simulations_GetInfos,
        simulations_FindOne,
        simulations_InsertOne,
        simulations_Replace,
        simulations_Delete,
    };
};
