import {FC, useState, useEffect, useCallback} from "react";
import {useParams, useNavigate} from "react-router";

import {EventListenerEditor} from "./event-listener-editor";
import {useNimflowEventListeners} from "../../api/nimflow-event-listeners";
import {blobStorageUpdatedType, customEventType, GenericEventListener, isContextEventListener} from "../../api/nimflow-api-extensions";

export const EventListenerUpdater: FC<{}> = () => {
    const {id} = useParams();
    const navigate = useNavigate();
    const [eventListener, setEventListener] = useState<GenericEventListener>();
    const {updateEventListener, findEventListener} = useNimflowEventListeners();

    useEffect(() => {
        findEventListener(id).then(s => {
            if (isContextEventListener(s) || s.event === customEventType || s.event === blobStorageUpdatedType) {
                setEventListener(s);
            }
        });
    }, [findEventListener, id]);

    const handleOnChange = useCallback(
        async (value: GenericEventListener) => {
            const result = await updateEventListener(value);
            value = {
                ...value,
                id: result,
            }
            setEventListener(value);
            if (result !== id) {
                setTimeout(() => {
                    navigate(`../${result}`, {replace: true});
                }, 0);
            }
            return true;
        },
        [id, navigate, updateEventListener]
    );

    if (!eventListener) {
        return null;
    }

    return (
        <EventListenerEditor
            value={eventListener}
            onSave={handleOnChange}
        />
    );
};
