import React, {FC, useEffect, useState, useCallback} from "react";
import axios from "axios";
import {useParams, useNavigate} from "react-router";
import {useSnackbar} from "notistack";
import {useTranslation} from "react-i18next";

import {readJsonFromBlob} from "../../utils/io-helpers";
import {useShowErrorInfos} from "../../components/use-show-error-infos";
import {useReportError} from "../../utils/use-report-error";
import {useOkCancelConfirm} from "../../components/confirm";
import {useNimflowDashboards, DashboardDefinition} from "../../api/nimflow-dashboard-api";
import {DashboardDefinitionEditor} from "./dashboard-definition-editor";
import {importDashboardDefinitionFromJson} from "./dashboard-definition-json-importer";

const importDashboardDefinition = async (dataUrl: string) => {
    try {
        const response = await axios.get(dataUrl, {responseType: "blob"});
        const json = await readJsonFromBlob(response.data);
        const importResult = await importDashboardDefinitionFromJson(json);
        return importResult;
    } finally {
        URL.revokeObjectURL(dataUrl);
    }
}

export const DashboardDefinitionImporter: FC<{}> = () => {
    const {t} = useTranslation();
    const {blobUrl} = useParams();
    const navigate = useNavigate();
    const {enqueueSnackbar} = useSnackbar();
    const showErrors = useShowErrorInfos();
    const {dashboards_GetInfos, dashboards_Replace, dashboards_InsertOne} = useNimflowDashboards();
    const [dashboardDefinition, setDashboardDefinition] = useState<DashboardDefinition>();
    const reportErrors = useReportError();
    const confirm = useOkCancelConfirm();

    useEffect(() => {
        const dataUrl = atob(blobUrl);
        if (!dataUrl) {
            return;
        }
        importDashboardDefinition(dataUrl)
            .then(async importResult => {
                if (importResult.type === "success") {
                    setDashboardDefinition(importResult.result); // Pass the raw json without caches.
                    return;
                } else {
                    reportErrors(t("FileIsNotAValidDashboardDefinition"), importResult.errors, {
                        dialogProps: {
                            maxWidth: "lg",
                        },
                    });
                    navigate("../..");
                }
            })
            .catch(() => {
                navigate("../..");
            });
    }, [blobUrl, navigate, enqueueSnackbar, showErrors, t, reportErrors]);

    const handleOnChange = useCallback(
        async (value: DashboardDefinition) => {
            const infos = await dashboards_GetInfos();
            const info = infos.find(s => s.name.toLocaleLowerCase() === value.name.toLocaleLowerCase());
            if (info) {
                const overwrite = await confirm({
                    description: t("ConfirmOverwriteDashboardDashboard", {name: value.name}),
                });
                if (!overwrite) {
                    return;
                }
                value.id = info.id;
                await dashboards_Replace(value);
            } else {
                value.id = await dashboards_InsertOne(value);
            }
            setDashboardDefinition(value);
            navigate(`../../edit/${value.id}`, {replace: true});
        },
        [confirm, dashboards_GetInfos, dashboards_InsertOne, dashboards_Replace, navigate, t]
    );

    if (!dashboardDefinition) {
        return null;
    }

    return <DashboardDefinitionEditor value={dashboardDefinition} onApplyChanges={handleOnChange} />;
};
