import React, {FC, useCallback, useMemo} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import MenuItem from "@material-ui/core/MenuItem";
import {useFormikContext} from "formik";
import {useTranslation} from "react-i18next";

import {autoRefreshIntervals, DashboardDefinition} from "../../api/nimflow-dashboard-api";
import {FormikSwitch} from "../../components/formik-components/formik-switch";
import {FormikTextField} from "../../components/formik-components/formik-text-field";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        textField: {
            width: "40ch",
        },
    })
);

export const DashboardDefinitionAutoRefreshEditor: FC<{}> = () => {
    const {t} = useTranslation();
    const classes = useStyles();
    const {values: dashboardDefinition, setFieldValue} = useFormikContext<DashboardDefinition>();
    const autoRefresh = useMemo(() => {
        return dashboardDefinition.autoRefresh ?? {enabled: false};
    }, [dashboardDefinition.autoRefresh]);

    const handleEnabledChange = useCallback(
        (ev: React.ChangeEvent<HTMLInputElement>) => {
            if (!ev.target.checked) {
                setFieldValue("autoRefresh", {enabled: false});
            }
        },
        [setFieldValue]
    );

    const renderMinIntervalValue = useCallback(
        (value: any): React.ReactNode => {
            if (!autoRefresh.enabled) {
                return "";
            }
            return !value ? t("AllowAllRefreshIntervals") : t(value);
        },
        [autoRefresh.enabled, t]
    );

    const renderDefaultIntervalValue = useCallback(
        (value: any): React.ReactNode => {
            if (!autoRefresh.enabled) {
                return "";
            }
            return !value ? t("Off") : t(value);
        },
        [autoRefresh.enabled, t]
    );

    return (
        <Grid container spacing={3}>
            <Grid item xs={12}>
                <FormikSwitch
                    name="autoRefresh.enabled"
                    label={t(autoRefresh.enabled ? "Enabled" : "Disabled")}
                    onChange={handleEnabledChange}
                />
            </Grid>
            <Grid item xs={12}>
                <FormikTextField
                    name="autoRefresh.minInterval"
                    label={t("MinInterval")}
                    select
                    fullWidth
                    className={classes.textField}
                    InputLabelProps={{shrink: true}}
                    controlValueAdapter={s => (s !== "" ? s : undefined)}
                    disabled={!autoRefresh.enabled}
                    SelectProps={{
                        displayEmpty: true,
                        renderValue: renderMinIntervalValue,
                    }}
                >
                    <MenuItem key="all" value={""}>
                        {t("AllowAllRefreshIntervals")}
                    </MenuItem>
                    {autoRefreshIntervals.map(s => (
                        <MenuItem key={s} value={s}>
                            {t(s)}
                        </MenuItem>
                    ))}
                </FormikTextField>
            </Grid>
            <Grid item xs={12}>
                <FormikTextField
                    name="autoRefresh.defaultInterval"
                    label={t("DefaultInterval")}
                    select
                    fullWidth
                    className={classes.textField}
                    InputLabelProps={{shrink: true}}
                    controlValueAdapter={s => (s !== "" ? s : undefined)}
                    disabled={!autoRefresh.enabled}
                    SelectProps={{
                        displayEmpty: true,
                        renderValue: renderDefaultIntervalValue,
                    }}
                >
                    <MenuItem key="off" value={""}>
                        {t("Off")}
                    </MenuItem>
                    {autoRefreshIntervals.map(s => (
                        <MenuItem key={s} value={s}>
                            {t(s)}
                        </MenuItem>
                    ))}
                </FormikTextField>
            </Grid>
            {/* {JSON.stringify(autoRefresh)} */}
        </Grid>
    );
};
