import React, {FC, useMemo} from "react";
import {useTheme} from "@material-ui/core/styles";
import {Console, Message, Methods, Styles} from "console-feed";

import {JsConsoleEntry} from "../api/nimflow-api";

export interface ConsoleViewerProps {
    entries: JsConsoleEntry[];
}

const lightTheme: Styles = {
    LOG_COLOR: "#000000",
    LOG_ERROR_BACKGROUND: "#FF9999",
    LOG_ERROR_BORDER: '#FF6666',
    LOG_ERROR_COLOR: '#4d0000',
    LOG_WARN_COLOR: '#664100',
    LOG_WARN_BORDER: '#ffe666',
    LOG_WARN_BACKGROUND: '#fff099',
};

const darkTheme: Styles = {

};

export const ConsoleViewer: FC<ConsoleViewerProps> = props => {
    const {entries} = props;
    const theme = useTheme();
    const logs: Message[] = useMemo(() => {
        const result = entries.map((s, i) => ({
            id: i.toString(),
            method: s.type as Methods,
            data: s.values ?? [],
        }));
        return result;
    }, [entries]) as any;
    if (!logs || logs.length === 0) {
        return null;
    }
    const paletteType = theme.palette.type;
    return <Console logs={logs} variant={paletteType} styles={paletteType === "light" ? lightTheme : darkTheme}/>;
};
