import React, {FC, useMemo, useCallback, useRef, createContext} from "react";
import {equals} from "ramda";
import {useTranslation} from "react-i18next";
import {useAsyncFn} from "react-use";
import {isPlainObject} from "is-plain-object";

import {useNimflowFunctions} from "../../api/nimflow-functions-api";

export interface FunctionDataSourceData {
    moduleName: string;
    functionName: string;
    jsArguments?: string;
}

interface FunctionDataSourceDataApi {
    refreshData: (data: FunctionDataSourceData, force: boolean) => Promise<any[]>;
    data?: any[];
    error?: any;
    isRefreshing: boolean;
    idKey: string;
}

const stub = () => {
    throw new Error("Forgot to wrap component in `FunctionDataSourceDataContextProvider`");
};

const FunctionDataSourceDataContextContext = createContext<FunctionDataSourceDataApi>({
    isRefreshing: false,
    refreshData: stub,
    idKey: "",
});

const idKey = "___id";

const initialData: FunctionDataSourceData = {
    moduleName: "",
    functionName: "",
};

export const useFunctionDataSourceData = () => {
    const {t} = useTranslation();
    const {evaluateArgsAndCall} = useNimflowFunctions();
    const dsRef = useRef<FunctionDataSourceData>(initialData);
    const [state, doFetch] = useAsyncFn(
        async (ds: FunctionDataSourceData) => {
            if (!ds.moduleName || !ds.functionName) {
                return [];
            }
            const result = await evaluateArgsAndCall(ds.moduleName, ds.functionName, ds.jsArguments);
            if (!Array.isArray(result) || (result.length > 1 && !isPlainObject(result[0]))) {
                throw new Error(t("FunctionMustReturnAndArrayOfObjects"));
            }
            for (let i = 0; i < result.length; i++) {
                result[i][idKey] = i;
            }
            return result;
        },
        [],
        {
            loading: false,
            value: [],
        }
    );
    const refreshData = useCallback(
        (data: FunctionDataSourceData, force: boolean) => {
            if (equals(dsRef.current, data) && !force) {
                return Promise.resolve(state.value ?? []);
            }
            return doFetch(data);
        },
        [doFetch, state.value]
    );

    const dataContextContext = useMemo(() => {
        const result: FunctionDataSourceDataApi = {
            refreshData,
            data: state.value,
            error: state.error,
            isRefreshing: state.loading,
            idKey,
        };
        return result;
    }, [refreshData, state]);

    return dataContextContext;
}

export const FunctionDataSourceDataContextProvider: FC<{api: FunctionDataSourceDataApi}> = props => {
    const {api, children} = props;

    return (
        <FunctionDataSourceDataContextContext.Provider value={api}>
            {children}
        </FunctionDataSourceDataContextContext.Provider>
    );
};

export const useFunctionDataSourceDataContext = () => React.useContext(FunctionDataSourceDataContextContext);
