import React, {FC, useState, useCallback, useMemo} from "react";
import {useParams, useNavigate} from "react-router";
import Alert from "@material-ui/lab/Alert";
import useSWR from "swr";

import {ContextSimulationContextProvider} from "./simulation-context";
import {ContextSimulationEditor} from "./context-simulation-editor";
import {useNimflow} from "../../api/nimflow-api-provider";
import {SimulationContextTypeProvider, newServerContextTypeProvider} from "./simulation-context-type-provider";
import {ContextType, ApiException} from "../../api/nimflow-api";
import {WaitSpinner} from "../../components/wait-spinner";
import {ContextSimulation, useNimflowSimulation} from "../../api/nimflow-simulation-api";
import {useKey} from "../../utils/use-key";

const useSimulation = (contextTypeName: string) => {
    const {contextTypes_FindOne} = useNimflow();
    const [simulation, setSimulation] = useState<ContextSimulation | undefined>();
    const [contextTypeProvider, setContextTypeProvider] = useState<SimulationContextTypeProvider | undefined>();

    const setContextType = useCallback(
        async (contextType: ContextType) => {
            const simulation: ContextSimulation = {
                pendingEvents: [],
                contextTypeName: contextType.name,
                history: [],
            };
            const provider = await newServerContextTypeProvider(
                contextType.name,
                name => contextTypes_FindOne({name}),
                contextType
            );
            setSimulation(simulation);
            setContextTypeProvider(provider);
        },
        [setSimulation, setContextTypeProvider, contextTypes_FindOne]
    );

    const queryKey = useKey(contextTypes_FindOne, contextTypeName);
    const {isValidating, error} = useSWR(
        queryKey,
        async () => {
            const contextType = await contextTypes_FindOne({name: contextTypeName});
            setContextType(contextType);
            return contextType;
        },
        {revalidateOnFocus: false}
    );

    return useMemo(() => {
        return {
            simulation,
            contextTypeProvider,
            isValidating,
            error,
        };
    }, [simulation, contextTypeProvider, isValidating, error]);
};

export const ContextSimulationCreator: FC<{}> = props => {
    const navigate = useNavigate();
    const {contextTypeName} = useParams();
    const {simulations_InsertOne} = useNimflowSimulation();
    const {simulation, contextTypeProvider, isValidating, error} = useSimulation(contextTypeName);

    const handleOnSaveChanges = useCallback(
        async (value: ContextSimulation) => {
            try {
                const id = await simulations_InsertOne(value);
                navigate(`../../edit/${id}`, {replace: true});
            } catch (e: any) {
                let msg = e.toString();
                if (ApiException.isApiException(e)) {
                    msg = e.message;
                    if ("status" in e) {
                        msg += ` (status: ${e.status})`;
                    }
                }
                return [{message: msg}];
            }
            return [];
        },
        [navigate, simulations_InsertOne]
    );

    if (isValidating) {
        return <WaitSpinner />;
    }

    if (error) {
        return <Alert severity="error">{error.toString()}</Alert>;
    }

    if (!contextTypeProvider) {
        return null;
    }

    return (
        <ContextSimulationContextProvider contextTypeProvider={contextTypeProvider} simulation={simulation}>
            <ContextSimulationEditor onSaveChanges={handleOnSaveChanges} />
        </ContextSimulationContextProvider>
    );
};
