import React, {useCallback} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Button from "@material-ui/core/Button";
import {useTranslation} from "react-i18next";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        list: {
            flexGrow: 1,
            maxHeight: "400",
            overflowY: "auto",
        },
        paper: {
            backgroundColor: theme.palette.background.default,
        },
    })
);

export interface SimplePopupSelectorOption<T> {
    value: T;
    description: string;
    icon?: React.ReactNode;
}

export interface SimplePopupSelectorProps<T> {
    open: boolean;
    options: SimplePopupSelectorOption<T>[];
    title?: string;
    onSelected?: (value: T) => void;
    onClose?: () => void;
}

export const SimplePopupSelector = <T extends any>(props: SimplePopupSelectorProps<T>) => {
    const {open, options, title, onSelected, onClose} = props;
    const classes = useStyles();
    const {t} = useTranslation();

    const handleDataSourceTypeSelected = useCallback(
        (value: T) => {
            onSelected?.(value);
            onClose?.();
        },
        [onClose, onSelected]
    );

    const handleDialogClose = useCallback(async (event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [onClose]);

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            fullWidth
            maxWidth="sm"
            classes={{paper: classes.paper}}
        >
            {!!title && <DialogTitle>{title}</DialogTitle>}
            <DialogContent style={{minWidth: "400px"}}>
                <List className={classes.list}>
                    {options.map(s => (
                        <ListItem key={s.description} onClick={() => handleDataSourceTypeSelected(s.value)} button={true}>
                            <ListItemText primary={s.description} />
                            {!!s.icon && <ListItemIcon>{s.icon}</ListItemIcon>}
                        </ListItem>
                    ))}
                </List>
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
