import {FC, useCallback, useMemo, useState} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import {JSONSchema7} from "json-schema";
import {UISchemaElement} from "@jsonforms/core";

import {ActionEvent, ActionType, ContextType} from "../../api/nimflow-api";
import {getActionJsonSchema} from "../../utils/json-schema";
import {PayloadEditorState, PayloadEditor} from "./payload-editor";
import {getSettings} from "../../api/nimflow-api-extensions";

const useStyles = makeStyles((_theme: Theme) =>
    createStyles({
        root: {
            flexGrow: 1,
            width: "100%",
            //marginTop: "20px",
            marginBottom: "20px",
        },
        textField: {
            width: "100%",
        },
        content: {
            padding: "8px 24px",
            "&:first-child": {
                // dialog without title
                paddingTop: 20,
            },
        },
    })
);

const getActionDataSchema = (actionType?: ActionType, contextType?: ContextType) => {
    if (!actionType || !contextType) {
        return undefined;
    }
    const payloadDefinitionName = actionType.payloadSchemaDefinitionName;
    if (!payloadDefinitionName) {
        return undefined;
    }
    const schema = getActionJsonSchema(contextType, actionType.name);
    return schema;
};

export interface ActionDataEditorState {
    value: ActionEvent;
    hasErrors: boolean;
}

export interface ActionDataEditorProps {
    value: ActionEvent;
    contextType: ContextType;
    onChange?: (state: ActionDataEditorState) => void;
    jsonEditorOpen: boolean;
    onJsonEditorClose?: () => void;
}

export const ActionDataEditor: FC<ActionDataEditorProps> = props => {
    const {value, contextType, onChange, jsonEditorOpen, onJsonEditorClose} = props;
    const classes = useStyles();
    const [actionDataState, setActionDataState] = useState<PayloadEditorState>({
        payload: {
            reference: value.reference,
        },
        hasErrors: false,
    });
    const [payloadState, setPayloadState] = useState<PayloadEditorState>(() => {
        return {
            payload: value.payload,
            hasErrors: false
        };
    });

    const actionType = useMemo(() => {
        return contextType?.actions.find(s => s.name === value.action);
    }, [contextType, value]);

    const actionDataSchema = useMemo(() => {
        const schema: JSONSchema7 = {
            type: "object",
            properties: {
                reference: {
                    type: "string",
                },
            },
            //required: ["reference"],
        };
        return schema;
    }, []);

    const payloadSchema = useMemo(() => {
        return getActionDataSchema(actionType, contextType);
    }, [actionType, contextType]);

    const payloadUischema = useMemo(() => {
        const schema = actionType ? getSettings(actionType)?.uiSchema : undefined;
        if (!schema) {
            return undefined;
        }
        return JSON.parse(schema) as UISchemaElement;
    }, [actionType]);

    const handleActionDataChange = useCallback(
        (state: PayloadEditorState) => {
            setActionDataState(state);
            if (!state || !payloadState) {
                return;
            }
            onChange?.({
                value: {
                    ...value,
                    ...state.payload,
                    payload: payloadState.payload,
                },
                hasErrors: state.hasErrors || payloadState.hasErrors
            });
        },
        [setActionDataState, onChange, value, payloadState]
    );

    const handlePayloadChange = useCallback(
        (state: PayloadEditorState) => {
            setPayloadState(state);
            if (!actionDataState || !state) {
                return;
            }
            onChange?.({
                value: {
                    ...value,
                    ...actionDataState.payload,
                    payload: state.payload,
                },
                hasErrors: actionDataState.hasErrors || state.hasErrors
            });
        },
        [setPayloadState, onChange, value, actionDataState]
    );

    const uiSchemaLibrary = useMemo(() => {
        const uiSchemaStr = getSettings(contextType)?.uiSchemaCommon;
        if (!uiSchemaStr) {
            return undefined;
        }
        return JSON.parse(uiSchemaStr) as Record<string, UISchemaElement>;
    }, [contextType]);

    if (!actionType) {
        return null;
    }

    return (
        <Grid container spacing={2} className={classes.root}>
            <Grid item xs={12} className={classes.content}>
                <PayloadEditor
                    initialPayload={actionDataState?.payload}
                    schema={actionDataSchema}
                    onChange={handleActionDataChange}
                    jsonEditorOpen={false}
                    uiSchemaLibrary={uiSchemaLibrary}
                />
            </Grid>
            <Grid item xs={12} className={classes.content}>
                {!!payloadSchema && (
                    <PayloadEditor
                        initialPayload={payloadState?.payload}
                        schema={payloadSchema}
                        uischema={payloadUischema}
                        onChange={handlePayloadChange}
                        jsonEditorOpen={jsonEditorOpen}
                        onJsonEditorClose={onJsonEditorClose}
                        uiSchemaLibrary={uiSchemaLibrary}
                    />
                )}
            </Grid>
        </Grid>
    );
};
