import { UnpackNestedValue } from "react-hook-form";
import {FieldError} from "react-hook-form/dist/types/errors";
import {Resolver} from "react-hook-form/dist/types/resolvers";
import {DeepMap} from "react-hook-form/dist/types/utils";
import {isPresent} from "ts-is-present";

export const get = (obj: any, path: string) => {
    const result = path
        .split(/[,[\].]+?/)
        .filter(s => !!s)
        .reduce((result, key) => (!isPresent(result) ? result : result[key]), obj);
    return result;
};

export const isMatchFieldArrayName = (name: string, searchName: string) =>
    RegExp(`^${searchName}[\\d+]`.replace(/\[/g, "\\[").replace(/\]/g, "\\]")).test(name);

export const isNameInFieldArray = (names: Set<string>, name: string) =>
    Array.from(names).some(current => isMatchFieldArrayName(name, current));

export const getErrors = <T>(errors: DeepMap<any, FieldError>, path: string): DeepMap<T, FieldError> | undefined => {
    return get(errors, path);
};

export const dynamicResolver = <TFieldValues extends Record<string, any>>(
    resolverSelector: (value: UnpackNestedValue<TFieldValues>) => Resolver<TFieldValues>,
    contextSelector: (value: UnpackNestedValue<TFieldValues>) => object | undefined
): Resolver<TFieldValues> => {
    return async (values, context, validateAllFieldCriteria = false) => {
        const resolver = resolverSelector(values);
        const dynContext = contextSelector(values);
        return resolver(values, Object.assign({}, context, dynContext), validateAllFieldCriteria);
    };
};
