import * as yup from "yup";
import i18next from "i18next";
import type {SchemaOf} from "yup";

import {
    DashboardWidgetDataSource,
    DashboardWidgetVisualization,
    dashboardWidgetAggregateDataSourceSourceCollections,
    dashboardWidgetParameterTypes,
    DashboardWidgetAggregateDataSourceSourceCollection,
    expressionPropertyAggregators,
    QueryExpression,
    getScalarPropertyNamesFor,
    timeGranularities,
    DashboardWidgetDefOrJsSpec,
    isDashboardWidgetJsSpec,
    EntityFilter,
    DashboardWidgetParameter,
    ContextFilter,
    ContextTaskFilter,
    EventFilter,
    CalculateTableExpression,
    CalculateExpression,
    SummarizeExpressionGrouping,
    SummarizeExpressionAggregate,
    SummarizeExpression,
    DashboardWidgetFunctionDataSource,
    DashboardWidgetVegaLiteVisualization,
    DashboardWidgetMarkdownVisualization,
    DashboardWidgetTabularVisualization,
    DashboardWidgetDefinition,
    DashboardWidgetJsSpec,
} from "../../api/nimflow-dashboard-widgets-api";
import {freeVarDefs} from "./free-variable-defs";
import {contextListPropertyNames} from "../../api/nimflow-api-extensions";
import {map} from "../../utils/map-schema";
import {SortDefinition} from "../../api/nimflow-api";

export const DashboardWidgetParameterSchema = yup.object().shape({
    name: yup.string().required().identifier(),
    type: yup
        .string()
        .oneOf([...dashboardWidgetParameterTypes])
        .required(),
    required: yup.boolean().required(),
}) as SchemaOf<DashboardWidgetParameter>;

export const StringFilterSchema = yup.string();
export const StringArrayFilterSchema = yup.array().of(yup.string().required());
export const DateFilterSchema = yup.string().iso8601Formatted();
export const NumberFilterSchema = yup.number();
export const BooleanFilterSchema = yup.boolean();
export const StringMapFilterSchema = map(yup.string().required(), StringFilterSchema);
export const NumberMapFilterSchema = map(yup.string().required(), NumberFilterSchema);
export const DateMapFilterSchema = map(yup.string().required(), DateFilterSchema);

export const ContextFilterSchema = yup.object().shape({
    id: StringFilterSchema,
    typeName: StringFilterSchema,
    reference: StringFilterSchema,
    active: BooleanFilterSchema,
    excludeContextTypeVersion: NumberFilterSchema,
    created: DateFilterSchema,
    modified: DateFilterSchema,
    reachedMilestones: StringArrayFilterSchema,
    milestones: DateMapFilterSchema,
    tags: StringMapFilterSchema,
    metrics: NumberMapFilterSchema,
}) as SchemaOf<ContextFilter>;

export const ContextTaskFilterSchema = yup.object().shape({
    contextId: StringFilterSchema,
    contextTypeName: StringFilterSchema,
    contextReference: StringFilterSchema,
    typeName: StringFilterSchema,
    title: StringFilterSchema,
    assignedTo: StringFilterSchema,
    unassigned: BooleanFilterSchema,
    status: StringFilterSchema,
    archived: BooleanFilterSchema,
    focused: BooleanFilterSchema,
    importance: NumberFilterSchema,
    dueDate: DateFilterSchema,
    labels: StringArrayFilterSchema,
}) as SchemaOf<ContextTaskFilter>;

export const EventFilterSchema = yup.object().shape({
    event: StringFilterSchema,
    timestamp: DateFilterSchema,
    requestTypeName: StringFilterSchema,
    contextTypeName: StringFilterSchema,
    sentBy: StringFilterSchema,
    reference: StringFilterSchema,
    subject: StringFilterSchema,
    contextIsNew: BooleanFilterSchema,
    action: StringFilterSchema,
    message: StringFilterSchema,
    dueDate: DateFilterSchema,
    taskTypeName: StringFilterSchema,
    assignedTo: StringFilterSchema,
    importance: NumberFilterSchema,
    responseTypeName: StringFilterSchema,
    oldImportance: NumberFilterSchema,
    oldLabels: StringArrayFilterSchema,
    oldDueDate: DateFilterSchema,
    oldAssignedTo: StringFilterSchema,
    milestoneName: StringFilterSchema,
    milestoneValue: DateFilterSchema,
    eventName: StringFilterSchema,
    tags: StringMapFilterSchema,
    metrics: NumberMapFilterSchema,
}) as SchemaOf<EventFilter>;

export const resolveEntityFilterSchema = (
    collection: DashboardWidgetAggregateDataSourceSourceCollection
): SchemaOf<EntityFilter<any>> | undefined => {
    switch (collection) {
        case "contexts":
            return ContextFilterSchema.required();
        case "tasks":
            return ContextTaskFilterSchema.required();
        case "events":
            return EventFilterSchema.required();
    }
};

export const SortDefinitionSchema = yup.object().shape({
    fieldName: yup.string().required(),
    direction: yup.number().oneOf([-1, 1]).required(),
}) as SchemaOf<SortDefinition>;

export const getCalculateTableExpressionSchema = (collection: DashboardWidgetAggregateDataSourceSourceCollection) =>
    yup.object().shape({
        type: yup.string().oneOf(["CalculateTable"]).required(),
        filter: yup.string().jsScript(freeVarDefs.params.name, freeVarDefs.queryFilter.name),
        sortProperty: yup.string(),
        sortDirection: yup
            .number()
            .oneOf([-1, 1])
            .when("sortProperty", (sortProperty: string | undefined, schema: any) =>
                !!sortProperty ? schema.required() : schema
            )
            .required(),
        skip: yup.number(),
        limit: yup.number(),
    }) as SchemaOf<CalculateTableExpression>;

export const getCalculateExpressionSchema = (collection: DashboardWidgetAggregateDataSourceSourceCollection) =>
    yup.object().shape({
        type: yup.string().oneOf(["Calculate"]).required(),
        filter: yup.string().jsScript(freeVarDefs.params.name, freeVarDefs.queryFilter.name),
        property: yup
            .string()
            .oneOf(
                getScalarPropertyNamesFor(collection).concat(collection === "contexts" ? contextListPropertyNames : [])
            )
            .when("aggregation", (property: string, schema: any) =>
                property !== "count" ? schema.required() : schema
            ),
        value: yup
            .string()
            .when("property", (property: string, schema: any) =>
                collection === "contexts" && contextListPropertyNames.includes(property as any)
                    ? schema.required()
                    : schema
            ),
        aggregation: yup
            .string()
            .oneOf([...expressionPropertyAggregators])
            .required(),
    }) as SchemaOf<CalculateExpression>;

export const getSummarizeExpressionGrouping = (collection: DashboardWidgetAggregateDataSourceSourceCollection) =>
    yup.object().shape({
        property: yup
            .string()
            .required()
            .oneOf(
                getScalarPropertyNamesFor(collection).concat(collection === "contexts" ? contextListPropertyNames : [])
            ),
        value: yup
            .string()
            .when("property", (property: string, schema: any) =>
                collection === "contexts" && contextListPropertyNames.includes(property as any)
                    ? schema.required()
                    : schema
            ),
        timeGranularity: yup.string().oneOf([...timeGranularities]),
    }) as SchemaOf<SummarizeExpressionGrouping>;

export const getSummarizeExpressionAggregate = (collection: DashboardWidgetAggregateDataSourceSourceCollection) =>
    yup.object().shape({
        name: yup.string().required(),
        aggregation: yup
            .string()
            .required()
            .oneOf([...expressionPropertyAggregators]),
        property: yup
            .string()
            .when("aggregation", (property: string, schema: any) => (property !== "count" ? schema.required() : schema))
            .oneOf(
                getScalarPropertyNamesFor(collection).concat(collection === "contexts" ? contextListPropertyNames : [])
            ),
        value: yup
            .string()
            .when("property", (property: string, schema: any) =>
                collection === "contexts" && contextListPropertyNames.includes(property as any)
                    ? schema.required()
                    : schema
            ),
    }) as SchemaOf<SummarizeExpressionAggregate>;

export const getSummarizeExpressionSchema = (collection: DashboardWidgetAggregateDataSourceSourceCollection) =>
    yup.object().shape({
        type: yup.string().oneOf(["Summarize"]).required(),
        filter: yup.string().jsScript(freeVarDefs.params.name, freeVarDefs.queryFilter.name),
        groupings: yup.array().of(getSummarizeExpressionGrouping(collection).required()).required().min(1),
        aggregates: yup.array().of(getSummarizeExpressionAggregate(collection).required()),
    }) as SchemaOf<SummarizeExpression>;

export const resolveQueryExpressionSchema = (
    query: QueryExpression,
    collection: DashboardWidgetAggregateDataSourceSourceCollection
): SchemaOf<QueryExpression> | undefined => {
    switch (query.type) {
        case "CalculateTable":
            return getCalculateTableExpressionSchema(collection).required();
        case "Calculate":
            return getCalculateExpressionSchema(collection).required();
        case "Summarize":
            return getSummarizeExpressionSchema(collection).required();
    }
};

export const getQueryExpressionSchema = (collection: DashboardWidgetAggregateDataSourceSourceCollection) => {
    return yup.lazy<SchemaOf<QueryExpression>>((s: QueryExpression): any => {
        if (!s) {
            return yup.mixed();
        }
        const result = resolveQueryExpressionSchema(s, collection);
        if (result) {
            return result;
        }
        return yup.mixed().test({
            test: function (val: any) {
                return this.createError({
                    path: this.path,
                    message: i18next.t("InvalidQueryExpressionType", {type: val.type}),
                });
            },
        });
    }) as unknown as SchemaOf<QueryExpression>;
};

export const DashboardWidgetQueryDataSourceSchema = yup.object().shape({
    type: yup.string().oneOf(["query"]).required(),
    name: yup.string().required().identifier(),
    sourceCollection: yup
        .string()
        .oneOf([...dashboardWidgetAggregateDataSourceSourceCollections])
        .required(),
    contextTypeName: yup.string(),
    query: yup
        .mixed<QueryExpression>()
        .when("sourceCollection", (sourceCollection: DashboardWidgetAggregateDataSourceSourceCollection) =>
            getQueryExpressionSchema(sourceCollection)
        ),
}); // as SchemaOf<DashboardWidgetQueryDataSource>;

export const DashboardWidgetFunctionDataSourceSchema = yup.object().shape({
    type: yup.string().oneOf(["function"]).required(),
    name: yup.string().required(),
    moduleName: yup.string().required(),
    functionName: yup.string().required(),
    jsArguments: yup.string(),
}) as SchemaOf<DashboardWidgetFunctionDataSource>;

export const resolveDashboardWidgetDataSourceSchema = (
    s: DashboardWidgetDataSource
): SchemaOf<DashboardWidgetDataSource> | undefined => {
    switch (s.type) {
        case "query":
            return DashboardWidgetQueryDataSourceSchema.required();
        case "function":
            return DashboardWidgetFunctionDataSourceSchema.required();
    }
    return undefined;
};

export const DashboardWidgetDataSourceSchema = yup.lazy<SchemaOf<DashboardWidgetDataSource>>(
    (s: DashboardWidgetDataSource): any => {
        if (!s) {
            return yup.mixed();
        }
        const result = resolveDashboardWidgetDataSourceSchema(s);
        if (result) {
            return result;
        }
        return yup.mixed().test({
            test: function (val: any) {
                return this.createError({
                    path: this.path,
                    message: i18next.t("InvalidDashboardWidgetDataSourceType", {type: val.type}),
                });
            },
        });
    }
) as unknown as SchemaOf<DashboardWidgetDataSource>; // as yup.ObjectSchema<yup.Shape<object | undefined, DashboardWidgetDataSource>>;

export const DashboardWidgetVegaLiteVisualizationSchema = yup.object().shape({
    type: yup.string().oneOf(["vega-lite"]).required(),
    jsSpecification: yup.string().required().jsScript(freeVarDefs.params.name, freeVarDefs.vegaLiteSpec.name),
    variant: yup
        .string()
        .oneOf([
            "verticalBarChart",
            "focusedVerticalBarChart",
            "horizontalBarChart",
            "lineChart",
            "areaChart",
            "areaLineChart",
            "pieChart",
            "donutChart",
            "scatterplotChart",
        ])
        .optional(),
}) as SchemaOf<DashboardWidgetVegaLiteVisualization>;

export const DashboardWidgetMarkdownVisualizationSchema = yup.object().shape({
    type: yup.string().oneOf(["markdown"]).required(),
    template: yup.string().required(),
}) as SchemaOf<DashboardWidgetMarkdownVisualization>;

export const DashboardWidgetTabularVisualizationSchema = yup.object().shape({
    type: yup.string().oneOf(["tabular"]).required(),
    jsConfiguration: yup.string().required(),
}) as SchemaOf<DashboardWidgetTabularVisualization>;

export const resolveDashboardWidgetVisualizationSchema = (
    s: DashboardWidgetVisualization
): SchemaOf<DashboardWidgetVisualization> | undefined => {
    switch (s.type) {
        case "markdown":
            return DashboardWidgetMarkdownVisualizationSchema.required();
        case "vega-lite":
            return DashboardWidgetVegaLiteVisualizationSchema.required();
        case "tabular":
            return DashboardWidgetTabularVisualizationSchema.required();
    }
    return undefined;
};

export const DashboardWidgetVisualizationSchema = yup.lazy<SchemaOf<DashboardWidgetVisualization>>(
    (s: DashboardWidgetVisualization): any => {
        if (!s) {
            return yup.mixed();
        }
        const result = resolveDashboardWidgetVisualizationSchema(s);
        if (result) {
            return result;
        }
        return yup.mixed().test({
            test: function (val: any) {
                return this.createError({
                    path: this.path,
                    message: i18next.t("InvalidDashboardWidgetVisualizationType", {type: val.type}),
                });
            },
        });
    }
) as unknown as SchemaOf<DashboardWidgetVisualization>;

export const DashboardWidgetDefinitionSchema = yup.object().shape({
    id: yup.string(),
    name: yup.string().required().identifier(),
    title: yup.string().required(),
    description: yup.string(),
    parameters: yup
        .array()
        .of(DashboardWidgetParameterSchema.required())
        .uniqueBy(
            s => s.name,
            ({duplicates}) => i18next.t("DuplicateParameterNames", {names: duplicates.join(", ")})
        ),
    dataSources: yup
        .array()
        .of(DashboardWidgetDataSourceSchema)
        .uniqueBy(
            s => s?.name,
            ({duplicates}) => i18next.t("DuplicateDataSourceNames", {names: duplicates.join(", ")})
        ),
    visualization: DashboardWidgetVisualizationSchema,
}) as SchemaOf<DashboardWidgetDefinition>;

export const DashboardWidgetJsSpecSchema = yup.object().shape({
    id: yup.string(),
    name: yup.string().required().identifier(),
    title: yup.string().required(),
    description: yup.string(),
    widgetJsSpec: yup.string().required().jsScript(),
}) as SchemaOf<DashboardWidgetJsSpec>;

export const resolveDashboardWidgetDefOrJsSpecSchema = (
    s: DashboardWidgetDefOrJsSpec
): SchemaOf<DashboardWidgetDefOrJsSpec> => {
    if (isDashboardWidgetJsSpec(s)) {
        return DashboardWidgetJsSpecSchema.required();
    }
    return DashboardWidgetDefinitionSchema.required();
};
