import Button from "@material-ui/core/Button";
import {FC, useCallback, useState} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";

import {IconSelectorDialog} from "./icon-selector-dialog";
import {MaterialFontIcon} from "../material-font-icon";
import {IconSpec} from "../../api/nimflow-api-extensions";
import { useTranslation } from "react-i18next";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            margin: 10,
        },
        icon: {
            verticalAlign: "text-bottom",
            marginRight: 10
        },
    })
);

export interface IconSelectorProps {
    value?: IconSpec;
    onChange: (value: IconSpec | undefined) => void;
}

export const IconSelector: FC<IconSelectorProps> = props => {
    const {value, onChange} = props;
    const {t} = useTranslation();
    const [selecting, setSelecting] = useState(false);
    const classes = useStyles();

    const handleClose = useCallback(() => setSelecting(false), []);

    return (
        <div className={classes.root}>
            {!!value && (
                <MaterialFontIcon family={value.family} className={classes.icon}>
                    {value.name}
                </MaterialFontIcon>
            )}
            <Button onClick={() => setSelecting(true)}>{t("Select")}</Button>
            <Button onClick={() => onChange?.(undefined)}>{t("Remove")}</Button>
            {selecting && <IconSelectorDialog open={selecting} onChange={onChange} onClose={handleClose} />}
        </div>
    );
};
