import React from "react";
import {Controller, Control} from "react-hook-form";
import {ControllerProps} from "react-hook-form/dist/types/props";
import Autocomplete, {AutocompleteProps} from "@material-ui/lab/Autocomplete";

export type AutocompleteControllerProps<
    T,
    Multiple extends boolean | undefined,
    DisableClearable extends boolean | undefined,
    FreeSolo extends boolean | undefined,
    TControl extends Control<Record<string, any>> = Control<Record<string, any>>
> = {} & Omit<
    ControllerProps<React.ComponentClass<AutocompleteProps<T, Multiple, DisableClearable, FreeSolo>>, TControl>,
    "as" | "render"
>;

export const AutocompleteController = <
    T,
    Multiple extends boolean | undefined,
    DisableClearable extends boolean | undefined,
    FreeSolo extends boolean | undefined,
    TControl extends Control<Record<string, any>> = Control<Record<string, any>>
>(
    props: AutocompleteControllerProps<T, Multiple, DisableClearable, FreeSolo, TControl>
) => {
    const {name, rules, onChange: outerOnChange, onFocus, defaultValue, control, ...rest} = props;
    return (
        <Controller
            render={({onChange, onBlur, value}) => (
                <Autocomplete
                    {...rest}
                    value={value ?? null}
                    onChange={(event, value, reason, details) => {
                        onChange(value === null ? undefined : value);
                        outerOnChange?.(event, value, reason, details);
                    }}
                    onBlur={onBlur}
                />
            )}
            control={control}
            name={name}
            rules={rules}
            onFocus={onFocus}
            defaultValue={defaultValue}
        />
    );
};
