import React, {FC, useState, useMemo, useCallback} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import RefreshIcon from "@material-ui/icons/Refresh";
import TranslateIcon from "@material-ui/icons/Translate";
import DeleteIcon from "@material-ui/icons/Delete";
import Alert from "@material-ui/lab/Alert";
import AddIcon from "@material-ui/icons/Add";
import * as yup from "yup";
import {useTranslation} from "react-i18next";
import {Action, Localization} from "@material-table/core";
import {sortBy} from "ramda";
import i18next from "i18next";

import {getCodes, LangCode} from "./lang-helpers";
import {BaseTable, BaseTableProps, BaseColumn, BaseEditComponentProps} from "../../components/base-table";
import {validateWithYupSchema} from "../../libs/yup";
import {ErrorInfo} from "../../models/error-info";
import {LocaleSelector} from "./locale-selector";
import {useTranslationEditionModel, TranslationEntry} from "./use-translation-edition-model";
import {DeleteLocalizationConfirmationDialog} from "./delete-localization-confirmation-dialog";
import {ErrorBoundary} from "../../components/error-boundary";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        subicon: {
            position: "absolute",
            fontSize: 14,
            top: 10,
            right: 5,
        },
    })
);

const AddLanguageIcon: FC<{}> = () => {
    const classes = useStyles();
    return (
        <>
            <TranslateIcon />
            <AddIcon className={classes.subicon} />
        </>
    );
};

const translationEntriesKey = "translationEntries";
const translationEntryKey = "translationEntry";

export const TranslationEntrySchema = yup.object().shape({
    key: yup
        .string()
        .required()
        .identifier()
        .test({
            message: ({value}) => i18next.t("KeyAlreadyExists", {name: value}),
            test: function (value: any) {
                const entry: TranslationEntry | undefined = (this.options.context as any)?.[translationEntryKey];
                if (!entry || !!entry.id) {
                    return true;
                }
                const entries: TranslationEntry[] | undefined = (this.options.context as any)?.[translationEntriesKey];
                if (!entries) {
                    return true;
                }
                if (entries.find(s => s.key === value && s !== this.parent)) {
                    return this.createError(i18next.t("KeyAlreadyExists", {name: value}));
                }
                return true;
            },
        })
        .test({
            message: ({value}) => i18next.t("AtLeastOneTranslationNeeded", {name: value}),
            test: function (value: any) {
                const entry: TranslationEntry | undefined = (this.options.context as any)?.[translationEntryKey];
                if (!entry) {
                    return true;
                }
                const setKeys = Object.keys(entry).filter(s => s !== "key" && s !== "id" && !!entry[s]);
                if (setKeys.length === 0) {
                    return this.createError(i18next.t("AtLeastOneTranslationNeeded", {name: value}));
                }
                return true;
            },
        }),
});

const inferColumns = (
    languages: string[],
    deleteLocalization: (language: string) => Promise<any>
): BaseColumn<TranslationEntry>[] => {
    const codes = getCodes();
    const langCols: BaseColumn<TranslationEntry>[] = languages.map(s => {
        const code = codes.find(c => c.code === s);
        let title = s;
        if (code) {
            title += ` (${code.names.result})`;
        }
        return {
            field: s,
            title: (
                <>
                    <span>{title}</span>
                    <Tooltip title={i18next.t("RemoveLocalization") as string}>
                        <IconButton size="small" onClick={() => deleteLocalization(s)}>
                            <DeleteIcon fontSize="small" />
                        </IconButton>
                    </Tooltip>
                </>
            ),
            editPlaceholder: title,
        };
    });
    return [
        {
            field: "key",
            title: i18next.t("Key"),
            editable: "onAdd",
            validateValue: async (key, _oldKey, rowData, data): Promise<ErrorInfo[]> => {
                const result = await validateWithYupSchema(TranslationEntrySchema.fields?.key, key, {
                    context: {
                        [translationEntriesKey]: data,
                        [translationEntryKey]: rowData,
                    },
                });
                return result;
            },
            editComponent: ({value, onChange, errorMessage}: BaseEditComponentProps<TranslationEntry>) => (
                <TextField
                    defaultValue={value}
                    onChange={e => onChange(e.target.value)}
                    autoFocus
                    placeholder={i18next.t("Key")}
                    error={!!errorMessage}
                    helperText={errorMessage}
                />
            ),
        },
        ...sortBy(s => s.title as string, langCols),
    ];
};

const buildAddLocalizationAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: () => <AddLanguageIcon />,
        tooltip: i18next.t("AddLocalization"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const buildRefreshAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: RefreshIcon,
        tooltip: i18next.t("RefreshData"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const localization: Localization = {
    body: {
        addTooltip: i18next.t("AddTranslation"),
    },
};

export const TranslationEditor: FC<{}> = props => {
    const [selectingLocale, setSelectingLocale] = useState(false);
    const {t} = useTranslation();
    const [deletingLocalization, setDeletingLocalization] = useState<string>();
    const {
        isLoading,
        error,
        data,
        languages,
        addLanguage,
        removeLanguage,
        addTranslation,
        updateTranslation,
        removeTranslation,
        refresh,
    } = useTranslationEditionModel();

    const deleteLocalization = useCallback(
        async (language: string) => {
            setDeletingLocalization(language);
        },
        [setDeletingLocalization]
    );

    const columns = useMemo(() => {
        return inferColumns(languages, deleteLocalization);
    }, [languages, deleteLocalization]);

    const refreshAction = useMemo(() => {
        return buildRefreshAction(refresh);
    }, [refresh]);

    const addLocalizationAction = useMemo(() => {
        return buildAddLocalizationAction(() => {
            setSelectingLocale(true);
        });
    }, []);

    const tableEditionHandler = useMemo(() => {
        const tableEditionHandler: BaseTableProps<TranslationEntry>["editable"] = {
            onRowAdd: columns.length > 1 ? addTranslation : undefined,
            onRowUpdate: updateTranslation,
            onRowDelete: removeTranslation,
        };
        return tableEditionHandler;
    }, [addTranslation, columns.length, removeTranslation, updateTranslation]);

    const handleOnCloseLocaleSelector = useCallback(() => {
        setSelectingLocale(false);
    }, [setSelectingLocale]);

    const handleLocaleSelected = useCallback(
        async (locale: LangCode) => {
            addLanguage(locale.code);
        },
        [addLanguage]
    );

    const handleOnCloseConfirm = useCallback(() => {
        setDeletingLocalization(undefined);
    }, [setDeletingLocalization]);

    const handleDeleteLocalization = useCallback(
        async (language: string) => {
            await removeLanguage(language);
        },
        [removeLanguage]
    );

    const actions = useMemo(() => {
        return [addLocalizationAction, refreshAction];
    }, [addLocalizationAction, refreshAction]);

    return (
        <ErrorBoundary>
            <BaseTable
                columns={columns}
                data={data ?? []}
                title={t("Localizations")}
                editable={tableEditionHandler}
                actions={actions}
                localization={localization}
            />
            {!isLoading && !!error && <Alert severity="error">{error.toString()}</Alert>}
            {selectingLocale && (
                <LocaleSelector
                    open={selectingLocale}
                    onClose={handleOnCloseLocaleSelector}
                    onSelected={handleLocaleSelected}
                />
            )}
            {!!deletingLocalization && (
                <DeleteLocalizationConfirmationDialog
                    open={!!deletingLocalization}
                    language={deletingLocalization}
                    onClose={handleOnCloseConfirm}
                    onConfirm={handleDeleteLocalization}
                />
            )}
        </ErrorBoundary>
    );
};
