import React, {useMemo, useCallback} from "react";
import TextField from "@material-ui/core/TextField";
import * as yup from "yup";

import {useTranslation} from "react-i18next";
import {BaseTable, BaseTableProps, BaseColumn, BaseEditComponentProps} from "./base-table";
import { validateWithYupSchema } from "../libs/yup";

export interface MapEditorRow<T = any> {
    key: string;
    value: T;
}

export type MapEditorColumn<T> = {} & Omit<BaseColumn<MapEditorRow<T>>, "field">;

export type MapEditorProps<T = any> = {
    value: Record<string, T>;
    keyColumn: MapEditorColumn<T>;
    valueColumn: MapEditorColumn<T>;
    keySchema?: yup.StringSchema;
    valueSchema?: yup.BaseSchema<T>;
    editable?: {
        isEditable?: (key: string, value: T) => boolean;
        isDeletable?: (key: string, value: T) => boolean;
        onFieldAdd?: (key: string, value: T) => Promise<any>;
        onFieldUpdate?: (key: string, value: T, oldKey?: string, oldValue?: T) => Promise<any>;
        onFieldDelete?: (key: string, value: T) => Promise<any>;
        editTooltip?: (key: string, value: T) => string;
        deleteTooltip?: (key: string, value: T) => string;
        onFieldAddCancelled?: () => void;
        onFieldUpdateCancelled?: () => void;
        isEditHidden?: (key: string, value: T) => boolean;
        isDeleteHidden?: (key: string, value: T) => boolean;
    };
    onBuildColumns?: (defaultColumns: BaseColumn<MapEditorRow<T>>[]) => BaseColumn<MapEditorRow<T>>[];
} & Omit<BaseTableProps<MapEditorRow<T>>, "columns" | "data" | "editable">;

const defaultKeyColumn: MapEditorColumn<any> = {
    editComponent: ({value, onChange, errorMessage}: BaseEditComponentProps<MapEditorRow<any>>) => (
        <TextField
            type="text"
            autoFocus
            defaultValue={value}
            onChange={e => onChange(e.target.value)}
            error={!!errorMessage}
            helperText={errorMessage}
        />
    ),
};

const defaultOptions: MapEditorProps<any>["options"] = {
    paging: false,
    search: false,
    draggable: false,
};

export function MapEditor<T = any>(props: MapEditorProps<T>): React.ReactElement {
    const {keyColumn, valueColumn, value, editable, keySchema, valueSchema, options, onBuildColumns, ...rest} = props;
    const {t} = useTranslation();
    const opts: MapEditorProps<T>["options"] = useMemo(() => ({
        ...defaultOptions,
        ...options,
    }), [options]);
    
    const data: MapEditorRow<T>[] = useMemo(() => {
        if (!value) {
            return [];
        }
        return Object.keys(value)
            .sort()
            .map(key => ({key, value: value[key], error: {}}));
    }, [value]);

    const getKeyValidationSchema = useCallback(
        (value: any, oldKey: any, rowData: MapEditorRow<T>, data: MapEditorRow<T>[]) => {
            return (keySchema ?? yup.string()).required().test("notDuplicated", "", function (key: any) {
                if (!key) {
                    return true;
                }
                if (oldKey === key) {
                    // Key did not change, so it must be still valid
                    return true;
                }
                const dup = data.find(s => s.key === key);
                if (dup) {
                    return this.createError({path: this.path, message: t("KeyAlreadyExists", {key})});
                }
                return true;
            });
        },
        [keySchema, t]
    );
    const columns = useMemo(() => {
        let cols: BaseColumn<MapEditorRow<T>>[] = [
            {
                ...defaultKeyColumn,
                ...keyColumn,
                field: "key",
                validateValue: async (value, oldValue, rowData, data) => {
                    return await validateWithYupSchema(getKeyValidationSchema(value, oldValue, rowData, data), value, {
                        context: {
                            oldValue,
                            rowData,
                        },
                    });
                },
            },
            {
                ...valueColumn,
                field: "value",
                validateValue: valueSchema
                    ? async (value, oldValue, rowData) => {
                          return await validateWithYupSchema(valueSchema, value, {
                              context: {
                                  oldValue,
                                  rowData,
                              },
                          });
                      }
                    : undefined,
            },
        ];
        if (onBuildColumns) {
            cols = onBuildColumns(cols);
            if (cols.every(s => s.field !== "key")) {
                throw new Error("Field 'key' missing");
            }
            if (cols.every(s => s.field !== "value")) {
                throw new Error("Field 'value' missing");
            }
        }
        return cols;
    }, [keyColumn, valueColumn, getKeyValidationSchema, valueSchema, onBuildColumns]);

    const tableEditionHandler = useMemo(() => {
        const tableEditionHandler: BaseTableProps<MapEditorRow<T>>["editable"] = {
            onRowAdd: async newData => {
                if (editable?.onFieldAdd) {
                    await editable.onFieldAdd(newData.key, newData.value);
                }
            },
            onRowUpdate: async (newData, oldData) => {
                await editable?.onFieldUpdate?.(newData.key, newData.value, oldData?.key, oldData?.value);
            },
            onRowDelete: async oldData => {
                await editable?.onFieldDelete?.(oldData.key, oldData.value);
            },
            onRowAddCancelled: () => {
                editable?.onFieldAddCancelled?.();
            },
            onRowUpdateCancelled: () => {
                editable?.onFieldUpdateCancelled?.();
            },
            isEditable: rowData => {
                return editable?.isEditable?.(rowData?.key, rowData?.value) ?? true;
            },
            isEditHidden: rowData => {
                return editable?.isEditHidden?.(rowData?.key, rowData?.value) ?? false;
            },
            isDeletable: rowData => {
                return editable?.isDeletable?.(rowData?.key, rowData?.value) ?? true;
            },
            isDeleteHidden: rowData => {
                return editable?.isDeleteHidden?.(rowData?.key, rowData?.value) ?? false;
            },
            deleteTooltip: editable?.deleteTooltip
                ? rowData => {
                      return editable.deleteTooltip!(rowData?.key, rowData?.value);
                  }
                : undefined,
            editTooltip: editable?.editTooltip
                ? rowData => {
                      return editable.editTooltip!(rowData?.key, rowData?.value);
                  }
                : undefined,
        };
        return tableEditionHandler;
    }, [editable]);

    return <BaseTable data={data} editable={tableEditionHandler} columns={columns} options={opts} {...rest} />;
}
