import {FC, useMemo, useCallback} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import {useFormContext} from "react-hook-form";
import i18next from "i18next";
import {Components} from "@material-table/core";

import {ActionType} from "../../api/nimflow-api";
import {JsExpressionEditor} from "../../components/jsexpression-editor";
import {MapEditorColumn} from "../../components/map-editor";
import {ActionTypeJsSetterSchema, jsCommonScriptKey} from "../../models/context-type-schema";
import {RhfObjectFieldEditor} from "../../components/rhf-object-field-editor";
import {ErrorInfo} from "../../models/error-info";
import {useContextTypeContext} from "./context-type-context";
import {validateWithYupSchema} from "../../libs/yup";
import {freeVarDefs} from "./free-variable-defs";
import {FormErrorViewer} from "../../components/form-error-viewer";
import {useEditionScopeCanAcceptVoting} from "../../components/edition-scope";

const useStyles = makeStyles((_theme: Theme) =>
    createStyles({
        textField: {
            width: "40ch",
        },
    })
);

const keySchema = ActionTypeJsSetterSchema.key;
const valueSchema = ActionTypeJsSetterSchema.value;
const freeVariables = [freeVarDefs.state, freeVarDefs.payload];
export interface ActionTypeSettersEditorProps {
    path: string;
    defaultValue?: Partial<Record<string, string>>;
}

const tableComponents: Components = {
    Container: props => <div {...props} />,
};

export const ActionTypeSettersEditor: FC<ActionTypeSettersEditorProps> = props => {
    const {path, defaultValue} = props;
    const {errors} = useFormContext<ActionType>();
    const classes = useStyles();
    const {jsCommon} = useContextTypeContext();
    const vote = useEditionScopeCanAcceptVoting({
        voteIdPrefix: "ActionTypeSettersEditor",
    });
    const jsValidator = useCallback(
        (js: string | undefined): Promise<ErrorInfo[]> => {
            return validateWithYupSchema(valueSchema, js, {
                context: {[jsCommonScriptKey]: jsCommon},
            });
        },
        [jsCommon]
    );

    const [keyColumn, valueColumn] = useMemo(() => {
        const keycol: MapEditorColumn<string | undefined> = {
            title: i18next.t("Key"),
            width: "20ch",
        };
        const valuecol: MapEditorColumn<string | undefined> = {
            title: i18next.t("Expression"),
            width: "40ch",
            editComponent: ({value, onChange, errorMessage}) => (
                <JsExpressionEditor
                    defaultValue={value}
                    onChange={onChange}
                    className={classes.textField}
                    error={!!errorMessage}
                    helperText={errorMessage}
                    validator={jsValidator}
                    freeVariables={freeVariables}
                />
            ),
        };
        return [keycol, valuecol];
    }, [classes.textField, jsValidator]);

    const handleEditionStateChanged = useCallback(
        editing => {
            vote(!editing);
        },
        [vote]
    );

    return (
        <>
            <RhfObjectFieldEditor<string | undefined>
                path={path}
                defaultValue={defaultValue}
                keyColumn={keyColumn}
                keySchema={keySchema}
                valueColumn={valueColumn}
                valueSchema={valueSchema}
                title=" "
                components={tableComponents}
                onEditionStateChanged={handleEditionStateChanged}
            />
            <FormErrorViewer errors={errors} names={path} />
        </>
    );
};
