import React, {FC, useCallback, useMemo} from "react";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";
import Chip from "@material-ui/core/Chip";
import EditIcon from "@material-ui/icons/Edit";
import CancelIcon from "@material-ui/icons/Cancel";
import DeleteIcon from "@material-ui/icons/Delete";
import AssignmentTurnedInIcon from "@material-ui/icons/AssignmentTurnedIn";
import ReplyIcon from "@material-ui/icons/Reply";
import {DiagramEngine} from "@projectstorm/react-diagrams-core";

import {ResponseType} from "../../../api/nimflow-api";
import {TaskTypeNodeModel} from "./task-type-node-model";
import {ContextTypeNodePanel, ContextTypeNodePanelAction} from "./context-type-node-panel";
import {ContextTypePortWidget} from "./context-type-port-widget";
import {ContextTypeNodeAvatar} from "./context-type-node-avatar";
import {SvgIconProps} from "@material-ui/core/SvgIcon";
import {DeleteResponseCommand, EditResponseCommand} from "./context-type-node-model";

export interface TaskTypeNodeWidgetProps {
    node: TaskTypeNodeModel;
    engine: DiagramEngine;
}

const responsePortSize = 8;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        responsePort: {
            top: `calc(50% - ${responsePortSize / 2}px)`,
            right: -responsePortSize,
            position: "absolute",
        },
        response: {
            position: "relative",
            marginBottom: 5,
        },
        responseChipRoot: {
            width: "100%",
            backgroundColor: theme.palette.background.paper,
        },
        responseChipLabel: {
            width: "100%",
        },
    })
);

const ResponseActions: FC<SvgIconProps> = props => {
    return (
        <>
            <EditIcon data-action-type="edit" {...props} />
            <CancelIcon data-action-type="delete" {...props} />
        </>
    );
};

export const TaskTypeNodeWidget: FC<TaskTypeNodeWidgetProps> = props => {
    const {engine, node} = props;
    const classes = useStyles(props);
    const actions = useMemo(() => {
        const actions: ContextTypeNodePanelAction[] = [
            {
                icon: <EditIcon />,
                onClick: () => {
                    node.dispatchCommand({
                        type: "edit",
                    });
                },
            },
            {
                icon: <DeleteIcon />,
                onClick: () => {
                    node.dispatchCommand({
                        type: "delete",
                    });
                },
            },
        ];
        return actions;
    }, [node]);

    const handleResponseAction = useCallback(
        (event: React.MouseEvent<SVGElement, MouseEvent>, response: ResponseType) => {
            let tgt = event.target as SVGElement;
            while (tgt && tgt.tagName !== "svg") {
                tgt = tgt.parentNode as SVGElement;
            }
            if (!tgt) {
                return;
            }
            const actionType = tgt.getAttribute("data-action-type");
            if (!actionType) {
                return;
            }
            if (actionType === "edit") {
                node.dispatchCommand({
                    type: "editResponse",
                    responseName: response.name,
                } as EditResponseCommand);
            } else if (actionType === "delete") {
                node.dispatchCommand({
                    type: "deleteResponse",
                    responseName: response.name,
                } as DeleteResponseCommand);
            }
        },
        [node]
    );

    return (
        <ContextTypeNodePanel
            engine={engine}
            node={node}
            avatar={
                <ContextTypeNodeAvatar>
                    <AssignmentTurnedInIcon />
                </ContextTypeNodeAvatar>
            }
            actions={actions}
            selected={node.isSelected()}
        >
            {node.instance.validResponses?.map(response => {
                const port = node.getResponsePort(response);
                return (
                    <div key={response.name} className={classes.response}>
                        <Chip
                            icon={<ReplyIcon />}
                            label={response?.name}
                            onDelete={event => handleResponseAction(event, response)}
                            deleteIcon={<ResponseActions />}
                            variant="outlined"
                            classes={{
                                root: classes.responseChipRoot,
                                label: classes.responseChipLabel,
                            }}
                        />
                        {port && (
                            <ContextTypePortWidget
                                port={port}
                                engine={engine}
                                portSize={responsePortSize}
                                className={classes.responsePort}
                            />
                        )}
                    </div>
                );
            })}
        </ContextTypeNodePanel>
    );
};
