import {FC, useState, useCallback, useMemo} from "react";
import {useTranslation} from "react-i18next";
import {useNavigate} from "react-router";
import * as yup from "yup";

import {UserDefinedFunction} from "../../api/nimflow-api";
import {useNimflowFunctions} from "../../api/nimflow-functions-api";
import {UdFunctionEditor} from "./ud-function-editor";
import {UserDefinedFunctionSchema} from "./ud-function-schema";

const createUdFunction = () => {
    const result: UserDefinedFunction = {
        id: "newFn",
        version: 0,
        name: "",
        script: `/** @type {typeof common.exportedFn}*/
export default function(args) {

};`,
    };
    return result;
};

export const UdFunctionCreator: FC<{}> = () => {
    const navigate = useNavigate();
    const {t} = useTranslation();
    const {addUdFunction, searchUdFunctions} = useNimflowFunctions();
    const [udFunction, setUdFunction] = useState<UserDefinedFunction>(() => {
        return createUdFunction();
    });

    const handleOnChange = useCallback(
        async (value: UserDefinedFunction) => {
            const result = await addUdFunction(value);
            setUdFunction(result);
            setTimeout(() => {
                navigate(`../edit/${result.id}`, {replace: true});
            }, 0);
            return true;
        },
        [addUdFunction, navigate]
    );

    const schema = useMemo(
        () =>
            UserDefinedFunctionSchema.shape({
                name: UserDefinedFunctionSchema.fields!.name.concat(
                    yup
                        .string()
                        .defined()
                        .test({
                            message: ({value}) => t("UserDefinedFunctionNameAlreadyExists", {name: value}),
                            test: async function (value: any) {
                                if (!value) {
                                    return true;
                                }
                                const fns = await searchUdFunctions({name: value});
                                return fns.filter(s => s.name === value).length === 0;
                            },
                        })
                ),
            }),
        [searchUdFunctions, t]
    );

    return <UdFunctionEditor value={udFunction} schema={schema} onSave={handleOnChange} />;
};
