interface CombinablePropertyEditorProps {
    isCombined: boolean;
    onSwitch?: (combine: boolean) => void;
}

const CombinablePropertyEditor: FC<CombinablePropertyEditorProps> = props => {
    const {isCombined, onSwitch, children} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const childrenClone = useMemo(
        () =>
            !children || typeof children !== "object" || !("type" in children)
                ? children
                : React.cloneElement(children),
        [children]
    );
    return (
        <div className={classes.combinableTable}>
            <div className={classes.combinableChildren}>{isCombined ? children : childrenClone}</div>
            <div className={classes.combinableButton}>
                <Tooltip title={t(isCombined ? "Separate" : "Unify") as string}>
                    <IconButton onClick={() => onSwitch?.(!isCombined)}>
                        {isCombined ? <LinkIcon /> : <LinkOffIcon />}
                    </IconButton>
                </Tooltip>
            </div>
        </div>
    );
};

interface DashboardDefinitionLayoutMenuProps {
    selectedLayout: number;
    onLayoutSelected?: (index: number) => void;
}

const cloneLayout = (layouts: DashboardResponsiveLayout[], index: number) => {
    const sourceLayout = layouts[index];
    let name = sourceLayout.name;
    let cnt = 1;
    do {
        cnt++;
        name = `${sourceLayout.name}_${cnt}`;
        // eslint-disable-next-line no-loop-func
    } while (layouts.findIndex(s => s.name === name) >= 0);
    let breakpoint = sourceLayout.breakpoint + 1;
    let i = index + 1;
    for (; i < layouts.length; i++) {
        const nextbp = layouts[i].breakpoint;
        if (breakpoint < nextbp) {
            break;
        }
        if (breakpoint === nextbp) {
            breakpoint++;
        }
    }
    const newLayout = clone(sourceLayout);
    newLayout.name = name;
    newLayout.breakpoint = breakpoint;
    const newLayouts = [...layouts];
    newLayouts.splice(i, 0, newLayout);
    return newLayouts;
}

const DashboardDefinitionLayoutMenu: FC<DashboardDefinitionLayoutMenuProps> = props => {
    const {selectedLayout, onLayoutSelected} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const confirm = useOkCancelConfirm();
    const {errors, setFieldValue, values: layoutSet} = useFormikContext<DashboardResponsiveLayoutSet>();
    const layouts = layoutSet.layouts;

    const remove = useCallback(
        (index: number) => {
            const newArray = [...layouts];
            newArray.splice(index, 1);
            setFieldValue("layouts", newArray);
        },
        [layouts, setFieldValue]
    );

    const handleDeleteLayout = useCallback(
        async (index: number) => {
            const result = await confirm({
                description: t("ConfirmDeleteResponsiveLayout", {name: layouts[index].name}),
            });
            if (result) {
                remove(index);
            }
        },
        [confirm, layouts, remove, t]
    );

    const handleCloneLayout = useCallback(
        (index: number) => {
            const newLayouts = cloneLayout(layouts, index);
            setFieldValue("layouts", newLayouts);
        },
        [layouts, setFieldValue]
    );

    return (
        <List component="div" className={classes.list}>
            {layouts?.map((s, i) => {
                const isSelected = selectedLayout === i;
                return (
                    <ListItem
                        key={s.name}
                        button
                        selected={isSelected}
                        onClick={() => onLayoutSelected?.(i)}
                        className={classes.nested}
                    >
                        <ListItemText
                            primary={`${layouts[i].name} (${layouts[i].breakpoint ?? "?"}px)`}
                            primaryTypographyProps={{
                                variant: "subtitle1",
                                className: clsx({[classes.selected]: isSelected}),
                                color: errors?.layouts?.[i] ? "error" : "textSecondary",
                            }}
                        />
                        <ListItemSecondaryAction>
                            <Tooltip title={t("Copy") as string}>
                                <IconButton edge="end" onClick={() => handleCloneLayout(i)}>
                                    <FileCopyIcon />
                                </IconButton>
                            </Tooltip>
                            <Tooltip title={t("Delete") as string}>
                                <span>
                                    <IconButton
                                        edge="end"
                                        onClick={() => handleDeleteLayout(i)}
                                        disabled={layouts.length <= 1}
                                    >
                                        <DeleteIcon />
                                    </IconButton>
                                </span>
                            </Tooltip>
                        </ListItemSecondaryAction>
                    </ListItem>
                );
            })}
        </List>
    );
};

interface DashboardDefinitionLayoutEditorProps {
    layoutIndex: number;
}

const DashboardDefinitionLayoutEditor: FC<DashboardDefinitionLayoutEditorProps> = props => {
    const {layoutIndex} = props;
    const {t} = useTranslation();
    const {errors, setFieldValue, values: layoutSet} = useFormikContext<DashboardResponsiveLayoutSet>();
    const layouts = layoutSet.layouts;
    const layout = layouts[layoutIndex];

    const [rowHeightPerLayout, setRowHeightPerLayout] = useState(!isPresent(layoutSet.rowHeight));
    const [marginPerLayout, setMarginPerLayout] = useState(() => {
        if (isPresent(layoutSet.margin)) {
            return false;
        }
        return layoutSet.layouts.some(s => isPresent(s.margin));
    });

    const handleSwitchRowHeightPerLayout = useCallback(() => {
        setRowHeightPerLayout(s => {
            if (s) {
                const rowHeight = layout.rowHeight;
                setFieldValue("rowHeight", rowHeight);
                for (let i = 0; i < layouts.length; i++) {
                    setFieldValue(`layouts[${i}].rowHeight`, undefined);
                }
            } else {
                const rowHeight = layoutSet.rowHeight;
                for (let i = 0; i < layouts.length; i++) {
                    setFieldValue(`layouts[${i}].rowHeight`, rowHeight);
                }
                setFieldValue(`rowHeight`, undefined);
            }
            return !s;
        });
    }, [layout.rowHeight, layoutSet.rowHeight, layouts.length, setFieldValue]);

    const handleSwitchMarginPerLayout = useCallback(() => {
        setMarginPerLayout(s => {
            if (s) {
                const margin = layout.margin;
                setFieldValue("margin", margin);
                for (let i = 0; i < layouts.length; i++) {
                    setFieldValue(`layouts[${i}].margin`, undefined);
                }
            } else {
                const margin = layoutSet.margin;
                for (let i = 0; i < layouts.length; i++) {
                    setFieldValue(`layouts[${i}].margin`, margin);
                }
                setFieldValue(`margin`, undefined);
            }
            return !s;
        });
    }, [layout.margin, layoutSet.margin, layouts.length, setFieldValue]);

    let layoutError = errors?.layouts?.[layoutIndex];
    if (typeof layoutError === "string") {
        layoutError = undefined;
    }

    return (
        <div role="tabpanel">
            <Grid container spacing={3}>
                <Grid item xs={6}>
                    <FormikTextField
                        name={`layouts[${layoutIndex}].name`}
                        label={t("Name")}
                        InputLabelProps={{required: true}}
                        fullWidth
                    />
                </Grid>
                <Grid item xs={6}></Grid>
                <Grid item xs={6}>
                    <FormikNumberField
                        name={`layouts[${layoutIndex}].breakpoint`}
                        label={t("Breakpoint")}
                        InputLabelProps={{required: true}}
                        fullWidth
                    />
                </Grid>
                <Grid item xs={6}>
                    <FormikNumberField
                        name={`layouts[${layoutIndex}].cols`}
                        label={t("Columns")}
                        InputLabelProps={{required: true}}
                        fullWidth
                    />
                </Grid>
                <Grid item xs={6}>
                    <CombinablePropertyEditor
                        isCombined={!rowHeightPerLayout}
                        onSwitch={handleSwitchRowHeightPerLayout}
                    >
                        <FormikNumberField
                            name={rowHeightPerLayout ? `layouts[${layoutIndex}].rowHeight` : "rowHeight"}
                            label={t("RowHeight")}
                            InputLabelProps={{required: true}}
                            fullWidth
                        />
                    </CombinablePropertyEditor>
                </Grid>
                <Grid item xs={6}></Grid>
                <Grid item xs={12}>
                    <CombinablePropertyEditor isCombined={!marginPerLayout} onSwitch={handleSwitchMarginPerLayout}>
                        <Grid container spacing={3}>
                            <Grid item xs={6}>
                                <FormikNumberField
                                    name={marginPerLayout ? `layouts[${layoutIndex}].margin[0]` : "margin[0]"}
                                    label={t("HorizontalMargin")}
                                    InputLabelProps={{required: true}}
                                    fullWidth
                                />
                            </Grid>
                            <Grid item xs={6}>
                                <FormikNumberField
                                    name={marginPerLayout ? `layouts[${layoutIndex}].margin[1]` : "margin[1]"}
                                    label={t("VerticalMargin")}
                                    InputLabelProps={{required: true}}
                                    fullWidth
                                />
                            </Grid>
                        </Grid>
                    </CombinablePropertyEditor>
                </Grid>
            </Grid>
        </div>
    );
};

export interface DashboardDefinitionLayoutGridEditorDialogProps {
    open: boolean;
    defaultValue: DashboardResponsiveLayoutSet;
    onChange?: (layoutSet: DashboardResponsiveLayoutSet) => void;
    onClose: () => void;
}

export const DashboardDefinitionLayoutGridEditorDialog: FC<DashboardDefinitionLayoutGridEditorDialogProps> = props => {
    const {open, defaultValue, onChange, onClose} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const [selectedLayout, setSelectedLayout] = useState(0);
    const formRef = useRef<FormikProps<DashboardResponsiveLayoutSet> | null>();

    const handleSubmit = useCallback(
        (value: Partial<DashboardResponsiveLayoutSet>, actions: FormikHelpers<DashboardResponsiveLayoutSet>) => {
            onChange?.(value as DashboardResponsiveLayoutSet);
            onClose?.();
        },
        [onChange, onClose]
    );

    return (
        <Dialog
            open={open}
            fullWidth
            maxWidth="lg"
            classes={{paper: classes.paper}}
        >
            <Formik<DashboardResponsiveLayoutSet>
                innerRef={s => (formRef.current = s)}
                initialValues={defaultValue}
                onSubmit={handleSubmit}
                validationSchema={DashboardResponsiveLayoutSetSchema}
                validateOnChange={true}
                //validateOnBlur={true}
            >
                <Form autoComplete="off">
                    <DialogTitle>{t("EditingLayoutDefinition")}</DialogTitle>
                    <DialogContent>
                        <div className={classes.root}>
                            <div className={classes.menu}>
                                <DashboardDefinitionLayoutMenu
                                    selectedLayout={selectedLayout}
                                    onLayoutSelected={setSelectedLayout}
                                />
                            </div>
                            <div className={classes.panels}>
                                <DashboardDefinitionLayoutEditor layoutIndex={selectedLayout} />
                                {/* <FormikErrorViewer errors={errors} /> */}
                            </div>
                        </div>
                    </DialogContent>
                    <DialogActions>
                        <Button type="submit" color="primary">
                            {t("Accept")}
                        </Button>
                        <Button onClick={onClose} color="primary">
                            {t("Cancel")}
                        </Button>
                    </DialogActions>
                </Form>
            </Formik>
        </Dialog>
    );
};
