import React, {FC} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import Typography from "@material-ui/core/Typography";
import {useTranslation} from "react-i18next";
import {useFormikContext} from "formik";

import {ReplaceIcon} from "../../components/icons/replace-icon";
import {DashboardWidgetDefinition} from "../../api/nimflow-dashboard-widgets-api";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        button: {
            marginTop: -10,
        },
    })
);

export interface DashboardWidgetVisualizationTitleProps {
    onChangeVisualization: () => void;
}

export const DashboardWidgetVisualizationTitle: FC<DashboardWidgetVisualizationTitleProps> = props => {
    const {onChangeVisualization} = props;
    const {values: widgetDefinition} = useFormikContext<DashboardWidgetDefinition>();
    const {t} = useTranslation();
    const classes = useStyles();

    return (
        <>
            <Typography component="span" variant="h5">
                {t(`DashboardWidgetVisualization_type_${widgetDefinition.visualization.type}`)}
            </Typography>
            <Tooltip title={t("ChangeVisualizationType") as string}>
                <IconButton onClick={onChangeVisualization} className={classes.button}>
                    <ReplaceIcon />
                </IconButton>
            </Tooltip>
        </>
    );
};
