import {FC, useMemo, useCallback} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import {useFormContext} from "react-hook-form";
import {useTranslation} from "react-i18next";
import TextField from "@material-ui/core/TextField";
import {Components} from "@material-table/core";

import {ValidatorEntry} from "../../api/nimflow-api";
import {JsExpressionEditor} from "../../components/jsexpression-editor";
import {ValidatorEntrySchema, jsCommonScriptKey} from "../../models/context-type-schema";
import {BaseColumn, BaseEditComponentProps, BaseTableProps} from "../../components/base-table";
import {RhfArrayFieldEditor} from "../../components/rhf-array-field-editor";
import {validateWithYupSchema} from "../../libs/yup";
import {ErrorInfo} from "../../models/error-info";
import {useContextTypeContext} from "./context-type-context";
import {freeVarDefs} from "./free-variable-defs";
import {FormErrorViewer} from "../../components/form-error-viewer";
import {useEditionScopeCanAcceptVoting} from "../../components/edition-scope";

const useStyles = makeStyles((_theme: Theme) =>
    createStyles({
        textField: {
            width: "40ch",
        },
    })
);

export interface ValidatorsEditorProps {
    path: string;
    defaultValue?: Partial<ValidatorEntry>[];
}

const options: BaseTableProps<any>["options"] = {
    search: false,
};

const tableComponents: Components = {
    Container: props => <div {...props} />,
};

const freeVariables = [freeVarDefs.state, freeVarDefs.payload];

export const ValidatorsEditor: FC<ValidatorsEditorProps> = props => {
    const {path, defaultValue} = props;
    const {t} = useTranslation();
    const {errors} = useFormContext();
    const classes = useStyles();
    const {jsCommon} = useContextTypeContext();
    const vote = useEditionScopeCanAcceptVoting({
        voteIdPrefix: "ValidatorsEditor",
    });

    const jsValidator = useCallback(
        (js: string | undefined): Promise<ErrorInfo[]> => {
            return validateWithYupSchema(ValidatorEntrySchema.fields?.jsValidator, js, {
                context: {[jsCommonScriptKey]: jsCommon},
            });
        },
        [jsCommon]
    );

    const columns = useMemo(() => {
        const cols: BaseColumn<Partial<ValidatorEntry>>[] = [
            {
                field: "jsValidator",
                title: t("JsValidator"),
                width: "30ch",
                editComponent: ({value, onChange, errorMessage}: BaseEditComponentProps<Partial<ValidatorEntry>>) => (
                    <JsExpressionEditor
                        defaultValue={value}
                        onChange={onChange}
                        autoFocus
                        className={classes.textField}
                        placeholder={t("EnterBooleanExpression")}
                        error={!!errorMessage}
                        helperText={errorMessage}
                        validator={jsValidator}
                        freeVariables={freeVariables}
                        returnType="boolean"
                    />
                ),
                validateValue: jsValidator,
            },
            {
                field: "errorMessageTemplate",
                title: t("ErrorMessageTemplate"),
                width: "30ch",
                editComponent: ({value, onChange, errorMessage}: BaseEditComponentProps<Partial<ValidatorEntry>>) => (
                    <TextField
                        type="text"
                        defaultValue={value}
                        onChange={e => onChange(e.target.value)}
                        placeholder={t("EnterHandlebarsTemplate")}
                        className={classes.textField}
                        error={!!errorMessage}
                        helperText={errorMessage}
                    />
                ),
                validateValue: async value => {
                    const schema = ValidatorEntrySchema.fields?.errorMessageTemplate;
                    return await validateWithYupSchema(schema, value);
                },
            },
        ];
        return cols;
    }, [classes, t, jsValidator]);

    const handleEditionStateChanged = useCallback(
        editing => {
            vote(!editing);
        },
        [vote]
    );

    return (
        <>
            <RhfArrayFieldEditor
                path={path}
                defaultValue={defaultValue}
                columns={columns}
                title=" "
                options={options}
                components={tableComponents}
                onEditionStateChanged={handleEditionStateChanged}
            />
            <FormErrorViewer errors={errors} names={path} />
        </>
    );
};
