import React, {useCallback} from "react";
import {DialogProps} from "@material-ui/core/Dialog";
import {ButtonProps} from "@material-ui/core/Button";

import {useConfirm} from "./confirm";
import {ErrorInfo} from "../models/error-info";
import {useTranslation} from "react-i18next";
import {ErrorViewer} from "./error-viewer";

export interface ShowErrorInfosOptions {
    title?: React.ReactNode;
    description?: React.ReactNode;
    dialogProps?: Omit<DialogProps, "open" | "onClose">;
    closeText?: React.ReactNode;
    closeButtonProps?: ButtonProps;
}

export const useShowErrorInfos = (): ((errors: ErrorInfo[], opts?: ShowErrorInfosOptions) => Promise<void>) => {
    const confirm = useConfirm();
    const {t} = useTranslation();
    return useCallback(
        (errors: ErrorInfo[], opts = {}) => {
            return confirm({
                title: opts.title,
                description: (
                    <span style={{display: "block", maxHeight: "200px"}}>
                        <ErrorViewer error={errors} />
                    </span>
                ),
                dialogProps: opts.dialogProps,
                mapCloseTo: "close",
                actions: [
                    {
                        type: "close",
                        text: opts.closeText ?? t("Close"),
                        buttonProps: opts.closeButtonProps,
                    },
                ],
            })
                .then(() => {})
                .catch(() => {});
        },
        [confirm, t]
    );
};
