import React, { useMemo } from "react";
import {useFormContext} from "react-hook-form";

import {useObjectField} from "../utils/use-object-field";
import {MapEditor, MapEditorProps} from "./map-editor";

export type RhfObjectFieldEditorProps<T> = {
    path: string;
    defaultValue?: Record<string, T | undefined>;
} & Omit<MapEditorProps<T | undefined>, "value">;

export const RhfObjectFieldEditor = <T extends any>(props: RhfObjectFieldEditorProps<T>) => {
    const {path, editable, defaultValue, ...rest} = props;
    const {control} = useFormContext();
    const {value: record, set, deleteKey} = useObjectField<T | undefined>({name: path, control, defaultValue});

    const mapEditorHandler = useMemo(() => {
        const result: MapEditorProps<T | undefined>["editable"] = {
            ...editable,
            onFieldAdd: async (key, value) => {
                await editable?.onFieldAdd?.(key, value);
                set(key, value);
            },
            onFieldUpdate: async (key, value, oldKey) => {
                await editable?.onFieldUpdate?.(key, value, oldKey);
                if (oldKey && oldKey !== key) {
                    deleteKey(oldKey);
                }
                set(key, value);
            },
            onFieldDelete: async (key, value) => {
                await editable?.onFieldDelete?.(key, value);
                deleteKey(key);
            },
        };
        return result;
    }, [deleteKey, editable, set]);
    return <MapEditor<T | undefined> value={record} editable={mapEditorHandler} {...rest} />;
};
