import React, {FC} from "react";
import {DiagramEngine} from "@projectstorm/react-diagrams-core";

import {ContextTypeLinkFactory} from "./context-type-link-factory";
import {ContextTypeLinkModel} from "./context-type-link-model";
import {ContextTypeLinkStatus} from "./context-type-link-widget";

export interface ContextTypeLinkSegmentWidgetProps {
    path: string;
    link: ContextTypeLinkModel;
    linkStatus: ContextTypeLinkStatus;
    isOutgoing: boolean;
    forwardRef: React.RefObject<SVGPathElement>;
    factory: ContextTypeLinkFactory;
    diagramEngine: DiagramEngine;
    onSelection: (selected: boolean) => any;
    extras: object;
}

export const ContextTypeLinkSegmentWidget: FC<ContextTypeLinkSegmentWidgetProps> = props => {
    const {path, link, forwardRef, factory, onSelection, extras, linkStatus, isOutgoing} = props;
    const isSelected = linkStatus === ContextTypeLinkStatus.selected || link.isSelected();
    const linkColor = !isSelected ? link.getOptions().color : isOutgoing ? link.getSelectedOutgoingColor() : link.getOptions().selectedColor;
    const Bottom = React.cloneElement(
        factory.generateLinkSegment(link, isSelected, path),
        {
            ref: forwardRef,
            strokeOpacity: linkStatus === ContextTypeLinkStatus.selected ? 0.8 : ContextTypeLinkStatus.unfocused ? 0.1 : 0.3,
            stroke: linkColor,
        }
    );

    const Top = React.cloneElement(Bottom, {
        strokeLinecap: "round",
        onMouseLeave: () => {
            onSelection(false);
        },
        onMouseEnter: () => {
            onSelection(true);
        },
        ...extras,
        ref: null,
        "data-linkid": link.getID(),
        strokeOpacity: linkStatus === ContextTypeLinkStatus.selected ? 0.1 : 0,
        strokeWidth: 20,
        fill: "none",
    });

    return (
        <g>
            {Bottom}
            {Top}
        </g>
    );
};
