import React, {FC} from "react";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import {useTranslation} from "react-i18next";
import {last, innerJoin} from "ramda";

import JsonViewer from "../../components/json-viewer";
import {StandardSection} from "../../components/standard-section";
import {StandardSectionHeader} from "../../components/standard-section-header";
import {FinalIcon} from "../../components/icons/final-icon";
import {MilestoneIcon} from "../../components/icons/milestone-icon";
import {ContextSimulation} from "../../api/nimflow-simulation-api";
import {ContextType} from "../../api/nimflow-api";

export interface StateSectionProps {
    simulation: ContextSimulation;
    contextType: ContextType;
    currentEventIndex: number;
}

export const StateSection: FC<StateSectionProps> = props => {
    const {simulation, contextType, currentEventIndex} = props;
    const {t} = useTranslation();

    const currentStep = simulation.history[currentEventIndex];
    const currentState = currentStep?.context.state ?? last(simulation.history)?.context.state ?? {};

    const milestones = currentStep?.reachedMilestones ?? [];
    const isFinalMilestone = innerJoin((m, r) => m.name === r, contextType.milestones, milestones).some(s => s.final);

    return (
        <StandardSection>
            <StandardSectionHeader
                title={t("CurrentState")}
                actions={
                    milestones.length > 0 && (
                        <Tooltip title={milestones.join(", ")}>
                            <IconButton disableRipple>
                                {isFinalMilestone ? <FinalIcon /> : <MilestoneIcon />}
                            </IconButton>
                        </Tooltip>
                    )
                }
            />
            {currentState && <JsonViewer data={currentState} />}
        </StandardSection>
    );
};
