import React, {FC, useMemo} from "react";
import {makeStyles, createStyles} from "@material-ui/core/styles";
import List from "@material-ui/core/List";

import {EditContextTypeIcon} from "../../components/icons/edit-context-type-icon";
import {SimulateIcon} from "../../components/icons/simulate-icon";
import {LocalizeIcon} from "../../components/icons/localize-icon";
import {useTranslation} from "react-i18next";
import {permissionRequests, permissions} from "../../utils/authorization";
import {useNimflowAuthorization} from "../../api/nimflow-authorization";
import {useAuthorization} from "../../components/authorization-context";
import {EditWidgetDefinitionIcon} from "../../components/icons/edit-widget-definition-icon";
import {EditDashboardDefinitionIcon} from "../../components/icons/edit-dashboard-definition-icon";
import {SideMenuItem, SideMenuItemProps} from "./side-menu-item";
import {AutomationIcon} from "../../components/icons/automation-icon";
import {DigitalWorkerIcon} from "../../components/icons/digital-worker-icon";
import {FunctionIcon} from "../../components/icons/function-icon";

const {modifyContextTypes} = permissionRequests;

const useStyles = makeStyles(theme =>
    createStyles({
        appMenu: {
            width: "100%",
            height: "100%",
        },
    })
);

export const SideMenu: FC<{}> = () => {
    const classes = useStyles();
    const {t} = useTranslation();
    const {can} = useAuthorization();
    const {canManageSimulations, canManageLocalizations, canManageWidgetDefinitions, canManageDashboardDefinitions} =
        useNimflowAuthorization();

    const menuDef = useMemo(() => {
        const result: SideMenuItemProps[] = [];
        if (can(modifyContextTypes)) {
            result.push({
                name: t("ContextTypeCatalog"),
                link: "/contextTypes",
                icon: <EditContextTypeIcon />,
            });
        }
        if (canManageSimulations()) {
            result.push({
                name: t("SimulateContextType"),
                link: "/simulator",
                icon: <SimulateIcon />,
            });
        }
        if (canManageLocalizations()) {
            result.push({
                name: t("EditLocalizations"),
                link: "/localization",
                icon: <LocalizeIcon />,
            });
        }
        if (canManageWidgetDefinitions()) {
            result.push({
                name: t("EditWidgetDefinitions"),
                link: "/widgetDefinitions",
                icon: <EditWidgetDefinitionIcon />,
            });
        }
        if (canManageDashboardDefinitions()) {
            result.push({
                name: t("EditDashboardDefinitions"),
                link: "/dashboardDefinitions",
                icon: <EditDashboardDefinitionIcon />,
            });
        }
        if (can([permissions.manage])) {
            result.push({
                name: t("DigitalWorkers"),
                link: "/digitalWorkers",
                icon: <DigitalWorkerIcon />,
            });
        }
        if (can([permissions.readContextTypes])) {
            result.push({
                name: t("UdFunctions"),
                link: "/udfunctions",
                icon: <FunctionIcon />,
            });
        }
        if (can([permissions.readContextTypes])) {
            result.push({
                name: t("Automation"),
                link: "/eventListeners",
                icon: <AutomationIcon />,
            });
        }
        return result;
    }, [
        can,
        canManageDashboardDefinitions,
        canManageLocalizations,
        canManageSimulations,
        canManageWidgetDefinitions,
        t,
    ]);

    return (
        <List component="nav" className={classes.appMenu} disablePadding>
            {menuDef.map((item, index) => (
                <SideMenuItem {...item} key={index} />
            ))}
        </List>
    );
};
