import React, {FC, useState, useCallback} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Button from "@material-ui/core/Button";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import {useTranslation} from "react-i18next";
import MonacoEditor, {EditorDidMount, EditorWillMount} from "react-monaco-editor";
import {useTheme} from "@material-ui/core/styles";
import FormHelperText from "@material-ui/core/FormHelperText";
import Autosizer from "react-virtualized-auto-sizer";
import ReactMarkdown from "react-markdown";

import {useLoseChangesConfirm} from "./specialized-confirmations";
import {defaultMonacoOptions} from "../libs/monaco";

const errorContainerHeight = 100;

interface StyleProps {
    showErrors: boolean;
    activeTab: number;
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        dialogPaper: {
            height: "80vh",
        },
        dialogContent: {},
        writeTab: {
            display: (props: StyleProps) => (props.activeTab === 0 ? "grid" : "none"),
            gridTemplateColumns: "auto",
            gridTemplateRows: (props: StyleProps) => (props.showErrors ? `auto ${errorContainerHeight}px` : "auto"),
            rowGap: "5px",
            placeItems: "stretch",
            overflowY: "hidden",
            //height: "100%",
            height: "calc(100% - 50px)",
        },
        previewTab: {
            height: "calc(100% - 50px)",
            overflowY: "auto",
        },
        editorContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
        },
        errorContainer: {
            gridColumn: "1 / 2",
            gridRow: "2 / 3",
            overflowY: "auto",
        },
    })
);

export interface MarkdownMonacoEditorModalProps {
    open: boolean;
    defaultValue?: string;
    onChange?: (value: string | undefined) => void;
    onClose: () => void;
    title?: string;
}

const editorWillMount: EditorWillMount = monaco => {
    const options: ReturnType<EditorWillMount> = {
        selectOnLineNumbers: true,
        //codeLens: false,
        minimap: {
            enabled: false,
        },
    };
    return options;
};

const editorDidMount: EditorDidMount = editor => {
    editor.focus();
};

export const MarkdownMonacoEditorModal: FC<MarkdownMonacoEditorModalProps> = props => {
    const {open, defaultValue, onChange, onClose, title} = props;
    const {t} = useTranslation();
    const theme = useTheme();
    const loseChangesConfirm = useLoseChangesConfirm();
    const [errors] = useState<string[]>([]);
    const [code, setCode] = useState(defaultValue);
    const [activeTab, setActiveTab] = useState(0);
    const classes = useStyles({showErrors: errors.length > 0, activeTab});
    const handleOnAccept = useCallback(() => {
        onChange?.(code);
        onClose();
    }, [code, onChange, onClose]);

    const handleTabChange = (event: React.ChangeEvent<{}>, newValue: number) => {
        setActiveTab(newValue);
    };

    const handleOnClose = useCallback(async () => {
        if (defaultValue !== code && !(await loseChangesConfirm())) {
            return;
        }
        onClose();
    }, [code, defaultValue, loseChangesConfirm, onClose]);

    const handleDialogClose = useCallback(async (event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (defaultValue !== code && !(await loseChangesConfirm())) {
            return;
        }
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [code, defaultValue, loseChangesConfirm, onClose]);

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            maxWidth="md"
            fullWidth
            classes={{
                paper: classes.dialogPaper,
            }}
        >
            <DialogTitle>{title ?? t("EditingMarkdown")}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <Tabs
                    value={activeTab}
                    onChange={handleTabChange}
                    variant="scrollable"
                    scrollButtons="on"
                    indicatorColor="primary"
                    textColor="primary"
                >
                    <Tab label={t("Write")} />
                    <Tab label={t("Preview")} />
                </Tabs>
                <div role="tabpanel" hidden={activeTab !== 0} className={classes.writeTab}>
                    <div className={classes.editorContainer}>
                        <Autosizer>
                            {({height, width}) => (
                                <MonacoEditor
                                    width={width}
                                    height={height}
                                    language="markdown"
                                    theme={theme.palette.type === "light" ? "vs-light" : "vs-dark"}
                                    value={code}
                                    options={defaultMonacoOptions}
                                    onChange={setCode}
                                    editorWillMount={editorWillMount}
                                    editorDidMount={editorDidMount}
                                />
                            )}
                        </Autosizer>
                    </div>
                    {errors.length > 0 && (
                        <div className={classes.errorContainer}>
                            <FormHelperText component="span" error>
                                {errors.map((s, i) => (
                                    <p key={i}>{s}</p>
                                ))}
                            </FormHelperText>
                        </div>
                    )}{" "}
                </div>
                <div role="tabpanel" hidden={activeTab !== 1} className={classes.previewTab}>
                    <ReactMarkdown>{code ?? ""}</ReactMarkdown>
                </div>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleOnAccept} color="primary">
                    {t("Accept")}
                </Button>
                <Button onClick={handleOnClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
