import React, {FC, useCallback, useMemo} from "react";
import {useField, useFormikContext} from "formik";
import Autocomplete, {AutocompleteProps, AutocompleteRenderInputParams} from "@material-ui/lab/Autocomplete";
import TextField, {TextFieldProps} from "@material-ui/core/TextField";
import useSWR from "swr";

import {useNimflowFunctions} from "../../api/nimflow-functions-api";
import {useKey} from "../../utils/use-key";

export const useFunctionInfos = () => {
    const {getFunctions} = useNimflowFunctions();
    const key = useKey(getFunctions);
    const {data, isValidating, mutate, error} = useSWR(
        key,
        async () => {
            return await getFunctions();
        },
        {
            revalidateOnFocus: false,
        }
    );
    return useMemo(() => {
        return {
            functionInfos: data ?? {},
            isValidating,
            error,
            revalidate: mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export interface FormikFunctionSelectProps {
    moduleNamePath: string;
    functionNamePath: string;
    onChange?: (moduleName?: string, functionName?: string) => void;
    AutocompleteProps?: Omit<
        AutocompleteProps<string, false, undefined, undefined>,
        "value" | "options" | "onChange" | "onBlur" | "renderInput"
    >;
    TextFieldProps?: Omit<TextFieldProps, "error" | "helperText" | keyof AutocompleteRenderInputParams>;
}

export const FormikFunctionSelect: FC<FormikFunctionSelectProps> = props => {
    const {
        moduleNamePath = "moduleName",
        functionNamePath = "functionName",
        onChange,
        AutocompleteProps,
        TextFieldProps,
    } = props;
    const {functionInfos, isValidating} = useFunctionInfos();
    const {isSubmitting, setFieldTouched} = useFormikContext<any>();
    const [moduleNameField, moduleNameMeta, moduleNameHelpers] = useField({name: moduleNamePath});
    const [nameField, nameMeta, nameHelpers] = useField({name: functionNamePath});
    let fieldError = moduleNameMeta.error;
    if (nameMeta.error) {
        if (fieldError) {
            fieldError += "\n" + nameMeta.error;
        } else {
            fieldError = nameMeta.error;
        }
    }
    const showError = moduleNameMeta.touched && !!fieldError;

    const value = useMemo(() => {
        if (!nameField.value || !moduleNameField.value) {
            return undefined;
        }
        return `${moduleNameField.value}/${nameField.value}`;
    }, [moduleNameField.value, nameField.value]);

    const options = useMemo(() => {
        return Object.keys(functionInfos).sort((a, b) => a.toLowerCase().localeCompare(b.toLowerCase()));
    }, [functionInfos]);

    const handleChange = useCallback(
        (event: React.ChangeEvent<{}>, value: string | null) => {
            const finfo = value ? functionInfos[value] : undefined;
            moduleNameHelpers.setValue(finfo?.moduleName);
            nameHelpers.setValue(finfo?.name);
            onChange?.(finfo?.moduleName, finfo?.name);
        },
        [functionInfos, moduleNameHelpers, nameHelpers, onChange]
    );

    const handleBlur = useCallback(
        (ev: React.FocusEvent<HTMLInputElement>) => {
            setFieldTouched(moduleNameField.name);
            setFieldTouched(nameField.name);
            moduleNameField.onBlur(ev);
            nameField.onBlur(ev);
        },
        [moduleNameField, nameField, setFieldTouched]
    );

    return (
        <Autocomplete
            disabled={isSubmitting}
            loading={isValidating || isSubmitting}
            {...AutocompleteProps}
            value={value ?? null}
            options={options}
            onChange={handleChange}
            onBlur={handleBlur}
            renderInput={params => (
                <TextField
                    {...TextFieldProps}
                    {...params}
                    error={showError}
                    helperText={showError ? fieldError : undefined}
                />
            )}
        />
    );
};
