import * as yup from "yup";
import type {SchemaOf} from "yup";

import {UserDefinedFunction} from "../../api/nimflow-api";

export const UserDefinedFunctionSchema = yup.object().shape({
    id: yup.string().required(),
    name: yup.string().required().identifier(),
    description: yup.string(),
    inputSchema: yup.string().jsonSchema(),
    outputSchema: yup.string().jsonSchema(),
    script: yup.string().required().jsModule(),
    version: yup.number().required(),
}) as SchemaOf<UserDefinedFunction>;
