import * as React from "react";
import {AbstractReactFactory, GenerateWidgetEvent} from "@projectstorm/react-canvas-core";
import {DiagramEngine} from "@projectstorm/react-diagrams-core";

import {SelectorNodeModel} from "./selector-node-model";
import {SelectorNodeWidget} from "./selector-node-widget";

export class SelectorNodeFactory extends AbstractReactFactory<SelectorNodeModel, DiagramEngine> {
    constructor() {
        super("selector");
    }

    generateModel(initialConfig: any) {
        const milestoneType = initialConfig.milestoneType;
        const instanceId = initialConfig.instanceId ?? "";
        const selector = {...initialConfig};
        delete selector.milestoneType;
        delete selector.instanceId;
        return new SelectorNodeModel(selector, instanceId, milestoneType);
    }

    generateReactWidget(event: GenerateWidgetEvent<SelectorNodeModel>): JSX.Element {
        return <SelectorNodeWidget engine={this.engine} node={event.model} />;
    }
}
