import React, {FC, useMemo, useCallback} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import {Column, Action} from "@material-table/core";
import Alert from "@material-ui/lab/Alert";
import {useTranslation} from "react-i18next";
import i18next from "i18next";
import RefreshIcon from "@material-ui/icons/Refresh";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import SaveAltIcon from "@material-ui/icons/SaveAlt";
import DeleteIcon from "@material-ui/icons/Delete";
import OpenInBrowserIcon from "@material-ui/icons/OpenInBrowser";
import {useNavigate} from "react-router";
import useSWR from "swr";

import MaterialTable from "../../libs/material-table";
import {useNimflow} from "../../api/nimflow-api-provider";
import {downloadJson} from "../../utils/io-helpers";
import {ContextTypeInfo} from "../../api/nimflow-api";
import {useOkCancelConfirm} from "../../components/confirm";
import {exportContextTypeToJson} from "../../models/context-type-exporter";
import {useKey} from "../../utils/use-key";

const columns: Column<any>[] = [
    {field: "name", title: i18next.t("Name")},
    {field: "description", title: i18next.t("Description")},
];

const useStyles = makeStyles((_theme: Theme) =>
    createStyles({
        fileInput: {
            opacity: 0,
            position: "absolute",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            width: "100%",
        },
    })
);

const buildRefreshAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: RefreshIcon,
        tooltip: i18next.t("RefreshData"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const buildAddAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: AddIcon,
        tooltip: i18next.t("AddContextType"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const buildImportAction = (icon: Action<any>["icon"], handler: () => void) => {
    const action: Action<any> = {
        icon,
        tooltip: i18next.t("ImportContextType"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const adaptHandler = <T extends object>(handler: (info: T) => void): Action<T>["onClick"] => {
    return (_, info) => {
        if (Array.isArray(info)) {
            info = info[0];
        }
        handler(info);
    };
};

const buildEditAction = (handler: (info: ContextTypeInfo) => void) => {
    const action: Action<ContextTypeInfo> = {
        icon: EditIcon,
        tooltip: i18next.t("Edit"),
        onClick: adaptHandler(handler),
    };
    return action;
};

const buildExportAction = (handler: (info: ContextTypeInfo) => void) => {
    const action: Action<ContextTypeInfo> = {
        icon: SaveAltIcon,
        tooltip: i18next.t("Export"),
        onClick: adaptHandler(handler),
    };
    return action;
};

const buildDeleteAction = (handler: (info: ContextTypeInfo) => void) => {
    const action: Action<ContextTypeInfo> = {
        icon: DeleteIcon,
        tooltip: i18next.t("Delete"),
        onClick: adaptHandler(handler),
    };
    return action;
};

const useContextTypes = () => {
    const {contextTypes_Search} = useNimflow();
    const queryKey = useKey(contextTypes_Search);
    const {data, error, mutate, isValidating} = useSWR(queryKey, async () => {
        const result = await contextTypes_Search({limit: 100});
        return result;
    });
    return useMemo(() => {
        return {
            contextTypeInfos: data ?? [],
            isValidating,
            error,
            mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export const ContextTypesViewer: FC<{}> = () => {
    const {t} = useTranslation();
    const confirm = useOkCancelConfirm();
    const {contextTypes_FindOne, contextTypes_Delete} = useNimflow();
    const navigate = useNavigate();
    const classes = useStyles();
    const {contextTypeInfos: data, error, mutate, isValidating} = useContextTypes();

    const handleFileUploaded = useCallback(
        async (e: React.ChangeEvent<HTMLInputElement>) => {
            const file = e.target?.files?.[0];
            if (file?.type.match(/.+\/json/)) {
                const url = URL.createObjectURL(file);
                navigate(`import/${btoa(url)}`);
            }
        },
        [navigate]
    );

    const refreshAction = useMemo(() => {
        return buildRefreshAction(mutate);
    }, [mutate]);
    const addAction = useMemo(() => {
        return buildAddAction(() => {
            navigate("new");
        });
    }, [navigate]);
    const importAction = useMemo(() => {
        return buildImportAction(
            () => (
                <>
                    <OpenInBrowserIcon />
                    <input
                        type="file"
                        title=""
                        accept=".context.json"
                        className={classes.fileInput}
                        onChange={handleFileUploaded}
                    />
                </>
            ),
            () => {}
        );
    }, [classes.fileInput, handleFileUploaded]);

    const rowActions = useMemo(() => {
        return [
            buildEditAction(row => {
                navigate(`edit/${row.name}`);
            }),
            buildExportAction(async row => {
                const ct = await contextTypes_FindOne({
                    name: row.name,
                });
                downloadJson(exportContextTypeToJson(ct), `${ct.name}.context.json`);
            }),
            buildDeleteAction(async row => {
                let result = await confirm({description: t("ConfirmDeleteContextType", {name: row.name})});
                if (result) {
                    result = await contextTypes_Delete({
                        name: row.name,
                    });
                    mutate();
                }
                return result;
            }),
        ];
    }, [navigate, mutate, t, confirm, contextTypes_FindOne, contextTypes_Delete]);

    return (
        <>
            <MaterialTable
                columns={columns}
                data={data}
                title={t("ContextTypeCatalog")}
                actions={[addAction, importAction, refreshAction, ...rowActions]}
            />
            {!isValidating && !!error && <Alert severity="error">{error.toString()}</Alert>}
        </>
    );
};
