import React, {FC, useCallback, useRef, useEffect, useMemo, useState} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import InputLabel from "@material-ui/core/InputLabel";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import {useTranslation} from "react-i18next";
import {FormikProps, useFormikContext, Formik, FormikHelpers, useField} from "formik";
import * as yup from "yup";
import {KeyboardTimePicker, KeyboardDatePicker} from "@material-ui/pickers";
import {isPresent} from "ts-is-present";
import {startOfDay} from "date-fns";

import {DashboardWidgetParameter} from "../../api/nimflow-dashboard-widgets-api";
import {ErrorBoundary} from "../../components/error-boundary";
import {ErrorViewer} from "../../components/error-viewer";
import {validateAndSubmit} from "../../utils/formikHelpers";
import {useConfirm} from "../../components/confirm";
import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {FormikCheckbox} from "../../components/formik-components/formik-checkbox";
import {FormikNumberField} from "../../components/formik-components/formik-number-field";
import { FormikErrorViewer } from "../../components/formik-error-viewer";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        form: {
            display: "flex",
            flexDirection: "column",
        },
        editors: {
            flexGrow: 1,
            padding: "10px 8px",
        },
        actions: {
            flexGrow: 0,
        },
        dateEditor: {
            width: "60%",
            paddingRight: 10,
        },
        timeEditor: {
            width: "40%",
        },
    })
);

interface StringListEditorProps {
    name: string;
    label: string;
    required: boolean;
}

const StringListEditor: FC<StringListEditorProps> = props => {
    const {name, label, required} = props;
    const [field, meta, helpers] = useField({name});
    const fieldError = meta.error;
    const showError = meta.touched && !!fieldError;

    const [text, setText] = useState(() => {
        return (field.value ?? []).join(", ");
    });
    const [t, setT] = useState<ReturnType<typeof setTimeout>>();

    const applyChange = useCallback(
        (ev: React.ChangeEvent<HTMLInputElement>) => {
            const vals = ev.target.value
                .split(",")
                .map(s => s.trim())
                .filter(s => s.length > 0);
            helpers.setValue(vals);
        },
        [helpers]
    );

    const handleChange = useCallback(
        (ev: React.ChangeEvent<HTMLInputElement>) => {
            setText(ev.target.value);
            ev.persist();
            if (t) {
                clearTimeout(t);
            }
            setT(setTimeout(() => applyChange(ev), 750));
        },
        [applyChange, t]
    );

    return (
        <TextField
            {...field}
            value={text}
            onChange={handleChange}
            label={label}
            InputLabelProps={{required}}
            fullWidth
            required={required}
            error={showError}
            helperText={showError ? fieldError : undefined}
        />
    );
};

interface InnerParameterValuesEditorProps {
    parameters: DashboardWidgetParameter[];
}

const InnerParameterValuesEditor: FC<InnerParameterValuesEditorProps> = props => {
    const {parameters} = props;
    const {values, errors, setFieldValue} = useFormikContext<Record<string, any>>();
    const {t} = useTranslation();
    const classes = useStyles();

    const handleDateChange = useCallback(
        (name: string, date: Date | null) => {
            if (date) {
                try {
                    if (!isPresent(values[name])) {
                        date = startOfDay(date);
                    }
                    setFieldValue(name, date.toISOString());
                } catch {}
            }
        },
        [setFieldValue, values]
    );

    if (parameters.length === 0) {
        return null;
    }
    return (
        <Grid container spacing={2}>
            {parameters.map(s => (
                <Grid item xs={12} key={s.name}>
                    {s.type === "string" && (
                        <FormikTextField
                            name={s.name}
                            label={s.name}
                            InputLabelProps={{required: s.required}}
                            fullWidth
                            required={s.required}
                        />
                    )}
                    {s.type === "number" && (
                        <FormikNumberField
                            name={s.name}
                            label={s.name}
                            InputLabelProps={{required: s.required}}
                            fullWidth
                            required={s.required}
                        />
                    )}
                    {s.type === "stringList" && <StringListEditor name={s.name} label={s.name} required={s.required} />}
                    {s.type === "datetime" && (
                        <>
                            <InputLabel shrink required={s.required}>
                                {s.name}
                            </InputLabel>
                            <div>
                                <KeyboardDatePicker
                                    margin="none"
                                    format={t("DATE_FORMAT")}
                                    required
                                    className={classes.dateEditor}
                                    value={(values[s.name] as any) ?? null}
                                    onChange={date => handleDateChange(s.name, date)}
                                    KeyboardButtonProps={{
                                        "aria-label": "change date",
                                    }}
                                />
                                <KeyboardTimePicker
                                    margin="none"
                                    className={classes.timeEditor}
                                    format={t("TIME_FORMAT")}
                                    value={(values[s.name] as any) ?? null}
                                    onChange={date => handleDateChange(s.name, date)}
                                    KeyboardButtonProps={{
                                        "aria-label": "change time",
                                    }}
                                    disabled={!isPresent(values[s.name])}
                                />
                            </div>
                            <FormikErrorViewer errors={errors} names={s.name} />
                        </>
                    )}
                    {s.type === "boolean" && (
                        <FormikCheckbox
                            name={s.name}
                            label={s.name}
                            //LabelProps={{required: s.required}}
                            inputProps={{required: s.required}}
                            required={s.required}
                        />
                    )}
                </Grid>
            ))}
        </Grid>
    );
};

const getSchema = (parameters: DashboardWidgetParameter[]) => {
    const shape: Record<string, yup.BaseSchema<any>> = {};
    for (const param of parameters) {
        switch (param.type) {
            case "boolean": {
                let schema = yup.boolean();
                if (param.required) {
                    schema = schema.required();
                }
                shape[param.name] = schema;
                break;
            }
            case "number": {
                let schema = yup.number();
                if (param.required) {
                    schema = schema.required();
                }
                shape[param.name] = schema;
                break;
            }
            case "string": {
                let schema = yup.string();
                if (param.required) {
                    schema = schema.required();
                }
                shape[param.name] = schema;
                break;
            }
            case "datetime": {
                let schema = yup.string().iso8601Formatted();
                if (param.required) {
                    schema = schema.required();
                }
                shape[param.name] = schema;
                break;
            }
            case "stringList": {
                let schema = yup.array(yup.string());
                if (param.required) {
                    schema = schema.required();
                }
                shape[param.name] = schema;
                break;
            }
        }
    }
    const result = yup.object().shape(shape);
    return result;
};

const getInitialValues = (parameters: DashboardWidgetParameter[], currentValues?: Record<string, any>) => {
    const result: Record<string, any> = {};
    for (const param of parameters) {
        result[param.name] = undefined;
        const currValue = currentValues?.[param.name];
        if (
            param.type === typeof currValue ||
            (param.type === "datetime" && typeof currValue === "string") ||
            (param.type === "stringList" && Array.isArray(currValue))
        ) {
            result[param.name] = currValue;
        }
    }
    return result;
};

export interface ParameterValuesEditorProps {
    parameters?: DashboardWidgetParameter[];
    onParameterValueChange: (values: Record<string, any>) => void;
}

export const ParameterValuesEditor: FC<ParameterValuesEditorProps> = props => {
    const {parameters = [], onParameterValueChange} = props;
    const {t} = useTranslation();
    const formRef = useRef<FormikProps<Record<string, any>> | null>();
    const classes = useStyles();
    const confirm = useConfirm();
    const initialValues = useMemo(() => {
        const currentValues = formRef.current?.values;
        return getInitialValues(parameters, currentValues);
    }, [parameters]);

    const schema = useMemo(() => {
        const result = getSchema(parameters);
        return result;
    }, [parameters]);

    useEffect(() => {
        formRef.current?.resetForm({values: initialValues});
    }, [initialValues]);

    const submitHandlerRef = useRef<(values: Record<string, any>) => Promise<void>>();

    const handleRefresh = useCallback(() => {
        const form = formRef.current;
        if (form && onParameterValueChange) {
            submitHandlerRef.current = async (data: Record<string, any>) => {
                let error: React.ReactNode;
                try {
                    onParameterValueChange(data);
                } catch (e) {
                    error = <ErrorViewer error={e} />;
                }
                if (error) {
                    await confirm({
                        title: t("ErrorApplyingParameters"),
                        description: error,
                        mapCloseTo: "ok",
                        actions: [{type: "ok", text: t("Ok")}],
                    });
                }
            };
            validateAndSubmit(form);
        }
        return Promise.resolve();
    }, [confirm, onParameterValueChange, t]);

    const handleSubmit = useCallback(
        async (values: Record<string, any>, actions: FormikHelpers<Record<string, any>>) => {
            try {
                if (submitHandlerRef.current) {
                    await submitHandlerRef.current(values);
                    submitHandlerRef.current = undefined;
                }
            } finally {
                actions.setSubmitting(false);
            }
        },
        []
    );

    return (
        <ErrorBoundary>
            <Formik<Record<string, any>>
                innerRef={s => (formRef.current = s)}
                initialValues={initialValues}
                onSubmit={handleSubmit}
                validationSchema={schema}
                validateOnChange={true}
                validateOnBlur={true}
            >
                <div className={classes.form}>
                    <div>
                        <Typography variant="body1">{t("Parameters")}</Typography>
                    </div>
                    <div className={classes.editors}>
                        <InnerParameterValuesEditor parameters={parameters} />
                    </div>
                    <DialogActions className={classes.actions}>
                        <Button onClick={handleRefresh} color="primary">
                            {t("Refresh")}
                        </Button>
                    </DialogActions>
                </div>
            </Formik>
        </ErrorBoundary>
    );
};
