import React, {FC, useMemo, useCallback} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import MenuItem from "@material-ui/core/MenuItem";
import TextField from "@material-ui/core/TextField";
import Checkbox from "@material-ui/core/Checkbox";
import {useTranslation} from "react-i18next";
import {Components} from "@material-table/core";
import * as yup from "yup";
import {useFormikContext} from "formik";

import {BaseColumn, BaseEditComponentProps, BaseTableProps} from "../../components/base-table";
import {validateWithYupSchema} from "../../libs/yup";
import {useEditionScopeCanAcceptVoting} from "../../components/edition-scope";
import {
    DashboardWidgetParameter,
    dashboardWidgetParameterTypes,
    DashboardWidgetDefinition,
} from "../../api/nimflow-dashboard-widgets-api";
import {DashboardWidgetParameterSchema} from "./widget-definition-schema";
import {FormikArrayFieldEditor, FormikArrayFieldEditorProps} from "../../components/formik-array-field-editor";
import {FormikErrorViewer} from "../../components/formik-error-viewer";

const useStyles = makeStyles((_theme: Theme) =>
    createStyles({
        textField: {
            width: "40ch",
        },
    })
);

export interface WidgetDefinitionParametersEditorProps {
    path: string;
}

const options: BaseTableProps<any>["options"] = {
    search: false,
};

const tableComponents: Components = {
    Container: props => <div {...props} />,
};

export const WidgetDefinitionParametersEditor: FC<WidgetDefinitionParametersEditorProps> = props => {
    const {path} = props;
    const {t} = useTranslation();
    const {errors} = useFormikContext<Partial<DashboardWidgetDefinition>>();
    const classes = useStyles();
    const vote = useEditionScopeCanAcceptVoting({
        voteIdPrefix: "ParametersEditor",
    });
    const columns = useMemo(() => {
        const cols: BaseColumn<Partial<DashboardWidgetParameter>>[] = [
            {
                field: "name",
                title: t("Name"),
                width: "30ch",
                editComponent: ({
                    value,
                    onChange,
                    errorMessage,
                }: BaseEditComponentProps<Partial<DashboardWidgetParameter>>) => (
                    <TextField
                        type="text"
                        defaultValue={value}
                        onChange={e => onChange(e.target.value)}
                        placeholder={t("EnterName")}
                        className={classes.textField}
                        error={!!errorMessage}
                        helperText={errorMessage}
                    />
                ),
                validateValue: async (value, oldValue, rowData, data) => {
                    const schema = DashboardWidgetParameterSchema.fields?.name.concat(
                        yup
                            .string()
                            .required()
                            .test("notDuplicated", "", function (name: any) {
                                if (!name) {
                                    return true;
                                }
                                if (oldValue === name) {
                                    // Key did not change, so it must be still valid
                                    return true;
                                }
                                const dup = data.find(s => s.name === name);
                                if (dup) {
                                    return this.createError({path: this.path, message: t("NameAlreadyInUse", {name})});
                                }
                                return true;
                            })
                    );
                    return await validateWithYupSchema(schema, value);
                },
            },
            {
                field: "type",
                title: t("Type"),
                width: "30ch",
                editComponent: ({
                    value,
                    onChange,
                    errorMessage,
                }: BaseEditComponentProps<Partial<DashboardWidgetParameter>>) => (
                    <TextField
                        select
                        defaultValue={value}
                        onChange={e => onChange(e.target.value)}
                        className={classes.textField}
                        error={!!errorMessage}
                        helperText={errorMessage}
                    >
                        {dashboardWidgetParameterTypes.map(s => (
                            <MenuItem key={s} value={s}>
                                {s}
                            </MenuItem>
                        ))}
                    </TextField>
                ),
                validateValue: async value => {
                    const schema = DashboardWidgetParameterSchema.fields?.type;
                    return await validateWithYupSchema(schema, value);
                },
            },
            {
                field: "required",
                title: t("Required"),
                width: "30ch",
                editComponent: ({value, onChange}: BaseEditComponentProps<Partial<DashboardWidgetParameter>>) => (
                    <Checkbox checked={value} onChange={e => onChange(e.target.checked)} color="default" />
                ),
                render: data => (data.required ? t("Yes") : t("No")),
            },
        ];
        return cols;
    }, [classes, t]);

    const handleEditionStateChanged = useCallback(
        editing => {
            vote(!editing);
        },
        [vote]
    );

    const editable = useMemo(() => {
        const editable: FormikArrayFieldEditorProps<Partial<DashboardWidgetParameter>>["editable"] = {
            onRowAdd: async newData => {
                if (typeof newData.required !== "boolean") {
                    newData.required = false;
                }
            },
        };
        return editable;
    }, []);

    return (
        <>
            <FormikArrayFieldEditor
                path={path}
                columns={columns}
                title=" "
                options={options}
                components={tableComponents}
                editable={editable}
                onEditionStateChanged={handleEditionStateChanged}
            />
            <FormikErrorViewer errors={errors} names={path} />
        </>
    );
};
