import React, {FC} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import {useTranslation} from "react-i18next";
import Toolbar from "@material-ui/core/Toolbar";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        button: {
            marginRight: theme.spacing(1),
        },
        content: {
            maxHeight: "60vh",
            overflowY: "auto",
        },
        grow: {
            flexGrow: 1,
        },
        toolbar: {
            minHeight: "48px",
            paddingLeft: 0,
            paddingRight: 0,
        },
    })
);

export interface StepContainerProps {
    onNext?: () => void;
    onBack?: () => void;
    onCancel: () => void;
    nextActionCaption?: string;
}

export const StepContainer: FC<StepContainerProps> = props => {
    const {onNext, onBack, onCancel, nextActionCaption, children} = props;
    const classes = useStyles();
    const {t} = useTranslation();

    return (
        <>
            <Grid container>
                <Grid item xs={12} className={classes.content}>
                    {children}
                </Grid>
            </Grid>
            <Grid container>
                <Grid item xs={12}>
                    <Toolbar className={classes.toolbar}>
                        <Button disabled={!onBack} onClick={onBack} className={classes.button}>
                            {t("Back")}
                        </Button>
                        {!!onNext && (
                            <Button onClick={onNext} className={classes.button}>
                                {nextActionCaption ?? t("Next")}
                            </Button>
                        )}
                        <div className={classes.grow} />
                        <Button onClick={onCancel} className={classes.button} color="primary">
                            {t("Cancel")}
                        </Button>
                    </Toolbar>
                </Grid>
            </Grid>
        </>
    );
};
