import {Selector} from "../../../api/nimflow-api";
import {ContextTypeNodeModel} from "./context-type-node-model";
import {TaggingType} from "../taggings";

export class SelectorNodeModel extends ContextTypeNodeModel<Selector> {
    constructor(instance: Partial<Selector> = {}, instanceId: string, public taggingType?: TaggingType) {
        super("selector", instance, instanceId, instance.key ?? "");
    }

    public serialize(): any {
        return {
            ...super.serialize(),
            taggingType: this.taggingType,
        }
    }
}
