import React, {FC, useCallback} from "react";
import {createStyles, makeStyles, Theme} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import Avatar from "@material-ui/core/Avatar";
import Badge from "@material-ui/core/Badge";
import {isPresent} from "ts-is-present";
import {usePopupState, bindMenu, bindTrigger} from "material-ui-popup-state/hooks";

import {TaggingTypeIcon, TaggingType, taggingType, getTaggingDescription} from "../taggings";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        smallAvatar: {
            width: 25,
            height: 25,
            border: (props: ContextTypeNodeTaggingProps) =>
                props.displayStandalone ? undefined : `2px solid ${theme.palette.primary.main}`,
            color: (props: ContextTypeNodeTaggingProps) =>
                props.displayStandalone ? theme.palette.text.primary : theme.palette.text.secondary,
            backgroundColor: (props: ContextTypeNodeTaggingProps) =>
                props.displayStandalone ? theme.palette.background.default : theme.palette.background.paper,
        },
        tagging: {
            width: 16,
            height: 16,
        },
    })
);

export interface ContextTypeNodeTaggingProps {
    value: TaggingType;
    onChange?: (value: TaggingType) => void;
    displayEmptyTagging?: boolean;
    displayStandalone?: boolean;
}

export const ContextTypeNodeTagging: FC<ContextTypeNodeTaggingProps> = props => {
    const {value, onChange, children, displayEmptyTagging} = props;
    const classes = useStyles(props);
    const popupState = usePopupState({
        variant: "popover",
        popupId: "taggingMenu",
    });

    const handleSelect = useCallback(
        (value: TaggingType) => {
            popupState.close();
            onChange?.(value);
        },
        [onChange, popupState]
    );

    return (
        <>
            <IconButton {...bindTrigger(popupState)} size="small">
                {(isPresent(value) || displayEmptyTagging) && (
                    <Badge
                        overlap="circular"
                        anchorOrigin={{
                            vertical: "bottom",
                            horizontal: "right",
                        }}
                        badgeContent={
                            <Avatar className={classes.smallAvatar}>
                                <TaggingTypeIcon taggingType={value} className={classes.tagging} />
                            </Avatar>
                        }
                    >
                        {children}
                    </Badge>
                )}
                {!isPresent(value) && children}
            </IconButton>
            <Menu {...bindMenu(popupState)}>
                {[undefined, ...taggingType].map((s, i) => {
                    return (
                        <MenuItem key={i} onClick={() => handleSelect(s)}>
                            <ListItemIcon>
                                <TaggingTypeIcon taggingType={s} fontSize="small" />
                            </ListItemIcon>
                            <Typography variant="inherit">{getTaggingDescription(s)}</Typography>
                        </MenuItem>
                    );
                })}
            </Menu>
        </>
    );
};
