import React, {FC, useCallback, useState, useRef, useMemo} from "react";
import {useFormikContext} from "formik";
import {useTranslation} from "react-i18next";
import Grid from "@material-ui/core/Grid";
import MenuItem from "@material-ui/core/MenuItem";
import TextField from "@material-ui/core/TextField";
import ListItemText from "@material-ui/core/ListItemText";
import Checkbox from "@material-ui/core/Checkbox";
import Typography from "@material-ui/core/Typography";
import RadioGroup from "@material-ui/core/RadioGroup";
import Radio from "@material-ui/core/Radio";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import {isPresent} from "ts-is-present";
import {remove, clone} from "ramda";
import {parseISO} from "date-fns";
import useSWR from "swr";
import {isPlainObject} from "is-plain-object";

import {
    SelectionDashboardParameter,
    DashboardParameterValue,
    BaseDashboardParameter,
    SelectionDashboardParameterDataSource,
    DashboardParameterState,
    DashboardParameterDataType,
    selectionDashboardParameterDataSourceTypes,
    SelectionDashboardParameterDataSourceType,
} from "../../api/nimflow-dashboard-api";
import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {FormikErrorViewer} from "../../components/formik-error-viewer";
import {FormikCheckbox} from "../../components/formik-components/formik-checkbox";
import {StaticDataSourceEditor} from "./static-data-source-editor";
import {FunctionDataSourceEditor} from "./function-data-source-editor";
import {useKey} from "../../utils/use-key";
import {useNimflowFunctions} from "../../api/nimflow-functions-api";

export const getValidSelectionDataTypes = (isMultiple: boolean) =>
    isMultiple ? ["string"] : ["string", "number", "boolean", "datetime"];

const useSelectionDashboardParameterDataSource = (ds: SelectionDashboardParameterDataSource) => {
    const {evaluateArgsAndCall} = useNimflowFunctions();
    const key = useKey(ds);
    const {data, error, isValidating, mutate} = useSWR(
        key,
        async () => {
            switch (ds.type) {
                case "static":
                    return ds.options;
                case "function":
                    if (!ds.moduleName || !ds.functionName) {
                        return [];
                    }
                    const data = await evaluateArgsAndCall(ds.moduleName, ds.functionName, ds.jsArguments);
                    if (!Array.isArray(data) || (data.length > 1 && !isPlainObject(data[0]))) {
                        return [];
                    }
                    const result: DashboardParameterValue[] = data.map(s => ({
                        value: s[ds.columns.value],
                        displayText: s[ds.columns.label ?? ds.columns.value],
                    }));
                    return result;
                default:
                    return [];
            }
        },
        {
            revalidateOnFocus: false,
        }
    );

    const result = useMemo(() => {
        return {
            data: data ?? [],
            error,
            isValidating,
            mutate,
        };
    }, [data, error, isValidating, mutate]);

    return result;
};

interface SingleSelectionDefaultValueEditorProps {
    value: DashboardParameterState;
    allowEmptySelection: boolean;
    dataSource: SelectionDashboardParameterDataSource;
    onChange: (defaultValue: DashboardParameterState) => void;
}

const SingleSelectionDefaultValueEditor: FC<SingleSelectionDefaultValueEditorProps> = props => {
    const {value, allowEmptySelection, dataSource: ds, onChange} = props;
    const {t} = useTranslation();
    const {data: selectionOptions} = useSelectionDashboardParameterDataSource(ds);

    const [selectedValue, setSelectedValue] = useState<number | undefined>(() => {
        if (value.selectAll) {
            return allowEmptySelection ? -1 : undefined;
        }
        const defvalue = value.values[0]?.value;
        const index = selectionOptions.findIndex(s => defvalue === s.value);
        return index === -1 ? undefined : index;
    });

    const handleChange = useCallback(
        (ev: React.ChangeEvent<any>, value: any) => {
            const index = value.props.value;
            setSelectedValue(index);
            if (index === -1) {
                onChange({
                    selectAll: true,
                    values: [],
                });
            } else {
                onChange({
                    selectAll: false,
                    values: [
                        {
                            value: selectionOptions[index].value,
                        },
                    ],
                });
            }
        },
        [onChange, selectionOptions]
    );

    return (
        <TextField
            label={t("DefaultValue")}
            select
            InputLabelProps={{required: true, shrink: true}}
            fullWidth
            required
            value={selectedValue}
            SelectProps={{
                displayEmpty: true,
                renderValue: value => {
                    if (!isPresent(value)) {
                        return t("NoDefaultValue");
                    }
                    if (value === -1) {
                        return t("All");
                    }
                    const defval = selectionOptions[value as any];
                    return defval?.displayText ?? defval?.value;
                },
                onChange: handleChange,
            }}
        >
            {allowEmptySelection && (
                <MenuItem key={-1} value={-1}>
                    {t("SelectAll")}
                </MenuItem>
            )}
            {selectionOptions.map((s, i) => (
                <MenuItem key={i} value={i}>
                    {s.displayText ?? s.value}
                </MenuItem>
            ))}
        </TextField>
    );
};

interface MultipleSelectionDefaultValueEditorProps {
    value: DashboardParameterState;
    dataSource: SelectionDashboardParameterDataSource;
    onChange: (defaultValue: DashboardParameterState) => void;
}

const MultipleSelectionDefaultValueEditor: FC<MultipleSelectionDefaultValueEditorProps> = props => {
    const {value, dataSource: ds, onChange} = props;
    const {t} = useTranslation();
    const {data: selectionOptions} = useSelectionDashboardParameterDataSource(ds);

    const [selectedValue, setSelectedValue] = useState<number[]>(() => {
        if (value.selectAll) {
            return [-1];
        }
        const defindices = value.values.map(s => selectionOptions.findIndex(o => o.value === s.value));
        return defindices.filter(s => s >= 0);
    });

    const handleChange = useCallback(
        (ev: React.ChangeEvent<any>, value: any) => {
            let index = value.props.value as number;
            if (index === -1) {
                setSelectedValue(s => {
                    const selectAll = !s.includes(-1);
                    onChange({
                        selectAll,
                        values: [],
                    });
                    return selectAll ? [-1] : [];
                });
            } else {
                setSelectedValue(s => {
                    const i = s.indexOf(index);
                    let result: number[];
                    if (i >= 0) {
                        result = remove(i, 1, s);
                    } else if (s.includes(-1)) {
                        result = selectionOptions.map((s, i) => i).filter(s => s !== index);
                    } else {
                        result = s.length + 1 === selectionOptions.length ? [-1] : [...s, index].sort();
                    }
                    if (result.includes(-1)) {
                        onChange({
                            selectAll: true,
                            values: [],
                        });
                    } else {
                        onChange({
                            selectAll: false,
                            values: result.map(index => ({
                                value: selectionOptions[index].value,
                            })),
                        });
                    }
                    return result;
                });
            }
        },
        [onChange, selectionOptions]
    );

    return (
        <TextField
            label={t("DefaultValue")}
            select
            InputLabelProps={{required: true, shrink: true}}
            fullWidth
            required
            value={selectedValue}
            SelectProps={{
                multiple: true,
                displayEmpty: true,
                renderValue: value => {
                    const values = value as number[];
                    if (values.length === 0) {
                        return t("NoDefaultValue");
                    }
                    if (values.includes(-1)) {
                        return t("All");
                    }
                    return values
                        .map(s => {
                            const defval = selectionOptions[s];
                            return defval.displayText ?? defval.value;
                        })
                        .join(", ");
                },
                onChange: handleChange,
            }}
        >
            <MenuItem key={-1} value={-1}>
                <Checkbox checked={selectedValue?.includes(-1)} color="default" />
                <ListItemText primary={t("SelectAll")} />
            </MenuItem>
            {selectionOptions.map((s, i) => (
                <MenuItem key={i} value={i}>
                    <Checkbox checked={selectedValue?.includes(-1) || selectedValue?.includes(i)} color="default" />
                    <ListItemText primary={s.displayText ?? s.value} />
                </MenuItem>
            ))}
        </TextField>
    );
};

const createDefaultDataSource = (
    type: SelectionDashboardParameterDataSourceType
): SelectionDashboardParameterDataSource | undefined => {
    switch (type) {
        case "static":
            return {
                type: "static",
                options: [],
            };
        case "function":
            return {
                type: "function",
                moduleName: "",
                functionName: "",
                jsArguments: "args = {\n\n};",
                columns: {
                    value: "",
                },
            };
    }
    return undefined;
};

export const SelectionParameterEditor: FC<{}> = props => {
    const {t} = useTranslation();
    const {values: param, setFieldValue, errors} = useFormikContext<SelectionDashboardParameter>();
    const ds = param.dataSource;
    const savedDataSourcesRef = useRef<Record<string, SelectionDashboardParameterDataSource>>({});

    const handleSingSelectionDefaultValueChange = useCallback(
        (value: BaseDashboardParameter["defaultValue"]) => {
            setFieldValue("defaultValue", value);
        },
        [setFieldValue]
    );

    const handleDataTypeChange = useCallback(
        (event: React.ChangeEvent<HTMLInputElement>) => {
            function setStaticDataSource(...data: DashboardParameterValue[]) {
                setFieldValue("dataSource", {
                    type: "static",
                    options: data,
                });
                savedDataSourcesRef.current = {};
            }
            function resetDataSource() {
                setStaticDataSource();
            }
            function resetDefaultValue() {
                setFieldValue("defaultValue", {
                    values: [],
                    selectAll: false,
                });
            }

            const newDataType = event.target.value as DashboardParameterDataType;
            const oldDataType = param.dataType;
            if (newDataType === oldDataType) {
                return;
            }
            setFieldValue("dataType", newDataType);
            if (oldDataType === "boolean") {
                resetDataSource();
                resetDefaultValue();
                return;
            }
            if (newDataType === "boolean") {
                setStaticDataSource({value: true}, {value: false});
                resetDefaultValue();
                return;
            }
            const ds = param.dataSource;
            if (ds.type === "function") {
                resetDataSource();
                resetDefaultValue();
            } else if (ds.type === "static") {
                if (ds.options.length === 0) {
                    return;
                }
                switch (newDataType) {
                    case "number": {
                        if (oldDataType === "string") {
                            const newds = clone(ds);
                            for (let i = newds.options.length - 1; i >= 0; i--) {
                                const opt = newds.options[i];
                                opt.value = parseFloat(opt.value as string);
                                if (isNaN(opt.value)) {
                                    newds.options.splice(i, 1);
                                }
                            }
                            setFieldValue("dataSource", newds);
                            if (param.defaultValue.values.length > 0) {
                                const valnum = parseFloat((param.defaultValue.values[0].value as string) ?? "");
                                if (isNaN(valnum)) {
                                    resetDefaultValue();
                                } else {
                                    setFieldValue("defaultValue.values[0].value", valnum);
                                }
                            }
                        } else {
                            resetDataSource();
                            resetDefaultValue();
                        }
                        break;
                    }
                    case "string": {
                        if (oldDataType === "number") {
                            const newds = clone(ds);
                            for (const val of newds.options) {
                                val.value = val.value.toString();
                            }
                            setFieldValue("dataSource", newds);
                            if (param.defaultValue.values.length > 0) {
                                setFieldValue(
                                    "defaultValue.values[0].value",
                                    param.defaultValue.values[0].value?.toString()
                                );
                            }
                        }
                        break;
                    }
                    case "datetime": {
                        if (oldDataType === "string") {
                            const newds = clone(ds);
                            for (let i = newds.options.length - 1; i >= 0; i--) {
                                const opt = newds.options[i];
                                try {
                                    parseISO(opt.value as string);
                                } catch {
                                    newds.options.splice(i, 1);
                                }
                            }
                            setFieldValue("dataSource", newds);
                        } else {
                            resetDataSource();
                        }
                        resetDefaultValue();
                        break;
                    }
                }
            }
        },
        [param.dataSource, param.dataType, param.defaultValue.values, setFieldValue]
    );

    const handleTypeChange = useCallback(
        (value: SelectionDashboardParameterDataSourceType) => {
            if (value === ds.type) {
                return;
            }
            savedDataSourcesRef.current[ds.type] = ds;
            const newDs = savedDataSourcesRef.current[value] ?? createDefaultDataSource(value);
            setFieldValue("dataSource", newDs);
        },
        [ds, setFieldValue]
    );

    return (
        <>
            <Grid item xs={12}>
                <FormikTextField
                    name="variableName"
                    label={t("VariableName")}
                    InputLabelProps={{required: true}}
                    fullWidth
                    required
                />
            </Grid>
            <Grid item xs={12}>
                <TextField
                    label={t("DataType")}
                    value={param.dataType}
                    onChange={handleDataTypeChange}
                    select
                    InputLabelProps={{required: true}}
                    fullWidth
                    required
                >
                    {getValidSelectionDataTypes(param.selectionType === "multiple").map(s => (
                        <MenuItem key={s} value={s}>
                            {s}
                        </MenuItem>
                    ))}
                </TextField>
            </Grid>
            <Grid item xs={12}>
                <Typography variant="h6">{t("SelectionDashboardParameterDataSource")}</Typography>
                <RadioGroup row>
                    {selectionDashboardParameterDataSourceTypes.map(s => (
                        <FormControlLabel
                            key={s}
                            control={<Radio checked={ds.type === s} onChange={() => handleTypeChange(s)} value={s} />}
                            label={t(`SelectionDashboardParameterDataSourceType.${s}`)}
                        />
                    ))}
                </RadioGroup>
            </Grid>
            {ds.type === "static" && <StaticDataSourceEditor />}
            {ds.type === "function" && <FunctionDataSourceEditor />}
            {param.selectionType === "single" && (
                <>
                    <FormikCheckbox name="allowEmptySelection" label={t("AddEmptySelection")} />
                    <SingleSelectionDefaultValueEditor
                        value={param.defaultValue}
                        allowEmptySelection={param.allowEmptySelection}
                        dataSource={param.dataSource}
                        onChange={handleSingSelectionDefaultValueChange}
                    />
                </>
            )}
            {param.selectionType === "multiple" && (
                <MultipleSelectionDefaultValueEditor
                    value={param.defaultValue}
                    dataSource={param.dataSource}
                    onChange={handleSingSelectionDefaultValueChange}
                />
            )}
            <FormikErrorViewer errors={errors} names={["defaultValue", "defaultValue.values"]} />
        </>
    );
};
