import { DashboardWidgetDefOrJsSpec } from './../../api/nimflow-dashboard-widgets-api';
import {clone} from "ramda";

import {resolveDashboardWidgetDefOrJsSpecSchema} from "./widget-definition-schema";
import {mapAjvErrors, ImportResult, importFailure, importSuccess} from "../../models/importers";
import {isValidationFailure, validateWidgetDefinitionSchema} from "../../libs/ajv";
import {validateWithYupSchema} from "../../libs/yup";

export const importWidgetDefinitionFromJson = async (json: any): Promise<ImportResult<DashboardWidgetDefOrJsSpec>> => {
    const result = validateWidgetDefinitionSchema(json);
    if (isValidationFailure(result)) {
        return importFailure(mapAjvErrors(result.errors));
    }
    const widgetDefOrJsSpec = clone(json) as DashboardWidgetDefOrJsSpec;
    delete (widgetDefOrJsSpec as any)["$schema"];
    const einfos = await validateWithYupSchema(resolveDashboardWidgetDefOrJsSpecSchema(widgetDefOrJsSpec), widgetDefOrJsSpec);
    if (einfos.length > 0) {
        return importFailure(einfos);
    }
    return importSuccess(widgetDefOrJsSpec);
};
