const validEventTypes = [...contextEventTypes, customEventType, blobStorageUpdatedType];

const InnerEventListenerEditor: FC<InnerEventListenerEditorProps> = props => {
    const {schema = GenericEventListenerSchema, jsParametersErrors, onSave, onJsParametersErrorChange} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const navigate = useNavigate();
    const {enqueueSnackbar} = useSnackbar();
    const {
        isSubmitting,
        errors,
        validateForm,
        values: eventListener,
        dirty,
        setFieldValue,
    } = useFormikContext<GenericEventListener>();
    const editionScope = useEditionScope();
    const canSave = !isSubmitting && editionScope.canAccept;
    const reportErrors = useReportError();
    const {functionInfos} = useFunctionInfos();

    const opts = useMemo(() => {
        return {
            description: t("ExitingEventListenerEditorWithoutSavingWarning"),
        };
    }, [t]);
    useExitWithoutSavingBlocker(dirty, opts);

    const handleSaveEventListenerClick = useThrottleCallback(
        useCallback(async () => {
            if (onSave) {
                await onSave();
            }
        }, [onSave])
    );

    const handleValidateEventListenerClick = useCallback(async () => {
        const errorInfos = await validateWithYupSchema(schema, eventListener);
        if (jsParametersErrors.length > 0) {
            if (!errorInfos.find(s => s.dataPath === "call.jsParametersBinding")) {
                errorInfos.push({
                    dataPath: "call.jsParametersBinding",
                    message: jsParametersErrors.map(s => "\n\t" + s).join(""),
                });
            }
        }
        await validateForm();
        if (errorInfos.length > 0) {
            reportErrors(t("EventListenerHasValidationErrors"), errorInfos, {
                dialogProps: {
                    maxWidth: "lg",
                },
            });
        } else {
            enqueueSnackbar(t("EventListenerIsValid"), {variant: "success"});
        }
    }, [schema, eventListener, jsParametersErrors, validateForm, reportErrors, t, enqueueSnackbar]);

    const handleGoBack = useCallback(() => {
        navigate(-1);
    }, [navigate]);

    const functionInfo = useMemo(() => {
        const call = eventListener.call;
        if (!call) {
            return undefined;
        }
        const key = `${call.moduleName}/${call.functionName}`;
        return functionInfos[key];
    }, [eventListener.call, functionInfos]);

    const freeVariableTypingProviders: Record<string, () => string | Promise<string>> = useMemo(() => {
        return getEventListenerTypingsProvider(eventListener, functionInfo);
    }, [eventListener, functionInfo]);

    const handleEventTypeChange = useCallback(() => {
        if (eventListener.event !== customEventType) {
            if ("eventName" in eventListener) {
                setFieldValue("eventName", undefined);
            }
            if ("category" in eventListener) {
                setFieldValue("category", undefined);
            }
        }
        if (eventListener.event !== responseSentEventType && "responseTypeName" in eventListener) {
            setFieldValue("responseTypeName", undefined);
        }
        if (!isContextTaskEventListener(eventListener) && "taskTypeName" in eventListener) {
            setFieldValue("taskTypeName", undefined);
        }
        if (!isMilestoneEventListener(eventListener) && "milestoneName" in eventListener) {
            setFieldValue("milestoneName", undefined);
        }
        if (eventListener.event !== actionDispatchedEventType && "action" in eventListener) {
            setFieldValue("action", undefined);
        }
    }, [eventListener, setFieldValue]);

    const handleContextTypeChange = useCallback(
        (value?: string) => {
            if (isContextTaskEventListener(eventListener)) {
                setFieldValue("taskTypeName", undefined);
                if (eventListener.event === responseSentEventType) {
                    setFieldValue("responseTypeName", undefined);
                }
            }
            if (isMilestoneEventListener(eventListener)) {
                setFieldValue("milestoneName", undefined);
            }
            if (eventListener.event === actionDispatchedEventType) {
                setFieldValue("action", undefined);
            }
        },
        [eventListener, setFieldValue]
    );

    const handleTaskTypeChange = useCallback(
        (value?: string) => {
            if (eventListener.event === responseSentEventType) {
                setFieldValue("responseTypeName", undefined);
            }
        },
        [eventListener, setFieldValue]
    );

    return (
        <EditionScopeProvider {...editionScope}>
            <JsContextDefinitionsContextProvider freeVariableTypingProviders={freeVariableTypingProviders}>
                <div className={classes.table}>
                    <div className={classes.tableRow}>
                        <Grid container spacing={3}>
                            <Grid item xs={12}>
                                <Toolbar className={classes.toolbar}>
                                    <Tooltip title={t("GoBack") as string}>
                                        <IconButton onClick={handleGoBack} disabled={isSubmitting}>
                                            <ArrowBackIcon />
                                        </IconButton>
                                    </Tooltip>
                                    <Tooltip title={t("SaveChanges") as string}>
                                        <IconButton onClick={handleSaveEventListenerClick} disabled={!canSave}>
                                            <SaveIcon />
                                        </IconButton>
                                    </Tooltip>
                                    <Tooltip title={t("Validate") as string}>
                                        <IconButton onClick={handleValidateEventListenerClick}>
                                            <AssignmentTurnedInIcon />
                                        </IconButton>
                                    </Tooltip>
                                </Toolbar>
                            </Grid>
                            <Grid item xs={3}>
                                <FormikTextField
                                    name="event"
                                    label={t("EventType")}
                                    select
                                    className={classes.textField}
                                    InputLabelProps={{required: true}}
                                    onChange={handleEventTypeChange}
                                    required
                                >
                                    {validEventTypes.map(s => (
                                        <MenuItem key={s} value={s}>
                                            {t(s)}
                                        </MenuItem>
                                    ))}
                                </FormikTextField>
                            </Grid>
                            <Grid item xs={3}>
                                {isContextEventListener(eventListener) && (
                                    <ContextTypeNameSelect onChange={handleContextTypeChange} />
                                )}
                                {eventListener.event === customEventType && (
                                    <FormikTextField
                                        name="eventName"
                                        label={t("EventName")}
                                        className={classes.textField}
                                    />
                                )}
                            </Grid>
                            <Grid item xs={3}>
                                {isContextTaskEventListener(eventListener) && (
                                    <TaskNameSelect
                                        contextTypeName={eventListener.contextTypeName}
                                        onChange={handleTaskTypeChange}
                                    />
                                )}
                                {isMilestoneEventListener(eventListener) && (
                                    <MilestoneNameSelect contextTypeName={eventListener.contextTypeName} />
                                )}
                                {eventListener.event === actionDispatchedEventType && (
                                    <ActionNameSelect contextTypeName={eventListener.contextTypeName} />
                                )}
                                {eventListener.event === customEventType && (
                                    <FormikTextField
                                        name="category"
                                        label={t("Category")}
                                        className={classes.textField}
                                    />
                                )}
                            </Grid>
                            <Grid item xs={3}>
                                {eventListener.event === responseSentEventType && (
                                    <ResponseNameSelect
                                        contextTypeName={eventListener.contextTypeName}
                                        taskTypeName={eventListener.taskTypeName}
                                    />
                                )}
                            </Grid>
                            <Grid item xs={3}>
                                <FunctionSelect />
                            </Grid>
                            <Grid item xs={9}>
                                {functionInfo?.description}
                            </Grid>
                            <Grid item xs={12}>
                                <Typography variant="subtitle1">{t("JsParametersBinding")}</Typography>
                                <JsParametersEditor onErrorChange={onJsParametersErrorChange} />
                            </Grid>
                        </Grid>
                    </div>
                    {/* {JSON.stringify(eventListener)} */}
                    <FormikErrorViewer errors={errors} names={["id"]} className={classes.tableRow} />
                </div>
            </JsContextDefinitionsContextProvider>
        </EditionScopeProvider>
    );
};

export interface EventListenerEditorProps {
    value: GenericEventListener;
    schema?: yup.ObjectSchema<any>;
    onSave: (value: GenericEventListener) => Promise<boolean>;
}

export const EventListenerEditor: FC<EventListenerEditorProps> = props => {
    const {value, onSave} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const editionScope = useEditionScope();
    const confirm = useConfirm();
    const {enqueueSnackbar} = useSnackbar();
    const [jsParametersErrors, setJsParametersErrors] = useState<string[]>([]);
    const initialValues = useMemo(() => {
        return value;
    }, [value]);
    const formRef = useRef<FormikProps<GenericEventListener> | null>();

    useEffect(() => {
        formRef.current?.resetForm({values: initialValues});
    }, [initialValues]);

    const internalValidateAndSubmit = useCallback(
        async (form: FormikProps<GenericEventListener>) => {
            if (jsParametersErrors.length > 0) {
                await validateForm(form);
                form.setFieldError("call.jsParametersBinding", jsParametersErrors.join("\n"));
                return;
            }
            validateAndSubmit(form);
        },
        [jsParametersErrors]
    );

    const submitHandlerRef = useRef<(values: GenericEventListener) => Promise<void>>();

    const handleSave = useCallback(() => {
        const form = formRef.current;
        if (form && onSave) {
            submitHandlerRef.current = async (data: GenericEventListener) => {
                let error: React.ReactNode;
                try {
                    const saved = await onSave(data);
                    if (!saved) {
                        return;
                    }
                } catch (e) {
                    error = <ErrorViewer error={e} />;
                }
                if (error) {
                    await confirm({
                        title: t("ErrorSavingEventListener"),
                        description: error,
                        mapCloseTo: "ok",
                        actions: [{type: "ok", text: t("Ok")}],
                    });
                } else {
                    enqueueSnackbar(t("EventListenerSaved"), {variant: "success"});
                }
            };
            internalValidateAndSubmit(form);
        }
        return Promise.resolve();
    }, [confirm, enqueueSnackbar, internalValidateAndSubmit, onSave, t]);

    const handleSubmit = useCallback(
        async (values: GenericEventListener, actions: FormikHelpers<GenericEventListener>) => {
            try {
                if (submitHandlerRef.current) {
                    await submitHandlerRef.current(values);
                    submitHandlerRef.current = undefined;
                }
            } finally {
                actions.setSubmitting(false);
            }
        },
        []
    );

    return (
        <ErrorBoundary>
            <Formik<GenericEventListener>
                innerRef={s => (formRef.current = s)}
                initialValues={initialValues}
                onSubmit={handleSubmit}
                validationSchema={GenericEventListenerSchema}
                validateOnChange={true}
                validateOnBlur={true}
            >
                <Form autoComplete="off" className={classes.form}>
                    <EditionScopeProvider {...editionScope}>
                        <InnerEventListenerEditor
                            {...props}
                            onSave={handleSave}
                            jsParametersErrors={jsParametersErrors}
                            onJsParametersErrorChange={setJsParametersErrors}
                        />
                    </EditionScopeProvider>
                </Form>
            </Formik>
        </ErrorBoundary>
    );
};
