import {DefaultLinkModel, DefaultLinkModelOptions} from "@projectstorm/react-diagrams";
import {DeserializeEvent} from "@projectstorm/react-canvas-core";
import {ContextTypeNodeModel} from "./context-type-node-model";
export interface ContextTypeLinkModelOptions extends DefaultLinkModelOptions {
    selectedOutgoingColor?: string;
}

export class ContextTypeLinkModel extends DefaultLinkModel {
    constructor(options: ContextTypeLinkModelOptions = {}) {
        super({
            type: "contextTypeLink",
            width: 2,
            selectedOutgoingColor: options.selectedOutgoingColor || "lime",
        } as DefaultLinkModelOptions);
    }

    public getInstanceId(): string {
        const sourceNode = this.sourcePort?.getNode();
        const targetNode = this.sourcePort?.getNode();
        if (sourceNode instanceof ContextTypeNodeModel && targetNode instanceof ContextTypeNodeModel) {
            return `edge:${sourceNode.instanceId}->${targetNode.instanceId}`;
        } else {
            return "UnknownEdge";
        }
    }

    public getSelectedOutgoingColor() {
        return (this.options as any).selectedOutgoingColor;
    }

    serialize() {
        return {
            ...super.serialize(),
            selectedOutgoingColor: this.getSelectedOutgoingColor(),
        };
    }

    deserialize(event: DeserializeEvent<this>) {
        super.deserialize(event);
        (this.options as any).selectedOutgoingColor = event.data.selectedOutgoingColor;
    }
}
