import langs from "./iso/langs";
import scripts from "./iso/scripts";
import countries from "./iso/countries";
import codelist from "./iso/codelist";

export const getPossibleFullQualifiedLocales = (lng: string) => {
    const res = codelist.filter(l => l.indexOf(`${lng}-`) === 0);
    if (res.length) return res;
    return null;
};

const getLanguagePartFromCode = (code: string) => {
    if (!code || code.indexOf("-") < 0) return code;

    const p = code.split("-");
    return p[0].toLowerCase();
};

export interface LangName {
    lngName?: string;
    scriptName?: string;
    countryName?: string;
    asArray?: string[];
    result?: string;
}

export interface LangCode {
    code: string; 
    names: LangName;
}

let builtCodes: LangCode[] | undefined;
export const getCodes = (): LangCode[] => {
    if (builtCodes) {
        return builtCodes;
    }

    builtCodes = codelist.reduce((mem, c) => {
        mem.push({
            code: c,
            names: getNames(c),
        });
        return mem;
    }, [] as LangCode[]);

    return builtCodes;
};

export const getNames = (lng: string): LangName => {
    const ret: LangName = {};
    const comb = [];
    const p = lng.split("-");

    if (p[0]) {
        const l = langs[p[0]];
        if (l) {
            ret.lngName = l.name;
            comb.push(l.name);
        }
    }

    if (p[1]) {
        const c = countries[p[1]];
        const s = scripts[p[1]];
        if (s) {
            ret.scriptName = s;
            comb.push(s);
        }
        if (!s && c) {
            ret.countryName = c;
            comb.push(c);
        }
    }

    if (p[2]) {
        const c = countries[p[2]];
        const s = scripts[p[2]];
        if (s) {
            ret.scriptName = s;
            comb.push(s);
        }
        if (!s && c) {
            ret.countryName = c;
            comb.push(c);
        }
    }

    ret.asArray = comb;
    ret.result = comb.join(" - ");

    return ret;
};

export class LanguageCollector {
    constructor() {
        this.lngs = {};
    }

    private lngs: any;

    public add(l: string | string[]) {
        if (typeof l === "string") {
            this.lngs[l] = true;
            return;
        }
        l.forEach(lng => {
            this.lngs[lng] = true;
        });
    }

    public has(l: string) {
        return this.lngs[l];
    }

    public getAll(): string[] {
        return Object.keys(this.lngs);
    }

    public getAllRelatedTo(l: string) {
        const lRoot = getLanguagePartFromCode(l);

        return this.getAll().reduce((mem, lng) => {
            if (getLanguagePartFromCode(lng) === lRoot) mem.push(lng);
            return mem;
        }, [] as string[]);
    }
}
