import React, {FC, useEffect, useRef} from "react";
import {makeStyles} from "@material-ui/core/styles";
import clsx from "clsx";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";

import {IconInfo} from "./icon-service";
import {MaterialFontIcon} from "../material-font-icon";
import {IconFamily} from "../../api/nimflow-api-extensions";

const useStyles = makeStyles(theme => ({
    container: {
        alignContent: "start",
        display: "grid",
        columnGap: 16,
        rowGap: 24,
        gridTemplateColumns: "repeat(auto-fill,112px)",
        justifyContent: "space-between",
        overflow: "auto",
    },
    iconButton: {
        position: "relative",
        height: 128,
        width: 112,
        borderRadius: 4,
        "&:hover": {
            backgroundColor: theme.palette.grey[300],
        },
    },
    icon: {
        display: "block",
        fontSize: 40,
        left: "50%",
        position: "absolute",
        top: 40,
        transform: "translate(-50%,-50%)",
        maxWidth: 88,
    },
    iconTitle: {
        bottom: 20,
        boxSizing: "border-box",
        left: "50%",
        padding: "0 8px",
        position: "absolute",
        transform: "translate(-50%,50%)",
        textAlign: "center",
        width: "100%",
    },
    selected: {
        position: "absolute",
        top: 10,
        right: 10,
        color: "green",
    },
}));

export interface IconsProps {
    icons: IconInfo[];
    family?: IconFamily;
    selected?: string;
    onIconSelect?: (name: string | undefined) => void;
}

interface IconsEntryProps {
    icon: IconInfo;
    family?: IconFamily;
    isSelected: boolean;
}

const IconsEntry: FC<IconsEntryProps> = props => {
    const {icon, family, isSelected} = props;
    const classes = useStyles();

    return (
        <div className={clsx(classes.iconButton, "iconButton")} data-iconname={icon.name}>
            <MaterialFontIcon family={family} tabIndex={-1} title={icon.title} className={classes.icon}>
                {icon.name}
            </MaterialFontIcon>
            <p className={classes.iconTitle}>{icon.title}</p>
            {isSelected && (
                <div className={classes.selected}>
                    <CheckCircleIcon />
                </div>
            )}
        </div>
    );
};

export const Icons: FC<IconsProps> = React.memo(function Icons(props: IconsProps) {
    const {icons, family, selected, onIconSelect} = props;
    const classes = useStyles();
    const divRef = useRef<HTMLDivElement | null>(null);
    const selectedRef = useRef<string | undefined>(selected);

    useEffect(() => {
        selectedRef.current = selected;
    }, [selected]);

    useEffect(() => {
        const div = divRef.current;
        if (!div) {
            return;
        }

        // Only add 1 listener for all icons
        function listener(this: HTMLDivElement, event: MouseEvent) {
            event.preventDefault();
            event.stopPropagation();
            let target: HTMLElement | null = event.target as any;
            while (target && !target.classList.contains("iconButton")) {
                target = target.parentElement;
            }
            if (!target) {
                return;
            }
            const iconName = target.dataset?.iconname;
            onIconSelect?.(iconName === selectedRef.current ? undefined : iconName);
        }
        div.addEventListener("click", listener, {
            capture: true,
        });
        return div.removeEventListener("click", listener);
    }, [onIconSelect]);

    return (
        <div ref={divRef} className={classes.container}>
            {icons.map(icon => {
                return <IconsEntry key={icon.name} icon={icon} isSelected={icon.name === selected} family={family} />;
            })}
        </div>
    );
});
