import React, {FC, useCallback, useMemo} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import {useAsync} from "react-use";
import {useTranslation} from "react-i18next";
import {sortBy} from "ramda";
import {KeyboardDatePicker} from "@material-ui/pickers";
import {useForm, FormProvider, Controller} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import * as yup from "yup";
import {parse, formatISO} from "date-fns";

import {SimulationStateSelectorStepProps} from "./simulation-data-selector";
import {StepContainer} from "./step-container";
import {useNimflow} from "../../../api/nimflow-api-provider";
import {useReportError} from "../../../utils/use-report-error";
import {SearchContexts} from "../../../api/nimflow-api";
import {AutocompleteController} from "../../../components/autocomplete-controller";
import {FormErrorViewer} from "../../../components/form-error-viewer";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            width: "50%",
            margin: "0 auto",
        },
        form: {
            height: "100%",
        },
    })
);

const schema = yup.object().shape({
    typeName: yup.string().required(),
    id: yup.string(),
    reference: yup.string(),
    modifiedOnOrAfter: yup.string().iso8601Formatted(),
});

export const ContextFilterStep: FC<SimulationStateSelectorStepProps> = props => {
    const {data: initialData, onNext, onCancel} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const reportError = useReportError();
    const {contextTypes_Search} = useNimflow();

    const formMethods = useForm<SearchContexts>({
        mode: "onChange",
        reValidateMode: "onChange",
        defaultValues: initialData.searchFilter ?? {},
        resolver: yupResolver(schema, {
            strict: true,
            abortEarly: false,
        }),
    });
    const {control, handleSubmit, errors, trigger} = formMethods;

    const {value: contextTypeInfos} = useAsync(async () => {
        try {
            const contextTypes = await contextTypes_Search({
                limit: 100,
            });
            return contextTypes;
        } catch (e) {
            reportError(t("ErrorRetrievingContextTypes"), e);
            return [];
        }
    });

    const options = useMemo(() => {
        return contextTypeInfos
            ? sortBy(
                  s => s.toLowerCase(),
                  contextTypeInfos.map(s => s.name)
              )
            : [];
    }, [contextTypeInfos]);

    const handleNext = useCallback(async () => {
        const result = await trigger();
        if (!result) {
            return;
        }
        handleSubmit(data => {
            onNext({...initialData, searchFilter: data});
        })();
    }, [handleSubmit, initialData, onNext, trigger]);

    return (
        <StepContainer onNext={handleNext} onCancel={onCancel}>
            <FormProvider {...formMethods}>
                <form className={classes.form}>
                    <Grid container alignItems="center" spacing={3} className={classes.root}>
                        <Grid item xs={12}>
                            <AutocompleteController
                                control={control}
                                name="typeName"
                                options={options}
                                size="small"
                                fullWidth
                                renderInput={params => <TextField {...params} label={t("ContextTypeName")} required autoFocus />}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <Controller
                                as={TextField}
                                control={control}
                                name="id"
                                label={t("ContextId")}
                                error={!!errors.id}
                                helperText={errors.id?.message}
                                fullWidth
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <Controller
                                as={TextField}
                                control={control}
                                name="reference"
                                label={t("Reference")}
                                error={!!errors.reference}
                                helperText={errors.reference?.message}
                                fullWidth
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <Controller
                                control={control}
                                name="modifiedOnOrAfter"
                                render={({name, value, onChange}) => {
                                    return (
                                        <KeyboardDatePicker
                                            name={name}
                                            label={t("ModifiedOnOrAfter")}
                                            value={value ?? null}
                                            onChange={(date, value) =>
                                                onChange(
                                                    value
                                                        ? formatISO(parse(value, t("DATE_FORMAT"), new Date()))
                                                        : undefined
                                                )
                                            }
                                            format={t("DATE_FORMAT")}
                                            variant="inline"
                                            disableToolbar
                                            error={!!errors.reference}
                                            helperText={errors.reference?.message}
                                            margin="normal"
                                            fullWidth
                                        />
                                    );
                                }}
                            />
                        </Grid>
                    </Grid>
                    <FormErrorViewer errors={errors} />
                </form>
            </FormProvider>
        </StepContainer>
    );
};
