import React, {createContext, FC, useContext, useMemo, useCallback} from "react";

interface JsContextDefinitionsService {
    parentContext?: JsContextDefinitionsService;
    freeVariableTypingProviders?: Record<string, () => string | Promise<string>>;
    jsCommonTypingProvider?: () => string | Promise<string>;
    getTypingForFreeVariable(variable: string): string | Promise<string> | undefined;
    getJsCommonTypings(): string | Promise<string> | undefined;
}

const initialContext: JsContextDefinitionsService = {
    freeVariableTypingProviders: {},
    getTypingForFreeVariable: () => "",
    getJsCommonTypings: () => "",
};

const JsContextDefinitionsContext = createContext<JsContextDefinitionsService>(initialContext);

export interface JsContextDefinitionsContextProviderProps {
    freeVariableTypingProviders?: Record<string, () => string | Promise<string>>;
    jsCommonTypingProvider?: () => string | Promise<string>;
}

export const useJsContextDefinitionsContext = () => useContext(JsContextDefinitionsContext);

export const JsContextDefinitionsContextProvider: FC<JsContextDefinitionsContextProviderProps> = props => {
    const {freeVariableTypingProviders, jsCommonTypingProvider, children} = props;

    const parentContext = useJsContextDefinitionsContext();
    const parent = parentContext !== initialContext ? parentContext : undefined;
    const getJsCommonTypings = useCallback(() => {
        if (jsCommonTypingProvider) {
            return jsCommonTypingProvider();
        }
        if (parent) {
            return parent.getJsCommonTypings();
        }
        return undefined;
    }, [jsCommonTypingProvider, parent]);
    
    const getTypingForFreeVariable = useCallback((variable: string) => {
        if (freeVariableTypingProviders) {
            if (variable in freeVariableTypingProviders) {
                return freeVariableTypingProviders[variable]();
            }
        }
        if (parent) {
            return parent.getTypingForFreeVariable(variable);
        }
        return undefined;
    }, [freeVariableTypingProviders, parent]);

    const jsContextDefinitionsContext = useMemo(() => {
        const result: JsContextDefinitionsService = {
            parentContext: parent,
            freeVariableTypingProviders,
            jsCommonTypingProvider,
            getTypingForFreeVariable,
            getJsCommonTypings,
        };
        return result;
    }, [parent, freeVariableTypingProviders, jsCommonTypingProvider, getTypingForFreeVariable, getJsCommonTypings]);

    return (
        <JsContextDefinitionsContext.Provider value={jsContextDefinitionsContext}>
            {children}
        </JsContextDefinitionsContext.Provider>
    );
};
