import {
    SimulationContextTypeProvider,
    newImportedContextTypeProvider,
    newServerContextTypeProvider,
} from "./simulation-context-type-provider";
import i18next from "i18next";
import {ImportResult, mapAjvErrors, importFailure, importSuccess} from "../../models/importers";
import {ContextSimulationSchema, contextTypeKey} from "./context-simulation-schema";
import {isValidationFailure, validateSimulationStateSchema} from "../../libs/ajv";
import {validateWithYupSchema} from "../../libs/yup";
import {jsCommonScriptKey, jsonSchemaScriptKey} from "../../models/context-type-schema";
import {ContextType} from "../../api/nimflow-api";
import {ContextSimulation} from "../../api/nimflow-simulation-api";

export const importSimulationStateFromJson = async (
    json: any,
    contextTypeResolver: (name: string) => Promise<ContextType>
): Promise<ImportResult<[ContextSimulation, SimulationContextTypeProvider]>> => {
    const result = validateSimulationStateSchema(json);
    if (isValidationFailure(result)) {
        return importFailure(mapAjvErrors(result.errors));
    }
    const simulation = json as ContextSimulation;
    delete (simulation as any)["$schema"];
    let contextType = simulation.contextType;
    let contextTypeProvider: SimulationContextTypeProvider;
    if (contextType) {
        contextTypeProvider = newImportedContextTypeProvider(contextType);
    } else {
        contextType = await contextTypeResolver(simulation.contextTypeName!);
        if (!contextType) {
            return importFailure([i18next.t("InvalidContextTypeName", {contextTypeName: json.contextTypeName})]);
        }
        contextTypeProvider = await newServerContextTypeProvider(
            simulation.contextTypeName!,
            contextTypeResolver,
            contextType
        );
    }
    simulation.history = simulation.history ?? [];
    simulation.pendingEvents = simulation.pendingEvents ?? [];
    const einfos = await validateWithYupSchema(ContextSimulationSchema, simulation, {
        context: {
            [contextTypeKey]: contextType,
            [jsCommonScriptKey]: contextType.jsCommon,
            [jsonSchemaScriptKey]: contextType.jsonSchema,
        },
    })
    if (einfos.length > 0) {
        return importFailure(einfos);
    }
    return importSuccess([simulation, contextTypeProvider]);
};
