import React, {FC, useState, useEffect, useCallback, useMemo} from "react";
import {useParams, useNavigate} from "react-router-dom";
import {clone} from "ramda";
import * as yup from "yup";
import {useTranslation} from "react-i18next";

import {DashboardDefinition, useNimflowDashboards} from "../../api/nimflow-dashboard-api";
import {DashboardDefinitionEditor} from "./dashboard-definition-editor";
import {DashboardDefinitionSchema} from "./dashboard-definition-schema";

export const DashboardDefinitionUpdater: FC<{}> = () => {
    const {name} = useParams();
    const navigate = useNavigate();
    const {t} = useTranslation();
    const [dashboardDefinition, setDashboardDefinition] = useState<DashboardDefinition>();
    const {dashboards_FindOne, dashboards_Replace, dashboards_InsertOne, dashboards_GetInfos} = useNimflowDashboards();

    useEffect(() => {
        dashboards_FindOne(name).then(s => {
            setDashboardDefinition(s);
        });
    }, [name, dashboards_FindOne]);

    const handleOnChange = useCallback(
        async (value: DashboardDefinition) => {
            await dashboards_Replace(value);
            setDashboardDefinition(value);
        },
        [setDashboardDefinition, dashboards_Replace]
    );

    const handleOnSaveAs = useCallback(
        async (value: DashboardDefinition, newName: string) => {
            const newDashboardDefinition = clone(value);
            newDashboardDefinition.name = newName;
            await dashboards_InsertOne(newDashboardDefinition);
            setDashboardDefinition(undefined);
            navigate(`../${newName}`, {replace: true});
        },
        [dashboards_InsertOne, navigate]
    );

    const DashboardDefinitionUpdaterSchema = useMemo(
        () =>
            DashboardDefinitionSchema.shape({
                name: DashboardDefinitionSchema.fields!.name.concat(
                    yup
                        .string()
                        .defined()
                        .test({
                            message: ({value}) => t("DashboardDefinitionNameAlreadyExists", {name: value}),
                            test: async function (value: any) {
                                if (!value) {
                                    return true;
                                }
                                const infos = await dashboards_GetInfos();
                                return !infos.some(s => s.name.toLowerCase() === value.toLowerCase() && s.id !== this.parent.id);
                            },
                        })
                ),
            }),
        [dashboards_GetInfos, t]
    );

    if (!dashboardDefinition) {
        return null;
    }

    return (
        <DashboardDefinitionEditor
            value={dashboardDefinition}
            onApplyChanges={handleOnChange}
            onSaveAs={handleOnSaveAs}
            schema={DashboardDefinitionUpdaterSchema}
            isPersistent={true}
        />
    );
};
