import React, {FC, useCallback, useState, useMemo, useEffect} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Toolbar from "@material-ui/core/Toolbar";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import {useTranslation} from "react-i18next";
import {v4 as uuid} from "uuid";
import GridLayout from "react-grid-layout";
import {isPresent} from "ts-is-present";
import {sort, uniq, clone, equals} from "ramda";
import {useFormikContext} from "formik";
import {useResizeDetector} from "react-resize-detector";

import {ErrorBoundary} from "../../components/error-boundary";
import {DashboardWidgetSelector} from "./dashboard-widget-selection-dialog";
import {useNimflowDashboardWidgets, DashboardWidgetDefinition} from "../../api/nimflow-dashboard-widgets-api";
import {
    DashboardWidgetInstantiation,
    DashboardDefinition,
    DashboardParameter,
    DashboardResponsiveLayoutSet,
    DashboardResponsiveLayout,
    getVariableNames,
    getParameterVariablesType,
} from "../../api/nimflow-dashboard-api";
import {DashboardWidgetInstanceEditorDialog} from "./dashboard-widget-instance-editor-dialog";
import {DashboardDefinitionLayoutGridEditorDialog} from "./dashboard-definition-layout-grid-editor-dialog";
import {FormikErrorViewer} from "../../components/formik-error-viewer";
import {WidgetInstanceViewer} from "./dashboard-widget-instance-viewer";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            flexGrow: 1,
        },
        toolbar: {
            minHeight: "48px",
            paddingLeft: 0,
        },
        paper: {
            padding: theme.spacing(2),
            color: theme.palette.text.secondary,
        },
        grow: {
            flexGrow: 1,
        },
        actionToolbarButtons: {
            display: "flex",
        },
        textField: {
            width: "40ch",
        },
        layoutSelector: {
            width: "20ch",
        },
        button: {
            margin: theme.spacing(1),
        },
        diagram: {
            marginTop: 20,
            marginBottom: 20,
            height: "calc(100% - 40px)",
        },
        table: {
            display: "table",
            width: "100%",
            height: "100%",
            minWidth: 400,
        },
        tableRow: {
            display: "table-row",
        },
        layoutTableRow: {
            display: "table-row",
            height: "100%",
        },
    })
);

interface LayoutParams {
    rowHeight: number;
    colWidth: number;
    cols: number;
    width: number;
    margin: [number, number];
}

const useLayoutGridStyles = makeStyles((theme: Theme) => {
    const gridColor = theme.palette.type === "light" ? "rgba(0,0,0,.2)" : "rgba(255,255,255,.2)";
    const handleColor = theme.palette.type === "light" ? "rgba(0,0,0,.5)" : "rgba(255,255,255,.5)";
    return createStyles({
        layoutGridContainer: {
            backgroundColor: theme.palette.background.default,
            borderRadius: theme.shape.borderRadius,
            width: "100%",
            position: "relative",
        },
        layoutInnerGridContainer: {
            position: "absolute",
            top: 0,
            left: 0,
            bottom: 0,
            right: 0,
            overflowX: "auto",
        },
        layoutGrid: {
            backgroundImage: `linear-gradient(${gridColor} 1px, transparent 1px), linear-gradient(90deg, ${gridColor} 1px, transparent 1px)`,
            backgroundSize: (props: LayoutParams) => {
                const width = Math.floor(props.colWidth);
                const height = props.rowHeight;
                const [mh, mv] = props.margin;
                return `${width + mh}px ${height + mv}px, ${width + mh}px ${height + mv}px`;
            },
            width: (props: LayoutParams) => props.width,
            backgroundPosition: (props: LayoutParams) => {
                let [mh, mv] = props.margin;
                mh = Math.round(mh / 2);
                mv = Math.round(mv / 2);
                return `${mh}px ${mv}px, ${mh}px ${mv}px`;
            },
        },
        handle: {
            backgroundImage: "none",
            bottom: 0,
            right: 0,
            cursor: "se-resize",
            "&::after": {
                borderRight: `2px solid ${handleColor}! important`,
                borderBottom: `2px solid ${handleColor}! important`,
                right: "-1px! important",
                bottom: "-1px! important",
            },
        },
    });
});

export const createDashboardWidgetInstantiation = (
    widget: DashboardWidgetDefinition,
    dashboardParams: DashboardParameter[] | undefined
) => {
    const parametersBindings: Record<string, string> = {};
    if (widget.parameters && widget.parameters.length > 0 && dashboardParams && dashboardParams.length > 0) {
        for (const param of widget.parameters) {
            const dbparam = dashboardParams.find(s => getVariableNames(s).includes(param.name));
            if (dbparam && getParameterVariablesType(dbparam) === param.type) {
                parametersBindings[param.name] = param.name;
            }
        }
    }
    const instantiation: DashboardWidgetInstantiation = {
        instantiationId: uuid(),
        widgetName: widget.name!,
        parametersBindings,
    };
    return instantiation;
};

interface DashboardGridProps {
    widgets: DashboardWidgetInstantiation[];
    layoutSet: DashboardResponsiveLayoutSet;
    layouts: DashboardResponsiveLayout[];
    selectedLayout: number;
    containerWidth: number;
    onLayoutChanged?: (layout: GridLayout.Layout[]) => void;
    onEditWidgetInstantiation?: (
        instantiation: DashboardWidgetInstantiation,
        widgetDefinition: DashboardWidgetDefinition
    ) => void;
    onDuplicateWidgetInstantiation?: (
        instantiation: DashboardWidgetInstantiation,
        widgetDefinition: DashboardWidgetDefinition
    ) => void;
    onDeleteWidgetInstantiation?: (
        instantiation: DashboardWidgetInstantiation,
        widgetDefinition: DashboardWidgetDefinition
    ) => void;
}

const DashboardGrid: FC<DashboardGridProps> = props => {
    const {
        widgets,
        layoutSet,
        layouts,
        selectedLayout,
        onLayoutChanged,
        containerWidth,
        onEditWidgetInstantiation,
        onDuplicateWidgetInstantiation,
        onDeleteWidgetInstantiation,
    } = props;
    const {dashboardWidgets_FindOneByName, getWidgetDefinition} = useNimflowDashboardWidgets();
    const [widgetDefinitions, setWidgetDefinitions] = useState<Record<string, DashboardWidgetDefinition>>({});
    const {height, ref} = useResizeDetector({
        handleWidth: false,
    });

    useEffect(() => {
        const promises: Promise<DashboardWidgetDefinition>[] = [];
        for (let name of uniq(widgets.map(s => s.widgetName)).filter(isPresent)) {
            if (!(name in widgetDefinitions)) {
                promises.push(dashboardWidgets_FindOneByName(name).then(getWidgetDefinition));
            }
        }
        if (promises.length === 0) {
            return;
        }
        Promise.allSettled(promises).then(results => {
            if (results.every(s => s.status !== "fulfilled")) {
                return;
            }
            setWidgetDefinitions(s => {
                const newCache = {...s};
                for (const result of results) {
                    if (result.status === "fulfilled") {
                        newCache[result.value.name] = result.value;
                    } 
                }
                return newCache;
            });
        });
    }, [widgets, widgetDefinitions, dashboardWidgets_FindOneByName, getWidgetDefinition]);

    const currentLayout = layouts[selectedLayout]!;
    const cols = currentLayout.cols ?? 12;
    const rowHeight = currentLayout.rowHeight ?? layoutSet.rowHeight ?? 30;
    const margin: [number, number] = useMemo(() => {
        return currentLayout.margin ?? layoutSet.margin ?? [10, 10];
    }, [currentLayout.margin, layoutSet.margin]);
    const containerPadding = currentLayout.containerPadding ?? layoutSet.containerPadding ?? margin;

    const width = useMemo(() => {
        let width = containerWidth;
        const bp = currentLayout.breakpoint;
        width = Math.max(width, bp);
        const nextbp = layouts[selectedLayout + 1]?.breakpoint;
        if (isPresent(nextbp)) {
            width = Math.min(width, Math.max(nextbp, 400));
        }
        return width;
    }, [containerWidth, currentLayout.breakpoint, layouts, selectedLayout]);

    const colWidth = useMemo(() => {
        return (width - margin[0] * (cols - 1) - containerPadding[0] * 2) / cols;
    }, [cols, containerPadding, margin, width]);

    const layoutGridClasses = useLayoutGridStyles({
        cols,
        width,
        rowHeight,
        colWidth,
        margin,
    });

    return (
        <div className={layoutGridClasses.layoutGridContainer} style={{height}}>
            <div className={layoutGridClasses.layoutInnerGridContainer}>
                <div ref={ref}>
                    <GridLayout
                        className={layoutGridClasses.layoutGrid}
                        layout={currentLayout.widgetsLayouts}
                        cols={cols}
                        rowHeight={rowHeight}
                        margin={margin}
                        containerPadding={containerPadding}
                        width={width}
                        onLayoutChange={onLayoutChanged}
                        resizeHandle={<span className={"react-resizable-handle " + layoutGridClasses.handle} />}
                    >
                        {widgets.map(s => (
                            <div key={s.instantiationId}>
                                {widgetDefinitions[s.widgetName] && (
                                    <WidgetInstanceViewer
                                        instantiation={s}
                                        widgetDefinition={widgetDefinitions[s.widgetName]}
                                        onEditWidgetInstantiation={onEditWidgetInstantiation}
                                        onDuplicateWidgetInstantiation={onDuplicateWidgetInstantiation}
                                        onDeleteWidgetInstantiation={onDeleteWidgetInstantiation}
                                    />
                                )}
                            </div>
                        ))}
                    </GridLayout>
                </div>
            </div>
        </div>
    );
};

export interface DashboardDefinitionLayoutEditorProps {}

export const DashboardDefinitionLayoutEditor: FC<DashboardDefinitionLayoutEditorProps> = () => {
    const {t} = useTranslation();
    const classes = useStyles();
    const {errors, values: dashboardDefinition, setFieldValue} = useFormikContext<DashboardDefinition>();
    const layoutSet = dashboardDefinition.layoutSet;
    const [addingWidget, setAddingWidget] = useState(false);
    const [editedWidget, setEditedWidget] = useState<DashboardWidgetInstantiation>();
    const [managingLayouts, setManagingLayouts] = useState(false);
    const [selectedLayout, setSelectedLayout] = useState(layoutSet ? layoutSet.layouts.length - 1 : 0);
    const {dashboardWidgets_FindOne, getWidgetDefinition} = useNimflowDashboardWidgets();
    const {width, ref} = useResizeDetector({
        handleHeight: false,
    });

    const widgets: DashboardWidgetInstantiation[] = dashboardDefinition.widgets;

    const widgetsAppend = useCallback(
        (value: DashboardWidgetInstantiation) => {
            setFieldValue("widgets", [...widgets, value]);
        },
        [setFieldValue, widgets]
    );

    const widgetsRemoveAt = useCallback(
        (index: number) => {
            const newWidgets = [...widgets];
            newWidgets.splice(index, 1);
            setFieldValue("widgets", newWidgets);
        },
        [setFieldValue, widgets]
    );
    const widgetsSetAt = useCallback(
        (index: number, value: DashboardWidgetInstantiation) => {
            const newWidgets = [...widgets];
            newWidgets[index] = value;
            setFieldValue("widgets", newWidgets);
        },
        [setFieldValue, widgets]
    );

    const handleAddWidget = useCallback(() => {
        setAddingWidget(true);
    }, []);

    const handleWidgetSelected = useCallback(
        async (widgetId: string) => {
            const widgetDefOrSpec = await dashboardWidgets_FindOne(widgetId);
            const widget = await getWidgetDefinition(widgetDefOrSpec);
            const instantiation = createDashboardWidgetInstantiation(widget, dashboardDefinition.parameters);
            if (widget.parameters && widget.parameters.length > 0) {
                setEditedWidget(instantiation);
            } else {
                widgetsAppend(instantiation);
            }
        },
        [dashboardWidgets_FindOne, getWidgetDefinition, dashboardDefinition.parameters, widgetsAppend]
    );

    const handleOnCloseSelector = useCallback(() => {
        setAddingWidget(false);
    }, []);

    const handleWidgetChanged = useCallback(
        (instantiation: DashboardWidgetInstantiation) => {
            const index = widgets.findIndex(s => s.instantiationId === instantiation.instantiationId);
            if (index === -1) {
                widgetsAppend(instantiation);
            } else {
                widgetsSetAt(index, instantiation);
            }
        },
        [widgets, widgetsAppend, widgetsSetAt]
    );

    const handleOnCloseEditor = useCallback(() => {
        setEditedWidget(undefined);
    }, []);

    const handleLayoutSelected = useCallback((event: React.ChangeEvent<{value: unknown}>) => {
        const value = event.target.value as string;
        const newSelectedLayout = parseInt(value);
        setSelectedLayout(newSelectedLayout);
    }, []);

    const handleManageLayoutSet = useCallback(() => {
        setManagingLayouts(true);
    }, []);

    const handleLayoutSetChange = useCallback(
        (layoutSet: DashboardResponsiveLayoutSet) => {
            if (selectedLayout >= layoutSet.layouts.length) {
                setSelectedLayout(layoutSet.layouts.length - 1);
            }
            setFieldValue("layoutSet", layoutSet);
        },
        [selectedLayout, setFieldValue]
    );

    const handleCloseLayoutSetManager = useCallback(() => {
        setManagingLayouts(false);
    }, []);

    const handleEditWidget = useCallback(
        (instantiation: DashboardWidgetInstantiation, widgetDefinition: DashboardWidgetDefinition) => {
            setEditedWidget(instantiation);
        },
        []
    );

    const handleDuplicateWidget = useCallback(
        (instantiation: DashboardWidgetInstantiation, widgetDefinition: DashboardWidgetDefinition) => {
            const newInstantiation = clone(instantiation);
            newInstantiation.instantiationId = uuid();
            if (!!widgetDefinition.parameters?.length) {
                setEditedWidget(newInstantiation);
            } else {
                widgetsAppend(newInstantiation);
            }
        },
        [widgetsAppend]
    );

    const handleDeleteWidget = useCallback(
        (instantiation: DashboardWidgetInstantiation, widgetDefinition: DashboardWidgetDefinition) => {
            widgetsRemoveAt(widgets.indexOf(instantiation));
        },
        [widgets, widgetsRemoveAt]
    );

    const layouts = useMemo(() => {
        let layouts = layoutSet?.layouts;
        if (!layouts) {
            return [];
        }
        if (layouts.length > 1) {
            layouts = sort((a, b) => a.breakpoint - b.breakpoint, layouts);
        }
        return layouts;
    }, [layoutSet]);

    const handleLayoutChanged = useCallback(
        (widgetLayouts: GridLayout.Layout[]) => {
            const currentWidgetLayouts = layouts[selectedLayout].widgetsLayouts;
            const cleanedWidgetLayouts = JSON.parse(JSON.stringify(widgetLayouts)); //Remove field with undefined values to prevent dirtying
            if (!equals(cleanedWidgetLayouts, currentWidgetLayouts)) {
                setFieldValue(`layoutSet.layouts[${selectedLayout}].widgetsLayouts`, cleanedWidgetLayouts);
            }
        },
        [layouts, selectedLayout, setFieldValue]
    );

    const gridStyle = useMemo(() => {
        return {
            minWidth: layouts?.[selectedLayout]?.breakpoint,
        };
    }, [layouts, selectedLayout]);

    return (
        <ErrorBoundary>
            <div className={classes.table}>
                <div className={classes.tableRow}>
                    <Grid container spacing={1}>
                        <Grid item xs={12}>
                            <Toolbar className={classes.toolbar}>
                                <Select
                                    value={selectedLayout}
                                    onChange={handleLayoutSelected}
                                    className={classes.layoutSelector}
                                >
                                    {layouts.map((s, i) => {
                                        const nextbp = layouts[i + 1]?.breakpoint;
                                        let str = s.name;
                                        if (isPresent(nextbp)) {
                                            str = `${s.name} (${s.breakpoint}-${nextbp}px)`;
                                        } else if (layouts.length > 1) {
                                            str = `${s.name} (>${s.breakpoint}px)`;
                                        }
                                        return (
                                            <MenuItem key={s.name} value={i}>
                                                {str}
                                            </MenuItem>
                                        );
                                    })}
                                </Select>
                                <Tooltip title={t("ManageGridDefinitions") as string}>
                                    <IconButton onClick={handleManageLayoutSet}>
                                        <EditIcon />
                                    </IconButton>
                                </Tooltip>
                                <Tooltip title={t("AddWidget") as string}>
                                    <IconButton onClick={handleAddWidget}>
                                        <AddIcon />
                                    </IconButton>
                                </Tooltip>
                            </Toolbar>
                        </Grid>
                    </Grid>
                </div>
                <div ref={ref} className={classes.layoutTableRow} style={gridStyle}>
                    {layouts.length > selectedLayout && layoutSet && isPresent(width) && (
                        <DashboardGrid
                            widgets={widgets}
                            layoutSet={layoutSet}
                            layouts={layouts}
                            selectedLayout={selectedLayout}
                            containerWidth={width}
                            onLayoutChanged={handleLayoutChanged}
                            onEditWidgetInstantiation={handleEditWidget}
                            onDuplicateWidgetInstantiation={handleDuplicateWidget}
                            onDeleteWidgetInstantiation={handleDeleteWidget}
                        />
                    )}
                </div>
                <FormikErrorViewer errors={errors} names={["widgets", "layout"]} className={classes.tableRow} />
            </div>

            {addingWidget && (
                <DashboardWidgetSelector
                    open={addingWidget}
                    onSelected={handleWidgetSelected}
                    onClose={handleOnCloseSelector}
                />
            )}
            {editedWidget && (
                <DashboardWidgetInstanceEditorDialog
                    open={!!editedWidget}
                    defaultValue={editedWidget}
                    dashboardParameters={dashboardDefinition.parameters ?? []}
                    onChange={handleWidgetChanged}
                    onClose={handleOnCloseEditor}
                />
            )}
            {managingLayouts && (
                <DashboardDefinitionLayoutGridEditorDialog
                    open={managingLayouts}
                    defaultValue={dashboardDefinition.layoutSet!}
                    onChange={handleLayoutSetChange}
                    onClose={handleCloseLayoutSetManager}
                />
            )}
        </ErrorBoundary>
    );
};
