import {FC, useCallback} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import {useFormikContext, FieldArray} from "formik";
import {useTranslation} from "react-i18next";
import InputLabel from "@material-ui/core/InputLabel";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import IconButton from "@material-ui/core/IconButton";
import DeleteIcon from "@material-ui/icons/Delete";
import AddIcon from "@material-ui/icons/Add";
import {KeyboardTimePicker, KeyboardDatePicker} from "@material-ui/pickers";
import {startOfDay} from "date-fns";

import {
    SelectionDashboardParameter,
    StaticSelectionDashboardParameterDataSource,
} from "../../api/nimflow-dashboard-api";
import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {FormikNumberField} from "../../components/formik-components/formik-number-field";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        staticDataSourceRow: {
            display: "grid",
            gridTemplateColumns: "1fr 1fr 32px",
            columnGap: 8,
            marginRight: -8,
        },
        dateTimeEditor: {
            width: "50%",
            paddingRight: 10,
        },
    })
);

export const StaticDataSourceEditor: FC<{}> = props => {
    const classes = useStyles();
    const {t} = useTranslation();
    const {values: param, setFieldValue} = useFormikContext<SelectionDashboardParameter>();
    const ds = param.dataSource as StaticSelectionDashboardParameterDataSource;

    const handleDateChange = useCallback(
        (index: number, date: Date | null) => {
            if (date) {
                setFieldValue(`dataSource.options[${index}].value`, date.toISOString());
            }
        },
        [setFieldValue]
    );

    return (
        <Grid item xs={12}>
            <div className={classes.staticDataSourceRow}>
                <InputLabel required shrink={false}>
                    {t("Value")}
                </InputLabel>
                <InputLabel shrink={false}>{t("DisplayName")}</InputLabel>
            </div>
            <FieldArray name="dataSource.options">
                {({remove, push}) => (
                    <>
                        {ds.options.map((s, i) => (
                            <div key={i} className={classes.staticDataSourceRow}>
                                {param.dataType === "string" && (
                                    <FormikTextField name={`dataSource.options[${i}].value`} fullWidth required />
                                )}
                                {param.dataType === "number" && (
                                    <FormikNumberField name={`dataSource.options[${i}].value`} fullWidth required />
                                )}
                                {param.dataType === "datetime" && (
                                    <div>
                                        <KeyboardDatePicker
                                            margin="none"
                                            format={t("DATE_FORMAT")}
                                            required
                                            className={classes.dateTimeEditor}
                                            value={ds.options[i].value as any}
                                            onChange={date => handleDateChange(i, date)}
                                            KeyboardButtonProps={{
                                                "aria-label": "change date",
                                            }}
                                        />
                                        <KeyboardTimePicker
                                            margin="none"
                                            className={classes.dateTimeEditor}
                                            format={t("TIME_FORMAT")}
                                            value={ds.options[i].value as any}
                                            onChange={date => handleDateChange(i, date)}
                                            KeyboardButtonProps={{
                                                "aria-label": "change time",
                                            }}
                                        />
                                    </div>
                                )}
                                {param.dataType === "boolean" && (
                                    <TextField value={t(ds.options[i].value.toString())} disabled fullWidth />
                                )}
                                <FormikTextField name={`dataSource.options[${i}].displayText`} fullWidth />
                                <IconButton onClick={() => remove(i)}>
                                    <DeleteIcon />
                                </IconButton>
                            </div>
                        ))}
                        <Button
                            onClick={() => {
                                push({
                                    value: param.dataType === "datetime" ? startOfDay(new Date()) : undefined,
                                    displayText: undefined,
                                });
                            }}
                            startIcon={<AddIcon />}
                        >
                            {t("AddValue")}
                        </Button>
                    </>
                )}
            </FieldArray>
        </Grid>
    );
};
