import {useCallback} from "react";

import {useAuthorization} from "./../components/authorization-context";
import {
    manageCollectionPermission,
    deleteCollectionPermission,
    insertCollectionPermission,
    readCollectionPermission,
    updateCollectionPermission,
    permissions,
} from "./../utils/authorization";
import {localizationCollectionName} from "./nimflow-localization-api";
import {dashboardWidgetsCollectionName} from "./nimflow-dashboard-widgets-api";
import {dashboardsCollectionName} from "./nimflow-dashboard-api";

export const contextSimulationCollectionName = "NimflowContextSimulations";

const manageCollections = manageCollectionPermission();
const deleteCollections = deleteCollectionPermission();
const insertCollections = insertCollectionPermission();
const readCollections = readCollectionPermission();
const updateCollections = updateCollectionPermission();

const manageSimulationsCollection = manageCollectionPermission(contextSimulationCollectionName);
const deleteSimulationsCollection = deleteCollectionPermission(contextSimulationCollectionName);
const insertSimulationsCollection = insertCollectionPermission(contextSimulationCollectionName);
const readSimulationsCollection = readCollectionPermission(contextSimulationCollectionName);
const updateSimulationsCollection = updateCollectionPermission(contextSimulationCollectionName);

const manageLocalizationsCollection = manageCollectionPermission(localizationCollectionName);
const deleteLocalizationsCollection = deleteCollectionPermission(localizationCollectionName);
const insertLocalizationsCollection = insertCollectionPermission(localizationCollectionName);
const readLocalizationsCollection = readCollectionPermission(localizationCollectionName);
const updateLocalizationsCollection = updateCollectionPermission(localizationCollectionName);

const manageWidgetDefinitionsCollection = manageCollectionPermission(dashboardWidgetsCollectionName);
const deleteWidgetDefinitionsCollection = deleteCollectionPermission(dashboardWidgetsCollectionName);
const insertWidgetDefinitionsCollection = insertCollectionPermission(dashboardWidgetsCollectionName);
const readWidgetDefinitionsCollection = readCollectionPermission(dashboardWidgetsCollectionName);
const updateWidgetDefinitionsCollection = updateCollectionPermission(dashboardWidgetsCollectionName);

const manageDashboardDefinitionsCollection = manageCollectionPermission(dashboardsCollectionName);
const deleteDashboardDefinitionsCollection = deleteCollectionPermission(dashboardsCollectionName);
const insertDashboardDefinitionsCollection = insertCollectionPermission(dashboardsCollectionName);
const readDashboardDefinitionsCollection = readCollectionPermission(dashboardsCollectionName);
const updateDashboardDefinitionsCollection = updateCollectionPermission(dashboardsCollectionName);

export const useNimflowAuthorization = () => {
    const {can, canAny} = useAuthorization();

    const canManageCollections = useCallback(() => {
        return can([manageCollections]);
    }, [can]);
    const canDeleteCollections = useCallback(() => {
        return canAny([manageCollections, deleteCollections]);
    }, [canAny]);
    const canInsertCollections = useCallback(() => {
        return canAny([manageCollections, insertCollections]);
    }, [canAny]);
    const canReadCollections = useCallback(() => {
        return canAny([manageCollections, readCollections]);
    }, [canAny]);
    const canUpdateCollections = useCallback(() => {
        return canAny([manageCollections, updateCollections]);
    }, [canAny]);

    const canManageSimulations = useCallback(() => {
        return canAny([manageCollections, manageSimulationsCollection]);
    }, [canAny]);
    const canDeleteSimulations = useCallback(() => {
        return canAny([manageCollections, manageSimulationsCollection, deleteCollections, deleteSimulationsCollection]);
    }, [canAny]);
    const canInsertSimulations = useCallback(() => {
        return canAny([manageCollections, manageSimulationsCollection, insertCollections, insertSimulationsCollection]);
    }, [canAny]);
    const canReadSimulations = useCallback(() => {
        return (
            can([permissions.readContextTypes]) &&
            canAny([manageCollections, manageSimulationsCollection, readCollections, readSimulationsCollection])
        );
    }, [can, canAny]);
    const canUpdateSimulations = useCallback(() => {
        return canAny([manageCollections, manageSimulationsCollection, updateCollections, updateSimulationsCollection]);
    }, [canAny]);

    const canManageLocalizations = useCallback(() => {
        return canAny([manageCollections, manageLocalizationsCollection]);
    }, [canAny]);
    const canDeleteLocalizations = useCallback(() => {
        return canAny([
            manageCollections,
            manageLocalizationsCollection,
            deleteCollections,
            deleteLocalizationsCollection,
        ]);
    }, [canAny]);
    const canInsertLocalizations = useCallback(() => {
        return canAny([
            manageCollections,
            manageLocalizationsCollection,
            insertCollections,
            insertLocalizationsCollection,
        ]);
    }, [canAny]);
    const canReadLocalizations = useCallback(() => {
        return (
            can([permissions.readContextTypes]) &&
            canAny([manageCollections, manageLocalizationsCollection, readCollections, readLocalizationsCollection])
        );
    }, [can, canAny]);
    const canUpdateLocalizations = useCallback(() => {
        return canAny([
            manageCollections,
            manageLocalizationsCollection,
            updateCollections,
            updateLocalizationsCollection,
        ]);
    }, [canAny]);

    const canManageWidgetDefinitions = useCallback(() => {
        return canAny([manageCollections, manageWidgetDefinitionsCollection]);
    }, [canAny]);
    const canDeleteWidgetDefinitions = useCallback(() => {
        return canAny([
            manageCollections,
            manageWidgetDefinitionsCollection,
            deleteCollections,
            deleteWidgetDefinitionsCollection,
        ]);
    }, [canAny]);
    const canInsertWidgetDefinitions = useCallback(() => {
        return canAny([
            manageCollections,
            manageWidgetDefinitionsCollection,
            insertCollections,
            insertWidgetDefinitionsCollection,
        ]);
    }, [canAny]);
    const canReadWidgetDefinitions = useCallback(() => {
        return (
            can([permissions.readContextTypes]) &&
            canAny([
                manageCollections,
                manageWidgetDefinitionsCollection,
                readCollections,
                readWidgetDefinitionsCollection,
            ])
        );
    }, [can, canAny]);
    const canUpdateWidgetDefinitions = useCallback(() => {
        return canAny([
            manageCollections,
            manageWidgetDefinitionsCollection,
            updateCollections,
            updateWidgetDefinitionsCollection,
        ]);
    }, [canAny]);

    const canManageDashboardDefinitions = useCallback(() => {
        return canAny([manageCollections, manageDashboardDefinitionsCollection]);
    }, [canAny]);
    const canDeleteDashboardDefinitions = useCallback(() => {
        return canAny([
            manageCollections,
            manageDashboardDefinitionsCollection,
            deleteCollections,
            deleteDashboardDefinitionsCollection,
        ]);
    }, [canAny]);
    const canInsertDashboardDefinitions = useCallback(() => {
        return canAny([
            manageCollections,
            manageDashboardDefinitionsCollection,
            insertCollections,
            insertDashboardDefinitionsCollection,
        ]);
    }, [canAny]);
    const canReadDashboardDefinitions = useCallback(() => {
        return (
            can([permissions.readContextTypes]) &&
            canAny([
                manageCollections,
                manageDashboardDefinitionsCollection,
                readCollections,
                readDashboardDefinitionsCollection,
            ])
        );
    }, [can, canAny]);
    const canUpdateDashboardDefinitions = useCallback(() => {
        return canAny([
            manageCollections,
            manageDashboardDefinitionsCollection,
            updateCollections,
            updateDashboardDefinitionsCollection,
        ]);
    }, [canAny]);

    return {
        canManageCollections,
        canDeleteCollections,
        canInsertCollections,
        canReadCollections,
        canUpdateCollections,

        canManageSimulations,
        canDeleteSimulations,
        canInsertSimulations,
        canReadSimulations,
        canUpdateSimulations,

        canManageLocalizations,
        canDeleteLocalizations,
        canInsertLocalizations,
        canReadLocalizations,
        canUpdateLocalizations,

        canManageWidgetDefinitions,
        canDeleteWidgetDefinitions,
        canInsertWidgetDefinitions,
        canReadWidgetDefinitions,
        canUpdateWidgetDefinitions,

        canManageDashboardDefinitions,
        canDeleteDashboardDefinitions,
        canInsertDashboardDefinitions,
        canReadDashboardDefinitions,
        canUpdateDashboardDefinitions,
    };
};
