import {FC, useMemo, useState} from "react";
import {Column, Action} from "@material-table/core";
import Alert from "@material-ui/lab/Alert";
import {useTranslation} from "react-i18next";
import i18next from "i18next";
import RefreshIcon from "@material-ui/icons/Refresh";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import SettingsInputComponentIcon from "@material-ui/icons/SettingsInputComponent";
import {useNavigate} from "react-router";
import useSWR from "swr";

import MaterialTable from "../../libs/material-table";
import {useNimflow} from "../../api/nimflow-api-provider";
import {useOkCancelConfirm} from "../../components/confirm";
import {useKey} from "../../utils/use-key";
import {DigitalWorker} from "../../api/nimflow-api";
import {DigitalWorkerConfigDialog} from "./digital-worker-config-dialog";

const columns: Column<DigitalWorker>[] = [
    {field: "name", title: i18next.t("Name")},
    {field: "role", title: i18next.t("Role")},
    {field: "expires", title: i18next.t("Expires"), type: "date"},
    {field: "active", title: i18next.t("Active"), type: "boolean"},
];

const buildRefreshAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: RefreshIcon,
        tooltip: i18next.t("RefreshData"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const buildAddAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: AddIcon,
        tooltip: i18next.t("AddDigitalWorker"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const adaptHandler = <T extends object>(handler: (info: T) => void): Action<T>["onClick"] => {
    return (_, info) => {
        if (Array.isArray(info)) {
            info = info[0];
        }
        handler(info);
    };
};

const buildEditAction = (handler: (info: DigitalWorker) => void) => {
    const action: Action<DigitalWorker> = {
        icon: EditIcon,
        tooltip: i18next.t("Edit"),
        onClick: adaptHandler(handler),
    };
    return action;
};

const buildDeleteAction = (handler: (info: DigitalWorker) => void) => {
    const action: Action<DigitalWorker> = {
        icon: DeleteIcon,
        tooltip: i18next.t("Delete"),
        onClick: adaptHandler(handler),
    };
    return action;
};

const buildShowConfigAction = (handler: (info: DigitalWorker) => void) => {
    const action: Action<DigitalWorker> = {
        icon: SettingsInputComponentIcon,
        tooltip: i18next.t("ShowDigitalWorkerConfiguration"),
        onClick: adaptHandler(handler),
    };
    return action;
};

const useDigitalWorkers = () => {
    const {digitalWorkers_Search} = useNimflow();
    const queryKey = useKey(digitalWorkers_Search);
    const {data, error, mutate, isValidating} = useSWR(queryKey, async () => {
        const result = (await digitalWorkers_Search({})) as DigitalWorker[];
        return result;
    });
    return useMemo(() => {
        return {
            digitalWorkers: data ?? [],
            isValidating,
            error,
            revalidate: mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export const DigitalWorkersViewer: FC<{}> = () => {
    const {t} = useTranslation();
    const confirm = useOkCancelConfirm();
    const {digitalWorkers_Remove} = useNimflow();
    const navigate = useNavigate();
    const {digitalWorkers, error, revalidate, isValidating} = useDigitalWorkers();
    const [dwConfig, setDwConfig] = useState<DigitalWorker>();

    const refreshAction = useMemo(() => {
        return buildRefreshAction(revalidate);
    }, [revalidate]);

    const addAction = useMemo(() => {
        return buildAddAction(() => {
            navigate("new");
        });
    }, [navigate]);

    const rowActions = useMemo(() => {
        return [
            buildShowConfigAction(row => {
                setDwConfig(row);
            }),
            buildEditAction(row => {
                navigate(`edit/${row.name}`);
            }),
            buildDeleteAction(async row => {
                let result = await confirm({
                    description: t("ConfirmDeleteDigitalWorker", {
                        name: row.name,
                    }),
                });
                if (result) {
                    result = await digitalWorkers_Remove({
                        id: row.id!,
                    });
                    revalidate();
                }
                return result;
            }),
        ];
    }, [confirm, digitalWorkers_Remove, navigate, revalidate, t]);

    return (
        <>
            <MaterialTable
                columns={columns}
                data={digitalWorkers ?? []}
                title={t("DigitalWorkers")}
                actions={[addAction, refreshAction, ...rowActions]}
            />
            {!isValidating && !!error && <Alert severity="error">{error.toString()}</Alert>}
            {!!dwConfig && (
                <DigitalWorkerConfigDialog
                    open={!!dwConfig}
                    digitalWorker={dwConfig}
                    onClose={() => setDwConfig(undefined)}
                />
            )}
        </>
    );
};
