import {FC, useCallback, HTMLAttributes, useState, useMemo} from "react";
import {createStyles, makeStyles, Theme, ClickAwayListener, emphasize} from "@material-ui/core";
import Autocomplete from "@material-ui/lab/Autocomplete";
import Fab from "@material-ui/core/Fab";
import TextField from "@material-ui/core/TextField";
import SearchIcon from "@material-ui/icons/Search";
import {sortBy} from "ramda";
import {useTranslation} from "react-i18next";
import {Transition, animated} from "react-spring";

import {ContextTypeNodeModel} from "./diagram-nodes/context-type-node-model";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            position: "relative",
        },
        editorContainer: {
            position: "absolute",
            top: 5,
            right: theme.spacing(7),
            backgroundColor: theme.palette.background.paper,
        },
        button: {
            color: theme.palette.text.secondary,
            backgroundColor: theme.palette.background.paper,
            "&:hover": {
                backgroundColor: emphasize(theme.palette.background.paper, 0.15),
            },
        },
    })
);

export interface ContextTypeDiagramSearchProps extends HTMLAttributes<HTMLDivElement> {
    nodes: ContextTypeNodeModel<any>[];
    onNodeSelected?: (node: ContextTypeNodeModel<any>) => void;
}

export const ContextTypeDiagramSearch: FC<ContextTypeDiagramSearchProps> = props => {
    const {nodes, onNodeSelected, ...rest} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const [visible, setVisible] = useState(false);
    const sortedNodes = useMemo(() => {
        return sortBy(s => `${t(s.getType())}:${s.title}`, nodes);
    }, [nodes, t]);

    const handleOnClick = useCallback(() => {
        setVisible(true);
    }, []);

    const handleClickAway = useCallback(() => {
        setVisible(false);
    }, []);

    const handleNodeSelected = useCallback(
        (event: any, newValue: ContextTypeNodeModel<any> | null) => {
            if (newValue) {
                onNodeSelected?.(newValue);
                setVisible(false);
            }
        },
        [onNodeSelected]
    );

    return (
        <ClickAwayListener mouseEvent="onMouseDown" touchEvent="onTouchStart" onClickAway={handleClickAway}>
            <div {...rest}>
                <div className={classes.root}>
                    <Fab className={classes.button} onClick={handleOnClick} size="medium">
                        <SearchIcon />
                    </Fab>
                    <div className={classes.editorContainer}>
                        <Transition
                            items={visible}
                            from={{width: 0, opacity: 0}}
                            enter={{width: 300, opacity: 1}}
                            leave={{width: 0, opacity: 0}}
                            reverse={visible}
                        >
                            {(props, visible) =>
                                visible && (
                                    <animated.div style={props}>
                                        <Autocomplete
                                            options={sortedNodes}
                                            getOptionLabel={option => option.title}
                                            size="small"
                                            groupBy={option => t(option.getType())}
                                            onChange={handleNodeSelected}
                                            renderInput={params => (
                                                <TextField {...params} autoFocus variant="outlined" />
                                            )}
                                        />
                                    </animated.div>
                                )
                            }
                        </Transition>
                    </div>
                </div>
            </div>
        </ClickAwayListener>
    );
};
