import React, {FC, useCallback} from "react";
import MuiCheckbox, {CheckboxProps} from "@material-ui/core/Checkbox";
import {useFormikContext, useField} from "formik";
import FormControlLabel, {FormControlLabelProps} from "@material-ui/core/FormControlLabel";
import FormHelperText, {FormHelperTextProps} from "@material-ui/core/FormHelperText";

export type FormikCheckboxProps = {
    name: string;
    controlValueAdapter?: (value: boolean) => any;
    error?: boolean;
    FormHelperTextProps?: Partial<FormHelperTextProps>;
    helperText?: React.ReactNode;
    label?: React.ReactNode;
    LabelProps?: Partial<FormControlLabelProps>;
} & Omit<CheckboxProps, "name" | "value" | "defaultValue" | "checked">;

export const FormikCheckbox: FC<FormikCheckboxProps> = props => {
    const {
        name,
        label,
        onChange,
        onBlur,
        helperText,
        error,
        disabled,
        controlValueAdapter,
        FormHelperTextProps,
        LabelProps,
        ...rest
    } = props;
    const {isSubmitting} = useFormikContext();
    const [field, meta, helpers] = useField({name});
    const fieldError = meta.error;
    const showError = meta.touched && !!fieldError;

    const handleChange = useCallback(
        (ev: React.ChangeEvent<HTMLInputElement>, checked: boolean) => {
            helpers.setValue(controlValueAdapter ? controlValueAdapter(checked) : checked);
            onChange?.(ev, checked);
        },
        [controlValueAdapter, helpers, onChange]
    );
    const handleBlur = useCallback(
        (ev: React.FocusEvent<HTMLButtonElement>) => {
            field.onBlur(ev);
            onBlur?.(ev);
        },
        [field, onBlur]
    );

    const Checkbox = (
        <MuiCheckbox
            {...field}
            checked={field.checked ?? field.value}
            onChange={handleChange}
            onBlur={handleBlur}
            disabled={disabled ?? isSubmitting}
            {...rest}
        />
    );

    return (
        <>
            {!!label && <FormControlLabel control={Checkbox} label={label} {...LabelProps}/>}
            {!label && <>{Checkbox}</>}
            {helperText && (
                <FormHelperText error={showError} {...FormHelperTextProps}>
                    {showError ? fieldError : helperText}
                </FormHelperText>
            )}
            {/* {JSON.stringify({showError, fieldError, touched: meta.touched})} */}
        </>
    );
};
