import {NodeModel, NodeModelGenerics, NodeModelListener} from "@projectstorm/react-diagrams";
import {BaseEntityEvent} from "@projectstorm/react-canvas-core";

import {ContextTypePortModel} from "./context-type-port-model";
import {TaggingType} from "../taggings";

export interface BaseNodeCommand {
    type: string;
}
export interface EditCommand extends BaseNodeCommand {
    type: "edit";
}

export interface DeleteCommand extends BaseNodeCommand {
    type: "delete";
}

export interface ToggleInitialActionCommand extends BaseNodeCommand {
    type: "toggleInitialAction";
}

export interface ToggleTaggingCommand extends BaseNodeCommand {
    type: "toggleTagging";
}

export interface SetTaggingCommand extends BaseNodeCommand {
    type: "setTagging";
    tagging: TaggingType;
}

export interface FocusNodeCommand extends BaseNodeCommand {
    type: "focusNode";
}

export interface ResponseCommand extends BaseNodeCommand {
    responseName: string;
}

export interface EditResponseCommand extends ResponseCommand {
    type: "editResponse";
}

export interface DeleteResponseCommand extends ResponseCommand {
    type: "deleteResponse";
}

export const isResponseCommand = (command: BaseNodeCommand): command is ResponseCommand => {
    return "responseName" in command;
};

export type ContextTypeNodeModelCommand =
    | EditCommand
    | DeleteCommand
    | EditResponseCommand
    | DeleteResponseCommand
    | ToggleInitialActionCommand
    | ToggleTaggingCommand
    | SetTaggingCommand
    | FocusNodeCommand;

export type ContextTypeNodeListener = NodeModelListener & {
    commandInvoked?: (
        event: BaseEntityEvent<NodeModel> & {command: ContextTypeNodeModelCommand; node: ContextTypeNodeModel<any>}
    ) => void;
    //hightlightedChanged?: (event: BaseEntityEvent<NodeModel> & {node: ContextTypeNodeModel<any>}) => void;
};

export interface ContextTypeNodeModelGenerics<T> extends NodeModelGenerics {
    LISTENER: ContextTypeNodeListener;
    instance: Partial<T>;
}

export class ContextTypeNodeModel<T> extends NodeModel<ContextTypeNodeModelGenerics<T>> {
    constructor(type: string, public instance: T, public instanceId: string, public title: string) {
        super({
            type,
        });
        if (this.hasInPort()) {
            this.inPort = this.addContextTypePort(true, "in");
        }
        if (this.hasOutPort()) {
            this.outPort = this.addContextTypePort(false, "out");
        }
    }

    protected addContextTypePort(isIn: boolean, name?: string, label?: string) {
        const port = new ContextTypePortModel(isIn, name, label);
        this.addPort(port);
        //Prevent manual port connection
        port.setLocked(true);
        return port;
    }

    public hasInPort() {
        return true;
    }

    public hasOutPort() {
        return true;
    }

    serialize() {
        return {
            ...super.serialize(),
            instance: this.instance,
            instanceId: this.instanceId,
        };
    }

    deserialize(event: any): void {
        super.deserialize(event);
        this.instance = event.data.instance;
    }

    dispatchCommand(command: ContextTypeNodeModelCommand) {
        this.fireEvent(
            {
                command,
                node: this,
            },
            "commandInvoked"
        );
    }

    public readonly inPort?: ContextTypePortModel;
    public readonly outPort?: ContextTypePortModel;
}
