import React, {FC, useMemo, useCallback} from "react";
import {createStyles, makeStyles, Theme} from "@material-ui/core";
import IconButton from "@material-ui/core/IconButton";
import Avatar from "@material-ui/core/Avatar";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import InputIcon from '@material-ui/icons/Input';
import Badge from "@material-ui/core/Badge";
import PlayCircleOutlineIcon from "@material-ui/icons/PlayCircleOutline";
import {DiagramEngine} from "@projectstorm/react-diagrams-core";

import {ActionTypeNodeModel} from "./action-type-node-model";
import {ContextTypeNodePanel, ContextTypeNodePanelAction} from "./context-type-node-panel";
import {ContextTypeNodeAvatar} from "./context-type-node-avatar";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        smallAvatar: {
            width: 25,
            height: 25,
            border: `2px solid ${theme.palette.primary.main}`,
            color: theme.palette.text.secondary,
            backgroundColor: theme.palette.background.paper,
        },
        initialAction: {
            width: 16,
            height: 16,
        },
    })
);

interface InitialActionBadgeProps {
    isInitialAction: boolean;
    onClick?: () => void;
}

const InitialActionBadge: FC<InitialActionBadgeProps> = props => {
    const {isInitialAction, onClick, children} = props;
    const classes = useStyles();

    return (
        <IconButton onClick={onClick} size="small">
            {isInitialAction && (
                <Badge
                    overlap="circular"
                    anchorOrigin={{
                        vertical: "bottom",
                        horizontal: "right",
                    }}
                    badgeContent={
                        <Avatar className={classes.smallAvatar}>
                            <PlayCircleOutlineIcon className={classes.initialAction} />
                        </Avatar>
                    }
                >
                    {children}
                </Badge>
            )}
            {!isInitialAction && children}
        </IconButton>
    );
};

export interface ActionTypeNodeWidgetProps {
    node: ActionTypeNodeModel;
    engine: DiagramEngine;
}

export const ActionTypeNodeWidget: FC<ActionTypeNodeWidgetProps> = props => {
    const {engine, node} = props;

    const handleToggleInitialAction = useCallback(() => {
        node.dispatchCommand({
            type: "toggleInitialAction",
        });
    }, [node]);
    const actions = useMemo(() => {
        const actions: ContextTypeNodePanelAction[] = [
            {
                icon: <EditIcon />,
                onClick: () => {
                    node.dispatchCommand({
                        type: "edit",
                    });
                },
            },
            {
                icon: <DeleteIcon />,
                onClick: () => {
                    node.dispatchCommand({
                        type: "delete",
                    });
                },
            },
        ];
        return actions;
    }, [node]);

    return (
        <ContextTypeNodePanel
            engine={engine}
            node={node}
            avatar={
                <InitialActionBadge isInitialAction={node.isInitialAction} onClick={handleToggleInitialAction}>
                    <ContextTypeNodeAvatar>
                        <InputIcon />
                    </ContextTypeNodeAvatar>
                </InitialActionBadge>
            }
            actions={actions}
            selected={node.isSelected()}
        ></ContextTypeNodePanel>
    );
};
