import React, {FC, useCallback, useState, useMemo} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Dialog, {DialogProps} from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import {useTranslation} from "react-i18next";
import ReactMarkdown from "react-markdown";
import {last} from "ramda";

import {ContextTask, ResponseType, TaskResponseEvent} from "../../api/nimflow-api";
import {useContextSimulationContext} from "./simulation-context";
import {useConfirm} from "../../components/confirm";
import {TaskResponseDataEditor, TaskResponseDataEditorState} from "./task-response-data-editor";
import {useNimflow} from "../../api/nimflow-api-provider";
import {getSettings} from "../../api/nimflow-api-extensions";
import {useReportError} from "../../utils/use-report-error";
import {JsonIcon} from "../../components/icons/json-icon";
import {getDialogMaxWidthForResponseType} from "../../utils/dialog-max-width";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        content: {
            minWidth: "400px",
            minHeight: "50px",
        },
        grow: {
            flexGrow: 1,
        },
    })
);

export interface TaskResponseEditorProps {
    stateIndex: number;
    task: ContextTask;
    responseType: ResponseType;
    open: boolean;
    onClose: () => void;
}

export const TaskResponseEditor: FC<TaskResponseEditorProps> = props => {
    const {stateIndex, task, responseType, open, onClose} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const confirm = useConfirm();
    const reportError = useReportError();
    const [jsonEditorOpen, setJsonEditorOpen] = useState(false);
    const [responseEvent, setResponseEvent] = useState<TaskResponseEvent>(() => {
        return {
            taskTypeName: task.typeName,
            typeName: responseType.name,
            payload: undefined,
        };
    });
    const [hasErrors, setHasErrors] = useState(false);
    const {getState, sendResponse} = useContextSimulationContext();
    const {simulation, contextType} = getState();
    const {simulation_BuildInitialResponsePayload} = useNimflow();

    const responsePayloadInitializer = useMemo(() => {
        if (!responseType.jsInitialPayloadBuilder) {
            return undefined;
        }
        const currentState = typeof stateIndex === "number" ? simulation.history[stateIndex] : last(simulation.history);
        if (!currentState) {
            return undefined;
        }
        return async () => {
            try {
                const result = await simulation_BuildInitialResponsePayload({
                    context: currentState.context,
                    task,
                    contextType: simulation.contextType,
                    responseTypeName: responseType.name,
                });
                return result.payload;
            } catch (e) {
                reportError(t("ServerErrorInitializingResponsePayload"), e);
                onClose();
                return undefined;
            }
        };
    }, [
        responseType.jsInitialPayloadBuilder,
        responseType.name,
        stateIndex,
        simulation.history,
        simulation.contextType,
        simulation_BuildInitialResponsePayload,
        task,
        reportError,
        t,
        onClose,
    ]);

    const handleResponseChange = useCallback(
        (state: TaskResponseDataEditorState) => {
            setResponseEvent(state.value);
            setHasErrors(state.hasErrors);
        },
        [setResponseEvent, setHasErrors]
    );

    const dialogWidth: DialogProps["maxWidth"] = useMemo(() => {
        if (!responseType) {
            return "md";
        }
        return getDialogMaxWidthForResponseType(contextType, task.typeName, responseType.name);
    }, [contextType, responseType, task.typeName]);

    const handleSendResponse = useCallback(async () => {
        if (hasErrors) {
            return;
        }
        let replaceAll = true;
        if (stateIndex < simulation.history.length - 1 || simulation.pendingEvents.length > 0) {
            const action = await confirm({
                title: t("InterleaveOrReplaceResponse"),
                description: <ReactMarkdown>{t("InterleaveOrReplaceResponseDescription")}</ReactMarkdown>,
                mapCloseTo: "cancel",
                actions: [
                    {type: "interleave", text: t("InterleaveTaskResponse")},
                    {type: "replace", text: t("ReplaceTaskResponse")},
                    {type: "cancel", text: t("Cancel")},
                ],
            });
            if (action === "cancel") {
                return;
            }
            replaceAll = action === "replace";
        }
        await sendResponse(stateIndex, responseEvent, {clearFollowingEvents: replaceAll});
        onClose();
    }, [confirm, stateIndex, simulation, sendResponse, t, onClose, responseEvent, hasErrors]);

    const handleOpenJsonEditor = useCallback(() => {
        setJsonEditorOpen(true);
    }, []);

    const handleJsonEditorClose = useCallback(() => {
        setJsonEditorOpen(false);
    }, []);

    return (
        <Dialog open={open} fullWidth maxWidth={dialogWidth}>
            <DialogTitle disableTypography>
                <Typography component="h2" variant="h6">
                    {task.title}
                </Typography>
                <Typography variant="subtitle1">
                    {getSettings(responseType)?.actionInProgressText ?? responseType.name}
                </Typography>
            </DialogTitle>
            <DialogContent className={classes.content}>
                <TaskResponseDataEditor
                    task={task}
                    value={responseEvent}
                    contextType={contextType}
                    onChange={handleResponseChange}
                    payloadInitializer={responsePayloadInitializer}
                    jsonEditorOpen={jsonEditorOpen}
                    onJsonEditorClose={handleJsonEditorClose}
                />
            </DialogContent>
            <DialogActions>
                <Tooltip title="JSON" onClick={handleOpenJsonEditor}>
                    <IconButton>
                        <JsonIcon />
                    </IconButton>
                </Tooltip>
                <div className={classes.grow}></div>
                <Button onClick={handleSendResponse} color="primary">
                    {t("SendResponse")}
                </Button>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
