import React, {FC, useState, useCallback} from "react";
import {useNavigate} from "react-router";

import {EventListenerEditor} from "./event-listener-editor";
import {GenericEventListener, taskCreatedEventType} from "../../api/nimflow-api-extensions";
import {useNimflowEventListeners} from "../../api/nimflow-event-listeners";

const createGenericEventListener = () => {
    const result: GenericEventListener = {
        event: taskCreatedEventType,
        call: {
            jsParametersBinding: "args = {\n\n};"
        }
    };
    return result;
}

export const EventListenerCreator: FC<{}> = () => {
    const navigate = useNavigate();
    const {addEventListener} = useNimflowEventListeners();
    const [eventListener, setEventListener] = useState<GenericEventListener>(() => {
        return createGenericEventListener();
    });

    const handleOnChange = useCallback(
        async (value: GenericEventListener) => {
            const result = await addEventListener(value);
            value.id = result;
            setEventListener(value);
            setTimeout(() => {
                navigate(`../edit/${result}`, {replace: true});
            }, 0);
            return true;
        },
        [addEventListener, navigate]
    );

    return (
        <EventListenerEditor value={eventListener} onSave={handleOnChange} />
    );
};
