import {FC, useMemo, useCallback, useRef} from "react";
import {makeStyles, createStyles} from "@material-ui/core/styles";
import Divider from "@material-ui/core/Divider";
import Button from "@material-ui/core/Button";
import RefreshIcon from "@material-ui/icons/Refresh";
import {Responsive} from "react-grid-layout";
import {isPresent} from "ts-is-present";
import {useTranslation} from "react-i18next";
import {useFormikContext} from "formik";
import useSWR from "swr";
import {useResizeDetector} from "react-resize-detector";

import {DashboardDefinition, DashboardResponsiveLayoutSet} from "../../../api/nimflow-dashboard-api";
import {DashboardWidgetDefinition, useNimflowDashboardWidgets} from "../../../api/nimflow-dashboard-widgets-api";
import {DashboardWidgetViewer} from "./dashboard-widget-viewer";
import {DashboardContextProvider, useDashboardContext} from "./dashboard-context";
import {DashboardParameters} from "./dashboard-parameters";
import {useKey} from "../../../utils/use-key";
import {WaitSpinner} from "../../../components/wait-spinner";

const useStyles = makeStyles(theme =>
    createStyles({
        header: {
            display: "flex",
            overflow: "hidden",
            flexDirection: "row",
        },
        layout: {
            backgroundColor: theme.palette.background.default,
        },
        spaceFiller: {
            flex: "1 1",
        },
    })
);

const getGridLayoutProps = (layoutSet: DashboardResponsiveLayoutSet) => {
    const los = layoutSet?.layouts;
    if (!los) {
        return {};
    }
    const layouts: ReactGridLayout.Layouts = {};
    const breakpoints: Record<string, number> = {};
    const cols: Record<string, number> = {};
    const margins: Record<string, [number, number]> = {};
    for (const lo of los) {
        if (lo.widgetsLayouts) {
            layouts[lo.name] = lo.widgetsLayouts;
        }
        breakpoints[lo.name] = lo.breakpoint;
        cols[lo.name] = lo.cols;
        if (lo.margin) {
            margins[lo.name] = lo.margin;
        }
    }
    const margin = isPresent(layoutSet.margin)
        ? layoutSet.margin
        : Object.keys(margins).length > 0
        ? margins
        : undefined;
    return {layouts, breakpoints, cols, margin};
};

const DashboardRefreshButton: FC<{}> = props => {
    const {t} = useTranslation();
    const {refreshAllWidgetData} = useDashboardContext();

    const handleRefresh = useCallback(() => {
        refreshAllWidgetData();
    }, [refreshAllWidgetData]);

    return (
        <Button startIcon={<RefreshIcon />} onClick={handleRefresh}>
            {t("Refresh")}
        </Button>
    );
};

const updateWidgetDefinitions = async (
    current: DashboardWidgetDefinition[],
    dashboard: DashboardDefinition,
    getWidgetDefinition: (name: string) => Promise<DashboardWidgetDefinition>
) => {
    const required = dashboard.widgets.map(s => s.widgetName);
    const retained: DashboardWidgetDefinition[] = [];
    for (const widget of current) {
        const idx = required.indexOf(widget.name);
        if (idx >= 0) {
            retained.push(widget);
            required.splice(idx, 1);
        }
    }
    if (retained.length === current.length && required.length === 0) {
        return current;
    }
    const promises: Promise<DashboardWidgetDefinition>[] = [];
    for (const name of required) {
        promises.push(getWidgetDefinition(name));
    }
    const resolved = await Promise.all(promises);
    retained.push(...resolved);
    return retained;
};

const useWidgets = (dashboard: DashboardDefinition) => {
    const {dashboardWidgets_FindOneByName, getWidgetDefinition} = useNimflowDashboardWidgets();
    const prevWidgetsRef = useRef<DashboardWidgetDefinition[]>([]);
    const key = useKey(dashboardWidgets_FindOneByName, dashboard.widgets);
    const {data, isValidating, mutate, error} = useSWR(
        key,
        async () => {
            const prevWidgets = prevWidgetsRef.current;
            const retained = await updateWidgetDefinitions(prevWidgets, dashboard, s =>
                dashboardWidgets_FindOneByName(s).then(getWidgetDefinition)
            );
            prevWidgetsRef.current = retained;
            return retained;
        },
        {
            revalidateOnFocus: false,
        }
    );
    return useMemo(() => {
        return {
            widgets: data ?? [],
            isValidating,
            error,
            revalidate: mutate,
        };
    }, [data, isValidating, mutate, error]);
};

const Previewer: FC<{}> = props => {
    const {values: dashboard} = useFormikContext<DashboardDefinition>();
    const classes = useStyles();
    const gridProps = useMemo(() => {
        const layoutSet = dashboard.layoutSet;
        return layoutSet ? getGridLayoutProps(layoutSet) : {};
    }, [dashboard.layoutSet]);
    const {width, ref} = useResizeDetector({
        handleHeight: false,
    });

    return (
        <>
            <div className={classes.header}>
                <DashboardParameters />
                <div className={classes.spaceFiller} />
                <DashboardRefreshButton />
            </div>
            <Divider ref={ref} />
            {isPresent(width) && (
                <Responsive
                    className={classes.layout}
                    {...gridProps}
                    isDraggable={false}
                    isResizable={false}
                    width={width}
                >
                    {dashboard.widgets.map(s => (
                        <div key={s.instantiationId}>
                            <DashboardWidgetViewer instantiation={s} />
                        </div>
                    ))}
                </Responsive>
            )}
        </>
    );
};

export const DashboardDefinitionPreviewer: FC<{}> = props => {
    const {values: dashboard} = useFormikContext<DashboardDefinition>();
    const {widgets, isValidating} = useWidgets(dashboard);

    if (isValidating) {
        return <WaitSpinner />;
    }

    if (!widgets) {
        return null;
    }

    return (
        <DashboardContextProvider dashboard={dashboard} widgets={widgets}>
            <Previewer />
        </DashboardContextProvider>
    );
};
