import {JSONSchema7} from "json-schema";
import {clone, omit} from "ramda";

import {DashboardDefinition} from "../../api/nimflow-dashboard-api";

export const exportDashboardDefinitionToJson = (
    dashboardDefinition: DashboardDefinition
): Omit<DashboardDefinition, "jsonSchema"> & {$schema: string; jsonSchema?: JSONSchema7} => {
    const result: any = {
        $schema: "http://nimflow.com/schemas/dashboard-definition.schema.json",
        name: dashboardDefinition.name, // Force order of insertion of name before the rest
        ...clone(omit(["name", "id", "_id"], dashboardDefinition)),
    };
    if (dashboardDefinition.autoRefresh?.enabled === false) {
        delete result.autoRefresh;
    }
    return result;
};
