import React, {FC, useMemo, useCallback, useState} from "react";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import {Components, Action} from "@material-table/core";
import i18next from "i18next";
import {isPresent} from "ts-is-present";
import {useFormikContext, getIn} from "formik";
import {clone} from "ramda";
import {useTranslation} from "react-i18next";
import {v4 as uuid} from "uuid";

import {BaseColumn, BaseTableProps} from "../../components/base-table";
import {DashboardParameter, DashboardDefinition, getVariableNames} from "../../api/nimflow-dashboard-api";
import {FormikErrorViewer} from "../../components/formik-error-viewer";
import MaterialTable from "../../libs/material-table";
import {DashboardParameterEditionDialog, createSelectionParameterTemplate} from "./dashboard-parameter-edition-dialog";
import {useOkCancelConfirm} from "../../components/confirm";

export interface DashboardDefinitionParametersEditorProps {
    path: string;
    defaultValue?: DashboardParameter[];
}

const columns: BaseColumn<DashboardParameter>[] = [
    {
        field: "displayName",
        title: i18next.t("DisplayName"),
    },
    {
        field: "selectionType",
        title: i18next.t("SelectionType"),
    },
    {
        field: "dataType",
        title: i18next.t("DataType"),
    },
    {
        field: "variableNames",
        title: i18next.t("VariableNames"),
        render: rowData => {
            return getVariableNames(rowData).join(", ");
        },
    },
];

const options: BaseTableProps<any>["options"] = {
    search: false,
};

const tableComponents: Components = {
    Container: props => <div {...props} />,
};

const buildAddAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: AddIcon,
        tooltip: i18next.t("AddDashboardParameter"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const adaptHandler = <T extends object>(handler: (info: T) => void): Action<T>["onClick"] => {
    return (_, info) => {
        if (Array.isArray(info)) {
            info = info[0];
        }
        handler(info);
    };
};

const buildEditAction = (handler: (param: DashboardParameter) => void, disabled?: boolean) => {
    const action: Action<DashboardParameter> = {
        icon: EditIcon,
        tooltip: i18next.t("Edit"),
        onClick: adaptHandler(handler),
        disabled,
    };
    return action;
};

const buildDeleteAction = (handler: (param: DashboardParameter) => void, disabled?: boolean) => {
    const action: Action<DashboardParameter> = {
        icon: DeleteIcon,
        tooltip: i18next.t("Delete"),
        onClick: adaptHandler(handler),
        disabled,
    };
    return action;
};

export const DashboardDefinitionParametersEditor: FC<DashboardDefinitionParametersEditorProps> = props => {
    const {path} = props;
    const {errors, setFieldValue, values} = useFormikContext<Partial<DashboardDefinition>>();
    const confirm = useOkCancelConfirm();
    const {t} = useTranslation();
    const [editedParameter, setEditedParamter] = useState<Partial<DashboardParameter>>();

    const addAction = useMemo(() => {
        return buildAddAction(() => {
            setEditedParamter(createSelectionParameterTemplate());
        });
    }, []);

    const handleParameterChanged = useCallback(
        (param: DashboardParameter) => {
            const params: DashboardParameter[] = getIn(values, path) ?? [];
            if (!param.id) {
                param.id = uuid();
                setFieldValue(path, [...params, param]);
            } else {
                setFieldValue(
                    path,
                    params.map(s => (s.id === param.id ? param : s))
                );
            }
        },
        [path, setFieldValue, values]
    );

    const handleClose = useCallback(() => {
        setEditedParamter(undefined);
    }, []);

    const usedVariableNames = useMemo(() => {
        const params: DashboardParameter[] | undefined = getIn(values, path);
        return (
            params
                ?.filter(s => s.id !== editedParameter?.id)
                .flatMap(s => getVariableNames(s))
                .filter(isPresent) ?? []
        );
    }, [editedParameter, path, values]);

    const usedDisplayNames = useMemo(() => {
        const params: DashboardParameter[] | undefined = getIn(values, path);
        return (
            params
                ?.filter(s => s.id !== editedParameter?.id)
                .map(s => s.displayName)
                .filter(isPresent) ?? []
        );
    }, [editedParameter, path, values]);

    const rowActions = useMemo(() => {
        return [
            buildEditAction(row => {
                setEditedParamter(clone(row));
            }),
            buildDeleteAction(async row => {
                let result = await confirm({
                    description: t("ConfirmDeleteDashboardParameter", {name: row.displayName}),
                });
                if (result) {
                    const params: DashboardParameter[] = getIn(values, path) ?? [];
                    setFieldValue(
                        path,
                        params.filter(s => s.id !== row.id)
                    );
                }
                return result;
            }),
        ];
    }, [confirm, path, setFieldValue, t, values]);

    const actions = useMemo(() => {
        return [addAction, ...rowActions];
    }, [addAction, rowActions]);

    const data = useMemo(() => {
        return clone(values.parameters) ?? []; //Clone to prevent MaterialTable from adding internal bookkeeping fields
    }, [values.parameters]);

    return (
        <>
            <MaterialTable
                columns={columns}
                data={data}
                title=" "
                options={options}
                components={tableComponents}
                actions={actions}
            />
            <FormikErrorViewer errors={errors} names={path} />
            {editedParameter && (
                <DashboardParameterEditionDialog
                    open={!!editedParameter}
                    value={editedParameter}
                    usedDisplayNames={usedDisplayNames}
                    usedVariableNames={usedVariableNames}
                    onChange={handleParameterChanged}
                    onClose={handleClose}
                />
            )}
        </>
    );
};
