import React, {useState, useCallback, useRef, FC, useMemo, useEffect} from "react";
import {v4 as uuid} from "uuid";

export interface EditionScopeProps {}

export interface EditionScope {
    canAccept: boolean;
    setCanAcceptVote: (id: string, canAccept: boolean | undefined) => void;
}

export const useEditionScope = (props?: EditionScopeProps) => {
    const [canAccept, setCanAccept] = useState(true);
    const canAcceptVotesRef = useRef<{[key: string]: boolean}>({});

    const setCanAcceptVote = useCallback((id: string, canAccept: boolean | undefined) => {
        const canAcceptVotes = canAcceptVotesRef.current;
        if (typeof canAccept === "boolean") {
            canAcceptVotes[id] = canAccept;
        } else {
            delete canAcceptVotes[id];
        }
        const globalVote = Object.values(canAcceptVotes).every(s => s !== false);
        setCanAccept(globalVote);
    }, []);

    const result: EditionScope = {
        canAccept,
        setCanAcceptVote,
    };
    return result;
};

// const stub = (): never => {
//     throw new Error("You forgot to wrap your component in <EditionScopeProvider>.");
// };

const initialEditionScopeContext: EditionScope = {
    canAccept: true,
    setCanAcceptVote: () => {/* Do nothing */}, // If running outside an EditionScope, then there is nothing to vote
};

const EditionScopeContext = React.createContext<EditionScope>(initialEditionScopeContext);
EditionScopeContext.displayName = "EditionScopeFormContext";

export const EditionScopeProvider: FC<EditionScope> = props => {
    const {children, ...rest} = props;
    return <EditionScopeContext.Provider value={{...rest} as EditionScope}>{children}</EditionScopeContext.Provider>
};

export const useEditionScopeContext = (): EditionScope =>
    React.useContext(EditionScopeContext) as EditionScope;

export interface EditionScopeVotingOptions {
    voteIdPrefix?: string;
}

export const useEditionScopeCanAcceptVoting = (options?: EditionScopeVotingOptions) => {
    const voteIdPrefix = options?.voteIdPrefix;
    const voteId = useMemo(() => {
        if (voteIdPrefix) {
            return voteIdPrefix + "-" + uuid();
        }
        return uuid();
    }, [voteIdPrefix]);

    const {setCanAcceptVote} = useEditionScopeContext();

    useEffect(() => {
        return () => {
            setCanAcceptVote(voteId, undefined);
        };
    }, [voteId, setCanAcceptVote]);

    const vote = useCallback((canAccept: boolean) => {
        setCanAcceptVote(voteId, canAccept);
    }, [setCanAcceptVote, voteId]);
    return vote;
};