import {FieldErrors, FieldError} from "react-hook-form/dist/types/errors";
import {depthFirstSearch} from "../utils/graph-algorithms";
import {isPresent} from "ts-is-present";

export interface ErrorInfo {
    location?: string;
    dataPath?: string;
    message: string;
}

const isFieldError = (value: any): value is FieldError => {
    return (
        typeof value === "object" &&
        value !== null &&
        "type" in value &&
        (typeof value.type === "string" || value.type === undefined) &&
        "message" in value &&
        (typeof value.type === "string" || value.type === undefined)
    );
};

export const mapRhfErrorsToErrorInfos = <T extends Record<string, any>>(rhfErrors: FieldErrors<T>) => {
    const errors: ErrorInfo[] = [];
    const currentPath: Array<string | number> = [];
    const seen: any[] = [];
    depthFirstSearch<[string | number, any]>(["", rhfErrors], {
        getNeighbors: v => {
            const value = v[1];
            if (isFieldError(value)) {
                return [];
            }
            if (Array.isArray(value)) {
                return value.map((s, i) => [i, s]);
            }
            if (typeof value === "object" && value !== null) {
                return Object.entries(value);
            }
            return [];
        },
        enterVertex: ({currentVertex: [key, value]}) => {
            currentPath.push(key);
            if (isFieldError(value)) {
                const dataPath = currentPath
                    .map(s => {
                        if (typeof s === "number") {
                            return `[${s}]`;
                        } else if (s.length > 0) {
                            return `.${s}`;
                        }
                        return "";
                    })
                    .join("");
                if (value.types) {
                    for (const [, validationResult] of Object.entries(value.types)) {
                        if (typeof validationResult === "string") {
                            errors.push({
                                dataPath,
                                message: validationResult,
                            });
                        }
                    }
                } else if (isPresent(value.message)) {
                    errors.push({
                        dataPath,
                        message: value.message ?? "",
                    });
                }
            }
        },
        leaveVertex: () => {
            currentPath.pop();
        },
        allowTraversal: ({nextVertex: [, value]}) => {
            if (seen.includes(value)) {
                return false;
            }
            seen.push(value);
            return true;
        },
    });
    return errors;
};
