import React, {FC} from "react";
import clsx from "clsx";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import ToggleButton, {ToggleButtonProps} from "@material-ui/lab/ToggleButton";
import Tooltip from "@material-ui/core/Tooltip";
import Badge from "@material-ui/core/Badge";
import NotificationsIcon from "@material-ui/icons/Notifications";
import {useTranslation} from "react-i18next";
import {useRecoilValue} from "recoil";

import {newNotificationsState} from "../../services/notification-service";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            color: "inherit",
            border: "none",
        },
    })
);

export interface NotificationsButtonProps extends ToggleButtonProps {}

export const NotificationsButton: FC<NotificationsButtonProps> = props => {
    const {className, ...rest} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const newNotifications = useRecoilValue(newNotificationsState);

    return (
        <ToggleButton className={clsx(classes.root, className)} value="notifications" {...rest}>
            <Tooltip title={t("Notifications") as string} enterDelay={300}>
                <Badge badgeContent={newNotifications} color="error">
                    <NotificationsIcon />
                </Badge>
            </Tooltip>
        </ToggleButton>
    );
};
