import React, {FC, useState, useCallback, useMemo, useEffect, useRef} from "react";
import {makeStyles, createStyles, Theme, useTheme} from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Tooltip from "@material-ui/core/Tooltip";
import Tabs, {TabsProps} from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import IconButton from "@material-ui/core/IconButton";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import SaveIcon from "@material-ui/icons/Save";
import SaveAltIcon from "@material-ui/icons/SaveAlt";
import AssignmentTurnedInIcon from "@material-ui/icons/AssignmentTurnedIn";
import {useTranslation} from "react-i18next";
import * as yup from "yup";
import {useThrottleCallback} from "@react-hook/throttle";
import {useNavigate} from "react-router-dom";
import {useSnackbar} from "notistack";
import {Form, FormikProps, Formik, FormikHelpers, useFormikContext} from "formik";

import {DashboardDefinition} from "../../api/nimflow-dashboard-api";
import {DashboardDefinitionSaveAsDialog} from "./dashboard-definition-save-as-dialog";
import {DashboardDefinitionSchema} from "./dashboard-definition-schema";
import {useConfirm} from "../../components/confirm";
import {useReportError} from "../../utils/use-report-error";
import {useExitWithoutSavingBlocker} from "../../utils/use-exit-without-saving-blocker";
import {ErrorBoundary} from "../../components/error-boundary";
import {ErrorViewer} from "../../components/error-viewer";
import {downloadJson} from "../../utils/io-helpers";
import {validateWithYupSchema} from "../../libs/yup";
import {exportDashboardDefinitionToJson} from "./dashboard-definition-json-exporter";
import {useEditionScope, EditionScopeProvider} from "../../components/edition-scope";
import {DashboardDefinitionParametersEditor} from "./dashboard-definition-parameters-editor";
import {DashboardDefinitionLayoutEditor} from "./dashboard-definition-layout-editor";
import {validateAndSubmit} from "../../utils/formikHelpers";
import {FormikErrorViewer} from "../../components/formik-error-viewer";
import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {DashboardDefinitionPreviewer} from "./previewer/dashboard-definition-previewer";
import {DashboardDefinitionAutoRefreshEditor} from "./dashboard-definition-auto-refresh-editor";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            flexGrow: 1,
        },
        toolbar: {
            minHeight: "48px",
            paddingLeft: 0,
        },
        paper: {
            padding: theme.spacing(2),
            color: theme.palette.text.secondary,
        },
        grow: {
            flexGrow: 1,
        },
        actionToolbarButtons: {
            display: "flex",
        },
        fileInput: {
            opacity: 0,
            position: "absolute",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
        },
        textField: {
            width: "40ch",
        },
        form: {
            height: "100%",
        },
        button: {
            margin: theme.spacing(1),
        },
        diagram: {
            marginTop: 20,
            marginBottom: 20,
            height: "calc(100% - 40px)",
        },
        table: {
            display: "table",
            width: "100%",
            height: "100%",
        },
        tableRow: {
            display: "table-row",
        },
        diagramTableRow: {
            display: "table-row",
            height: "100%",
        },
        saveAsEllipsis: {
            position: "absolute",
            left: 17,
            bottom: 4,
            fontWeight: "bold",
        },
    })
);

const useTabClassesErrorStyles = makeStyles((theme: Theme) =>
    createStyles({
        wrapper: {
            color: theme.palette.error.main,
        },
    })
);

export interface DashboardDefinitionEditorProps {
    value: DashboardDefinition;
    schema?: yup.ObjectSchema<any>;
    onApplyChanges?: (dashboardDefinition: DashboardDefinition) => Promise<void>;
    onSaveAs?: (dashboardDefinition: DashboardDefinition, newName: string) => Promise<void>;
    isPersistent?: boolean;
}

const SaveAsButton = (props: {name: string; canSave: boolean; onSaveAs?: (newName: string) => void}) => {
    const {name, canSave, onSaveAs} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const {validateForm} = useFormikContext<DashboardDefinition>();
    const [saveAsDialogOpen, setSaveAsDialogOpen] = useState(false);

    const handleSaveAsDashboardDefinitionClick = useCallback(async () => {
        const result = await validateForm();
        if (Object.keys(result).length > 0) {
            return;
        }
        setSaveAsDialogOpen(true);
    }, [validateForm]);

    const handleOnChange = useCallback(
        (value: string) => {
            onSaveAs?.(value);
        },
        [onSaveAs]
    );
    const handleOnClose = useCallback(() => {
        setSaveAsDialogOpen(false);
    }, []);

    return (
        <>
            <Tooltip title={t("SaveAs") as string}>
                <IconButton onClick={handleSaveAsDashboardDefinitionClick} disabled={!canSave}>
                    <SaveIcon />
                    <Typography className={classes.saveAsEllipsis}>...</Typography>
                </IconButton>
            </Tooltip>
            {saveAsDialogOpen && (
                <DashboardDefinitionSaveAsDialog
                    open={saveAsDialogOpen}
                    defaultValue={name}
                    onChange={handleOnChange}
                    onClose={handleOnClose}
                />
            )}
        </>
    );
};

interface DashboardDefinitionEditorInnerProps {
    schema?: yup.ObjectSchema<any>;
    onSave?: () => Promise<void>;
    onSaveAs?: (newName: string) => Promise<void>;
    isPersistent?: boolean;
}

const DashboardDefinitionEditorInner: FC<DashboardDefinitionEditorInnerProps> = props => {
    const {schema = DashboardDefinitionSchema, onSave, onSaveAs, isPersistent = false} = props;
    const {t} = useTranslation();
    const theme = useTheme();
    const classes = useStyles();
    const tabClassesError = useTabClassesErrorStyles();
    const navigate = useNavigate();
    const {enqueueSnackbar} = useSnackbar();
    const {
        isSubmitting,
        errors,
        validateForm,
        values: dashboardDefinition,
        dirty,
    } = useFormikContext<DashboardDefinition>();
    const editionScope = useEditionScope();
    const canSave = !isSubmitting && editionScope.canAccept;
    const [activeTab, setActiveTab] = useState(0);
    const [previewActive, setPreviewActive] = useState(false);
    const reportErrors = useReportError();

    const opts = useMemo(() => {
        return {
            description: t("ExitingDashboardDefinitionEditorWithoutSavingWarning"),
        };
    }, [t]);
    useExitWithoutSavingBlocker(dirty, opts);

    const handleSaveDashboardDefinitionClick = useThrottleCallback(
        useCallback(async () => {
            if (onSave) {
                await onSave();
            }
        }, [onSave])
    );

    const handleOnSaveAs = useCallback(
        async (newName: string) => {
            if (onSaveAs) {
                await onSaveAs(newName);
            }
        },
        [onSaveAs]
    );

    const handleExportDashboardDefinitionClick = useCallback(() => {
        downloadJson(
            exportDashboardDefinitionToJson(dashboardDefinition),
            `${dashboardDefinition.name}.dashboard-definition.json`
        );
    }, [dashboardDefinition]);

    const handleValidateContextTypeClick = useCallback(async () => {
        const errorInfos = await validateWithYupSchema(schema, dashboardDefinition);
        await validateForm();
        if (errorInfos.length > 0) {
            reportErrors(t("DashboardDefinitionHasValidationErrors"), errorInfos, {
                dialogProps: {
                    maxWidth: "lg",
                },
            });
        } else {
            enqueueSnackbar(t("DashboardDefinitionTypeIsValid"), {variant: "success"});
        }
    }, [schema, dashboardDefinition, validateForm, reportErrors, t, enqueueSnackbar]);

    const handleGoBack = useCallback(() => {
        navigate(-1);
    }, [navigate]);

    const parametersClasses = useMemo(() => {
        return !!errors.parameters ? tabClassesError : {};
    }, [errors.parameters, tabClassesError]);
    const widgetsClasses = useMemo(() => {
        return !!errors.widgets || !!errors.layoutSet ? tabClassesError : {};
    }, [errors.widgets, errors.layoutSet, tabClassesError]);
    const tabIndicatorProps: TabsProps["TabIndicatorProps"] = useMemo(() => {
        const tabIndicatorColor = theme.palette.error.main;
        if ((activeTab === 0 && !!errors.parameters) || (activeTab === 1 && (!!errors.widgets || !!errors.layoutSet))) {
            return {
                style: {
                    backgroundColor: tabIndicatorColor,
                },
            };
        }
        return undefined;
    }, [activeTab, errors, theme.palette.error.main]);

    const handleTabChange = (event: React.ChangeEvent<{}>, newValue: number) => {
        if (newValue === 3) {
            setPreviewActive(true);
        }
        setActiveTab(newValue);
    };

    return (
        <div className={classes.table}>
            <div className={classes.tableRow}>
                <Grid container spacing={1}>
                    <Grid item xs={12}>
                        <Toolbar className={classes.toolbar}>
                            <Tooltip title={t("GoBack") as string}>
                                <IconButton onClick={handleGoBack} disabled={isSubmitting}>
                                    <ArrowBackIcon />
                                </IconButton>
                            </Tooltip>
                            <Tooltip title={t("SaveChanges") as string}>
                                <IconButton onClick={handleSaveDashboardDefinitionClick} disabled={!canSave}>
                                    <SaveIcon />
                                </IconButton>
                            </Tooltip>
                            {isPersistent && (
                                <SaveAsButton
                                    name={dashboardDefinition.name}
                                    canSave={canSave}
                                    onSaveAs={handleOnSaveAs}
                                />
                            )}
                            <Tooltip title={t("Export") as string}>
                                <IconButton onClick={handleExportDashboardDefinitionClick}>
                                    <SaveAltIcon />
                                </IconButton>
                            </Tooltip>
                            <Tooltip title={t("Validate") as string}>
                                <IconButton onClick={handleValidateContextTypeClick}>
                                    <AssignmentTurnedInIcon />
                                </IconButton>
                            </Tooltip>
                        </Toolbar>
                    </Grid>
                    <Grid item xs={3}>
                        <FormikTextField
                            name="name"
                            label={t("Name")}
                            InputLabelProps={{required: true}}
                            className={classes.textField}
                        />
                    </Grid>
                    <Grid item xs={3}>
                        <FormikTextField
                            name="title"
                            label={t("Title")}
                            InputLabelProps={{required: true}}
                            className={classes.textField}
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <FormikTextField name="description" label={t("Description")} multiline fullWidth maxRows={4} />
                    </Grid>
                    <Grid item xs={12}>
                        <Tabs
                            value={activeTab}
                            onChange={handleTabChange}
                            variant="scrollable"
                            scrollButtons="on"
                            TabIndicatorProps={tabIndicatorProps}
                            indicatorColor="primary"
                            textColor="primary"
                        >
                            <Tab label={t("Parameters")} classes={parametersClasses} />
                            <Tab label={t("Widgets")} classes={widgetsClasses} />
                            <Tab label={t("AutoRefresh")} />
                            <Tab label={t("Preview")} />
                        </Tabs>
                        <div role="tabpanel" hidden={activeTab !== 0}>
                            <Paper className={classes.paper} elevation={3}>
                                <DashboardDefinitionParametersEditor path="parameters" />
                            </Paper>
                        </div>
                        <div role="tabpanel" hidden={activeTab !== 1}>
                            <Paper className={classes.paper} elevation={3}>
                                <DashboardDefinitionLayoutEditor />
                            </Paper>
                        </div>
                        <div role="tabpanel" hidden={activeTab !== 2}>
                            <Paper className={classes.paper} elevation={3}>
                                <DashboardDefinitionAutoRefreshEditor />
                            </Paper>
                        </div>
                        {previewActive && (
                            <div role="tabpanel" hidden={activeTab !== 3}>
                                <Paper className={classes.paper} elevation={3}>
                                    <ErrorBoundary>
                                        <DashboardDefinitionPreviewer />
                                    </ErrorBoundary>
                                </Paper>
                            </div>
                        )}
                    </Grid>
                </Grid>
            </div>
            {/* <FormikErrorViewer errors={errors} className={classes.tableRow} /> */}
            <FormikErrorViewer errors={errors} names={["id"]} className={classes.tableRow} />
            {/* {JSON.stringify(dashboardDefinition)} */}
        </div>
    );
};

export const DashboardDefinitionEditor: FC<DashboardDefinitionEditorProps> = props => {
    const {value, schema = DashboardDefinitionSchema, onApplyChanges, onSaveAs} = props;
    const {t} = useTranslation();
    const editionScope = useEditionScope();
    const confirm = useConfirm();
    const {enqueueSnackbar} = useSnackbar();
    const initialValues = useMemo(() => {
        return {
            ...value,
            description: value.description ?? "",
            autoRefresh: value.autoRefresh ?? {enabled: false},
        };
    }, [value]);
    const formRef = useRef<FormikProps<DashboardDefinition> | null>();

    useEffect(() => {
        formRef.current?.resetForm({values: initialValues});
    }, [initialValues]);

    const submitHandlerRef = useRef<(values: DashboardDefinition) => Promise<void>>();

    const handleSave = useCallback(() => {
        const form = formRef.current;
        if (form && onApplyChanges) {
            submitHandlerRef.current = async (data: DashboardDefinition) => {
                let error: React.ReactNode;
                try {
                    await onApplyChanges(data);
                } catch (e) {
                    error = <ErrorViewer error={e} />;
                }
                if (error) {
                    await confirm({
                        title: t("ErrorSavingDashboardDefinition"),
                        description: error,
                        mapCloseTo: "ok",
                        actions: [{type: "ok", text: t("Ok")}],
                    });
                } else {
                    enqueueSnackbar(t("DashboardDefinitionSaved"), {variant: "success"});
                }
            };
            validateAndSubmit(form);
        }
        return Promise.resolve();
    }, [confirm, enqueueSnackbar, onApplyChanges, t]);

    const handleSaveAs = useCallback(
        (newValue: string) => {
            const form = formRef.current;
            if (form && onSaveAs) {
                submitHandlerRef.current = async (data: DashboardDefinition) => {
                    let error: React.ReactNode;
                    try {
                        await onSaveAs(data, newValue);
                    } catch (e) {
                        error = <ErrorViewer error={e} />;
                    }
                    if (error) {
                        await confirm({
                            title: t("ErrorSavingDashboardDefinition"),
                            description: error,
                            mapCloseTo: "ok",
                            actions: [{type: "ok", text: t("Ok")}],
                        });
                    } else {
                        enqueueSnackbar(t("DashboardDefinitionSaved"), {variant: "success"});
                    }
                };
                validateAndSubmit(form);
            }
            return Promise.resolve();
        },
        [confirm, enqueueSnackbar, onSaveAs, t]
    );

    const handleSubmit = useCallback(
        async (values: DashboardDefinition, actions: FormikHelpers<DashboardDefinition>) => {
            try {
                if (submitHandlerRef.current) {
                    if (values.autoRefresh?.enabled === false) {
                        delete values.autoRefresh;
                    }
                    await submitHandlerRef.current(values);
                    submitHandlerRef.current = undefined;
                }
            } finally {
                actions.setSubmitting(false);
            }
        },
        []
    );

    return (
        <ErrorBoundary>
            <Formik<DashboardDefinition>
                innerRef={s => (formRef.current = s)}
                initialValues={initialValues}
                onSubmit={handleSubmit}
                validationSchema={schema}
                validateOnChange={true}
                validateOnBlur={true}
            >
                <Form autoComplete="off">
                    <EditionScopeProvider {...editionScope}>
                        <DashboardDefinitionEditorInner {...props} onSave={handleSave} onSaveAs={handleSaveAs} />
                    </EditionScopeProvider>
                </Form>
            </Formik>
        </ErrorBoundary>
    );
};
