import React, {createContext, FC, useState, useCallback, useEffect, useMemo} from "react";
import {clone, last} from "ramda";
import produce from "immer";
import {isPresent} from "ts-is-present";
import {yupResolver} from "@hookform/resolvers/yup";
import {FieldErrors} from "react-hook-form/dist/types/errors";
import {useTranslation} from "react-i18next";
import {atom, useRecoilState} from "recoil";

import {
    ContextType,
    SimulateSteps,
    TaskResponseEvent,
    SimulationEvent,
    SimulateStepsResult,
    SimulateStepsResultStatus,
} from "../../api/nimflow-api";
import {useNimflow, NimflowApi} from "../../api/nimflow-api-provider";
import {SimulationContextTypeProvider, SimulationResultAnalysis} from "./simulation-context-type-provider";
import {ContextSimulation, useNimflowSimulation} from "../../api/nimflow-simulation-api";
import {ContextSimulationSchema} from "./context-simulation-schema";
import { isAxiosError } from "../../libs/axios";

export interface ContextSimulationState {
    contextTypeProvider: SimulationContextTypeProvider;
    contextType: ContextType;
    simulation: ContextSimulation;
}

export interface ContextSimulationOptions {
    shouldExecuteAllPendingEvents?: boolean;
    ensureContextTypeUptodate?: boolean;
}

export interface ContextSimulationService {
    getState(): ContextSimulationState;
    resetSimulation(): void;
    setSimulation(simulation: ContextSimulation, contextTypeProvider: SimulationContextTypeProvider): void;
    executeNextPendingEvent(): Promise<void>;
    executeAllPendingEvents(): Promise<void>;
    updateSimulation(
        mutator: (draft: ContextSimulation) => any,
        options?: {executeAllPendingEvents?: boolean}
    ): Promise<void>;
    sendResponse(
        stateIndex: number,
        response: TaskResponseEvent,
        options?: {executeAllPendingEvents?: boolean; clearFollowingEvents?: boolean}
    ): Promise<void>;
    deleteEvent(
        eventIndex: number,
        options?: {executeAllPendingEvents?: boolean; deleteFollowingEvents?: boolean}
    ): Promise<void>;
    updateEventData(
        eventIndex: number,
        event: SimulationEvent,
        options?: {executeAllPendingEvents?: boolean}
    ): Promise<void>;
    validate(): Promise<FieldErrors<ContextSimulation>>;
    errors: FieldErrors<ContextSimulation>;
    options: ContextSimulationOptions;
    setOptions(options: ContextSimulationOptions): void;
}

const stub = (): never => {
    throw new Error("You forgot to wrap your component in <ContextSimulationContextProvider>.");
};

const initialContextSimulationContext: ContextSimulationService = {
    getState: stub,
    resetSimulation: stub,
    setSimulation: stub,
    executeNextPendingEvent: stub,
    executeAllPendingEvents: stub,
    updateSimulation: stub,
    sendResponse: stub,
    deleteEvent: stub,
    updateEventData: stub,
    validate: stub,
    errors: {},
    options: {
        shouldExecuteAllPendingEvents: true,
    },
    setOptions: stub,
};

const ContextSimulationContext = createContext<ContextSimulationService>(initialContextSimulationContext);

export interface ContextSimulationContextProviderProps {
    contextTypeProvider: SimulationContextTypeProvider;
    simulation?: ContextSimulation;
    options?: ContextSimulationOptions;
}

const ensureContextTypeConsistency = (simulation: ContextSimulation, contextType: ContextType) => {
    const simContextTypeName = simulation.contextTypeName ?? simulation.contextType?.name;
    if (contextType.name !== simContextTypeName) {
        const msg =
            "Simulation context type name (" +
            simContextTypeName +
            ") is different from context type provided (" +
            contextType.name +
            ")";
        throw new Error(msg);
    }
};

const cloneSimulationAndEnsureContextTypeConsistency = (simulation: ContextSimulation, contextType: ContextType) => {
    ensureContextTypeConsistency(simulation, contextType);
    simulation = clone(simulation);
    // Ensure context type consistency
    if (simulation.contextType) {
        simulation.contextType = contextType;
    }
    return simulation;
};

const getResetSimulation = (simulation: ContextSimulation): ContextSimulation => {
    let result: ContextSimulation = produce(simulation, sim => {
        sim.pendingEvents.splice(0, 0, ...sim.history.map(s => s.event));
        sim.history = [];
        delete sim.initialState;
    });
    return result;
};

const _executeEventsAtState = async (
    contextTypeProvider: SimulationContextTypeProvider,
    simulation_Simulate: NimflowApi["simulation_Simulate"],
    simulation: ContextSimulation,
    events: SimulationEvent | SimulationEvent[],
    stateIndex?: number
): Promise<ContextSimulation> => {
    function buildResult(result: SimulateStepsResult): ContextSimulation {
        const startIndex = Array.isArray(events) ? events.length : 1;
        return {
            ...simulation,
            pendingEvents: simulation.pendingEvents.slice(startIndex),
            history: [...simulation.history, ...result.states],
        };
    }

    if (!Array.isArray(events)) {
        events = [events];
    }
    const steps: SimulateSteps = contextTypeProvider.configureSimulationRequest({
        events,
    });
    const currentState = typeof stateIndex === "number" ? simulation.history[stateIndex] : last(simulation.history);
    if (currentState) {
        steps.currentState = {
            context: currentState.context,
            tasks: currentState.tasks,
        };
    }
    try {
        const result = await simulation_Simulate(steps);
        const responseAnalysis = contextTypeProvider.analyzeSimulationResponse(result);
        if (responseAnalysis >= SimulationResultAnalysis.UnrecognizedExecutionStatus) {
            throw new Error(
                `Unexpected result from simulation: ${SimulationResultAnalysis[result.status]} (${result.status})`
            );
        }
        if (responseAnalysis === SimulationResultAnalysis.ReexecuteFromInitialState) {
            const resetSimulation = getResetSimulation(simulation);
            const historySlice =
                typeof stateIndex === "number" ? simulation.history.slice(0, stateIndex) : simulation.history;
            const newEvents = [...historySlice.map(s => s.event), ...events];
            return _executeEventsAtState(contextTypeProvider, simulation_Simulate, resetSimulation, newEvents);
        }
        return buildResult(result);
    } catch (e) {
        return buildResult({
            status: SimulateStepsResultStatus.Completed,
            states: events.map(s => ({
                event: s,
                context: {
                    id: "",
                    typeName: contextTypeProvider.contextType.name,
                    state: {},
                    dateCreated: "",
                    dateModified: "",
                },
                tasks: [],
                errors: [isAxiosError(e) ? e.message + "\n" + e.response : ""],
            })),
        });
    }
};

export const shouldExecuteAllPendingEventsState = atom({
    key: "simulator:shouldExecuteAllPendingEvents",
    default: initialContextSimulationContext.options.shouldExecuteAllPendingEvents ?? true,
});

export const ContextSimulationContextProvider: FC<ContextSimulationContextProviderProps> = props => {
    const {
        contextTypeProvider: initialContextTypeProvider,
        simulation: initialSimulation,
        options: initialOptions,
        children,
    } = props;
    const {t} = useTranslation();
    const {simulation_Simulate} = useNimflow();
    const {simulations_GetInfos} = useNimflowSimulation();
    const [shouldExecuteAllPendingEvents, setShouldExecuteAllPendingEvents] = useRecoilState(
        shouldExecuteAllPendingEventsState
    );
    const [contextTypeProvider, setContextTypeProvider] = useState<SimulationContextTypeProvider>(
        initialContextTypeProvider
    );
    const [simulation, setSimulation] = useState<ContextSimulation>(() => {
        if (initialSimulation) {
            return cloneSimulationAndEnsureContextTypeConsistency(initialSimulation, contextTypeProvider.contextType);
        }
        const sim: ContextSimulation = {
            contextTypeName: contextTypeProvider.contextType.name,
            pendingEvents: [],
            history: [],
        };
        return sim;
    });
    const [errors, setErrors] = useState<FieldErrors<ContextSimulation>>({});

    const getState = useCallback(() => {
        const result: ContextSimulationState = {
            simulation,
            contextType: contextTypeProvider.contextType,
            contextTypeProvider,
        };
        return result;
    }, [contextTypeProvider, simulation]);

    const setSimulationAndContextType = useCallback(
        (
            newSimulation: ContextSimulation,
            newContextTypeProvider: SimulationContextTypeProvider = contextTypeProvider
        ) => {
            newSimulation = cloneSimulationAndEnsureContextTypeConsistency(
                newSimulation,
                newContextTypeProvider.contextType
            );
            if (newContextTypeProvider !== contextTypeProvider) {
                setContextTypeProvider(newContextTypeProvider);
            }
            setSimulation(newSimulation);
        },
        [setContextTypeProvider, setSimulation, contextTypeProvider]
    );

    const resetSimulation = useCallback(() => {
        setSimulation(getResetSimulation(simulation));
    }, [simulation]);

    const executeEventsAtState = useCallback(
        (
            simulation: ContextSimulation,
            events: SimulationEvent | SimulationEvent[],
            stateIndex?: number
        ): Promise<ContextSimulation> =>
            _executeEventsAtState(contextTypeProvider, simulation_Simulate, simulation, events, stateIndex),
        [simulation_Simulate, contextTypeProvider]
    );

    const updateSimulation = useCallback(
        async (
            simulation: ContextSimulation,
            mutator: (draft: ContextSimulation) => any,
            opts?: {executeAllPendingEvents?: boolean}
        ) => {
            let newSim: ContextSimulation = produce(simulation, mutator);
            ensureContextTypeConsistency(newSim, contextTypeProvider.contextType);
            if ((opts?.executeAllPendingEvents ?? shouldExecuteAllPendingEvents) && newSim.pendingEvents.length > 0) {
                newSim = await executeEventsAtState(newSim, newSim.pendingEvents);
            }
            setSimulation(newSim);
        },
        [setSimulation, contextTypeProvider, executeEventsAtState, shouldExecuteAllPendingEvents]
    );

    const executeNextPendingEvent = useCallback(async () => {
        if (simulation.pendingEvents.length === 0) {
            return;
        }
        const newSim = await executeEventsAtState(simulation, simulation.pendingEvents[0]);
        setSimulation(newSim);
    }, [simulation, setSimulation, executeEventsAtState]);

    const executeAllPendingEvents = useCallback(async () => {
        if (simulation.pendingEvents.length === 0) {
            return;
        }
        const newSim = await executeEventsAtState(simulation, simulation.pendingEvents);
        setSimulation(newSim);
    }, [simulation, setSimulation, executeEventsAtState]);

    const sendResponse = useCallback(
        async (
            stateIndex: number,
            response: TaskResponseEvent,
            options?: {executeAllPendingEvents?: boolean; clearFollowingEvents?: boolean}
        ) => {
            const state = simulation.history[stateIndex];
            if (!state) {
                throw new Error(`Invalid stateIndex: ${stateIndex}`);
            }
            const sim = produce(simulation, draft => {
                const statesToRemove = draft.history.length - stateIndex + 1;
                const removedStates = draft.history.splice(stateIndex + 1, statesToRemove);
                if (options?.clearFollowingEvents ?? true) {
                    draft.pendingEvents = [response];
                } else {
                    draft.pendingEvents = [response, ...removedStates.map(s => s.event), ...draft.pendingEvents];
                }
            });
            const newSim = await executeEventsAtState(
                sim,
                options?.executeAllPendingEvents ? response : sim.pendingEvents,
                stateIndex
            );
            setSimulation(newSim);
        },
        [simulation, setSimulation, executeEventsAtState]
    );

    const deleteEvent = useCallback(
        (eventIndex: number, options?: {executeAllPendingEvents?: boolean; deleteFollowingEvents?: boolean}) => {
            if (options?.deleteFollowingEvents ?? true) {
                return updateSimulation(
                    simulation,
                    draft => {
                        if (eventIndex >= draft.history.length) {
                            draft.pendingEvents = draft.pendingEvents.splice(0, eventIndex - draft.history.length);
                        } else {
                            draft.pendingEvents = [];
                            draft.history = draft.history.slice(0, eventIndex);
                        }
                    },
                    {executeAllPendingEvents: options?.executeAllPendingEvents}
                );
            } else {
                return updateSimulation(
                    simulation,
                    draft => {
                        if (eventIndex >= draft.history.length) {
                            draft.pendingEvents.splice(eventIndex - draft.history.length, 1);
                        } else {
                            if (eventIndex < draft.history.length - 1) {
                                const events = draft.history.slice(eventIndex + 1).map(s => s.event);
                                draft.pendingEvents = [...events, ...draft.pendingEvents];
                            }
                            draft.history = draft.history.slice(0, eventIndex);
                        }
                    },
                    {executeAllPendingEvents: options?.executeAllPendingEvents}
                );
            }
        },
        [updateSimulation, simulation]
    );

    const updateEventData = useCallback(
        (eventIndex: number, event: SimulationEvent, options?: {executeAllPendingEvents?: boolean}) => {
            return updateSimulation(
                simulation,
                draft => {
                    if (eventIndex < draft.history.length) {
                        const newPending = draft.history.slice(eventIndex).map(s => s.event);
                        draft.pendingEvents = [...newPending, ...draft.pendingEvents];
                        draft.history = draft.history.slice(0, eventIndex);
                    }
                    const pendingIndex = eventIndex - draft.history.length;
                    draft.pendingEvents[pendingIndex] = event;
                },
                options
            );
        },
        [updateSimulation, simulation]
    );

    const options = useMemo(() => {
        return {
            shouldExecuteAllPendingEvents,
        };
    }, [shouldExecuteAllPendingEvents]);

    const setOptions = useCallback(
        (options: ContextSimulationOptions) => {
            if (
                isPresent(options.shouldExecuteAllPendingEvents) &&
                options.shouldExecuteAllPendingEvents !== shouldExecuteAllPendingEvents
            ) {
                setShouldExecuteAllPendingEvents(options.shouldExecuteAllPendingEvents);
                if (options.shouldExecuteAllPendingEvents) {
                    executeAllPendingEvents();
                }
            }
        },
        [setShouldExecuteAllPendingEvents, shouldExecuteAllPendingEvents, executeAllPendingEvents]
    );

    const simulationSchema = useMemo(() => {
        return ContextSimulationSchema.clone().test({
            message: () => t("AvoidDuplications"),
            test: async function (val: any) {
                const value = val as ContextSimulation;
                if (!value) {
                    return true;
                }
                try {
                    const infos = await simulations_GetInfos();
                    const possibleCollision = infos.find(s => s.name === value.name);
                    if (possibleCollision && (!value.id || value.id !== possibleCollision.id)) {
                        return this.createError({
                            path: "name",
                            message: t("ContextSimulationNameAlreadyInUse", {name: value.name}),
                        });
                    }
                } catch {}
                return true;
            },
        });
    }, [simulations_GetInfos, t]);

    const validate = useCallback(async () => {
        const {errors} = await yupResolver(simulationSchema)(simulation, {}, true);
        setErrors(errors);
        return errors;
    }, [simulationSchema, simulation]);

    useEffect(() => {
        if (!initialSimulation) {
            return;
        }
        if (initialOptions?.shouldExecuteAllPendingEvents && initialSimulation.pendingEvents.length > 0) {
            executeEventsAtState(initialSimulation, initialSimulation.pendingEvents).then(newSim => {
                setSimulation(newSim);
            });
        }
    }, [initialOptions, initialSimulation, executeEventsAtState]);

    const contextSimulationContext: ContextSimulationService = {
        getState,
        resetSimulation,
        setSimulation: setSimulationAndContextType,
        executeNextPendingEvent,
        executeAllPendingEvents,
        updateSimulation: useCallback((mutator, opts) => updateSimulation(simulation, mutator, opts), [
            updateSimulation,
            simulation,
        ]),
        sendResponse,
        deleteEvent,
        updateEventData,
        validate,
        errors,
        options,
        setOptions,
    };

    return (
        <ContextSimulationContext.Provider value={contextSimulationContext}>
            {children}
        </ContextSimulationContext.Provider>
    );
};

export const useContextSimulationContext = () => React.useContext(ContextSimulationContext);
