import React, {FC, useState, useCallback, useMemo} from "react";
import {useNavigate} from "react-router";
import * as yup from "yup";
import {useTranslation} from "react-i18next";

import {ContextTypeEditor} from "./context-type-editor";
import {useNimflow} from "../../api/nimflow-api-provider";
import {ContextType} from "../../api/nimflow-api";
import {ContextTypeSchema} from "../../models/context-type-schema";

export const ContextTypeCreator: FC<{}> = () => {
    const navigate = useNavigate();
    const {t} = useTranslation();
    const {contextTypes_Add, contextTypes_FindOne} = useNimflow();
    const [contextType, setContextType] = useState<ContextType>(() => {
        const result: ContextType = {
            name: "",
            version: 0,
            actions: [],
            tasks: [],
            initialActions: [],
            milestones: [],
            tags: [],
            metrics: []
        };
        return result;
    });

    const handleOnChange = useCallback(
        async (value: ContextType) => {
            const result = await contextTypes_Add(value);
            setContextType(result);
            navigate(`../edit/${result.name}`, {replace: true});
            return true;
        },
        [setContextType, navigate, contextTypes_Add]
    );

    const ContextTypeCreatorSchema = useMemo(
        () =>
            ContextTypeSchema.shape({
                name: ContextTypeSchema.fields!.name.concat(
                    yup
                        .string()
                        .defined()
                        .test({
                            message: ({value}) => t("ContextTypeNameAlreadyExists", {name: value}),
                            test: async function (value: any) {
                                if (!value) {
                                    return true;
                                }
                                const searchResult = await contextTypes_FindOne({name: value});
                                return searchResult == null;
                            },
                        })
                ),
            }),
        [contextTypes_FindOne, t]
    );

    return <ContextTypeEditor value={contextType} schema={ContextTypeCreatorSchema} onApplyChanges={handleOnChange} />;
};
