import {clone} from "ramda";

import {ContextType} from "../../api/nimflow-api";
import {ContextSimulation} from "../../api/nimflow-simulation-api";

export interface SimulationStateExportOptions {
    inlineContextType?: boolean;
    exportOnlyActions?: boolean; //Export all actions executed as pending actions without state history
}

export const exportSimulationStateToJson = (
    simulation: ContextSimulation,
    contextType: ContextType,
    options?: SimulationStateExportOptions
): any => {
    const clonedSim = clone(simulation);
    delete clonedSim.id;
    if (options?.inlineContextType !== true) {
        clonedSim.contextTypeName = contextType.name;
        delete clonedSim.contextType;
    } else if (options?.inlineContextType === true && !clonedSim.contextType && clonedSim.contextTypeName) {
        clonedSim.contextType = contextType;
        delete clonedSim.contextTypeName;
    }
    if (options?.exportOnlyActions) {
        clonedSim.pendingEvents = clonedSim.history.map(s => s.event);
        delete (clonedSim as any).history;
    }
    const result: any = {
        $schema: "http://nimflow.com/schemas/simulation-state.schema.json",
    };
    if ((clonedSim as any).contextTypeName) {
        result.contextTypeName = (clonedSim as any).contextTypeName;
    }
    return {
        ...result,
        ...clonedSim,
    };
};
