import React, {FC, useState, useEffect} from "react";
import ReactMarkdown from "react-markdown";

import {DashboardWidgetMarkdownVisualization} from "../../api/nimflow-dashboard-widgets-api";
import {ErrorBoundary} from "../../components/error-boundary";
import {evaluateHandlebars} from "../../libs/handlebars";

export interface DashboardWidgetMarkdownVisualizationPreviewerProps {
    visualization: DashboardWidgetMarkdownVisualization;
    requiredParamsHaveValue: boolean;
    args: Record<string, any>;
    data?: Record<string, any>;
}

export const DashboardWidgetMarkdownVisualizationPreviewer: FC<DashboardWidgetMarkdownVisualizationPreviewerProps> = props => {
    const {visualization, requiredParamsHaveValue, args, data} = props;
    const [template, setTemplate] = useState<string>(visualization.template);

    useEffect(() => {
        if (!requiredParamsHaveValue) {
            return;
        }
        //const templ = evaluateHandlebars(visualization.template + "\n\nContext: {{debug}}", {params: args, data, debug: JSON.stringify({params, data})});
        try {
            const templ = evaluateHandlebars(visualization.template, {params: args, data});
            setTemplate(templ);
        } catch (error) {
            setTemplate(error instanceof Error ? error.message : "");
        }
    }, [args, data, requiredParamsHaveValue, visualization.template]);

    return (
        <ErrorBoundary>
            <ReactMarkdown>{template}</ReactMarkdown>;
        </ErrorBoundary>
    );
};
