import {FC, useMemo} from "react";
import {Column, Action} from "@material-table/core";
import Alert from "@material-ui/lab/Alert";
import {useTranslation} from "react-i18next";
import i18next from "i18next";
import RefreshIcon from "@material-ui/icons/Refresh";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import {useNavigate} from "react-router";
import useSWR from "swr";

import MaterialTable from "../../libs/material-table";
import {useOkCancelConfirm} from "../../components/confirm";
import {useKey} from "../../utils/use-key";
import {UserDefinedFunction} from "../../api/nimflow-api";
import {useNimflowFunctions} from "../../api/nimflow-functions-api";

const columns: Column<UserDefinedFunction>[] = [
    {field: "name", title: i18next.t("Name")},
    {field: "description", title: i18next.t("Description")},
];

const buildRefreshAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: RefreshIcon,
        tooltip: i18next.t("RefreshData"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const buildAddAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: AddIcon,
        tooltip: i18next.t("AddUdFunction"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const adaptHandler = <T extends object>(handler: (info: T) => void): Action<T>["onClick"] => {
    return (_, info) => {
        if (Array.isArray(info)) {
            info = info[0];
        }
        handler(info);
    };
};

const buildEditAction = (handler: (info: UserDefinedFunction) => void) => {
    const action: Action<UserDefinedFunction> = {
        icon: EditIcon,
        tooltip: i18next.t("Edit"),
        onClick: adaptHandler(handler),
    };
    return action;
};

const buildDeleteAction = (handler: (info: UserDefinedFunction) => void) => {
    const action: Action<UserDefinedFunction> = {
        icon: DeleteIcon,
        tooltip: i18next.t("Delete"),
        onClick: adaptHandler(handler),
    };
    return action;
};

const useUdFunctions = () => {
    const {getAllUdFunctions} = useNimflowFunctions();
    const queryKey = useKey(getAllUdFunctions);
    const {data, error, mutate, isValidating} = useSWR(queryKey, async () => {
        const result = (await getAllUdFunctions()) as UserDefinedFunction[];
        return result;
    });
    return useMemo(() => {
        return {
            udFunctions: data ?? [],
            isValidating,
            error,
            revalidate: mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export const UdFunctionsViewer: FC<{}> = () => {
    const {t} = useTranslation();
    const confirm = useOkCancelConfirm();
    const {deleteUdFunction} = useNimflowFunctions();
    const navigate = useNavigate();
    const {udFunctions, error, revalidate, isValidating} = useUdFunctions();

    const refreshAction = useMemo(() => {
        return buildRefreshAction(revalidate);
    }, [revalidate]);

    const addAction = useMemo(() => {
        return buildAddAction(() => {
            navigate("new");
        });
    }, [navigate]);

    const rowActions = useMemo(() => {
        return [
            buildEditAction(row => {
                navigate(`edit/${row.id}`);
            }),
            buildDeleteAction(async row => {
                let result = await confirm({
                    description: t("ConfirmDeleteUdFunction", {
                        name: row.name,
                    }),
                });
                if (result) {
                    result = await deleteUdFunction(row.id);
                    revalidate();
                }
                return result;
            }),
        ];
    }, [navigate, confirm, t, deleteUdFunction, revalidate]);

    return (
        <>
            <MaterialTable
                columns={columns}
                data={udFunctions ?? []}
                title={t("UdFunctions")}
                actions={[addAction, refreshAction, ...rowActions]}
            />
            {!isValidating && !!error && <Alert severity="error">{error.toString()}</Alert>}
        </>
    );
};
