import React, {FC, useCallback, useState, useMemo, useLayoutEffect, useRef} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import clsx from "clsx";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import Button from "@material-ui/core/Button";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import InfoIcon from "@material-ui/icons/Info";
import BlockIcon from "@material-ui/icons/Block";
import ErrorIcon from "@material-ui/icons/Error";
import WarningIcon from "@material-ui/icons/Warning";
import NotificationsIcon from "@material-ui/icons/Notifications";
import TimerIcon from "@material-ui/icons/Timer";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
import CircularProgress from "@material-ui/core/CircularProgress";
import CloseIcon from "@material-ui/icons/Close";
import IconButton from "@material-ui/core/IconButton";
import Link from "@material-ui/core/Link";
import Divider from "@material-ui/core/Divider";
import {useTranslation} from "react-i18next";
import {isPresent} from "ts-is-present";
import {useRecoilValue} from "recoil";

import {ContextPanel, ContextPanelProps} from "../../components/context-panel";
import {
    useNotificationCommands,
    Notification,
    NotificationType,
    notificationsState,
} from "../../services/notification-service";
import {Result} from "../../components/result";
import {format} from "../../libs/timeago";

interface NotificationViewerProps {
    notification: Notification;
    onDismiss: (notification: Notification) => void;
}

type NotificationTypeKey = keyof typeof NotificationType;

const getNotificationTypeKey = (notification: Notification): NotificationTypeKey => {
    return NotificationType[notification.type] as NotificationTypeKey;
};

const iconMap: Record<NotificationTypeKey, React.ComponentType<any>> = {
    informational: InfoIcon,
    error: ErrorIcon,
    warning: WarningIcon,
    waiting: TimerIcon,
    inProgress: CircularProgress,
    completed: CheckCircleIcon,
    canceled: BlockIcon,
} as const;

const iconColor: Record<NotificationTypeKey, string> = {
    error: "#ff4d4f",
    informational: "#1890ff",
    warning: "#faad14",
    waiting: "#999999",
    inProgress: "#999999",
    completed: "#52c41a",
    canceled: "#ff4d4f",
};

const iconSize = 21;

const notificationVieweUseStyles = makeStyles((theme: Theme) =>
    createStyles({
        notificationIcon: {
            color: (props: NotificationViewerProps) => {
                return isPresent(props.notification.type)
                    ? iconColor[getNotificationTypeKey(props.notification)]
                    : undefined;
            },
        },
        wrapper: {
            padding: "10px 5px 10px 10px",
            display: "flex",
            position: "relative",
            width: "100%",
        },
        dismissButton: {
            opacity: 0.4,
            position: "absolute",
            top: -15,
            right: -15,
        },
        notification: {
            width: "100%",
        },
        notificationTitle: {
            display: "flex",
            paddingBottom: 2,
            maxWidth: 450,
        },
        notificationTitleIcon: {
            height: iconSize,
            width: iconSize,
            marginRight: 10,
            minWidth: iconSize,
        },
        notificationDescription: {
            display: "block",
            maxHeight: 144,
            overflow: "hidden",
            textOverflow: "ellipsis",
            position: "relative",
            paddingTop: 5,
            whiteSpace: "pre-wrap",
            width: "100%",
        },
        notificationDescriptionExpanded: {
            maxHeight: "100%",
            overflowY: "auto",
        },
        notificationDescriptionExpansionIcon: {
            verticalAlign: "bottom",
        },
        notificationUserTimeInfo: {
            paddingTop: 10,
            textDecoration: "#7a7a7a",
            display: "flex",
            justifyContent: "right",
        },
    })
);

const NotificationViewer: FC<NotificationViewerProps> = props => {
    const {notification, onDismiss} = props;
    const descriptionRef = useRef<HTMLDivElement>(null);
    const [seeMoreVisible, setSeeMoreVisible] = useState(false);
    const [seeMoreActive, setSeeMoreActive] = useState(false);
    const {t} = useTranslation();
    const classes = notificationVieweUseStyles(props);
    const iconComponent = iconMap[getNotificationTypeKey(notification)];
    const iconNode = React.createElement(iconComponent, {
        className: classes.notificationIcon,
        style: {
            width: iconSize,
            height: iconSize,
        },
    });
    const timeago = format(notification.moment);

    useLayoutEffect(() => {
        const div = descriptionRef.current;
        if (div && div.scrollHeight > div.clientHeight) {
            setSeeMoreVisible(true);
        }
    }, []);
    const handleDismiss = useCallback(() => {
        onDismiss(notification);
    }, [onDismiss, notification]);
    const handleToggleSeeMore = useCallback((event: React.MouseEvent<any>) => {
        event.preventDefault();
        setSeeMoreActive(s => !s);
    }, []);

    return (
        <>
            <ListItem>
                <div className={classes.wrapper}>
                    <div className={classes.notification}>
                        <div className={classes.notificationTitle}>
                            <div className={classes.notificationTitleIcon}>{iconNode}</div>
                            {notification.title}
                        </div>
                        <div
                            ref={descriptionRef}
                            className={clsx(classes.notificationDescription, {
                                [classes.notificationDescriptionExpanded]: seeMoreActive,
                            })}
                        >
                            {notification.description}
                        </div>
                        {seeMoreVisible && (
                            <Link href="#" onClick={handleToggleSeeMore} color="textSecondary">
                                {t(seeMoreActive ? "SeeLess" : "SeeMore")}
                                {seeMoreActive ? (
                                    <ExpandLessIcon className={classes.notificationDescriptionExpansionIcon} />
                                ) : (
                                    <ExpandMoreIcon className={classes.notificationDescriptionExpansionIcon} />
                                )}
                            </Link>
                        )}
                        <div className={classes.notificationUserTimeInfo}>{timeago}</div>
                    </div>
                    <IconButton className={classes.dismissButton} onClick={handleDismiss}>
                        <CloseIcon fontSize="small" />
                    </IconButton>
                </div>
                {/* <ListItemIcon>{iconNode}</ListItemIcon>
                <ListItemText primary={notification.title} secondary={notification.description} />
                <ListItemSecondaryAction>
                    <IconButton edge="end" onClick={handleDismiss}>
                        <CloseIcon />
                    </IconButton>
                </ListItemSecondaryAction> */}
            </ListItem>
            <Divider />
        </>
    );
};

export interface NotificationsViewerProps {
    open: boolean;
    onClose?: () => void;
}

const notificationsVieweUseStyles = makeStyles((theme: Theme) =>
    createStyles({
        emptyIcon: {
            fontSize: 72,
            color: "lightgray",
        },
    })
);

export const NotificationsViewer: FC<NotificationsViewerProps> = props => {
    const {open, onClose} = props;
    const {t} = useTranslation();
    const classes = notificationsVieweUseStyles();
    const notifications = useRecoilValue(notificationsState);
    const {resetNewNotifications, dismissNotification, dismissAll} = useNotificationCommands();
    const contentContainerProps: ContextPanelProps["contentContainerProps"] = useMemo(
        () => ({
            style: {
                width: 525,
            },
        }),
        []
    );

    resetNewNotifications();
    return (
        <ContextPanel
            header={t("Notifications")}
            open={open}
            onClose={onClose}
            contentContainerProps={contentContainerProps}
        >
            {notifications.length === 0 && (
                <Result icon={<NotificationsIcon className={classes.emptyIcon} />} title={t("NoNewNotifications")} />
            )}
            {notifications.length > 0 && (
                <>
                    <div style={{display: "flex", justifyContent: "end"}}>
                        <Button onClick={() => dismissAll()}>{t("DismissAll")}</Button>
                    </div>
                    <List>
                        <Divider />
                        {notifications.map((s, i) => {
                            return (
                                <NotificationViewer key={i} notification={s} onDismiss={() => dismissNotification(s)} />
                            );
                        })}
                    </List>
                </>
            )}
        </ContextPanel>
    );
};
