import React, {FC, useState, useMemo, useCallback} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import Button from "@material-ui/core/Button";
import Alert from "@material-ui/lab/Alert";
import IconButton from "@material-ui/core/IconButton";
import CheckIcon from "@material-ui/icons/Check";
import {useTranslation} from "react-i18next";
import useSWR from "swr";

import {SearchField} from "../../components/search-field";
import {useKey} from "../../utils/use-key";
import {useNimflowDashboardWidgets, DashboardWidgetDefinitionInfo} from "../../api/nimflow-dashboard-widgets-api";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        list: {
            flexGrow: 1,
            maxHeight: "400",
            overflowY: "auto",
        },
        paper: {
            backgroundColor: theme.palette.background.default,
        },
    })
);

const filterData = (data?: DashboardWidgetDefinitionInfo[], search?: string): DashboardWidgetDefinitionInfo[] => {
    if (!data) {
        return [];
    }
    if (!search) {
        return data;
    }
    const lcsearch = search.toLowerCase();
    return data.filter(
        s => s.name.toLowerCase().includes(lcsearch) || (s.title && s.title.includes(lcsearch))
    );
};

export interface DashboardWidgetSelectorProps {
    open: boolean;
    onSelected: (widgetId: string) => void;
    onClose: () => void;
}

const useWidgetInfos = (filter: string) => {
    const {dashboardWidgets_GetInfos} = useNimflowDashboardWidgets();
    const queryKey = useKey(dashboardWidgets_GetInfos);
    const {data, isValidating, mutate, error} = useSWR(queryKey, async () => {
        const result = await dashboardWidgets_GetInfos({});
        return result;
    }, {revalidateOnFocus: false});
    return useMemo(() => {
        return {
            data: filterData(data, filter),
            isValidating,
            error,
            revalidate: mutate,
        };
    }, [data, filter, isValidating, error, mutate]);
};

export const DashboardWidgetSelector: FC<DashboardWidgetSelectorProps> = props => {
    const {open, onSelected, onClose} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const [filter, setFilter] = useState("");
    const {data, error, isValidating} = useWidgetInfos(filter);

    const handleWidgetSelected = useCallback(
        (id: string) => {
            onSelected?.(id);
            onClose();
        },
        [onClose, onSelected]
    );

    const handleDialogClose = useCallback(async (event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [onClose]);

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            fullWidth
            maxWidth="sm"
            classes={{paper: classes.paper}}
        >
            <DialogTitle>{t("SelectingWidget")}</DialogTitle>
            <DialogContent style={{minWidth: "400px"}}>
                <div style={{textAlign: "right"}}>
                    <SearchField value={filter} onChange={setFilter} />
                </div>
                <List className={classes.list}>
                    {data.map(s => (
                        <ListItem key={s.name}>
                            {/* <ListItemIcon>
                                <ContextTypeIcon />
                            </ListItemIcon> */}
                            <ListItemText primary={s.name} secondary={s.title} />
                            <ListItemSecondaryAction>
                                <IconButton edge="end" onClick={() => s.id && handleWidgetSelected(s.id)}>
                                    <CheckIcon />
                                </IconButton>
                            </ListItemSecondaryAction>
                        </ListItem>
                    ))}
                </List>
                {!isValidating && !!error && <Alert severity="error">{error.toString()}</Alert>}
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
