import {useState, useCallback, useMemo} from "react";
import {useTranslation} from "react-i18next";
import {v4 as uuid} from "uuid";
import useSWR from "swr";
import {sortBy} from "ramda";

import {Localization, useNimflowLocalization} from "../../api/nimflow-localization-api";
import {useKey} from "../../utils/use-key";

export interface TranslationEntry {
    id?: string;
    key: string;
    [lang: string]: string | undefined;
}

const buildData = (localizations: Localization[]): TranslationEntry[] => {
    const map: Record<string, TranslationEntry> = {};
    for (const loc of localizations) {
        const lang = loc.language;
        for (const [key, value] of Object.entries(loc.translations)) {
            const entry = map[key] ?? (map[key] = {key});
            entry.id = uuid();
            entry[lang] = value;
        }
    }
    const keys = sortBy(s => s, Object.keys(map));
    return keys.map(s => map[s]);
};

export const useTranslationEditionModel = () => {
    const {t} = useTranslation();
    const [localizations, setLocalizations] = useState<Localization[]>([]);
    const {localizations_GetAll, localizations_Add, localizations_Remove, localizations_UpdateTranslation} = useNimflowLocalization();
    const queryKey = useKey(localizations_GetAll, t);
    const {data, error, isValidating, mutate} = useSWR(
        [queryKey],
        async (_key: string) => {
            const result = await localizations_GetAll();
            setLocalizations(result);
            return buildData(result);
        },
        {revalidateOnFocus: false}
    );

    const languages = useMemo(() => {
        return sortBy(
            s => s,
            localizations.map(s => s.language)
        );
    }, [localizations]);

    const addLanguage = useCallback(
        async (language: string) => {
            const localization: Localization = {
                language,
                translations: {},
            };
            localization.id = await localizations_Add(localization);
            setLocalizations([...localizations, localization]);
        },
        [localizations, localizations_Add]
    );

    const removeLanguage = useCallback(
        async (language: string) => {
            const localization = localizations.find(s => s.language === language);
            if (localization?.id) {
                await localizations_Remove(localization.id);
                mutate();
            }
        },
        [localizations, localizations_Remove, mutate]
    );

    const addTranslation = useCallback(
        async (newEntry: TranslationEntry) => {
            const key = newEntry.key;
            const promises: Promise<any>[] = [];
            for (const lang of languages) {
                if (newEntry[lang]) {
                    promises.push(localizations_UpdateTranslation(lang, key, newEntry[lang]));
                }
            }
            if (data) {
                newEntry.id = uuid();
                const updatedData = [...data];
                const idx = updatedData.findIndex(s => s.key > newEntry.key);
                if (idx === -1) {
                    updatedData.push(newEntry);
                } else {
                    updatedData.splice(idx, 0, newEntry);
                }
                mutate(updatedData, false);
            }
            if (promises.length > 0) {
                await Promise.all(promises);
            }
        },
        [data, languages, localizations_UpdateTranslation, mutate]
    );

    const updateTranslation = useCallback(
        async (newEntry: TranslationEntry, oldEntry?: TranslationEntry) => {
            const key = oldEntry?.key ?? newEntry.key;
            const promises: Promise<any>[] = [];
            for (const lang of languages) {
                if (newEntry[lang] !== oldEntry?.[lang]) {
                    promises.push(localizations_UpdateTranslation(lang, key, newEntry[lang]));
                }
            }
            const entry = data?.find(s => s.id === oldEntry?.id);
            if (entry) {
                for (const lang of languages) {
                    const newValue = newEntry[lang];
                    if (!!newValue) {
                        entry[lang] = newValue;
                    } else {
                        delete entry[lang];
                    }
                }
            }
            if (promises.length > 0) {
                await Promise.all(promises);
            }
        },
        [data, languages, localizations_UpdateTranslation]
    );

    const removeTranslation = useCallback(
        async (oldEntry: TranslationEntry) => {
            const key = oldEntry.key;
            const promises: Promise<any>[] = [];
            for (const lang of languages) {
                promises.push(localizations_UpdateTranslation(lang, key, undefined));
            }
            if (data) {
                const updatedData = data.filter(s => s.id !== oldEntry.id);
                mutate(updatedData, false);
            }
            if (promises.length > 0) {
                await Promise.all(promises);
            }
        },
        [data, languages, localizations_UpdateTranslation, mutate]
    );

    return {
        languages,
        data,
        isLoading: isValidating,
        error,
        refresh: mutate,
        addLanguage,
        removeLanguage,
        addTranslation,
        updateTranslation,
        removeTranslation,
    };
};
