import React, {useMemo, useCallback, useState, useRef, useEffect} from "react";
import {Column, Components, MTableEditRow, MTableBodyRow, Action, Options, EditComponentProps} from "@material-table/core";
import {mergeDeepRight} from "ramda";

import MaterialTable, {MaterialTableProps} from "../libs/material-table";
import {ErrorInfo} from "../models/error-info";

interface BaseTableService<RowData extends object> {
    editOnClick: boolean;
    data?: RowData[];
}

const BaseTableContext = React.createContext<BaseTableService<any> | null>(null);

const useBaseTableContext = <RowData extends object>(): BaseTableService<RowData> =>
    React.useContext(BaseTableContext) as BaseTableService<RowData>;

interface BaseTableRowEditionService {
    errors?: Record<string, string>;
}

const BaseTableRowEditionContext = React.createContext<BaseTableRowEditionService | null>(null);

const useBaseTableRowEditionContext = (): BaseTableRowEditionService =>
    React.useContext(BaseTableRowEditionContext) as BaseTableRowEditionService;

export interface BaseEditComponentProps<RowData extends object> extends EditComponentProps<RowData> {
    errorMessage?: string;
}

export interface BaseEditComponentWrapperProps<RowData extends object> extends BaseEditComponentProps<RowData> {
    originalEditComponent: (props: BaseEditComponentProps<RowData>) => React.ReactElement<any>;
}

export const BaseEditComponentWrapper = <RowData extends object>(props: BaseEditComponentWrapperProps<RowData>) => {
    const {originalEditComponent: EditComponent, columnDef, errorMessage, ...rest} = props;
    const {errors} = useBaseTableRowEditionContext();
    let error = errorMessage;
    if (columnDef?.field) {
        error = errors?.[columnDef.field];
    }
    return <EditComponent {...rest} columnDef={columnDef} errorMessage={error} />;
};

export interface BaseColumn<RowData extends object> extends Column<RowData> {
    validateValue?: (value: any, oldValue: any, rowData: RowData, data: RowData[]) => Promise<ErrorInfo[]>;
    editComponent?: (props: BaseEditComponentProps<RowData>) => React.ReactElement<any>;
}

export interface BaseRowProps<RowData extends object> {
    actions?: (Action<RowData> | ((rowData: RowData) => Action<RowData>))[];
    onRowClick?: (
        event?: React.MouseEvent,
        rowData?: RowData,
        toggleDetailPanel?: (panelIndex?: number) => void
    ) => boolean | undefined;
}

export const BaseRow = <RowData extends object>(props: BaseRowProps<RowData>) => {
    const {editOnClick} = useBaseTableContext();
    const {onRowClick, actions, ...rest} = props;
    const handleRowClick = useCallback(
        (event: React.MouseEvent, rowData: RowData, togglePanelIndex: (index?: number) => void) => {
            const result = onRowClick?.(event, rowData, togglePanelIndex);
            if (result !== false && actions && editOnClick) {
                for (let actOrFn of actions) {
                    if (typeof actOrFn === "function") {
                        actOrFn = actOrFn(rowData);
                    }
                    if (!actOrFn.position) {
                        actOrFn.onClick(event, rowData);
                        break;
                    }
                }
            }
        },
        [onRowClick, actions, editOnClick]
    );

    return <MTableBodyRow actions={actions} onRowClick={handleRowClick} {...rest} />;
};

export interface BaseEditRowProps<RowData extends object> {
    columns: BaseColumn<RowData>[];
    data: RowData;
    onEditingApproved: (mode: string, newData: RowData, oldData: RowData) => void;
}

export const BaseEditRow = <RowData extends object>(props: BaseEditRowProps<RowData>) => {
    const {data, onEditingApproved, columns, ...rest} = props;
    const [editionData, setEditionData] = useState<RowData>(data);
    const [editionContext, setEditionContext] = useState<BaseTableRowEditionService>({});
    const {data: fullData} = useBaseTableContext<RowData>();
    const handleEditingApproved = useCallback(
        async (mode: string, newData: RowData, oldData: RowData) => {
            const cols = columns.filter(
                s => !s.hidden && !((s as any).tableData.groupOrder > -1) && s.validateValue && s.field
            );
            const errors: Record<string, string> = {};
            await Promise.all(
                cols.map(async col => {
                    const fld = col.field as string;
                    const e = await col.validateValue!(
                        (newData as any)[fld],
                        data ? (data as any)[fld] : undefined,
                        newData,
                        fullData ?? []
                    );
                    const msg = e?.[0];
                    if (msg) {
                        errors[fld] = msg.message;
                    }
                })
            );
            if (Object.keys(errors).length === 0) {
                setEditionContext({});
                onEditingApproved(mode, newData, data);
            } else {
                setEditionContext({errors});
                if (oldData) {
                    (newData as any).tableData = (oldData as any).tableData;
                }
                setEditionData(newData);
            }
        },
        [onEditingApproved, columns, data, fullData]
    );
    return (
        <BaseTableRowEditionContext.Provider value={editionContext}>
            <MTableEditRow columns={columns} data={editionData} onEditingApproved={handleEditingApproved} {...rest} />
        </BaseTableRowEditionContext.Provider>
    );
};

export interface BaseTableOptions<RowData extends object> extends Options<RowData> {
    editOnClick?: boolean;
}

export interface BaseTableProps<RowData extends object> extends MaterialTableProps<RowData> {
    columns: BaseColumn<RowData>[];
    data: RowData[];
    onRowClick?: (
        event?: React.MouseEvent,
        rowData?: RowData,
        toggleDetailPanel?: (panelIndex?: number) => void
    ) => boolean | undefined;
    options?: BaseTableOptions<RowData>;
    onEditionStateChanged?: (editing: boolean) => void;
}

const defaultOptions: BaseTableProps<any>["options"] = {
    paging: false,
    draggable: false,
};

export const BaseTable = <RowData extends object>(props: BaseTableProps<RowData>) => {
    const {components, columns, data, options, editable, onEditionStateChanged, ...rest} = props;
    const tableRef = useRef<any>();
    const isEditingRef = useRef(false);
    const comps = useMemo(() => {
        const comps: Components = {
            EditRow: BaseEditRow,
            Row: BaseRow,
            ...components,
        };
        return comps;
    }, [components]);

    const opts = useMemo(() => {
        const opts = mergeDeepRight(defaultOptions, options ?? {}) as BaseTableOptions<RowData>;
        if (!editable?.onRowUpdate) {
            opts.editOnClick = false;
        }
        return opts;
    }, [options, editable]);

    const context: BaseTableService<RowData> = {
        editOnClick: props.options?.editOnClick ?? false,
        data,
    };

    const cols = useMemo(() => {
        return columns.map(s => {
            if (!s.editComponent) {
                return s;
            }
            const result: BaseColumn<RowData> = {
                ...s,
                editComponent: props => {
                    return <BaseEditComponentWrapper {...props} originalEditComponent={s.editComponent!} />;
                },
            };
            return result;
        });
    }, [columns]);

    useEffect(() => {
        const table = tableRef.current;
        if (!table || !onEditionStateChanged) {
            return;
        }

        const prevComponentDidUpdate: Function = table.componentDidUpdate;

        function componentDidUpdate(this: any, prevProps: any) {
            const isEditing = this.state.showAddRow || !!this.dataManager.lastEditingRow;
            if (isEditing !== isEditingRef.current) {
                isEditingRef.current = isEditing;
                onEditionStateChanged?.(isEditing);
            }
            prevComponentDidUpdate.call(this, prevProps);
        }
        table.componentDidUpdate = componentDidUpdate;

        return () => {
            table.componentDidUpdate = prevComponentDidUpdate;
        };
    }, [onEditionStateChanged]);

    return (
        <BaseTableContext.Provider value={context}>
            <MaterialTable
                columns={cols}
                components={comps}
                data={data}
                options={opts}
                editable={editable}
                {...rest}
                tableRef={props.tableRef ?? tableRef}
            />
        </BaseTableContext.Provider>
    );
};
