import {clone} from "ramda";

import {mapAjvErrors, ImportResult, importFailure, importSuccess} from "./importers";
import {ContextTypeSchema, jsCommonScriptKey, jsonSchemaScriptKey} from "./context-type-schema";
import {isValidationFailure, validateContextTypeSchema} from "../libs/ajv";
import {validateWithYupSchema} from "../libs/yup";
import {ContextType} from "../api/nimflow-api";

export const importContextTypeFromJson = async (json: any): Promise<ImportResult<ContextType>> => {
    // Legacy 
    delete json.jsInitializers;

    const result = validateContextTypeSchema(json);
    if (isValidationFailure(result)) {
        return importFailure(mapAjvErrors(result.errors));
    }
    const contextType = clone(json) as ContextType;
    delete (contextType as any)["$schema"];
    if (typeof contextType.jsonSchema === "object") {
        contextType.jsonSchema = JSON.stringify(contextType.jsonSchema, undefined, 2);
    }
    if (typeof contextType.settings?.uiSchemaCommon === "object") {
        contextType.settings!.uiSchemaCommon = JSON.stringify(contextType.settings!.uiSchemaCommon, undefined, 2);
    }
    for(const action of contextType.actions) {
        const settings = action.settings;
        const uiSchema = settings?.uiSchema;
        if (typeof uiSchema === "object") {
            settings!.uiSchema = JSON.stringify(uiSchema, undefined, 2);
        }
    }
    for(const response of contextType.tasks.flatMap(s => s.validResponses)) {
        const settings = response.settings;
        const uiSchema = settings?.uiSchema;
        if (typeof uiSchema === "object") {
            settings!.uiSchema = JSON.stringify(uiSchema, undefined, 2);
        }
    }
    const einfos = await validateWithYupSchema(ContextTypeSchema, contextType, {
        context: {
            [jsCommonScriptKey]: contextType.jsCommon,
            [jsonSchemaScriptKey]: contextType.jsonSchema,
        },
    })
    if (einfos.length > 0) {
        return importFailure(einfos);
    }
    return importSuccess(contextType);
};
