import {isPlainObject} from "./types";
import $RefParser from "@apidevtools/json-schema-ref-parser";
import {isPresent} from "ts-is-present";
import merger from "json-schema-merge-allof";
import {equals} from "ramda";

import { ModuleFunctionInfo } from "../api/nimflow-functions-api";

export class UnionType {
    constructor(public types: JsonType[]) {}

    public isAnyType() {
        const requiredTypes: JsonType[] = ["number", "string", "boolean", "null"];
        if (!requiredTypes.some(s => this.types.includes(s))) {
            return false;
        }
        const rec = this.types.find(s => s instanceof RecordType) as RecordType | undefined;
        if (!rec || rec.valueType !== "any") {
            return false;
        }
        const array = this.types.find(s => Array.isArray(s)) as JsonType[] | undefined;
        if (!array || array.length !== 1 || array[0] !== "any") {
            return false;
        }
        return true;
    }
}

export class IntersectionType {
    constructor(public types: JsonType[]) {}
}

export class RecordType {
    constructor(public valueType: JsonType, public declaredEntries?: {[key: string]: JsonType}) {}
}

export class LiteralType {
    constructor(public value: number | string | boolean | null) {}
}

export class TupleType {
    constructor(public types: JsonType[], public additional?: JsonType) {}
}

export type JsonType =
    | "any"
    | "number"
    | "string"
    | "boolean"
    | "null"
    | JsonType[]
    | UnionType
    | IntersectionType
    | LiteralType
    | RecordType
    | TupleType
    | {[key: string]: JsonType};

    
type RemoveArrays<T> = T extends any[] ? never : T;

type JsonTypeName = NonNullable<RemoveArrays<$RefParser.JSONSchema["type"]>>;

const processRecordSchema = (schema: $RefParser.JSONSchema): RecordType => {
    const valueSchema = schema.additionalProperties;
    if (!isPresent(valueSchema) || valueSchema === true) {
        return new RecordType("any");
    }
    if (!valueSchema) {
        return new RecordType("null");
    }
    const valueType = processSchema(valueSchema);
    return new RecordType(valueType);
};

const processObjectSchema = (schema: $RefParser.JSONSchema): JsonType => {
    if (!schema.properties) {
        return processRecordSchema(schema);
    }
    const result: {[key: string]: JsonType} = {};
    for (const [key, valueSchema] of Object.entries(schema.properties)) {
        result[key] = processSchema(valueSchema);
    }
    const required = !!schema.required ? schema.required : [];
    for (const key of Object.keys(schema.properties)) {
        if (!required.includes(key)) {
            let type = result[key];
            if (type instanceof UnionType) {
                if (!type.types.includes("null")) {
                    type.types.push("null");
                }
            } else if (type !== "null") {
                result[key] = new UnionType([type, "null"]);
            }
        }
    }
    if (schema.additionalProperties) {
        const rectype = processRecordSchema(schema);
        rectype.declaredEntries = result;
        return rectype;
    }
    return result;
};

const processTupleSchema = (schema: $RefParser.JSONSchema): JsonType => {
    const itemSchema = schema.items;
    if (!Array.isArray(itemSchema)) {
        throw new Error();
    }
    const types = itemSchema.map(s => (typeof s === "boolean" ? "any" : processSchema(s)));
    const tuple = new TupleType(types);
    const additional = schema.additionalItems;
    if (additional) {
        tuple.additional = typeof additional === "boolean" ? "any" : processSchema(additional);
    }
    return tuple;
};

const processSingleType = (type: JsonTypeName, schema: $RefParser.JSONSchema): JsonType => {
    switch (type) {
        case "array":
            const itemSchema = schema.items;
            if (Array.isArray(itemSchema)) {
                return processTupleSchema(schema);
            }
            if (!isPresent(itemSchema) || itemSchema === true) {
                return ["any"];
            }
            if (!itemSchema) {
                return "null";
            }
            return [processSchema(itemSchema)];
        case "object":
            return processObjectSchema(schema);
        case "integer":
            return "number";
        default:
            return type;
    }
};

const hasLiteralType = (value: any) => {
    const validLiteralTypes = ["string", "number", "boolean"];
    return validLiteralTypes.includes(typeof value) || value === null;
};

const processSimpleSchema = (schema: $RefParser.JSONSchema): JsonType | undefined => {
    if (schema.const) {
        if (!hasLiteralType(schema.const)) {
            return "any";
        }
        return new LiteralType(schema.const);
    }
    if (schema.enum) {
        if (schema.enum.every(hasLiteralType)) {
            return new UnionType(schema.enum.map(s => new LiteralType(s as any)));
        }
        return "any";
    }
    const type = schema.type;
    if (Array.isArray(type)) {
        const types: JsonType[] = [];
        for (const t of type) {
            const resType = processSingleType(t, schema);
            const newTypes = resType instanceof UnionType ? resType.types : [resType];
            for (const newType of newTypes) {
                if (types.every(s => !equals(s, newType))) {
                    types.push(newType);
                }
            }
        }
        return new UnionType(types);
    } else if (type) {
        return processSingleType(type, schema);
    } else if (schema.properties || schema.additionalProperties) {
        return processSingleType("object", schema);
    } else if (schema.items) {
        return processSingleType("array", schema);
    }
    return undefined;
};

const processSchema = (schema: $RefParser.JSONSchema): JsonType => {
    let commonType = processSimpleSchema(schema);
    if (Array.isArray(schema.allOf) || Array.isArray(schema.anyOf)) {
        const unionType = new UnionType([]);
        if (Array.isArray(schema.allOf)) {
            for (const item of schema.allOf) {
                if (typeof item !== "boolean") {
                    unionType.types.push(processSchema(item));
                }
            }
        }
        if (Array.isArray(schema.anyOf)) {
            for (const item of schema.anyOf) {
                if (typeof item !== "boolean") {
                    unionType.types.push(processSchema(item));
                }
            }
        }
        commonType = commonType ? new IntersectionType([commonType, unionType]) : unionType;
    }
    return commonType ?? "any";
};

const hasSimpleSyntax = (type: JsonType) => {
    if (typeof type === "string") {
        return true;
    }
    if (Array.isArray(type) && type.length > 0 && hasSimpleSyntax(type[0])) {
        return true;
    }
    return false;
};

const jsonTypeToTypescriptTyping = (type: JsonType, level: number = 0): string => {
    function AddObjectFields(typing: string, objType: {[key: string]: JsonType}): string {
        const spaces = Array(level * 4)
            .fill(" ")
            .join("");
        for (const [field, fieldType] of Object.entries(type)) {
            typing += `\n${spaces}    ${field}`;
            let ft = fieldType;
            if (ft instanceof UnionType && ft.types.includes("null")) {
                const idx = ft.types.indexOf("null");
                if (idx >= 0) {
                    typing += "?";
                    ft.types.splice(idx, 1);
                    if (ft.types.length === 1) {
                        ft = ft.types[0];
                    }
                }
            }
            typing += ": " + jsonTypeToTypescriptTyping(ft, level + 1) + ";";
        }
        return typing;
    }

    if (Array.isArray(type)) {
        const item = type[0];
        let itemType = item ? jsonTypeToTypescriptTyping(item, level + 1) : "any";
        if (item instanceof UnionType) {
            itemType = `(${itemType})`;
        }
        return itemType + "[]";
    } else if (type instanceof TupleType) {
        let result = "[";
        const add = type.additional;
        if (type.types.every(hasSimpleSyntax) && (!add || hasSimpleSyntax(add))) {
            result += type.types.map(s => jsonTypeToTypescriptTyping(s, level + 1)).join(", ");
            if (add) {
                result += `, ...${jsonTypeToTypescriptTyping(add, level + 1)}[]`;
            }
        } else {
            const spaces = Array(level * 4)
                .fill(" ")
                .join("");
            for (const subtype of type.types) {
                result += `\n${spaces}    ${jsonTypeToTypescriptTyping(subtype, level + 1)},`;
            }
            if (add) {
                result += `\n${spaces}    ...${jsonTypeToTypescriptTyping(add, level + 1)}[]`;
            }
            result += `\n${spaces}`;
        }
        return result + "]";
    } else if (type instanceof IntersectionType) {
        if (type.types.every(hasSimpleSyntax)) {
            return type.types.map(s => jsonTypeToTypescriptTyping(s, level + 1)).join(" & ");
        } else {
            const spaces = Array((level + 1) * 4)
                .fill(" ")
                .join("");
            let result =
                "\n" + type.types.map(s => `(${spaces}${jsonTypeToTypescriptTyping(s, level + 1)})`).join(" &\n");
            return result;
        }
    } else if (type instanceof UnionType) {
        if (type.isAnyType()) {
            return "any";
        }
        if (type.types.every(hasSimpleSyntax)) {
            return type.types.map(s => jsonTypeToTypescriptTyping(s, level + 1)).join(" | ");
        } else {
            const spaces = Array((level + 1) * 4)
                .fill(" ")
                .join("");
            let result =
                "\n" + type.types.map(s => `${spaces}${jsonTypeToTypescriptTyping(s, level + 1)}`).join(" |\n");
            return result;
        }
    } else if (type instanceof LiteralType) {
        switch (typeof type.value) {
            case "boolean":
                return type.value.toString();
            case "number":
                return type.value.toString();
            case "string":
                return `"${type.value}"`;
            default:
                return "null";
        }
    } else if (type instanceof RecordType) {
        const valueType = jsonTypeToTypescriptTyping(type.valueType, level + 1);
        if (!type.declaredEntries) {
            return `{[key: string]: ${valueType};}`;
        }
        let result = "{";
        result = AddObjectFields(result, type.declaredEntries);
        const spaces = Array(level * 4)
            .fill(" ")
            .join("");
        result += `\n${spaces}    [key: string]: ${valueType};`;
        return result + `\n${spaces}}`;
    } else if (typeof type === "object") {
        let result = "{";
        result = AddObjectFields(result, type);
        const spaces = Array(level * 4)
            .fill(" ")
            .join("");
        return result + `\n${spaces}}`;
    } else if (type === "null") {
        return "(null | undefined)";
    } else {
        return type;
    }
};

const hasCircularity = (schema: any, visited: any[] = []): boolean => {
    if (visited.includes(schema)) {
        return true;
    }
    visited.push(schema);
    try {
        if (Array.isArray(schema)) {
            return schema.some(s => hasCircularity(s));
        } else if (isPlainObject(schema)) {
            return Object.values(schema).some(s => hasCircularity(s));
        }
    } finally {
        visited.pop();
    }
    return false;
};

export const getTypingFromSchema = async (jsonSchemaStr: string, jsonTypePostProcessor?: (jsonType: JsonType) => JsonType) => {
    let typing: string;
    try {
        const jsonSchema = JSON.parse(jsonSchemaStr);
        const dereferencedSchema = await $RefParser.dereference(jsonSchema);
        if (hasCircularity(dereferencedSchema)) {
            console.log("Schema contains circularity and cannot be represented in a pure structural type", jsonSchema);
            return "any"; //Cannot represent types with circularity;
        }
        const mergedSchema = merger(dereferencedSchema, {
            ignoreAdditionalProperties: true,
        });
        const jsonType = processSchema(mergedSchema);
        const postProcessedJsonType = jsonTypePostProcessor?.(jsonType) ?? jsonType;
        typing = jsonTypeToTypescriptTyping(postProcessedJsonType);
    } catch (e) {
        console.log("Error converting schema to structural type: " + e, jsonSchemaStr);
        typing = "any";
    }
    return typing;
};

//Helpers for debugging jsontype processing output
const getFunctionInfoDeclaration = async (finfo: ModuleFunctionInfo) => {
    let result = "";
    const name = `${finfo.moduleName}_${finfo.name}`;
    const input = finfo.inputSchema ? "input: " + (await getTypingFromSchema(finfo.inputSchema)) : "";
    const output = finfo.outputSchema ? await getTypingFromSchema(finfo.outputSchema) : "any";
    result += `const ${name}: (${input}) => (${output}) = () => throw new Error();`;
    result += "\n\n";
    return result;
};

export const getFunctionInfosDeclarations = async (finfos: ModuleFunctionInfo[]) => {
    let result = "";
    for (const finfo of finfos) {
        // if (finfo.name === "EvaluateScript") {
        //     result += finfo.inputSchema;
        //     result += "\n\n";
        //     result += finfo.outputSchema;
        //     result += "\n\n";
        //     result += await getDecl(finfo);
        // }
        result += await getFunctionInfoDeclaration(finfo);
    }
    return result;
};
