import React, {FC, useCallback, useMemo, useRef, useEffect, useState} from "react";
import {makeStyles, createStyles, Theme, useTheme} from "@material-ui/core/styles";
import * as yup from "yup";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Tooltip from "@material-ui/core/Tooltip";
import Tabs, {TabsProps} from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import IconButton from "@material-ui/core/IconButton";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import SaveIcon from "@material-ui/icons/Save";
import SaveAltIcon from "@material-ui/icons/SaveAlt";
import AssignmentTurnedInIcon from "@material-ui/icons/AssignmentTurnedIn";
import VisibilityIcon from "@material-ui/icons/Visibility";
import VisibilityOffIcon from "@material-ui/icons/VisibilityOff";
import {useTranslation} from "react-i18next";
import {useSnackbar} from "notistack";
import {useNavigate} from "react-router-dom";
import {useThrottleCallback} from "@react-hook/throttle";
import {useFormikContext, FormikProps, FormikHelpers, Formik, Form} from "formik";
import {equals} from "ramda";
import Splitter from "m-react-splitters";

import {
    DashboardWidgetDefOrJsSpec,
    DashboardWidgetDefinition,
    isDashboardWidgetJsSpec,
    DashboardWidgetJsSpec,
    useNimflowDashboardWidgets,
} from "../../api/nimflow-dashboard-widgets-api";
import {resolveDashboardWidgetDefOrJsSpecSchema} from "./widget-definition-schema";
import {ErrorBoundary} from "../../components/error-boundary";
import {EditionScopeProvider, useEditionScope} from "../../components/edition-scope";
import {useConfirm} from "../../components/confirm";
import {ErrorViewer} from "../../components/error-viewer";
import {validateAndSubmit, validateForm} from "../../utils/formikHelpers";
import {WidgetDefinitionSaveAsDialog} from "./widget-definition-save-as-dialog";
import {useReportError} from "../../utils/use-report-error";
import {useExitWithoutSavingBlocker} from "../../utils/use-exit-without-saving-blocker";
import {downloadJson} from "../../utils/io-helpers";
import {exportWidgetDefinitionToJson} from "./widget-definition-json-exporter";
import {validateWithYupSchema} from "../../libs/yup";
import {JsContextDefinitionsContextProvider} from "../../components/js-context-definitions-provider";
import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {FormikErrorViewer} from "../../components/formik-error-viewer";
import {WidgetDefinitionParametersEditor} from "./widget-definition-parameters-editor";
import {WidgetDefinitionDataSourcesEditor} from "./widget-definition-data-sources-editor";
import {WidgetDefinitionVisualizationEditor} from "./widget-definition-visualization-editor";
import {WidgetJsSpecScriptEditor} from "./widget-js-spec-script-editor";
import {useNimflow} from "../../api/nimflow-api-provider";
import {useWidgetDefinitionTypingsProvider, useWidgetJsSpecTypingsProvider} from "./widget-definition-typings-provider";
import {WidgetDefinitionPreviewer} from "./widget-definition-previewer";
import {useFunctionInfos} from "../../components/formik-components/formik-function-select";

interface StyleProps {
    showPreview?: boolean;
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            flexGrow: 1,
        },
        toolbar: {
            minHeight: "48px",
            paddingLeft: 0,
        },
        paper: {
            padding: theme.spacing(2),
            color: theme.palette.text.secondary,
        },
        grow: {
            flexGrow: 1,
        },
        actionToolbarButtons: {
            display: "flex",
        },
        fileInput: {
            opacity: 0,
            position: "absolute",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
        },
        textField: {
            width: "40ch",
        },
        form: {
            height: "100%",
        },
        button: {
            margin: theme.spacing(1),
        },
        diagram: {
            marginTop: 20,
            marginBottom: 20,
            height: "calc(100% - 40px)",
        },
        table: {
            display: "table",
            width: "100%",
            height: "100%",
        },
        tableRow: {
            display: "table-row",
        },
        diagramTableRow: {
            display: "table-row",
            height: "100%",
        },
        saveAsEllipsis: {
            position: "absolute",
            left: 17,
            bottom: 4,
            fontWeight: "bold",
        },
        hide: {
            display: "none",
        },
        splitter: {
            "& .handle-bar": {
                display: (props: StyleProps) => (props.showPreview ? "flex" : "none"),
                alignSelf: "center",
            },
        },
    })
);

const useTabClassesErrorStyles = makeStyles((theme: Theme) =>
    createStyles({
        wrapper: {
            color: theme.palette.error.main,
        },
    })
);

const SaveAsButton = (props: {name: string; canSave: boolean; onSaveAs?: (newName: string) => void}) => {
    const {name, canSave, onSaveAs} = props;
    const classes = useStyles({});
    const {t} = useTranslation();
    const {validateForm} = useFormikContext<DashboardWidgetDefOrJsSpec>();
    const [saveAsDialogOpen, setSaveAsDialogOpen] = useState(false);

    const handleSaveAsContextTypeClick = useCallback(async () => {
        const result = await validateForm();
        if (Object.keys(result).length > 0) {
            return;
        }
        setSaveAsDialogOpen(true);
    }, [validateForm]);

    const handleOnChange = useCallback(
        (value: string) => {
            onSaveAs?.(value);
        },
        [onSaveAs]
    );
    const handleOnClose = useCallback(() => {
        setSaveAsDialogOpen(false);
    }, []);

    return (
        <>
            <Tooltip title={t("SaveAs") as string}>
                <IconButton onClick={handleSaveAsContextTypeClick} disabled={!canSave}>
                    <SaveIcon />
                    <Typography className={classes.saveAsEllipsis}>...</Typography>
                </IconButton>
            </Tooltip>
            {saveAsDialogOpen && (
                <WidgetDefinitionSaveAsDialog
                    open={saveAsDialogOpen}
                    defaultValue={name}
                    onChange={handleOnChange}
                    onClose={handleOnClose}
                />
            )}
        </>
    );
};

const InnerWidgetDefinitionBodyEditor: FC<{}> = props => {
    const {t} = useTranslation();
    const classes = useStyles({});
    const theme = useTheme();
    const tabClassesError = useTabClassesErrorStyles();
    const [activeTab, setActiveTab] = useState(0);
    const {errors, values: widgetDefinition} = useFormikContext<DashboardWidgetDefinition>();

    const parametersClasses = useMemo(() => {
        return !!errors.parameters ? tabClassesError : {};
    }, [errors.parameters, tabClassesError]);
    const dataSourcesClasses = useMemo(() => {
        return !!errors.dataSources ? tabClassesError : {};
    }, [errors.dataSources, tabClassesError]);
    const visualizationClasses = useMemo(() => {
        return !!errors.visualization ? tabClassesError : {};
    }, [errors.visualization, tabClassesError]);
    const tabIndicatorProps: TabsProps["TabIndicatorProps"] = useMemo(() => {
        const tabIndicatorColor = theme.palette.error.main;
        if (
            (activeTab === 0 && !!errors.parameters) ||
            (activeTab === 1 && !!errors.dataSources) ||
            (activeTab === 2 && !!errors.visualization)
        ) {
            return {
                style: {
                    backgroundColor: tabIndicatorColor,
                },
            };
        }
        return undefined;
    }, [activeTab, errors, theme.palette.error.main]);

    const handleTabChange = (event: React.ChangeEvent<{}>, newValue: number) => {
        setActiveTab(newValue);
    };

    const freeVariableTypingProviders: Record<
        string,
        () => string | Promise<string>
    > = useWidgetDefinitionTypingsProvider(widgetDefinition.parameters);

    return (
        <JsContextDefinitionsContextProvider freeVariableTypingProviders={freeVariableTypingProviders}>
            <Tabs
                value={activeTab}
                onChange={handleTabChange}
                variant="scrollable"
                scrollButtons="on"
                TabIndicatorProps={tabIndicatorProps}
                indicatorColor="primary"
                textColor="primary"
            >
                <Tab label={t("Parameters")} classes={parametersClasses} />
                <Tab label={t("DataSources")} classes={dataSourcesClasses} />
                <Tab label={t("Visualization")} classes={visualizationClasses} />
                <Tab label={t("Preview")} />
            </Tabs>
            <div role="tabpanel" hidden={activeTab !== 0}>
                <Paper className={classes.paper} elevation={3}>
                    <WidgetDefinitionParametersEditor path="parameters" />
                </Paper>
            </div>
            <div role="tabpanel" hidden={activeTab !== 1}>
                <Paper className={classes.paper} elevation={3}>
                    <WidgetDefinitionDataSourcesEditor path="dataSources" />
                </Paper>
            </div>
            <div role="tabpanel" hidden={activeTab !== 2}>
                <Paper className={classes.paper} elevation={3}>
                    <WidgetDefinitionVisualizationEditor path="visualization" visible={activeTab === 2} />
                </Paper>
            </div>
            <div role="tabpanel" hidden={activeTab !== 3}>
                <Paper className={classes.paper} elevation={3}>
                    <WidgetDefinitionPreviewer
                        parameters={widgetDefinition.parameters}
                        dataSources={widgetDefinition.dataSources}
                        visualization={widgetDefinition.visualization}
                        visible={activeTab === 3}
                    />
                </Paper>
            </div>
        </JsContextDefinitionsContextProvider>
    );
};

interface InnerWidgetJsSpecBodyEditorProp {
    showPreview: boolean;
    onJsSpecErrorChange?: (errors: string[]) => void;
}

const InnerWidgetJsSpecBodyEditor: FC<InnerWidgetJsSpecBodyEditorProp> = props => {
    const {showPreview, onJsSpecErrorChange} = props;
    const classes = useStyles({showPreview});
    const {values: widgetJsSpec} = useFormikContext<DashboardWidgetJsSpec>();
    const {getWidgetDefinition} = useNimflowDashboardWidgets();
    const {contextTypes_Search} = useNimflow();
    const [widgetDefinition, setWidgetDefinition] = useState<DashboardWidgetDefinition>();
    const [refreshingPreviewer, setRefreshingPreviewer] = React.useState(false);
    const {functionInfos} = useFunctionInfos();

    useEffect(() => {
        getWidgetDefinition(widgetJsSpec)
            .then(newDef => {
                setWidgetDefinition(oldDef => {
                    if (oldDef) {
                        if (equals(newDef.parameters, oldDef.parameters)) {
                            newDef.parameters = oldDef.parameters;
                        }
                        if (equals(newDef.dataSources, oldDef.dataSources)) {
                            newDef.dataSources = oldDef.dataSources;
                        }
                        if (equals(newDef.visualization, oldDef.visualization)) {
                            newDef.visualization = oldDef.visualization;
                        }
                    }
                    return newDef;
                });
            })
            .catch(() => {});
    }, [getWidgetDefinition, widgetJsSpec]);

    const widgetParams = widgetDefinition?.parameters;
    const widgetDataSources = widgetDefinition?.dataSources;

    const getContextTypeNames = useCallback(async () => (await contextTypes_Search({limit: 100})).map(s => s.name), [
        contextTypes_Search,
    ]);
    const freeVariableTypingProviders: Record<string, () => string | Promise<string>> = useWidgetJsSpecTypingsProvider(
        widgetParams,
        widgetDataSources,
        functionInfos,
        getContextTypeNames
    );

    const handleDragFinished = useCallback(() => {
        setRefreshingPreviewer(true);
    }, []);

    useEffect(() => {
        if (refreshingPreviewer) {
            setRefreshingPreviewer(false);
        }
    }, [refreshingPreviewer]);

    return (
        <JsContextDefinitionsContextProvider freeVariableTypingProviders={freeVariableTypingProviders}>
            <Paper className={classes.paper} elevation={3}>
                <Splitter
                    position="vertical"
                    primaryPaneWidth="50%"
                    primaryPaneMaxWidth="80%"
                    primaryPaneMinWidth="20%"
                    dispatchResize={true}
                    postPoned={true}
                    maximizedPrimaryPane={!showPreview}
                    className={classes.splitter}
                    onDragFinished={handleDragFinished}
                >
                    <div>
                        <WidgetJsSpecScriptEditor
                            path="widgetJsSpec"
                            visible={true}
                            onErrorChange={onJsSpecErrorChange}
                        />
                    </div>
                    <div>
                        {widgetDefinition && (
                            <WidgetDefinitionPreviewer
                                refreshing={refreshingPreviewer}
                                parameters={widgetDefinition.parameters}
                                dataSources={widgetDefinition.dataSources}
                                visualization={widgetDefinition.visualization}
                                visible={showPreview}
                            />
                        )}
                    </div>
                </Splitter>
            </Paper>
        </JsContextDefinitionsContextProvider>
    );
};

interface InnerWidgetDefOrJsSpecEditorProps {
    schema: yup.ObjectSchema<any>;
    onSave?: () => Promise<void>;
    onSaveAs?: (newName: string) => Promise<void>;
    jsSpecErrors: string[];
    onJsSpecErrorChange?: (errors: string[]) => void;
    isPersistent?: boolean;
}

const InnerWidgetDefOrJsSpecEditor: FC<InnerWidgetDefOrJsSpecEditorProps> = props => {
    const {schema, onSave, onSaveAs, jsSpecErrors, onJsSpecErrorChange, isPersistent = false} = props;
    const {t} = useTranslation();
    const classes = useStyles({});
    const navigate = useNavigate();
    const {enqueueSnackbar} = useSnackbar();
    const {isSubmitting, errors, validateForm, values: widgetDefOrJsSpec, dirty} = useFormikContext<
        DashboardWidgetDefOrJsSpec
    >();
    const editionScope = useEditionScope();
    const canSave = !isSubmitting && editionScope.canAccept;
    const reportErrors = useReportError();
    const [showPreview, setShowPreview] = useState(false);

    const opts = useMemo(() => {
        return {
            description: t("ExitingWidgetDefinitionEditorWithoutSavingWarning"),
        };
    }, [t]);
    useExitWithoutSavingBlocker(dirty, opts);

    const handleSaveWidgetDefinitionClick = useThrottleCallback(
        useCallback(async () => {
            if (onSave) {
                await onSave();
            }
        }, [onSave])
    );

    const handleOnSaveAs = useCallback(
        async (newName: string) => {
            if (onSaveAs) {
                await onSaveAs(newName);
            }
        },
        [onSaveAs]
    );

    const handleExportWidgetDefinitionClick = useCallback(() => {
        downloadJson(
            exportWidgetDefinitionToJson(widgetDefOrJsSpec),
            `${widgetDefOrJsSpec.name}.widget-definition.json`
        );
    }, [widgetDefOrJsSpec]);

    const handleValidateContextTypeClick = useCallback(async () => {
        const errorInfos = await validateWithYupSchema(schema, widgetDefOrJsSpec);
        if (jsSpecErrors.length > 0) {
            if (!errorInfos.find(s => s.dataPath === "widgetJsSpec")) {
                errorInfos.push({
                    dataPath: "widgetJsSpec",
                    message: jsSpecErrors.map(s => "\n\t" + s).join(""),
                });
            }
        }
        await validateForm();
        if (errorInfos.length > 0) {
            reportErrors(t("WidgetDefinitionHasValidationErrors"), errorInfos, {
                dialogProps: {
                    maxWidth: "lg",
                },
            });
        } else {
            enqueueSnackbar(t("WidgetDefinitionTypeIsValid"), {variant: "success"});
        }
    }, [enqueueSnackbar, jsSpecErrors, reportErrors, schema, t, validateForm, widgetDefOrJsSpec]);

    const handleGoBack = useCallback(() => {
        navigate(-1);
    }, [navigate]);

    const handleTogglePreview = useCallback(() => {
        setShowPreview(s => !s);
    }, []);

    return (
        <EditionScopeProvider {...editionScope}>
            <div className={classes.table}>
                <div className={classes.tableRow}>
                    <Grid container spacing={1}>
                        <Grid item xs={12}>
                            <Toolbar className={classes.toolbar}>
                                <Tooltip title={t("GoBack") as string}>
                                    <IconButton onClick={handleGoBack} disabled={isSubmitting}>
                                        <ArrowBackIcon />
                                    </IconButton>
                                </Tooltip>
                                <Tooltip title={t("SaveChanges") as string}>
                                    <IconButton onClick={handleSaveWidgetDefinitionClick} disabled={!canSave}>
                                        <SaveIcon />
                                    </IconButton>
                                </Tooltip>
                                {isPersistent && (
                                    <SaveAsButton
                                        name={widgetDefOrJsSpec.name}
                                        canSave={canSave}
                                        onSaveAs={handleOnSaveAs}
                                    />
                                )}
                                <Tooltip title={t("Export") as string}>
                                    <IconButton onClick={handleExportWidgetDefinitionClick}>
                                        <SaveAltIcon />
                                    </IconButton>
                                </Tooltip>
                                <Tooltip title={t("Validate") as string}>
                                    <IconButton onClick={handleValidateContextTypeClick}>
                                        <AssignmentTurnedInIcon />
                                    </IconButton>
                                </Tooltip>
                                {isDashboardWidgetJsSpec(widgetDefOrJsSpec) && (
                                    <Tooltip title={t("TogglePreview") as string}>
                                        <IconButton onClick={handleTogglePreview}>
                                            {showPreview ? <VisibilityOffIcon /> : <VisibilityIcon />}
                                        </IconButton>
                                    </Tooltip>
                                )}
                            </Toolbar>
                        </Grid>
                        <Grid item xs={3}>
                            <FormikTextField
                                name="name"
                                label={t("Name")}
                                className={classes.textField}
                                InputLabelProps={{required: true}}
                                debounce={500}
                                required
                            />
                        </Grid>
                        <Grid item xs={3}>
                            <FormikTextField
                                name="title"
                                label={t("Title")}
                                className={classes.textField}
                                InputLabelProps={{required: true}}
                                debounce={500}
                                required
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <FormikTextField
                                name="description"
                                label={t("Description")}
                                multiline
                                fullWidth
                                maxRows={4}
                                debounce={500}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            {isDashboardWidgetJsSpec(widgetDefOrJsSpec) && (
                                <InnerWidgetJsSpecBodyEditor
                                    showPreview={showPreview}
                                    onJsSpecErrorChange={onJsSpecErrorChange}
                                />
                            )}
                            {!isDashboardWidgetJsSpec(widgetDefOrJsSpec) && <InnerWidgetDefinitionBodyEditor />}
                        </Grid>
                    </Grid>
                </div>
                <FormikErrorViewer errors={errors} names={["id"]} className={classes.tableRow} />
            </div>
        </EditionScopeProvider>
    );
};

export interface WidgetDefOrJsSpecEditorProps {
    value: DashboardWidgetDefOrJsSpec;
    schema?: yup.ObjectSchema<any>;
    onApplyChanges?: (widgetDefinition: DashboardWidgetDefOrJsSpec) => Promise<boolean>;
    onSaveAs?: (widgetDefinition: DashboardWidgetDefOrJsSpec, newName: string) => Promise<boolean>;
    isPersistent?: boolean;
}

export const WidgetDefOrJsSpecEditor: FC<WidgetDefOrJsSpecEditorProps> = props => {
    const {value, schema = resolveDashboardWidgetDefOrJsSpecSchema(value), onApplyChanges, onSaveAs} = props;

    const {t} = useTranslation();
    const classes = useStyles({});
    const editionScope = useEditionScope();
    const confirm = useConfirm();
    const {enqueueSnackbar} = useSnackbar();
    const [jsSpecErrors, setJsSpecErrors] = useState<string[]>([]);
    const initialValues = useMemo(() => {
        return {
            ...value,
            description: value.description ?? "",
        };
    }, [value]);
    const formRef = useRef<FormikProps<DashboardWidgetDefOrJsSpec> | null>();

    useEffect(() => {
        formRef.current?.resetForm({values: initialValues});
    }, [initialValues]);

    const submitHandlerRef = useRef<(values: DashboardWidgetDefOrJsSpec) => Promise<void>>();

    const internalValidateAndSubmit = useCallback(
        async (form: FormikProps<DashboardWidgetDefOrJsSpec>) => {
            if (jsSpecErrors.length > 0 && isDashboardWidgetJsSpec(form.values)) {
                await validateForm(form);
                form.setFieldError("widgetJsSpec", jsSpecErrors.join("\n"));
                return;
            }
            validateAndSubmit(form);
        },
        [jsSpecErrors]
    );

    const handleSave = useCallback(() => {
        const form = formRef.current;
        if (form && onApplyChanges) {
            submitHandlerRef.current = async (data: DashboardWidgetDefOrJsSpec) => {
                let error: React.ReactNode;
                try {
                    const saved = await onApplyChanges(data);
                    if (!saved) {
                        return;
                    }
                } catch (e) {
                    error = <ErrorViewer error={e} />;
                }
                if (error) {
                    await confirm({
                        title: t("ErrorSavingWidgetDefinition"),
                        description: error,
                        mapCloseTo: "ok",
                        actions: [{type: "ok", text: t("Ok")}],
                    });
                } else {
                    enqueueSnackbar(t("WidgetDefinitionSaved"), {variant: "success"});
                }
            };
            internalValidateAndSubmit(form);
        }
        return Promise.resolve();
    }, [confirm, enqueueSnackbar, internalValidateAndSubmit, onApplyChanges, t]);

    const handleSaveAs = useCallback(
        (newValue: string) => {
            const form = formRef.current;
            if (form && onSaveAs) {
                submitHandlerRef.current = async (data: DashboardWidgetDefOrJsSpec) => {
                    let error: React.ReactNode;
                    try {
                        const saved = await onSaveAs(data, newValue);
                        if (!saved) {
                            return;
                        }
                    } catch (e) {
                        error = <ErrorViewer error={e} />;
                    }
                    if (error) {
                        await confirm({
                            title: t("ErrorSavingWidgetDefinition"),
                            description: error,
                            mapCloseTo: "ok",
                            actions: [{type: "ok", text: t("Ok")}],
                        });
                    } else {
                        enqueueSnackbar(t("WidgetDefinitionSaved"), {variant: "success"});
                    }
                };
                internalValidateAndSubmit(form);
            }
            return Promise.resolve();
        },
        [confirm, enqueueSnackbar, internalValidateAndSubmit, onSaveAs, t]
    );

    const handleSubmit = useCallback(
        async (values: DashboardWidgetDefOrJsSpec, actions: FormikHelpers<DashboardWidgetDefOrJsSpec>) => {
            try {
                if (submitHandlerRef.current) {
                    await submitHandlerRef.current(values);
                    submitHandlerRef.current = undefined;
                }
            } finally {
                actions.setSubmitting(false);
            }
        },
        []
    );

    return (
        <ErrorBoundary>
            <Formik<DashboardWidgetDefOrJsSpec>
                innerRef={s => (formRef.current = s)}
                initialValues={initialValues}
                onSubmit={handleSubmit}
                validationSchema={schema}
                validateOnChange={true}
                validateOnBlur={true}
            >
                <Form autoComplete="off" className={classes.form}>
                    <EditionScopeProvider {...editionScope}>
                        <InnerWidgetDefOrJsSpecEditor
                            {...props}
                            schema={schema}
                            onSave={handleSave}
                            onSaveAs={handleSaveAs}
                            jsSpecErrors={jsSpecErrors}
                            onJsSpecErrorChange={setJsSpecErrors}
                        />
                    </EditionScopeProvider>
                </Form>
            </Formik>
        </ErrorBoundary>
    );
};
