import React, {FC, useState, useEffect, useCallback} from "react";
import Button from "@material-ui/core/Button";
import {useNavigate, useParams} from "react-router";
import {useSnackbar} from "notistack";
import {useTranslation} from "react-i18next";
import axios from "axios";

import {useNimflow} from "../../api/nimflow-api-provider";
import {SimulationContextTypeProvider} from "./simulation-context-type-provider";
import {ContextSimulationContextProvider} from "./simulation-context";
import {ContextSimulationEditor} from "./context-simulation-editor";
import {WaitSpinner} from "../../components/wait-spinner";
import {readJsonFromBlob} from "../../utils/io-helpers";
import {importSimulationStateFromJson} from "./simulation-state-importer";
import {useShowErrorInfos} from "../../components/use-show-error-infos";
import {ContextSimulation, useNimflowSimulation} from "../../api/nimflow-simulation-api";
import {ApiException} from "../../api/nimflow-api";

export const ContextSimulationImporter: FC<{}> = props => {
    const {t} = useTranslation();
    const navigate = useNavigate();
    const {blobUrl} = useParams();
    const {enqueueSnackbar} = useSnackbar();
    const showErrors = useShowErrorInfos();
    const {contextTypes_FindOne} = useNimflow();
    const {simulations_InsertOne} = useNimflowSimulation();
    const [simulation, setSimulation] = useState<ContextSimulation>();
    const [contextTypeProvider, setContextTypeProvider] = useState<SimulationContextTypeProvider | undefined>();

    useEffect(() => {
        const dataUrl = atob(blobUrl);
        if (!dataUrl) {
            return;
        }
        axios
            .get(dataUrl, {responseType: "blob"})
            .then(async response => {
                const json = await readJsonFromBlob(response.data);
                const importResult = await importSimulationStateFromJson(json, name => contextTypes_FindOne({name}));
                if (importResult.type === "success") {
                    setSimulation(importResult.result[0]);
                    setContextTypeProvider(importResult.result[1]);
                    return;
                } else {
                    enqueueSnackbar(t("FileIsNotAValidContextSimulation"), {
                        variant: "error",
                        action: (
                            <Button
                                onClick={() =>
                                    showErrors(importResult.errors, {
                                        title: t("ImportErrors"),
                                    })
                                }
                            >
                                {t("ShowDetails")}
                            </Button>
                        ),
                    });
                    navigate("../..");
                }
            })
            .catch(() => {
                navigate("../..");
            })
            .finally(() => {
                URL.revokeObjectURL(dataUrl);
            });
    }, [blobUrl, navigate, enqueueSnackbar, showErrors, t, contextTypes_FindOne]);

    const handleOnSaveChanges = useCallback(
        async (value: ContextSimulation) => {
            try {
                const id = await simulations_InsertOne(value);
                navigate(`../../edit/${id}`, {replace: true});
            } catch (e: any) {
                let msg = e.toString();
                if (ApiException.isApiException(e)) {
                    msg = e.message;
                    if ("status" in e) {
                        msg += ` (status: ${e.status})`;
                    }
                }
                return [{message: msg}];
            }
            return [];
        },
        [navigate, simulations_InsertOne]
    );

    if (!simulation || !contextTypeProvider) {
        return <WaitSpinner />;
    }

    return (
        <ContextSimulationContextProvider
            contextTypeProvider={contextTypeProvider}
            simulation={simulation}
        >
            <ContextSimulationEditor onSaveChanges={handleOnSaveChanges} />
        </ContextSimulationContextProvider>
    );
};
