import React, {FC, useMemo} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import MuiAccordion from "@material-ui/core/Accordion";
import MuiAccordionSummary from "@material-ui/core/AccordionSummary";
import MuiAccordionDetails from "@material-ui/core/AccordionDetails";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import withStyles from "@material-ui/core/styles/withStyles";
import Divider from "@material-ui/core/Divider";
import Chip from "@material-ui/core/Chip";
import AssignmentIcon from "@material-ui/icons/Assignment";
import ReactMarkdown from "react-markdown";
import {useTranslation} from "react-i18next";
import {parseISO, format} from "date-fns";
import {isPresent} from "ts-is-present";

import JsonViewer from "../../components/json-viewer";
import {ContextTask, ResponseType, TaskType} from "../../api/nimflow-api";
import {getSettings, getCommandText, getImportanceString} from "../../api/nimflow-api-extensions";
import {evaluateHandlebars} from "../../libs/handlebars";
import {MaterialFontIcon} from "../../components/material-font-icon";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        inline: {
            display: "inline",
        },
        actionHeading: {
            fontSize: theme.typography.pxToRem(15),
            fontWeight: theme.typography.fontWeightRegular,
        },
        summaryTextContainer: {
            width: "100%",
            height: "100%",
            display: "flex",
            alignItems: "center",
        },
        taskDivider: {
            // marginLeft: -16,
            // marginRight: -16,
            margin: "10px -16px",
        },
        responses: {
            marginTop: 16,
            display: "flex",
            justifyContent: "center",
            flexWrap: "wrap",
            "& > *": {
                margin: theme.spacing(0.5),
            },
        },
    })
);

const Accordion = withStyles({
    root: {
        backgroundColor: "transparent",
        boxShadow: "none",
        "&:not(:last-child)": {
            borderBottom: 0,
        },
        "&:before": {
            display: "none",
        },
        "&$expanded": {
            border: "1px solid rgba(0, 0, 0, .125)",
            margin: "auto",
        },
    },
    expanded: {},
})(MuiAccordion);

const AccordionSummary = withStyles({
    root: {
        marginBottom: -1,
        height: 32,
        minHeight: 32,
        papdding: 0,
        "&$expanded": {
            borderBottom: "1px solid rgba(0, 0, 0, .125)",
            minHeight: 56,
        },
    },
    content: {
        margin: 0,
        height: "100%",
        alignItems: "center",
        "&$expanded": {
            margin: 0,
        },
    },
    expandIcon: {
        paddingRight: "16px",
    },
    expanded: {},
})(MuiAccordionSummary);

const AccordionDetails = withStyles(theme => ({
    root: {
        padding: theme.spacing(2),
        flexDirection: "column",
    },
}))(MuiAccordionDetails);

export interface TaskViewerProps {
    taskType?: TaskType;
    task: ContextTask;
    onAddResponse?: (task: ContextTask, responseType: ResponseType) => void;
}

export const TaskViewer: FC<TaskViewerProps> = props => {
    const {taskType, task, onAddResponse} = props;
    const {t} = useTranslation();
    const classes = useStyles();

    const responseTypes = taskType?.validResponses ?? [];
    let taskDetails: string | undefined;
    if (taskType) {
        const taskDetailsTemplate = getSettings(taskType)?.taskDetailsTemplate;
        if (taskDetailsTemplate) {
            taskDetails = evaluateHandlebars(taskDetailsTemplate, {task});
        }
    }

    const importanceDescription = useMemo(() => {
        const str = getImportanceString(task.importance);
        if (!isPresent(str)) {
            return "";
        }
        return t(`Importance.${str}`)
    }, [t, task.importance]);

    return (
        <>
            <Accordion>
                <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                    <div className={classes.summaryTextContainer}>
                        <Typography className={classes.actionHeading}>{task.title}</Typography>
                    </div>
                    <Chip
                        label={task.typeName}
                        variant="outlined"
                        title={task.typeName}
                        style={{maxWidth: 150, textOverflow: "ellipsis"}}
                    />
                </AccordionSummary>
                <AccordionDetails>
                    <div style={{width: "100%"}}>
                        <ReactMarkdown>{taskDetails ?? ""}</ReactMarkdown>
                    </div>
                    <Divider className={classes.taskDivider} />
                    <Typography variant="body1" component="div">
                        {t("AssignedTo_value", {value: task.assignedTo})}
                    </Typography>
                    <Typography variant="body1" component="div">
                        {t("DueDate_value", {
                            value: task.dueDate ? format(parseISO(task.dueDate), t("DATETIME_FORMAT")) : undefined,
                        })}
                    </Typography>
                    <Typography variant="body1" component="div">
                        {t("Priority_value", {value: importanceDescription})}
                    </Typography>

                    {!!task.data && Object.keys(task.data).length > 0 && (
                        <>
                            <Divider className={classes.taskDivider} />
                            <Typography variant="body1" component="div">
                                {t("Data")}
                            </Typography>
                            <div style={{width: "100%"}}>
                                <JsonViewer data={task.data ?? {}} />
                            </div>
                        </>
                    )}
                    {responseTypes.length > 0 && (
                        <>
                            <Divider className={classes.taskDivider} />
                            <div className={classes.responses}>
                                {responseTypes.map((response, i) => {
                                    const icon = getSettings(response)?.icon;
                                    return (
                                        <Chip
                                            key={i}
                                            icon={
                                                icon ? (
                                                    <MaterialFontIcon family={icon.family}>
                                                        {icon.name}
                                                    </MaterialFontIcon>
                                                ) : (
                                                    <AssignmentIcon />
                                                )
                                            }
                                            label={getCommandText(response)}
                                            onClick={() => onAddResponse?.(task, response)}
                                        />
                                    );
                                })}
                            </div>
                        </>
                    )}
                </AccordionDetails>
            </Accordion>
        </>
    );
};
