import * as React from "react";
import {AbstractReactFactory, GenerateWidgetEvent} from "@projectstorm/react-canvas-core";
import {DiagramEngine} from "@projectstorm/react-diagrams-core";

import {TaskTypeNodeWidget} from "./task-type-node-widget";
import {TaskTypeNodeModel} from "./task-type-node-model";

export class TaskTypeNodeFactory extends AbstractReactFactory<TaskTypeNodeModel, DiagramEngine> {
    constructor() {
        super("taskType");
    }

    generateModel(initialConfig: any) {
        const instanceId = initialConfig.instanceId ?? "";
        const taskType = {...initialConfig};
        delete taskType.instanceId;
        return new TaskTypeNodeModel(taskType, instanceId);
    }

    generateReactWidget(event: GenerateWidgetEvent<TaskTypeNodeModel>): JSX.Element {
        return <TaskTypeNodeWidget engine={this.engine as DiagramEngine} node={event.model} />;
    }
}
