import React, {FC, useCallback, useMemo, useState} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import * as yup from "yup";
import {useForm, useFormContext, FormProvider} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import i18next from "i18next";
import {useTranslation} from "react-i18next";
import {DevTool} from "@hookform/devtools";

import {TaskType, ContextType} from "../../api/nimflow-api";
import {
    TaskTypeSchema,
    jsCommonScriptKey,
    jsonSchemaScriptKey,
    ResponseTypeSchema,
} from "../../models/context-type-schema";
import {TaskTypeEditor} from "./task-type-editor";
import {mapObjIndexed} from "ramda";
import {useEditionScope, EditionScopeProvider} from "../../components/edition-scope";
import {defaultContextJsonSchemaString} from "../../models/default-values";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            backgroundColor: theme.palette.background.default,
        },
    })
);

export interface TaskTypeModalEditorProps {
    open: boolean;
    defaultValue?: Partial<TaskType>;
    definitionJsonSchemas: Record<string, string | undefined>;
    onChange: (value: TaskType, definitionJsonSchemas: Record<string, string | undefined>) => void;
    onClose: () => void;
}

const getExtendedSchema = (usedNames?: string[]) => {
    const reponseTypeSchema = ResponseTypeSchema.shape({
        payloadSchemaDefinitionName: yup.string() /*.jsonSchemaDefinitionName(jsonSchemaScriptKey)*/, // Guaranteed by editor
    });

    let nameSchema = TaskTypeSchema.fields!.name;
    if (usedNames && usedNames.length > 0) {
        nameSchema = nameSchema.concat(
            yup
                .string()
                .defined()
                .test({
                    message: ({value}) => i18next.t("TaskTypeNameAlreadyExists", {name: value}),
                    test: function (value: any) {
                        return !usedNames.includes(value);
                    },
                })
        );
    }

    const result = TaskTypeSchema.shape({
        name: nameSchema,
        validResponses: yup
            .array()
            .of(reponseTypeSchema.required())
            .defined()
            .uniqueBy(
                s => s.name,
                ({duplicates}) => i18next.t("DuplicateResponseTypeNames", {names: duplicates.join(", ")})
            ),
    });
    return result;
};

export const TaskTypeEditorDialog: FC<TaskTypeModalEditorProps> = props => {
    const {open, defaultValue: value, definitionJsonSchemas: initialDefinitionJsonSchemas, onChange, onClose} = props;
    const classes = useStyles();
    const contextTypeFormMethods = useFormContext<ContextType>();
    const schema = useMemo(() => {
        const names = contextTypeFormMethods
            ?.getValues()
            .tasks?.map(s => s.name)
            .filter(s => s !== value?.name);
        return getExtendedSchema(names);
    }, [contextTypeFormMethods, value]);
    const [definitionJsonSchemas, setDefinitionJsonSchemas] = useState(initialDefinitionJsonSchemas);
    const formMethods = useForm({
        mode: "onChange",
        reValidateMode: "onChange",
        defaultValues: value ?? {},
        context: {
            [jsCommonScriptKey]: contextTypeFormMethods.getValues("jsCommon"),
            [jsonSchemaScriptKey]: () => {
                let schema = contextTypeFormMethods.getValues("jsonSchema");
                const schemaObj = JSON.parse(schema ?? defaultContextJsonSchemaString);
                if (definitionJsonSchemas !== initialDefinitionJsonSchemas) {
                    schemaObj.definitions = {
                        ...schemaObj.definitions,
                        ...mapObjIndexed(s => (!!s ? JSON.parse(s) : undefined), definitionJsonSchemas),
                    };
                }
                return schemaObj;
            },
        },
        resolver: yupResolver(schema, {
            strict: true,
            abortEarly: false,
        }),
    });
    const {control, handleSubmit} = formMethods;
    const {t} = useTranslation();
    const editionScope = useEditionScope();

    const onSubmit = useCallback(
        (value: Partial<TaskType>) => {
            onChange?.(value as TaskType, definitionJsonSchemas);
            onClose?.();
        },
        [definitionJsonSchemas, onChange, onClose]
    );

    return (
        <Dialog
            open={open}
            fullWidth
            maxWidth="lg"
            classes={{paper: classes.paper}}
        >
            <FormProvider {...formMethods}>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <EditionScopeProvider {...editionScope}>
                        <DialogTitle>{t("EditingTaskType")}</DialogTitle>
                        <DialogContent>
                            <TaskTypeEditor
                                definitionJsonSchemas={definitionJsonSchemas}
                                onDefinitionJsonSchemasChange={setDefinitionJsonSchemas}
                            />
                            {/* <FormErrorViewer errors={contextTypeFormMethods.errors} /> */}
                        </DialogContent>
                        <DialogActions>
                            <Button type="submit" color="primary" disabled={!editionScope.canAccept}>
                                {t("Accept")}
                            </Button>
                            <Button onClick={onClose} color="primary">
                                {t("Cancel")}
                            </Button>
                        </DialogActions>
                    </EditionScopeProvider>
                </form>
            </FormProvider>
            <DevTool control={control} />
        </Dialog>
    );
};
