import * as React from "react";
import {AbstractReactFactory, GenerateWidgetEvent} from "@projectstorm/react-canvas-core";
import {DiagramEngine} from "@projectstorm/react-diagrams-core";

import {ActionTypeNodeModel} from "./action-type-node-model";
import {ActionTypeNodeWidget} from "./action-type-node-widget";

export class ActionTypeNodeFactory extends AbstractReactFactory<ActionTypeNodeModel, DiagramEngine> {
    constructor() {
        super("actionType");
    }

    generateModel(initialConfig: any) {
        const isInitialAction = initialConfig.isInitialAction ?? false;
        const instanceId = initialConfig.instanceId ?? "";
        const actionType = {...initialConfig};
        delete actionType.isInitialAction;
        delete actionType.instanceId;
        return new ActionTypeNodeModel(actionType, instanceId, isInitialAction);
    }

    generateReactWidget(event: GenerateWidgetEvent<ActionTypeNodeModel>): JSX.Element {
        return <ActionTypeNodeWidget engine={this.engine} node={event.model}/>;
    }
}
