import React, {FC, useState, useCallback, useMemo} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import InputAdornment from "@material-ui/core/InputAdornment";
import Tooltip from "@material-ui/core/Tooltip";
import Button from "@material-ui/core/Button";
import Dialog, {DialogProps} from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Typography from "@material-ui/core/Typography";
import ErrorIcon from "@material-ui/icons/Error";
import WarningIcon from "@material-ui/icons/Warning";
import {useTranslation} from "react-i18next";

import {getCodes} from "./lang-helpers";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        warningBlock: {
            marginBottom: 10,
        },
        warningIcon: {
            width: 50,
            height: 50,
            marginRight: 15,
            float: "left",
            verticalAlign: "middle",
            color: theme.palette.warning.main,
        },
    })
);

export type DeleteLocalizationConfirmationDialogProps = {
    language: string;
    onConfirm: (language: string) => void;
} & Pick<DialogProps, "open" | "onClose">;

export const DeleteLocalizationConfirmationDialog: FC<DeleteLocalizationConfirmationDialogProps> = props => {
    const {open, language, onClose, onConfirm} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const [confirmText, setConfirmText] = useState("");
    const description = useMemo(() => {
        const code = getCodes().find(s => s.code === language);
        let description = language;
        if (code) {
            description += ` (${code?.names.result})`;
        }
        return description;
    }, [language]);

    const handleDelete = useCallback(
        (ev: React.MouseEvent<HTMLButtonElement>) => {
            onConfirm(language);
            onClose?.(ev, "escapeKeyDown");
        },
        [onConfirm, language, onClose]
    );

    return (
        <Dialog fullWidth open={open} onClose={onClose}>
            <DialogTitle>{t("DeleteLocalizationConfirmationTitle", {name: language})}</DialogTitle>
            <DialogContent>
                <div className={classes.warningBlock}>
                    <WarningIcon className={classes.warningIcon} />
                    <Typography variant="body2" display="block">
                        {t("DeleteLocalizationConfirmationWarning", {name: description})}
                    </Typography>
                </div>
                <Typography variant="overline" display="block">
                    {t("DeleteLocalizationConfirmationTextFieldInstruction")}
                </Typography>
                <TextField
                    autoFocus
                    fullWidth
                    value={confirmText}
                    onChange={ev => setConfirmText(ev.target.value)}
                    InputProps={{
                        endAdornment: (
                            <InputAdornment position="end">
                                <Tooltip title={t("DeleteLocalizationConfirmationTooltip", {name: language}) as string}>
                                    <ErrorIcon />
                                </Tooltip>
                            </InputAdornment>
                        ),
                    }}
                />
            </DialogContent>
            <DialogActions>
                <Button disabled={language.toLowerCase() !== confirmText?.toLowerCase()} onClick={handleDelete}>
                    {t("Delete")}
                </Button>
                <Button onClick={ev => onClose?.(ev, "escapeKeyDown")}>{t("Cancel")}</Button>
            </DialogActions>
        </Dialog>
    );
};
