import {FC} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import {useTranslation} from "react-i18next";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";

import {DigitalWorker} from "../../api/nimflow-api";
import {useCurrentUnitInfo} from "../../api/nimflow-business-directory-provider";

const titleHeight = 64;
const footerHeight = 52;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            backgroundColor: theme.palette.background.default,
            height: "100%",
            width: "100%",
            maxHeight: "50vh",
        },
        dialogTitle: {
            height: titleHeight,
        },
        dialogContent: {
            height: `calc(100% - ${titleHeight}px - ${footerHeight}px)`,
            width: "100%",
        },
        dialogFooter: {
            height: footerHeight,
        },
    })
);

export interface DigitalWorkerConfigDialogProps {
    open: boolean;
    digitalWorker: DigitalWorker;
    onClose: () => void;
}

export const DigitalWorkerConfigDialog: FC<DigitalWorkerConfigDialogProps> = props => {
    const {open, digitalWorker, onClose} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const {unit, hubBaseUrl: endpointUrl} = useCurrentUnitInfo();

    return (
        <Dialog open={open} onClose={onClose} fullWidth maxWidth="md" classes={{paper: classes.paper}}>
            <DialogTitle className={classes.dialogTitle}>{t("DigitalWorkerConfiguration")}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <TextField
                            label={t("Name")}
                            value={digitalWorker.name}
                            InputProps={{readOnly: true}}
                            fullWidth
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            label={t("ApiKey")}
                            value={digitalWorker.apiKey}
                            InputProps={{readOnly: true}}
                            fullWidth
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField label={t("UnitName")} value={unit?.name} InputProps={{readOnly: true}} fullWidth />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField label={t("UnitId")} value={unit?.id} InputProps={{readOnly: true}} fullWidth />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            label={t("EndpointUrl")}
                            value={endpointUrl}
                            InputProps={{readOnly: true}}
                            fullWidth
                        />
                    </Grid>
                </Grid>
            </DialogContent>
            <DialogActions className={classes.dialogFooter}>
                <Button onClick={onClose} color="primary">
                    {t("Close")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
