import React, {FC, useCallback, useMemo} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import {isPresent} from "ts-is-present";

import {StepContainer} from "./step-container";
import {NewActionStep} from "./new-action-editor";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            flexGrow: 1,
            width: "100%",
            marginTop: "20px",
            marginBottom: "20px",
        },
    })
);

export interface ActionTypeSelectorStepProps extends NewActionStep {}

export const ActionTypeSelectorStep: FC<ActionTypeSelectorStepProps> = props => {
    const {value, isInitialAction, contextType, onNext, onCancel} = props;
    const classes = useStyles();

    const candidateActions = useMemo(() => {
        if (isInitialAction) {
            return contextType.initialActions.map(s => contextType.actions.find(a => a.name === s)).filter(isPresent);
        }
        return contextType.actions;
    }, [contextType, isInitialAction]);

    const handleActionSelected = useCallback(
        (name: string) => {
            if (value.action === name) {
                onNext(value);
            } else {
                onNext({
                    action: name,
                });
            }
        },
        [value, onNext]
    );

    return (
        <StepContainer onCancel={onCancel}>
            <Grid container direction="column" alignItems="center" spacing={3} className={classes.root}>
                {candidateActions.map(s => (
                    <Grid item key={s.name} xs={10}>
                        <Button variant="outlined" onClick={() => handleActionSelected(s.name)}>
                            {s.name}
                        </Button>
                    </Grid>
                ))}
            </Grid>
        </StepContainer>
    );
};
