import {isValid, parseISO} from "date-fns";
import i18next from "i18next";
import * as yup from "yup";

import {DigitalWorker} from "./../../api/nimflow-api";

export const DigitalWorkerSchema = yup.object().shape({
    name: yup.string().required().identifier(),
    role: yup.string().required(),
    expires: yup
        .string()
        .iso8601Formatted()
        .test({
            message: "ExpirationDateMustBeAFutureDate",
            test: function (value: any) {
                if (!value || typeof value !== "string") {
                    return true;
                }
                const date = parseISO(value);
                if (!isValid(date)) {
                    return true;
                }
                const dw: DigitalWorker = this.parent;
                if (date < new Date() && !dw.id) {
                    // Expiration must be in the future for new digital workers
                    return this.createError({
                        path: "expires",
                        message: i18next.t("ExpirationDateMustBeAFutureDate"),
                    });
                }
                return true;
            },
        }),
    active: yup.boolean(),
});
