import {FC, useMemo, useState} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import {useFormikContext} from "formik";
import {
    DashboardWidgetQueryDataSource,
    DashboardWidgetAggregateDataSourceSourceCollection,
    FilterTypeDescriptor,
    contextFilterDescriptor,
    contextTaskFilterDescriptor,
    eventFilterDescriptor,
} from "../../api/nimflow-dashboard-widgets-api";
import {Typography} from "@material-ui/core";
import {useTranslation} from "react-i18next";
import Autosizer from "react-virtualized-auto-sizer";
import {useDebounce} from "react-use";

import {JavascriptMonacoEditor} from "../../components/javascript-monaco-editor";
import {freeVarDefs} from "./free-variable-defs";
import {JsContextDefinitionsContextProvider} from "../../components/js-context-definitions-provider";
import {FormikErrorViewer} from "../../components/formik-error-viewer";

const titleContainerHeight = 40;
const errorContainerHeight = 100;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        grid: {
            display: "grid",
            gridTemplateColumns: "auto",
            gridTemplateRows: (props: {showErrors: boolean}) =>
                `${titleContainerHeight}px ` + (props.showErrors ? `auto ${errorContainerHeight}px` : "auto"),
            rowGap: "5px",
            placeItems: "stretch",
            overflowY: "hidden",
            height: "calc(100% - 50px)",
        },
        titleContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
            marginTop: 10,
        },
        editorContainer: {
            gridColumn: "1 / 2",
            gridRow: "2 / 3",
            minHeight: 200,
        },
        errorContainer: {
            gridColumn: "1 / 2",
            gridRow: "3 / 4",
            overflowY: "auto",
        },
    })
);

export interface QueryFilterEditorProps {}

const getTypeDefinition = (baseType: FilterTypeDescriptor<any>[string]): string => {
    function interval(type: string) {
        return `[${type} | undefined, ${type} | undefined]`;
    }

    switch (baseType) {
        case "number":
            return `number | ${interval("number")}`;
        case "Date":
            const dateType = "Date | string";
            return `${dateType} | ${interval(dateType)}`;
        case "StringMap":
            return `Record<string, ${getTypeDefinition("string")}>`;
        case "NumberMap":
            return `Record<string, ${getTypeDefinition("number")}>`;
        case "DateMap":
            return `Record<string, ${getTypeDefinition("Date")}>`;
        case "string":
        case "boolean":
        case "string[]":
            return baseType;
    }
};

const getQueryFilterTypingsProvider = (collection: DashboardWidgetAggregateDataSourceSourceCollection) => {
    let descriptor: FilterTypeDescriptor<any>;
    switch (collection) {
        case "contexts":
            descriptor = contextFilterDescriptor;
            break;
        case "tasks":
            descriptor = contextTaskFilterDescriptor;
            break;
        case "events":
            descriptor = eventFilterDescriptor;
            break;
    }
    let typeDef = "";
    for (const [key, value] of Object.entries(descriptor)) {
        typeDef += `\t\t${key}?: ${getTypeDefinition(value)}`;
    }
    return `
    declare let queryFilter: {
        ${typeDef}
    };
`;
};

const freeVariables = [freeVarDefs.params, freeVarDefs.queryFilter];

export const QueryFilterEditor: FC<QueryFilterEditorProps> = props => {
    const {t} = useTranslation();
    const {errors, values: dataSource, setFieldValue} = useFormikContext<Partial<DashboardWidgetQueryDataSource>>();
    const filterErrors = (errors.query as any)?.filter;
    const classes = useStyles({showErrors: !!filterErrors});
    const collection = dataSource.sourceCollection;
    const [code, setCode] = useState(dataSource.query?.filter);
    useDebounce(
        () => {
            setFieldValue("query.filter", code);
        },
        1000,
        [code]
    );

    const freeVariableTypingProviders: Record<string, () => string | Promise<string>> = useMemo(() => {
        if (!collection) {
            return {
                queryFilter: () => "",
            };
        }
        const filter = getQueryFilterTypingsProvider(collection);
        return {
            queryFilter: () => filter,
        };
    }, [collection]);

    if (!collection) {
        return null;
    }

    return (
        <JsContextDefinitionsContextProvider freeVariableTypingProviders={freeVariableTypingProviders}>
            <div className={classes.grid}>
                <div className={classes.titleContainer}>
                    <Typography variant="subtitle2">{t("QueryFilter")}</Typography>
                </div>
                <div className={classes.editorContainer}>
                    <Autosizer>
                        {({height, width}) => {
                            return (
                                <JavascriptMonacoEditor
                                    height={height}
                                    width={width}
                                    value={code}
                                    onChange={setCode}
                                    freeVariables={freeVariables}
                                    importJsCommon={false}
                                ></JavascriptMonacoEditor>
                            );
                        }}
                    </Autosizer>
                </div>
                {!!filterErrors && (
                    <div className={classes.errorContainer}>
                        <FormikErrorViewer errors={errors} names="query.filter" />
                    </div>
                )}
            </div>
        </JsContextDefinitionsContextProvider>
    );
};
