import {FC, useEffect, useMemo, useState} from "react";
import {Column, Options} from "@material-table/core";
import {isPresent} from "ts-is-present";
import {ExportCsv} from "@material-table/exporters";
import i18next from "i18next";

import {DashboardWidgetTabularVisualization} from "../../api/nimflow-dashboard-widgets-api";
import MaterialTable from "../../libs/material-table";
import JsonViewer from "../../components/json-viewer";
import {evaluateScript} from "../../utils/scripting";

export interface DashboardWidgetTabularVisualizationPreviewerProps {
    visualization: DashboardWidgetTabularVisualization;
    requiredParamsHaveValue: boolean;
    args: Record<string, any>;
    data?: Record<string, any>;
}

interface TabularConfig {
    dataSourceName: string;
    columns: Column<any>[];
    title?: string;
    options?: Options<any>;
}

export const DashboardWidgetTabularVisualizationPreviewer: FC<DashboardWidgetTabularVisualizationPreviewerProps> =
    props => {
        const {visualization, requiredParamsHaveValue, args, data} = props;
        const [config, setConfig] = useState<TabularConfig>();

        useEffect(() => {
            if (!requiredParamsHaveValue) {
                return;
            }
            const outputValues = {
                tabularConfig: undefined as TabularConfig | undefined,
            };
            evaluateScript(visualization.jsConfiguration, {
                inputValues: {params: args},
                outputValues
            });
            const config = outputValues.tabularConfig;
            if (!config) {
                return;
            }
            if (!config.title) {
                config.title = "";
            }
            let options = config.options;
            if (!options) {
                config.options = options = {};
            }
            if (!isPresent(options.exportMenu)) {
                options.exportMenu = [
                    {
                        label: i18next.t("ExportCSV"),
                        exportFunc: (cols, datas) => ExportCsv(cols, datas, "myCsvFileName"),
                    },
                ];
            }
            if (!isPresent(options.exportAllData))
                options.exportAllData = true;
            setConfig(config);
        }, [args, requiredParamsHaveValue, visualization.jsConfiguration]);

        const dataSourceData = useMemo(() => {
            if (!config) return [];
            const rawData = data?.[config.dataSourceName] ?? [];
            if (Array.isArray(rawData) && rawData.length > 0 && config.columns?.length > 0) {
                for (const column of config.columns.filter(column => column.field)) {
                    const sampleData = rawData[0][column.field as string];
                    if (typeof sampleData === "object" && sampleData !== null && !(sampleData instanceof Date)) {
                        column.render = rawData => {
                            return <JsonViewer data={rawData[column.field as string]}></JsonViewer>;
                        };
                    }
                }
            }
            return rawData;
        }, [data, config]);

        if (!config) {
            return null;
        }

        return (
            <MaterialTable
                columns={config.columns}
                data={dataSourceData}
                title={config.title}
                options={config.options}
            />
        );
    };
