import {FC, useState, useEffect, useCallback, useMemo} from "react";
import * as yup from "yup";
import {useParams} from "react-router";
import {useTranslation} from "react-i18next";

import {DigitalWorker} from "../../api/nimflow-api";
import {useNimflow} from "../../api/nimflow-api-provider";
import {DigitalWorkerEditor} from "./digital-worker-editor";
import {DigitalWorkerSchema} from "./digital-worker-schema";

export const DigitalWorkerUpdater: FC<{}> = () => {
    const {name} = useParams();
    const {t} = useTranslation();
    const [digitalWorker, setDigitalWorker] = useState<DigitalWorker>();
    const {digitalWorkers_Update, digitalWorkers_Search} = useNimflow();

    useEffect(() => {
        digitalWorkers_Search({name}).then(s => {
            if (s.length > 0) {
                setDigitalWorker(s[0]);
            }
        });
    }, [digitalWorkers_Search, name]);

    const handleOnChange = useCallback(
        async (value: DigitalWorker) => {
            await digitalWorkers_Update({
                id: value.id,
                name: value.name,
                expires: value.expires,
                active: value.active,
            });
            setDigitalWorker(value);
            return true;
        },
        [digitalWorkers_Update]
    );

    const DigitalWorkerUpdaterSchema = useMemo(
        () =>
            DigitalWorkerSchema.shape({
                name: DigitalWorkerSchema.fields!.name.concat(
                    yup
                        .string()
                        .defined()
                        .test({
                            message: ({value}) => t("DashboardDefinitionNameAlreadyExists", {name: value}),
                            test: async function (value: any) {
                                if (!value) {
                                    return true;
                                }
                                const dws = await digitalWorkers_Search({name: value});
                                return dws.every(s => s.id === this.parent.id);
                            },
                        })
                ),
            }),
        [digitalWorkers_Search, t]
    );

    if (!digitalWorker) {
        return null;
    }

    return <DigitalWorkerEditor value={digitalWorker} schema={DigitalWorkerUpdaterSchema} onSave={handleOnChange} />;
};
