import {FC, useCallback, useEffect, useMemo, useRef} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Toolbar from "@material-ui/core/Toolbar";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import SaveIcon from "@material-ui/icons/Save";
import AssignmentTurnedInIcon from "@material-ui/icons/AssignmentTurnedIn";
import {Form, Formik, FormikHelpers, FormikProps, useFormikContext} from "formik";
import * as yup from "yup";
import {useTranslation} from "react-i18next";
import {useSnackbar} from "notistack";
import {useNavigate} from "react-router";
import {useThrottleCallback} from "@react-hook/throttle";
import {KeyboardDatePicker} from "@material-ui/pickers";

import {DigitalWorker} from "../../api/nimflow-api";
import {ErrorBoundary} from "../../components/error-boundary";
import {DigitalWorkerSchema} from "./digital-worker-schema";
import {useConfirm} from "../../components/confirm";
import {ErrorViewer} from "../../components/error-viewer";
import {useExitWithoutSavingBlocker} from "../../utils/use-exit-without-saving-blocker";
import {useReportError} from "../../utils/use-report-error";
import {validateWithYupSchema} from "../../libs/yup";
import {validateAndSubmit} from "../../utils/formikHelpers";
import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {FormikCheckbox} from "../../components/formik-components/formik-checkbox";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        form: {
            height: "100%",
        },
        root: {
            flexGrow: 1,
        },
        toolbar: {
            minHeight: "48px",
            paddingLeft: 0,
        },
        paper: {
            padding: theme.spacing(2),
            color: theme.palette.text.secondary,
        },
        grow: {
            flexGrow: 1,
        },
        actionToolbarButtons: {
            display: "flex",
        },
        textField: {
            width: "40ch",
        },
        button: {
            margin: theme.spacing(1),
        },
        saveAsEllipsis: {
            position: "absolute",
            left: 17,
            bottom: 4,
            fontWeight: "bold",
        },
        booleanEditor: {
            marginTop: 15,
        },
    })
);

interface InnerDigitalWorkerEditorProps {
    schema?: yup.ObjectSchema<any>;
    onSave?: () => Promise<void>;
}

const InnerDigitalWorkerEditor: FC<InnerDigitalWorkerEditorProps> = props => {
    const {schema = DigitalWorkerSchema, onSave} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const navigate = useNavigate();
    const {enqueueSnackbar} = useSnackbar();
    const {
        isSubmitting,
        errors,
        validateForm,
        values: digitalWorker,
        dirty,
        setFieldValue,
    } = useFormikContext<DigitalWorker>();
    const canSave = !isSubmitting;
    const reportErrors = useReportError();

    const opts = useMemo(() => {
        return {
            description: t("ExitingDigitalWorkerEditorWithoutSavingWarning"),
        };
    }, [t]);
    useExitWithoutSavingBlocker(dirty, opts);

    const handleSaveDigitalWorkerClick = useThrottleCallback(
        useCallback(async () => {
            if (onSave) {
                await onSave();
            }
        }, [onSave])
    );

    const handleValidateDigitalWorkerClick = useCallback(async () => {
        const errorInfos = await validateWithYupSchema(schema, digitalWorker);
        await validateForm();
        if (errorInfos.length > 0) {
            reportErrors(t("DigitalWorkerHasValidationErrors"), errorInfos, {
                dialogProps: {
                    maxWidth: "lg",
                },
            });
        } else {
            enqueueSnackbar(t("DigitalWorkerIsValid"), {variant: "success"});
        }
    }, [schema, digitalWorker, validateForm, reportErrors, t, enqueueSnackbar]);

    const handleGoBack = useCallback(() => {
        navigate(-1);
    }, [navigate]);

    const handleExpiresChange = useCallback(
        (date: Date | null) => {
            setFieldValue("expires", date ? date.toISOString() : undefined);
        },
        [setFieldValue]
    );

    return (
        <Grid container spacing={3}>
            <Grid item xs={12}>
                <Toolbar className={classes.toolbar}>
                    <Tooltip title={t("GoBack") as string}>
                        <IconButton onClick={handleGoBack} disabled={isSubmitting}>
                            <ArrowBackIcon />
                        </IconButton>
                    </Tooltip>
                    <Tooltip title={t("SaveChanges") as string}>
                        <IconButton onClick={handleSaveDigitalWorkerClick} disabled={!canSave}>
                            <SaveIcon />
                        </IconButton>
                    </Tooltip>
                    <Tooltip title={t("Validate") as string}>
                        <IconButton onClick={handleValidateDigitalWorkerClick}>
                            <AssignmentTurnedInIcon />
                        </IconButton>
                    </Tooltip>
                </Toolbar>
            </Grid>
            <Grid item xs={3}>
                <FormikTextField
                    name="name"
                    label={t("Name")}
                    fullWidth
                    //className={classes.textField}
                    InputLabelProps={{required: true}}
                    required
                />
            </Grid>
            <Grid item xs={3}>
                <FormikTextField
                    name="role"
                    label={t("Role")}
                    fullWidth
                    InputLabelProps={{required: true}}
                    required
                    disabled={!!digitalWorker.id}
                />
            </Grid>
            <Grid item xs={6}></Grid>
            {!!digitalWorker.id && (
                <>
                    <Grid item xs={6}>
                        <FormikTextField
                            name="apiKey"
                            label={t("ApiKey")}
                            fullWidth
                            //InputProps={{readOnly: true}}
                        />
                    </Grid>
                    <Grid item xs={6}></Grid>
                </>
            )}
            <Grid item xs={3}>
                <KeyboardDatePicker
                    margin="none"
                    label={t("Expires")}
                    format={t("DATE_FORMAT")}
                    fullWidth
                    value={digitalWorker.expires ?? null}
                    onChange={handleExpiresChange}
                    error={!!errors.expires}
                    helperText={errors.expires}
                />
            </Grid>
            <Grid item xs={3}>
                {!!digitalWorker.id && (
                    <FormikCheckbox
                        name="active"
                        label={t("Active")}
                        inputProps={{required: true}}
                        required
                        LabelProps={{
                            className: classes.booleanEditor,
                        }}
                    />
                )}
            </Grid>
            <Grid item xs={6}></Grid>
            {/* {JSON.stringify(digitalWorker)} */}
        </Grid>
    );
};

export interface DigitalWorkerEditorProps {
    value: DigitalWorker;
    schema?: yup.ObjectSchema<any>;
    onSave: (value: DigitalWorker) => Promise<boolean>;
}

export const DigitalWorkerEditor: FC<DigitalWorkerEditorProps> = props => {
    const {value, schema = DigitalWorkerSchema, onSave} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const confirm = useConfirm();
    const {enqueueSnackbar} = useSnackbar();

    const formRef = useRef<FormikProps<DigitalWorker> | null>();

    useEffect(() => {
        formRef.current?.resetForm({values: value});
    }, [value]);

    const submitHandlerRef = useRef<(values: DigitalWorker) => Promise<void>>();

    const handleSave = useCallback(() => {
        const form = formRef.current;
        if (form && onSave) {
            submitHandlerRef.current = async (data: DigitalWorker) => {
                let error: React.ReactNode;
                try {
                    const saved = await onSave(data);
                    if (!saved) {
                        return;
                    }
                } catch (e) {
                    error = <ErrorViewer error={e} />;
                }
                if (error) {
                    await confirm({
                        title: t("ErrorSavingDigitalWorker"),
                        description: error,
                        mapCloseTo: "ok",
                        actions: [{type: "ok", text: t("Ok")}],
                    });
                } else {
                    enqueueSnackbar(t("DigitalWorkerSaved"), {variant: "success"});
                }
            };
            validateAndSubmit(form);
        }
        return Promise.resolve();
    }, [confirm, enqueueSnackbar, onSave, t]);

    const handleSubmit = useCallback(async (values: DigitalWorker, actions: FormikHelpers<DigitalWorker>) => {
        try {
            if (submitHandlerRef.current) {
                await submitHandlerRef.current(values);
                submitHandlerRef.current = undefined;
            }
        } finally {
            actions.setSubmitting(false);
        }
    }, []);

    return (
        <ErrorBoundary>
            <Formik<DigitalWorker>
                innerRef={s => (formRef.current = s)}
                initialValues={value}
                onSubmit={handleSubmit}
                validationSchema={schema}
                validateOnChange={true}
                validateOnBlur={true}
            >
                <Form autoComplete="off" className={classes.form}>
                    <InnerDigitalWorkerEditor {...props} onSave={handleSave} />
                </Form>
            </Formik>
        </ErrorBoundary>
    );
};
