import {FC, useCallback, useMemo, useState} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import MenuItem from "@material-ui/core/MenuItem";
import {useFormikContext} from "formik";
import {useTranslation} from "react-i18next";
import useSWR from "swr";
import {isPlainObject} from "is-plain-object";

import {
    FunctionSelectionDashboardParameterDataSource,
    SelectionDashboardParameter,
} from "../../api/nimflow-dashboard-api";
import {useFunctionInfos} from "../../components/formik-components/formik-function-select";
import Button from "@material-ui/core/Button";
import {FunctionDataSourceEditorDialog} from "./function-data-source-editor-dialog";
import {FunctionDataSourceData} from "./function-data-source-editor-context";
import {useKey} from "../../utils/use-key";
import {useNimflowFunctions} from "../../api/nimflow-functions-api";
import {FormikTextField} from "../../components/formik-components/formik-text-field";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        button: {
            marginLeft: 15,
        },
    })
);

const useColumnCandidates = (ds: FunctionSelectionDashboardParameterDataSource) => {
    const {evaluateArgsAndCall} = useNimflowFunctions();
    const key = useKey(ds.functionName, ds.functionName, ds.jsArguments);
    const {data, error, isValidating, mutate} = useSWR(
        key,
        async () => {
            if (!ds.moduleName || !ds.functionName) {
                return [];
            }
            const result = await evaluateArgsAndCall(ds.moduleName, ds.functionName, ds.jsArguments);
            if (!Array.isArray(result) || (result.length > 1 && !isPlainObject(result[0]))) {
                return [];
            }
            const cols = Object.keys(result[0]).sort();
            return cols;
        },
        {
            revalidateOnFocus: false,
        }
    );

    const result = useMemo(() => {
        return {
            columns: data ?? [],
            error,
            isValidating,
            mutate,
        };
    }, [data, error, isValidating, mutate]);

    return result;
};

export const FunctionDataSourceEditor: FC<{}> = props => {
    const {t} = useTranslation();
    const classes = useStyles();
    const {values: param, setFieldValue} = useFormikContext<SelectionDashboardParameter>();
    const ds = param.dataSource as FunctionSelectionDashboardParameterDataSource;
    const {functionInfos, isValidating} = useFunctionInfos();
    const [fndata, setFndata] = useState<FunctionDataSourceData>();
    const {columns} = useColumnCandidates(ds);

    const functionInfo = useMemo(() => {
        const key = `${ds.moduleName}/${ds.functionName}`;
        return functionInfos[key];
    }, [ds.functionName, ds.moduleName, functionInfos]);

    const handleEditFunctionData = useCallback(() => {
        setFndata({
            moduleName: ds.moduleName,
            functionName: ds.functionName,
            jsArguments: ds.jsArguments,
        });
    }, [ds.functionName, ds.jsArguments, ds.moduleName]);

    const handleFunctionDataChange = useCallback(
        (data: FunctionDataSourceData) => {
            setFieldValue("dataSource.moduleName", data.moduleName);
            setFieldValue("dataSource.functionName", data.functionName);
            setFieldValue("dataSource.jsArguments", data.jsArguments);
        },
        [setFieldValue]
    );

    return (
        <>
            <Grid item xs={12}>
                {(!isValidating || Object.keys(functionInfos).length > 0) && (
                    <>
                        {functionInfo ? `${functionInfo.moduleName}.${functionInfo.name}` : t("NoFunctionSelected")}
                        <Button variant="outlined" onClick={handleEditFunctionData} className={classes.button}>
                            {t(functionInfo ? "EditFunction" : "SelectFunction")}
                        </Button>
                    </>
                )}
            </Grid>
            <Grid item xs={12}>
                <FormikTextField
                    select
                    name="dataSource.columns.value"
                    label={t("Value")}
                    InputLabelProps={{required: true}}
                    fullWidth
                    required
                >
                    {columns!.map(s => (
                        <MenuItem key={s} value={s}>
                            {s}
                        </MenuItem>
                    ))}
                </FormikTextField>
            </Grid>
            <Grid item xs={12}>
                <FormikTextField
                    select
                    name="dataSource.columns.label"
                    label={t("Label")}
                    fullWidth
                    controlValueAdapter={s => (s !== "" ? s : undefined)}
                >
                    <MenuItem key="0" value={""}>
                        &nbsp;
                    </MenuItem>
                    {columns!.map(s => (
                        <MenuItem key={s} value={s}>
                            {s}
                        </MenuItem>
                    ))}
                </FormikTextField>
            </Grid>
            {!!fndata && (
                <FunctionDataSourceEditorDialog
                    open={!!fndata}
                    value={fndata}
                    onChange={handleFunctionDataChange}
                    onClose={() => setFndata(undefined)}
                />
            )}
        </>
    );
};
