import {useEffect, useMemo} from "react";

function createRootElement(id: string): HTMLElement {
    const rootContainer = document.createElement("div");
    rootContainer.setAttribute("id", id);
    return rootContainer;
}

function ensureRootElement(id: string): HTMLElement {
    const elem = createRootElement(id);
    document.body.insertBefore(elem, document.body.lastElementChild?.nextElementSibling ?? null);
    return elem;
}

/**
 * Hook to create a React Portal.
 * Automatically handles creating and tearing-down the root elements (no SRR
 * makes this trivial), so there is no need to ensure the parent target already
 * exists.
 * @example
 * const target = usePortal(id, [id]);
 * return createPortal(children, target);
 * @param {String} id The id of the target container, e.g 'modal' or 'spotlight'
 * @returns {HTMLElement} The DOM node to use as the Portal target.
 */
function usePortal(id: string): HTMLElement {
    const rootElem = useMemo<HTMLElement>(() => document.createElement("div"), []);

    useEffect(
        function setupElement() {
            const providedParent = document.querySelector(`#${id}`);
            const parentElem = providedParent instanceof HTMLElement ? providedParent : ensureRootElement(id);

            parentElem.appendChild(rootElem);

            return function removeElement() {
                rootElem.remove();
                if (parentElem.childNodes.length <= 0) {
                    parentElem.remove();
                }
            };
        },
        [rootElem, id]
    );

    return rootElem;
}

export default usePortal;
