import {ActionType} from "../../../api/nimflow-api";
import {ContextTypeNodeModel} from "./context-type-node-model";

export class ActionTypeNodeModel extends ContextTypeNodeModel<Partial<ActionType>> {
    constructor(instance: Partial<ActionType> = {}, instanceId: string, public isInitialAction: boolean = false) {
        super("actionType", instance, instanceId, instance.name ?? "");
    }

    public serialize(): any {
        return {
            ...super.serialize(),
            isInitialAction: this.isInitialAction,
        }
    }

    public hasInPort() {
        return false;
    }
}
