import React, {FC, useMemo, useEffect, useCallback} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import clsx from "clsx";
import Tooltip from "@material-ui/core/Tooltip";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import Chip from "@material-ui/core/Chip";
import Typography from "@material-ui/core/Typography";
import {useTranslation} from "react-i18next";
import {useFormContext} from "react-hook-form";
import {usePopupState, bindMenu, bindTrigger} from "material-ui-popup-state/hooks";

import {ContextType, ActionType, TaskType} from "../../api/nimflow-api";
import {StandardSection} from "../../components/standard-section";
import {TaggingTypeIcon, useTaggings, getTaggingDescription, taggingType, TaggingType} from "./taggings";
import {FormErrorViewer} from "../../components/form-error-viewer";
import {StandardSectionHeader} from "../../components/standard-section-header";
import {inferStateNames} from "../../api/nimflow-api-extensions";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        textField: {
            width: "20ch",
        },
        actionHeading: {
            //fontSize: theme.typography.pxToRem(15),
            //fontWeight: theme.typography.fontWeightRegular,
            "&.highlighted": {
                textDecoration: "underline",
            },
        },
        name: {
            maxWidth: "20ch",
            maxHeight: "10ch",
            overflowY: "auto",
            wordWrap: "break-word",
        },
        jsExpression: {
            maxWidth: "40ch",
            overflowX: "hidden",
            whiteSpace: "nowrap",
            textOverflow: "ellipsis",
        },
        chips: {
            marginTop: 5,
            display: "flex",
            flexWrap: "wrap",
            alignItems: "center",
            "& > *": {
                margin: theme.spacing(0.5),
            },
        },
        tagging: {
            width: 16,
            height: 16,
        },
        fixHeight: {
            height: 16,
            paddingLeft: 4,
        },
    })
);

export interface ContextTypeStateEditorProps {
    currentActionTypeName?: string;
}

interface StateEntry {
    name: string;
}

const determineEntries = (actions: Partial<ActionType>[], tasks: Partial<TaskType>[]): StateEntry[] => {
    const inferredEntries = inferStateNames(actions, tasks)
        .sort()
        .map(s => ({name: s}));
    return inferredEntries;
};

interface StateEntryViewerProps {
    name: string;
    tagging: TaggingType;
    highlighted: boolean;
    onChange?: (name: string, tagging: TaggingType) => void;
}

const StateEntryViewer: FC<StateEntryViewerProps> = props => {
    const {name, tagging, highlighted, onChange} = props;
    const classes = useStyles();
    const popupState = usePopupState({
        variant: "popover",
        popupId: "taggingMenu",
    });

    const handleClick = useCallback(
        (tagging: TaggingType) => {
            popupState.close();
            onChange?.(name, tagging);
        },
        [name, onChange, popupState]
    );

    return (
        <>
            <Chip
                label={name}
                //size="small"
                variant="outlined"
                title={name}
                icon={
                    <Tooltip title={getTaggingDescription(tagging)}>
                        <div className={classes.fixHeight}>
                            <TaggingTypeIcon taggingType={tagging} className={classes.tagging} />
                        </div>
                    </Tooltip>
                }
                className={clsx(classes.name, classes.actionHeading, {
                    highlighted,
                })}
                {...bindTrigger(popupState)}
            />
            <Menu {...bindMenu(popupState)}>
                {[undefined, ...taggingType].map((type, i) => {
                    return (
                        <MenuItem key={i} onClick={() => handleClick(type)}>
                            <ListItemIcon>
                                <TaggingTypeIcon taggingType={type} fontSize="small" />
                            </ListItemIcon>
                            <Typography variant="inherit">{getTaggingDescription(type)}</Typography>
                        </MenuItem>
                    );
                })}
            </Menu>
        </>
    );
};

export const ContextTypeStateEditor: FC<ContextTypeStateEditorProps> = props => {
    const {currentActionTypeName} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const {watch, errors} = useFormContext<ContextType>();
    const actions = watch("actions");
    const tasks = watch("tasks");
    const {getTaggingType, setTaggingType, removeInvalidTaggings} = useTaggings();

    const entries = useMemo(() => {
        return determineEntries(actions, tasks);
    }, [actions, tasks]);
    const highlighted = useMemo(() => {
        const action = actions.find(s => s.name === currentActionTypeName);
        return Object.keys(action?.jsSetters ?? {});
    }, [actions, currentActionTypeName]);

    useEffect(() => {
        if (actions && tasks) {
            removeInvalidTaggings();
        }
    }, [actions, removeInvalidTaggings, tasks]);

    const handleTaggingChange = useCallback(
        (name: string, tagging: TaggingType) => {
            setTaggingType(name, tagging);
        },
        [setTaggingType]
    );

    return (
        <StandardSection>
            <StandardSectionHeader title={t("Fields")} />
            <div className={classes.chips}>
                {entries.map(s => {
                    return (
                        <StateEntryViewer
                            key={s.name}
                            name={s.name}
                            tagging={getTaggingType(s.name)}
                            highlighted={highlighted.includes(s.name)}
                            onChange={handleTaggingChange}
                        />
                    );
                })}
            </div>
            <FormErrorViewer errors={errors} names={["milestones", "tags"]} />
        </StandardSection>
    );
};
