import React, {FC} from "react";
import {makeStyles, Theme, createStyles} from "@material-ui/core/styles";
import clsx from "clsx";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import CancelIcon from "@material-ui/icons/Cancel";
import ErrorIcon from "@material-ui/icons/Error";
import WarningIcon from "@material-ui/icons/Warning";

const IconMap = {
    success: CheckCircleIcon,
    error: CancelIcon,
    info: ErrorIcon,
    warning: WarningIcon,
} as const;

export type ResultStatusType = keyof typeof IconMap;

const iconColor: Record<ResultStatusType, string> = {
    success: "#52c41a",
    error: "#ff4d4f",
    info: "#1890ff",
    warning: "#faad14",
};

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            padding: "48px 32px",
        },
        iconContainer: {
            marginBottom: 24,
            textAlign: "center",
        },
        icon: {
            fontSize: 72,
            color: (props: ResultProps) => (props.status ? iconColor[props.status] : undefined),
        },
        title: {
            color: theme.palette.text.primary, // @heading-color,
            fontSize: 24,
            lineHeight: 1.8,
            textAlign: "center",
        },
        subtitle: {
            color: theme.palette.text.secondary, // @text-color-secondary;
            fontSize: 14,
            lineHeight: 1.6,
            textAlign: "center",
        },
        extra: {
            margin: "24px 0 0", 
            textAlign: "center",
            "> *": {
                marginRight: 8,
                "&:last-child": {
                    marginRight: 0,
                },
            },
        },
        content: {
            marginTop: 24,
            padding: "24px 40px",
            backgroundColor: theme.palette.background.paper,
        },
    })
);

export interface ResultProps {
    icon?: React.ReactNode;
    status?: ResultStatusType;
    title?: React.ReactNode;
    subTitle?: React.ReactNode;
    extra?: React.ReactNode;
    className?: string;
    style?: React.CSSProperties;
}

export const Result: FC<ResultProps> = props => {
    const {className, icon, extra, subTitle, title, style, children, status} = props;
    const classes = useStyles(props);
    const iconNode = icon ?? React.createElement(IconMap[status as ResultStatusType], {className: classes.icon});
    return (
        <div className={clsx(classes.root, className)} style={style}>
            <div className={classes.iconContainer}>{iconNode}</div>
            <div className={classes.title}>{title}</div>
            {subTitle && <div className={classes.subtitle}>{subTitle}</div>}
            {extra && <div className={classes.extra}>{extra}</div>}
            {children && <div className={classes.content}>{children}</div>}
        </div>
    );
};
