import React, {FC, useCallback} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Toolbar from "@material-ui/core/Toolbar";
import Box from "@material-ui/core/Box";
import {useTranslation} from "react-i18next";
import {useFormContext, Controller} from "react-hook-form";

import {JsExpressionEditor} from "../../components/jsexpression-editor";
import {TaskType} from "../../api/nimflow-api";
import {useContextTypeContext} from "./context-type-context";
import {ErrorInfo} from "../../models/error-info";
import {validateWithYupSchema} from "../../libs/yup";
import {jsCommonScriptKey, TaskTypeSchema} from "../../models/context-type-schema";
import {freeVarDefs} from "./free-variable-defs";
import {MarkdownEditor} from "../../components/markdown-editor";

const useStyles = makeStyles((_theme: Theme) =>
    createStyles({
        textField: {
            width: "40ch",
        },
    })
);

const jsConditionFreeVariables = [freeVarDefs.state] ;
const jsAssignedToFreeVariables = [freeVarDefs.state, freeVarDefs.task];
const jsDueDateValidatorFreeVariables = [freeVarDefs.state, freeVarDefs.task];
const jsImportanceFreeVariables = [freeVarDefs.state, freeVarDefs.task];

export const TaskTypeHeaderEditor: FC<{}> = () => {
    const {t: origT} = useTranslation();
    const t = useCallback((s: string | undefined | null) => origT(s ?? ""), [origT]);
    const classes = useStyles();
    const {control, getValues, errors} = useFormContext<TaskType>();
    const {jsCommon} = useContextTypeContext();
    const jsConditionValidator = useCallback(
        (js: string | undefined): Promise<ErrorInfo[]> => {
            return validateWithYupSchema(TaskTypeSchema.fields?.jsCondition, js, {
                context: {[jsCommonScriptKey]: jsCommon},
            });
        },
        [jsCommon]
    );
    const jsAssignedToValidator = useCallback(
        (js: string | undefined): Promise<ErrorInfo[]> => {
            return validateWithYupSchema(TaskTypeSchema.fields?.jsAssignedTo, js, {
                context: {[jsCommonScriptKey]: jsCommon},
            });
        },
        [jsCommon]
    );
    const jsDueDateValidator = useCallback(
        (js: string | undefined): Promise<ErrorInfo[]> => {
            return validateWithYupSchema(TaskTypeSchema.fields?.jsDueDate, js, {
                context: {[jsCommonScriptKey]: jsCommon},
            });
        },
        [jsCommon]
    );
    const jsImportanceValidator = useCallback(
        (js: string | undefined): Promise<ErrorInfo[]> => {
            return validateWithYupSchema(TaskTypeSchema.fields?.jsImportance, js, {
                context: {[jsCommonScriptKey]: jsCommon},
            });
        },
        [jsCommon]
    );

    return (
        <>
            <Toolbar>
                <Typography variant="h6">{t("Task")}</Typography>
            </Toolbar>
            <Box pl={3} pr={3}>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Controller
                            as={TextField}
                            control={control}
                            name="name"
                            label={t("Name")}
                            className={classes.textField}
                            autoFocus
                            InputLabelProps={{required: true}}
                            error={!!errors.name}
                            helperText={t(errors.name?.message)}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <Controller
                            as={TextField}
                            control={control}
                            name="titleTemplate"
                            defaultValue={getValues("titleTemplate") ?? ""}
                            label={t("TitleTemplate")}
                            fullWidth
                            InputLabelProps={{required: true}}
                            placeholder={t("EnterHandlebarsTemplate")}
                            error={!!errors.titleTemplate}
                            helperText={t(errors.titleTemplate?.message)}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <Controller
                            as={MarkdownEditor}
                            control={control}
                            name="settings.taskDetailsTemplate"
                            defaultValue={getValues("settings.taskDetailsTemplate") ?? ""}
                            label={t("TaskDetailsTemplate")}
                            fullWidth
                            placeholder={t("EnterHandlebarsTemplate")}
                            error={!!errors.settings?.taskDetailsTemplate}
                            helperText={t(errors.settings?.taskDetailsTemplate?.message)}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <Controller
                            as={JsExpressionEditor}
                            control={control}
                            name="jsCondition"
                            defaultValue={getValues("jsCondition") ?? ""}
                            label={t("JsCondition")}
                            fullWidth
                            placeholder={t("EnterBooleanExpression")}
                            error={!!errors.jsCondition}
                            helperText={t(errors.jsCondition?.message)}
                            validator={jsConditionValidator}
                            freeVariables={jsConditionFreeVariables}
                            returnType="boolean"
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <Controller
                            as={JsExpressionEditor}
                            control={control}
                            name="jsAssignedTo"
                            defaultValue={getValues("jsAssignedTo") ?? ""}
                            label={t("JsAssignedTo")}
                            fullWidth
                            placeholder={t("EnterStringExpression")}
                            error={!!errors.jsAssignedTo}
                            helperText={t(errors.jsAssignedTo?.message)}
                            validator={jsAssignedToValidator}
                            freeVariables={jsAssignedToFreeVariables}
                            returnType="string"
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <Controller
                            as={JsExpressionEditor}
                            control={control}
                            name="jsDueDate"
                            defaultValue={getValues("jsDueDate") ?? ""}
                            label={t("JsDueDate")}
                            fullWidth
                            placeholder={t("EnterDateExpression")}
                            error={!!errors.jsDueDate}
                            helperText={t(errors.jsDueDate?.message)}
                            validator={jsDueDateValidator}
                            freeVariables={jsDueDateValidatorFreeVariables}
                            returnType="Date | string"
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <Controller
                            as={JsExpressionEditor}
                            control={control}
                            name="jsImportance"
                            defaultValue={getValues("jsImportance") ?? ""}
                            label={t("JsImportance")}
                            fullWidth
                            placeholder={t("EnterImportanceExpression")}
                            error={!!errors.jsImportance}
                            helperText={t(errors.jsImportance?.message)}
                            validator={jsImportanceValidator}
                            freeVariables={jsImportanceFreeVariables}
                            returnType="number | string"
                        />
                    </Grid>
                </Grid>
            </Box>
        </>
    );
};
