import {FC, useMemo, useCallback, useState} from "react";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import {useTranslation} from "react-i18next";
import {Components, Column, Action} from "@material-table/core";
import i18next from "i18next";
import {useFormikContext} from "formik";
import {clone} from "ramda";
import {isPresent} from "ts-is-present";

import {BaseTableProps, BaseTable} from "../../components/base-table";
import {
    DashboardWidgetDataSource,
    DashboardWidgetQueryDataSource,
    DashboardWidgetFunctionDataSource,
    DashboardWidgetDefinition,
} from "../../api/nimflow-dashboard-widgets-api";
import {useOkCancelConfirm} from "../../components/confirm";
import {DashboardWidgetDataSourceEditorDialog} from "./dashboard-widget-data-source-editor-dialog";
import {SimplePopupSelectorOption, SimplePopupSelector} from "../../components/simple-popup-selector";
import {FormikErrorViewer} from "../../components/formik-error-viewer";
import {useFieldArray} from "../../components/formik-components/use-field-array";

export interface WidgetDefinitionDataSourcesEditorProps {
    path: string;
    defaultValue?: Partial<DashboardWidgetDataSource>[];
}

const options: BaseTableProps<any>["options"] = {
    search: false,
};

const tableComponents: Components = {
    Container: props => <div {...props} />,
};

const columns: Column<Partial<DashboardWidgetDataSource>>[] = [
    {field: "name", title: i18next.t("Name")},
    {field: "type", title: i18next.t("Type"), render: data => i18next.t(`DashboardWidgetDataSource_type_${data.type}`)},
    {field: "sourceCollection", title: i18next.t("SourceCollection")},
];

const buildAddAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: AddIcon,
        tooltip: i18next.t("AddWidgetDefinitionDataSource"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const adaptHandler = <T extends object>(handler: (info: T) => void): Action<T>["onClick"] => {
    return (_, info) => {
        if (Array.isArray(info)) {
            info = info[0];
        }
        handler(info);
    };
};

const buildEditAction = (handler: (dataSource: Partial<DashboardWidgetDataSource>) => void, disabled?: boolean) => {
    const action: Action<Partial<DashboardWidgetDataSource>> = {
        icon: EditIcon,
        tooltip: i18next.t("Edit"),
        onClick: adaptHandler(handler),
        disabled,
    };
    return action;
};

const buildDeleteAction = (handler: (dataSource: Partial<DashboardWidgetDataSource>) => void, disabled?: boolean) => {
    const action: Action<Partial<DashboardWidgetDataSource>> = {
        icon: DeleteIcon,
        tooltip: i18next.t("Delete"),
        onClick: adaptHandler(handler),
        disabled,
    };
    return action;
};

interface DataSourceEditionInfo {
    dataSource: Partial<DashboardWidgetDataSource>;
    index?: number;
}

const instantiateNewDataSource = (type: DashboardWidgetDataSource["type"]): Partial<DashboardWidgetDataSource> => {
    switch (type) {
        case "query": {
            const result: Partial<DashboardWidgetQueryDataSource> = {
                type,
                name: "",
                sourceCollection: "contexts",
                query: {
                    type: "CalculateTable",
                },
            };
            return result;
        }
        case "function":
            const result: Partial<DashboardWidgetFunctionDataSource> = {
                type,
                moduleName: "",
                functionName: "",
                jsArguments: "args = {\n\n};",
            };
            return result;
    }
    return {type};
};

const dataSourceTypes: SimplePopupSelectorOption<DashboardWidgetDataSource["type"]>[] = (
    ["query", "function"] as const
).map(s => ({
    value: s,
    description: i18next.t(`DashboardWidgetDataSource_type_${s}`),
}));

export const WidgetDefinitionDataSourcesEditor: FC<WidgetDefinitionDataSourcesEditorProps> = props => {
    const {path} = props;
    const {t} = useTranslation();
    const confirm = useOkCancelConfirm();
    const [addingDataSource, setAddingDataSource] = useState(false);
    const [dataSourceEditionInfo, setDataSourceEditionInfo] = useState<DataSourceEditionInfo>();
    const {errors} = useFormikContext<Partial<DashboardWidgetDefinition>>();
    const [field, , helpers] = useFieldArray<Partial<DashboardWidgetDataSource>>({name: "dataSources"});
    const dataSources = field.value;
    const {push: dataSourcesAppend, remove: dataSourcesRemove, setAt: dataSourcesSetAt} = helpers;

    const addAction = useMemo(() => {
        return buildAddAction(() => {
            setAddingDataSource(true);
        });
    }, []);

    const handleDataSourceTypeSelected = useCallback((type: DashboardWidgetDataSource["type"]) => {
        setTimeout(() => {
            setDataSourceEditionInfo({
                dataSource: instantiateNewDataSource(type),
            });
        }, 0);
    }, []);

    const handleCloseDataSourceTypeSelector = useCallback(() => {
        setAddingDataSource(false);
    }, []);

    const handleDataSourceChanged = useCallback(
        (value: DashboardWidgetDataSource) => {
            if (!dataSourceEditionInfo) {
                return;
            }
            if (isPresent(dataSourceEditionInfo.index)) {
                dataSourcesSetAt(dataSourceEditionInfo.index, value);
            } else {
                dataSourcesAppend(value);
            }
        },
        [dataSourceEditionInfo, dataSourcesAppend, dataSourcesSetAt]
    );

    const handleCloseDataSourceEditor = useCallback(() => {
        setDataSourceEditionInfo(undefined);
    }, []);

    const rowActions = useMemo(() => {
        return [
            buildEditAction(row => {
                setDataSourceEditionInfo({
                    dataSource: clone(row),
                    index: dataSources!.indexOf(row),
                });
            }),
            buildDeleteAction(async row => {
                let result = await confirm({
                    description: t("ConfirmDeleteWidgetDefinitionDataSource", {name: row.name}),
                });
                if (result) {
                    const index = dataSources!.indexOf(row);
                    if (index >= 0) {
                        dataSourcesRemove(index);
                    }
                }
            }),
        ];
    }, [confirm, dataSourcesRemove, dataSources, t]);

    const actions = useMemo(() => {
        return [addAction, ...rowActions];
    }, [addAction, rowActions]);

    return (
        <>
            <BaseTable
                columns={columns}
                data={dataSources}
                title={" "}
                actions={actions}
                components={tableComponents}
                options={options}
            />
            <FormikErrorViewer errors={errors} names={path} />
            {addingDataSource && (
                <SimplePopupSelector
                    options={dataSourceTypes}
                    title={t("SelectingDataSourceType")}
                    open={addingDataSource}
                    onClose={handleCloseDataSourceTypeSelector}
                    onSelected={handleDataSourceTypeSelected}
                />
            )}
            {!!dataSourceEditionInfo && (
                <DashboardWidgetDataSourceEditorDialog
                    defaultValue={dataSourceEditionInfo.dataSource as DashboardWidgetDataSource}
                    existingDataSources={dataSources as DashboardWidgetDataSource[]}
                    open={!!dataSourceEditionInfo}
                    onClose={handleCloseDataSourceEditor}
                    onChange={handleDataSourceChanged}
                />
            )}
        </>
    );
};
