import {FC, useState, useEffect} from "react";
import {VisualizationSpec} from "react-vega";

import {DashboardWidgetVegaLiteVisualization} from "../../api/nimflow-dashboard-widgets-api";
import {VegaLiteViewer} from "../../components/vega-lite-viewer";
import {evaluateScript} from "../../utils/scripting";

export interface DashboardWidgetVegaLiteVisualizationPreviewerProps {
    visualization: DashboardWidgetVegaLiteVisualization;
    requiredParamsHaveValue: boolean;
    args: Record<string, any>;
    data?: Record<string, any>;
}

export const DashboardWidgetVegaLiteVisualizationPreviewer: FC<DashboardWidgetVegaLiteVisualizationPreviewerProps> =
    props => {
        const {visualization, requiredParamsHaveValue, args, data} = props;
        const [spec, setSpec] = useState<VisualizationSpec>();

        useEffect(() => {
            if (!requiredParamsHaveValue) {
                return;
            }
            const outputValues = {
                vegaLiteSpec: undefined,
            };
            evaluateScript(visualization.jsSpecification, {
                inputValues: {params: args},
                outputValues,
            });
            setSpec(outputValues.vegaLiteSpec);
        }, [args, requiredParamsHaveValue, visualization.jsSpecification]);

        if (!spec) {
            return null;
        }

        return <VegaLiteViewer data={data} spec={spec} variant={visualization.variant} />;
    };
