import React, {FC, useCallback, useMemo, useState} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import useSWR from "swr";
import {useNavigate} from "react-router";
import Alert from "@material-ui/lab/Alert";
import RefreshIcon from "@material-ui/icons/Refresh";
import AddIcon from "@material-ui/icons/Add";
import AddAPhotoIcon from "@material-ui/icons/AddAPhoto";
import EditIcon from "@material-ui/icons/Edit";
import SaveAltIcon from "@material-ui/icons/SaveAlt";
import DeleteIcon from "@material-ui/icons/Delete";
import OpenInBrowserIcon from "@material-ui/icons/OpenInBrowser";
import {Column, Action} from "@material-table/core";
import i18next from "i18next";
import {useTranslation} from "react-i18next";
import {useSnackbar} from "notistack";
import {useSearchParams} from "react-router-dom";

import {useNimflow} from "../../api/nimflow-api-provider";
import MaterialTable from "../../libs/material-table";
import {downloadJson} from "../../utils/io-helpers";
import {exportSimulationStateToJson} from "./simulation-state-exporter";
import {useOkCancelConfirm} from "../../components/confirm";
import {useNimflowSimulation} from "../../api/nimflow-simulation-api";
import {useNimflowAuthorization} from "../../api/nimflow-authorization";
import {ContextTypeSelector} from "./context-type-selector";
import {useKey} from "../../utils/use-key";
import {ErrorBoundary} from "../../components/error-boundary";
import {SimulationStateSelector} from "./simulation-data-selector/simulation-data-selector";
import {SimulationData} from "./context-simulation-generator-from-context";

const useStyles = makeStyles((_theme: Theme) =>
    createStyles({
        fileInput: {
            opacity: 0,
            position: "absolute",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            width: "100%",
        },
    })
);

const columns: Column<any>[] = [
    {field: "name", title: i18next.t("Name")},
    {field: "description", title: i18next.t("Description")},
    {field: "contextTypeName", title: i18next.t("ContextTypeName")},
];

interface ContextSimulationInfo {
    id: string;
    name: string;
    description?: string;
}

export interface SimulatorProps {}

const buildRefreshAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: RefreshIcon,
        tooltip: i18next.t("RefreshData"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const buildAddAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: AddIcon,
        tooltip: i18next.t("AddContextSimulation"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const buildAddFromContextAction = (handler: () => void) => {
    const action: Action<any> = {
        icon: AddAPhotoIcon,
        tooltip: i18next.t("AddContextSimulationFromContext"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const buildImportAction = (icon: Action<any>["icon"], handler: () => void) => {
    const action: Action<any> = {
        icon,
        tooltip: i18next.t("ImportContextSimulation"),
        isFreeAction: true,
        onClick: handler,
    };
    return action;
};

const adaptHandler = <T extends object>(handler: (info: T) => void): Action<T>["onClick"] => {
    return (_, info) => {
        if (Array.isArray(info)) {
            info = info[0];
        }
        handler(info);
    };
};

const buildEditAction = (handler: (info: ContextSimulationInfo) => void, disabled?: boolean) => {
    const action: Action<ContextSimulationInfo> = {
        icon: EditIcon,
        tooltip: i18next.t("Edit"),
        onClick: adaptHandler(handler),
        disabled,
    };
    return action;
};

const buildExportAction = (handler: (info: ContextSimulationInfo) => void) => {
    const action: Action<ContextSimulationInfo> = {
        icon: SaveAltIcon,
        tooltip: i18next.t("Export"),
        onClick: adaptHandler(handler),
    };
    return action;
};

const buildDeleteAction = (handler: (info: ContextSimulationInfo) => void, disabled?: boolean) => {
    const action: Action<ContextSimulationInfo> = {
        icon: DeleteIcon,
        tooltip: i18next.t("Delete"),
        onClick: adaptHandler(handler),
        disabled,
    };
    return action;
};

const useSimulations = (contextTypeName?: string) => {
    const {canReadSimulations} = useNimflowAuthorization();
    const {simulations_GetInfos} = useNimflowSimulation();
    const {t} = useTranslation();
    const {enqueueSnackbar} = useSnackbar();

    const queryKey = useKey(simulations_GetInfos);
    const {data, error, mutate, isValidating} = useSWR(
        [queryKey, contextTypeName],
        async (_key: string, contextTypeName: string | undefined) => {
            if (!canReadSimulations()) {
                enqueueSnackbar(t("CannotAccessServerSimulations"), {
                    variant: "warning",
                });
                return [];
            }
            const result: ContextSimulationInfo[] = await simulations_GetInfos({
                contextTypeName,
            });
            return result;
        },
        {revalidateOnFocus: false}
    );
    return useMemo(() => {
        return {
            data: data ?? [],
            isValidating,
            error,
            revalidate: mutate,
        };
    }, [data, isValidating, mutate, error]);
};

export const Simulator: FC<SimulatorProps> = () => {
    const {t} = useTranslation();
    const [searchParams] = useSearchParams();
    const contextTypeName = searchParams.get("contextTypeName") ?? undefined;
    const navigate = useNavigate();
    const classes = useStyles();
    const confirm = useOkCancelConfirm();
    const {contextTypes_FindOne} = useNimflow();
    const {canUpdateSimulations, canDeleteSimulations} = useNimflowAuthorization();
    const {simulations_FindOne, simulations_Delete} = useNimflowSimulation();
    const [selectingContextType, setSeletingContextType] = useState(false);
    const [selectingContextId, setSeletingContextId] = useState(false);
    const {data, error, revalidate, isValidating} = useSimulations(contextTypeName);

    const handleFileUploaded = useCallback(
        async (e: React.ChangeEvent<HTMLInputElement>) => {
            const file = e.target?.files?.[0];
            if (file?.type.match(/.+\/json/)) {
                const url = URL.createObjectURL(file);
                navigate(`import/${btoa(url)}`);
            }
        },
        [navigate]
    );

    const refreshAction = useMemo(() => {
        return buildRefreshAction(revalidate);
    }, [revalidate]);

    const handleOnCloseContextTypeSelector = useCallback(() => {
        setSeletingContextType(false);
    }, []);

    const handleContextTypeNameSelected = useCallback(
        (contextTypeName: string) => {
            navigate(`new/${contextTypeName}`);
        },
        [navigate]
    );

    const addAction = useMemo(() => {
        return buildAddAction(() => {
            if (contextTypeName) {
                navigate(`new/${contextTypeName}`);
            } else {
                setSeletingContextType(true);
            }
        });
    }, [contextTypeName, navigate]);

    const handleOnCloseContextIdSelector = useCallback(() => {
        setSeletingContextId(false);
    }, []);

    const addFromContextAction = useMemo(() => {
        return buildAddFromContextAction(() => {
            setSeletingContextId(true);
        });
    }, []);

    const handleSimulationDataSelected = useCallback(
        (simulationData: SimulationData) => {
            const blob = new Blob([JSON.stringify(simulationData, null, 2)]);
            const url = URL.createObjectURL(blob);
            navigate(`generateFromContext/${btoa(url)}`);
        },
        [navigate]
    );

    const importAction = useMemo(() => {
        return buildImportAction(
            () => (
                <>
                    <OpenInBrowserIcon />
                    <input
                        type="file"
                        title=""
                        accept=".simulation-state.json"
                        className={classes.fileInput}
                        onChange={handleFileUploaded}
                    />
                </>
            ),
            () => {}
        );
    }, [classes.fileInput, handleFileUploaded]);

    const rowActions = useMemo(() => {
        return [
            buildEditAction(row => {
                navigate(`edit/${row.id}`);
            }, !canUpdateSimulations()),
            buildExportAction(async row => {
                const sim = await simulations_FindOne(row.id);
                const ct = sim.contextType ?? (await contextTypes_FindOne({name: sim.contextTypeName!}));
                downloadJson(exportSimulationStateToJson(sim, ct), `${ct.name}.simulation-state.json`);
            }),
            buildDeleteAction(async row => {
                let result = await confirm({description: t("ConfirmDeleteContextSimulation", {name: row.name})});
                if (result) {
                    result = (await simulations_Delete(row.id)).deletedCount > 0;
                    revalidate();
                }
                return result;
            }, !canDeleteSimulations()),
        ];
    }, [
        canUpdateSimulations,
        canDeleteSimulations,
        navigate,
        simulations_FindOne,
        contextTypes_FindOne,
        confirm,
        t,
        simulations_Delete,
        revalidate,
    ]);

    const actions = useMemo(() => {
        return [addAction, addFromContextAction, importAction, refreshAction, ...rowActions];
    }, [addAction, addFromContextAction, importAction, refreshAction, rowActions]);

    return (
        <ErrorBoundary>
            <MaterialTable columns={columns} data={data ?? []} title={t("SimulationsCatalog")} actions={actions} />
            {!isValidating && !!error && <Alert severity="error">{error.toString()}</Alert>}
            {selectingContextType && (
                <ContextTypeSelector
                    open={selectingContextType}
                    onClose={handleOnCloseContextTypeSelector}
                    onSelected={handleContextTypeNameSelected}
                />
            )}
            {selectingContextId && (
                <SimulationStateSelector
                    open={selectingContextId}
                    onClose={handleOnCloseContextIdSelector}
                    onSimulationDataSelected={handleSimulationDataSelected}
                />
            )}
        </ErrorBoundary>
    );
};

export default Simulator;
