import {useMemo} from "react";
import useSWR from "swr";
import {isPresent} from "ts-is-present";

import {ModuleFunctionInfo} from "./../../api/nimflow-functions-api";
import {DashboardWidgetDataSource} from "./../../api/nimflow-dashboard-widgets-api";
import {
    FilterTypeDescriptor,
    DashboardWidgetParameterType,
    dashboardWidgetParameterTypes,
    dashboardWidgetAggregateDataSourceSourceCollections,
    eventFilterDescriptor,
    contextFilterDescriptor,
    contextTaskFilterDescriptor,
    getScalarPropertyNamesFor,
    expressionPropertyAggregators,
    timeGranularities,
    DashboardWidgetParameter,
} from "../../api/nimflow-dashboard-widgets-api";
import {contextListPropertyNames, eventListPropertyNames} from "../../api/nimflow-api-extensions";
import {useAsync} from "react-use";
import {
    getTypingFromSchema,
    IntersectionType,
    JsonType,
    LiteralType,
    RecordType,
    TupleType,
    UnionType,
} from "../../utils/json-schema-to-typescript";
import {groupBy, mapObjIndexed} from "ramda";
import {isObjectLike} from "../../utils/types";

const extendWithParameters = (
    type: string,
    baseTypes: DashboardWidgetParameterType | DashboardWidgetParameterType[],
    widgetParams: DashboardWidgetParameter[] | undefined
) => {
    if (!widgetParams) {
        return type;
    }
    if (!Array.isArray(baseTypes)) {
        baseTypes = [baseTypes];
    }
    const names = widgetParams.filter(s => baseTypes.includes(s.type)).map(s => `"${s.name}"`);
    if (names.length === 0) {
        return type;
    }
    return `${type} | {"$parameter": ${names.join(" | ")}}`;
};

const getFilterValueTypeDefinition = (
    baseType: FilterTypeDescriptor<any>[string],
    widgetParams: DashboardWidgetParameter[] | undefined
): string => {
    function interval(type: string) {
        return `[${type} | undefined, ${type} | undefined]`;
    }

    switch (baseType) {
        case "number":
            const numberType = extendWithParameters("number", "number", widgetParams);
            return `${numberType} | ${interval(numberType)}`;
        case "Date":
            const dateType = extendWithParameters("Date | string", "datetime", widgetParams);
            return `${dateType} | ${interval(dateType)}`;
        case "string":
            return extendWithParameters("string | string[]", ["string", "stringList"], widgetParams);
        case "boolean":
            return extendWithParameters(baseType, baseType, widgetParams);
        case "string[]":
            return extendWithParameters("string[]", "stringList", widgetParams);
        case "StringMap":
            return `Record<string, ${getFilterValueTypeDefinition("string", widgetParams)}>`;
        case "NumberMap":
            return `Record<string, ${getFilterValueTypeDefinition("number", widgetParams)}>`;
        case "DateMap":
            return `Record<string, ${getFilterValueTypeDefinition("Date", widgetParams)}>`;
    }
};

const getQueryDataSourceTypings = (
    widgetParams: DashboardWidgetParameter[] | undefined,
    contextTypeNames: string[]
): [string[], string | undefined] => {
    const sourceCollectionTypes = dashboardWidgetAggregateDataSourceSourceCollections.map(s => `"${s}"`).join(" | ");

    const contextTypeNamesType = contextTypeNames
        .sort()
        .map(s => `"${s}"`)
        .join(" | ");
    const referencedTypes = `
    interface EventFilter {
        ${Object.entries(eventFilterDescriptor)
            .map(([key, value]) => `${key}?: ${getFilterValueTypeDefinition(value, widgetParams)};`)
            .join("\n")}
    }
    
    interface ContextFilter {
        ${Object.entries(contextFilterDescriptor)
            .map(([key, value]) => `${key}?: ${getFilterValueTypeDefinition(value, widgetParams)};`)
            .join("\n")}
    }
    
    interface ContextTaskFilter {
        ${Object.entries(contextTaskFilterDescriptor)
            .map(([key, value]) => `${key}?: ${getFilterValueTypeDefinition(value, widgetParams)};`)
            .join("\n")}
    }
    
    type SourceCollection = ${sourceCollectionTypes};
    
    type SourceCollectionScalarProperties<T extends SourceCollection> = T extends "contexts"
        ? ${getScalarPropertyNamesFor("contexts")
            .map(s => `"${s}"`)
            .join(" | ")}
        : T extends "tasks"
        ? ${getScalarPropertyNamesFor("tasks")
            .map(s => `"${s}"`)
            .join(" | ")}
        : T extends "events"
        ? ${getScalarPropertyNamesFor("events")
            .map(s => `"${s}"`)
            .join(" | ")}
        : never
    
    type SourceCollectionListProperties<T extends SourceCollection> = T extends "contexts"
        ? ${contextListPropertyNames.map(s => `"${s}"`).join(" | ")}
        : T extends "events"
        ? ${eventListPropertyNames.map(s => `"${s}"`).join(" | ")}
        : never
    
    type SourceCollectionListPropertyValue<T extends SourceCollection> = T extends "contexts"
        ? string
        : T extends "events"
        ? string
        : never;
    
    type EntityFilter<T extends SourceCollection> = T extends "contexts"
        ? ContextFilter
        : T extends "tasks"
        ? ContextTaskFilter
        : T extends "events"
        ? EventFilter
        : {}
    
    interface CalculateTable<T extends SourceCollection> {
        type: "CalculateTable";
        filter?: EntityFilter<T>;
        sortProperty?: SourceCollectionScalarProperties<T>;
        sortDirection?: "asc" | "desc";
        skip?: number;
        limit?: number;
    }
    
    interface Calculate<T extends SourceCollection> {
        type: "Calculate";
        filter?: EntityFilter<T>; 
        property?: SourceCollectionScalarProperties<T> | SourceCollectionListProperties<T>;
        /** When property is milestones/tags/metrics. */
        value?: SourceCollectionListPropertyValue<T>; 
        aggregation: ${expressionPropertyAggregators.map(s => `"${s}"`).join(" | ")};
    };
    
    interface SummarizeExpressionGrouping<T extends SourceCollection> {
        property: SourceCollectionScalarProperties<T> | SourceCollectionListProperties<T>;
        /** When property is milestones/tags/metrics. */
        value?: SourceCollectionListPropertyValue<T>;
        timeGranularity?: ${timeGranularities.map(s => `"${s}"`).join(" | ")};
    }
    
    interface SummarizeExpressionAggregate<T extends SourceCollection> {
        name: string;
        aggregation: ${expressionPropertyAggregators.map(s => `"${s}"`).join(" | ")};
        property?: SourceCollectionScalarProperties<T> | SourceCollectionListProperties<T>;
        /** When property is milestones/tags/metrics. */
        value?: SourceCollectionListPropertyValue<T>;
    }
    
    interface Summarize<T extends SourceCollection> {
        type: "Summarize";
        filter?: EntityFilter<T>; 
        groupings: Array<SummarizeExpressionGrouping<T>>;
        aggregates?: Array<SummarizeExpressionAggregate<T>>;
    }
    
    type QueryExpression<T extends SourceCollection> = CalculateTable<T> | Calculate<T> | Summarize<T>;`;

    const queryDataSourceTypes = [
        `{
            type: "query";
            sourceCollection: "contexts";
            contextTypeName?: ${contextTypeNamesType};
            query: QueryExpression<"contexts">;
        }`,
        `{
            type: "query";
            sourceCollection: "events";
            query: QueryExpression<"events">;
        }`,
        `{
            type: "query";
            sourceCollection: "tasks";
            query: QueryExpression<"tasks">;
        }`,
    ];
    return [queryDataSourceTypes, referencedTypes];
};

const isParameterType = (value: JsonType): value is {$parameter: UnionType} => {
    return isObjectLike(value) && "$parameter" in value && value.$parameter instanceof UnionType;
};

// Widget types: "number", "string", "boolean", "datetime", "stringList"
const injectParameterTypingsInJsonType = (
    widgetParams?: DashboardWidgetParameter[]
): ((jsonType: JsonType) => JsonType) => {
    if (!widgetParams || widgetParams.length === 0) {
        return jsonType => jsonType;
    }

    const widgetTypeMap = mapObjIndexed(
        (s: DashboardWidgetParameter[]) => new UnionType(s.map(p => new LiteralType(p.name))),
        groupBy(s => s.type !== "datetime" ? s.type : "string", widgetParams)
    );

    const injectParams = (type: JsonType): JsonType => {
        if (type === "any") {
            const allParams = Object.values(widgetTypeMap).flatMap(s => s.types);
            return new UnionType(["any", {$parameter: new UnionType(allParams)}]);
        } else if (type === "boolean" || type === "number" || type === "string") {
            const params = widgetTypeMap[type];
            if (params) {
                return new UnionType([type, {$parameter: params}]);
            }
        } else if (Array.isArray(type)) {
            if ((type[0] === "string" || type[0] === "any") && "stringList" in widgetTypeMap) {
                return new UnionType([["string"], {$parameter: widgetTypeMap["stringList"]}]);
            } else {
                return [injectParams(type[0])];
            }
        } else if (type instanceof UnionType) {
            const finalTypes: JsonType[] = [];
            const params: LiteralType[] = [];
            for (const innerType of type.types) {
                const injectedType = injectParams(innerType);
                const flattenedTypes = injectedType instanceof UnionType ? injectedType.types : [injectedType];
                for (const finalType of flattenedTypes) {
                    if (isParameterType(finalType)) {
                        for (const lit of finalType.$parameter.types) {
                            if (lit instanceof LiteralType && !params.find(s => s.value === lit.value)) {
                                params.push(lit);
                            }
                        }
                    } else if (!finalTypes.includes(finalType)) {
                        finalTypes.push(finalType);
                    }
                }
            }
            if (params.length > 0) {
                finalTypes.push({$parameter: new UnionType(params)});
            }
            return new UnionType(finalTypes);
        } else if (type instanceof IntersectionType) {
            return new IntersectionType(type.types.map(injectParams));
        } else if (type instanceof LiteralType) {
            const params = widgetTypeMap["string"];
            if (params) {
                return new UnionType([type, {$parameter: params}]);
            }
        } else if (type instanceof RecordType) {
            const declEntries = type.declaredEntries ? mapObjIndexed(injectParams, type.declaredEntries) : undefined;
            return new RecordType(injectParams(type.valueType), declEntries);
        } else if (type instanceof TupleType) {
            const additional = type.additional ? injectParams(type.additional) : undefined;
            return new TupleType(type.types.map(injectParams), additional);
        } else if (isObjectLike(type)) {
            return mapObjIndexed(injectParams, type);
        }
        return type;
    };
    return injectParams;
};

const getFunctionDataSourceTypings = async (
    widgetParams: DashboardWidgetParameter[] | undefined,
    functionInfos: Record<string, ModuleFunctionInfo>
): Promise<[string[], string | undefined]> => {
    const dataSourceTypes: string[] = [];
    for (const s of Object.values(functionInfos)) {
        let args: string = "";
        const argsTyping = s.inputSchema
            ? await getTypingFromSchema(s.inputSchema, injectParameterTypingsInJsonType(widgetParams))
            : "{}";
        args = `args?: ${argsTyping};`;
        dataSourceTypes.push(`{
            type: "function";
            functionName: "${s.moduleName}/${s.name}";
            ${args}
        }`);
    }
    // const dataSourceTypes = [
    //     `{
    //         type: "function";
    //         functionName: string;
    //         args: Record<string, any>;
    //     }`,
    // ];
    return [dataSourceTypes, undefined];
};

const getParamsTyping = (widgetParams: DashboardWidgetParameter[] | undefined) => {
    if (!widgetParams) {
        return "";
    }
    return `declare const params: {
        ${widgetParams
            .map(s => {
                let type = "any";
                switch (s.type) {
                    case "boolean":
                    case "number":
                        type = s.type;
                        break;
                    case "string":
                    case "datetime":
                        type = "string";
                        break;
                    case "stringList":
                        type = "string[]";
                        break;
                }
                let name = s.name;
                if (!s.required && type !== "any") {
                    name += "?";
                }
                return `${name}: ${type};`;
            })
            .join("\n")}
    }`;
};

const getVisualizationTypings = (dataSources: DashboardWidgetDataSource[] | undefined) => {
    const dataSourceNames = dataSources?.map(s => `"${s.name}"`).join(" | ") ?? "string";
    const result = `
    interface TabularVisualizationColumn {
        align?: "center" | "inherit" | "justify" | "left" | "right";
        cellStyle?: Records<string, any>;
        currencySetting?: {
            locale?: string;
            currencyCode?: string;
            minimumFractionDigits?: number;
            maximumFractionDigits?: number;
        };
        dateSetting?: { locale?: string; format?: string };
        defaultFilter?: any;
        defaultGroupOrder?: number;
        defaultGroupSort?: "asc" | "desc";
        defaultSort?: "asc" | "desc";
        disableClick?: boolean;
        emptyValue?: string
        export?: boolean;
        field?: string;
        filtering?: boolean;
        filterPlaceholder?: string;
        filterCellStyle?: Records<string, any>;
        grouping?: boolean;
        groupTitle?: string;
        headerStyle?: Records<string, any>;
        hiddenByColumnsButton?: boolean;
        hideFilterIcon?: boolean;
        removable?: boolean;
        resizable?: boolean;
        searchable?: boolean;
        sorting?: boolean;
        title?: string;
        tooltip?: string;
        type?:
            | "string"
            | "boolean"
            | "numeric"
            | "date"
            | "datetime"
            | "time"
            | "currency";
        width?: string | number;
    }

    interface TabularVisualizationOptions {
        columnsButton?: boolean;
        columnResizable?: boolean;
        debounceInterval?: number;
        doubleHorizontalScroll?: boolean;
        draggable?: boolean;
        emptyRowsWhenPaging?: boolean;
        filtering?: boolean;
        filterCellStyle?: Records<string, any>;
        filterRowStyle?: Records<string, any>;
        fixedColumns?: { left?: number; right?: number };
        groupRowSeparator?: string;
        header?: boolean;
        headerSelectionProps?: object;
        headerStyle?: Records<string, any>;
        hideFilterIcons?: boolean;
        initialPage?: number;
        loadingType?: "overlay" | "linear";
        maxBodyHeight?: number | string;
        minBodyHeight?: number | string;
        padding?: "default" | "dense";
        paging?: boolean;
        grouping?: boolean;
        overflowY?: "visible" | "hidden" | "scroll" | "auto" | "initial" | "inherit";
        pageSize?: number;
        pageSizeOptions?: number[];
        paginationType?: "normal" | "stepped";
        paginationPosition?: "bottom" | "top" | "both";
        rowStyle?: Records<string, any>;
        showEmptyDataSourceMessage?: boolean;
        showFirstLastPageButtons?: boolean;
        showTitle?: boolean;
        showTextRowsSelected?: boolean;
        search?: boolean;
        searchText?: string;
        searchFieldAlignment?: "left" | "right";
        searchFieldStyle?: Records<string, any>;
        searchFieldVariant?: "standard" | "filled" | "outlined";
        searchAutoFocus?: boolean;
        sorting?: boolean;
        keepSortDirectionOnColumnSwitch?: boolean;
        tableLayout?: "auto" | "fixed";
        thirdSortClick?: boolean;
        toolbar?: boolean;
        toolbarButtonAlignment?: "left" | "right";
        exportAllData?: boolean;
    }

    interface TabularVisualizationConfiguration {
        dataSourceName: ${dataSourceNames};
        columns: TabularVisualizationColumn[];
        title?: string;
        options?: TabularVisualizationOptions;
    }

    declare let visualization: {
        type: "markdown";
        template: string;
    } | {
        type: "vega-lite";
        specification: any;
        variant?: "verticalBarChart" | "focusedVerticalBarChart" | "horizontalBarChart" | "lineChart" | "areaChart" | "areaLineChart" | "pieChart" | "donutChart" | "scatterplotChart";
    } | {
        type: "tabular";
        configuration: TabularVisualizationConfiguration;
    };`;
    return result;
};

export const useWidgetDefinitionTypingsProvider = (
    widgetParams: DashboardWidgetParameter[] | undefined
): Record<string, () => string | Promise<string>> => {
    const params = useMemo(() => getParamsTyping(widgetParams), [widgetParams]);

    return {
        params: () => {
            return params;
        },
        data: () => {
            return `declare const data: any;`;
        },
        stages: () => {
            return `declare let stages: object[];`;
        },
        vegaLiteSpec: () => {
            return `declare let vegaLiteSpec: {[key: string]: any};`;
        },
    };
};

const parametersTyping = `declare let parameters: {[name: string]: {
    type: ${dashboardWidgetParameterTypes.map(s => `"${s}"`).join(" | ")};
    required: boolean;
}};`;

export const useWidgetJsSpecTypingsProvider = (
    widgetParams: DashboardWidgetParameter[] | undefined,
    dataSources: DashboardWidgetDataSource[] | undefined,
    functionInfos: Record<string, ModuleFunctionInfo>,
    getContextTypeNames: () => Promise<string[]>
) => {
    const {data: contextTypeNames} = useSWR(`getContextTypeNames`, async () => {
        return getContextTypeNames();
    });

    const visualization = useMemo(() => {
        return getVisualizationTypings(dataSources);
    }, [dataSources]);

    const {value: functionDataSourceTypings = [[], undefined]} = useAsync(async () => {
        return await getFunctionDataSourceTypings(widgetParams, functionInfos);
    }, [functionInfos, widgetParams]);

    const queryDataSourceTypings = useMemo(() => {
        return getQueryDataSourceTypings(widgetParams, contextTypeNames ?? ["string"]);
    }, [contextTypeNames, widgetParams]);

    const dataSourceTypings = useMemo(() => {
        const typingsSources = [queryDataSourceTypings, functionDataSourceTypings];
        const referencedTypes = typingsSources
            .map(s => s[1])
            .filter(isPresent)
            .join("\n\n");
        const dsTypes = typingsSources.flatMap(s => s[0]).join(" | ");
        const result = `${referencedTypes}

        declare let dataSources: {[name: string]: ${dsTypes}};`;
        return result;
    }, [functionDataSourceTypings, queryDataSourceTypings]);

    const result: Record<string, () => string | Promise<string>> = {
        parameters: () => parametersTyping,
        dataSources: () => dataSourceTypings,
        visualization: () => visualization,
    };
    return result;
};
