export const downloadJson = (json: any, name: string = "file.json") => {
    const str = JSON.stringify(json, undefined, 4);
    downloadString(str, name, "application/json");
};

export const downloadString = (str: string, name: string = "file.txt", mimeType?: string) => {
    const blob = new Blob([str]);
    downloadBlob(blob, name, mimeType);
};

export const downloadBlob = (blob: Blob, name: string = "file.txt", mimeType?: string) => {
    if ("msSaveBlob" in navigator) {
        // Prefer msSaveBlob if available - Edge supports a[download] but ignores the filename provided, using the blob UUID instead.
        // msSaveBlob will respect the provided filename
        (navigator as any).msSaveBlob(blob, name);
        return;
    }
    // Convert your blob into a Blob URL (a special url that points to an object in the browser's memory)
    const blobUrl = URL.createObjectURL(blob);
    const link = document.createElement("a");
    // Set link's href to point to the Blob URL
    link.href = blobUrl;
    link.download = name;
    if (mimeType) {
        link.type = mimeType;
    }

    // Append link to the body
    document.body.appendChild(link);
    try {
        // Dispatch click event on the link
        // This is necessary as link.click() does not work on the latest firefox
        link.dispatchEvent(
            new MouseEvent("click", {
                bubbles: true,
                cancelable: true,
                view: window,
            })
        );
    } finally {
        // Remove link from body
        document.body.removeChild(link);
        setTimeout(() => URL.revokeObjectURL(link.href), 0);
    }
};

export const readJsonFromBlob = (blob: Blob): Promise<any> => {
    return new Promise((resolve, reject) => {
        let reader = new FileReader();
        reader.onload = e => {
            const json = e.target?.result;
            if (typeof json === "string") {
                const result = JSON.parse(json);
                resolve(result);
            }
            reject(`Invalid data type in blob ${typeof json}`);
        };
        reader.onerror = e => {
            reject(`Error reading blob`);
        };
        reader.readAsText(blob);
    });
};
