import React, {FC} from "react";

import {FormikTextFieldProps, FormikTextField} from "./formik-text-field";

export type FormikNumberFieldProps = Omit<FormikTextFieldProps, "controlValueAdapter">;

const parseVal = (val: string) => {
    if (!val) {
        return undefined;
    }
    try {
        return parseInt(val);
    } catch {
        return undefined;
    }
};

export const FormikNumberField: FC<FormikNumberFieldProps> = props => {
    return <FormikTextField type="number" controlValueAdapter={parseVal} {...props} />;
};
