import * as yup from "yup";
import i18next from "i18next";
import {isPresent} from "ts-is-present";
import {uniq} from "ramda";
import {JSONSchema7} from "json-schema";
import type {SchemaOf} from "yup";

import {map} from "../../utils/map-schema";
import {isValidationFailure, validate} from "../../libs/ajv";
import {
    Importance,
    ContextType,
    SimulationEvent,
    ActionEvent,
    TaskResponseEvent,
    InitialStateEvent,
    ContextTask,
    Context,
    MilestoneState,
    TagState,
    MetricState,
    ContextSimulationState,
    JsConsoleEntry,
} from "../../api/nimflow-api";
import {isActionEvent, isResponseEvent, isInitialStateEvent} from "../../api/nimflow-api-extensions";
import {buildDefinitionSchema} from "../../utils/json-schema";
import {ContextSimulation} from "../../api/nimflow-simulation-api";

export const contextTypeKey = "contextTypeKey";

export const ActionEventSchema = yup.object().shape({
    action: yup.string().required(),
    reference: yup.string(),
    payload: map(yup.string().required(), yup.mixed()),
}) as SchemaOf<ActionEvent>;

export const TaskResponseEventSchema = yup.object().shape({
    taskTypeName: yup.string().required(),
    typeName: yup.string().required(),
    payload: map(yup.string().required(), yup.mixed()),
}) as SchemaOf<TaskResponseEvent>;

export const InitialStateEventSchema = yup.object().shape({
    reference: yup.string(),
    initialState: map(yup.string().required(), yup.mixed()).required(),
    payload: map(yup.string().required(), yup.mixed()),
}) as SchemaOf<InitialStateEvent>;

export const SimulationEventSchema = yup.lazy<SchemaOf<SimulationEvent>>((s: SimulationEvent) => {
    if (isActionEvent(s)) {
        return ActionEventSchema.required();
    }
    if (isInitialStateEvent(s)) {
        return InitialStateEventSchema.required();
    }
    return TaskResponseEventSchema.required();
}) as unknown as SchemaOf<SimulationEvent>; // as yup.ObjectSchema<yup.Shape<object | undefined, SimulationEvent>>;

export const ContextTaskSchema = yup.object().shape({
    id: yup.string().required(),
    contextId: yup.string().required(),
    contextTypeName: yup.string().required(),
    contextReference: yup.string(),
    typeName: yup.string().required(),
    title: yup.string(),
    data: map(yup.string().required(), yup.mixed()),
    labels: yup.array().of(yup.string().required()),
    dateCreated: yup.string().iso8601Formatted(), // Date in ISO 8601 format (UTC with offsets)
    dateUpdated: yup.string().iso8601Formatted(), // Date in ISO 8601 format (UTC with offsets)
    assignedTo: yup.string(),
    status: yup.string(),
    archived: yup.bool(),
    dueDate: yup.string().iso8601Formatted(), // Date in ISO 8601 format (UTC with offsets)
    importance: yup.mixed<Importance>().oneOf(Object.values(Importance).filter(s => typeof s === "number") as number[]),
    focused: yup.boolean().required(),
}) as SchemaOf<ContextTask>;

export const MilestoneStateSchema = yup.object().shape({
    name: yup.string(),
    lastReached: yup.string(),
}) as SchemaOf<MilestoneState>;

export const TagStateSchema = yup.object().shape({
    name: yup.string(),
    value: yup.string(),
}) as SchemaOf<TagState>;

export const MetricStateSchema = yup.object().shape({
    name: yup.string(),
    value: yup.number(),
}) as SchemaOf<MetricState>;

export const ContextSchema = yup.object().shape({
    id: yup.string(),
    name: yup.string(),
    typeName: yup.string().required(),
    reference: yup.string().required(),
    state: map(yup.string().required(), yup.mixed()),
    dateCreated: yup.string().iso8601Formatted(), // Date in ISO 8601 format (UTC with offsets)
    dateModified: yup.string().iso8601Formatted(), // Date in ISO 8601 format (UTC with offsets)
    version: yup.string(),
    contextTypeVersion: yup.number(),
    milestones: yup.array().of(MilestoneStateSchema),
    tags: yup.array().of(TagStateSchema),
    metrics: yup.array().of(MetricStateSchema),
}) as SchemaOf<Context>;

export const JsConsoleEntrySchema = yup.object().shape({
    type: yup.string().required(),
    values: yup.array(),
}) as SchemaOf<JsConsoleEntry>;

export const ContextSimulationStateSchema = yup.object().shape({
    event: SimulationEventSchema,
    context: ContextSchema.required(),
    tasks: yup.array().of(ContextTaskSchema.required()).defined(),
    reachedMilestones: yup.array().of(yup.string().required()),
    errors: yup.array().of(yup.string().required()),
    consoleEntries: yup.array().of(JsConsoleEntrySchema.required()),
}) as SchemaOf<ContextSimulationState>;

export const ContextSimulationSchema = yup
    .object()
    .shape({
        id: yup.string(),
        name: yup.string().required().identifier(),
        description: yup.string(),
        contextTypeName: yup.string(),
        contextType: yup.mixed(), //ContextTypeSchema,
        history: yup.array().of(ContextSimulationStateSchema.required()),
        initialState: yup.mixed(), //{[key: string]: any};
        pendingEvents: yup
            .array()
            .of(SimulationEventSchema)
            .test({
                message: "ReferenceUnique",
                test: function (value: any) {
                    const values = value as SimulationEvent[];
                    const actions = values?.filter(isActionEvent);
                    if (!actions || actions.length === 0) {
                        return true;
                    }
                    const uniqueValues = uniq(actions.map(s => s.reference).filter(isPresent));
                    if (uniqueValues.length === 0) {
                        return this.createError({
                            path: "pendingEvents",
                            message: i18next.t("ReferenceFieldIsNotDefined"),
                        });
                    }
                    if (uniqueValues.length === 1) {
                        return true;
                    }
                    return this.createError({
                        path: "pendingEvents",
                        message: i18next.t("ReferenceUnique"),
                    });
                },
            }),
    })
    .test({
        message: "PendingActionNamesValid",
        test: function (value: any) {
            const sim = value as ContextSimulation;
            const contextType = (this.options.context as any)?.[contextTypeKey] as ContextType | undefined;
            if (!contextType) {
                return true;
            }
            const validNames = contextType.actions.map(s => s.name);
            const invalidActions = sim.pendingEvents
                .filter(isActionEvent)
                .map(s => s.action)
                .filter(s => !validNames.includes(s));
            if (invalidActions.length > 0) {
                return this.createError({
                    path: "pendingEvents",
                    message: i18next.t("InvalidActionNames", {actionNames: invalidActions.join(", ")}),
                });
            }
            return true;
        },
    })
    .test({
        message: "PendingResponseNamesValid",
        test: function (value: any) {
            const sim = value as ContextSimulation;
            const contextType = (this.options.context as any)?.[contextTypeKey] as ContextType | undefined;
            if (!contextType) {
                return true;
            }
            const validNames = contextType.tasks.flatMap(s => s.validResponses.map(r => s.name + "." + r.name));
            const invalidResponses = sim.pendingEvents
                .filter(isResponseEvent)
                .map(s => s.taskTypeName + "." + s.typeName)
                .filter(s => !validNames.includes(s));
            if (invalidResponses.length > 0) {
                return this.createError({
                    path: "pendingEvents",
                    message: i18next.t("InvalidResponseNames", {responseNames: invalidResponses.join(", ")}),
                });
            }
            return true;
        },
    })
    .test({
        message: () => i18next.t("InitialPendingEventMustBeAnInitialActionWithReference"),
        test: function (value: any) {
            const sim = value as ContextSimulation;
            const contextType = (this.options.context as any)?.[contextTypeKey] as ContextType | undefined;
            if (!contextType) {
                return true;
            }
            if ((sim.history && sim.history.length > 0) || sim.pendingEvents.length === 0) {
                return true;
            }
            const initialEvent = sim.pendingEvents[0];
            if (
                isActionEvent(initialEvent) &&
                isPresent(initialEvent.reference) &&
                contextType.initialActions.includes(initialEvent.action)
            ) {
                return true;
            }
            return this.createError({
                path: "pendingEvents[0]",
                message: i18next.t("InitialPendingEventMustBeAnInitialActionWithReference"),
            });
        },
    })
    .test({
        message: () => i18next.t("SimulationStateMustProvideEitherHistoryOrPendingInitialAction"),
        test: function (value: any) {
            const sim = value as ContextSimulation;
            if (
                sim.history.length > 0 ||
                (sim.pendingEvents.length > 0 &&
                    (isActionEvent(sim.pendingEvents[0]) || isInitialStateEvent(sim.pendingEvents[0])))
            ) {
                return true;
            }
            return this.createError({
                path: "pendingEvents",
                message: i18next.t("SimulationStateMustProvideEitherHistoryOrPendingInitialAction"),
            });
        },
    })
    .test({
        message: "EventPayloadsAreValid",
        test: function (value: any) {
            const sim = value as ContextSimulation;
            if (sim.pendingEvents.length === 0) {
                return true;
            }
            const contextType = (this.options.context as any)?.[contextTypeKey] as ContextType | undefined;
            if (!contextType || !contextType.jsonSchema) {
                return true;
            }
            const globalSchema = JSON.parse(contextType.jsonSchema) as JSONSchema7;
            const errors: yup.ValidationError[] = [];
            sim.pendingEvents.forEach((pe, i) => {
                const schemaName = getPayloadJsonSchemaDefinitionName(pe, contextType);
                if (schemaName) {
                    const schema = buildDefinitionSchema(globalSchema, schemaName);
                    if (schema) {
                        const result = validate(schema, pe.payload);
                        if (isValidationFailure(result)) {
                            errors.push(
                                ...result.errors.map(e => {
                                    const params = Object.entries(e.params)
                                        .map(([key, value]) => `${key}: ${value}`)
                                        .join("; ");
                                    return this.createError({
                                        path: `pendingEvents[${i}].payload${e.instancePath}`,
                                        message: `${e.message} (${params})`,
                                    });
                                })
                            );
                        }
                    }
                }
            });
            if (errors.length > 0) {
                const error = this.createError({});
                error.inner = errors;
                return error;
            }
            return true;

            function getPayloadJsonSchemaDefinitionName(event: SimulationEvent, contextType: ContextType) {
                if (isActionEvent(event)) {
                    const action = contextType.actions.find(s => s.name === event.action);
                    return action?.payloadSchemaDefinitionName;
                } else if (isResponseEvent(event)) {
                    const response = contextType.tasks
                        .find(s => s.name === event.taskTypeName)
                        ?.validResponses.find(s => s.name === event.typeName);
                    return response?.payloadSchemaDefinitionName;
                } else {
                    return undefined;
                }
            }
        },
    })
    .atLeastOneRequired(["contextTypeName", "contextType"]) as SchemaOf<ContextSimulation>;
