import React, {createContext, FC, useMemo, useCallback} from "react";
import {Control} from "react-hook-form";

import {ContextType, TaskType, ActionType, ResponseType} from "../../api/nimflow-api";
import {getResponsePayloadDefinitionName, getActionPayloadDefinitionName, ContextTypeSettings} from "../../api/nimflow-api-extensions";

export interface ContextTypeService {
    jsCommon?: string;
    jsonSchema?: Record<string, any>;
    uiSchemaCommon?: Record<string, any>;
    getJsonSchemaDefinition: (name: string) => string | undefined;
    getJsonSchemaDefinitions: (names: string[]) => Record<string, string | undefined>;
    setJsonSchemaDefinition: (name: string, jsonSchema: string | undefined) => void;
    setJsonSchemaDefinitions: (definitions: Record<string, string | undefined>) => void;
    updateTaskJsonSchemaDefinitions: (
        taskType: TaskType,
        definitionJsonSchemas: Record<string, string | undefined>
    ) => void;
    updateActionJsonSchemaDefinition: (actionType: ActionType, definitionJsonSchema: string | undefined) => void;
    updateResponseJsonSchemaDefinition: (
        responseType: ResponseType,
        taskTypeName: string,
        definitionJsonSchema: string | undefined
    ) => void;
}

const stub = () => {
    throw new Error("Forgot to wrap component in `ContextTypeContextProvider`");
};

const ContextTypeContext = createContext<ContextTypeService>({
    getJsonSchemaDefinition: stub,
    getJsonSchemaDefinitions: stub,
    setJsonSchemaDefinition: stub,
    setJsonSchemaDefinitions: stub,
    updateTaskJsonSchemaDefinitions: stub,
    updateActionJsonSchemaDefinition: stub,
    updateResponseJsonSchemaDefinition: stub,
});

export interface ContextTypeContextProviderProps {
    control: Control<ContextType>;
}

export const ContextTypeContextProvider: FC<ContextTypeContextProviderProps> = props => {
    const {
        control: {watchInternal, setValue},
        children,
    } = props;

    const jsCommon = watchInternal("jsCommon") as string | undefined;
    const jsonSchema = watchInternal("jsonSchema") as string | undefined;
    const settings = watchInternal("settings") as ContextTypeSettings | undefined;

    const uiSchemaCommon = useMemo(() => {
        const schema = settings?.uiSchemaCommon;
        return typeof schema === "string" ? JSON.parse(schema) as Record<string, any> : schema;
    }, [settings]);

    const getJsonSchemaDefinition = useCallback(
        (name: string): string | undefined => {
            if (!jsonSchema) {
                return undefined;
            }
            const schema = JSON.parse(jsonSchema);
            const defSchema = schema["definitions"]?.[name];
            return defSchema ? JSON.stringify(defSchema, undefined, 4) : undefined;
        },
        [jsonSchema]
    );

    const getJsonSchemaDefinitions = useCallback(
        (names: string[]): Record<string, string | undefined> => {
            if (!jsonSchema) {
                return {};
            }
            const schema = JSON.parse(jsonSchema);
            const result: Record<string, string | undefined> = {};
            for (const name of names) {
                const defSchema = schema["definitions"]?.[name];
                result[name] = defSchema ? JSON.stringify(defSchema, undefined, 4) : undefined;
            }
            return result;
        },
        [jsonSchema]
    );

    const setJsonSchemaDefinition = useCallback(
        (name: string, json: string | undefined): void => {
            const schema = jsonSchema ? JSON.parse(jsonSchema) : {};
            let defs = schema["definitions"];
            if (!defs) {
                schema["definitions"] = defs = {};
            }
            if (!json) {
                delete defs[name];
                if (Object.keys(defs).length === 0) {
                    delete schema["definitions"];
                }
            } else {
                defs[name] = JSON.parse(json);
            }
            setValue("jsonSchema", Object.keys(schema).length > 0 ? JSON.stringify(schema, undefined, 4) : undefined);
        },
        [jsonSchema, setValue]
    );

    const setJsonSchemaDefinitions = useCallback(
        (definitions: Record<string, string | undefined>): void => {
            const schema = jsonSchema ? JSON.parse(jsonSchema) : {};
            let defs = schema["definitions"];
            if (!defs) {
                schema["definitions"] = defs = {};
            }
            for (const [name, json] of Object.entries(definitions)) {
                if (!json) {
                    delete defs[name];
                    if (Object.keys(defs).length === 0) {
                        delete schema["definitions"];
                    }
                } else {
                    defs[name] = JSON.parse(json);
                }
            }
            setValue("jsonSchema", Object.keys(schema).length > 0 ? JSON.stringify(schema, undefined, 4) : undefined);
        },
        [jsonSchema, setValue]
    );

    const updateTaskJsonSchemaDefinitions = useCallback(
        (taskType: TaskType, definitionJsonSchemas: Record<string, string | undefined>) => {
            if (taskType.validResponses) {
                for (const resp of taskType.validResponses.filter(s => s.payloadSchemaDefinitionName)) {
                    const correctDefName = getResponsePayloadDefinitionName(taskType.name!, resp.name);
                    if (correctDefName === resp.payloadSchemaDefinitionName) {
                        continue;
                    }
                    if (resp.payloadSchemaDefinitionName! in definitionJsonSchemas) {
                        definitionJsonSchemas[correctDefName] =
                            definitionJsonSchemas[resp.payloadSchemaDefinitionName!];
                    } else {
                        definitionJsonSchemas[correctDefName] = getJsonSchemaDefinition(
                            resp.payloadSchemaDefinitionName!
                        );
                    }
                    definitionJsonSchemas[resp.payloadSchemaDefinitionName!] = undefined;
                    resp.payloadSchemaDefinitionName = correctDefName;
                }
            }
            setJsonSchemaDefinitions(definitionJsonSchemas);
        },
        [getJsonSchemaDefinition, setJsonSchemaDefinitions]
    );

    const updateActionJsonSchemaDefinition = useCallback(
        (actionType: ActionType, definitionJsonSchema: string | undefined) => {
            if (actionType.payloadSchemaDefinitionName) {
                const correctDefName = getActionPayloadDefinitionName(actionType.name);
                if (actionType.payloadSchemaDefinitionName !== correctDefName) {
                    setJsonSchemaDefinitions({
                        [actionType.payloadSchemaDefinitionName]: undefined,
                        [correctDefName]: definitionJsonSchema,
                    });
                    actionType.payloadSchemaDefinitionName = correctDefName;
                } else {
                    setJsonSchemaDefinition(actionType.payloadSchemaDefinitionName, definitionJsonSchema);
                }
            }
        },
        [setJsonSchemaDefinition, setJsonSchemaDefinitions]
    );

    const updateResponseJsonSchemaDefinition = useCallback(
        (responseType: ResponseType, taskTypeName: string, definitionJsonSchema: string | undefined) => {
            const correctDefName = getResponsePayloadDefinitionName(taskTypeName, responseType.name);
            if (responseType.payloadSchemaDefinitionName !== correctDefName) {
                setJsonSchemaDefinitions({
                    [responseType.payloadSchemaDefinitionName!]: undefined,
                    [correctDefName]: definitionJsonSchema,
                });
                responseType.payloadSchemaDefinitionName = correctDefName;
            } else {
                setJsonSchemaDefinition(responseType.payloadSchemaDefinitionName, definitionJsonSchema);
            }
        },
        [setJsonSchemaDefinition, setJsonSchemaDefinitions]
    );

    const contextTypeContext = useMemo(() => {
        const result: ContextTypeService = {
            jsCommon,
            jsonSchema: jsonSchema ? JSON.parse(jsonSchema) : undefined,
            uiSchemaCommon,
            getJsonSchemaDefinition,
            getJsonSchemaDefinitions,
            setJsonSchemaDefinition,
            setJsonSchemaDefinitions,
            updateActionJsonSchemaDefinition,
            updateResponseJsonSchemaDefinition,
            updateTaskJsonSchemaDefinitions,
        };
        return result;
    }, [
        getJsonSchemaDefinition,
        getJsonSchemaDefinitions,
        jsCommon,
        jsonSchema,
        uiSchemaCommon,
        setJsonSchemaDefinition,
        setJsonSchemaDefinitions,
        updateActionJsonSchemaDefinition,
        updateResponseJsonSchemaDefinition,
        updateTaskJsonSchemaDefinitions,
    ]);

    return <ContextTypeContext.Provider value={contextTypeContext}>{children}</ContextTypeContext.Provider>;
};

export const useContextTypeContext = () => React.useContext(ContextTypeContext);
