import React, {FC, useState, useMemo, useCallback} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import Button from "@material-ui/core/Button";
import Alert from "@material-ui/lab/Alert";
import IconButton from "@material-ui/core/IconButton";
import CheckIcon from "@material-ui/icons/Check";
import {useTranslation} from "react-i18next";
import useSWR from "swr";

import {ContextTypeInfo} from "../../api/nimflow-api";
import {useNimflow} from "../../api/nimflow-api-provider";
import {SearchField} from "../../components/search-field";
import {ContextTypeIcon} from "../../components/icons/context-type-icon";
import {useKey} from "../../utils/use-key";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        list: {
            flexGrow: 1,
            maxHeight: "400",
            overflowY: "auto",
        },
        paper: {
            backgroundColor: theme.palette.background.default,
        },
    })
);

const filterData = (data?: ContextTypeInfo[], search?: string): ContextTypeInfo[] => {
    if (!data) {
        return [];
    }
    if (!search) {
        return data;
    }
    const lcsearch = search.toLowerCase();
    return data.filter(
        s => s.name.toLowerCase().includes(lcsearch) || (s.description && s.description.includes(lcsearch))
    );
};

export interface ContextTypeSelectorProps {
    open: boolean;
    onSelected: (contextTypeName: string) => void;
    onClose: () => void;
}

const useContextTypes = (filter: string) => {
    const {contextTypes_Search} = useNimflow();
    const queryKey = useKey(contextTypes_Search);
    const {data, error, isValidating, mutate} = useSWR(queryKey, async () => {
        const result = await contextTypes_Search({limit: 100});
        return result;
    });
    return useMemo(() => {
        return {
            data: filterData(data, filter),
            isValidating,
            error,
            revalidate: mutate,
        };
    }, [data, filter, isValidating, error, mutate]);
}

export const ContextTypeSelector: FC<ContextTypeSelectorProps> = props => {
    const {open, onSelected, onClose} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const [filter, setFilter] = useState("");
    const {data: filteredData, error, isValidating} = useContextTypes(filter);

    const handleContextTypeSelected = useCallback(
        (name: string) => {
            onSelected?.(name);
        },
        [onSelected]
    );

    const handleDialogClose = useCallback(async (event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [onClose]);

    return (
        <Dialog
            open={open}
            onClose={handleDialogClose}
            fullWidth
            maxWidth="sm"
            classes={{paper: classes.paper}}
        >
            <DialogTitle>{t("SelectingContextType")}</DialogTitle>
            <DialogContent style={{minWidth: "400px"}}>
                <div style={{textAlign: "right"}}>
                    <SearchField value={filter} onChange={setFilter} />
                </div>
                <List className={classes.list}>
                    {filteredData.map(s => (
                        <ListItem key={s.name}>
                            <ListItemIcon>
                                <ContextTypeIcon />
                            </ListItemIcon>
                            <ListItemText primary={s.name} secondary={s.description} />
                            <ListItemSecondaryAction>
                                <IconButton edge="end" onClick={() => handleContextTypeSelected(s.name)}>
                                    <CheckIcon />
                                </IconButton>
                            </ListItemSecondaryAction>
                        </ListItem>
                    ))}
                </List>
                {!isValidating && !!error && <Alert severity="error">{error.toString()}</Alert>}
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
