import {useMemo} from "react";
import Axios from "axios";
import URI from "urijs";
import useSWRImmutable from "swr/immutable";

import {useNimflow} from "../api/nimflow-api-provider";
import {useCurrentUnitInfo} from "../api/nimflow-business-directory-provider";
import {JavascriptImport} from "../api/nimflow-api";
import {ExtraLib} from "../components/javascript-monaco-editor";
import {makeIdentifier} from "../utils/string-utils";

export interface GlobalImportTyping {
    import: JavascriptImport;
    typingsMap?: Record<string, string>;
    importBindings?: string[];
    typingsPackageJson?: any;
    rootTypingFile?: string;
}

const importsRegex =
    /^\s*import\s*(?:(?:\w+|(?:\w+\s*,\s*)?(?:(?:\*\s*as\s*\w+)|(?:\{[^}]*\})))\s*from)?\s*(['"])([^'"]*)\1\s*;*\s*$/gm;
const exportsRegex = /^\s*export\s*(?:(?:\*\s*(?:as\s*\w+)*)|\{[^}]*\})\s*from\s*(['"])([^'"]*)\1\s*;*\s*$/gm;

const findImports = (src: string) => {
    const importPaths: string[] = [];
    let matches: RegExpExecArray | null = null;
    while ((matches = importsRegex.exec(src)) !== null) {
        // This is necessary to avoid infinite loops with zero-width matches
        if (matches.index === importsRegex.lastIndex) {
            importsRegex.lastIndex++;
        }
        importPaths.push(matches[2]);
    }
    importsRegex.lastIndex = 0;
    while ((matches = exportsRegex.exec(src)) !== null) {
        // This is necessary to avoid infinite loops with zero-width matches
        if (matches.index === exportsRegex.lastIndex) {
            exportsRegex.lastIndex++;
        }
        importPaths.push(matches[2]);
    }
    exportsRegex.lastIndex = 0;
    return importPaths;
};

const failedSource = "/*FAILED*/";

const retrieveWithImports = (initialUrl: string, packageBaseUrl: string) => {
    const urlMap: Record<string, string> = {};
    let pending = 0;

    const processUrl = (url: string, resolve: (value: Record<string, string>) => void) => {
        const path = url.startsWith(packageBaseUrl) ? url.substring(packageBaseUrl.length - 1) : url;
        if (path in urlMap) {
            return;
        }
        urlMap[path] = "";
        pending++;
        Axios.get(url)
            .then(response => {
                const src = response.data as string;
                urlMap[path] = src;
                const importPaths = findImports(src);
                for (const impPath of importPaths) {
                    if (impPath.startsWith(".")) {
                        const absolutePath = URI(impPath).absoluteTo(url).valueOf();
                        processUrl(absolutePath + ".d.ts", resolve);
                    } /* package.json module relative path */ else {
                        urlMap[impPath] = "";
                    }
                }
            })
            .catch(() => {
                urlMap[path] = failedSource;
                if (url.endsWith(".d.ts")) {
                    processUrl(url.substring(0, url.length - 5) + ".ts", resolve);
                }
            })
            .finally(() => {
                pending--;
                if (pending <= 0) {
                    resolve(urlMap);
                }
            });
    };

    const result = new Promise<Record<string, string>>((resolve, reject) => {
        processUrl(initialUrl, (map: Record<string, string>) => {
            const failedKeys = Object.entries(map)
                .filter(([, value]) => value === failedSource)
                .map(([key]) => key);
            for (const key of failedKeys) {
                delete map[key];
            }
            resolve(map);
        });
    });
    return result;
};

const importClauseRegex = /^(((?<def2>\w+)\s*,\s*)?((\*\s*as\s*(?<ns>\w+))|(\{(?<list>[^}]*)\})))|(?<def>\w+)$/m;

const extractImportedBindings = (clause: string): string[] => {
    const result: string[] = [];
    let matches = importClauseRegex.exec(clause);
    if (matches) {
        // This is necessary to avoid infinite loops with zero-width matches
        if (matches.index === importClauseRegex.lastIndex) {
            importClauseRegex.lastIndex++;
        }
        if (matches.groups) {
            const importedDefaultBinding = matches.groups.def ?? matches.groups.def2;
            if (importedDefaultBinding) {
                result.push(importedDefaultBinding);
            }
            const namespaceImport = matches.groups.ns;
            if (namespaceImport) {
                result.push(namespaceImport);
            }
            if (matches.groups.list) {
                const list = matches.groups.list;
                for (const entry of list.split(",")) {
                    const parts = entry
                        .trim()
                        .split(" ")
                        .filter(s => s.trim().length > 0);
                    if (parts.length > 0) {
                        result.push(parts[parts.length === 3 ? 2 : 0]); // Consider "name as binding" definitions
                    }
                }
            }
        }
    }
    return result;
};

const getTypingsPackageJsonUrl = (imp: JavascriptImport) => {
    if (!imp.typingsPackageUrl && !imp.packageUrl) {
        return undefined;
    }
    let packageJsonUrl = imp.typingsPackageUrl ?? imp.packageUrl!;
    if (!packageJsonUrl.endsWith("/package.json")) {
        if (!packageJsonUrl.endsWith("/")) {
            packageJsonUrl += "/";
        }
        packageJsonUrl += "package.json";
    }
    return packageJsonUrl;
};

const retrieveTypings = async (globalImports: JavascriptImport[]) => {
    const pendingPromises: Promise<any>[] = [];
    const importsWithContents = globalImports.map(s => {
        const result: GlobalImportTyping = {
            import: s,
        };
        if (s.eS6ImportClause) {
            result.importBindings = extractImportedBindings(s.eS6ImportClause);
        }
        const packageJsonUrl = getTypingsPackageJsonUrl(s);
        if (packageJsonUrl) {
            const basePackageUrl = packageJsonUrl.substring(0, packageJsonUrl.lastIndexOf("/") + 1);
            pendingPromises.push(
                Axios.get(packageJsonUrl).then(response => {
                    const json = response.data;
                    result.typingsPackageJson = json;
                    if (typeof json === "object") {
                        let typingsPath: string | undefined = undefined;
                        if ("typings" in json && typeof json.typings === "string") {
                            typingsPath = json.typings as string;
                        } else if ("types" in json && typeof json.types === "string") {
                            typingsPath = json.types as string;
                        }
                        if (!typingsPath) {
                            return;
                        }
                        if (!typingsPath.startsWith(".")) {
                            if (!typingsPath.startsWith("/")) {
                                typingsPath = "/" + typingsPath;
                            }
                            typingsPath = "." + typingsPath;
                        }
                        result.rootTypingFile = typingsPath;
                        const rootTsUrl = URI(typingsPath).absoluteTo(basePackageUrl).valueOf();
                        return retrieveWithImports(rootTsUrl, basePackageUrl).then(map => {
                            result.typingsMap = map;
                        });
                    }
                })
            );
        } else if (s.from) {
            pendingPromises.push(
                Axios.get(s.from)
                    .then(response => {
                        result.typingsMap = {[`${s.name}/index.ts`]: response.data as string};
                    })
                    .catch()
            );
        }
        return result;
    });
    await Promise.allSettled(pendingPromises);
    return importsWithContents;
};

const synthesizeGlobalAmbientsSource = (importContents: GlobalImportTyping[]) => {
    const imports: string[] = [];
    const tempTypes: string[] = [];
    const globals: string[] = [];

    for (const imp of importContents.filter(s => !!s.typingsMap && s.import.synthesizeTypingGlobal !== false)) {
        let importClause = imp.import.eS6ImportClause;
        let importBindings = imp.importBindings;
        if (!importClause || !importBindings) {
            const bindingName = makeIdentifier(imp.import.name!);
            importClause = `* as ${bindingName}`;
            importBindings = [bindingName];
        }
        let modname = imp.typingsPackageJson!.name as string;
        if (modname?.startsWith("@types/")) {
            modname = modname.substring(7);
        }
        imports.push(`import ${importClause} from '${modname}';`);
        const baseIdx = tempTypes.length;
        importBindings.forEach((s, i) => {
            tempTypes.push(`\nexport type ImportedType${baseIdx + i + 1} = typeof ${s};`);
            globals.push(`\n\tconst ${s}: ImportedType${baseIdx + i + 1};`);
        });
    }
    return `${imports.join("\n")}
    ${tempTypes.join("")}
    declare global {
        ${globals.join("")}
    }`;
};

const generateExtraLibs = (importContents: GlobalImportTyping[]) => {
    const extraLibs: ExtraLib[] = [];
    if (importContents) {
        for (const imp of importContents.filter(s => !!s.typingsMap)) {
            let pkgName = imp.typingsPackageJson.name as string;
            extraLibs.push({
                content: JSON.stringify(imp.typingsPackageJson),
                filePath: `file:///node_modules/${pkgName}/package.json`,
            });
            for (const [key, value] of Object.entries(imp.typingsMap!)) {
                if (key.startsWith("/")) {
                    extraLibs.push({
                        content: value,
                        filePath: `file:///node_modules/${pkgName}${key}`,
                    });
                }
            }
        }
        const globalSrc = synthesizeGlobalAmbientsSource(importContents);
        extraLibs.push({
            content: globalSrc,
            filePath: `file:///node_modules/@types/__globals__/index.d.ts`,
        });
    }
    return extraLibs;
};

export const useGlobalImports = () => {
    const {unit: currentUnit} = useCurrentUnitInfo();
    const {javascript_GetGlobalImports} = useNimflow();
    const {data: monacoExtraLibs} = useSWRImmutable(currentUnit?.id ?? "", async () => {
        if (!currentUnit) {
            return [];
        }
        const imports = await javascript_GetGlobalImports().catch(() => [] as JavascriptImport[]);
        if (!imports) {
            return [];
        }
        const importsWithContents = await retrieveTypings(imports);
        if (!importsWithContents) {
            return [];
        }
        const extraLibs = generateExtraLibs(importsWithContents);
        return extraLibs;
    });

    const result = useMemo(() => {
        const result = {
            monacoExtraLibs: Array.isArray(monacoExtraLibs) ? monacoExtraLibs : [],
        };
        return result;
    }, [monacoExtraLibs]);

    return result;
};
