import React, {FC, useState, useMemo, useCallback} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import {useTranslation} from "react-i18next";

import {ContextSelectionStep} from "./context-selection-step";
import {ContextFilterStep} from "./context-filter-step";
import {SearchContexts, Context, SimulationEvent} from "../../../api/nimflow-api";
import {SimulationData} from "../context-simulation-generator-from-context";
import {ContextStateSelectionStep} from "./context-state-selection-step";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        content: {
            minWidth: "400px",
            minHeight: "50px",
        },
    })
);

export interface SimulationStateSelectorData {
    searchFilter?: Partial<SearchContexts>;
    selectedContext?: Context;
    pendingEvents?: SimulationEvent[];
}

export interface SimulationStateSelectorProps {
    open: boolean;
    onClose: () => void;
    onSimulationDataSelected?: (simulationData: SimulationData) => void;
}

export interface SimulationStateSelectorStepProps {
    data: SimulationStateSelectorData;
    onNext: (data: SimulationStateSelectorData) => void;
    onBack: () => void;
    onCancel: () => void;
}

const stepComponents = [ContextFilterStep, ContextSelectionStep, ContextStateSelectionStep];

export const SimulationStateSelector: FC<SimulationStateSelectorProps> = props => {
    const {open, onClose, onSimulationDataSelected} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const [activeStep, setActiveStep] = useState(0);
    const [data, setData] = useState<SimulationStateSelectorData>({});

    const steps = useMemo(() => {
        return [t("FillContextFilter"), t("SelectContext"), t("SelectContextState")];
    }, [t]);

    const handleBack = useCallback(() => {
        setActiveStep(prevActiveStep => prevActiveStep - 1);
    }, [setActiveStep]);

    const handleNext = useCallback(
        (data: SimulationStateSelectorData) => {
            if (activeStep === stepComponents.length - 1) {
                onSimulationDataSelected?.({
                    context: data.selectedContext!,
                    pendingEvents: data.pendingEvents!,
                });
                onClose();
            }
            setData(data);
            setActiveStep(prevActiveStep => prevActiveStep + 1);
        },
        [activeStep, setActiveStep, onClose, onSimulationDataSelected]
    );

    const Component = stepComponents[activeStep];

    const handleDialogClose = useCallback(async (event: object, reason: 'backdropClick' | 'escapeKeyDown') => {
        if (reason !== "backdropClick") {
            onClose?.();
        }
    }, [onClose]);

    return (
        <Dialog open={open} onClose={handleDialogClose} fullWidth maxWidth="lg">
            <DialogTitle>{t("SelectingContext")}</DialogTitle>
            <DialogContent className={classes.content}>
                <Stepper activeStep={activeStep} alternativeLabel>
                    {steps.map((label, i) => (
                        <Step key={i}>
                            <StepLabel>{label}</StepLabel>
                        </Step>
                    ))}
                </Stepper>
                {!!Component && <Component data={data} onNext={handleNext} onBack={handleBack} onCancel={onClose} />}
            </DialogContent>
        </Dialog>
    );
};
