import {useCallback} from "react";
import {omit} from "ramda";

import {useNimflow} from "./nimflow-api-provider";
import {
    GenericEventListener,
    actionDispatchedEventType,
    taskCreatedEventType,
    taskArchivedEventType,
    taskUpdatedEventType,
    responseSentEventType,
    milestoneReachedEventType,
    milestoneClearedEventType,
    customEventType,
    requestProcessedEventType,
    blobStorageUpdatedType,
} from "./nimflow-api-extensions";
import { CancelToken } from "axios";

export const useNimflowEventListeners = () => {
    const {
        events_AddRequestProcessedEventListener,
        events_AddActionDispatchedEventListener,
        events_AddTaskCreatedEventListener,
        events_AddTaskArchivedEventListener,
        events_AddTaskUpdatedEventListener,
        events_AddResponseSentEventListener,
        events_AddMilestoneReachedEventListener,
        events_AddMilestoneClearedEventListener,
        events_AddCustomEventListener,
        events_AddBlobStorageUpdatedEventListener,
        events_RemoveListener,
        events_SearchContextEventListeners,
    } = useNimflow();

    const addEventListener = useCallback(
        async (listener: GenericEventListener, cancelToken?: CancelToken) => {
            let result: {id: string};
            switch (listener.event) {
                case requestProcessedEventType:
                    result = await events_AddRequestProcessedEventListener(omit(["id", "event"], listener), cancelToken);
                    break;
                case actionDispatchedEventType:
                    result = await events_AddActionDispatchedEventListener(omit(["id", "event"], listener), cancelToken);
                    break;
                case taskCreatedEventType:
                    result = await events_AddTaskCreatedEventListener(omit(["id", "event"], listener), cancelToken);
                    break;
                case taskArchivedEventType:
                    result = await events_AddTaskArchivedEventListener(omit(["id", "event"], listener), cancelToken);
                    break;
                case taskUpdatedEventType:
                    result = await events_AddTaskUpdatedEventListener(omit(["id", "event"], listener), cancelToken);
                    break;
                case responseSentEventType:
                    result = await events_AddResponseSentEventListener(omit(["id", "event"], listener), cancelToken);
                    break;
                case milestoneReachedEventType:
                    result = await events_AddMilestoneReachedEventListener(omit(["id", "event"], listener), cancelToken);
                    break;
                case milestoneClearedEventType:
                    result = await events_AddMilestoneClearedEventListener(omit(["id", "event"], listener), cancelToken);
                    break;
                case customEventType:
                    result = await events_AddCustomEventListener(omit(["id", "event"], listener), cancelToken);
                    break;
                case blobStorageUpdatedType:
                    result = await events_AddBlobStorageUpdatedEventListener(omit(["id", "event"], listener), cancelToken);
                    break;    
                default:
                    throw new Error("Unrecognized event listener type");
            }
            return result.id;
        },
        [
            events_AddActionDispatchedEventListener,
            events_AddCustomEventListener,
            events_AddBlobStorageUpdatedEventListener,
            events_AddMilestoneClearedEventListener,
            events_AddMilestoneReachedEventListener,
            events_AddRequestProcessedEventListener,
            events_AddResponseSentEventListener,
            events_AddTaskArchivedEventListener,
            events_AddTaskCreatedEventListener,
            events_AddTaskUpdatedEventListener,
        ]
    );

    const removeEventListener = useCallback(
        async (listenerId: string, cancelToken?: CancelToken) => {
            await events_RemoveListener({id: listenerId}, cancelToken);
        },
        [events_RemoveListener]
    );

    const updateEventListener = useCallback(
        async (listener: GenericEventListener, cancelToken?: CancelToken) => {
            await removeEventListener(listener.id!, cancelToken);
            return await addEventListener(listener);
        },
        [addEventListener, removeEventListener]
    );

    const findEventListener = useCallback(
        async (listenerId: string, cancelToken?: CancelToken) => {
            const listener = (await events_SearchContextEventListeners({}, cancelToken)).find(s => s.id === listenerId);
            return listener as GenericEventListener;
        },
        [events_SearchContextEventListeners]
    );

    const api = {
        addEventListener,
        removeEventListener,
        updateEventListener,
        findEventListener,
    };

    return api;
};
