import {deburr, trim, upperFirst} from "lodash";
import {JSONSchema7Definition} from "json-schema";
import {JSONSchema} from "./types/JSONSchema";

// keys that shouldn't be traversed by the catchall step
const BLACKLISTED_KEYS = new Set([
    "$id",
    "$schema",
    "title",
    "description",
    "default",
    "multipleOf",
    "maximum",
    "exclusiveMaximum",
    "minimum",
    "exclusiveMinimum",
    "maxLength",
    "minLength",
    "pattern",
    "additionalItems",
    "items",
    "maxItems",
    "minItems",
    "uniqueItems",
    "maxProperties",
    "minProperties",
    "required",
    "additionalProperties",
    "definitions",
    "properties",
    "patternProperties",
    "dependencies",
    "enum",
    "type",
    "allOf",
    "anyOf",
    "oneOf",
    "not",
]);
function traverseObjectKeys(
    obj: Record<string, JSONSchema7Definition | string[]>,
    callback: (schema: JSONSchema, isRoot: boolean) => void
) {
    Object.keys(obj).forEach(k => {
        const val = obj[k];
        if (val && typeof val === "object" && !Array.isArray(val)) {
            traverse(val, callback, false);
        }
    });
}
function traverseArray(arr: JSONSchema7Definition[], callback: (schema: JSONSchema, isRoot: boolean) => void) {
    arr.forEach(i => typeof i !== "boolean" && traverse(i, callback, false));
}
export function traverse(
    schema: JSONSchema,
    callback: (schema: JSONSchema, isRoot: boolean) => void,
    isRoot: boolean
): void {
    callback(schema, isRoot);

    if (schema.anyOf) {
        traverseArray(schema.anyOf, callback);
    }
    if (schema.allOf) {
        traverseArray(schema.allOf, callback);
    }
    if (schema.oneOf) {
        traverseArray(schema.oneOf, callback);
    }
    if (schema.properties) {
        traverseObjectKeys(schema.properties, callback);
    }
    if (schema.patternProperties) {
        traverseObjectKeys(schema.patternProperties, callback);
    }
    if (schema.additionalProperties && typeof schema.additionalProperties === "object") {
        traverse(schema.additionalProperties, callback, false);
    }
    if (schema.items) {
        const {items} = schema;
        if (Array.isArray(items)) {
            traverseArray(items, callback);
        } else if (typeof items !== "boolean") {
            traverse(items, callback, false);
        }
    }
    if (schema.additionalItems && typeof schema.additionalItems === "object") {
        traverse(schema.additionalItems, callback, false);
    }
    if (schema.dependencies) {
        traverseObjectKeys(schema.dependencies, callback);
    }
    if (schema.definitions) {
        traverseObjectKeys(schema.definitions, callback);
    }
    if (schema.not && typeof schema.not !== "boolean") {
        traverse(schema.not, callback, false);
    }

    // technically you can put definitions on any key
    Object.keys(schema)
        .filter(key => !BLACKLISTED_KEYS.has(key))
        .forEach(key => {
            const child = (schema as any)[key];
            if (child && typeof child === "object") {
                traverseObjectKeys(child, callback);
            }
        });
}

/**
 * Convert a string that might contain spaces or special characters to one that
 * can safely be used as a TypeScript interface or enum name.
 */
export function toSafeString(string: string) {
    // identifiers in javaScript/ts:
    // First character: a-zA-Z | _ | $
    // Rest: a-zA-Z | _ | $ | 0-9

    return upperFirst(
        // remove accents, umlauts, ... by their basic latin letters
        deburr(string)
            // replace chars which are not valid for typescript identifiers with whitespace
            .replace(/(^\s*[^a-zA-Z_$])|([^a-zA-Z_$\d])/g, " ")
            // uppercase leading underscores followed by lowercase
            .replace(/^_[a-z]/g, match => match.toUpperCase())
            // remove non-leading underscores followed by lowercase (convert snake_case)
            .replace(/_[a-z]/g, match => match.substr(1, match.length).toUpperCase())
            // uppercase letters after digits, dollars
            .replace(/([\d$]+[a-zA-Z])/g, match => match.toUpperCase())
            // uppercase first letter after whitespace
            .replace(/\s+([a-zA-Z])/g, match => trim(match.toUpperCase()))
            // remove remaining whitespace
            .replace(/\s/g, "")
    );
}

export function generateName(from: string, usedNames: Set<string>) {
    let name = toSafeString(from);
    if (!name) {
        name = "NoName";
    }

    // increment counter until we find a free name
    if (usedNames.has(name)) {
        let counter = 1;
        let nameWithCounter = `${name}${counter}`;
        while (usedNames.has(nameWithCounter)) {
            nameWithCounter = `${name}${counter}`;
            counter++;
        }
        name = nameWithCounter;
    }

    usedNames.add(name);
    return name;
}

/**
 * escape block comments in schema descriptions so that they don't unexpectedly close JSDoc comments in generated typescript interfaces
 */
export function escapeBlockComment(schema: JSONSchema) {
    const replacer = "* /";
    if (schema === null || typeof schema !== "object") {
        return;
    }
    for (const key of Object.keys(schema)) {
        if (key === "description" && typeof schema[key] === "string") {
            schema[key] = schema[key]!.replace(/\*\//g, replacer);
        }
    }
}
