import React, {FC, useCallback, useState} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import {useFormikContext} from "formik";
import Chip from "@material-ui/core/Chip";
import IconButton from "@material-ui/core/IconButton";
import InputLabel from "@material-ui/core/InputLabel";
import Tooltip from "@material-ui/core/Tooltip";
import AddIcon from "@material-ui/icons/Add";
import {useTranslation} from "react-i18next";

import {
    DashboardWidgetQueryDataSource,
    SummarizeExpression,
    SummarizeExpressionAggregate,
    SummarizeExpressionGrouping,
} from "../../api/nimflow-dashboard-widgets-api";
import {QueryFilterEditor} from "./query-filter-editor";
import {SummarizeAggregateEditorDialog} from "./summarize-aggregate-editor-dialog";
import {FormikErrorViewer} from "../../components/formik-error-viewer";
import {SummarizeGroupingEditorDialog} from "./summarize-grouping-editor-dialog";

const headerContainerHeight = 60;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        textField: {
            maxWidth: "60ch",
            width: "100%"
        },
        main: {
            display: "grid",
            gridTemplateColumns: "1fr 1fr",
            gridTemplateRows: `${headerContainerHeight}px auto`,
            rowGap: `${theme.spacing(2)}px`,
            columnGap: `${theme.spacing(3)}px`,
            placeItems: "stretch",
            height: "100%",
            overflowY: "hidden",
        },
        groupingsContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
            "& .viewers": {
                marginTop: 5,
                display: "flex",
                flexWrap: "wrap",
                alignItems: "center",
                "& > *": {
                    margin: theme.spacing(0.5),
                },
            },
        },
        aggregatesContainer: {
            gridColumn: "2 / 3",
            gridRow: "1 / 2",
            "& .viewers": {
                marginTop: 5,
                display: "flex",
                flexWrap: "wrap",
                alignItems: "center",
                "& > *": {
                    margin: theme.spacing(0.5),
                },
            },
        },
        filterContainer: {
            gridColumn: "1 / 3",
            gridRow: "2 / 3",
            paddingTop: 10,
        },
        chip: {},
    })
);

interface GroupingViewerProps {
    grouping: SummarizeExpressionGrouping;
    onDelete?: (grouping: SummarizeExpressionGrouping) => void;
}

const GroupingViewer: FC<GroupingViewerProps> = props => {
    const {grouping, onDelete} = props;

    let label = `${grouping.property}${grouping.value ? "_" + grouping.value : ""}`;
    if (grouping.timeGranularity) {
        label += ` (timeGranularity: ${grouping.timeGranularity})`;
    }

    return <Chip label={label} size="small" onDelete={event => onDelete?.(grouping)} />;
};

interface AggregateViewerProps {
    aggregate: SummarizeExpressionAggregate;
    onDelete?: (aggregate: SummarizeExpressionAggregate) => void;
}

const AggregateViewer: FC<AggregateViewerProps> = props => {
    const {aggregate, onDelete} = props;

    const label = `${aggregate.name}: ${aggregate.aggregation}(${aggregate.property ? aggregate.property : "*"}${
        aggregate.value ? "_" + aggregate.value : ""
    })`;

    return <Chip label={label} size="small" onDelete={event => onDelete?.(aggregate)} />;
};

export interface SummarizeQueryEditorProps {}

export const SummarizeQueryEditor: FC<SummarizeQueryEditorProps> = props => {
    const {t} = useTranslation();
    const {values: dataSource, setFieldValue, errors} = useFormikContext<Partial<DashboardWidgetQueryDataSource>>();
    const classes = useStyles();
    const [editingGrouping, setEditingGrouping] = useState<SummarizeExpressionGrouping>();
    const [editingAggregate, setEditingAggregate] = useState<SummarizeExpressionAggregate>();
    const query = dataSource.query as SummarizeExpression;

    const handleDeleteGrouping = useCallback(
        (grouping: SummarizeExpressionGrouping) => {
            const groupings = query.groupings;
            if (groupings) {
                setFieldValue(
                    "query.groupings",
                    groupings.filter(
                        s =>
                            s.property !== grouping.property ||
                            (s.property === grouping.property && s.value !== grouping.value)
                    )
                );
            }
        },
        [query.groupings, setFieldValue]
    );

    const handleAddGrouping = useCallback(() => {
        setEditingGrouping({property: ""});
    }, []);

    const handleDeleteAggregate = useCallback(
        (aggregate: SummarizeExpressionAggregate) => {
            const aggregates = query.aggregates;
            if (aggregates) {
                setFieldValue(
                    "query.aggregates",
                    aggregates.filter(s => s.name !== aggregate.name)
                );
            }
        },
        [query.aggregates, setFieldValue]
    );

    const handleAddAggregate = useCallback(() => {
        setEditingAggregate({name: "", aggregation: "count", property: ""});
    }, []);

    const handleAggregatesChange = useCallback(
        (value: SummarizeExpressionAggregate) => {
            let aggregates = query.aggregates ?? [];
            if (editingAggregate?.name && editingAggregate.name !== value.name) {
                aggregates = aggregates.map(s => (s.name !== editingAggregate.name ? s : value));
            } else {
                aggregates = [...aggregates, value];
            }
            setFieldValue("query.aggregates", aggregates);
        },
        [editingAggregate?.name, query.aggregates, setFieldValue]
    );

    const handleAggregatesClose = useCallback(() => {
        setEditingAggregate(undefined);
    }, []);

    const handleGroupingsChange = useCallback(
        (value: SummarizeExpressionGrouping) => {
            let groupings = query.groupings ?? [];
            if (
                editingGrouping?.property &&
                (editingGrouping.property !== value.property || editingGrouping.value === value.value)
            ) {
                groupings = groupings.map(s =>
                    s.property === editingGrouping.property && s.value === editingGrouping.value ? value : s
                );
            } else {
                groupings = [...groupings, value];
            }
            setFieldValue("query.groupings", groupings);
        },
        [editingGrouping, query.groupings, setFieldValue]
    );

    const handleGroupingsClose = useCallback(() => {
        setEditingGrouping(undefined);
    }, []);

    if (!dataSource.sourceCollection) {
        return null;
    }

    return (
        <div className={classes.main}>
            <div className={classes.groupingsContainer}>
                <InputLabel shrink>{t("Groupings")}</InputLabel>
                <div className="viewers">
                    {query.groupings?.map(s => (
                        <GroupingViewer key={s.property + "" + s.value} grouping={s} onDelete={handleDeleteGrouping} />
                    ))}
                    <Tooltip title={t("AddAggregate") as string}>
                        <IconButton onClick={handleAddGrouping} size="small">
                            <AddIcon />
                        </IconButton>
                    </Tooltip>
                </div>
                <FormikErrorViewer errors={errors} names="query.groupings" />
            </div>
            <div className={classes.aggregatesContainer}>
                <InputLabel shrink>{t("Aggregates")}</InputLabel>
                <div className="viewers">
                    {query.aggregates?.map(s => (
                        <AggregateViewer key={s.name} aggregate={s} onDelete={handleDeleteAggregate} />
                    ))}
                    <Tooltip title={t("AddAggregate") as string}>
                        <IconButton onClick={handleAddAggregate} size="small">
                            <AddIcon />
                        </IconButton>
                    </Tooltip>
                </div>
                <FormikErrorViewer errors={errors} names="query.aggregates" />
            </div>
            <div className={classes.filterContainer}>
                <QueryFilterEditor />
            </div>
            {!!editingAggregate && (
                <SummarizeAggregateEditorDialog
                    open={!!editingAggregate}
                    defaultValue={editingAggregate}
                    collection={dataSource.sourceCollection}
                    contextTypeName={dataSource.contextTypeName}
                    existingNames={query.aggregates?.map(s => s.name).filter(s => s !== editingAggregate.name)}
                    onChange={handleAggregatesChange}
                    onClose={handleAggregatesClose}
                />
            )}
            {!!editingGrouping && (
                <SummarizeGroupingEditorDialog
                    open={!!editingGrouping}
                    defaultValue={editingGrouping}
                    collection={dataSource.sourceCollection}
                    contextTypeName={dataSource.contextTypeName}
                    existingProperties={query.groupings?.map(s => s.property)}
                    onChange={handleGroupingsChange}
                    onClose={handleGroupingsClose}
                />
            )}
        </div>
    );
};
