import React, {FC, useState} from "react";
import {makeStyles, createStyles, Theme, useTheme} from "@material-ui/core/styles";
import {useTranslation} from "react-i18next";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Autosizer from "react-virtualized-auto-sizer";
import MonacoEditor, {EditorDidMount, EditorWillMount} from "react-monaco-editor";
import ReactMarkdown from "react-markdown";
import {useFormikContext} from "formik";
import {useDebounce} from "react-use";

import {DashboardWidgetDefinition} from "../../api/nimflow-dashboard-widgets-api";
import {DashboardWidgetVisualizationTitle} from "./dashboard-widget-visualization-title";
import {FormikErrorViewer} from "../../components/formik-error-viewer";
import {defaultMonacoOptions} from "../../libs/monaco";

export interface DashboardWidgetMarkdownVisualizationEditorProps {
    visible: boolean;
    onChangeVisualization: () => void;
}

const errorContainerHeight = 100;

interface StyleProps {
    showErrors: boolean;
    activeTab: number;
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        editor: {
            height: "60vh",
        },
        writeTab: {
            display: (props: StyleProps) => (props.activeTab === 0 ? "grid" : "none"),
            gridTemplateColumns: "auto",
            gridTemplateRows: (props: StyleProps) => (props.showErrors ? `auto ${errorContainerHeight}px` : "auto"),
            rowGap: "5px",
            placeItems: "stretch",
            overflowY: "hidden",
            //height: "100%",
            height: "calc(100% - 50px)",
        },
        previewTab: {
            height: "calc(100% - 50px)",
            overflowY: "auto",
        },
        editorContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
        },
        errorContainer: {
            gridColumn: "1 / 2",
            gridRow: "2 / 3",
            overflowY: "auto",
        },
    })
);

const editorWillMount: EditorWillMount = monaco => {
    const options: ReturnType<EditorWillMount> = {
        selectOnLineNumbers: true,
        //codeLens: false,
        minimap: {
            enabled: false,
        },
    };
    return options;
};

const editorDidMount: EditorDidMount = editor => {
    editor.focus();
};

export const DashboardWidgetMarkdownVisualizationEditor: FC<DashboardWidgetMarkdownVisualizationEditorProps> = props => {
    const {visible, onChangeVisualization} = props;
    const {errors, values: widgetDefinition, setFieldValue} = useFormikContext<Partial<DashboardWidgetDefinition>>();
    const {t} = useTranslation();
    const theme = useTheme();
    const [activeTab, setActiveTab] = useState(0);
    const templateErrors = (errors.visualization as any)?.template;
    const classes = useStyles({showErrors: !!templateErrors, activeTab});
    const visualization = widgetDefinition.visualization;
    const [code, setCode] = useState(visualization?.type === "markdown" ? visualization.template : undefined);
    useDebounce(
        () => {
            setFieldValue("visualization.template", code);
        },
        1000,
        [code]
    );

    const handleTabChange = (event: React.ChangeEvent<{}>, newValue: number) => {
        setActiveTab(newValue);
    };

    if (visualization?.type !== "markdown") {
        return null;
    }

    return (
        <>
            <DashboardWidgetVisualizationTitle onChangeVisualization={onChangeVisualization} />
            <div className={classes.editor}>
                <Tabs
                    value={activeTab}
                    onChange={handleTabChange}
                    variant="scrollable"
                    scrollButtons="on"
                    indicatorColor="primary"
                    textColor="primary"
                >
                    <Tab label={t("Write")} />
                    <Tab label={t("Preview")} />
                </Tabs>
                <div role="tabpanel" hidden={activeTab !== 0} className={classes.writeTab}>
                    <div className={classes.editorContainer}>
                        <Autosizer>
                            {({height, width}) => {
                                if (!visible) {
                                    return null;
                                }
                                return (
                                    <MonacoEditor
                                        width={width}
                                        height={height}
                                        language="markdown"
                                        theme={theme.palette.type === "light" ? "vs-light" : "vs-dark"}
                                        value={code}
                                        options={defaultMonacoOptions}
                                        onChange={setCode}
                                        editorWillMount={editorWillMount}
                                        editorDidMount={editorDidMount}
                                    />
                                );
                            }}
                        </Autosizer>
                    </div>
                    {!!templateErrors && (
                        <div className={classes.errorContainer}>
                            <FormikErrorViewer errors={errors} names="visualization.template" />
                        </div>
                    )}
                </div>
                {activeTab === 1 && (
                    <div role="tabpanel" className={classes.previewTab}>
                        <ReactMarkdown>{visualization.template}</ReactMarkdown>
                    </div>
                )}
            </div>
        </>
    );
};
