import {contextSimulationCollectionName} from "../api/nimflow-simulation-api";
import {localizationCollectionName} from "../api/nimflow-localization-api";
import {dashboardWidgetsCollectionName} from "../api/nimflow-dashboard-widgets-api";
import {dashboardsCollectionName} from "../api/nimflow-dashboard-api";

export const roles = {
    bumember: "bu-member",
    bucoordinator: "bu-coordinator",
};

export const permissions = {
    dispatchContextActions: "dispatch:context-actions",
    manageTasks: "manage:tasks",
    manage: "manage",
    readContexts: "read:contexts",
    readContextTypes: "read:context-types",
    readTasks: "read:tasks",
    readUsers: "read:users",
    sendTaskResponses: "send:task-responses",
    writeContextTypes: "write:context-types",
};

const collectionPermission = (permission: string, collectionName?: string) => {
    return `${permission}:${collectionName ?? "collections"}`;
};

export const readCollectionPermission = (collectionName?: string) => {
    return collectionPermission("read", collectionName);
};

export const deleteCollectionPermission = (collectionName?: string) => {
    return collectionPermission("read", collectionName);
};

export const insertCollectionPermission = (collectionName?: string) => {
    return collectionPermission("read", collectionName);
};

export const updateCollectionPermission = (collectionName?: string) => {
    return collectionPermission("update", collectionName);
};

export const manageCollectionPermission = (collectionName?: string) => {
    return collectionPermission("manage", collectionName);
};

export const permissionRequests = {
    modifyContextTypes: [permissions.readContextTypes, permissions.writeContextTypes],
    simulate: [
        permissions.readContextTypes,
        [manageCollectionPermission(contextSimulationCollectionName), manageCollectionPermission()],
    ],
    modifyLocalizations: [[manageCollectionPermission(localizationCollectionName), manageCollectionPermission()]],
    modifyWidgetDefinitions: [
        [manageCollectionPermission(dashboardWidgetsCollectionName), manageCollectionPermission()],
    ],
    modifyDashboardDefinitions: [[manageCollectionPermission(dashboardsCollectionName), manageCollectionPermission()]],
    modifyDigitalWorkers: [permissions.manage],
};
