import React, {FC, useCallback, useState} from "react";
import {useTranslation} from "react-i18next";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";

import {StepContainer} from "./step-container";
import {NewActionStep} from "./new-action-editor";
import {ActionEvent} from "../../../api/nimflow-api";
import {ActionDataEditor, ActionDataEditorState} from "../action-data-editor";
import {JsonIcon} from "../../../components/icons/json-icon";

export interface ActionDataEditorStepProps extends NewActionStep {}

export const ActionDataEditorStep: FC<ActionDataEditorStepProps> = props => {
    const {value, contextType, onBack, onNext, onCancel} = props;
    const {t} = useTranslation();
    const [jsonEditorOpen, setJsonEditorOpen] = useState(false);
    const [actionEvent, setActionEvent] = useState<ActionEvent>({
        action: value.action!,
        reference: value.reference,
        payload: value.payload,
    });
    const [hasErrors, setHasErrors] = useState(false);

    const handleNext = useCallback(() => {
        if (hasErrors) {
            return;
        }
        onNext(actionEvent);
    }, [onNext, hasErrors, actionEvent]);

    const handleActionChange = useCallback(
        (state: ActionDataEditorState) => {
            setActionEvent(state.value);
            setHasErrors(state.hasErrors);
        },
        [setActionEvent, setHasErrors]
    );

    const handleOpenJsonEditor = useCallback(() => {
        setJsonEditorOpen(true);
    }, []);

    const handleJsonEditorClose = useCallback(() => {
        setJsonEditorOpen(false);
    }, []);

    return (
        <StepContainer onBack={onBack} onNext={handleNext} nextActionCaption={t("AddAction")} onCancel={onCancel}>
            <ActionDataEditor
                value={actionEvent}
                contextType={contextType}
                onChange={handleActionChange}
                jsonEditorOpen={jsonEditorOpen}
                onJsonEditorClose={handleJsonEditorClose}
            />
            <Tooltip title="JSON" onClick={handleOpenJsonEditor}>
                <IconButton>
                    <JsonIcon />
                </IconButton>
            </Tooltip>
        </StepContainer>
    );
};
