import {FC, useState} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Link from "@material-ui/core/Link";
import OpenInNewIcon from "@material-ui/icons/OpenInNew";
import Autosizer from "react-virtualized-auto-sizer";
import {useFormikContext} from "formik";
import {useDebounce} from "react-use";
import {useTranslation} from "react-i18next";

import {DashboardWidgetVisualizationTitle} from "./dashboard-widget-visualization-title";
import {DashboardWidgetDefinition} from "../../api/nimflow-dashboard-widgets-api";
import {JavascriptMonacoEditor} from "../../components/javascript-monaco-editor";
import {freeVarDefs} from "./free-variable-defs";
import {FormikErrorViewer} from "../../components/formik-error-viewer";

export interface DashboardWidgetVegaLiteVisualizationEditorProps {
    visible: boolean;
    onChangeVisualization: () => void;
}

const errorContainerHeight = 100;

interface StyleProps {
    showErrors: boolean;
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        content: {
            height: "60vh",
            display: "flex",
            flexDirection: "column",
        },
        header: {
            flexGrow: 0,
            display: "flex",
        },
        body: {
            flexGrow: 1,
            marginTop: 5,
            display: "grid",
            gridTemplateColumns: "auto",
            gridTemplateRows: (props: StyleProps) => (props.showErrors ? `auto ${errorContainerHeight}px` : "auto"),
            rowGap: "5px",
            placeItems: "stretch",
            overflowY: "hidden",
            height: "calc(100% - 50px)",
        },
        editorContainer: {
            gridColumn: "1 / 2",
            gridRow: "1 / 2",
        },
        errorContainer: {
            gridColumn: "1 / 2",
            gridRow: "2 / 3",
            overflowY: "auto",
        },
        title: {
            display: "inline-block",
        },
        grow: {
            flexGrow: 1,
        },
        openIcon: {
            fontSize: "1rem",
            verticalAlign: "middle",
        },
    })
);

const freeVariables = [freeVarDefs.params, freeVarDefs.vegaLiteSpec];

export const DashboardWidgetVegaLiteVisualizationEditor: FC<DashboardWidgetVegaLiteVisualizationEditorProps> = props => {
    const {onChangeVisualization, visible} = props;
    const {errors, values: widgetDefinition, setFieldValue} = useFormikContext<Partial<DashboardWidgetDefinition>>();
    const {t} = useTranslation();
    const specErrors = (errors.visualization as any)?.jsSpecification;
    const classes = useStyles({showErrors: !!specErrors});

    const visualization = widgetDefinition.visualization;
    const [code, setCode] = useState(visualization?.type === "vega-lite" ? visualization.jsSpecification : undefined);
    useDebounce(
        () => {
            setFieldValue("visualization.jsSpecification", code);
        },
        1000,
        [code]
    );

    if (visualization?.type !== "vega-lite") {
        return null;
    }

    return (
        <div className={classes.content}>
            <div className={classes.header}>
                <div className={classes.title}>
                    <DashboardWidgetVisualizationTitle onChangeVisualization={onChangeVisualization} />
                </div>
                <div className={classes.grow} />
                <Link href="https://vega.github.io/vega-lite/docs/" rel="noreferrer" target="_blank" color="inherit">
                    {t("VegaLiteDocumentation")}
                    &nbsp;
                    <OpenInNewIcon className={classes.openIcon} />
                </Link>
            </div>
            <div className={classes.body}>
                <div className={classes.editorContainer}>
                    <Autosizer>
                        {({height, width}) => {
                            if (!visible) {
                                return null;
                            }
                            return (
                                <JavascriptMonacoEditor
                                    height={height}
                                    width={width}
                                    value={code}
                                    onChange={setCode}
                                    freeVariables={freeVariables}
                                    importJsCommon={false}
                                />
                            );
                        }}
                    </Autosizer>
                </div>
                {!!specErrors && (
                    <div className={classes.errorContainer}>
                        <FormikErrorViewer errors={errors} names="visualization.jsSpecification" />
                    </div>
                )}
            </div>
        </div>
    );
};
