import React, {FC, useCallback, useMemo, useRef} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Grid from "@material-ui/core/Grid";
import MenuItem from "@material-ui/core/MenuItem";
import * as yup from "yup";
import {useTranslation} from "react-i18next";
import i18next from "i18next";
import {FormikProps, FormikHelpers, Formik, Form, useFormikContext} from "formik";

import {
    SummarizeExpressionAggregate,
    DashboardWidgetAggregateDataSourceSourceCollection,
    getScalarPropertyNamesFor,
    getAggregatorsForProperty,
} from "../../api/nimflow-dashboard-widgets-api";
import {validateAndSubmit} from "../../utils/formikHelpers";
import {getSummarizeExpressionAggregate} from "./widget-definition-schema";
import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {contextListPropertyNames} from "../../api/nimflow-api-extensions";
import {DataSourcePropertySelector} from "./datasource-property-selector";

const titleHeight = 64;
const footerHeight = 52;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            backgroundColor: theme.palette.background.default,
            height: "100%",
            width: "100%",
            //minHeight: "90vh",
            maxHeight: "40vh",
            "& form": {
                height: "100%",
            },
        },
        dialogTitle: {
            height: titleHeight,
        },
        dialogContent: {
            height: `calc(100% - ${titleHeight}px - ${footerHeight}px)`,
            width: "100%",
        },
        dialogFooter: {
            height: footerHeight,
        },
    })
);

interface EditorProps {
    collection: DashboardWidgetAggregateDataSourceSourceCollection;
    contextTypeName?: string;
}

const Editor: FC<EditorProps> = props => {
    const {collection, contextTypeName} = props;
    const {t} = useTranslation();
    const {values: aggregate} = useFormikContext<SummarizeExpressionAggregate>();

    const propertyNames = useMemo(() => {
        let result = getScalarPropertyNamesFor(collection);
        if (collection === "contexts" && contextTypeName) {
            result = [...result, ...contextListPropertyNames];
        }
        return result;
    }, [collection, contextTypeName]);

    const aggregators = useMemo(() => {
        return getAggregatorsForProperty(collection, aggregate.property);
    }, [collection, aggregate.property]);

    return (
        <Grid container spacing={1}>
            <Grid item xs={12}>
                <FormikTextField
                    name="name"
                    label={t("Name")}
                    fullWidth
                    InputLabelProps={{required: true}}
                    required
                    autoFocus
                />
            </Grid>
            <Grid item xs={12}>
                <DataSourcePropertySelector
                    name="property"
                    valuePath="value"
                    sourceCollection={collection}
                    contextTypeName={contextTypeName}
                    label={t("Property")}
                    fullWidth
                    InputLabelProps={{required: true}}
                    propertyNames={propertyNames}
                    required
                />
            </Grid>
            <Grid item xs={12}>
                <FormikTextField
                    name="aggregation"
                    label={t("Aggregation")}
                    select
                    fullWidth
                    InputLabelProps={{required: true}}
                    required
                >
                    {aggregators.map(s => (
                        <MenuItem key={s} value={s}>
                            {s}
                        </MenuItem>
                    ))}
                </FormikTextField>
            </Grid>
        </Grid>
    );
};

export interface SummarizeAggregateEditorDialogProps {
    open: boolean;
    defaultValue: SummarizeExpressionAggregate;
    collection: DashboardWidgetAggregateDataSourceSourceCollection;
    contextTypeName?: string;
    existingNames?: string[];
    properties?: string[];
    onChange?: (value: SummarizeExpressionAggregate) => void;
    onClose: () => void;
}

const getExtendedSchema = (collection: DashboardWidgetAggregateDataSourceSourceCollection, usedNames?: string[]) => {
    let schema = getSummarizeExpressionAggregate(collection);
    if (!usedNames || usedNames.length === 0) {
        return schema;
    }
    return schema.shape({
        name: getSummarizeExpressionAggregate(collection).fields!.name.concat(
            yup
                .string()
                .defined()
                .test({
                    message: ({value}) => i18next.t("SummarizeExpressionAggregateNameAlreadyExists", {name: value}),
                    test: function (value: any) {
                        return !usedNames.includes(value);
                    },
                })
        ),
    });
};

export const SummarizeAggregateEditorDialog: FC<SummarizeAggregateEditorDialogProps> = props => {
    const {open, defaultValue, collection, contextTypeName, existingNames, onChange, onClose} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const schema = useMemo(() => {
        const names = existingNames?.map(s => s).filter(s => s !== defaultValue?.name);
        return getExtendedSchema(collection, names);
    }, [existingNames, collection, defaultValue?.name]);
    const formRef = useRef<FormikProps<SummarizeExpressionAggregate> | null>();
    const submitHandlerRef = useRef<(values: SummarizeExpressionAggregate) => void>();

    const handleAccept = useCallback(() => {
        const form = formRef.current;
        if (form && onChange) {
            submitHandlerRef.current = (data: SummarizeExpressionAggregate) => {
                onChange?.(data);
                onClose?.();
            };
            validateAndSubmit(form);
        }
    }, [onChange, onClose]);

    const handleSubmit = useCallback(
        async (values: SummarizeExpressionAggregate, actions: FormikHelpers<SummarizeExpressionAggregate>) => {
            try {
                if (submitHandlerRef.current) {
                    submitHandlerRef.current(values);
                    submitHandlerRef.current = undefined;
                }
            } finally {
                actions.setSubmitting(false);
            }
        },
        []
    );

    return (
        <Dialog
            open={open}
            fullWidth
            maxWidth="sm"
            classes={{paper: classes.paper}}
        >
            <Formik<SummarizeExpressionAggregate>
                innerRef={s => (formRef.current = s)}
                initialValues={defaultValue}
                onSubmit={handleSubmit}
                validationSchema={schema}
                validateOnChange={true}
                validateOnBlur={true}
            >
                <Form autoComplete="off">
                    <DialogTitle className={classes.dialogTitle}>{t("EditingAggregate")}</DialogTitle>
                    <DialogContent className={classes.dialogContent}>
                        <Editor collection={collection} contextTypeName={contextTypeName}/>
                    </DialogContent>
                    <DialogActions className={classes.dialogFooter}>
                        <Button onClick={handleAccept} color="primary">
                            {t("Accept")}
                        </Button>
                        <Button onClick={onClose} color="primary">
                            {t("Cancel")}
                        </Button>
                    </DialogActions>
                </Form>
            </Formik>
        </Dialog>
    );
};
