import {ContextTypePortModel} from "./context-type-port-model";
import {TaskType, ResponseType} from "../../../api/nimflow-api";
import {ContextTypeNodeModel} from "./context-type-node-model";

export class TaskTypeNodeModel extends ContextTypeNodeModel<Partial<TaskType>> {
    constructor(instance: Partial<TaskType>, instanceId: string) {
        super("taskType", instance, instanceId, instance.name ?? "");

        if (instance.validResponses) {
            for (const response of instance.validResponses.filter(s => s.name)) {
                this.addContextTypePort(false, `response-${response.name}`);
            }
        }
    }

    public getResponsePort(response: Partial<ResponseType>): ContextTypePortModel | undefined {
        return (this.getPort(`response-${response.name}`) as ContextTypePortModel) ?? undefined;
    }

    public hasOutPort() {
        return false;
    }
}
