import {useRef, useEffect, useCallback} from "react";
import {useField, useFormikContext, FieldHookConfig} from "formik";

export const useFieldArray = <Val extends any>(props: string | FieldHookConfig<Val[]>) => {
    const [field, meta] = useField(props);
    const fieldArrayRef = useRef(field.value);
    const {setFieldValue} = useFormikContext();

    useEffect(() => {
        fieldArrayRef.current = field.value;
    }, [field.value]);

    const updateValue = useCallback(
        (fn: (currentValue: Val[]) => Val[]) => {
            fieldArrayRef.current = fn(fieldArrayRef.current);
            setFieldValue(field.name, fieldArrayRef.current);
        },
        [field.name, setFieldValue]
    );

    const push = useCallback(
        (value: Val) => {
            updateValue(s => [...s, value]);
        },
        [updateValue]
    );

    const swap = useCallback(
        (indexA: number, indexB: number) => {
            updateValue(s => {
                const swapA = s[indexA];
                const swapB = s[indexB];
                const result = [...s];
                result.splice(indexA, 1, swapB);
                result.splice(indexB, 1, swapA);
                return result;
            });
        },
        [updateValue]
    );

    const move = useCallback(
        (from: number, to: number) => {
            if (from === to) {
                return;
            }
            updateValue(s => {
                const toMove = s[from];
                const result = [...s];
                result.splice(from, 1);
                result.splice(to, 0, toMove);
                return result;
            });
        },
        [updateValue]
    );

    const insert = useCallback(
        (index: number, value: Val) => {
            updateValue(s => {
                const result = [...s];
                result.splice(index, 0, value);
                return result;
            });
        },
        [updateValue]
    );

    const unshift = useCallback(
        (value: Val) => {
            updateValue(s => [value, ...s]);
        },
        [updateValue]
    );

    const remove = useCallback(
        (index: number) => {
            let removedItem: Val | undefined;
            updateValue(s => {
                const result = [...s];
                removedItem = result.splice(index, 1)[0];
                return result;
            });
            return removedItem;
        },
        [updateValue]
    );

    const pop = useCallback(() => {
        let poppedItem: Val | undefined;
        updateValue(s => {
            const result = [...s];
            poppedItem = result.pop();
            return result;
        });
        return poppedItem;
    }, [updateValue]);

    const replace = useCallback(
        (index: number, value: Val) => {
            updateValue(s => {
                const result = [...s];
                result.splice(index, 1, value);
                return result;
            });
        },
        [updateValue]
    );

    const reset = useCallback(
        (v: Val[]) => {
            updateValue(() => v);
        },
        [updateValue]
    );

    const setAt = useCallback(
        (index: number, value: Val) => {
            updateValue(s => {
                const result = [...s];
                result[index] = value;
                return result;
            });
        },
        [updateValue]
    );

    return [
        field,
        meta,
        {
            push,
            swap,
            move,
            insert,
            unshift,
            remove,
            pop,
            replace,
            reset,
            setAt,
        },
    ] as const;
};
