import React, {FC, useCallback, useMemo, useRef} from "react";
import {createStyles, Theme, makeStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import {useTranslation} from "react-i18next";
import * as yup from "yup";
import type {SchemaOf} from "yup";
import {Form, FormikProps, Formik, FormikHelpers, useFormikContext} from "formik";
import {isPresent} from "ts-is-present";
import i18next from "i18next";

import {
    DashboardParameter,
    dashboardParameterSelectionTypes,
    SelectionDashboardParameter,
    TimeRangeDashboardParameter,
    FreeTextDashboardParameter,
    DashboardParameterSelectionType,
    isSelectionDashboardParameter,
} from "../../api/nimflow-dashboard-api";
import {
    TimeRangeDashboardParameterSchema,
    FreeTextDashboardParameterSchema,
    SelectionDashboardParameterSchema,
} from "./dashboard-definition-schema";
import {ErrorBoundary} from "../../components/error-boundary";
import {FormikTextField} from "../../components/formik-components/formik-text-field";
import {TimeRangeParameterEditor} from "./time-range-parameter-editor";
import {FreeTextParameterEditor} from "./free-text-parameter-editor";
import {SelectionParameterEditor, getValidSelectionDataTypes} from "./selection-parameter-editor";
import {validateAndSubmit} from "../../utils/formikHelpers";

const titleHeight = 64;
const footerHeight = 52;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            backgroundColor: theme.palette.background.default,
            height: "90vh",
            width: "100%",
            minHeight: "30em",
        },
        dialogTitle: {
            height: titleHeight,
        },
        dialogContent: {
            height: `calc(100% - ${titleHeight}px - ${footerHeight}px)`,
            width: "100%",
        },
        dialogFooter: {
            height: footerHeight,
        },
    })
);

export const createSelectionParameterTemplate = () => {
    const param: Partial<SelectionDashboardParameter> = {
        selectionType: "single",
        dataType: "string",
        allowEmptySelection: false,
        dataSource: {
            type: "static",
            options: [],
        },
        defaultValue: {
            values: [],
            selectAll: false,
        },
    };
    return param;
};

export const createTimeRangeParameterTemplate = () => {
    const param: Partial<TimeRangeDashboardParameter> = {
        selectionType: "timeRange",
        dataType: "dateinterval",
        defaultValue: {
            values: [
                {
                    value: {
                        type: "dynamic",
                        range: {
                            count: 15,
                            unit: "minutes",
                        },
                    },
                },
            ],
            selectAll: false,
        },
    };
    return param;
};

export const createFreeTextParameterTemplate = () => {
    const param: Partial<FreeTextDashboardParameter> = {
        selectionType: "freeText",
        dataType: "string",
        defaultValue: {
            values: [
                {
                    value: "",
                },
            ],
            selectAll: false,
        },
    };
    return param;
};

const ParameterEditor: FC<{}> = props => {
    const {values: param, setValues} = useFormikContext<DashboardParameter>();
    const {t} = useTranslation();

    const selectionType = param.selectionType;

    const handleSelectionTypeChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const newSelectionType = event.target.value as DashboardParameterSelectionType;
        switch (newSelectionType) {
            case "freeText": {
                const newParam = createFreeTextParameterTemplate();
                newParam.displayName = param.displayName;
                if (["string", "number"].includes(param.dataType)) {
                    newParam.dataType = param.dataType;
                    if (isPresent(param.defaultValue?.values[0]?.value)) {
                        newParam.defaultValue!.values[0].value = param.defaultValue?.values[0]?.value;
                    }
                }
                newParam.variableName =
                    param.selectionType === "timeRange" ? param.beginVariableName : param.variableName;
                setValues(newParam as any, false);
                break;
            }
            case "timeRange": {
                const newParam = createTimeRangeParameterTemplate();
                newParam.displayName = param.displayName;
                newParam.beginVariableName =
                    param.selectionType === "timeRange" ? param.beginVariableName : param.variableName;
                if (newParam.dataType === param.dataType) {
                    newParam.defaultValue = param.defaultValue;
                }
                setValues(newParam as any, false);
                break;
            }
            case "single":
            case "multiple": {
                const newParam = createSelectionParameterTemplate();
                newParam.displayName = param.displayName;
                newParam.selectionType = newSelectionType;
                newParam.allowEmptySelection = newSelectionType === "multiple";
                const validTypes = getValidSelectionDataTypes(newSelectionType === "multiple");
                if (validTypes.includes(param.dataType)) {
                    newParam.dataType = param.dataType;
                    newParam.defaultValue = param.defaultValue;
                }
                newParam.variableName =
                    param.selectionType === "timeRange" ? param.beginVariableName : param.variableName;
                if (isSelectionDashboardParameter(param)) {
                    newParam.dataSource = param.dataSource;
                }
                setValues(newParam as any, false);
                break;
            }
        }
    };

    return (
        <Grid container spacing={3}>
            <Grid item xs={12}>
                <FormikTextField
                    name="displayName"
                    label={t("DisplayName")}
                    InputLabelProps={{required: true}}
                    fullWidth
                    required
                />
            </Grid>
            <Grid item xs={12}>
                <TextField
                    label={t("SelectionType")}
                    select
                    InputLabelProps={{required: true}}
                    fullWidth
                    required
                    value={param.selectionType}
                    onChange={handleSelectionTypeChange}
                >
                    {dashboardParameterSelectionTypes.map(s => (
                        <MenuItem key={s} value={s}>
                            {t(`ParameterSelectionType_${s}`)}
                        </MenuItem>
                    ))}
                </TextField>
            </Grid>
            {selectionType === "timeRange" && <TimeRangeParameterEditor />}
            {selectionType === "freeText" && <FreeTextParameterEditor />}
            {isSelectionDashboardParameter(param) && <SelectionParameterEditor />}
            {/* <FormikErrorViewer errors={errors}/> */}
        </Grid>
    );
};

const getSchema = (
    value: Partial<DashboardParameter>,
    usedDisplayNames: string[],
    usedVariableNames: string[]
): SchemaOf<DashboardParameter> => {
    const displayName = yup
        .string()
        .required()
        .test({
            test: function (val: any) {
                if (usedDisplayNames.includes(val)) {
                    return this.createError({
                        path: this.path,
                        message: i18next.t("DuplicateParameterDisplayName", {displayName: val}),
                    });
                }
                return true;
            },
        });
    const variableName = yup
        .string()
        .required()
        .test({
            test: function (val: any) {
                if (usedVariableNames.includes(val)) {
                    return this.createError({
                        path: this.path,
                        message: i18next.t("DuplicateParameterVariableName", {variableName: val}),
                    });
                }
                return true;
            },
        });
    switch (value.selectionType) {
        case "timeRange":
            return TimeRangeDashboardParameterSchema.shape({
                displayName,
                beginVariableName: variableName.test({
                    test: function (this: yup.TestContext<any>, val: any) {
                        if (this.parent.endVariableName === val) {
                            return this.createError({
                                path: this.path,
                                message: i18next.t("DuplicateParameterVariableName", {variableName: val}),
                            });
                        }
                        return true;
                    },
                }),
                endVariableName: variableName.test({
                    test: function (this: yup.TestContext<any>, val: any) {
                        if (this.parent.beginVariableName === val) {
                            return this.createError({
                                path: this.path,
                                message: i18next.t("DuplicateParameterVariableName", {variableName: val}),
                            });
                        }
                        return true;
                    },
                }),
            });
        case "freeText":
            return FreeTextDashboardParameterSchema.shape({displayName, variableName});
        case "single":
        case "multiple":
            return SelectionDashboardParameterSchema.shape({displayName, variableName});
        default:
            throw new Error("Unrecognized parameter selectionType");
    }
};

export interface DashboardParameterEditionDialogProps {
    open: boolean;
    value: Partial<DashboardParameter>;
    usedDisplayNames: string[];
    usedVariableNames: string[];
    onChange?: (value: DashboardParameter) => void;
    onClose?: () => void;
}

export const DashboardParameterEditionDialog: FC<DashboardParameterEditionDialogProps> = props => {
    const {open, value, usedDisplayNames, usedVariableNames, onChange, onClose} = props;
    const classes = useStyles();
    const {t} = useTranslation();
    const initialValues = useMemo(() => {
        return {
            ...value,
        };
    }, [value]);
    const formRef = useRef<FormikProps<Partial<DashboardParameter>> | null>();

    const schema = useMemo(() => {
        const schema = yup.lazy<SchemaOf<DashboardParameter>>((s: DashboardParameter) => {
            return getSchema(s, usedDisplayNames, usedVariableNames);
        }) as unknown as SchemaOf<DashboardParameter>;
        return schema;
    }, [usedDisplayNames, usedVariableNames]);

    const handleAccept = useCallback(() => {
        const form = formRef.current;
        if (form) {
            validateAndSubmit(form);
        }
    }, []);

    const handleSubmit = useCallback(
        async (values: Partial<DashboardParameter>, actions: FormikHelpers<Partial<DashboardParameter>>) => {
            try {
                onChange?.(values as DashboardParameter);
                onClose?.();
            } finally {
                actions.setSubmitting(false);
            }
        },
        [onChange, onClose]
    );

    return (
        <Dialog
            open={open}
            fullWidth
            maxWidth="md"
            classes={{paper: classes.paper}}
        >
            <DialogTitle className={classes.dialogTitle}>{t("EditingDashboardParameter")}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <ErrorBoundary>
                    <Formik<Partial<DashboardParameter>>
                        innerRef={s => (formRef.current = s)}
                        initialValues={initialValues}
                        onSubmit={handleSubmit}
                        validationSchema={schema}
                        validateOnChange={true}
                        validateOnBlur={true}
                    >
                        <Form autoComplete="off">
                            <ParameterEditor />
                        </Form>
                    </Formik>
                </ErrorBoundary>
            </DialogContent>
            <DialogActions className={classes.dialogFooter}>
                <Button onClick={handleAccept} color="primary">
                    {t("Accept")}
                </Button>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};
