import {useCallback, useMemo} from "react";
import {useTranslation} from "react-i18next";
import {useBlocker} from "react-router-dom";
import {useOkCancelConfirm, OkCancelConfirmOptions} from "../components/confirm";

export const useExitWithoutSavingBlocker = (when?: boolean, options?: OkCancelConfirmOptions) => {
    const confirm = useOkCancelConfirm();
    const {t} = useTranslation();
    const opts = useMemo(() => {
        const opts = {
            description: t("ExitingWithoutSavingWarning"),
            confirmationText: t("Continue"),
            ...options,
        };
        return opts;
    }, [options, t]);
    const blocker = useCallback(
        async tx => {
            let result = await confirm(opts);
            if (result) {
                tx.retry();
            }
        },
        [confirm, opts]
    );

    useBlocker(blocker, when);
};
