import {FC, useCallback, useState, useMemo} from "react";
import {makeStyles, createStyles, Theme} from "@material-ui/core/styles";
import Dialog, {DialogProps} from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import Button from "@material-ui/core/Button";
import {useTranslation} from "react-i18next";

import {ActionEvent, TaskResponseEvent, SimulationEvent, ContextType} from "../../api/nimflow-api";
import {ActionDataEditor, ActionDataEditorState} from "./action-data-editor";
import {TaskResponseDataEditor, TaskResponseDataEditorState} from "./task-response-data-editor";
import {JsonIcon} from "../../components/icons/json-icon";
import {useContextSimulationContext} from "./simulation-context";
import {isActionEvent, isResponseEvent} from "../../api/nimflow-api-extensions";
import {getDialogMaxWidthForActionType, getDialogMaxWidthForResponseType} from "../../utils/dialog-max-width";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        grow: {
            flexGrow: 1,
        },
        dialogContent: {
            minWidth: "400px",
            minHeight: "50px",
        },
    })
);

export interface ActionEventEditorProps {
    event: Partial<ActionEvent>;
    contextType: ContextType;
    open: boolean;
    onChange?: (event: ActionEvent) => void;
    onClose: () => void;
}

export const ActionEventEditor: FC<ActionEventEditorProps> = props => {
    const {event: initialEvent, contextType, open, onClose, onChange} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const [event, setEvent] = useState(initialEvent);
    const [jsonEditorOpen, setJsonEditorOpen] = useState(false);
    const [hasErrors, setHasErrors] = useState(false);

    const actionType = useMemo(() => {
        return contextType?.actions.find(s => s.name === event.action);
    }, [contextType, event]);

    const handleActionDataChange = useCallback(
        (state: ActionDataEditorState) => {
            setHasErrors(state.hasErrors);
            setEvent(state.value);
        },
        [setEvent, setHasErrors]
    );

    const handleSendResponse = useCallback(async () => {
        if (hasErrors) {
            return;
        }
        onChange?.(event as ActionEvent);
        onClose();
    }, [hasErrors, event, onChange, onClose]);

    const handleOpenJsonEditor = useCallback(() => {
        setJsonEditorOpen(true);
    }, []);

    const handleJsonEditorClose = useCallback(() => {
        setJsonEditorOpen(false);
    }, []);

    const dialogWidth: DialogProps["maxWidth"] = useMemo(() => {
        if (!actionType) {
            return "md";
        }
        return getDialogMaxWidthForActionType(contextType, actionType?.name);
    }, [contextType, actionType]);

    return (
        <Dialog open={open} fullWidth maxWidth={dialogWidth}>
            <DialogTitle>{t("EditingActionData", {name: event.action})}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <ActionDataEditor
                    value={event as ActionEvent}
                    contextType={contextType}
                    onChange={handleActionDataChange}
                    jsonEditorOpen={jsonEditorOpen}
                    onJsonEditorClose={handleJsonEditorClose}
                />
            </DialogContent>
            <DialogActions>
                <Tooltip title="JSON" onClick={handleOpenJsonEditor}>
                    <IconButton disabled={!actionType?.payloadSchemaDefinitionName}>
                        <JsonIcon />
                    </IconButton>
                </Tooltip>
                <div className={classes.grow}></div>
                <Button onClick={handleSendResponse} color="primary">
                    {t("Ok")}
                </Button>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export interface TaskResponseEventEditorProps {
    event: Partial<TaskResponseEvent>;
    contextType: ContextType;
    open: boolean;
    onChange?: (event: TaskResponseEvent) => void;
    onClose: () => void;
}

export const TaskResponseEventEditor: FC<TaskResponseEventEditorProps> = props => {
    const {event: initialEvent, contextType, open, onClose, onChange} = props;
    const {t} = useTranslation();
    const classes = useStyles();
    const [event, setEvent] = useState(initialEvent);
    const [jsonEditorOpen, setJsonEditorOpen] = useState(false);
    const [hasErrors, setHasErrors] = useState(false);

    const responseType = useMemo(() => {
        const taskType = contextType.tasks.find(s => s.name === event.taskTypeName);
        return taskType?.validResponses.find(s => s.name === event.typeName);
    }, [contextType.tasks, event.taskTypeName, event.typeName]);

    const handleTaskResponseDataChange = useCallback(
        (state: TaskResponseDataEditorState) => {
            setHasErrors(state.hasErrors);
            setEvent(state.value);
        },
        [setEvent, setHasErrors]
    );

    const handleSendResponse = useCallback(async () => {
        if (hasErrors) {
            return;
        }
        onChange?.(event as TaskResponseEvent);
        onClose();
    }, [hasErrors, event, onChange, onClose]);

    const handleOpenJsonEditor = useCallback(() => {
        setJsonEditorOpen(true);
    }, []);

    const handleJsonEditorClose = useCallback(() => {
        setJsonEditorOpen(false);
    }, []);

    const dialogWidth: DialogProps["maxWidth"] = useMemo(() => {
        if (!responseType) {
            return "md";
        }
        return getDialogMaxWidthForResponseType(contextType, event.taskTypeName!, responseType.name);
    }, [contextType, event.taskTypeName, responseType]);

    return (
        <Dialog open={open} fullWidth maxWidth={dialogWidth}>
            <DialogTitle>{t("EditingResponseData", {name: event.typeName})}</DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <TaskResponseDataEditor
                    value={event as TaskResponseEvent}
                    contextType={contextType}
                    onChange={handleTaskResponseDataChange}
                    jsonEditorOpen={jsonEditorOpen}
                    onJsonEditorClose={handleJsonEditorClose}
                />
            </DialogContent>
            <DialogActions>
                <Tooltip title="JSON" onClick={handleOpenJsonEditor}>
                    <IconButton disabled={!responseType?.payloadSchemaDefinitionName}>
                        <JsonIcon />
                    </IconButton>
                </Tooltip>
                <div className={classes.grow}></div>
                <Button onClick={handleSendResponse} color="primary">
                    {t("Ok")}
                </Button>
                <Button onClick={onClose} color="primary">
                    {t("Cancel")}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export interface EventEditorProps {
    event: Partial<SimulationEvent>;
    open: boolean;
    onChange?: (event: SimulationEvent) => void;
    onClose: () => void;
}

export const EventEditor: FC<EventEditorProps> = props => {
    const {event, open, onClose, onChange} = props;
    const {getState} = useContextSimulationContext();
    const {contextType} = getState();

    return (
        <>
            {isActionEvent(event) && (
                <ActionEventEditor
                    event={event}
                    contextType={contextType}
                    open={open}
                    onClose={onClose}
                    onChange={onChange}
                />
            )}
            {isResponseEvent(event) && (
                <TaskResponseEventEditor
                    event={event}
                    contextType={contextType}
                    open={open}
                    onClose={onClose}
                    onChange={onChange}
                />
            )}
        </>
    );
};
