import React, {FC, useCallback, useMemo} from "react";
import ReactDOM from "react-dom";
import {v4 as uuid} from "uuid";
import List from "@material-ui/core/List";
import {OverridableComponent} from "@material-ui/core/OverridableComponent";
import {ExtendButtonBase} from "@material-ui/core";
import ListItem, {ListItemProps, ListItemTypeMap} from "@material-ui/core/ListItem";
import {
    DragDropContext,
    Droppable,
    Draggable,
    DropResult,
    DraggableProvided,
    DroppableProps,
} from "react-beautiful-dnd";
import usePortal from "../utils/use-portal";
import {ReactComponentProps} from "../utils/types";

export type PortaledListItemProps = {
    provided: DraggableProvided;
} & ListItemProps;

const PortaledListItem: FC<PortaledListItemProps> = props => {
    const {provided, children, ...rest} = props;
    //Workaround typescript union limitation (https://github.com/mui-org/material-ui/issues/15697#issuecomment-493419773)
    const typedListItemProps = rest as OverridableComponent<ListItemTypeMap<{button?: false}, "li">> &
        ExtendButtonBase<ListItemTypeMap<{button: true}, "div">>;
    const target = usePortal("OrderableListItemPortal");
    const child = (
        <div ref={provided.innerRef} {...provided.draggableProps}>
            <ListItem {...provided.dragHandleProps} {...typedListItemProps}>
                {children}
            </ListItem>
        </div>
    );
    return ReactDOM.createPortal(child, target);
};

export type OrderableListItemProps<D extends React.ElementType = "li", P = {}> = {
    index: number;
    draggableId?: string;
    isDragDisabled?: boolean;
    disableInteractiveElementBlocking?: boolean;
    shouldRespectForcePress?: boolean;
    beforeItem?: React.ReactNode | null;
    afterItem?: React.ReactNode | null;
} & Omit<ListItemProps<D, P>, "button">;

export const OrderableListItem: FC<OrderableListItemProps> = props => {
    const {
        index,
        draggableId,
        isDragDisabled,
        disableInteractiveElementBlocking,
        shouldRespectForcePress,
        beforeItem,
        afterItem,
        children,
        ...rest
    } = props;
    const id = useMemo(() => {
        return draggableId ?? uuid();
    }, [draggableId]);
    const draggableProps = {
        draggableId: id,
        index,
        isDragDisabled,
        disableInteractiveElementBlocking,
        shouldRespectForcePress,
    };

    return (
        <Draggable {...draggableProps}>
            {(provided, snapshot) => (
                <>
                    {beforeItem}
                    {!snapshot.isDragging && (
                        <ListItem
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                            {...rest}
                            ref={provided.innerRef}
                        >
                            {children}
                        </ListItem>
                    )}
                    {snapshot.isDragging && (
                        <PortaledListItem provided={provided} {...rest}>
                            {children}
                        </PortaledListItem>
                    )}
                    {afterItem}
                </>
            )}
        </Draggable>
    );
};

type ListProps = ReactComponentProps<typeof List>;

export type OrderableListProps = {
    onItemReorder: (from: number, to: number) => void;
    droppableId?: string;
} & Omit<DroppableProps, "children" | "droppableId"> &
    ListProps;

export const OrderableList: FC<OrderableListProps> = props => {
    const {
        onItemReorder,
        droppableId,
        type,
        mode,
        isDropDisabled,
        isCombineEnabled,
        direction,
        ignoreContainerClipping,
        renderClone,
        getContainerForClone,
        children,
        ...rest
    } = props;
    const handleOnDragEnd = useCallback(
        (result: DropResult) => {
            // dropped outside the list
            if (!result.destination) {
                return;
            }
            onItemReorder?.(result.source.index, result.destination.index);
        },
        [onItemReorder]
    );
    const id = useMemo(() => {
        return droppableId ?? uuid();
    }, [droppableId]);
    const droppableProps = {
        droppableId: id,
        type,
        mode,
        isDropDisabled,
        isCombineEnabled,
        direction,
        ignoreContainerClipping,
        renderClone,
        getContainerForClone,
    };

    return (
        <DragDropContext onDragEnd={handleOnDragEnd}>
            <Droppable {...droppableProps}>
                {(provided, snapshot) => (
                    <List {...rest} ref={provided.innerRef}>
                        {children}
                        {provided.placeholder}
                    </List>
                )}
            </Droppable>
        </DragDropContext>
    );
};
