import React, {useState, useCallback} from "react";
import TextField, {TextFieldProps} from "@material-ui/core/TextField";
import InputAdornment from "@material-ui/core/InputAdornment";
import IconButton from "@material-ui/core/IconButton";
import OpenInNewIcon from "@material-ui/icons/OpenInNew";
import {isPresent} from "ts-is-present";

import {useForwardedRef} from "../utils/use-forwarded-ref";
import {MarkdownMonacoEditorModal} from "./markdown-monaco-editor-modal";

export type MarkdownEditorProps = {
    defaultValue?: string;
    value?: string;
    onChange?: (value: string | undefined) => void;
} & Omit<TextFieldProps, "onChange">;

export const MarkdownEditor = React.forwardRef<HTMLInputElement, MarkdownEditorProps>((props, ref) => {
    const {defaultValue, value, onChange, ...rest} = props;
    const isControlled = "value" in props;
    const [editInMonaco, setEditInMonaco] = useState(false);
    const inputRef = useForwardedRef<HTMLInputElement>(ref);

    //Workaround typescript union limitation (https://github.com/mui-org/material-ui/issues/15697#issuecomment-493419773)
    const textFieldProps = rest as TextFieldProps;

    const handleMonacoEditorOnChange = useCallback(
        (newValue?: string) => {
            if (!isControlled && inputRef.current) {
                //uncontrolled
                inputRef.current.value = newValue ?? "";
            }
            onChange?.(newValue);
        },
        [onChange, isControlled, inputRef]
    );

    return (
        <>
            <TextField
                inputRef={inputRef}
                type="text"
                defaultValue={defaultValue}
                value={isControlled && !isPresent(value) ? "" : value}
                onChange={e => onChange?.(e.target.value)}
                InputProps={{
                    endAdornment: (
                        <InputAdornment position="end">
                            <IconButton onClick={() => setEditInMonaco(true)}>
                                <OpenInNewIcon />
                            </IconButton>
                        </InputAdornment>
                    ),
                }}
                multiline
                maxRows={3}
                {...textFieldProps}
            />
            {editInMonaco && (
                <MarkdownMonacoEditorModal
                    open={editInMonaco}
                    onClose={() => setEditInMonaco(false)}
                    defaultValue={value ?? defaultValue}
                    onChange={handleMonacoEditorOnChange}
                />
            )}
        </>
    );
});
